.PSPParkingMap {
  height: 100%;

  &__container {
    height: 100%;
    display: flex;
    margin-bottom: 10px;
    flex-direction: column;
    overflow: hidden;

    &--popupParkingMap_Open {
      opacity: var(--touchDisabledState);
    }

    &__firstList {
      overflow: hidden;
      height: 309px;
      flex-shrink: 0;

      &__listHeaderContainer {
        height: calc(var(--ueListTileHeaderHeight) - var(--zA4));
      }
    }

    &__secondList {
      height: 100%;
      overflow: hidden;
      margin-bottom: 10px;

      &__listHeaderContainer {
        height: calc(var(--ueListTileHeaderHeight) - var(--zA4));
        margin-top: 9px;
      }

      &__listVerticalContainer {
        height: calc(100% - 79px);
      }
      &--hideScrollbarShowStepper {
        .buttonContainer {
          max-width: unset;
        }
      }
    }
  }

  &__popupParkingMap {
    display: flex;
    width: fit-content;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-top: calc(var(--popupInsetWidth) + 14px);

    &__line {
      width: calc(100% - (var(--uiStateMargin) + 6px) * 2);
      border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
      margin: 14px calc(var(--uiStateMargin) + 6px) 0;
    }

    &__garageType {
      display: flex;
      margin-top: 30px;
      padding: 0 var(--popupContentPadding);
    }

    &__OperationPanelContainer {
      margin-top: 30px;
      width: 100%;
    }
  }
}
