/* CLASSIFICATION: CONFIDENTIAL */

.button {
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  position: relative;
}

.uiState {
  position: absolute;
  width: 0%;
  height: 0%;
  left: var(--uiStateMargin);
  right: var(--uiStateMargin);
  top: var(--uiStateMargin);
  bottom: var(--uiStateMargin);
  opacity: 0;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  display: inline-flex;
  flex-direction: column;
  border-radius: var(--standardBorderRadius);
}

.interactionBackground.fullSize {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.uiState.fullSize {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
}

.primary {
}

.uiState.fullSize {
  height: 100%;
}

.touchIcon {
  z-index: 1;
}

.uiState,
.uiState.borderTop,
.uiState.borderBottom {
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve),
    width var(--uiStateTransitionDuration) var(--animationCurve);
  height: calc(100% - 2 * var(--uiStateMargin));
}

.uiState.uiState.borderLeft,
.uiState.uiState.borderRight {
  height: 0;
}

.uiState.fullSize,
.uiState.fullSize.borderTop,
.uiState.fullSize.borderBottom {
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve),
    width var(--uiStateTransitionDuration) var(--animationCurve);
  height: 100%;
}

.uiState.borderLeft,
.uiState.borderRight {
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve);
  width: calc(100% - 2 * var(--uiStateMargin));
}

.uiState.fullSize.borderLeft,
.uiState.fullSize.borderRight {
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve);
  width: 100%;
}

.button:active .uiState,
.button:active .borderTop,
.threeStates:active .borderTop,
.button:active .borderBottom {
  width: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}

.button:active .borderLeft,
.button:active .borderRight {
  height: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}

.button:active .uiState.fullSize,
.threeStates:active .uiState.fullSize {
  width: 100%;
  height: 100%;
}

.button:active .uiState.borderLeft.fullSize,
.button:active .uiState.borderRight.fullSize {
  height: 100%;
}

.gradient.fading {
  opacity: 0;
}
.gradient.fading.editMode {
  opacity: 0.8;
}
.button:active .fading {
  opacity: 0.8;
}

.threeStates .uiState {
  -webkit-mask-image: linear-gradient(
    to right,
    black 0px,
    black calc((100% - var(--threeStatesGapSize) * 2) / 3),
    transparent calc((100% - var(--threeStatesGapSize) * 2) / 3),
    transparent
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 + var(--threeStatesGapSize)
      ),
    black
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 + var(--threeStatesGapSize)
      ),
    black
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 * 2 +
          var(--threeStatesGapSize)
      ),
    transparent
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 * 2 +
          var(--threeStatesGapSize)
      ),
    transparent
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 * 2 +
          var(--threeStatesGapSize) * 2
      ),
    black
      calc(
        (100% - var(--threeStatesGapSize) * 2) / 3 * 2 +
          var(--threeStatesGapSize) * 2
      )
  );
}
.threeStates .statesHidden {
  -webkit-mask-image: none;
}

.gradient {
  height: 100%;
  width: 100%;
  display: block;
  text-align: center;
  opacity: 1;
  position: absolute;
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve);
}

.primary .borderTop .primary,
.borderBottom .primary {
  height: calc(100% - 4px);
}

.ProgressState .uiState {
  margin-left: 0;
  max-width: calc(100% - 2 * var(--uiStateMargin));
}
/* 
.ProgressState.activated .uiState {
  margin-left: var(--uiStateMargin);
  margin-top: var(--uiStateMargin);
} */

.ProgressState .uiState.fullSize {
  margin-left: unset !important;
  margin-top: unset !important;
}

.toggle.activated .uiState,
.ProgressState.activated .uiState,
.ProgressState.activated .borderTop,
.ProgressState.activated .borderBottom,
.toggle.activated .borderTop,
.toggle.activated .borderBottom,
.ProgressState.activated .borderLeft,
.ProgressState.activated .borderRight,
.toggle.activated .borderLeft,
.toggle.activated .borderRight {
  width: calc(100% - 2 * var(--uiStateMargin));
  height: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}

.toggle.activated .uiState.fullSize,
.ProgressState.activated .uiState.fullSize,
.ProgressState.activated .uiState.fullSize.borderTop,
.ProgressState.activated .uiState.fullSize.borderBottom,
.toggle.activated .uiState.fullSize.borderTop,
.toggle.activated .uiState.fullSize.borderBottom {
  width: 100%;
  height: 100%;
  opacity: 1;
}

.toggle.activated .uiState.fullSize,
.ProgressState.activated .uiState.fullSize,
.ProgressState.activated .uiState.fullSize.borderTop,
.ProgressState.activated .uiState.fullSize.borderBottom,
.toggle.activated .uiState.fullSize.borderTop,
.toggle.activated .uiState.fullSize.borderBottom {
  width: 100%;
  height: 100%;
  opacity: 1;
}

