/*-------------------------------------------------- border ---------------------------------------------*/
/*----- top -----*/
.borderTop .white,
.borderTopBottom .primary.white {
  border-bottom: unset;
  border-top: 4px solid rgb(var(--audiWhite));
}
.borderTop .orange,
.borderTopBottom .primary.orange {
  border-bottom: unset;
  border-top: 4px solid rgb(var(--climateOrange));
}
.borderTop .red,
.borderTopBottom .primary.red {
  border-bottom: unset;
  border-top: 4px solid rgb(var(--audiRed));
}
.borderTop .blue,
.borderTopBottom .primary.blue {
  border-bottom: unset;
  border-top: 4px solid rgb(var(--climateBlue));
}
.borderTop .audiAI,
.borderTopBottom .primary.audiAI {
  border-bottom: unset;
  border-top: 4px solid rgb(var(--audiAI));
}

/*----- bottom -----*/
.white,
.borderBottom .white,
.borderTopBottom .secondary.white {
  border-bottom: 4px solid rgb(var(--audiWhite));
}
.orange,
.borderBottom .orange,
.borderTopBottom .secondary.orange {
  border-bottom: 4px solid rgb(var(--climateOrange));
}
.red,
.borderBottom .red,
.borderTopBottom .secondary.red {
  border-bottom: 4px solid rgb(var(--audiRed));
}
.blue,
.borderBottom .blue,
.borderTopBottom .secondary.blue {
  border-bottom: 4px solid rgb(var(--climateBlue));
}
.audiAI,
.borderBottom .audiAI,
.borderTopBottom .secondary.audiAI {
  border-bottom: 4px solid rgb(var(--audiAI));
}
.ambientColor,
.borderBottom .ambientColor,
.borderTopBottom .secondary.ambientColor {
  border-bottom: 4px solid rgb(var(--ambientcolor1));
  height: calc(100% - 8px);
  border-radius: 0;
  top: 0;
}

/*----- left -----*/
.borderLeft .white {
  border-bottom: unset;
  border-left: 4px solid rgb(var(--audiWhite));
}
.borderLeft .orange {
  border-bottom: unset;
  border-left: 4px solid rgb(var(--climateOrange));
}
.borderLeft .red {
  border-bottom: unset;
  border-left: 4px solid rgb(var(--audiRed));
}
.borderLeft .blue {
  border-bottom: unset;
  border-left: 4px solid rgb(var(--climateBlue));
}
.borderLeft .audiAI {
  border-bottom: unset;
  border-left: 4px solid rgb(var(--audiAI));
}

/*----- right -----*/
.borderRight .white {
  border-bottom: unset;
  border-right: 4px solid rgb(var(--audiWhite));
}
.borderRight .orange {
  border-bottom: unset;
  border-right: 4px solid rgb(var(--climateOrange));
}
.borderRight .red {
  border-bottom: unset;
  border-right: 4px solid rgb(var(--audiRed));
}
.borderRight .blue {
  border-bottom: unset;
  border-right: 4px solid rgb(var(--climateBlue));
}
.borderRight .audiAI {
  border-bottom: unset;
  border-right: 4px solid rgb(var(--audiAI));
}

.state {
  position: relative;
  width: 100%;
  height: 100%;
  flex: 1;
  transition: width var(--uiStateTransitionDuration) var(--animationCurve);
  border-radius: var(--standardBorderRadius);
}

.button .uiState .state.secondary {
  display: none;
}

.threeStates .uiState.borderTopBottom .state.secondary {
  display: block;
}

.threeStates .stepZero {
  width: 0;
}

.threeStates .stepOne {
  width: calc((100% - var(--threeStatesGapSize) * 2) / 3);
}
.threeStates .stepTwo {
  width: calc(
    (100% - var(--threeStatesGapSize) * 2) / 3 * 2 + var(--threeStatesGapSize)
  );
}
.threeStates .stepThree {
  width: 100%;
}

.threeStates .uiState {
  width: calc(100% - 2 * var(--uiStateMargin));
  height: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}

.threeStates .uiState.fullSize {
  width: 100%;
  height: 100%;
}

.borderLeft .primary,
.borderRight .primary {
  width: calc(100% - 4px);
}

.doubleBorder .secondary {
  display: block;
}

.hidden {
  display: none;
}

.threeStates .hidden {
  display: block;
  width: 0;
  opacity: 0;
  height: 0;
}

.threeStates .statesHidden {
  width: 0;
  opacity: 0;
}

.ProgressState:active .uiState,
.ProgressState:active .borderTop,
.ProgressState:active .borderBottom {
  width: 0;
}

.ProgressState:active .borderLeft,
.ProgressState:active .borderRight {
  height: 0;
}

.textContainer {
  display: flex;
  flex: initial;
  height: 100%;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  box-sizing: border-box;
}

.button.disabled {
  pointer-events: none; /* temporary */
}
.button.disabled .touchIcon,
.button.disabled p,
.button.disabled .uiState {
  opacity: var(--touchDisabledState);
}
