/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --cmcOuterSize: 49px;
  --cmcInnerSize: 45px;
  --cmcTouchSize: 35px;
}

#root > .SE_0106_Button_with_ConnectorBody {
  background-color: rgb(20, 20, 20);
}

.SE_0106_Button_with_ConnectorBody {
  width: 1px;
  height: 1px;
}

:global(#root)
  > .SE_0106_Button_with_ConnectorBody
  .SE_0106_Button_with_Connector {
  left: 500px;
  top: 255px;
}

.SE_0106_Button_with_Connector {
  position: absolute;
  width: var(--cmcOuterSize);
  height: var(--cmcOuterSize);
}

.touchable .touchArea {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  background-color: rgb(var(--boundingBoxGreen));
  left: calc(-1 * var(--cmcTouchSize));
  top: calc(-1 * var(--cmcTouchSize));
}

.squareContainer {
  position: absolute;
  border: 2px solid rgba(var(--audiBlack), 0);
  width: var(--cmcInnerSize);
  height: var(--cmcInnerSize);
}
.touchable .squareContainer {
  border: 2px solid rgba(var(--audiBlack), 0.2);
  border-radius: 0;
}
.touchable .innerSquareContainer {
  position: absolute;
  width: var(--cmcInnerSize);
  height: var(--cmcInnerSize);
  background-color: rgba(var(--audiWhite), 0.2);
  border-radius: var(--standardBorderRadius);
}
.rounded .squareContainer {
  border-radius: 50%;
}

.squareBlack {
  position: absolute;
  top: 17px;
  left: 17px;
  width: 15px;
  height: 15px;
  background-color: rgb(var(--audiBlack), 0.8);
  border-radius: var(--standardBorderRadius);
  transition: top var(--uiStateTransitionDuration) var(--animationCurve),
    left var(--uiStateTransitionDuration) var(--animationCurve),
    width var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve);
}
.activated .squareBlack {
  top: 12px;
  left: 12px;
  width: 25px;
  height: 25px;
}
.rounded .squareBlack {
  border-radius: 50%;
  top: 3px;
  left: 3px;
  width: 29px;
  height: 29px;
}

.squareBlack .square {
  position: absolute;
  top: 3px;
  left: 3px;
  width: 9px;
  height: 9px;
  background-color: rgb(var(--audiWhite), 1);
  z-index: 1;
  transition: width var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve),
    background-color var(--uiStateTransitionDuration) var(--animationCurve);
}
.activated .squareBlack .square {
  width: 19px;
  height: 19px;
  background-color: rgb(var(--ambientcolor1), 0.9);
}
.activated.constantColor .squareBlack .square {
  background-color: rgb(var(--parkingCameraTouchPointsActive), 0.9);
}
.rounded .squareBlack .square {
  border-radius: 50%;
  width: 21px;
  height: 21px;
}

.top.left .firstSegment {
  transform: translate(15px, 16px) rotate(225deg);
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
}
.top.left .firstSegmentShadow {
  transform: translate(16px, 20px) rotate(225deg);
}
.touchable.top.left .firstSegmentShadow {
  transform: translate(1px, 5px) rotate(225deg);
}

.top.right .firstSegment {
  transform: translate(29px, 15px) rotate(315deg);
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
}
.top.right .firstSegmentShadow {
  transform: translate(28px, 17px) rotate(315deg);
}
.touchable.top.right .firstSegmentShadow {
  transform: translate(44px, 1px) rotate(315deg);
}

.bottom.left .firstSegment {
  transform: translate(16px, 30px) rotate(135deg);
  clip-path: polygon(0 0, 100% 0%, 100%, 100%, 0 100%);
}
.bottom.left .firstSegmentShadow {
  transform: translate(21px, 32px) rotate(135deg);
}
.touchable.bottom.left .firstSegmentShadow {
  transform: translate(5px, 48px) rotate(135deg);
}

.bottom.right .firstSegment {
  transform: translate(30px, 29px) rotate(45deg);
}
.bottom.right .firstSegmentShadow {
  transform: translate(31px, 27px) rotate(45deg);
}
.touchable.bottom.right .firstSegmentShadow {
  transform: translate(48px, 44px) rotate(45deg);
}

.firstSegment {
  top: 2px;
  left: 2px;
  height: 1.5px;
  background-color: rgba(var(--audiWhite), 0.3);
  position: absolute;
  transform-origin: 0% 0%;
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
}
.firstSegmentShadow {
  top: 0px;
  left: 0px;
  height: 5.5px;
  background-color: rgba(var(--audiBlack), 0.4);
  position: absolute;
  transform-origin: 0% 0%;
  border-radius: 2px;
  clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);
}

.secondSegment {
  top: 2px;
  left: 10px;
  height: 1px;
  background-color: rgba(var(--audiWhite), 0.3);
  position: absolute;
  transform-origin: 0% 0%;
}
.secondSegmentShadow {
  top: 2px;
  left: 2px;
  height: 5px;
  background-color: rgba(var(--audiBlack), 0.4);
  position: absolute;
  transform-origin: 0% 0%;
}

.SE_0106_Button_with_ConnectorText {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
}

.SE_0106_Button_with_ConnectorText p {
  position: relative;
  padding: 0 10px;
  box-sizing: border-box;
}

.SE_0106_Button_with_ConnectorText .touchArea {
  position: absolute;
  min-height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  align-self: center;
  height: 100%;
  width: 100%;
  left: unset;
  top: unset;
}

.connectorButton {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  left: calc(-1 * var(--cmcTouchSize));
  top: calc(-1 * var(--cmcTouchSize));
}

.connectorButton.button .uiState {
  position: absolute;
  height: var(--cmcInnerSize);
  width: 0px;
  min-width: unset;
  left: unset;
  right: unset;
  top: unset;
  bottom: unset;
}
.connectorButton.button:active .uiState {
  position: absolute;
  height: var(--cmcInnerSize);
  width: var(--cmcInnerSize);
  min-width: unset;
  left: unset;
  right: unset;
  top: unset;
  bottom: unset;
}

.settings {
  position: absolute;
  top: 500px;
  width: 800px;
}
