.flex {
  --flex: initial;
  display: flex;
  flex: var(--flex);
  box-sizing: border-box;
}

.flexColumn {
  flex-direction: column;
}

.w100 {
  width: 100%;
}

.h100 {
  height: 100%;
}

.itemsCenter {
  align-items: center;
}

.itemsStart {
  align-items: flex-start;
}

.itemsEnd {
  align-items: flex-end;
}

.justifyStart {
  justify-content: flex-start;
}

.justifyEnd {
  justify-content: flex-end;
}

.justifyCenter {
  justify-content: center;
}

.justifyAround {
  justify-content: space-around;
}

.justifyBetween {
  justify-content: space-between;
}

.alignStart {
  align-self: flex-start;
}

.alignEnd {
  align-self: flex-end;
}

.alignCenter {
  align-self: flex-center;
}

.centerAll {
  align-items: center;
  justify-content: center;
}
