.InteractionConcept {
  position: absolute;
  bottom: 0;
  width: 723px;
  height: 168px;
  left: 305px;
  overflow: hidden;
}

.InteractionConcept--rhd {
  right: 305px;
  left: unset;
}

.hudTabbar {
  display: flex;
  height: 100%;
  align-items: flex-end;
  justify-content: flex-end;
}

.hudTab {
  height: 100%;
  width: 523px;
  margin: 0 6px;
}
.hudTab.hudTabStatic {
  margin: 0 2px;
}

.hudTab
  .T_0103_ListVertical
  .listWrapper
  .cursorContainer
  > div
  div:first-child {
  box-shadow: unset;
}

.hudActiveBtn.inactiveBtn {
  width: 94px;
  opacity: 0.6;
}

.hudActiveBtn.inactiveBtn.twoTabsInactiveButton {
  opacity: 0;
}

.hudActiveBtn.inactiveBtn .state.white {
  border-bottom: 2px solid rgba(var(--audiWhite), 0.3);
}

.hudActiveBtn.inactiveBtnLeft {
  left: 0;
  transition:
    left 0.3s,
    width 0.3s;
}

.hudActiveBtn.inactiveBtnRight {
  left: 628px;
}

.hudActiveBtn.moveOut {
  transform: translateX(-95px);
  transition:
    transform 0.3s,
    left 0.1s 0.5s,
    visibility 0.2s 0.3s;
}

.hudActiveBtn.moveOut p {
  opacity: 0;
}

.hudActiveBtn.showUp {
  transform: unset;
  left: unset;
  right: 100px;
  transition-timing-function: ease-out;
  transition:
    transform 0.1s,
    right 0.1s,
    visibility 0.2s,
    width 0.3s,
    opacity 0.4s;
}

.hudActiveBtn.showUp p {
  visibility: visible;
  opacity: 1;
  transition:
    opacity 0.3s 0.2s,
    visibility 0.3s 0.2s;
}

.hudActiveBtn.inactiveBtn p {
  visibility: hidden;
  position: absolute;
}

.hudActiveBtn.moveOutRight {
  left: 505px;
  transform: translateX(195px);
  visibility: hidden;
  opacity: 0.2;
  transition:
    left 0.4s,
    transform 0.01s 0.4s,
    visibility 0.03s,
    opacity 0.1s;
}

.hudActiveBtn.moveInRight {
  transform: unset;
  left: 628px;
  visibility: visible;
  transition:
    left 0.01s,
    transform 0.2s,
    visibility 0.01s;
}

/* .hudActiveBtn p {
    transition: visibility 0.3s;
  } */

.inactiveTab,
.inactiveTab .hudActiveBtn {
  visibility: hidden;
  position: absolute;
  right: 0;
  opacity: 0;
}

.inactiveTab .hudActiveBtn {
  opacity: 0;
}

.hudInactiveBtn {
  display: flex;
  height: 70px;
  width: 94px;
  justify-content: center;
  align-items: center;
  margin-bottom: 5px;
  opacity: 0.6;
}

.hudInactiveBtn.rightBtn {
  position: absolute;
  visibility: hidden;
  right: -94px;
}

.hudTab.hide,
.hudInactiveBtn .hide,
.hudInactiveBtn.hide {
  visibility: hidden;
  position: absolute;
}

.InteractionConcept.enableContent .hudInactiveBtn,
.InteractionConcept.enableContent .hudInactiveBtn .listIcon,
.InteractionConcept.enableContent .hudActiveBtn,
.hideHudInactiveButton {
  visibility: hidden;
}

.hudActiveBtn {
  position: absolute;
  bottom: 0px;
  width: 523px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s;
  opacity: 1;
}

.hudActiveBtn.hudHiddenBtn {
  visibility: hidden;
  position: absolute;
}

.hudActiveBtn .uiState,
.hudInactiveBtn .uiState {
  width: 100%;
  opacity: 1;
  height: 100%;
}

.hudActiveBtn .fading {
  opacity: 1;
}

.hudInactiveBtn .gradient,
.hudInactiveBtn .fading {
  opacity: 1;
}

.hudActiveBtn .borderBottom .white {
  border-bottom: 2px solid white;
}

.hudInactiveBtn .state.white,
.hudInactiveBtn1 .state.white {
  border-bottom: 2px solid rgba(var(--audiWhite), 0.3);
}

.icList {
  height: 100%;
}

/*-----------------------------------------------------*/

.hudActiveList {
  position: absolute;
  width: 523px;
  height: 223px;
  opacity: 1;
  transition: opacity 3s;
}

.hudActiveList.moveOut {
  width: 523px;
  opacity: 0;
  transform: translateX(-250px);
  transition:
    transform 0.3s,
    visibility 0.2s 0.3s;
}
.hudActiveList.showUp {
  transform: translateX(0px);
  left: unset;
  right: 100px;
  transition-timing-function: ease-out;
  transition:
    transform 1s,
    right 1s,
    visibility 2s,
    width 3s,
    opacity 0.4s;
}

/*--------------------------*/

.initialZero,
.initialOne,
.initialTwo,
.initialThree {
  height: 100%;

  transform: translateX(270px);
  opacity: 0;
}
.initialTwoList {
  width: calc(100% - 200px);
  height: 100%;
  opacity: 0;
}

.initialZero,
.initialOne {
  transform: translateX(250px);
}

.initialZero.animateInZero,
.initialOne.animateInOne,
.initialTwo.animateInTwo,
.initialThree.animateInThree {
  opacity: 1;
  transform: translateX(-8px);
  transition:
    opacity 0.15s 0.125s,
    transform 0.15s 0.125s;
  width: 523px;
}
.initialZero.animateOutZero,
.initialOne.animateOutOne,
.initialTwo.animateOutTwo,
.initialTwoList.animateOutTwo,
.initialThree.animateOutThree {
  position: absolute;
  opacity: 0;
  transform: translateX(-200px);
  transition:
    opacity 0.3s,
    transform 0.3s;
  width: 523px;
}

