.visible {
  visibility: visible;
  position: absolute;
}

.icList,
.inactiveViewer {
  height: 100%;
  z-index: -1;
  opacity: 0.6;
  mask-image: linear-gradient(black 0%, transparent 75%, transparent 100%);
  -webkit-mask-image: linear-gradient(
    black 0%,
    transparent 75%,
    transparent 100%
  );
}

.InteractionConcept.enableContent .icList {
  opacity: unset;
  mask-image: unset;
  -webkit-mask-image: unset;
  z-index: unset;
  pointer-events: unset;
}

.icList
  .UE_1002_ListItemStandard.singleLine.fpk
  .listElement
  .textContainer
  .textColumn
  .textPrio2,
.phoneList
  .UE_1002_ListItemStandard.singleLine.fpk
  .listElement
  .textContainer
  .textColumn
  .textPrio2 {
  display: block;
}

.infoLine {
  width: 94%;
  height: var(--hudListItemHeight);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--listSeparatorLineColor);
  justify-content: space-between;
  padding: 0 12px 0 16px;
  box-sizing: border-box;
  margin: 0 auto;
}

.infoLine:last-of-type {
  margin-bottom: 30px;
}

.infoLine p:first-of-type {
  /* margin-left: 30px; */
}

.infoLine p:last-of-type {
  /* margin-right: 15px; */
}

.noPhoneText,
.inactiveNaviText {
  height: calc(100% - var(--hudListItemHeight) - 12px);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.noPhoneText p,
.inactiveNaviText p {
  white-space: unset;
  line-height: 40px;
  text-align: center;
  max-height: 112px;
  max-width: 468px;
}

.hudImgViewer {
  height: calc(100% - 5px);
  overflow: scroll;
  overflow-x: hidden;
  padding-right: 2px;
}

.viewList {
  width: 100%;
  height: fit-content;
}

.hudImg {
  width: 100%;
  height: 223px;
  background-position: center;
  background-repeat: no-repeat;
}

.placeholderImg1 {
  background-image: url('../../assets/Tiltangle.png');
}

.placeholderImg2 {
  background-image: url('../../assets/Game\ 3D\ Pong.png');
}

.placeholderImg3 {
  background-image: url('../../assets/Game\ Tunnel\ Flight.png');
}

.placeholderImg4 {
  background-image: url('../../assets/Nav+FAS.png');
}
