/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --recommendationMinHeight: 86px;
  --recommendationMaxHeight: 108px;
  --assistantHeight: 190px;
  --assistantWidth: 312px;
  --recommendationMargin: calc(
    (var(--recommendationMinHeight) - var(--listIconSize)) / 2
  );
}

/* Standalone */

:global(#root) > .SE_0048_AI_Screen {
  position: absolute;
  width: 1920px;
  height: 698px;
  margin: 0;
}

/* SE_0048_AI_Screen */

.SE_0048_AI_Screen {
}

:global(#root) > .SE_0048_AI_Screen .bodyBackground {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.AIScreen {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

