/*************************************************************************
 -------------------------- Recommendations ------------------------------
**************************************************************************/
.AIScreen .recommendation {
  position: absolute;
  width: fit-content;
  height: var(--recommendationMinHeight);
  display: flex;
  justify-content: flex-start;
}

.AIScreen .recommendation .background {
  height: 100%;
  position: absolute;
  width: 100%;
  background-color: rgba(var(--audiBlack), 0.75);
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.15) 0%,
    rgba(var(--interactionBackground), 0.15) 100%
  );
  border-radius: var(--standardBorderRadius);
}

.AIScreen .recommendation .background .lineHighlite {
  position: absolute;
  width: calc(100% - 4px);
  height: 1px;
  background-color: rgba(var(--ambientcolor1), 0.8);
  bottom: 5px;
  left: 2px;
}
.AIScreen .recommendation .background .bluredHighlite {
  position: absolute;
  width: calc(100%);
  height: 18px;
  background-color: rgba(var(--ambientcolor1), 0.1);
  bottom: 0px;
  left: 0px;
  filter: blur(20px);
}

.twoTextRows .AIScreen .recommendation {
  height: var(--recommendationMaxHeight);
}

.AIScreen .recommendationContainer .animated .recommendation .text,
.AIScreen .recommendationContainer .animated .recommendation .background {
  visibility: hidden;
}

.AIScreen .recommendation .listIconContainer {
  transform: scale(0);
  position: absolute;
  width: var(--recommendationMinHeight);
  height: var(--recommendationMinHeight);
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
}

.AIScreen .recommendation .text {
  position: relative;
  margin: auto var(--recommendationMargin) auto var(--recommendationMinHeight);
  overflow: hidden;
}

.AIScreen .center .recommendation .text {
  margin-left: var(--recommendationMargin);
}

.AIScreen .recommendation .text p {
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.AIScreen .center .recommendation .text p {
  text-align: center;
}

