/*---------- animation ----------*/

.AIScreen .patternContainer .animated.hide .pattern,
.AIScreen .patternHighlightContainer .animated.hide .pattern {
  animation-name: animationFadeOut;
  animation-duration: calc(1000ms / 60 * 10);
  animation-iteration-count: 1;
  animation-fill-mode: backwards;
  animation-timing-function: linear;
}

.AIScreen .patternContainer .animated.hide .pattern:last-child {
  animation-duration: calc(1000ms / 60 * 20);
}

.AIScreen .patternContainer .animated.show .pattern,
.AIScreen .patternHighlightContainer .animated.show .pattern {
  animation-name: animationFadeIn;
  animation-duration: calc(1000ms / 60 * 10);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
}

.AIScreen .patternContainer .animated.show .pattern:last-child {
  animation-duration: calc(1000ms / 60 * 20);
}

.AIScreen .patternHighlightContainer .animated.hide .pattern {
  animation-name: animationPatternHighlightFadeOut;
  animation-duration: calc(1000ms / 60 * 20);
}

.AIScreen .patternHighlightContainer .animated.show .pattern {
  animation-name: animationPatternHighlightFadeIn;
  animation-duration: calc(1000ms / 60 * 30);
}

/* FadeOut Delay */
.AIScreen .patternHighlightContainer .animated.hide .pattern {
  animation-delay: calc(1000ms / 60 * 32);
}

.AIScreen .patternContainer .animated:nth-child(4).hide .pattern:nth-child(1) {
  animation-delay: calc(1000ms / 60 * 32 + 500ms / 6 * 1);
}

.AIScreen .patternContainer .animated:nth-child(3).hide .pattern:nth-child(1),
.AIScreen .patternContainer .animated:nth-child(5).hide .pattern:nth-child(1) {
  animation-delay: calc(1000ms / 60 * 32 + 500ms / 6 * 2);
}

.AIScreen .patternContainer .animated:nth-child(1).hide .pattern:nth-child(1),
.AIScreen .patternContainer .animated:nth-child(2).hide .pattern:nth-child(1) {
  animation-delay: calc(1000ms / 60 * 32 + 500ms / 6 * 3);
}

/* FadeIn Delay*/

.AIScreen .patternContainer .animated.show .pattern:nth-child(1) {
  animation-delay: 0ms;
}

.AIScreen .patternHighlightContainer .animated:nth-child(5).show .pattern,
.AIScreen .patternHighlightContainer .animated:nth-child(4).show .pattern,
.AIScreen .patternHighlightContainer .animated:nth-child(3).show .pattern,
.AIScreen .patternHighlightContainer .animated:nth-child(1).show .pattern,
.AIScreen .patternHighlightContainer .animated:nth-child(2).show .pattern {
  animation-delay: 50ms;
}

/* recommendationContainer */

.AIScreen .animated .recommendationContainer .recommendation,
.AIScreen .animated .patternContainer,
.AIScreen .animated .patternHighlightContainer {
  visibility: hidden;
}

.AIScreen .animated.hide,
.AIScreen .animated.show {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.AIScreen .animated.show .patternContainer,
.AIScreen .animated.show .patternHighlightContainer,
.AIScreen .animated.hide .patternContainer,
.AIScreen .animated.hide .patternHighlightContainer {
  visibility: visible;
}

.AIScreen .recommendationContainer .animated.show .recommendation .background {
  animation-name: recommendationBackgroundFadeIn;
  animation-duration: calc(1000ms / 60 * 22);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

.AIScreen .recommendationContainer .animated.hide .recommendation .background {
  visibility: visible;
  animation-name: recommendationBackgroundFadeOut;
  animation-duration: calc(1000ms / 60 * 22);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms / 60 * 10);
}

.AIScreen
  .recommendationContainer
  .animated.show
  .recommendation
  .listIconContainer {
  transform: scale(0);
  animation-name: iconBackgroundFadeIn;
  animation-duration: calc(1000ms / 60 * 30);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms / 60 * 42);
}

.AIScreen .recommendationContainer .animated.show .recommendation .listIcon {
  animation-name: iconFadeIn;
  animation-duration: calc(1000ms / 60 * 30);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms / 60 * 42);
}

.AIScreen
  .recommendationContainer
  .animated.hide
  .recommendation
  .listIconContainer {
  transform: scale(1);
  animation-name: iconBackgroundFadeOut;
  animation-duration: calc(1000ms / 60 * 30);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms / 60 * 20);
}

.AIScreen .recommendationContainer .animated.hide .recommendation .listIcon {
  animation-name: iconFadeOut;
  animation-duration: calc(1000ms / 60 * 30);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms / 60 * 20);
}

.AIScreen .recommendationContainer .animated.show .recommendation .text {
  visibility: hidden;
  animation-name: recommendationTextFadeIn;
  animation-duration: calc(1000ms / 60 * 10);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-delay: calc(1000ms + 1000ms / 60 * 22);
}

.AIScreen .recommendationContainer .animated.hide .recommendation .text {
  visibility: visible;
  animation-name: recommendationTextFadeOut;
  animation-duration: calc(1000ms / 60 * 10);
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes animationFadeIn {
  0% {
    opacity: 0;
    height: 0%;
    width: 0%;
  }
  100% {
    opacity: 1;
    height: 100%;
    width: 100%;
  }
}

@keyframes animationFadeOut {
  0% {
    opacity: 1;
    height: 100%;
    width: 100%;
  }
  100% {
    opacity: 0;
    height: 0%;
    width: 0%;
  }
}

@keyframes animationPatternHighlightFadeIn {
  0% {
    opacity: 0;
    height: 0%;
    width: 0%;
  }
  33% {
    opacity: 0.1;
    height: 50%;
    width: 50%;
  }
  100% {
    opacity: 1;
    height: 100%;
    width: 100%;
  }
}

@keyframes animationPatternHighlightFadeOut {
  0% {
    opacity: 1;
    height: 100%;
    width: 100%;
  }
  100% {
    opacity: 0;
    height: 0%;
    width: 0%;
  }
}

@keyframes iconBackgroundFadeIn {
  0% {
    transform: scale(0);
  }
  60% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes iconFadeIn {
  0% {
    transform: scale(0);
  }
  60%,
  100% {
    transform: scale(1);
  }
}

@keyframes iconBackgroundFadeOut {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes iconFadeOut {
  40% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes recommendationFadeIn {
  from {
    visibility: visible;
    width: 0;
  }
  to {
    visibility: visible;
    width: fit-content;
  }
}

@keyframes recommendationFadeOut {
  from {
    visibility: visible;
    width: fit-content;
  }
  to {
    visibility: hidden;
    width: 0;
  }
}

@keyframes recommendationBackgroundFadeIn {
  from {
    visibility: visible;
    width: 0;
  }
  to {
    visibility: visible;
    width: 100%;
  }
}

@keyframes recommendationBackgroundFadeOut {
  from {
    visibility: visible;
    width: 100%;
  }
  to {
    visibility: hidden;
    width: 0;
  }
}

@keyframes recommendationTextFadeIn {
  from {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}

@keyframes recommendationTextFadeOut {
  from {
    visibility: visible;
    opacity: 1;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}

