/* CLASSIFICATION: CONFIDENTIAL */

:root {
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_0504_HWR {
  background-color: rgb(var(--bodyColorGrey));
  width: 1800px;
  height: 470px;
  margin: 0;
}

/*-------------------- .UE_0504_HWR --------------------*/

.UE_0504_HWR {
  position: absolute;
  width: 100%;
  height: 100%;
}

.touchPad {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}

.handWriting {
  position: absolute;
  margin: 10px 12px;
  height: calc(100% - 20px);
  width: calc(100% - 24px);
}

.background {
  height: 100%;
  width: 100%;
  background-image: radial-gradient(
    circle at 2px 2px,
    rgb(var(--audiGrey)) 1px,
    transparent 0
  );
  background-size: 20px 20px;
  margin-left: 8px;
}

.handTrace {
  position: absolute;
  width: 287px;
  height: 192px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url('../img/UE_0504_HWR_Handtrace.png');
}
.handTrace.ar {
  width: 255px;
  height: 204px;
  background-image: url('../img/UE_0504_HWR_HandtraceAR.png');
}

.buttonsWrapper {
  position: absolute;
  height: var(--keyboardLineHeight);
  width: calc(100% - 418px);
  max-height: var(--keyboardLineHeight);
  flex: 1;
  display: flex;
  bottom: 0;
  background-color: rgb(var(--audiBlack));
  border-top: 1px solid rgb(var(--audiGrey));
  border-left: 1px solid rgb(var(--audiGrey));
  border-right: 1px solid rgb(var(--audiGrey));
}

.buttonsWrapper .button:first-of-type,
.buttonsWrapper .button:last-of-type {
  flex: 2;
}

.buttonsWrapper .button:nth-of-type(2) {
  flex: 4;
}

.buttonsWrapper .button:first-child:before,
.buttonsWrapper .button:last-child:before,
.touchPad .buttonsWrapper .button:first-child:before {
  background: none;
}

.buttonsWrapper .button {
  position: relative;
  min-width: var(--keyWidth);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  flex: 1;
}

.buttonsWrapper .button:before,
.buttonsWrapper .button:before {
  content: '';
  position: absolute;
  width: 1px;
  height: var(--keyboardSeparatorHeight);
  background: rgb(var(--audiGrey));
  left: 0;
}

.buttonsWrapper .button.noBorder:before,
.buttonsWrapper .button.noBorder:before {
  background: none;
}
