:root {
  --diagramMarginBottom: calc(
    (
        var(--lineHeightPrio1) - var(--characterHeightPrio1) -
          (var(--lineHeightPrio2) - var(--characterHeightPrio2))
      ) / 2
  );
  --diagramElementstatusInformationWidth: calc(
    495px + var(--listIconSize) + 2 * var(--iA1)
  );
  --diagramElementTypeDistance: 16px;
  --diagramElementWidthDiff: 31px;
}

:global(#root) > .SE_0063_DiagramElement {
  margin: 0px;
  background-color: rgb(var(--bodyColorGrey));
  position: absolute;
  width: 100%;
  height: 100%;
}

.hide {
  visibility: hidden;
}

.diagramElement {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: var(--iA1) 0 var(--iA1) var(--listVerticalPadding);
  box-sizing: border-box;
}

/* ---------------------------- CONTENT CONTAINER --------------------------- */
.statusInformationContainer {
  height: 100%;
  width: var(--diagramElementstatusInformationWidth);
  margin: 0 var(--iA1);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.dateLine {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: var(--minTouchSize);
  justify-content: unset;
}

.listIcon {
  margin-left: var(--iA1);
}

.dateLine p {
  margin-left: var(--iA1);
}

.divider {
  height: 1px;
  background-color: rgba(var(--audiWhite), 0.2);
  width: 100%;
}

.textContainer {
  flex: 1;
  justify-content: center;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: calc(var(--iA1) - var(--diagramMarginBottom)) var(--iA1);
  box-sizing: border-box;
}

.textLine {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  overflow: hidden;
}

.textLine:not(:first-child) {
  margin-top: var(--iA1);
}

.textLine .textPrio1 {
  margin-left: var(--iA1);
}
/* ---------------------------- DIAGRAM CONTAINER --------------------------- */

.diagramContainer {
  display: flex;
  position: relative;
  height: 100%;
  margin-left: var(--iA1);
  flex-direction: row;
  flex: 1;
  width: calc(
    100% - var(--diagramElementstatusInformationWidth) - 3 * var(--iA1)
  );
}

.typeContainer {
  position: relative;
  flex: 1;
  max-width: calc(
    (
        100% -
          (
            var(--diagramElementTypeDistance) * 3 +
              var(--diagramElementTypeDistance) / 2
          )
      ) / 4
  );
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: var(--iA1) var(--diagramElementTypeDistance) 0px 0px;
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer:last-child {
  margin-right: calc(var(--diagramElementTypeDistance) / 2);
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .textPrio2 {
  position: relative;
  max-width: calc(100% - 2 * var(--zA1));
  margin: var(--iA1) var(--zA1) var(--iA1) var(--zA1);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .textPrio2:last-child,
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .firstTextLine,
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .secondTextLine {
  margin-bottom: 0;
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .firstTextLine,
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .secondTextLine {
  position: absolute;
  bottom: 0;
}

.SE_0063_DiagramElement
  .diagramElement.twoTextLines
  .diagramContainer
  .typeContainer
  .firstTextLine {
  position: absolute;
  bottom: 51px;
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .typeContainer
  .textPrio3 {
  margin-top: var(--zA1);
  max-width: calc(100% - 2 * var(--zA1));
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: none;
}
.SE_0063_DiagramElement
  .diagramElement.twoYears
  .diagramContainer
  .typeContainer
  .textPrio3 {
  display: block;
}

.SE_0063_DiagramElement .diagramElement .diagramContainer .dataContainer {
  position: relative;
  flex: 1;
  display: flex;
  width: 100%;
  margin-bottom: 51px;
  justify-content: center;
}

.SE_0063_DiagramElement
  .diagramElement.twoTextLines
  .diagramContainer
  .dataContainer {
  margin-bottom: 102px;
}

.SE_0063_DiagramElement .diagramElement .diagramContainer .bargraphContainer {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  overflow: hidden;
  width: calc(2 * var(--minTouchSize));
}
.SE_0063_DiagramElement
  .diagramElement.twoYears
  .diagramContainer
  .bargraphContainer {
  width: var(--minTouchSize);
}
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .bargraphContainer.lastYear {
  display: none;
}
.SE_0063_DiagramElement
  .diagramElement.twoYears
  .diagramContainer
  .bargraphContainer.lastYear {
  display: flex;
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .bargraphContainer
  .bargraph {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  background-color: rgb(var(--audiBlack));
}
.SE_0063_DiagramElement
  .diagramElement.twoYears
  .diagramContainer
  .bargraphContainer
  .bargraph {
  width: calc(100% - var(--diagramElementWidthDiff));
}
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .bargraphContainer.lastYear
  .bargraph {
  border: 1px solid rgba(var(--ambientcolor1), 0.25);
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .bargraphContainer
  .bargraph
  .background {
  width: 100%;
  height: 100%;
  border-radius: var(--standardBorderRadius);
  background-color: rgba(var(--ambientcolor1), 0.45);
}
.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .bargraphContainer.lastYear
  .bargraph
  .background {
  background-color: rgba(var(--ambientcolor1), 0.25);
}

.SE_0063_DiagramElement
  .diagramElement
  .diagramContainer
  .dataContainer
  .bargraphContainer.selected
  .bargraph
  .background {
  background-color: rgba(var(--audiWhite), 0.8);
}

.bargraphHundred {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--buttonBackgroundColor);
  border-radius: var(--standardBorderRadius);
}

.diagramElement.twoYears .bargraphHundred {
  width: calc(100% - var(--diagramElementWidthDiff));
  bottom: calc(var(--lineHeightPrio3) + var(--zA1));
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  .textContainer {
    padding-top: 5px;
  }

  .textLine:not(:first-child) {
    margin-top: var(--zA2);
  }

  .textLine .textPrio1 {
    font-size: var(--fontSizePrio2);
    line-height: var(--lineHeightPrio2);
  }

  .diagramContainer {
    margin: 0 var(--cA1);
    flex: 1;
  }
}

.settings {
  width: 100%;
  position: absolute;
  bottom: -60px;
}
