/* CLASSIFICATION: CONFIDENTIAL */

/* width */
::-webkit-scrollbar {
  height: 6px;
  width: 2px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border: 1px solid transparent;
  background-clip: content-box;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(var(--audiWhite));
  border: 1px solid rgb(var(--audiWhite));
}

.scrollbarWithTrack .horizontalList::-webkit-scrollbar-track,
.scrollbarWithTrack .list::-webkit-scrollbar-track,
.scrollbarWithTrack::-webkit-scrollbar-track {
  border: 1px solid rgba(var(--audiWhite), 0.15);
  background-clip: content-box;
  margin-top: 4px;
  margin-bottom: 4px;
}
