/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --ospLegendHeight: 486px;
  --ospLegendWidth: 58px;
  --ospLegendItemHeight: 94px;
}

:global(#root) > .SE_0034_OnstreetLegend {
  background-color: rgb(var(--audiGrey));
  padding: 20px;
}

.SE_0034_OnstreetLegend {
}

.OSPlegendBox {
  position: relative;
  height: var(--ospLegendHeight);
  width: var(--ospLegendWidth);
  display: flex;
  flex-direction: column;
  background-color: rgba(var(--audiBlack), 0.85);
}

.ospItem {
  position: relative;
  border-right: rgb(var(--ospGreen)) solid 4px;
  height: var(--ospLegendItemHeight);
  margin-right: 12px;
}

.ospItem.orange {
  border-color: rgb(var(--ospOrange));
}

.ospItem.red {
  border-color: rgb(var(--ospRed));
}

.ospItem + .ospItem {
  margin-top: 30px;
}

.ospItem:first-of-type {
  margin-top: 11px;
}

.ospItem .opsSpace {
  background-repeat: no-repeat;
  background-position: 0px 2px;
  background-size: 18px 39px;
  width: 18px;
  height: 39px;
  margin-left: 16px;
  padding: 2px;
  padding-left: unset;
  border: 1px solid rgba(var(--parkingSpaceBlue), 1);
  border-left: unset;
}

.ospItem .opsSpace.blocked {
  background-image: linear-gradient(
    to right,
    rgba(var(--audiWhite), 0.2),
    rgba(var(--audiWhite), 0.2)
  );
  border: 1px solid rgba(var(--audiWhite), 0.6);
  border-left: unset;
}

.ospItem .opsSpace + .opsSpace {
  margin-top: 4px;
}

.ospItem .opsSpace p {
  color: rgb(var(--audiWhite));
  left: calc(50% + 3px);
  top: 50%;
  position: relative;
  transform: translate(-50%, -50%);
  font-family: var(--fontNormal);
}

.ospItem .opsSpace.blocked p {
  display: none;
}

/* --------------------------------- unknown -------------------------------- */

.ospItem.unknown {
  border-color: rgb(var(--FIDtelltaleGrey));
}

.ospItem.unknown .opsSpace {
  display: none;
}
