:global(#root) > .SE_0093_01_Clock_DashboardTile {
  background: rgb(var(--bodyColorGrey));
  position: absolute;
  width: 550px;
  height: calc(956px - var(--statusBarHeight) - var(--climateBarHeight));
  margin: 0;
}

.clock_DashboardTile {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: var(--zA4) 0 var(--zA4) 0;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.clock_DashboardTile.variant4 {
  justify-content: center;
}

.variant4_Container {
  position: relative;
  margin-top: 105px;
}

.variant4_timeContainer {
  display: flex;
  align-items: center;
}

.clock_DashboardTile.reduced {
  padding: 0 0 5px 0;
}

.clockImage {
  display: flex;
  position: relative;
  flex: 1;
  width: 100%;
  max-height: 511px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  align-items: center;
  justify-content: center;
}
.clock_DashboardTile.variant1 .clockImage {
  background-image: url(../img/V1.png);
}
.clock_DashboardTile.variant2 .clockImage {
  background-image: url(../img/V2.png);
}
.clock_DashboardTile.variant3 .clockImage {
  background-image: url(../img/V3.png);
}

.clockColor {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-size: contain;
}

.clock_DashboardTile.variant1 .clockImage .clockColor {
  -webkit-mask-image: url(../img/V1_color.png);
}
.clock_DashboardTile.variant2 .clockImage .clockColor {
  -webkit-mask-image: url(../img/V2_color.png);
}
.clock_DashboardTile.variant3 .clockImage .clockColor {
  -webkit-mask-image: url(../img/V3_color.png);
}

.clock_DashboardTile.variant4 .clockColor {
  display: none;
}

.innerTimeContainer {
  display: flex;
  position: relative;
}

.hours,
.minutes {
  display: flex;
  position: absolute;
}
.hours {
  right: 18px;
}
.minutes {
  left: 18px;
}

.placeholder {
  display: flex;
  position: relative;
  height: var(--lineHeightPrioA);
  margin-top: var(--zA6);
}

.reduced .placeholder {
  height: var(--lineHeightPrio1);
  margin-top: 0;
}

.timeContainer {
  display: flex;
  position: relative;
  margin-top: var(--zA6);
}

.variant4_timeContainer .textPrioBIG {
  margin-bottom: 27px;
  margin-right: 10px;
}

.variant4_minutes {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 108px;
  position: relative;
  -webkit-mask-image: linear-gradient(
    0deg,
    rgba(var(--audiBlack), 0) 0%,
    rgba(var(--audiBlack), 1) 35%,
    rgba(var(--audiBlack), 1) 65%,
    rgba(var(--audiBlack), 0) 100%
  );
}

.variant4_minutes .textPrio1 {
  margin: 21px 0;
}

.variant4_minutes .before,
.variant4_minutes .after {
  position: absolute;
  color: rgba(var(--audiWhite), 0.3);
}

.variant4_minutes .before {
  top: -6px;
}

.variant4_minutes .after {
  bottom: -6px;
}

.reduced .timeContainer {
  margin-top: 0;
}

.ampmContainer {
  display: flex;
  position: absolute;
  left: 92px;
}

.variant4_ampmContainer {
  position: absolute;
  left: 15px;
  top: -24px;
}

.reduced .ampmContainer {
  left: 68px;
}

.ampmContainer .am {
  margin-top: 10px;
}

.ampmContainer .pm {
  margin-top: 37px;
  margin-left: 1px;
}

.dateContainer {
  display: flex;
  position: relative;
  margin-top: var(--zA1);
}

.variant4 .dateContainer {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
}

.variant4 .textPrio1 + .textPrio2 {
  margin-top: unset;
}

.dateContainer .textPrio2 {
  margin-top: 0;
}

.variant4 .dateContainer .line {
  height: 1px;
  width: 30px;
  background-color: rgb(var(--audiWhite));
  margin: 12px 0;
}

.settings {
  position: absolute;
  background-color: rgb(var(--audiBlack));
  height: 20%;
  width: 200%;
  top: 825px;
  overflow: auto;
}
