/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --brightnessSliderWidth: 700px;
}

:global(#root) > .SE_0098_WHUD_Preview {
  width: 1800px;
  height: 697px;
}

.SE_0098_WHUD_Preview {
  width: 100%;
  height: 100%;
  display: flex;
}

.SE_0098_WHUD_Preview.icl {
  background-color: rgb(var(--audiBlack));
}

.left,
.right {
  display: flex;
  width: 50%;
  position: relative;
}

.T_0103_ListVertical {
  width: 100%;
  height: 100%;
}

.previewImageBox {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
  background-image: url('../img/PPC_wHUD_dummy_preview.png');
  width: 700px;
  height: 300px;
  mix-blend-mode: exclusion;
}

.SE_0098_WHUD_Preview.brightnessSlider .previewImageBox {
  top: 100px;
  transform: translateY(0) translateX(-50%);
}
.SE_0098_WHUD_Preview.brightnessSlider.oneInfotainment .previewImageBox {
  top: 180px;
  transform: translateY(0) translateX(-50%);
}

.verstellecken {
  position: absolute;
  background-image: url('../img/SE_0098_wHUD_Preview_Verstellecken.png');
  width: 100%;
  height: 100%;
}

/*------------------ SLIDER ------------------*/
.SE_0098_WHUD_Preview .UE_0601_Slider {
  position: relative;
  width: var(--brightnessSliderWidth);
  height: var(--minTouchSize);
}

.oneInfotainment .previewImageBox {
  background-image: url('../img/SE_0098_wHUD_Previews_dummy_OneInfo.png');
  width: 480px;
  height: 140px;
}
.oneInfotainment .verstellecken {
  background-image: url('../img/SE_0098_wHUD_Preview_Verstellecken_oneInfotainmen.png');
}

.icl .previewImageBox {
  background-image: url('../img/ICL_preview_dummy.png');
  width: 900px;
  height: 434px;
  top: 144px !important;
  transform: translateY(0) translateX(-50%);
}
.icl .verstellecken {
  display: none;
}

@media only screen and (max-width: 1920px) {
  .icl .previewImageBox {
    top: 100px !important;
  }
}

.settings {
  position: absolute;
  top: 700px;
  width: 80%;
  left: var(--hA1);
  color: white;
}
