/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --listItemHeight: 100px;
  --listItemHeightDataPicker: 119px;
  --fadeOutSize: 55px;
  --activeListItemMargin: 0px; /*0 if active list item shall have the same height as other list items*/
  --numListItems: 3; /* note: odd number expected */
  --dataPickerHeight: calc(
    (var(--numListItems)) * var(--listItemHeightDataPicker) +
      var(--activeListItemMargin) * 2 + var(--fadeOutSize) * 2
  );
  --dataPickerWidthTime: 175px;
  background-color: rgb(var(--ambientBackground1));
}

:global(#root) > .bodyWrapper > .UE_1301_DataPicker {
  position: absolute;
  width: 284px;
  height: var(--dataPickerHeight);
}

.UE_1301_DataPicker {
  position: relative;
  height: fit-content;
  width: fit-content;
}

.UE_1301_DataPicker div::-webkit-scrollbar {
  display: none;
}

.bodyWrapper {
  background-color: rgb(var(--ambientBackground1));
  width: 284px;
  position: absolute;
  height: 100%;
}

.dataPicker {
  position: relative;
  width: 100%;
  height: 100%;
}

.dataPickerParkingStepper {
  position: relative;
  width: fit-content;
  max-width: calc(2 * var(--minTouchSize));
  min-width: var(--minTouchSize);
  height: calc(3 * var(--listItemHeightDataPicker));
  background-color: rgba(var(--audiBlack), 0.5);
}

.dataPicker .list {
  overflow-y: scroll;
  position: absolute;
  width: calc(100% - 10px);
  height: calc(
    (var(--numListItems)) * var(--listItemHeightDataPicker) +
      var(--activeListItemMargin) * 2
  );
  scroll-snap-type: y mandatory;
  z-index: 2;
  margin-top: var(--fadeOutSize);
  margin-left: 5px;
}

.dataPicker.dataPickerParkingStepper .list {
  position: unset;
  width: fit-content;
  min-width: var(--minTouchSize);
  max-width: calc(var(--minTouchSize) * 2 - 5px);
  margin-top: 0px;
}

.iconDataPicker .dataPicker .list,
.unit .dataPicker .list {
  width: calc(100% - 94px);
}

.UE_1301_DataPicker.iconDataPicker .dataPicker .list {
  right: 0;
  margin-right: 5px;
}

.UE_1301_DataPicker.unit .dataPicker .list,
.UE_1301_DataPicker.iconDataPicker .dataPicker .list,
.UE_1301_DataPicker.iconDataPicker.unit .dataPicker .list {
  width: calc(100% - (90px * 2));
  left: 50%;
  transform: translateX(-50%);
  margin-right: 0;
  margin-left: 0;
}

.dataPicker .list .listelement.activated {
  opacity: 1;
}

.dataPicker .list .listelement {
  color: rgb(var(--audiWhite));
  height: var(--listItemHeightDataPicker);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.dataPicker.dataPickerParkingStepper .list .listelement {
  margin-left: 0px;
  position: relative;
}

.dataPicker.dataPickerParkingStepper .list .listelement p {
  max-width: 100%;
  text-align: center;
}

.dataPicker.dataPickerParkingStepper .list .listelement .textIcon {
  margin: 0 var(--zA4);
}

.destinationFlagFirst {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E762_specialflag_20x20.svg');
  width: var(--listIconSize);
  height: var(--listIconSize);
  position: absolute;
  right: 0px;
  bottom: 50%;
  transform: translateY(calc(var(--listIconSize) / 4));
}

.flagsContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  bottom: 10px;
}

.destinationFlagSecondTop {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E762_specialflag_20x20.svg');
  width: var(--listIconSize);
  height: var(--listIconSize);
  position: absolute;
  right: 0px;
  bottom: calc(2 * var(--listIconSize));
}

.destinationFlagSecondCentered {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E762_specialflag_20x20.svg');
  width: var(--listIconSize);
  height: var(--listIconSize);
  position: absolute;
  right: 0px;
  bottom: 50%;
  transform: translateY(calc(var(--listIconSize) / 4));
}

.dataPicker .list .listelement.active {
  opacity: 1;
  z-index: 1;
  margin: var(--activeListItemMargin);
  scroll-margin: var(--activeListItemMargin);
  scroll-snap-align: center;
}

.dataPicker .list .listelement.padDummy {
  height: calc(
    (var(--numListItems) / 2 - 0.5) * var(--listItemHeightDataPicker)
  );
}

.dataPicker .headline {
  position: absolute;
  margin: 5px var(--cA1);
  height: var(--lineHeightPrio2);
  width: calc(100% - 2 * var(--cA1));
  visibility: hidden;
}

.UE_1301_DataPicker.headline .dataPicker .headline,
.UE_1301_DataPicker.unit .dataPicker .selectionFrame .statictext,
.UE_1301_DataPicker.iconDataPicker .dataPicker .selectionFrame .statictext {
  visibility: visible;
}

.dataPicker .headline p {
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-transform: uppercase;
}

.dataPicker .selectionFrame .statictext {
  position: absolute;
  margin-left: 175px;
  height: 100%;
  width: 80px;
  display: flex;
  align-items: center;
  color: rgb(var(--audiWhite));
  z-index: 2;
  visibility: hidden;
}

.UE_1301_DataPicker.unit .dataPicker .selectionFrame .statictext {
  margin-left: unset;
  right: 5px;
}

.UE_1301_DataPicker.iconDataPicker .dataPicker .selectionFrame .statictext {
  margin-left: 5px;
  display: flex;
  justify-content: center;
  right: unset;
}

.UE_1301_DataPicker.iconDataPicker.unit
  .dataPicker
  .selectionFrame
  .statictext {
  right: unset;
  position: unset;
  margin-right: 5px;
}
.UE_1301_DataPicker.iconDataPicker.unit
  .dataPicker
  .selectionFrame
  .statictext.unitContainer {
  justify-content: flex-start;
}

.dataPicker .background {
  pointer-events: none;
  width: calc(100% - 14px);
  height: 100%;
  z-index: 1;
  position: absolute;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiWhite), 0),
    rgba(var(--audiWhite), 0.1),
    rgba(var(--audiWhite), 0)
  );
  border: 2px solid;
  border-image: linear-gradient(
      to bottom,
      rgba(var(--audiWhite), 0),
      rgba(var(--audiWhite), 0.1),
      rgba(var(--audiWhite), 0)
    )
    1;
  margin-left: 5px;
}

.dataPicker .selectionFrame {
  pointer-events: none;
  width: 100%;
  margin-top: calc(
    (var(--numListItems) / 2 - 0.5) * var(--listItemHeightDataPicker) +
      var(--activeListItemMargin) + var(--fadeOutSize)
  );
  height: calc(var(--listItemHeightDataPicker) - 2px);
  z-index: 3;
  position: absolute;
  background-color: rgb(var(--audiWhite), 0.2);
}
.dataPicker.dataPickerParkingStepper .selectionFrame {
  margin-top: calc(
    (var(--numListItems) / 2 - 0.5) * var(--listItemHeightDataPicker) +
      var(--activeListItemMargin)
  );
}

.UE_1301_DataPicker.iconDataPicker.unit .dataPicker .selectionFrame {
  display: flex;
  justify-content: space-between;
}

.dataPicker .selectionFrame .selectionBorders {
  pointer-events: none;
  width: 100%;
  height: 100%;
  z-index: 4;
  position: absolute;
  border-top: 1px solid rgba(var(--audiWhite), 1);
  border-bottom: 1px solid rgba(var(--audiWhite), 1);
}

.settings {
  position: absolute;
  bottom: 0px;
}
