.ST_0134_DashcamOverview {
  width: 100%;
  height: 100%;
  padding: 95px;
  box-sizing: border-box;
  padding-bottom: 0;
  display: flex;
  justify-content: space-between;

  &__buttonsContainer {
    height: 521px;
    width: 437px;
    display: flex;
    flex-direction: column;

    > div {
      flex: 1;
    }

    &__content {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0 50px;
      box-sizing: border-box;

      &__iconContainer {
        position: relative;
        width: var(--touchIconSize);
        height: var(--touchIconSize);
        margin-right: var(--iA1);
      }
    }
  }

  &__imageContainer {
    height: 100%;
    padding-top: 4px;

    &__image {
      width: 926px;
      height: 513px;
      background-color: rgba(var(--boundingBoxGreen));
      margin-bottom: var(--iA1);
    }
  }
}
