/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --keyCount: 11;
  --separatorCount: calc((var(--keyCount) - 1) * var(--keyboardSeparatorWidth));
  --keyWidth: calc(100% / var(--keyCount));
  --longPressExpensionALeft: 63px;
  --longPressExpensionOLeftOne: 65px;
  --longPressExpensionOLeftTwo: 237px;
  --longPressExpensionOLeftThree: 411px;
  --longPressExpensionOLeftFour: 584px;
  --longPressExpensionOLeftFive: 756px;
  --menuKeyWidth: 172px;
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_0502_Keyboard {
  margin: 0;
}

.UE_0502_Keyboard {
  width: 100%;
  height: 100%;
}

:global(#root) > .UE_0502_Keyboard .keyboard {
  top: calc(2 * var(--minTouchSize) + var(--iA3));
}

/*-------------------- .UE_0502_Keyboard --------------------*/

.keyboard {
  position: relative;
  height: calc(4 * var(--keyboardLineHeight));
  display: flex;
  flex-direction: column;
}

.keyboardLine {
  height: calc(var(--keyboardLineHeight) - 1px);
  display: flex;
  justify-content: center;
  border-bottom: 1px solid rgb(var(--audiGrey));
}
.LPE .keyboardLine {
  opacity: var(--touchDisabledState);
}

/* LAST LINE */
.keyboard .keyboardLine:last-of-type {
  border-bottom: 1px solid rgba(var(--audiGrey), 0);
}

/* KEYBOARD BUTTON */

.keyboardLine .button {
  position: relative;
  width: var(--keyWidth);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.keyboardLine .button:before {
  content: '';
  position: absolute;
  width: var(--keyboardSeparatorWidth);
  height: var(--keyboardSeparatorHeight);
  background: rgb(var(--audiGrey));
  left: 0;
}

.keyboard .keyboardLine .button p {
  text-align: center;
  align-self: center;
}

.keyboardLine .button:first-child:before,
.keyboardLine .noBorder:before {
  background: none;
}

.keyboardLine .button.okButton:before {
  background: none;
}

.keyboardLine .specialButton {
  width: calc(var(--keyWidth) * 2);
}

.keyboardLine .spaceButton {
  width: calc(var(--keyWidth) * 4);
  overflow: hidden;
}

.keyboardLine .spaceButton .textPrio2 {
  text-transform: uppercase;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 0 var(--textButtonMargin);
  flex: 1;
}

.SE_0081_LongPressExtension {
  position: absolute;
  z-index: 3;
}

.SE_0081_LongPressExtension.A_Extension {
  top: -68px;
}

.SE_0081_LongPressExtension.A_Extension.twoLines {
  top: -189px;
}

.SE_0081_LongPressExtension .button {
  width: var(--menuKeyWidth) !important;
}

.SE_0081_LongPressExtension.O_Extension {
  right: 350px;
  top: -157px;
}

.SE_0081_LongPressExtension.O_Extension.twoLines {
  top: -270px;
}

.SE_0081_LongPressExtension.A_Extension .button:hover .uiState,
.SE_0081_LongPressExtension.O_Extension .button:hover .uiState {
  width: 100%;
  opacity: 1;
}

@media only screen and (max-width: 1768px) {
  :root {
    --menuKeyWidth: 158.35px;
  }

  .SE_0081_LongPressExtension.O_Extension {
    right: 319px;
  }
}

.settings {
  position: relative;
  top: calc(2 * var(--minTouchSize) + var(--iA3));
}
