/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_0304_GenericWizard {
  position: absolute;
  height: 554px;
  width: var(--dashboardWidth);
  margin: 0;
}

.genericWizard {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.contentContainer {
  position: relative;
  flex: 1;
}

.genericWizard.full .contentContainer.right {
  display: none;
}

.genericWizard.oneOne .contentContainer.right,
.genericWizard.oneOne .contentContainer.left {
  flex: 1;
}

.genericWizard.oneTwo .contentContainer.right,
.genericWizard.twoOne .contentContainer.left {
  flex: 2;
}

.genericWizard.twoThree .contentContainer.right,
.genericWizard.threeTwo .contentContainer.left {
  flex: 3;
}

.genericWizard.twoThree .contentContainer.left,
.genericWizard.threeTwo .contentContainer.right {
  flex: 2;
}

/*----------------- CONTENT ------------------*/
.contentContainer .T_0103_ListVertical,
.contentContainer .T_0104_TextAndButtons,
.contentContainer .location,
.contentContainer .flowText,
.contentContainer .decorator {
  height: 100%;
  width: 100%;
  position: absolute;
}

.contentContainer.listVertical .T_0103_ListVertical,
.contentContainer.textAndButtons .T_0104_TextAndButtons {
  display: block;
}

.contentContainer.flowText .scrollableText {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

/*----------------- LOCATION ------------------*/
.contentContainer.location .location {
  display: flex;
  align-items: stretch;
  overflow: hidden;
}

.location .name,
.location .address {
  display: flex;
  flex: 1;
  margin: 0 var(--cA1);
}

.location .name {
  justify-content: space-evenly;
  flex-direction: column;
  width: 500px;
}

.name .inputWrapper {
}

.name .UE_0501_InputField {
  height: var(--minTouchSize);
}

.name .UE_0101_ActionButton {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
}

.name .UE_0101_ActionButton p {
  position: relative;
  text-align: center;
  align-self: center;
  padding: 0 45px;
  margin: unset;
  box-sizing: border-box;
  white-space: unset;
}

.location .address {
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.location .address .touchIcon {
  position: relative;
  left: auto;
  top: auto;
  transform: none;
  margin-right: var(--cA1);
}
.location .address .textWrapper {
  width: fit-content;
}

.location .address .textWrapper .Text {
  white-space: unset;
}

/*----------------- DECORATOR ------------------*/
.contentContainer.decorator .decorator {
  display: flex;
  align-items: center;
}

.contentContainer .decorator .background {
  background-image: url(../img/200626_UGDO_BG.png);
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 100%;
  width: 100%;
}

.contentContainer .decorator .car {
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  transform: rotateZ(180deg);
  height: 40%;
  width: 100%;
}

.inputFieldHeader {
  text-transform: uppercase;
  margin-bottom: 5px;
}

/*----------------- SETTINGS ------------------*/
.settings {
  position: absolute;
  top: 600px;
  display: flex;
  width: auto;
  min-height: 500px;
}
