/* CLASSIFICATION: CONFIDENTIAL */

.osd {
  width: 100%;
  height: 100%;
}

:global(#root) > .T_0109_OSD {
  width: 1677px; /* Screensizes are just for Demo in Standalone, see integration in BasicLayout */
  height: 672px;
  margin: 8px;
}

/*----------------POPUP FRAME DESIGN--------------*/
.osdContainer {
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  position: relative;
  display: flex;
  justify-content: center;
}
.osdContainer .videoContainer {
  position: absolute;
  height: 100%;
  background-image: url(../img/osdvideoImage_Example.webp);
  background-size: cover;
  aspect-ratio: 16/9;
}

/* --------------------------- UE_0402_Tabbarmenu --------------------------- */

.UE_0402_Tabbarmenu {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
}

/*----------------FSDialog CONTENT--------------*/
.osdContent {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: var(--popupInsetWidth);
  width: calc(100% - var(--popupInsetWidth) * 2);
}

.splitContainer {
  position: relative;
  display: flex;
  width: 0px;
  flex-direction: column;
  flex-grow: 2;
  background-color: rgba(var(--audiBlack), 0.7);
}

/*-----------------Operation Panel-----------------*/
.UE_0401_OperationPanel {
  position: relative;
  width: calc(100% - var(--operationPanelReduction));
  left: var(--operationPanelShift);
}

/* ------------------------------ blockingText ------------------------------ */

.osdBlockingText {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  margin: 0 var(--cA1);
  width: calc(100% - 2 * var(--cA1));
}

.osdBlockingText p {
  position: relative;
  width: 100%;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/* --------------------------------- timeBar -------------------------------- */

.UE_0703_TimeBar {
  margin-left: var(--timebarLeftGap);
  margin-right: var(--timebarLeftGap);
  width: calc(100% - var(--timebarLeftGap) - var(--timebarLeftGap));
}

/* ------------------------------ blocked logik ----------------------------- */

.T_0109_OSD.blocked .osdBlockingText {
  display: block;
}
.T_0109_OSD.blocked .osdContainer .videoContainer {
  display: none;
}

/* ------------------------------- fullscreen ------------------------------- */

:global(#root) > .T_0109_OSD.full {
  width: 1920px;
  height: 956px;
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0109_OSD .osd {
    width: 1525px;
    height: 543px;
  }

  :global(#root) > .T_0109_OSD.full .osd {
    width: 1768px;
    height: 828px;
  }

  .T_0109_OSD.blocked p:nth-last-of-type(-n + 2) {
    display: none;
  }

  .osdBlockingText {
    top: 45%;
  }
}

/*-------------------------------- settings --------------------------------*/
.settings {
  margin-top: 40px;
}
