/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --dateSelectorGap: 50px;
  --dateSelectorFixedGap: 50px;
}

:global(#root) > .SE_0095_CalendarDateSelector {
  width: 1798px;
  height: 566px;
}

:global(#root) > .SE_0095_CalendarDateSelector .dateSelector {
  background-color: rgb(var(--audiBlack));
}

.SE_0095_CalendarDateSelector {
  height: 100%;
}

.dateSelector {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.header {
  width: calc(100% - 2 * var(--popupOutlineWidth));
  height: var(--minTouchSize);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  margin: 0 var(--popupOutlineWidth);
}

.today {
  min-width: fit-content;
  margin: 0 var(--cA1);
}

.buttonContainer {
  height: 100%;
  display: flex;
  flex: 1;
  justify-content: flex-end;
  overflow: hidden;
}

.buttonContainer .button {
  height: 100%;
}

.buttonContainer .button div:last-of-type {
  overflow: hidden;
}

.extensibleBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: calc(100% - 500px);
}

.extensibleBtn {
  width: unset;
  min-width: var(--minTouchSize);
}

.buttonContainer .button p {
  margin: 0 var(--textButtonMargin);
  position: relative;
}

.buttonGrid {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.row {
  display: flex;
  flex-direction: row;
}
.row:not(:last-of-type) {
  margin-bottom: var(--dateSelectorGap);
}

.row .button:not(:last-of-type) {
  margin-right: var(--dateSelectorFixedGap);
}

.weekWrapper {
  display: flex;
  flex-direction: column;
}

.weekWrapper .row {
  margin-bottom: var(--dateSelectorGap);
}

.dayWrapper {
  width: var(--minTouchSize);
  text-align: center;
}

.dayWrapper:not(:last-of-type) {
  margin-right: var(--dateSelectorFixedGap);
}

.currentDay {
  border: 1px solid rgb(var(--ambientcolor1));
  box-sizing: border-box;
}

/*-------------------------------------------------------------------- SMALL DISPLAY ----------------------------------------------------------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0095_CalendarDateSelector {
    width: 1646px;
    height: 437px;
  }

  :root {
    --dateSelectorGap: var(--zA8);
  }
}
