/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --phevPinButtonHeight: 72px;
  --phevDecoratorHeight: 556px;
}

:global(#root) > .ST_0018_PHEVMenu {
  position: absolute;
  height: 601px;
  width: 1678px;
  margin: 0;
}

:global(#root) > .ST_0018_PHEVMenu > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.ST_0018_PHEVMenu .PHEV_Menu {
  height: 100%;
  width: 100%;
  position: absolute;
  display: flex;
  overflow: hidden;
  flex-direction: column;
}

.ST_0018_PHEVMenu .phevContainer {
  position: relative;
  display: flex;
  width: 100%;
  min-height: var(--phevDecoratorHeight);
  flex: 1;
}
.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .phevContainer {
  min-height: unset;
}

.ST_0018_PHEVMenu .PHEV_Menu .phevInfotext,
.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .bargraphSettings,
.ST_0018_PHEVMenu .PHEV_Menu.phev .bargraphSettings,
.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel {
  position: relative;
  width: calc(50% - var(--cA1) * 2);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0 var(--cA1);
  flex-shrink: 0;
}
.ST_0018_PHEVMenu .PHEV_Menu.phev .bargraphSettings {
  height: calc(var(--minTouchSize) * 3);
  align-self: center;
  justify-content: flex-end;
}

.ST_0018_PHEVMenu .PHEV_Menu .phevInfotext {
  height: var(--listIconSize);
  width: calc(100% - var(--cA1) * 2);
  flex-direction: row;
  justify-content: flex-start;
}

.ST_0018_PHEVMenu .PHEV_Menu .phevInfoMarginTop,
.ST_0018_PHEVMenu .PHEV_Menu .phevInfoMarginBottom {
  position: relative;
  width: calc(100% - var(--cA1) * 2);
  min-height: 0px;
  max-height: var(--lA1);
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.ST_0018_PHEVMenu .PHEV_Menu .phevInfoMarginBottom {
  max-height: var(--listIconSize);
}

.ST_0018_PHEVMenu .PHEV_Menu.ev .evDecorator {
  position: relative;
  width: 1096px;
  height: var(--phevDecoratorHeight);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  align-self: center;
}

.ST_0018_PHEVMenu .PHEV_Menu .evDecorator,
.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .evDecorator,
.ST_0018_PHEVMenu .PHEV_Menu .bargraphSettings {
  display: none;
}

.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .UE_0201_ToggleButtonGroup {
  width: 100%;
}

.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .PHEV_setting {
  display: flex;
  height: var(--minTouchSize);
  width: 100%;
  flex-shrink: 0;
  position: relative;
}

.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .PHEV_settingPanel .PHEV_setting {
  display: none;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .PHEV_settingPanel
  .PHEV_setting
  .PHEV_settingThird {
  display: flex;
  height: var(--minTouchSize);
  width: calc(100% - 119px);
  flex-shrink: 0;
  position: relative;
}

.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .PHEV_settingManSoC {
  display: flex;
  height: var(--minTouchSize);
  width: 100%;
  flex-shrink: 0;
  position: relative;
  visibility: hidden;
}
.ST_0018_PHEVMenu .PHEV_Menu.manualLoad .PHEV_settingPanel .PHEV_settingManSoC {
  visibility: visible;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .PHEV_settingPanel
  .PHEV_settingManSoC
  .UE_1103_Switch,
.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .PHEV_setting .UE_1103_Switch {
  width: var(--minTouchSize);
  margin-left: auto;
  flex-shrink: 0;
  pointer-events: none;
}

.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .PHEV_settingManSoC p,
.ST_0018_PHEVMenu .PHEV_Menu .PHEV_settingPanel .PHEV_setting p {
  align-self: center;
  margin-left: var(--textButtonMargin);
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .PHEV_settingPanel
  .PHEV_setting
  .UE_0101_ActionButton.button {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  flex-shrink: 0;
}

.ST_0018_PHEVMenu .PHEV_Menu > .decoratorCover {
  position: absolute;
  width: 845px;
  height: 175px;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0.1),
    rgba(var(--audiBlack), 0.6) 35%,
    rgba(var(--audiBlack), 0.9)
  );
  -webkit-mask-image: linear-gradient(
    to left,
    rgb(var(--audiBlack)) 85%,
    transparent
  );
  top: 300px;
  right: 0;
}

.ST_0018_PHEVMenu .PHEV_Menu .bargraphSettings .titleBox {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .bargraphSettings .titleBox {
  width: calc(100% - (2 * var(--minTouchSize) + (10px)));
}

.ST_0018_PHEVMenu .PHEV_Menu .bargraphSettings .titleBox p {
  align-self: center;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: calc(100% - 2 * var(--iA1));
}

.ST_0018_PHEVMenu .PHEV_Menu .bargraphSettings .SE_0002_SOCBargraph {
  position: absolute;
  width: calc(100% - var(--cA1) * 2 - var(--iA1) * 2);
  top: 53px;
  z-index: 1;
}

.ST_0018_PHEVMenu .PHEV_Menu .evDecorator .carWheels,
.ST_0018_PHEVMenu .PHEV_Menu .evDecorator .carDecorator,
.ST_0018_PHEVMenu .PHEV_Menu .evDecorator .carDecoratorColor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 0 -34px;
  background-size: cover;
}

.ST_0018_PHEVMenu .PHEV_Menu .evDecorator .carDecoratorColor {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: 0 -34px;
  -webkit-mask-size: cover;
  opacity: 0.6;
}

.ST_0018_PHEVMenu .PHEV_Menu .UE_0405_MoreMenu {
  left: calc(60% - 43px) !important;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .SE_0002_SOCBargraph
  .socBargraph
  .pinContainer
  .boundingBox {
  z-index: 10;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .UE_0405_MoreMenu
  .moreMenu
  .moreMenuContainer
  .button:not(:last-child) {
  display: none;
}

.ST_0018_PHEVMenu
  .PHEV_Menu
  .SE_0002_SOCBargraph
  .socBargraph
  .bargraphContent
  .distanceLabelWrapper {
  display: none;
}

.ST_0018_PHEVMenu .SE_0002_SOCBargraph .socBargraph .destinationPosition {
  display: none;
}

.ST_0018_PHEVMenu
  .SE_0002_SOCBargraph.nonInteractive
  .socBargraph
  .bargraphContent
  .range::-webkit-slider-thumb {
  margin-top: -1px;
}

/*----infotainment logic ----*/

.ST_0018_PHEVMenu .PHEV_Menu.ev.oneInfotainment .evDecorator {
  width: calc(55% + (var(--minTouchSize) * 2) - (var(--cA1) * 2) + 10px);
}

.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .bargraphSettings {
  width: calc(50% + (var(--minTouchSize) * 2) - (var(--cA1) * 2) + 10px);
  height: calc(var(--minTouchSize) * 2);
  align-self: center;
  justify-content: flex-end;
}
.ST_0018_PHEVMenu
  .PHEV_Menu.oneInfotainment
  .bargraphSettings
  .SE_0002_SOCBargraph {
  top: -65px;
}

.ST_0018_PHEVMenu .PHEV_Menu.oneInfotainment .PHEV_settingPanel {
  width: calc(50% - ((var(--minTouchSize) + var(--cA1)) * 2) - 10px);
}

.settings {
  position: absolute;
  top: 601px;
  left: var(--hA1);
  color: white;
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0018_PHEVMenu {
    height: 425px;
    width: 1526px;
  }

  .ST_0018_PHEVMenu .settings {
    top: 430px;
  }
  .ST_0018_PHEVMenu .PHEV_Menu.ev .evDecorator {
    width: 986px;
    height: 427px;
  }
}

@media only screen and (max-height: 720px) {
  .ST_0018_PHEVMenu .PHEV_Menu:not(.oneInfotainment) .phevInfotext {
    position: absolute;
    bottom: var(--iA1);
  }
}
