/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --tachometerPHEVWidth: 269px;
  --powermeterPHEVWidth: 130px;
  --scalePHEVSpacing: 68px;
  --scalePHEVOffset: 125px;
}

:global(#root) > .UE_6039_Drehzahlmesser_PHEV_klein {
  left: 10px;
}

.UE_6039_Drehzahlmesser_PHEV_klein {
  position: absolute;
  width: calc(var(--tachometerPHEVWidth) + var(--powermeterPHEVWidth) + 4px);
  height: 70px;
}

.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_01_Benzin {
}

.tachometerKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.tachometerKlein .rpmContainer {
  position: absolute;
  width: var(--tachometerPHEVWidth);
  top: 34px;
  left: calc(var(--powermeterPHEVWidth) + 4px);
}

.tachometerKlein .electricContainer {
  position: absolute;
  width: var(--powermeterPHEVWidth);
  top: 34px;
  left: 0px;
}

/*     scale     */
.tachometerKlein .scale {
  position: absolute;
  top: 10px;
  width: 100%;
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem {
  position: absolute;
  background-repeat: no-repeat;
  height: 16px;
  width: 14px;
  background-color: rgb(var(--audiWhite));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/zeroAsset.png');
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem.zero {
  left: var(--scalePHEVOffset);
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem.two {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/twoAsset.png');
  height: 15px;
  width: 13px;
  left: calc(var(--scalePHEVOffset) + var(--scalePHEVSpacing));
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem.four {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/fourAsset.png');
  height: 14px;
  top: 1px;
  left: calc(var(--scalePHEVOffset) + 2 * var(--scalePHEVSpacing) - 1px);
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem.six {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/sixAsset.png');
  left: calc(var(--scalePHEVOffset) + 3 * var(--scalePHEVSpacing) - 1px);
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .scale .scaleItem.eight {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/eightAsset.png');
  right: -1px;
  background-color: rgb(var(--warningRed));
}

.tachometerKlein .legend {
  position: absolute;
  height: 14px;
  width: 128px;
  top: 53px;
  left: 204px;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/rpmLegend.png');
  background-color: rgb(var(--fpkUnit));
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_00_Off_Benzin
  .tachometerKlein
  .scale
  .scaleItem,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_00_Off_Benzin
  .tachometerKlein
  .legend,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel
  .tachometerKlein
  .legend {
  background-color: rgb(var(--bargraphInactive));
}

/*     bargraph     */
.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKlein .Bargraph {
  position: absolute;
  top: 26px;
  width: var(--tachometerPHEVWidth);
}

/*---------- UE_6039_02_Diesel Variant ---------- */
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_02_Diesel {
  --scalePHEVSpacing: 91px;
}

.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_02_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six {
  background-color: rgb(var(--warningRed));
  left: unset;
  right: -1px;
}
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six {
  background-color: rgb(var(--bargraphInactive));
}

.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.eight,
.UE_6039_Drehzahlmesser_PHEV_klein.UE_6039_02_Diesel
  .tachometerKlein
  .scale
  .scaleItem.eight {
  display: none;
}

/*---------- Bordcomputer Config ---------- */
.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKleinConfig {
  position: absolute;
  top: 200px;
  color: white;
}

.UE_6039_Drehzahlmesser_PHEV_klein .tachometerKleinConfig .level {
  margin-top: 20px;
}

.settings {
  top: 30%;
  position: absolute;
}
