/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --activeCallHeaderHeight: 100px;
}

:global(#root) > .T_5301_ActiveCall {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthGB);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}

.T_5301_ActiveCall {
}

.activeCall {
  position: absolute;
  width: 100%;
  height: 100%;
}

.activeCall .UE_1002_ListItemStandard .listElement .iconContainer .listIcon {
  background-color: transparent !important;
}

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall > .UE_6100_C1Cursor {
  position: relative;
  display: block;
  z-index: 1;
  transition: var(--AG_0401_Scroll_List_Vertical);
  height: var(--FPKListItemHeight);
  width: calc(100% - 30px);
  top: calc(var(--activeCallHeaderHeight) - 1px);
  left: 15px;
}

.T_5301_ActiveCall .activeCall .callOptionsList {
  z-index: 2;
}

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall {
  height: calc(100% - 1px);
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  pointer-events: none;
}

.fadeOutTop {
  -webkit-mask-image: var(--listStartFadeout);
}

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall.fadeOutBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall.fadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.header {
  position: absolute;
  top: 0px;
  left: 0px;
  width: calc(100% - 30px);
  height: calc(var(--activeCallHeaderHeight) - 6px);
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 5px;
  border-bottom: 2px solid rgba(var(--audiGrey), 0.8);
}

.T_5301_ActiveCall .activeCall .firstCall .headlineDivider {
  display: none;
}

.separationLine {
  position: absolute;
  top: calc(var(--activeCallHeaderHeight) + 61px);
  left: 0px;
  height: 2px;
  width: calc(100% - 30px);
  margin-left: 15px;
  margin-right: 15px;
  background-color: rgba(var(--audiWhite), 0.6);
}

.header .textPrio1,
.header .textPrio2 {
  margin-left: var(--iA1);
}

/*    one call    */
.T_5301_ActiveCall .activeCall .firstCall {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.T_5301_ActiveCall .activeCall .firstCall .T_0103_ListVertical {
  position: absolute;
  top: calc(var(--activeCallHeaderHeight) + 5px);
  width: 100%;
  height: calc(100% - var(--activeCallHeaderHeight) - 5px);
  overflow: hidden;
}

/*    two calls   */

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall .callOptionsList {
  position: absolute;
  left: 0px;
  width: calc(100% - 30px);
  height: var(--FPKListItemHeight);
  margin: 0 15px;
  padding: 0;
  overflow: hidden;
}
.T_5301_ActiveCall.T_5301_01_TwoCalls .firstCall .callOptionsList,
.T_5301_ActiveCall.T_5301_01_TwoCalls .secondCall .callOptionsList {
  top: var(--activeCallHeaderHeight);
}

.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall .callOptionsList,
.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall .separationLine,
.T_5301_ActiveCall.T_5301_01_TwoCalls .activeCall .secondCall {
  display: initial;
}

.secondCall {
  position: absolute;
  top: calc(var(--activeCallHeaderHeight) + var(--FPKListItemHeight) + 2px);
  left: 0px;
  width: 100%;
}

.T_5301_ActiveCall.T_5301_01_TwoCalls
  .activeCall.scrollbarWithTrack::-webkit-scrollbar-track {
  border: 1px solid rgb(var(--scrollbarTrackColor));
  background-clip: content-box;
  margin-top: 4px;
  margin-bottom: 4px;
}

/*    testing settings   */
.T_5301_ActiveCall .activeCallConfig {
  position: absolute;
  top: 300px;
  color: white;
  width: 1000px;
}

.T_5301_ActiveCall .activeCallConfig .content_config {
  margin-top: 20px;
}

.list {
  display: block;
  width: calc(100% - 18px);
  height: calc(100% - 1px);
  list-style-type: none;
  padding: 0 var(--listVerticalPadding);
  margin: 0;
}

.settings {
  position: absolute;
  top: 500px;
}
