:global(#root) > .ST_0016_ExteriorLight {
  position: absolute;
  width: 1800px;
  height: 697px;
  margin: 0;
}

.ST_0016_ExteriorLight {
}

.exteriorLight {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
}

.ST_0016_ExteriorLight > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--audiBlack));
}

.exteriorLight .UE_1303_Stepper_Decorator {
  position: absolute;
  width: 100%;
  height: 100%;
}

.exteriorLight .UE_0202_FloatingContainer .button {
  display: flex;
  justify-content: center;
}
.exteriorLight .UE_0202_FloatingContainer .buttonCenter {
  height: var(--minTouchSize);
}

.exteriorLight .buttonCenter.UE_0101_ActionButton p:nth-child(2) {
  opacity: var(--secondaryTextOpacity);
}

.exteriorLight .UE_0202_FloatingContainer .buttonCenter .detailText {
  width: 100%;
}
.decoratorContainer {
  position: absolute;
  width: 100%;
  height: calc(100% - 1px);
}
.additionalAdviceContainer {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
}

.ST_0016_ExteriorLight .additionalAdviceContainer .textWrapper p {
  margin: 0;
  width: auto;
}
.additionalAdviceContainer .textWrapper {
  width: auto;
  margin: 0 var(--lA1) 0 var(--lA1);
  max-width: 42%;
  display: flex;
  height: var(--minTouchSize);
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ST_0016_ExteriorLight
  .exteriorLight
  .UE_1303_Stepper_Decorator
  .imagesContainer {
  display: none;
}

.ST_0016_ExteriorLight .exteriorLight .decoratorContainer .fullDecorator,
.ST_0016_ExteriorLight .exteriorLight .decoratorContainer .fullDecoratorLight {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.ST_0016_ExteriorLight .exteriorLight .decoratorContainer .decoratorDevider {
  display: inline;
  position: absolute;
  top: calc(50% - 8px);
  left: calc(var(--minTouchSize) + 2px);
  width: calc(100% - var(--minTouchSize) * 2 - 4px);
  height: 15px;
  background-color: rgba(var(--audiBlack), 0.7);
}

.ST_0016_ExteriorLight .exteriorLight.halfView .decoratorContainer .frontHalf {
  top: 0px;
}
.ST_0016_ExteriorLight .exteriorLight.halfView .decoratorContainer .rearHalf {
  bottom: 0px;
}

.ST_0016_ExteriorLight .exteriorLight .activateButton .button {
  display: flex;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  justify-content: center;
}

.frontHalf,
.frontHalfLight,
.rearHalf,
.rearHalfLight {
  position: absolute;
  width: 100%;
  height: calc(50% - 7px);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.exteriorLight.halfView .frontHalf,
.exteriorLight.halfView .frontHalfLight {
  top: 0px;
  background-size: 81%;
  background-position-y: -182px;
}

.exteriorLight.halfView .rearHalf,
.exteriorLight.halfView .rearHalfLight {
  bottom: 0px;
  background-size: 81%;
  background-position-y: -129px;
}

.ST_0016_ExteriorLight::not(.SE_0106_Button_with_ConnectorText) p {
  position: relative;
  margin: 0 var(--textButtonMargin);
  width: calc(100% - var(--textButtonMargin) * 2);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.projectionAnimationContainer {
  position: absolute;
  background-image: url(../img/SE_0070_defaultProjection.png);
  width: 1560px;
  height: 224px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* -------------------- Signature Light ------------------------------------*/

.frontConnector {
  position: absolute;
  top: 120px;
  left: 655px;
}
.rearConnector {
  position: absolute;
  top: 450px;
  left: 1100px;
}
:global(#root) > .ST_0016_ExteriorLight .frontConnector {
  top: 120px;
  left: 595px;
}
:global(#root) > .ST_0016_ExteriorLight .rearConnector {
  top: 400px;
  left: 1040px;
}

.settings {
  position: absolute;
  top: 700px;
  width: 800px;
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  .exteriorLight.halfView .frontHalf,
  .exteriorLight.halfView .frontHalfLight {
    background-size: 76%;
    background-position-y: -182px;
  }
  .exteriorLight.halfView .rearHalf,
  .exteriorLight.halfView .rearHalfLight {
    background-size: 76%;
    background-position-y: -151px;
  }
  /* ------- Signature Light ----------*/
  .frontConnector {
    left: 580px;
  }

  .rearConnector {
    top: 320px;
    left: 1025px;
  }
}

/* Default decorators */
/* Decorator Exterieur Front_Light */
.ST_0016_ExteriorLight
  .exteriorLight.frontView
  .decoratorContainer
  .fullDecorator.defaultDecorator,
.ST_0016_ExteriorLight
  .exteriorLight.halfView
  .decoratorContainer
  .frontHalf.defaultDecorator {
  background-image: url('../../../../../Globals/assets/carDecorator/Exterior/default_Front_Light.png');
}
/* Decorator Exterieur Front_Light */
.ST_0016_ExteriorLight
  .exteriorLight.rearView
  .decoratorContainer
  .fullDecorator.defaultDecorator,
.ST_0016_ExteriorLight
  .exteriorLight.halfView
  .decoratorContainer
  .rearHalf.defaultDecorator {
  background-image: url('../../../../../Globals/assets/carDecorator/Exterior/default_Rear_Light.png');
}

/*--------------------------- B10 DISPLAY ---------------------------*/
@media only screen and (min-width: 2219px) {
  /* ------- Signature Light ----------*/
  .rearButton {
    top: 560px;
  }

  .rearConnector {
    top: 525px;
  }
}
