/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --ue1201AppGridTileHeight: 260px;
  --ue1201AppGridTileWidth: 326px;
  --ue1201DecoratorSize: 150px;
  --ue1201titleMarginTop: 15px;
  --ue1201titleMarginBottom: 8px;
  --ue1201titleHeigth: calc(var(--lineHeightPrio2) * 2 + var(--zA2));
}

:global(#root) > .UE_1201_AppGridTile {
  background-color: rgb(var(--bodyColorGrey));
  height: var(--ue1201AppGridTileHeight);
  width: var(--ue1201AppGridTileWidth);
  box-sizing: border-box;
  margin: 10px;
}

.UE_1201_AppGridTile {
  position: relative;
  height: var(--ue1201AppGridTileHeight);
  width: var(--ue1201AppGridTileWidth);
}

.UE_1201_AppGridTile_intentChoser {
  position: relative;
  height: 200px;
  width: var(--ue1201AppGridTileWidth);
}

.appGridTile {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.intentChoserAppGridTile {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 326px;
  width: 100%;
  height: 100%;

  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.title {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(var(--lineHeightPrio2) * 2 + var(--zA2));
  width: calc(100% - var(--zA6) * 2);
  margin-left: var(--zA6);
  margin-right: var(--zA6);
  bottom: 5px;
}

.intentChoserTitle {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(var(--lineHeightPrio2) * 2 + var(--zA2));
  width: calc(100% - var(--zA6) * 2);
  margin-left: var(--zA6);
  margin-right: var(--zA6);
}

.UE_1201_AppGridTile .appGridTile .title .textPrio2 {
  width: 100%;
  text-align: center;
}

.appGridTile .iconWrapper {
  display: none;
}

.appIconWrapper {
  position: relative;
  height: var(--ue1201DecoratorSize);
  width: var(--ue1201DecoratorSize);
}
.appIconWrapper .appIcon:not(.color) {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Decorator/E721_main_menue_Navigation_AG.png);
}

.appGridTile.folder .iconContainer .appFolderIconContainer {
  position: relative;
  left: unset;
  top: unset;
  transform: unset;
  margin-top: 20px;
  margin-bottom: 20px;
  min-width: var(--ue1201AppGridTileWidth);
  width: var(--appFolderIconSize);
  height: var(--appFolderIconSize);
  flex: 1;
}

.appGridTile.folder .iconContainer .appFolderIconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Decorator/E721_main_menue_Navigation_AG.png);
}

.appGridTile.folder
  .iconContainer
  .appFolderIconContainer
  .daIcon.appFolderIcon {
  width: var(--appFolderIconSize);
  height: var(--appFolderIconSize);
  background-size: var(--appFolderIconSize);
}

.appGridTile.folder .iconWrapper {
  position: relative;
  width: 100%;
  height: var(--ue1201DecoratorSize);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: calc(var(--ue1201titleMarginTop) + 5px);
}

.appGridTile.folder .iconContainer {
  position: relative;
  display: flex;
  align-self: center;
  flex: 1;
  padding-left: 0;
  margin-left: 23px;
  margin-right: 23px;
}

.intentChoserAppIconWrapper {
  width: var(--daIconSize) !important;
  height: var(--daIconSize) !important;
}

.settings {
  position: absolute;
  top: 400px;
}

@media only screen and (max-width: 1768px) {
  :root {
    --ue1201DecoratorSize: 132px;
  }
}
