/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --jokerKeyTileSpacing: 18px;
}

:global(#root) > .ST_0017_JokerkeyMenu {
  height: 956px;
}

:global(#root) > .ST_0017_JokerkeyMenu .wallpaper {
  position: absolute;
  width: 100%;
  height: calc(100% + 20px);
  top: -20px;
  background-color: rgb(var(--ambientBackground1));
}

/*-------------------Joker Keys--------------------*/
.ST_0017_JokerkeyMenu {
  height: 100%;
}
.ST_0017_JokerkeyMenu .jokerKeyMenu {
  position: relative;
  height: 100%;
}

.ST_0017_JokerkeyMenu .operationLine {
  bottom: 0;
  display: flex;
  flex-direction: row;
  height: 120px;
  justify-content: center;
}

.ST_0017_JokerkeyMenu .jokerKeyRow .touchIcon {
  pointer-events: none;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .UE_0101_ActionButton.cursor {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
}

.ST_0017_JokerkeyMenu .operationLine.jokerOperation {
  display: block;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality {
  display: flex;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality {
  display: flex;
  flex-direction: column;
  padding: var(--iA1) var(--iA1) 0 var(--iA1);
  width: 100%;
  height: calc(100% - 120px);
  overflow: hidden;
  box-sizing: border-box;
  user-select: none;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .button {
  user-select: none;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .split {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .headline {
  position: relative;
  text-align: center;
  margin-bottom: 28px;
  top: -11px;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeySelection {
  display: flex;
  width: 100%;
  align-items: center;
  overflow-y: hidden;
  overflow-x: hidden;
}

.ST_0017_JokerkeyMenu
  .jokerKeyFunctionality
  .jokerKeySelection::-webkit-scrollbar {
  display: none;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow {
  display: flex;
  width: 100%;
  height: var(--minTouchSize);
  align-items: center;
  justify-content: center;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .button {
  position: relative;
  min-width: var(--minTouchSize);
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
  margin-left: var(--jokerKeyTileSpacing);
}
.ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow .button {
  margin-left: 0;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroup {
  display: flex;
  position: relative;
  height: 100%;
}

.ST_0017_JokerkeyMenu .operationLine.jokerOperation {
  bottom: 0;
  display: flex;
  flex-direction: row;
  position: absolute;
  width: 100%;
}

.ST_0017_JokerkeyMenu .jokerKeyRow .touchIcon {
  pointer-events: none;
}

.ST_0017_JokerkeyMenu .jokerKeyFunctionality .UE_0101_ActionButton.cursor {
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
}

/*-------------------Joker Keys--------------------*/
.ST_0017_JokerkeyMenu .climateScreen .operationLine.jokerOperation,
.ST_0017_JokerkeyMenu .climateScreen .jokerKeyFunctionality,
.ST_0017_JokerkeyMenu .climateScreen .operationLine.top,
.ST_0017_JokerkeyMenu .climateScreen .content {
  display: none;
}

.ST_0017_JokerkeyMenu .climateScreen .operationLine.jokerOperation {
  display: block;
}

.ST_0017_JokerkeyMenu .climateScreen .jokerKeyFunctionality {
  display: flex;
}

.ST_0017_JokerkeyMenu .operationLine.bottom .UE_0401_OperationPanel {
  width: calc(6 * var(--minTouchSize));
  height: 100%;
  bottom: 0px;
  z-index: 1;
  position: relative;
}

/* ------------------------------ B10 screen ------------------------------ */
@media only screen and (min-width: 2219px) {
  .ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupLeft {
    margin-right: calc(18px * 4);
  }
  .ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupLeft,
  .ST_0017_JokerkeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupRight {
    position: relative;
    height: 100%;
  }
}

/* ------------------------------ 12inch screen ------------------------------ */
@media only screen and (min-width: 1920px) and (max-width: 1920px) {
  .SE_0075_JokerKeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupLeft {
    margin-right: calc(18px * 4);
  }
  .SE_0075_JokerKeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupLeft,
  .SE_0075_JokerKeyMenu .jokerKeyFunctionality .jokerKeyRow .jokerGroupRight {
    position: relative;
    height: 100%;
  }
}
