#root > .SE_0080_TwoColumnsList {
  width: 1920px;
  height: 800px;
}

.SE_0080_TwoColumnsList {
  width: 100%;
  height: 100%;

  &__twoColumnsList {
    display: inline-grid;
    grid-template-columns: auto auto;
    grid-gap: 2px var(--iA1);
    width: calc(100% - var(--iA1) - (var(--listVerticalPadding) * 2));
    height: calc(100% - 32px);
    list-style-type: none;
    padding-right: calc(var(--listVerticalPadding) * 2);
    margin: var(--iA1) 0;
    overflow-y: auto;
    overflow-x: hidden;

    &--scrollbarWithTrack::-webkit-scrollbar-track {
      border: 1px solid rgba(var(--audiWhite), 0.15);
      background-clip: content-box;
      margin-top: 4px;
      margin-bottom: 4px;
    }

    &--fadeOutTop {
      -webkit-mask-image: var(--listStartFadeout);
    }

    &--fadeOutBottom {
      -webkit-mask-image: var(--listEndFadeout);
    }

    &--fadeOutTopBottom {
      -webkit-mask-image: var(--listStartEndFadeout);
    }

    &__gridElement {
      position: relative;

      .UE_1002_ListItemStandard {
        &__listElement {
          padding: 0 11px;
        }
      }
    }
  }
}

@media only screen and (max-width: 1768px) {
  #root > .SE_0080_TwoColumnsList {
    width: 1768px;
  }
}
