.phoneOptions {
  position: absolute;
  width: 523px;
  height: 168px;
  bottom: 0;
  left: 405px;
}

.phoneOptions--rhd {
  left: unset;
  right: 405px;
}

.phoneList {
  height: 100%;
  transform: translateX(-8px);
}

.noPhoneText {
  height: calc(100% - var(--hudListItemHeight) - 12px);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.noPhoneText p {
  white-space: unset;
  line-height: 40px;
  text-align: center;
  max-height: 112px;
  max-width: 468px;
}

.phoneList li:nth-child(2) .textColumn,
.phoneList li:nth-child(3) .textColumn {
  margin-left: 6px;
}
