.ipaPopup {
  flex-direction: column;
  position: relative;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: calc(100% - var(--popupInsetWidth) * 2)
    calc(100% - var(--popupInsetWidth) * 2);
  border-color: rgba(var(--audiWhite), 0.3);
  box-sizing: border-box;
}

.ipaTitleLine {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: none;
}

.ipaTitleLine {
  display: flex;
}

.ipaTitleLine .UE_0101_ActionButton {
  position: relative;
  height: 100%;
  width: 119px;
  align-self: center;
}

.ipaTitleLine p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.headline {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  max-width: calc(100% - 119px - (var(--lA1) * 2));
  align-items: center;
}

.headline.noButton {
  max-width: calc(100% - (var(--lA1) * 2));
}

.headline p {
  position: relative;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}

.ipaPopup.var1 .ipaContent,
.ipaPopup.var2 .ipaContent {
  height: calc(100% - var(--minTouchSize));
}

.ipaPopup.var3 .ipaContent {
  flex: 1;
  overflow: hidden;
}

.ipaPopup.var4 .ipaContent {
  height: calc(100% - (var(--minTouchSize)));
}

.ipaPopup.var4 .headline {
  width: 100%;
  text-align: center;
}

.ipaPopup.var5 .ipaContent {
  height: 100%;
  overflow: hidden;
}

.ipaPopup.var6 .ipaContent {
  height: calc(100% - var(--minTouchSize));
}
