/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --previewWidth: 1600px;
  --previewHeight: 900px;
}

:global(#root) > .screen {
  margin: 0;
}

.div1 {
  position: relative;
  width: 100px;
  height: 36px;
  margin: calc(var(--previewHeight) / 3 - 18px)
    calc(var(--previewWidth) / 2 - 50px);
  background-image: url(../img/Rings_wh-RGB.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.div2 {
  position: absolute;
  width: 120px;
  height: 60px;
  margin: calc(var(--previewHeight) / 3 - 30px)
    calc(var(--previewWidth) / 2 - 60px);
  background-color: rgb(187, 10, 48);
  border-radius: 10px;
}

.div3 {
  position: absolute;
  width: 120px;
  height: 60px;
  opacity: 0;
  margin: calc(var(--previewHeight) / 3 - 30px)
    calc(var(--previewWidth) / 2 - 60px);
  background-color: rgb(102, 112, 199);
  border-radius: 10px;
}

.buttonContainer {
  position: absolute;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: flex-start;
}

.buttonContainer.line1 {
  bottom: 240px;
}
.buttonContainer.line2 {
  bottom: 120px;
}
.buttonContainer.line3 {
  bottom: 0px;
}

.button {
  position: relative;
  width: 155px;
  height: 100px;
  font-family: var(--fontLight);
  background-image: -webkit-repeating-linear-gradient(
    -30deg,
    transparent,
    transparent 2px,
    rgba(255, 255, 255, 0.07) 2px,
    rgba(255, 255, 255, 0.13) 4px,
    rgba(255, 255, 255, 0.07) 5.5px
  );
  color: white;
  background-color: black;
  border: none;
  margin-left: 15px;
}

button:focus {
  outline: 0;
}

.screen {
  position: absolute;
  width: var(--previewWidth);
  height: var(--previewHeight);
  background-color: black;
  overflow: hidden;
}

.div1.moveLeft {
  margin-left: calc(var(--previewWidth) / 2 - 168px);
}
.div1.moveRight {
  margin-left: calc(var(--previewWidth) / 2 + 100px);
}
.div1.moveTop {
  margin-top: calc(var(--previewHeight) / 3 - 168px);
}
.div1.moveBottom {
  margin-top: calc(var(--previewHeight) / 3 + 100px);
}

.div1.fadeIn {
  opacity: 1;
}

.div1.fadeOut {
  opacity: 0;
}

.div1.scaleUp {
  transform: scale(2, 2);
}

.div1.scaleDown {
  transform: scale(0.5, 0.5);
}

.div2.crossFade {
  opacity: 0;
}

.div3.crossFade {
  opacity: 1;
}

.button.active {
  background-color: rgb(255, 255, 255, 0.1);
}

.div2.reSize {
  width: 200px;
  height: 72px;
  margin: calc((var(--previewHeight) - 105px) / 3)
    calc((var(--previewWidth) - 200px) / 2);
}
