/* CLASSIFICATION: CONFIDENTIAL */
.T_0110_LoadingScreen {
}

:global(#root) > .T_0110_LoadingScreen {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--statusBarHeight) - var(--climateBarHeight));
}

.loadingScreen {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.UE_0702_ProgressBar {
  position: relative;
  display: initial;
  width: calc(100% - (2 * var(--cA1)));
  max-width: 1135px;
  height: 82px;
  margin-top: 50px;
}

.loadingScreen .textContainer {
  position: relative;
  margin: 0 var(--cA1);
  width: calc(100% - (2 * var(--cA1)));
  max-width: 1280px;
}

.loadingScreen .textContainer p {
  width: 100%;
  text-align: center;
  vertical-align: middle;
}

.settings {
  position: absolute;
  bottom: 20px;
}
