.TiltAngle__Wrapper {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
}

.TiltAngle__sideTiltContainer {
  height: 70%;
  width: 70%;
  mask: linear-gradient(
    180deg,
    #0000 27%,
    rgba(0, 0, 0, 1) 38%,
    rgba(0, 0, 0, 1) 64%,
    #0000 71%
  );
  -webkit-mask: linear-gradient(
    180deg,
    #0000 27%,
    rgba(0, 0, 0, 1) 38%,
    rgba(0, 0, 0, 1) 64%,
    #0000 71%
  );
  position: absolute;
  bottom: -69px;
  left: 50%;
  transform: translateX(-50%);
}

.TiltAngle__sideTilt {
  position: absolute;
  background-image: url('../../img/TiltAngle_circle.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  width: 100%;
}

.TiltAngle__sideTilt__tag {
  position: absolute;
  top: 50%;
  left: 280px;
  transform: translateY(-50%);
}

.TiltAngle__lengthTiltContainer {
  height: 70%;
  width: 70%;
  grid-area: 1/1;

  /* clip-path: polygon(0 35%, 100% 34%, 100% 79%, 0 40%); */
  mask: linear-gradient(
    180deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 40%,
    rgba(0, 0, 0, 1) 57%,
    #0000 60%
  );

  -webkit-mask: linear-gradient(
    180deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 40%,
    rgba(0, 0, 0, 1) 57%,
    #0000 60%
  );

  position: absolute;
  bottom: -69px;
  left: 50%;
  transform: translateX(-50%);
}

.TiltAngle__lengthTilt {
  position: absolute;
  background-image: url('../../img/TiltAngle_line.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  width: 100%;
}

.TiltAngle__lengthTilt__tag {
  position: absolute;
  top: 50%;
  left: 420px;
  transform: translate(-5%, -50%);
}

.TiltAngle__tag {
  display: flex;
  align-items: center;
}

.TiltAngle__tag__text {
  box-sizing: border-box;
  border: 1px solid rgba(var(--ambientcolor1));
  width: 67px;
  height: 33px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.TiltAngle__tag__text p {
  font-family: var(--fontLight);
}

.TiltAngle__tag__tip {
  box-sizing: border-box;
  height: 1px;
  width: 18px;
  background-color: rgba(var(--ambientcolor1));
}
