$arrowExternalMargin: 200px;

:global(#root) > .ST_0013_DrivingSchoolMode {
  margin: 8px;
}

.ST_0013_DrivingSchoolMode {
  height: 100%;
  &__drivingSchoolMode {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 0;
    &__centralContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: var(--popupContentPadding);
      height: 100%;
      &__drivingSchoolArrow {
        width: var(--minTouchSize);
        height: var(--minTouchSize);
        background-color: rgba(var(--boundingBoxGreenFull));

        &:first-of-type {
          margin-right: var(--iA1);
          margin-left: $arrowExternalMargin;
        }
        &:last-of-type {
          margin-left: var(--iA1);
          margin-right: $arrowExternalMargin;
        }
      }

      &__textContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        flex: 1;
        p {
          margin-top: var(--cA1);
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          max-width: calc(
            var(--dashboardWidth) - (var(--minTouchSize) * 2) -
              (var(--popupContentPadding) * 2) - (var(--iA1) * 2) -
              ($arrowExternalMargin * 2)
          );
          &:first-of-type {
            margin-top: calc(var(--hA2) + var(--hA1));
          }
        }
      }
    }
  }
}
