/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --linesColor: rgb(var(--ambientcolor1));
  --surfaceColor: rgb(var(--ambientcolor1));
  --interiorDashboardImgHeight: 530px;
}

:global(#root) > .ST_0001_InteriorLight {
  width: 1800px;
  height: 697px;
  margin: 0;
}

.ST_0001_InteriorLight {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgb(var(--audiBlack));
}

.interiorLight {
  width: 100%;
  height: 100%;
  position: relative;
}

.interiorImgContainer {
  width: 100%;
  height: 100%;
  position: absolute;
}

.interiorImgContainer .intBasis {
  position: absolute;
  width: 100%;
  height: var(--interiorDashboardImgHeight);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.interiorImgContainer .intLines {
  position: absolute;
  width: 100%;
  height: var(--interiorDashboardImgHeight);
  background-repeat: no-repeat;
  background-color: var(--linesColor);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
}
.interiorImgContainer .intICL {
  position: absolute;
  width: 100%;
  height: var(--interiorDashboardImgHeight);
  background-repeat: no-repeat;
  background-color: var(--linesColor);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
}

.interiorImgContainer .intSurface {
  position: absolute;
  width: 100%;
  height: var(--interiorDashboardImgHeight);
  background-repeat: no-repeat;
  background-color: var(--surfaceColor);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
}

/* :global(.Balanced) .interiorImgContainer .intSurface {
  background-color: rgb(var(--SE_5006_01_neutral_background));
} */

/***********  CONNECTORS  ************/

.leftConnector {
  position: absolute;
  top: 127px;
  left: 413px;
  z-index: 1;
}

.rightConnector {
  position: absolute;
  top: 235px;
  right: 840px;
  z-index: 1;
}

.rightConnectorVariant2 {
  position: absolute;
  top: 64px;
  right: 745px;
  z-index: 1;
}

/***********   BUTTONS   ************/

.lightBarButtonGroup {
  left: 1289px;
  top: 27px;
  position: absolute;
  justify-content: center;
  box-sizing: border-box;
  display: flex;
  max-width: 505px;
}

.button.lightBarButton {
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  justify-content: center;
  box-sizing: border-box;
  display: flex;
}

.button.lightBarButton .btnContainerText {
  max-width: calc(100% - var(--minTouchSize));
}

.button.lightBarButton .UE_1103_Switch {
  width: var(--minTouchSize);
  margin-left: auto;
  flex-shrink: 0;
  background-color: var(--buttonBackgroundColor);
}

.interiorLight .button p {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.ST_0001_InteriorLight
  .interiorLight
  .UE_0201_ToggleButtonGroup
  .interiorLightToggleGroup {
  display: flex;
}

.buttonWrapper > .interactionBackground {
  -webkit-mask-image: linear-gradient(
    to left,
    rgba(0, 0, 0, 1) 0px,
    rgba(0, 0, 0, 1) 113px,
    rgba(0, 0, 0, 0) 114px,
    rgba(0, 0, 0, 0) 116px,
    rgba(0, 0, 0, 1) 117px,
    rgba(0, 0, 0, 1) 100%
  );
}

.buttonWrapper {
  position: relative;
  display: flex;
}

.interiorLight.variant3 .functionalAmbientButton .interactionBackground {
  width: calc(100% - (var(--opticalTouchReduction) / 2));
}

.ST_0001_InteriorLight .functionalAmbientButton.toggle.activated .uiState,
.ST_0001_InteriorLight .functionalAmbientButton.toggle.activated .borderBottom,
.ST_0001_InteriorLight .functionalAmbientButton.button:active .uiState,
.ST_0001_InteriorLight .functionalAmbientButton.button:active .borderBottom {
  width: calc(100% - var(--uiStateMargin));
}

.ST_0001_InteriorLight .interiorLight.variant3 .functionalAmbientButton .state {
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
}

.ST_0001_InteriorLight .interiorLight.variant3 .switchButton .state {
  border-radius: 0 var(--standardBorderRadius) var(--standardBorderRadius) 0;
}

.ST_0001_InteriorLight .interactionBackground.leftBorderRadius {
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
}
.ST_0001_InteriorLight .interactionBackground.rightBorderRadius {
  border-radius: 0 var(--standardBorderRadius) var(--standardBorderRadius) 0;
}

/* --------------------------------- slider --------------------------------- */

.ST_0001_InteriorLight
  .interiorLight.variant2
  .UE_0202_FloatingContainer
  .UE_0601_Slider {
  height: var(--minTouchSize);
  position: relative;
  display: block;
  z-index: 1;
}

.ST_0001_InteriorLight
  .interiorLight.variant2
  .UE_0202_FloatingContainer
  .UE_0601_Slider:nth-child(2) {
  margin-bottom: 15px;
}

.ST_0001_InteriorLight
  .interiorLight.variant2
  .UE_0202_FloatingContainer
  .floatingContainer {
  max-height: calc(2 * var(--minTouchSize) + 15px);
  width: 1150px;
}

/*------------------ Variant 3 ------------------*/
.interiorLight.variant3 .UE_0202_FloatingContainer .floatingContainer {
  display: flex;
}

.ambientLightButton {
  max-width: 365px;
  margin-right: 2px;
}

.functionalAmbientButton {
  max-width: 575px;
}

.AU401_0 .interiorLight .leftConnector,
.AU401_0_LWB .interiorLight .leftConnector,
.AU401_0_RS .interiorLight .leftConnector,
.AU402_0 .interiorLight .leftConnector,
.AU402_0_RS .interiorLight .leftConnector {
  transform: translateY(40px);
}

/* ----------------------------- OneInfotainment ------------------------------ */

.interiorLight.oneInf .UE_0202_FloatingContainer .bottom {
  max-height: unset;
}

/* --------------------------------- settings --------------------------------- */

.settings {
  position: absolute;
}
/*------------------ SMALL DISPLAY ------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0001_InteriorLight {
    width: 1648px;
    height: 450px;
  }

  .ST_0001_InteriorLight .interiorLight .interiorImgContainer {
    transform: scale(0.85) translate(0, -45px);
  }

  .rightConnectorVariant2 {
    position: absolute;
    top: 100px;
    right: 700px;
    z-index: 1;
  }

  .lightBarButtonGroup {
    left: 1140px;
  }

  .rightConnectorVariant2 {
    position: absolute;
    top: 33px;
    right: 720px;
    z-index: 1;
  }

  .ST_0001_InteriorLight .interiorLight .leftConnector {
    transform: translate(6px, -25px);
  }

  .ST_0001_InteriorLight .interiorLight .rightConnector {
    transform: translate(0px, -40px);
  }
}

@media only screen and (max-height: 720px) {
  .translateYPID {
    transform: translateY(-55px);
  }

  .screenisPID {
    transform: translateY(-55px);
    mask: linear-gradient(0deg, #0000 171px, rgba(0, 0, 0, 1) 215px);
    -webkit-mask: linear-gradient(0deg, #0000 171px, rgba(0, 0, 0, 1) 215px);
  }
}

@media only screen and (max-width: 1920px) {
  :root {
    --interiorDashboardImgHeight: 480px;
  }
  .translateYPID {
    transform: translateY(-55px);
  }

  .screenIs12Inch {
    transform: translateY(-45px);
    mask: linear-gradient(0deg, #0000 171px, rgba(0, 0, 0, 1) 215px);
    -webkit-mask: linear-gradient(0deg, #0000 171px, rgba(0, 0, 0, 1) 215px);
  }

  .leftConnector {
    top: 217px;
    left: 433px;
  }

  .rightConnector {
    top: 190px;
  }
}
