/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --tachometerWidth: 403px;
  --scaleSpacing: 101px;
}

:global(#root) > .UE_6035_Drehzahlmesser_klein {
  left: 10px;
}

.UE_6035_Drehzahlmesser_klein {
  position: absolute;
  width: var(--tachometerWidth);
  height: 70px;
}

.UE_6035_Drehzahlmesser_klein.UE_6035_01_Benzin {
}

.tachometerKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.tachometerKlein .rpmContainer {
  position: absolute;
  width: var(--tachometerWidth);
  top: 34px;
  left: 0px;
}

/*     scale     */
.tachometerKlein .scale {
  position: absolute;
  top: 10px;
  left: -7px;
  width: calc(100% + 4px);
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem {
  position: absolute;
  background-repeat: no-repeat;
  height: 16px;
  width: 14px;
  background-color: rgb(var(--audiWhite));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/zeroAsset.png');
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem.zero {
  left: 6px;
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem.two {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/twoAsset.png');
  height: 15px;
  width: 13px;
  left: var(--scaleSpacing);
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem.four {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/fourAsset.png');
  height: 14px;
  top: 1px;
  left: calc(2 * var(--scaleSpacing) - 1px);
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem.six {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/sixAsset.png');
  left: calc(3 * var(--scaleSpacing) - 1px);
}

.UE_6035_Drehzahlmesser_klein .tachometerKlein .scale .scaleItem.eight {
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/eightAsset.png');
  right: -4px;
  background-color: rgb(var(--warningRed));
}

.tachometerKlein .legend {
  position: absolute;
  height: 14px;
  width: 128px;
  top: 53px;
  left: 138px;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/rpmLegend.png');
  background-color: rgb(var(--fpkUnit));
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6035_Drehzahlmesser_klein.UE_6035_00_Off_Benzin
  .tachometerKlein
  .scale
  .scaleItem,
.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem,
.UE_6035_Drehzahlmesser_klein.UE_6035_00_Off_Benzin .tachometerKlein .legend,
.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel .tachometerKlein .legend {
  background-color: rgb(var(--bargraphInactive));
}

/*     bargraph     */
.UE_6035_Drehzahlmesser_klein .tachometerKlein .Bargraph {
  position: absolute;
  top: 26px;
  width: var(--tachometerWidth);
}

/*---------- UE_6035_02_Diesel Variant ---------- */
.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel,
.UE_6035_Drehzahlmesser_klein.UE_6035_02_Diesel {
  --scaleSpacing: 135px;
}

.UE_6035_Drehzahlmesser_klein.UE_6035_02_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six,
.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six {
  background-color: rgb(var(--warningRed));
  right: -4px;
  left: unset;
}
.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.six {
  background-color: rgb(var(--bargraphInactive));
}

.UE_6035_Drehzahlmesser_klein.UE_6035_03_Off_Diesel
  .tachometerKlein
  .scale
  .scaleItem.eight,
.UE_6035_Drehzahlmesser_klein.UE_6035_02_Diesel
  .tachometerKlein
  .scale
  .scaleItem.eight {
  display: none;
}

/*---------- Bordcomputer Config ---------- */
.UE_6035_Drehzahlmesser_klein .tachometerKleinConfig {
  position: absolute;
  top: 200px;
  color: white;
}

.UE_6035_Drehzahlmesser_klein .tachometerKleinConfig .level {
  margin-top: 20px;
}

.settings {
  top: 30%;
  position: absolute;
}
