}

.naviActiveGuidance {
  position: absolute;
  width: calc(100% - var(--zA4) * 2);
  height: 100%;
  margin-left: var(--zA4);
  margin-right: var(--zA4);
  overflow: hidden;
}

.content {
  position: relative;
  width: 100%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.textLine {
  position: relative;
  width: 100%;
  margin-top: var(--zA1);
  display: flex;
  flex-direction: row;
  align-items: baseline;
  overflow: hidden;
}

.textLine .textPrio1 {
  margin-top: 0;
}

.textLine p:first-child {
  flex: 1;
}

.textLine p:nth-child(2) {
  width: 126px;
  text-align: right;
}
.textLine p:last-child {
  min-width: 50px;
}

.textLine p:not(:first-child) {
  margin-left: var(--zA8);
}

.naviActiveGuidance > .UE_6100_C1Cursor {
  height: var(--FPKListItemHeight);
  width: 100%;
  display: block;
  position: absolute;
  top: 186px;
  z-index: 1;
}

.cancel {
  position: absolute;
  width: calc(100% - var(--zA6) * 2);
  top: 200px;
  margin-left: var(--zA6);
  margin-right: var(--zA6);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  z-index: 2;
}

.separationLine {
  position: absolute;
