/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0064_List_Element_with_Text_Button {
  margin: 0px;
  border: 1px solid yellow;
  background-color: rgb(var(--bodyColorGrey));
  position: relative;
}

.SE_0064_List_Element_with_Text_Button {
}

.shopListElement {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-wrap: nowrap;
  width: 100%;
  height: var(--minTouchSize);
}

.UE_1002_ListItemStandard {
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-wrap: nowrap;
  position: relative;
  overflow: hidden;
}

.priceLabel {
  display: inline-flex;
  align-items: center;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  flex-grow: 0;
}

/* ---------------------------- BUTTON CONTAINER ---------------------------- */
.buttonContainer {
  position: relative;
  margin-left: var(--iA1);
  max-width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* -------------------------------- settings -------------------------------- */
.settings {
  position: absolute;
  margin-top: 50px;
}
