/* CLASSIFICATION: CONFIDENTIAL */

/* ------------------ Standalone ------------------ */

:global(#root) > .SE_0037_AlternativeRoute {
  background: rgb(var(--bodyColorGrey));
  height: 360px;
  width: 100%;
  margin: 0;
}

/* ------------------ .SE_0037_AlternativeRoute ------------------ */

.SE_0037_AlternativeRoute {
}

.listContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.routeListItem {
  flex: 1 1 var(--minTouchSize);
  position: relative;
  width: 100%;
  height: unset;
}

.listElement {
  min-height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-wrap: nowrap;
  position: relative;
  height: 100%;
}

.listElement::before {
  content: '';
  width: var(--hA1);
  margin: var(--iA1) 0 var(--iA1) var(--iA1);
}

.routeListItem.red .listElement::before {
  background-color: rgb(var(--routeActiveColor));
}

.routeListItem.violet .listElement::before {
  background-color: rgb(var(--routeAlternativeOneBorder));
}

.routeListItem.pink .listElement::before {
  background-color: rgb(var(--routeAlternativeTwoBorder));
}

.textContainer {
  display: inline-flex;
  flex: 1;
  flex-direction: row;
  min-width: 0;
  position: relative;
  margin-left: var(--iA1);
  margin-right: calc(2 * (var(--iA1)));
}

.textColumn {
  display: flex;
  position: relative;
  flex: 1;
  justify-content: flex-end;
  align-self: center;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-direction: column;
  height: calc(var(--lineHeightPrio1) + var(--lineHeightPrio2) + var(--zA1));
  bottom: 0;
  margin-right: var(--cA1);
}

.textColumn:last-of-type {
  align-items: flex-start;
  margin-right: 0;
  min-width: 298px;
  max-width: 298px;
}

.textColumn .textPrio1,
.textColumn .textPrio2 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.textColumn .iconText {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: var(--lineHeightPrio2);
}

.textColumn:nth-child(3) .iconText:last-child {
  margin-top: calc((var(--lineHeightPrio1) - var(--lineHeightPrio2) + 1px) / 2);
}

.separatorLine {
  height: calc(var(--zA1) - var(--zA2));
  margin: 0 var(--iA1);
  background-color: rgba(var(--audiWhite), 0.2);
}

.SE_0037_AlternativeRoute .button.routeListItem .uiState {
  margin: var(--iA1) 0 var(--iA1) var(--hA1);
  height: calc(100% - var(--iA1) * 2);
  width: calc(100% - (var(--iA1) + var(--hA1)));
  top: 0px;
  left: 0px;
  z-index: 1;
}

.listContainer .UE_1009_C2Cursor {
  position: absolute;
  width: calc(100% - (var(--iA1) + var(--hA1)));
  margin: var(--iA1) 0 var(--iA1) var(--hA1);
  height: calc(100% - var(--iA1) * 2);
}

/*-------------------------------------------------------------- Configuration for asian countries----------------------------------------------------------*/

.asia .listElement .textContainer .textColumn:first-of-type {
  flex: unset;
  width: calc((100% - (298px + var(--cA1) * 2)) / 2);
}

.asia .listElement .textContainer .textColumn:last-of-type {
  max-width: unset;
  margin-right: var(--cA1);
}

.asia .listElement .textContainer .textColumn .iconText.asianConfig {
  display: flex;
  margin-bottom: var(--zA5);
}

.asia .listElement .textContainer .textColumn .iconText.asianConfig2 {
  min-width: 298px;
  width: 50%;
}

.asia .listElement .textContainer .textColumn .iconText.asianConfig2,
.asia .listElement .textContainer .textColumn .subContainer {
  overflow: hidden;
  display: flex;
}

.asia .listElement .textContainer .textColumn .subContainer {
  width: 100%;
}

.asia
  .listElement
  .textContainer
  .textColumn
  .subContainer
  .iconText:first-of-type {
  width: 50%;
  max-width: calc(100% - 298px);
}

.asia .listElement .textContainer .textColumn p {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.settings {
  position: relative;
  top: 100px;
  margin: var(--lA1);
}

.chargingTime {
  display: flex;
  margin-bottom: var(--zA1);
}

.chargingTime .time_am {
  margin-top: 5px;
}

.chargingTime .time_pm {
  margin-top: 19px;
}

.chargingTime.am_pm.am .time_am {
  display: inline-block;
}

.chargingTime.am_pm.pm .time_pm {
  display: inline-block;
}

/*-------------------------------------------------------------------- SMALL DISPLAY ----------------------------------------------------------------------*/

@media only screen and (max-width: 1768px) {
  :root {
    --smScrn0037Adj: 6px;
  }

  .SE_0037_AlternativeRoute {
    padding-right: var(--popupInsetWidth);
    margin: var(--popupInsetWidth) 0;
  }

  .listContainer {
    display: block;
    padding: 0 var(--listVerticalPadding);
    box-sizing: border-box;
  }

  .listContainer .UE_1009_C2Cursor {
    margin: calc(var(--iA1) - var(--smScrn0037Adj)) 0
      calc(var(--iA1) - var(--smScrn0037Adj)) var(--hA1);
    height: calc(100% - 2 * (var(--iA1) - var(--smScrn0037Adj)));
  }

  .listContainer .routeListItem .listElement::before {
    margin: calc(var(--iA1) - var(--smScrn0037Adj)) 0
      calc(var(--iA1) - var(--smScrn0037Adj)) var(--iA1);
  }

  .SE_0037_AlternativeRoute .button.routeListItem .uiState {
    margin: calc(var(--iA1) - var(--smScrn0037Adj)) 0
      calc(var(--iA1) - var(--smScrn0037Adj)) var(--hA1);
    height: calc(100% - 2 * (var(--iA1) - var(--smScrn0037Adj)));
  }

  .listContainer .routeListItem .listElement {
    min-height: calc(var(--minTouchSize) - var(--smScrn0037Adj));
  }
}
