/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --balanceFader_height: 578px;
  --balanceFader_width: 540px;
  --balanceFaderCursorWidth: 414px;
  --balanceFader_draggableSize: 49px;
  --balanceFaderMargin: 25px;
}

:global(#root) > .SE_0013_BalanceFader {
  margin: 0;
  width: 1678px;
  height: 554px;
}

.SE_0013_BalanceFader {
  user-select: none;
  position: absolute;
  width: 100%;
  height: 100%;
}

.balanceFader {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
}

.seatsTopView {
  position: absolute;
  width: var(--balanceFader_width);
  height: var(--balanceFader_height);
  top: 50%;
  transform: translateY(-50%);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.7;
}

.controlContainer {
  position: relative;
  width: var(--balanceFaderCursorWidth);
  height: 100%;
  overflow: hidden;
}

.xAxisContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0) 0px,
    rgba(0, 0, 0) 20px,
    rgba(0, 0, 0) calc(100% - 20px),
    rgba(0, 0, 0, 0) 100%
  );
}
.yAxisContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0px,
    rgba(0, 0, 0) 20px,
    rgba(0, 0, 0) calc(100% - 20px),
    rgba(0, 0, 0, 0) 100%
  );
}

.SE_0106_Button_with_ConnectorBody {
  position: absolute;
  left: calc(50% - (var(--balanceFader_draggableSize) / 2));
  top: calc(50% - (var(--balanceFader_draggableSize) / 2));
  transition: transform 0.15s linear;
}
.SE_0106_Button_with_ConnectorBody.resetConnecter {
  opacity: 0.5;
}

.xAxis {
  position: relative;
  left: calc(-50%);
  top: 50%;
  width: calc(var(--balanceFaderCursorWidth) * 2);
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgb(var(--audiWhite)) calc(50% - var(--balanceFader_draggableSize) / 2),
    transparent calc(50% - var(--balanceFader_draggableSize) / 2),
    transparent calc(50% + var(--balanceFader_draggableSize) / 2 + 2px),
    rgb(var(--audiWhite)) calc(50% + var(--balanceFader_draggableSize) / 2)
  );
  opacity: 0.7;
  transition: transform 0.15s linear;
}

.yAxis {
  position: relative;
  left: 50%;
  top: calc(-50% + var(--balanceFaderMargin));
  height: calc(100% * 2 - 50px);
  width: 1px;
  background-image: linear-gradient(
    to bottom,
    rgb(var(--audiWhite))
      calc(50% - var(--balanceFader_draggableSize) / 2 - 2px),
    transparent calc(50% - var(--balanceFader_draggableSize) / 2 - 2px),
    transparent calc(50% + var(--balanceFader_draggableSize) / 2),
    rgb(var(--audiWhite)) calc(50% + var(--balanceFader_draggableSize) / 2)
  );
  opacity: 0.7;
  transition: transform 0.15s linear;
}

.resetButton {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.resetButton:active .uiState {
  height: 45px;
  width: 45px;
}
.resetButton .uiState {
  height: 45px;
}

@media only screen and (max-width: 1768px) {
  :root {
    --balanceFader_height: 450px;
    --balanceFader_width: 440px;
    --balanceFaderCursorWidth: 314px;
  }

  :global(#root) > .SE_0013_BalanceFader {
    height: 427px;
  }
}

@media only screen and (min-width: 2219px) {
  :root {
    --balanceFader_height: 697px;
    --balanceFader_width: 682px;
    --balanceFaderCursorWidth: 540px;
  }

  :global(#root) > .SE_0013_BalanceFader {
    height: 697px;
  }
}
