.rightSide {
  width: 57%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.rightSide.isIndexMinusOne {
  justify-content: flex-start;
}
.rightSide.isIndexZeroOne .textWrapper {
  width: 100%;
}
.rightSide.restrictionReason {
  justify-content: center;
}

.rightSide .movingSeat,
.rightSide .movingSeatHighlight {
  width: 100%;
  height: 60%;
  margin-top: 20px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;

  pointer-events: none;
}
.movingSeat {
  background-image: url(../../img/SeatsFrontrowTopview_movingSeat.png);
}

.rightSide .movingSeatHighlight {
  background-color: rgb(var(--ambientcolor1));
  position: absolute;

  mask-size: contain;
  -webkit-mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-position: center;

  transition: opacity 0.1s ease-in-out;

  pointer-events: none;

  mask-image: url(../../img/ST_0012_SeatMenu_third-level.png);
  -webkit-mask-image: url(../../img/ST_0012_SeatMenu_third-level.png);

  display: block;
  opacity: 0.85;
}

.seatText,
.seatTextLong {
  max-width: calc(100% - 10px);
  text-align: center;
}

.seatTextWithSideButton {
  position: relative;
  max-width: calc(100% - (var(--minTouchSize) + 10px));
}

.seatTextLong {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  bottom: 2%;
  white-space: pre-wrap;
  overflow: hidden;
}

.seatTextWithBtn {
  bottom: 21%;
  -webkit-line-clamp: 3;
}
.popupButtonWrapper {
  display: flex;

  max-width: calc(100% - 10px);
  text-align: center;
  width: calc(100% - 10px);
  align-items: center;
  justify-content: center;
}
.popupButton {
  margin-left: 10px;
}

.seatsTopView {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}
.cargoButtonsContainer {
  display: flex;
  margin-bottom: 10px;
}
.cargoButtons {
  width: 250px;
}
.zoomImageContainer {
  width: 270px;
  height: 200px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0%;
  left: 55px;
}
.zoomImage {
  width: 194px;
  height: calc(100% - 6px);
  background-repeat: no-repeat;
  background-size: contain;
  border-style: solid;
  border-width: 3px;
  border-color: rgba(var(--audiGrey), 0.5);
  background-image: url(../../img/SeatPosition_manualControl.png);
}
.line {
  width: 70px;
  border-top: 3px solid rgba(var(--audiGrey), 0.5);
}

.rightSide .refreshIcon {
  position: absolute;
  left: 50%;
  top: 28%;
  transform: translate(-50%, -50%);
  height: 70px;
  width: 70px;
  background-color: rgba(var(--audiGreen), 0.5);
}
.personDetection {
  position: absolute;
  top: 19%;
  right: 20%;
  transform: translate(-50%, -50%);
  height: 9%;
  width: 6%;
  background-color: rgba(var(--audiGreen), 0.5);
}

.textWrapper {
  width: calc(100% - 200px);
}

/*--------------------------------------------- Half Seat ----------------------------------------------------*/

.halfSeat,
.blockedSeat,
.lockIcon,
.blockedSquare {
  width: 991px;
  height: 381px;
}

.halfSeat {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  pointer-events: none;
  background-image: url(./img/halfSeat.png);
}

.blockedSeat {
  background-color: rgb(var(--audiWhite));
  position: absolute;
  mask-image: url(./img/halfSeatHighlight.png);
  -webkit-mask-image: url(./img/halfSeatHighlight.png);
}

.blockedSeat.active {
  background-color: rgb(var(--ambientcolor1));
}

.lockIcon {
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  pointer-events: none;
  background-image: url(./img/lock-icon.png);
}

.blockedSquare {
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  pointer-events: none;
  background-image: url(./img/black-rectangular-_locked-.png);
}

/*-------------------------------------------------------------------- SMALL DISPLAY ----------------------------------------------------------------------*/
@media only screen and (max-width: 1768px) {
  .zoomImageContainer {
    left: 50px;
  }
}

/*-------------------------------------------------------------------- PID DISPLAY ----------------------------------------------------------------------*/
