  .squareContainer3rdRightSeat,
  .squareContainer2ndLeftSeat,
  .squareContainer2ndRightSeat {
    position: absolute;
    width: 37px;
    height: 37px;
    top: 64.5%;
    right: 23.5%;
  }
  .squareContainer3rdRightSeat {
    top: 24%;
  }

  .squareContainer2ndLeftSeat {
    right: 52%;
    top: 71%;
  }
  .squareContainer2ndRightSeat {
    top: 17%;
    right: 52%;
  }
  .squareContainer2ndRightSeat.centering,
  .squareContainer2ndLeftSeat.centering {
    right: 35%;
  }

  .seatRow3rdRightPersonBBox {
    bottom: 56%;
    right: 21%;
  }
  .seatRow3rdLeftPersonBBox {
    bottom: 16%;
    right: 21%;
  }
  .seatRow2ndRightPersonBBox {
    bottom: 9%;
    right: 49%;
  }
  .seatRow2ndLeftPersonBBox {
    bottom: 64%;
    right: 49%;
  }
  .fourSeatsRow2ndRightPersonBBox {
    bottom: 63%;
    right: 31.5%;
  }
  .fourSeatsRow2ndLeftPersonBBox {
    bottom: 9%;
    right: 31.5%;
  }
  .rightSide .refreshIcon {
    left: 51%;
    top: 30%;
  }
  .personBoundingBox {
    bottom: 53.5%;
    right: 27%;
  }
}

/*-------------------------------------------------------------------- B10 DISPLAY ----------------------------------------------------------------------*/
@media only screen and (min-width: 2219px) {
  .zoomImageContainer {
    top: 1%;
    left: 10px;
  }
}
