:global(#root) > .SE_0060_CalendarWeek {
  height: 650px;
  margin: 0;
  position: relative;
}

:global(#root) > .SE_0060_CalendarWeek .calendarWeek {
  background-color: rgb(var(--audiBlack));
}

.SE_0060_CalendarWeek {
}

.calendarWeek {
  position: relative;
  height: 100%;
  overflow: hidden;
}

.stickyHeaderStyling .button .uiState {
  margin-bottom: -4px;
  z-index: 1;
}

.calendarWeek .hourSeparator {
  position: absolute;
  width: 2px;
  background-color: rgba(var(--audiWhite), 0.2);
  height: 100%;
  left: calc(var(--cA1) + 180px - 2px);
  top: var(--minTouchSize);
}

.calendarWeek .stickyHeaderStyling {
  display: grid;
  grid-template-columns:
    [line1] calc(var(--zA4) * 3) [line2] calc(var(--zA4) * 3)
    [line4] 1fr [line3] 1fr [line4] 1fr [line5] 1fr [line6] 1fr [line7] 1fr [line8] 1fr [line9] 1fr [line10] 1fr [line11] 1fr [line12] 1fr [line13] 1fr [line14] 1fr [line15] 1fr;
  height: calc(var(--minTouchSize) - 3px);
  padding: 0 var(--cA1);
  position: sticky;
  left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
  box-sizing: border-box;
  box-shadow: 0 7px 0 rgba(var(--audiBlack), 0.8);
}

.calendarWeek .stickyHeaderStyling .day {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  box-sizing: border-box;
  grid-column-end: span 2;
  width: unset;
  height: initial;
}
.day:active .gradient,
.day:active .fading {
  height: calc(100vh - (var(--minTouchSize) - 10px));
  margin-top: -120px;
}

.day .borderTop .white {
  top: 114px;
}

.calendarWeek .stickyHeaderStyling .day.first {
  justify-content: flex-end;
  align-items: stretch;
}

.calendarWeek .stickyHeaderStyling .day .dateWrapper,
.calendarWeek .stickyHeaderStyling .day .event {
  flex: 1;
  display: flex;
  padding: 0 var(--iA1);
  align-items: center;
  margin-left: calc(var(--zA1) / 2);
  margin-right: calc(var(--zA1) / 2);
}

.calendarWeek .stickyHeaderStyling .day .event {
  margin-top: 0;
  margin-bottom: -1px;
  max-height: 45%;
}

.calendarWeek .stickyHeaderStyling .day .dateWrapper p {
  margin-top: 8px;
}

.calendarWeek .stickyHeaderStyling .day .hourLabel {
  display: flex;
  flex: 1;
  max-height: 45%;
  align-items: center;
  justify-content: flex-end;
  padding: 0 var(--iA1);
}

.calendarWeek.workWeek .stickyHeaderStyling,
.calendarWeek.workWeek .stickyDayHighlighter {
  grid-template-columns:
    [line1] calc(var(--zA4) * 3) [line2] calc(var(--zA4) * 3)
    [line4] 1fr [line3] 1fr [line4] 1fr [line5] 1fr [line6] 1fr [line7] 1fr [line8] 1fr [line9] 1fr [line10] 1fr [line11] 1fr;
}

.calendarWeek.workWeek .day.weekend {
  display: none;
}

.calendarWeek.workWeek .weekView .eventsWrapper {
  grid-template-columns:
    [line1] calc(var(--zA4) * 3) [line2] calc(var(--zA4) * 3)
    [line4] 1fr [line3] 1fr [line4] 1fr [line5] 1fr [line6] 1fr [line7] 1fr [line8] 1fr [line9] 1fr [line10] 1fr [line11] 1fr;
}

.calendarWeek .stickyHeaderStyling .day.current {
  background-image: -webkit-linear-gradient(
    bottom,
    rgba(var(--ambientcolor1), var(--uiStateGradientOpacity)) 0%,
    rgba(var(--ambientcolor1), 0) 100%
  );
}

.calendarWeek .stickyHeaderStyling .day.current:after {
  content: '';
  position: absolute;
  width: 100%;
  bottom: -4px;
  left: 0px;
  height: var(--zA1);
  background-color: rgba(var(--ambientcolor1), 1);
}

.calendarWeek .stickyHeaderStyling .day p,
.calendarWeek .weekView .event p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.calendarWeek .eventsWrapper {
  grid-template-rows: repeat(48, calc(var(--minTouchSize) / 2));
}

.calendarWeek .stickyDayHighlighter {
  top: var(--minTouchSize);
}

.calendarWeek .stickyDayHighlighter,
.calendarWeek .weekView .hoursWrapper,
.calendarWeek .weekView .eventsWrapper {
  display: grid;
  grid-template-columns:
    [line1] calc(var(--zA4) * 3) [line2] calc(var(--zA4) * 3)
    [line4] 1fr [line3] 1fr [line4] 1fr [line5] 1fr [line6] 1fr [line7] 1fr [line8] 1fr [line9] 1fr [line10] 1fr [line11] 1fr [line12] 1fr [line13] 1fr [line14] 1fr [line15] 1fr;
  padding: 0 var(--cA1);
  position: absolute;
  width: 100%;
  height: calc(100% - var(--minTouchSize));
  box-sizing: border-box;
}

.calendarWeek .stickyDayHighlighter:after {
  content: ' ';
  grid-column-start: 5;
  grid-column-end: span 2;
  background-image: -webkit-linear-gradient(
    bottom,
    rgba(var(--ambientcolor1), var(--uiStateGradientOpacity)) 0%,
    rgba(var(--ambientcolor1), 0.1) 100%
  );
  width: 100%;
  height: 100%;
  display: block;
}

.calendarWeek .weekView {
  width: 100%;
  height: calc(100% - var(--minTouchSize));
  display: grid;
  padding: 0 calc(var(--cA1) - var(--zA1)) 0 var(--cA1);
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  box-sizing: border-box;
  top: var(--minTouchSize);
}

.calendarWeek .weekView .timeLine {
  position: absolute;
  top: 1280px;
  left: var(--cA1);
  height: var(--zA2);
  width: calc(100% - (var(--cA1) * 2 - var(--zA1)));
  background-color: rgba(var(--ambientcolor1));
}

.calendarWeek .weekView::-webkit-scrollbar-track {
  display: none;
}
/*-------------------------- HOURS --------------------------*/

.calendarWeek .weekView .hoursWrapper {
  grid-template-rows: repeat(48, calc(var(--minTouchSize) / 2));
}

.calendarWeek .weekView.b12h p.h24,
.calendarWeek .weekView.b24h p.ampm,
.calendarWeek .weekView.b24h p.textPrio3 {
  display: none;
}

.calendarWeek .weekView.b24h p.h24,
.calendarWeek .weekView.b12h p.ampm,
.calendarWeek .weekView.b12h p.textPrio3 {
  display: block;
}

.calendarWeek .weekView .hour {
  position: relative;
  display: flex;
  grid-column-start: 1;
  grid-column-end: 17;
}

.calendarWeek .weekView .hour p {
  margin-top: var(--zA5);
}

.calendarWeek .weekView .hour .textPrio2 {
  width: 88px;
  text-align: right;
}

.calendarWeek .weekView .hour p.am {
  margin-top: calc(var(--zA2) * 3);
}

.calendarWeek .weekView .hour p.pm {
  margin-top: var(--zA6);
}

.calendarWeek .weekView .hour:not(:first-of-type):before {
  content: ' ';
  position: absolute;
  background-color: rgba(var(--audiWhite));
  height: 1px;
  width: calc(100% - 180px);
  left: 180px;
}
.calendarWeek .weekView .hour:after {
  content: ' ';
  position: absolute;
  background-color: rgba(var(--audiWhite), 0.2);
  height: 1px;
  width: calc(100% - 180px);
  left: 180px;
  top: 50%;
}
.calendarWeek .weekView .hour.h00 {
  grid-row-start: 1;
  grid-row-end: 3;
}

.calendarWeek .weekView .hour.h01 {
  grid-row-start: 3;
  grid-row-end: 5;
}

.calendarWeek .weekView .hour.h02 {
  grid-row-start: 5;
  grid-row-end: 7;
}
.calendarWeek .weekView .hour.h03 {
  grid-row-start: 7;
  grid-row-end: 9;
}
.calendarWeek .weekView .hour.h04 {
  grid-row-start: 9;
  grid-row-end: 11;
}
.calendarWeek .weekView .hour.h05 {
  grid-row-start: 11;
  grid-row-end: 13;
}
.calendarWeek .weekView .hour.h06 {
  grid-row-start: 13;
  grid-row-end: 15;
}
.calendarWeek .weekView .hour.h07 {
  grid-row-start: 15;
  grid-row-end: 17;
}
.calendarWeek .weekView .hour.h08 {
  grid-row-start: 17;
  grid-row-end: 19;
}
.calendarWeek .weekView .hour.h09 {
  grid-row-start: 19;
  grid-row-end: 21;
}
.calendarWeek .weekView .hour.h10 {
  grid-row-start: 21;
  grid-row-end: 23;
}
.calendarWeek .weekView .hour.h11 {
  grid-row-start: 23;
  grid-row-end: 25;
}
.calendarWeek .weekView .hour.h12 {
  grid-row-start: 25;
  grid-row-end: 27;
}
.calendarWeek .weekView .hour.h13 {
  grid-row-start: 27;
  grid-row-end: 29;
}
.calendarWeek .weekView .hour.h14 {
  grid-row-start: 29;
  grid-row-end: 31;
}
.calendarWeek .weekView .hour.h15 {
  grid-row-start: 31;
  grid-row-end: 33;
}
.calendarWeek .weekView .hour.h16 {
  grid-row-start: 33;
  grid-row-end: 35;
}
.calendarWeek .weekView .hour.h17 {
  grid-row-start: 35;
  grid-row-end: 37;
}
.calendarWeek .weekView .hour.h18 {
  grid-row-start: 37;
  grid-row-end: 39;
}
.calendarWeek .weekView .hour.h19 {
  grid-row-start: 39;
  grid-row-end: 41;
}
.calendarWeek .weekView .hour.h20 {
  grid-row-start: 41;
  grid-row-end: 43;
}
.calendarWeek .weekView .hour.h21 {
  grid-row-start: 43;
  grid-row-end: 45;
}
.calendarWeek .weekView .hour.h22 {
  grid-row-start: 45;
  grid-row-end: 47;
}
.calendarWeek .weekView .hour.h23 {
  grid-row-start: 47;
  grid-row-end: 49;
}

/*-------------------------- EVENTS --------------------------*/

.calendarWeek .event {
  margin: var(--zA1) calc(var(--zA1) / 2);
  position: relative;
  padding: var(--iA1);
  box-sizing: border-box;
  align-items: center;
  border-width: 1px 1px 1px var(--zA2);
  border-style: solid;
  border-top-color: rgba(var(--audiWhite), 0.4);
  border-bottom-color: rgba(var(--audiWhite), 0.4);
  border-right-color: rgba(var(--audiWhite), 0.4);
  background-color: rgba(var(--ambientBackground2), 0.9);
}

.calendarWeek .event.blue {
  border-left-width: 1px;
  border-left-color: rgba(var(--audiWhite), 0.4);
}

.calendarWeek .event.blue::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -2px;
  height: inherit;
  width: 3px;
  background-color: rgb(var(--calendarWeek_blue));
}

.calendarWeek .event.blueHeading {
  border-left-width: 1px;
  border-left-color: rgba(var(--audiWhite), 0.4);
}

.calendarWeek .event.blueHeading::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -2px;
  height: 102%;
  width: 3px;
  background-color: rgb(var(--calendarWeek_blue));
}

.calendarWeek .event.yellow {
  border-left-width: 1px;
  border-left-color: rgba(var(--audiWhite), 0.4);
}

.calendarWeek .event.yellow::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -2px;
  height: inherit;
  width: 3px;
  background-color: rgb(var(--calendarWeek_yellow));
}

.calendarWeek .event.grey {
  display: flex;
  border-left-color: rgb(var(--audiWhite));
  justify-content: center;
  align-items: center;
}

.calendarWeek .weekView .monday {
  grid-area: 17 / 3 / span 6 / span 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.calendarWeek .weekView .monday .event.event1 {
  width: calc(100% - var(--zA1));
  height: calc(var(--minTouchSize) - var(--zA1) * 2);
}
.calendarWeek .weekView .monday .event.event2 {
  width: calc(100% - var(--zA1));
  height: calc(var(--minTouchSize) - var(--zA1));
  margin-top: calc(var(--minTouchSize) + var(--zA1));
}

.leftEvent {
  width: calc(50% - var(--iA1));
}

.rightEvent {
  width: calc(50% - var(--iA1));
}

.rightEvent .textPrio2 {
  margin-left: var(--iA2);
}

.leftEvent::after {
  content: '';
  position: absolute;
  top: 0px;
  left: 50%;
  height: 100%;
  width: 2px;
  background-color: rgba(var(--audiWhite), 0.4);
}

.calendarWeek .weekView .tuesday {
  grid-area: 17 / 5 / span 4 / span 2;
  display: flex;
  align-items: flex-end;
}
.calendarWeek .weekView .tuesday .event.event3 {
  height: calc(var(--minTouchSize) * 2 - var(--zA1) * 2);
  flex: 1;
}
.calendarWeek .weekView .tuesday .event.event4 {
  height: calc(var(--minTouchSize) - var(--zA1) * 2);
  flex: 1;
}

.calendarWeek .weekView .wednesday {
  grid-area: 19 / 7 / span 4 / span 2;
  display: flex;
  align-items: flex-end;
}
.calendarWeek .weekView .wednesday .event.event5 {
  height: calc(var(--minTouchSize) * 2 - var(--zA1) * 2);
  flex: 1;
}
.calendarWeek .weekView .wednesday .event.event6 {
  height: calc(var(--minTouchSize) - var(--zA1) * 2);
  flex: 1;
}

.calendarWeek .weekView .thursday {
  grid-area: 17 / 9 / span 6 / span 2;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.calendarWeek .weekView .thursday .event.event7 {
  width: calc(100% - var(--zA1));
  height: calc(var(--minTouchSize) - var(--zA1) * 2);
}
.calendarWeek .weekView .thursday .event.event8 {
  width: calc(100% - var(--zA1));
  height: calc(var(--minTouchSize) - var(--zA1));
  margin-top: calc(var(--minTouchSize) + var(--zA1));
}

.calendarWeek .weekView .friday {
  grid-area: 17 / 11 / span 6 / span 2;
  display: flex;
  align-items: flex-end;
}
.calendarWeek .weekView .friday .event.event9 {
  height: calc(var(--minTouchSize) * 3 - var(--zA1) * 2);
  flex: 1;
}
.calendarWeek .weekView .friday .event.event10 {
  height: calc(var(--minTouchSize) - var(--zA1) * 2);
  flex: 1;
  margin-bottom: calc(var(--minTouchSize) / 2 + var(--zA1));
}

.calendarWeek .weekView .sunday {
  grid-area: 23 / 15 / span 1 / span 2;
  display: flex;
  align-items: flex-start;
}
.calendarWeek .weekView .sunday .event.event11 {
  height: calc(var(--minTouchSize) / 2 - var(--zA1) * 2);
  flex: 1;
}

.calendarWeek .event.halfBlue {
  border-left-width: 1px;
  border-left-color: rgba(var(--audiWhite), 0.4);
}

.calendarWeek .event.halfBlue::after {
  content: '';
  position: absolute;
  top: -1px;
  left: -2px;
  height: 67%;
  width: 3px;
  background-color: rgb(var(--calendarWeek_blue));
}
/* ------------- No Events ------------- */

.calendarWeek .noEventsAlert {
  height: calc(100% - var(--minTouchSize));
  width: 100%;
  display: none;
  position: absolute;
  top: var(--minTouchSize);
  background-color: rgba(var(--audiBlack), 0.8);
}
.calendarWeek .noEventsAlert p {
  position: absolute;
  top: var(--minTouchSize);
  left: 50%;
  transform: translate(-50%, 0%);
  max-width: 75%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.calendarWeek.noEvents .weekView .hoursWrapper .hour p,
.calendarWeek.noEvents .weekView .timeLine {
  z-index: 1;
  pointer-events: none;
}

.calendarWeek.noEvents .noEventsAlert {
  display: block;
}

.calendarWeek.noEvents .weekView .event {
  display: none;
}

.calendarWeek.blocking .blockingText {
  display: flex;
  z-index: 2;
}

.blockingText {
  height: calc(100% - var(--minTouchSize));
  width: 100%;
  display: none;
  position: absolute;
  top: var(--minTouchSize);
  background-color: rgba(var(--audiBlack), 1);
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.blockingText p {
  top: var(--minTouchSize);
  width: 100%;
  max-width: 75%;
  text-align: center;
}

.settings {
  margin: var(--iA1);
}

/* ------------- List Header ------------- */
.calendarWeek.listHeader .stickyHeaderStyling {
  top: calc(var(--minTouchSize));
}

.calendarWeek.listHeader .hourSeparator,
.calendarWeek.listHeader .stickyDayHighlighter,
.calendarWeek.listHeader .weekView,
.calendarWeek.listHeader .noEventsAlert,
.calendarWeek.listHeader .noEventsAlert p,
.calendarWeek.listHeader .blockingText,
.calendarWeek.listHeader .blockingText p {
  top: calc(var(--minTouchSize) * 2);
}

.calendarWeek.listHeader .noEventsAlert p {
  z-index: 1;
}

.header {
  position: absolute;
  top: 0;
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
  border-bottom: 0px;
}

.today {
  min-width: fit-content;
  margin: 0 var(--cA1);
}

.buttonContainer {
  height: 100%;
  display: flex;
  flex: 1;
  justify-content: flex-end;
  overflow: hidden;
}

.buttonContainer .button {
  height: 100%;
}

.buttonContainer .button div:last-of-type {
  overflow: hidden;
}

.kw,
.extensibleBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: calc(100% - 500px);
}

.extensibleBtn {
  width: unset;
  min-width: var(--minTouchSize);
}

.kw {
  width: 250px;
  min-width: 250px;
}

.buttonContainer .button p {
  margin: 0 var(--textButtonMargin);
  position: relative;
}
