:root {
  --airQualitySegmentWidth: 298px;
  --airQualitySegmentHeight: 16px;
  --firstColumnTextWidth: 145px;
  --aqiCurrentWidth: 50%;
  --aqiColor: var(--airQualityColor5);
}

:global(#root) > .T_5304_AQI {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: calc(var(--cardContentWidthGB) - 6px);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}

.airQuality {
  height: 100%;
  width: 100%;
  position: absolute;
}

.headlinePM {
  position: absolute;
  width: calc(100% - var(--iA1) * 2);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  top: 15px;
  left: var(--iA1);
}

.headlinePM p {
  position: relative;
  right: 50px;
}

.insideValue,
.outsideValue {
  width: calc(100% - 45px);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 75px;
  position: absolute;
  left: 45px;
  transition: left var(--animationDuration) var(--animationCurve),
    width var(--animationDuration) var(--animationCurve);
}
.insideValue {
  top: 50px;
}
.outsideValue {
  top: 150px;
}

.firstColumn {
  display: flex;
  justify-content: flex-end;
  margin-right: 35px;
  width: var(--firstColumnTextWidth);
}

.secondColumn {
  display: flex;
  justify-content: flex-start;
  width: calc(100% - var(--firstColumnTextWidth) - 35px);
}

.statusText {
  display: block;
  width: calc(100% - var(--iA1));
}

/*----------Segment section--------------*/
.T_5304_AQI .airQuality .UE_6076_AQI_Bargraph {
  position: relative;
  height: 50px;
  top: -3px;
}

.T_5304_AQI .airQuality .UE_6076_AQI_Bargraph .segmentText {
  top: 0;
}

/*-------------------- segmentOverlay --------------------*/
.T_5304_AQI .airQuality .UE_6076_AQI_Bargraph .segmentOverlayMask {
  position: relative;
  /* width: 100%; */
  height: var(--airQualitySegmentHeight);
  top: 20px;
  left: 2px;
  display: block;
}

/*-------------------- chargingGlow --------------------*/
.T_5304_AQI .airQuality .UE_6076_AQI_Bargraph .chargingGlowContainer {
  display: block;
  position: absolute;
  height: 100%;
  top: 3px;
  left: 2px;
  overflow: hidden;
}
.settings {
  position: absolute;
  top: 300px;
  left: 20px;
  width: 1200px;
}
