:global(#root) > .T_5303_ScrollableText {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthGB);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}

.scrollableText {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}

.scrollbarWithTrack::-webkit-scrollbar-track {
  border: 1px solid rgb(var(--scrollbarTrackColor));
  background-clip: content-box;
  margin-top: 4px;
  margin-bottom: 4px;
}

.scrollableText p {
  margin-left: var(--zA4);
  margin-right: var(--zA4);
  width: calc(100% - 2 * var(--zA4));
}

.scrollableText p:first-child {
  margin-top: var(--zA1);
}

.scrollableText p:last-child {
  margin-bottom: 7px;
}
