/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --loginScreenPadding: calc(var(--iA1) + var(--lA1) + 9px);
}

:global(#root) > .T_0111_LoginScreen {
  height: 956px;
  width: 1920px;
  position: absolute;
  background-color: black;
}

.loginScreen {
  position: absolute;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 0 var(--loginScreenPadding) var(--minTouchSize)
    var(--loginScreenPadding);
}

/*---------------------- TEXT AND BUTTON -------------------*/
.textButtonWrapper {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.textContainer {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2px 0 3px 0;
}
.textContainer .textPrio2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.textContainer + .UE_0101_ActionButton {
  position: relative;
  display: flex;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  align-self: center;
  margin-left: var(--cA1);
}

/*---------------------- INPUTFIELDS -------------------*/
.loginContainer {
  display: flex;
  flex-direction: row;
  height: var(--minTouchSize);
  width: 100%;
  justify-content: center;
}
.UE_0501_InputField {
  height: var(--minTouchSize);
  flex: 1;
  max-width: 100%;
}
.loginContainer .UE_0101_ActionButton {
  margin-left: var(--cA1);
}
.loginScreen .UE_0101_ActionButton {
  width: auto;
  min-width: var(--minTouchSize);
}
.loginContainer .UE_0101_ActionButton,
.UE_0202_FloatingContainer
  .floatingContainer
  .buttonWrapper
  .UE_0101_ActionButton {
  position: relative;
  width: auto;
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
}

/*---------------------- FLOATINGCONTAINER BUTTONS -------------------*/
.UE_0202_FloatingContainer .floatingContainer .buttonWrapper {
  display: flex;
  width: fit-content;
}

/*---------------------- KEYBOARD VISIBLE -------------------*/
.keyboardVisible .T_0111_LoginScreen .loginScreen,
.T_0111_LoginScreen.keyboardVisible .loginScreen {
  padding: 0 var(--loginScreenPadding)
    calc(
      var(--inputMethodsHeight) - var(--climateBarHeight) -
        var(--dashboardMarginBottom)
    )
    var(--loginScreenPadding);
}
.keyboardVisible .T_0111_LoginScreen .loginScreen .textButtonWrapper,
.keyboardVisible .T_0111_LoginScreen .loginScreen .UE_0202_FloatingContainer {
  display: none;
}

/*---------------------- NUMPAD VISIBLE -------------------*/
.T_0111_LoginScreen.numPadVisible .loginScreen {
  padding: 0 var(--loginScreenPadding)
    calc(
      var(--inputMethodsReducedHeight) - var(--climateBarHeight) -
        var(--dashboardMarginBottom)
    )
    var(--loginScreenPadding);
}
.T_0111_LoginScreen.numPadVisible .loginScreen .textButtonWrapper,
.T_0111_LoginScreen.numPadVisible .loginScreen .UE_0202_FloatingContainer {
  display: none;
}
/*---------------------- SETTINGS -------------------*/
.settings {
  position: absolute;
  top: 1000px;
}
