/* CLASSIFICATION: CONFIDENTIAL */
:global(#root) > .UE_0202_FloatingContainer {
  position: absolute;
  background-color: rgb(var(--audiBlack));
  height: 800px;
  width: 1600px;
  margin: 0;
}

:global(#root) > .UE_0202_FloatingContainer .floatingContainer {
  background-color: rgba(var(--boundingBoxGreen));
}

.UE_0202_FloatingContainer {
}

/*------------ FLOATING CONTAINER ------------*/
.floatingContainer {
  position: absolute;
  width: auto;
  height: auto;
  min-height: calc(var(--minTouchSize));
  min-width: calc(var(--minTouchSize));
}

.floatingContainer.top,
.floatingContainer.bottom {
  max-height: var(--minTouchSize);
}
.floatingContainer.right,
.floatingContainer.left {
  max-width: var(--minTouchSize);
}

/*------------ POSITION TOP ------------*/
.floatingContainer.top {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

/*------------ POSITION RIGHT ------------*/
.floatingContainer.right {
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
}

/*------------ POSITION BOTTOM ------------*/
.floatingContainer.bottom {
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
}

/*------------ POSITION LEFT ------------*/
.floatingContainer.left {
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

/*------------ COMBINED POSITIONS ------------*/
.floatingContainer.bottom.right,
.floatingContainer.bottom.left,
.floatingContainer.top.right,
.floatingContainer.top.left {
  transform: none;
  max-height: none;
}
.floatingContainer.top.right,
.floatingContainer.top.left {
  top: 0;
  bottom: auto;
}
.floatingContainer.bottom.right,
.floatingContainer.bottom.left {
  top: auto;
}

.floatingContainer.top.right,
.floatingContainer.bottom.right {
  left: auto;
}

.UE_1202_DashboardTile .floatingContainer.top.right,
.UE_1202_DashboardTile .floatingContainer.bottom.right {
  right: 0px;
}

.floatingContainer.top.left,
.floatingContainer.bottom.left {
  left: 0;
  right: auto;
}

.UE_1202_DashboardTile .floatingContainer.top.left,
.UE_1202_DashboardTile .floatingContainer.bottom.left {
  left: 0px;
  right: auto;
}

/*------------ FULLWIDTH ------------*/
.floatingContainer.fullWidth {
  width: 100%;
  right: 0;
  left: 0px;
  transform: none;
}

.floatingContainer.top.fullWidth {
  width: 100%;
  right: 0;
  left: 0px;
  top: 0px;
  transform: none;
}

/*------------ FULLHEIGHT ------------*/
.floatingContainer.right.fullHeight {
  height: calc(100% - 5px);
  top: 0px;
  right: 0px;
  bottom: 0;
  transform: none;
}

.floatingContainer.left.fullHeight {
  height: calc(100% - 5px);
  top: 0px;
  left: 0px;
  bottom: 0;
  transform: none;
}

/*------------ SETTINGS ------------*/
.settings {
  position: absolute;
  bottom: 100px;
  width: 600px;
}
