:root {
  --inputMethodsLongPressExtensionKeyWidth: 123px;
}

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.A_Extension .button,
.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension .button {
  width: var(--inputMethodsLongPressExtensionKeyWidth) !important;
}

.T_0112_InputMethods {
  position: absolute;
  opacity: 0;
  width: calc(100% - (var(--directAccessWidth)));
  height: fit-content;
  right: 0px;
  bottom: -280px;
  transition: var(--AG_0303_ScaleResize), var(--AG_0202_FadeOut),
    var(--AG_0101_Move);
  z-index: 4;
  pointer-events: none;
}

.T_0112_InputMethods.active {
  opacity: 1;
  transition: var(--AG_0303_ScaleResize), var(--AG_0201_FadeIn),
    var(--AG_0101_Move);
  transition-delay: 0.1s;
  bottom: -1px;
  pointer-events: initial;
}

/* .T_0112_InputMethods.oneInfotainment.active {
  bottom: 289px;
} */

/* .T_0112_InputMethods.reducedHeight {
  height: var(--inputMethodsReducedHeight);
} */

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.A_Extension {
  top: -70px;
  left: 6px;
}

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension {
  top: -162px;
}
.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.A_Extension.twoLines {
  top: -188px;
}

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension.twoLines {
  top: -282px;
}

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.A_Extension .button p,
.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension .button p {
  margin: unset;
}

.UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension {
  right: 251px;
}

@media only screen and (max-width: 1768px) {
  :root {
    --inputMethodsLongPressExtensionKeyWidth: 112px;
  }

  .UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.A_Extension .button,
  .UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension .button {
    min-width: unset;
  }

  .UE_0502_Keyboard .keyboard .SE_0081_LongPressExtension.O_Extension {
    right: 230px;
  }
}

/*-------------------------------------------------------------------- B10 DISPLAY ----------------------------------------------------------------------*/
@media only screen and (min-width: 2219px) {
  .T_0112_InputMethods {
    width: calc(100% - (var(--directAccessWidth)));
    z-index: 11;
  }
}
