/* CLASSIFICATION: CONFIDENTIAL */

.dashboard div.UE_1202_DashboardTile .detailContent {
  transition: var(--AG_0202_FadeOut);
  opacity: 0;
}
.dashboard div.UE_1202_DashboardTile.detailView .detailContent {
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.2s;
  opacity: 1;
}

.dashboard div.UE_1202_DashboardTile {
  opacity: 1;
  transition: var(--AG_0201_FadeIn), var(--AG_0303_ScaleResize);
}

.T_0101_Dashboard .dashboard div.UE_1202_DashboardTile.detailView#leftTile,
.T_0101_Dashboard .dashboard div.UE_1202_DashboardTile.detailView#centerTile,
.T_0101_Dashboard .dashboard div.UE_1202_DashboardTile.detailView#rightTile {
  transition: var(--AG_0303_ScaleResize);
}

/* Push away unselected Tiles*/
/* left tile selected others pushed to right*/
.T_0101_Dashboard .dashboard.leftDetail div.UE_1202_DashboardTile#centerTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(var(--dashboardWidth) + var(--dashboardTileSpacing));
  opacity: 0;
}
.T_0101_Dashboard .dashboard.leftDetail div.UE_1202_DashboardTile#rightTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(
    var(--dashboardWidth) + (var(--dashboardTileSpacing) * 2) + var(--tileWidth)
  );
  opacity: 0;
}

/* center tile selected left tile pushed to left, right tile pushed to right*/
.T_0101_Dashboard .dashboard.centerDetail div.UE_1202_DashboardTile#leftTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * -1);
  opacity: 0;
}
.T_0101_Dashboard .dashboard.centerDetail div.UE_1202_DashboardTile#rightTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(var(--tileWidth) * 3 + var(--dashboardTileSpacing) * 3);
  opacity: 0;
}

/* right tile selected others pushed to left*/
.T_0101_Dashboard .dashboard.rightDetail div.UE_1202_DashboardTile#leftTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(
    ((var(--tileWidth) * 2) + (var(--dashboardTileSpacing) * 2)) * -1
  );
  opacity: 0;
}
.T_0101_Dashboard .dashboard.rightDetail div.UE_1202_DashboardTile#centerTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * -1);
  opacity: 0;
}
