/*-----------------------Popup--------------------*/

.popupContainer {
  width: 100%;
  height: 100%;
}
.popupWrapper {
  position: absolute;
  left: 0px;
  top: -119px;
  width: calc(var(--dashboardWidth) - (var(--popupOutlineWidth)));
  height: calc(var(--dashboardHeight) - (var(--popupOutlineWidth)));
  z-index: 2;
}
.splitContainer {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  flex-grow: 2;
}

.seatMenuTitleLine {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.seatMenuTitleLine .button.close {
  margin-left: 2px;
}
.seatMenuTitleLine .button.right {
  margin-right: 2px;
}
.seatMenuTitleLine > .headline {
  position: relative;
  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  width: calc(100% - (2 * var(--minTouchSize) + 2 * var(--lA1)));
}

.seatMenuPopupContent {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: var(--popupInsetWidth);
  width: calc(100% - var(--popupInsetWidth));
  overflow: hidden;
  box-sizing: border-box;
  padding: var(--iA3);
}
.seatMenuPopupContentContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(100% - 2 * var(--lA1));
  height: 100%;
  margin: 0 var(--lA1) 0 var(--lA1);
}

.seatMenuContent {
  display: flex;
  height: 100%;
  width: 100%;
  gap: 20px;
}
.seatMenuContent .seatMenuLeft {
  width: 52%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.seatMenuContent .seatMenuRight {
  width: 48%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.paraOne {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;

  white-space: pre-wrap;
  overflow: hidden;
}

.carDec {
  width: 95%;
  height: 100%;
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
.popupPersonHighlight,
.popupObjectHighlight {
  background-color: rgba(var(--audiBlack), 1);
  width: 95%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;

  pointer-events: none;
  mask-size: contain;
  -webkit-mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-position: center;

  mask-image: url(../../../../img/SeatPosition_3row_right_person.png);
  -webkit-mask-image: url(../../../../img/SeatPosition_3row_right_person.png);
}

/* ---- person bounding boxes ----- */
.personBBox {
  position: absolute;
  width: 40px;
  height: 40px;
  position: absolute;

  transform: translate(-50%, -50%);
  background-color: rgba(var(--audiWhite), 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}
.seatRow3rdRightPersonBBox {
  bottom: 59%;
  right: 16%;
}
.seatRow3rdLeftPersonBBox {
  bottom: 18%;
  right: 16%;
}
.seatRow2ndRightPersonBBox {
  bottom: 11.5%;
  right: 50%;
}
.seatRow2ndLeftPersonBBox {
  bottom: 66%;
  right: 50%;
}
.fourSeatsRow2ndRightPersonBBox {
  bottom: 66%;
  right: 32.5%;
}
.fourSeatsRow2ndLeftPersonBBox {
  bottom: 11%;
  right: 32.5%;
}
.personBoundingBox {
  width: 40px;
  height: 40px;
  position: absolute;
  bottom: 56.5%;
  right: 21%;
  transform: translate(-50%, -50%);
  background-color: rgba(var(--audiWhite), 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
}
.popupObjectHighlight,
.objectHighlight {
  background-color: rgba(var(--audiWhite), 1);
  mask-image: url(../../../../img/SeatPosition_3row_right_obstacle.png);
  -webkit-mask-image: url(../../../../img/SeatPosition_3row_right_obstacle.png);
}
.popupPersonHighlight,
.personHighlight {
  background-color: rgba(var(--audiBlack), 1);
  mask-image: url(../../../../img/SeatPosition_3row_right_person.png);
  -webkit-mask-image: url(../../../../img/SeatPosition_3row_right_person.png);
}

.carDecWrapper {
  position: relative;
  width: 680px;
  height: 338px;
  margin-top: 20px;
}
.textWrapper {
  width: calc(100% - 200px);
}

@media only screen and (max-width: 1920px) {
  .personBoundingBox {
    bottom: 53.5%;
    right: 27%;
  }
}

/***** UTILITY CLASSES *****/
.centerElement {
  margin: auto 0;
  width: 100%;
}
