/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --pwmPPELeftBarWidth: 197px;
  --pwmPPERightBarWidth: 204px;
  --pwmPPEBarCenterGap: 3px;
}

:global(#root) > .UE_6010_Powermeter_PPE_klein {
}

.UE_6010_Powermeter_PPE_klein {
  position: absolute;
  width: calc(
    var(--pwmPPELeftBarWidth) + var(--pwmPPEBarCenterGap) +
      var(--pwmPPERightBarWidth) + 1px
  );
  height: 70px;
}

/*--------------------------- Labels ----------------------------------*/

.powermeterKlein .valueLabels {
  position: absolute;
  top: 5px;
  width: 100%;
}

.valueLabels .maxLbl {
  position: absolute;
  top: 6px;
  background-color: rgb(var(--audiWhite));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/MAXlbl.png);
  width: 52px;
  height: 14px;
}

.valueLabels .lblLeft {
  left: 0px;
}
.valueLabels .lblRight {
  right: 1px;
}

.powermeterKlein .textLabels {
  position: absolute;
  top: 48px;
  width: 100%;
}

.textLabels .chargeLbl {
  position: absolute;
  top: 5px;
  background-color: rgb(var(--fpkUnit));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/CHARGElbl.png);
  width: 80px;
  height: 14px;
}
.textLabels .lblLeft {
  left: 58px;
}

.textLabels .powerLbl {
  position: absolute;
  top: 5px;
  background-color: rgb(var(--fpkUnit));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/POWERlbl.png);
  width: 74px;
  height: 14px;
}
.textLabels .lblRight {
  left: 264px;
}

.valueLabels .SpritesheetDigits {
  position: absolute;
  top: 5px;
  left: calc(var(--pwmPPELeftBarWidth) + var(--pwmPPEBarCenterGap) - 11px);
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6010_Powermeter_PPE_klein.UE_6010_00_state_1
  .powermeterKlein
  .valueLabels
  .maxLbl,
.UE_6010_Powermeter_PPE_klein.UE_6010_00_state_1
  .powermeterKlein
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6010_Powermeter_PPE_klein.UE_6010_00_state_1
  .powermeterKlein
  .valueLabels
  .SpritesheetDigits
  .percent,
.UE_6010_Powermeter_PPE_klein.UE_6010_00_state_1
  .powermeterKlein
  .textLabels
  > *,
.UE_6010_Powermeter_PPE_klein.chargeReduced
  .powermeterKlein
  .valueLabels
  .maxLbl.lblLeft,
.UE_6010_Powermeter_PPE_klein.throttleReduced
  .powermeterKlein
  .valueLabels
  .maxLbl.lblRight {
  background-color: rgb(var(--bargraphInactive));
}

/*--------------------------- powermeter ----------------------------------*/

.powermeterKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.powermeterKlein .leftbar {
  position: absolute;
  width: var(--pwmPPELeftBarWidth);
  top: 34px;
  left: -1px;
}

.powermeterKlein .rightbar {
  position: absolute;
  width: calc(var(--pwmPPERightBarWidth));
  top: 34px;
  left: calc(var(--pwmPPELeftBarWidth) + var(--pwmPPEBarCenterGap) - 1px);
}

/*---------- Config --------*/

.pwrConfig {
  position: absolute;
  margin-top: 100px;
  width: 100%;
  color: white;
}

.sliderDemo {
  margin-top: 100px;
  width: var(--gaugeBarWidth);
  color: white;
}

.sliderDemo .pwrslider {
  width: 100%;
}

.ThrottleLimitedDemo {
  color: white;
}

.RecuLimitedDemo {
  color: white;
}

.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
