:root {
  --airQualitySegmentWidth: 298px;
  --airQualitySegmentHeight: 16px;
  --aqiColor: var(--airQualityColor5);
  --aqiCurrentWidth: 150px;
}

:global(#root) > .UE_6076_AQI_Bargraph {
  width: 1000px;
  height: 200px;
  padding: 30px;
}

.AQIBargraph {
  width: 300px;
  height: 50px;
}

/*-------------------- segment --------------------*/
.segment {
  width: var(--airQualitySegmentWidth);
  height: var(--airQualitySegmentHeight);
  border: 2px solid rgba(var(--audiBlack), 0.3);
  margin-right: 1px;
  margin-top: 2px;
}
.segment .filling {
  position: relative;
  width: 100%;
  height: 100%;
}
.segment .filling.primary {
  background-color: rgba(var(--audiWhite), 0.15);
}

/*-------------------- chargingGlow --------------------*/

.chargingGlowContainer {
  position: absolute;
  height: calc(var(--airQualitySegmentHeight) * 4);
  left: 33px;
  overflow: hidden;
  width: var(--aqiCurrentWidth);
  top: 16px;
}

.chargingGlowContainer .chargingGlow {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    to right,
    transparent 0%,
    rgb(var(--aqiColor)) 100%
  );
  left: 0;
  top: 0%;
  opacity: 0.2;
}

/*-------------------- segmentOverlay --------------------*/
.segmentOverlayMask {
  position: absolute;
  width: var(--aqiCurrentWidth);
  height: var(--airQualitySegmentHeight);
  top: 34px;
  left: 32px;
}
.segmentOverlayMask .segmentOverlay {
  position: absolute;
  width: var(--aqiCurrentWidth);
  height: 100%;
  top: 0;
  opacity: 0.7;
  background-color: rgb(var(--aqiColor));
}

.segmentText {
  display: flex;
  flex-direction: row;
  width: fit-content;
  position: relative;
  top: -20px;
  left: -8px;
}

.segmentText p {
  position: relative;
}
