/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --myEtronTileWheelSize: 188px;
  --myEtronTileFlowWidth: 550px;
  --myEtronTileFlowHeight: 230px;
  --myEtronTileGradientHeight: 120px;
  --myEtronTileGradientPosition: 75px;
  --myEtronTileImageOffset: 60px;
  --myEtronFlowVerticalOffset: (
      var(--myEtronTileFlowHeight) - var(--myEtronTileWheelSize)
    ) / 2;
}

:global(#root) > .SE_0041_myEtron_DashboardTile {
  background: rgb(var(--bodyColorGrey));
  position: absolute;
  height: 697px;
  width: 590px;
  margin: 0;
  overflow: hidden;
}

.SE_0041_myEtron_DashboardTile {
}

.myEtron_DashboardTile {
  position: absolute;
  height: 100%;
  width: 100%;
}

.infoContainer {
  width: 100%;
  height: 48px;
  margin-top: 45px;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  justify-content: center;
}

.info {
  margin: var(--hA1) 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.info:first-of-type {
  position: relative;
  width: 242px;
}
.info:first-of-type .listIcon {
  margin-bottom: 8px;
}

.info:nth-of-type(2) {
  width: 160px;
  margin-left: 20px;
}

.info p {
  margin-left: var(--zA8);
}

.info .textPrio2 {
  margin-top: 7px;
}

.switchContainer {
  display: flex;
  height: var(--minTouchSize);
  width: 100%;
  flex-shrink: 0;
  position: absolute;
  bottom: 0;
}

.UE_1103_Switch {
  width: var(--minTouchSize);
  margin-left: auto;
  flex-shrink: 0;
}

.switchContainer p {
  align-self: center;
  margin-left: var(--textButtonMargin);
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
}

.imageContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  bottom: 0;
}

.imageContainer .gradient {
  position: absolute;
  bottom: var(--myEtronTileGradientPosition);
  height: var(--myEtronTileGradientHeight);
  width: 100%;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0.7),
    transparent 100%
  );
}

.pidScreen .imageContainer .gradient {
  bottom: 90px;
}

/*-------------------------- CAR DECORATOR -------------------------*/
.imagesContainer {
  height: 100%;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  top: 0;
}

.imagesContainer .decoratorCarColor {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

/* Front + Heck */
.imagesContainer .decoratorCar,
.imagesContainer .decoratorWheels,
.imagesContainer .decoratorCarColor {
  position: absolute;
  width: 540px;
  height: 390px;
  left: 25px;
  top: 185px;
  bottom: auto;
  pointer-events: none;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.pidScreen .imagesContainer .decoratorCar,
.pidScreen .imagesContainer .decoratorWheels,
.pidScreen .imagesContainer .decoratorCarColor {
  top: 142px;
}
.energyFlow {
  position: absolute;
  background-image: url(../img/energyFlow.png);
  width: 240px;
  height: 125px;
  top: 370px;
  left: 77px;
  background-size: contain;
  background-repeat: no-repeat;
}

.energyFlow.right {
  left: 380px;
}

.pidScreen .energyFlow {
  top: 339px;
}

/* 3D EnergyFlow */
.carDecorator,
.energyFlowTilePlaceholder,
.bodyLights,
.carDecoratorColor,
.carWheels {
  position: absolute;
  width: 760px;
  height: 420px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  left: -100px;
  top: 170px;
}
.carWheels {
  z-index: 1;
}
.carDecoratorColor {
  background-color: rgb(var(--ambientcolor1));
  background-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  background-image: unset;
}

/*-------------------------- AU436_1_LWB -------------------------*/
.AU436_1 .myEtron_DashboardTile .energyFlow.right {
  left: 409px;
}

/*-------------------------- AU401 / AU402 -------------------------*/
.AU401_0 .myEtron_DashboardTile .energyFlow,
.AU401_0_LWB .myEtron_DashboardTile .energyFlow,
.AU402_0 .myEtron_DashboardTile .energyFlow {
  transform: scale(0.9);
  left: 76px;
  top: 383px;
}
.AU401_0_LWB .myEtron_DashboardTile .energyFlow {
  left: 72px;
}

.AU401_0 .myEtron_DashboardTile .energyFlow.right,
.AU401_0_LWB .myEtron_DashboardTile .energyFlow.right,
.AU402_0 .myEtron_DashboardTile .energyFlow.right {
  left: 386px;
}
.AU401_0_LWB .myEtron_DashboardTile .energyFlow.right {
  left: 389px;
}

/*-------------------------- AU512 / AU513 -------------------------*/
.AU512_4 .myEtron_DashboardTile .energyFlow,
.AU513_4 .myEtron_DashboardTile .energyFlow {
  transform: scale(0.92);
  left: 81px;
  top: 383px;
}
.AU512_4 .myEtron_DashboardTile .energyFlow.right,
.AU513_4 .myEtron_DashboardTile .energyFlow.right {
  left: 386px;
}

/*-------------------------- AU591 / AU592 -------------------------*/
.AU591_0 .myEtron_DashboardTile .energyFlow,
.AU592_0_Allroad .myEtron_DashboardTile .energyFlow,
.AU592_0 .myEtron_DashboardTile .energyFlow {
  transform: scale(0.92);
  left: 72px;
  top: 381px;
}
.AU591_0 .myEtron_DashboardTile .energyFlow.right,
.AU592_0_Allroad .myEtron_DashboardTile .energyFlow.right,
.AU592_0 .myEtron_DashboardTile .energyFlow.right {
  left: 387px;
}

/*-------------------------- AU336 -------------------------*/
.AU336_0 .myEtron_DashboardTile .energyFlow,
.AU336_1 .myEtron_DashboardTile .energyFlow {
  left: 104px;
  top: 390px;
}
.AU336_0 .myEtron_DashboardTile .energyFlow.right,
.AU336_1 .myEtron_DashboardTile .energyFlow.right {
  left: 400px;
}

/*-------hybridMode------*/
.myEtron_DashboardTile.hybridMode .info:first-of-type .listIcon {
  width: calc(2 * var(--listIconSize));
  background-color: rgb(var(--boundingBoxGreen));
  background-image: none;
}

.settings {
  position: absolute;
  top: 800px;
  width: 1000px;
}

@media only screen and (max-width: 1768px) {
  .infoContainer {
    margin-top: 30px;
  }
}

@media only screen and (min-width: 2219px) {
  :root {
    --myEtronTileWheelSize: 200px;
    --myEtronTileFlowWidth: 585px;
    --myEtronTileFlowHeight: 245px;
    --myEtronTileImageOffset: 0px;
    --myEtronTileGradientPosition: 145px;
  }
}
