/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --lowResMenuWidth: 685px;
  --displayMenuWidth: 1800px;
  --displayMenuHeight: 578px;
  --sliderWidth: 1150px;
}

:global(#root) > .ST_0007_DisplayMenu {
  position: absolute;
  width: var(--displayMenuWidth);
  height: var(--displayMenuHeight);
  margin: 0;
}

.ST_0007_DisplayMenu > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.ST_0007_DisplayMenu > .darkBackground {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(var(--audiBlack), 0.5);
}

.ST_0007_DisplayMenu .displayMenu {
  height: 100%;
  width: 100%;
  position: absolute;
}

.defaultDashboard {
}

.ST_0007_DisplayMenu .displayMenu .optionContainer {
  position: absolute;
  height: fit-content;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button .btnContainerText {
  z-index: 1;
  max-width: 100%;
}

.button .btnContainerText {
  z-index: 1;
  max-width: 100%;
}

.ST_0007_DisplayMenu
  .displayMenu
  .optionContainer
  .SE_0106_Button_with_ConnectorBody {
  position: relative;
  width: 37px;
  height: 37px;
}
.ST_0007_DisplayMenu .displayMenu .optionContainer .touchArea {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 255, 0, 0.3);
}

.ST_0007_DisplayMenu .displayMenu .lowResMenu {
  display: none;
}

/*------------------ DECORATOR ------------------*/
.ST_0007_DisplayMenu .displayMenu .decorator {
  position: absolute;
  width: 100%;
  height: 555px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

/*------------------ HEADUP DISPLAY ------------------*/
.ST_0007_DisplayMenu .displayMenu .optionContainer.headUpDisplay {
  left: 528px;
  top: 0px;
  width: 160px;
  height: var(--minTouchSize);
}

/*------------------ FPK DISPLAY ------------------*/
.ST_0007_DisplayMenu .displayMenu .optionContainer.fpkDisplay {
  left: 478px;
  top: 130px;
  width: 260px;
  height: var(--minTouchSize);
}
.ST_0007_DisplayMenu
  .displayMenu
  .optionContainer.fpkDisplay
  .SE_0106_Button_with_Connector {
  transform: translateY(-30px);
}

/*------------------ CENTER DISPLAY ------------------*/
.ST_0007_DisplayMenu .displayMenu .optionContainer.centralDisplay {
  left: 760px;
  top: 120px;
  width: 260px;
  height: 130px;
}
.AU336_0 .displayMenu .optionContainer.centralDisplay,
.AU336_1 .displayMenu .optionContainer.centralDisplay {
  top: 110px;
}

/*------------------ PID DISPLAY ------------------*/
.ST_0007_DisplayMenu .displayMenu .optionContainer.pidDisplay {
  left: 1060px;
  top: 155px;
  width: 260px;
  height: var(--minTouchSize);
}
.AU436_0 .displayMenu .optionContainer.pidDisplay,
.AU436_1 .displayMenu .optionContainer.pidDisplay {
  top: 150px;
}

/*------------------ VIRTUAL MIRROR DISPLAY ------------------*/
.ST_0007_DisplayMenu .displayMenu .optionContainer.virtualMirrorDisplay {
  left: 220px;
  top: 130px;
  width: 160px;
  height: var(--minTouchSize);
}
.AU416_2 .displayMenu .optionContainer.virtualMirrorDisplay,
.AU416_3 .displayMenu .optionContainer.virtualMirrorDisplay,
.AU416_2_RS .displayMenu .optionContainer.virtualMirrorDisplay,
.AU416_3_RS .displayMenu .optionContainer.virtualMirrorDisplay {
  left: 240px;
}

/*------------------ SLIDER ------------------*/
.ST_0007_DisplayMenu .displayMenu .UE_0601_Slider {
  position: absolute;
  width: var(--sliderWidth);
  height: var(--minTouchSize);
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.ST_0007_DisplayMenu .displayMenu .UE_0601_Slider .absoluteSlider {
  position: static;
}

/*------------------ SMALL DISPLAY ------------------*/
@media only screen and (max-width: 1768px) {
  :root {
    --displayMenuWidth: 1648px;
    --displayMenuHeight: 450px;
  }

  :global(#root) > .ST_0007_DisplayMenu {
    width: var(--displayMenuWidth);
    height: var(--displayMenuHeight);
  }

  .ST_0007_DisplayMenu .displayMenu .optionContainer,
  .ST_0007_DisplayMenu.virtualMirror
    .displayMenu
    .optionContainer.virtualMirrorDisplay,
  .ST_0007_DisplayMenu .displayMenu .lowResMenu .virtualMirrorDisplay {
    display: none;
  }

  .ST_0007_DisplayMenu .displayMenu .decorator {
    width: calc(100% - 570px);
    height: 100%;
    top: -30px;
    left: 630px;
    background-size: contain;
  }

  .ST_0007_DisplayMenu .displayMenu .floatingContainer.bottom {
    left: calc((var(--displayMenuWidth) + (var(--lowResMenuWidth))) / 2);
  }

  .ST_0007_DisplayMenu .displayMenu .lowResMenu {
    display: flex;
    flex-direction: column;
    width: var(--lowResMenuWidth);
    height: 100%;
    justify-content: center;
  }

  .ST_0007_DisplayMenu .displayMenu .lowResMenu::-webkit-scrollbar-track {
    margin: var(--listVerticalPadding) 0;
  }

  .ST_0007_DisplayMenu .displayMenu .lowResMenu .actionButtonWrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .ST_0007_DisplayMenu .displayMenu .lowResMenu .T_0103_ListVertical {
    position: relative;
    width: 100%;
    height: 100%;
  }

  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .coverContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .iconContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .multiLineContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .buttonContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .loaderContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .textColumn:not(:first-of-type),
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard:not(:first-child)
    .listElement
    .controlContainer,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard
    .listElement
    .textContainer
    .textColumn
    .col1Subtext,
  .ST_0007_DisplayMenu:not(.virtualMirror)
    .displayMenu
    .lowResMenu
    .UE_1002_ListItemStandard:last-child,
  .ST_0007_DisplayMenu
    .displayMenu
    .lowResMenu
    .T_0103_ListVertical
    .list
    > :not(.UE_1002_ListItemStandard) {
    display: none !important;
  }
}

.settings {
  position: absolute;
  top: 700px;
  width: 800px;
}

.ST_0007_DisplayMenu .settings input[type='checkbox'] {
  position: relative;
  margin-left: 0px;
  margin-right: 60px;
}

@media only screen and (min-width: 2219px) {
  :global(#root) > .ST_0007_DisplayMenu {
    height: 697px;
  }
  .ST_0007_DisplayMenu .displayMenu .decorator {
    height: 650px;
  }

  .ST_0007_DisplayMenu .displayMenu .optionContainer.headUpDisplay {
    left: 501px;
    top: 10px;
  }
  .ST_0007_DisplayMenu .displayMenu .optionContainer.centralDisplay {
    left: 760px;
    top: 160px;
  }
  .AU401_0 .displayMenu .optionContainer.centralDisplay,
  .AU401_0_LWB .displayMenu .optionContainer.centralDisplay,
  .AU402_0 .displayMenu .optionContainer.centralDisplay,
  .AU401_0_RS .displayMenu .optionContainer.centralDisplay,
  .AU402_0_RS .displayMenu .optionContainer.centralDisplay {
    top: 150px;
  }
  .AU401_0_RS
    .displayMenu
    .optionContainer.centralDisplay
    .SE_0106_Button_with_Connector,
  .AU402_0_RS
    .displayMenu
    .optionContainer.centralDisplay
    .SE_0106_Button_with_Connector {
    transform: translateX(70px);
  }
  .ST_0007_DisplayMenu .displayMenu .optionContainer.virtualMirrorDisplay {
    left: 170px;
    top: 160px;
  }
  .AU416_2 .displayMenu .optionContainer.virtualMirrorDisplay,
  .AU416_3 .displayMenu .optionContainer.virtualMirrorDisplay,
  .AU416_2_RS .displayMenu .optionContainer.virtualMirrorDisplay,
  .AU416_3_RS .displayMenu .optionContainer.virtualMirrorDisplay {
    left: 190px;
  }
  .ST_0007_DisplayMenu .displayMenu .optionContainer.fpkDisplay {
    left: 451px;
    top: 150px;
  }
  .ST_0007_DisplayMenu
    .displayMenu
    .optionContainer.fpkDisplay
    .SE_0106_Button_with_Connector {
    transform: translateY(-20px);
  }
  .ST_0007_DisplayMenu .displayMenu .optionContainer.pidDisplay {
    left: 1095px;
    top: 200px;
  }
  .AU436_0 .displayMenu .optionContainer.pidDisplay,
  .AU436_1 .displayMenu .optionContainer.pidDisplay {
    top: 190px;
  }
}
