.msgBorderWrapper {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  left: 0;
  flex-direction: column-reverse;
  bottom: 110px;
}

.invalidArea {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 235px;
  background-color: rgba(255, 0, 0, 0.3);
  display: none;
}

.welcomeText {
  background-color: rgba(255, 255, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}

.msgBorderWrapper .welcomeText {
  margin-top: var(--iA1);
  width: 900px;
  height: 41px;
}

.bcIconsGroup {
  display: flex;
}

.iconsContainer {
  display: flex;
  margin-right: var(--iA3);
}

.iconsContainer:last-child {
  margin-right: 0;
}

.bcIcon {
  width: 63px;
  height: 63px;
  background-color: rgba(var(--boundingBoxGreen));
}

.msgBorderWrapper .bcIcon:nth-child(odd) {
  margin-right: var(--lA1);
}
