:global(#root) > .T_0104_TextAndButtons {
  margin: 0;
  width: 1678px;
  height: 554px;
  border: 1px solid rgb(var(--audiGrey));
  background-color: rgb(var(--audiBlack));
}

.T_0104_TextAndButtons {
}

.TextAndButtons {
  display: flex;
  width: 100%;
  height: 100%;
}

.leftPanel,
.rightPanel {
  display: flex;
  flex: 1;
  flex-direction: column;
  overflow: hidden;
}

/*-------------------Text Container------------------*/

.textLinesContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  /*padding: var(--iA1) var(--cA1);*/
  padding-top: var(--iA1);
  padding-bottom: var(--iA1);
  padding-left: var(--cA1);
  padding-right: calc(var(--cA1) - 5px);
  box-sizing: border-box;
}

.textLines {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.UE_1101_Checkbox,
.infoText {
  height: var(--minTouchSize);
  margin: var(--hA3) var(--cA1) 0 var(--cA1);
}

.infoText {
  display: flex;
  align-self: center;
  align-items: center;
}

.infoText .listIcon {
  margin-left: var(--iA1);
}

/*--------------Bounding box Container--------------*/

.boundingBoxContainer {
  display: flex;
  height: fit-content;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  margin: auto;
  max-width: 100%;
  padding: 0 var(--iA1);
}

.boundingBoxDecorator {
  height: var(--decoratorHeight);
  width: var(--decoratorWidth);
  background-color: rgb(var(--boundingBoxGreen));
  margin-top: var(--zA6);
}

.boundingBoxContainer .UE_0202_FloatingContainer {
  position: relative;
  max-height: calc(var(--minTouchSize) + 12px);
  width: calc(100% - 80px);
  margin: 50px 40px 0 40px;
}
.boundingBoxContainer .UE_0202_FloatingContainer .floatingContainer {
  position: relative;
}

.boundingBoxContainer .UE_0601_Slider {
  width: 100%;
  height: var(--minTouchSize);
}

.secondTextLine .boundingBoxDecorator {
  margin-top: var(--hA1);
}

.decorator {
  height: 100%;
  width: 100%;
  margin: unset;
  padding: unset;
}

.decorator .boundingBoxDecorator {
  height: 100%;
  width: 100%;
  margin-top: unset;
}

/*------------------List Vertical------------------*/

.listVerticalContainer {
  flex: 1;
  height: 100%;
  overflow: hidden;
}

.T_0103_ListVertical {
  width: 100%;
  height: 100%;
}

/*-----------------Operation Panel-----------------*/

.UE_0401_OperationPanel {
  position: relative;
  width: calc(100% - var(--operationPanelReduction));
  left: var(--operationPanelShift);
}

.UE_0401_OperationPanel .Text {
  top: unset;
  transform: unset;
}

/*------------------Car Decorator------------------*/

.UGDODecorator {
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;
}

/*--------------------Settings--------------------*/

.settings {
  margin-top: 20px;
  margin-left: 20px;
}
