:global(#root) > .SE_0093_NativeGoodbyePopup {
  margin: 8px;
}
.SE_0093_NativeGoodbyePopup {
  max-width: 1390px;
}

.NativeGoodbyePopup_topButton {
  position: absolute;
}

.NativeGoodbyePopup_Center {
  padding: var(--popupContentPadding) var(--popupContentPadding)
    var(--popupContentPadding) calc(var(--minTouchSize) + var(--iA1));
}

.listIcon {
  top: 50%;
  left: 0;
  position: absolute;
  transform: translateY(-50%);
}

.NativeGoodbyePopup_topButton .UE_0101_ActionButton {
  width: var(--minTouchSize);
  min-width: unset;
}

.NativeGoodbyePopup_Text p {
  position: relative;
  padding-left: 70px;
}

.NativeGoodbyePopup_Container {
  width: 100%;
  height: auto;
  flex-direction: column;
}
