:root {
  --textColor: rgb(230, 230, 230);
  --iconWidth: 100px;
  --iconHeight: 100px;
}

@font-face {
  font-family: 'Audi Type Display High Light';
  src: url('./fonts/AudiTypeDisplayHigh-Light.ttf');
}

* {
  font-family: Audi Type Display High Light;
}

.searchSection:hover .filterTB {
  width: 150px;
}

/* body .iconViewerContainer {
  margin-left: 150px;
} */

.searchSection {
  position: absolute;
  width: 40px;
  height: 40px;
  top: 340px;
  left: 110px;
}
.hdln {
  display: flex;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
.filterTB {
  position: absolute;
  height: 32px;
  top: 3px;
  left: 20px;
  border: unset;
  z-index: 1;
  width: 0px;
  transition: width 0.6s ease;
  border-radius: 0 4px 4px 0;
  text-align: center;
}

.filterTB:focus {
  width: 150px;
}
.searchContainer {
  top: 0px;
  left: 0px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: mediumseagreen;
  position: absolute;
  box-shadow: 0px 0px 14px 1px rgb(150, 150, 150);
  cursor: pointer;
  z-index: 2;
}

.iconsSave,
.fileDownload {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: mediumseagreen;
  position: absolute;
  top: 47px;
  left: 110px;
  box-shadow: 0px 0px 14px 1px rgb(150, 150, 150);
  cursor: pointer;
  z-index: 2;
}

.fileDownload {
  background-color: dodgerblue;
  top: 400px;
}
.iconsSave {
  background-color: dodgerblue;
  top: 280px;
}

.searchContainer > img,
.fileDownload > img,
.iconsSave > img {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: white;
  position: relative;
}

.leftAreaEnabled {
  margin-left: 0;
}
.leftArea {
  position: relative;
  width: 300px;
  height: 100%;
  background-color: rgb(42, 42, 43);
  flex-shrink: 0;
  margin-left: -300px;
  transition: margin-left 0.5s ease;
}
.centerArea {
  flex: 1;
  position: relative;
  height: 100%;
}
.annotationTable {
  color: white;
  width: 90%;
  left: 5%;
  border-collapse: collapse;
  position: relative;
  margin-top: 20px;
}

.annotationTable tr th:last-child {
  text-align: left;
}

.annotationTable tr td:first-child {
  text-align: center;
}

.annotationTable td {
  font-size: 0.8em;
  padding: 5px;
  word-break: break-word;
}
.annotationTable td img {
  max-width: 30px;
  max-height: 30px;
}
.annotationTable tbody tr {
  border-bottom: 16px solid transparent;
  cursor: default;
}
.annotationTable tr:last-child {
  border-bottom: none;
}
.annotationTable tbody tr:last-child {
  border-bottom: 16px solid transparent;
}
.annotationTable tr {
  border-bottom: 1px solid rgb(80, 80, 80);
  cursor: pointer;
  transition: background-color 0.2s ease, border-radius 0.2s ease;
}
.annotationTable tr:hover {
  background-color: crimson;
  border-radius: 4px;
}
.annotationTable tr:first-child:hover {
  background-color: unset;
}

.pageArea {
  display: flex;
  height: calc(100% - 60px);
  width: 100%;
}
.basket {
  width: 300px;
  height: 100%;
  overflow-y: auto;
}

.fa.fa-arrow-circle-up {
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -34%);
  font-size: 1.3em;
  color: rgb(30, 30, 30);
}
.setannotations {
  position: sticky;
  bottom: -200px;
  width: 100%;
  min-height: 240px;
}
.headlineSpan {
  color: crimson;
}
.arrow {
  position: relative;
  border-radius: 80px 80px 0 0;
  width: 80px;
  height: 40px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgb(230, 230, 230);
  cursor: pointer;
}
.content {
  position: relative;
  width: 100%;
  min-height: 200px;
  background-color: green;
}
.checked {
  background-color: crimson;
}
body {
  background-color: rgb(37, 37, 38) !important;
  margin: 0;
  width: 100%;
  height: 100%;
  position: absolute;
}

.pencil {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 30px;
  color: crimson;
  margin-left: 40px;
}

.fa.fa-times {
  color: crimson;
}
.fa.fa-check {
  color: mediumseagreen;
}

.elementGridSpace {
  padding: 0 180px;
  background-color: rgb(37, 37, 38);
  height: calc(100% - 60px);
  width: calc(100% - 360px);
}
.download {
  margin: 0px 10px 0px 0px;
}

body .filterSection {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgb(60, 60, 60, 0.8);
}

body .headlineWrapper {
  display: flex;
  height: 60px;
  background-color: rgb(50, 50, 51);
}

body .headlineWrapper .headline {
  width: auto;
  color: var(--textColor);
  height: 1.5em;
  margin-bottom: 40px;
  flex: 1;
  margin: 0;

  position: relative;
  top: 50%;
  transform: translateY(-42%);
  padding: 0 10px;
}

body .headlineWrapper button {
  background-color: mediumseagreen;
  border: none;
  color: white;
  padding: 9px;
  font-size: 16px;
  cursor: pointer;
  position: relative;
  top: 0px;
  border-radius: 0 4px 4px 0;
  position: relative;
}

.findWrapper {
  margin: 0px 10px 0px 0px;
  border-radius: 4px;
  height: auto;
  display: none;
}

.filterBox {
  height: 2.6em;
  border-radius: 4px 0 0 4px;
  border: none;
  padding: 0 10px;
}
.filterBox:focus {
  outline: none;
}
.search {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  background-color: white;
  border-radius: 4px;
}
body .dropZone {
  text-align: center;
  font: 20pt bold 'Vollkorn';
  color: #bbb;
  width: 100%;
  height: calc(100vh - 60px);
  position: relative;
  background-color: rgb(37, 37, 38);
}
body .dropZone .dropZoneWrapper {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
body .dropZone .dropZoneWrapper .openFolder {
  font-size: 2em;
}
body .dropZone .dropZoneWrapper .picture {
  font-size: 1em;
  left: -16px;
  top: 5px;
  position: relative;
}
body .dropZone .dropZoneWrapper .dropText {
  position: relative;
  margin-top: 1.5em;
  top: 50%;
  transform: translateY(-50%);
  font-family: Audi Type Display High Light;
}

body .fileDownload p {
  display: inline-block;
  margin: 0;
  position: relative;
  top: -3px;
}

body .elementGrid {
  width: calc(100% - 260px);
  height: calc(100% - 30px);
  overflow-y: auto;
  padding: 15px 100px 15px 160px;
  display: flex;
  flex-wrap: wrap;
}

body .elementGrid .elementContainer {
  position: relative;
  width: calc(var(--iconWidth) + 40px);
  max-width: calc(var(--iconWidth) + 40px);
  height: fit-content;
  height: -moz-fit-content;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 10px;
  border-radius: 10px;
  transition: background-color 0.2s ease;
  background-color: rgb(30, 30, 30);
  margin: 15px;
  cursor: pointer;
}

body .elementGrid .elementContainer .iconWrapper {
  position: relative;
  width: var(--iconWidth);
  height: var(--iconHeight);
}

body .elementGrid .elementContainer img {
  position: relative;
  width: 100%;
  height: 100%;
}

body .customSize {
  top: 10px;
}

body .customSize input {
  transform: scale(1.5);
}

body .fileSize.disabled .sizeInput {
  background: darkgray;
}

body .fileSize {
  width: 130px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  left: 30px;
}

body .fileSize.disabled p {
  color: darkgray;
}
body .fileWidth {
  top: 55px;
}
body .fileHeight {
  top: 110px;
}
body .fileSize .sizeInput {
  position: relative;
  height: 40px;
  width: 60px;
  margin-left: 0px;
  border-radius: 4px;
  text-align: center;
  margin-right: unset;
  margin-top: unset;
  margin-right: unset;
  transform: unset;
}
body .fileSize p {
  position: relative;
  width: fit-content;
  color: var(--textColor);
  font-family: Audi Type Display High Light;
}
body .elementGrid .elementContainer p {
  width: 100%;
  height: 100%;
  color: var(--textColor);
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: center;
  word-break: break-word;
  font-family: Audi Type Display High Light;
}

body .elementGrid .elementContainer:hover {
  background-color: rgb(70, 70, 70);
}

body .elementGrid .elementContainer .captionWrapper {
  width: 100%;
  height: fit-content;
}

body .elementGrid .elementContainer .annotationWrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  height: calc(100% + 5px);
  background-color: var(--textColor);
  border-radius: 10px;
  padding: 10px;
  z-index: 1;
}

body .elementGrid .elementContainer .annotationWrapper input {
  width: calc(100% - 10px);
}

body .elementGrid .elementContainer .annotationWrapper h6 {
  text-align: center;
  font-family: Audi Type Display High Light;
  font-size: 1em;
}

body .elementGrid .elementContainer .annotationWrapper .buttonWrapper {
  position: relative;
  display: inline-block;
  width: 100%;
  display: flex;
  justify-content: center;
}

body .elementGrid .elementContainer .annotationWrapper button {
  margin-top: 10px;
  position: relative;
  width: 50px;
  height: 22px;
}
body .elementGrid .elementContainer .annotationWrapper button:first-child {
  margin-right: 10px;
}

body .loadingScreen {
  position: relative;
  width: 400px;
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--textColor);
  border-radius: 10px;
}
body .loadingScreen .progressLabel {
  font-size: 2em;
  margin-top: 2em;
  color: rgb(60, 60, 60);
  font-family: Audi Type Display High Light;
  text-align: center;
}
body .loadingScreen .loadingWrapper {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}

.animation {
  animation-name: find;
  animation-duration: 2s;
  animation-iteration-count: 1;
}

.noSelect {
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.hidden {
  display: none;
}

/* find animation */
@keyframes find {
  0% {
    background-color: none;
    transform: rotate(0deg);
  }
  12% {
    transform: rotate(-45deg);
  }
  25% {
    transform: rotate(0deg);
  }
  38% {
    transform: rotate(45deg);
  }
  50% {
    background-color: rgb(220, 20, 60);
    transform: rotate(0deg);
  }
  75% {
  }
  100% {
    background-color: none;
  }
}

/* Loading animation */
.skChase {
  position: relative;
  width: 140px;
  height: 140px;
  margin: auto;
  animation: sk-chase 2.5s infinite linear both;
}
.skChaseDot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  animation: skChaseDot 2s infinite ease-in-out both;
}
.skChaseDot:before {
  display: none;
  content: '';
  display: block;
  width: 25%;
  height: 25%;

  background-color: dodgerblue;
  border-radius: 100%;
  animation: skChaseDot-before 2s infinite ease-in-out both;
}
.skChaseDot:nth-child(1) {
  animation-delay: -1.1s;
}
.skChaseDot:nth-child(2) {
  animation-delay: -1s;
}
.skChaseDot:nth-child(3) {
  animation-delay: -0.9s;
}
.skChaseDot:nth-child(4) {
  animation-delay: -0.8s;
}
.skChaseDot:nth-child(5) {
  animation-delay: -0.7s;
}
.skChaseDot:nth-child(6) {
  animation-delay: -0.6s;
}
.skChaseDot:nth-child(1):before {
  animation-delay: -1.1s;
}
.skChaseDot:nth-child(2):before {
  animation-delay: -1s;
}
.skChaseDot:nth-child(3):before {
  animation-delay: -0.9s;
}
.skChaseDot:nth-child(4):before {
  animation-delay: -0.8s;
}
.skChaseDot:nth-child(5):before {
  animation-delay: -0.7s;
}
.skChaseDot:nth-child(6):before {
  animation-delay: -0.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes skChaseDot {
  80%,
  100% {
    transform: rotate(360deg);
  }
}
@keyframes skChaseDot-before {
  50% {
    transform: scale(0.4);
  }
  100%,
  0% {
    transform: scale(1);
  }
}

