.touchIcon {
  position: absolute;
  width: var(--touchIconSize);
  height: var(--touchIconSize);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../Globals/assets/defaultTouchIcon.png);
}

.listIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  min-width: var(--listIconSize);
  min-height: var(--listIconSize);
  display: inline-block;
  background-image: url(../../Globals/assets/defaultListIcon.png);
  background-position: center;
  background-repeat: no-repeat;
}

.listIcon.colourized {
  background-image: unset;
  -webkit-mask-image: url(../../Globals/assets/defaultListIcon.png);
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}
/* 
.listIcon + .listIcon {
  margin-left: 0;
  padding-left: 5px;
} */
.listIcon + .listIcon,
.textPrio1 + .listIcon,
.textPrio2 + .listIcon,
.listIcon + .textPrio1,
.listIcon + .textPrio2,
.textClimateLarge + .listIcon,
.textIconMargin {
  margin-left: var(--iA1);
}

.daIcon {
  position: absolute;
  width: var(--daIconSize);
  height: var(--daIconSize);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../Globals/assets/defaultDaIcon.png);
  background-size: var(--adaptiveIconMaskScale);
}

.appIonBackground {
  position: absolute;
  width: var(--daIconSize);
  height: var(--daIconSize);
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-position: center;
  background-size: var(--adaptiveIconMaskScale);
  background-image: url(../../Globals/icons/IconMaster/CID-Icons/Detail/E71A_Background_adaptive.svg);
  -webkit-mask-image: url(../../Globals/icons/IconMaster/CID-Icons/Detail/shape_of_adaptive_icons.svg);
}

.appListIcon {
  background-size: 150%;
  background-position: center;
  -webkit-mask-size: 150%;
  -webkit-mask-position: center;
}
