/* CLASSIFICATION: CONFIDENTIAL */

/*-------------------- standalone properties --------------------*/
:global(#root) > .UE_0702_ProgressBar {
  margin: 20px;
}

/*-------------------- UE_0702_ProgressBar --------------------*/

.UE_0702_ProgressBar {
}
.progressBar {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.progressBar > div {
  flex: 1;
}

.topLine {
  text-align: center;
  margin-bottom: 10px;
}

.centerLine,
.bottomLine {
  display: flex;
}

.currentProgress {
  flex: 1;
  position: relative;
  margin: 0px 20px;
  height: var(--lineHeightPrio2);
}

.label {
  width: 175px;
}

.label:first-child {
  text-align: right;
}

.barBackground {
  position: absolute;
  width: 100%;
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgb(var(--audiGrey));
}

.progress {
  position: absolute;
  width: 75%;
  height: 6px;
  top: 50%;
  transform: translateY(-50%);
  background: -webkit-linear-gradient(
    left,
    rgb(var(--audiLightGrey)),
    rgb(var(--audiWhite))
  );
}

:global(.fpkOrange) .progress {
  background: none;
  background-color: rgb(var(--fpkOrange));
}

.gap {
  position: absolute;
  width: 5px;
  height: 2px;
  right: -5px;
  bottom: 2px;
  background-color: rgb(var(--audiBlack));
}

.bottomLine {
  padding: 0 calc(175px + 20px);
  margin-top: 10px;
}

.bottomLine p {
  flex: 1;
  margin: 0 !important;
}

.bottomLine p:last-child {
  text-align: right;
}

/* TODO: handle this rule in fpk */
.progressBar.fpk .topLine {
  margin-bottom: 0px;
}

/*--------------settings--------------------*/

.settings {
  margin: 150px 0 0 20px;
  background-color: none;
}
