/* CLASSIFICATION: CONFIDENTIAL */

.UE_1009_C2Cursor {
  width: 100%;
  height: 100%;
}

:global(#root) > .UE_1009_C2Cursor {
  margin: 0;
  width: 100%;
  height: 119px;
}

.C2Cursor {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.C2Cursor .cursorBackground {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--ambientcolor1), 0.2);
  border-radius: var(--standardBorderRadius);
}

.C2Cursor:before {
  content: '';
  position: absolute;
  width: 4px;
  height: 100%;
  top: 0px;
  left: 0;
  background-color: rgba(var(--ambientcolor1), 1);
  box-shadow: 3px 0px 5px 1px rgba(var(--ambientcolor1), 0.2);
  border-radius: var(--standardBorderRadius);
}

.C2Cursor.borderBottom:before {
  height: 4px;
  width: 100%;
  bottom: 0px;
  top: unset;
}
