/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .ST_0002_TimerSettings {
  position: absolute;
  height: 554px;
  width: 1678px;
  top: 0;
  left: 0;
  margin: 0px;
}

:global(#root) > .ST_0002_TimerSettings .wallpaper {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

:global(#root) > .ST_0002_TimerSettings .backgroundLayer {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--ambientBackground1), 0.9);
}

.timerSettings {
  position: absolute;
  width: 100%;
  height: 100%;
}

.settingsContainer {
  margin-left: 40px;
  width: calc(100% - 202px);
  height: 100%;
  display: flex;
  align-items: center;
}

.settingsContainer.wizard {
  justify-content: center;
}

.settingsContainer.centered {
  margin-left: 50%;
  transform: translateX(-50%);
  justify-content: center;
}

.dataPickerContainer {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
}

.day.unique .buttonMatrix,
.day .UE_1301_DataPicker {
  display: none;
}

.day.unique .UE_1301_DataPicker {
  display: inherit;
}

.day,
.day .UE_1301_DataPicker {
  width: 445px;
}
.UE_1301_DataPicker {
  height: var(--dataPickerHeight);
  width: var(--dataPickerWidthTime);
}

.day {
  height: fit-content;
}

.buttonMatrix {
  display: flex;
  flex-direction: row;
  width: fit-content;
  height: fit-content;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  background-color: var(--buttonBackgroundColor);
  background-position: center;
}

.column {
  width: var(--minTouchSize);
}
.column .button p {
  text-align: center;
  width: calc(100% - 30px);
}

.options {
  justify-content: left;
  align-items: center;
  flex: 1;
  max-width: 360px;
}

.options .actionButtonWrapper {
  position: relative;
  height: fit-content;
  width: 100%;
}

.options .actionButtonWrapper .button {
  width: 100%;
}
.actionButtonWrapper {
  background-image: none;
  background-color: transparent;
}

.options .iconWrapper {
  right: 0;
}

.button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}
.tabButtons .button {
  width: 100%;
}

.iconWrapper {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

.tabButtons {
  height: 357px;
  max-width: 300px;
  flex: 1;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.tabButtons .actionButtonWrapper {
  position: relative;
  height: fit-content;
  background-color: unset;
  background-image: none;
}

.UE_0202_FloatingContainer .button {
  display: flex;
  justify-content: center;
  padding-left: var(--textButtonMargin);
  padding-right: var(--textButtonMargin);
  box-sizing: border-box;
  align-items: center;
}

.floatingContainer.right {
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings {
  position: absolute;
  top: 60%;
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0002_TimerSettings {
    height: 425px;
    width: 1526px;
  }

  .tabButtons {
    max-width: 198px;
  }
}
