/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_0101_Dashboard {
  margin: 10px;
  width: 1800px;
  height: 817px;
  background-color: rgb(var(--bodyColorGrey));
}

:global(#root) > .T_0101_Dashboard .dashboard {
  padding-left: 0;
}

.T_0101_Dashboard .dashboard {
  position: relative;
  width: calc(var(--dashboardWidth));
  height: 100%;
  padding-left: var(--directAccessWidth);
  overflow: hidden;
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: calc(var(--animationDuration) / 4);
}
.T_0101_Dashboard .dashboard .backgroundSpotOverlay {
  position: absolute;
  width: 100%;
  height: 100%;
}

.T_0101_Dashboard .dashboard .backgroundSpotOverlay .transparentGap {
  position: absolute;
  height: 100%;
  transition: all 0.1s ease-in-out;
  width: var(--tileWidth);
}

.T_0101_Dashboard .dashboard .backgroundSpotOverlay .transparentGap::after {
  content: '';
  position: absolute;
  height: 100%;
  width: var(--dashboardTileSpacing);
  right: calc(var(--dashboardTileSpacing) * (-1));
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 1),
    rgba(var(--audiBlack), 0.3)
  );
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard
  .dashboard
  .backgroundSpotOverlay
  .transparentGap.rightGap::after {
  right: calc(var(--directAccessWidth) * (-1));
  width: var(--directAccessWidth);
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard .dashboard .backgroundSpotOverlay .transparentGap.centerGap {
  margin-left: calc(var(--tileWidth) + var(--dashboardTileSpacing));
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard
  .dashboard
  .backgroundSpotOverlay.medium
  .transparentGap.centerGap {
  display: none;
}

.T_0101_Dashboard .dashboard .backgroundSpotOverlay .transparentGap.rightGap {
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * 2);
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard .dashboard.leftDetail .backgroundSpotOverlay .transparentGap,
.T_0101_Dashboard .dashboard.rightDetail .backgroundSpotOverlay .transparentGap,
.T_0101_Dashboard
  .dashboard.centerDetail
  .backgroundSpotOverlay
  .transparentGap {
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard
  .dashboard.leftDetail
  .backgroundSpotOverlay
  .transparentGap.leftGap,
.T_0101_Dashboard
  .dashboard.rightDetail
  .backgroundSpotOverlay
  .transparentGap.rightGap,
.T_0101_Dashboard
  .dashboard.centerDetail
  .backgroundSpotOverlay
  .transparentGap.centerGap {
  width: 100%;
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard
  .dashboard.leftDetail
  .backgroundSpotOverlay
  .transparentGap::after,
.T_0101_Dashboard
  .dashboard.centerDetail
  .backgroundSpotOverlay
  .transparentGap::after,
.T_0101_Dashboard
  .dashboard.rightDetail
  .backgroundSpotOverlay
  .transparentGap::after,
.T_0101_Dashboard
  .dashboard.leftDetail
  .backgroundSpotOverlay
  .transparentGap:not(.leftGap),
.T_0101_Dashboard
  .dashboard.centerDetail
  .backgroundSpotOverlay
  .transparentGap:not(.centerGap),
.T_0101_Dashboard
  .dashboard.rightDetail
  .backgroundSpotOverlay
  .transparentGap:not(.rightGap) {
  width: 0%;
  transition: all 0.1s ease-in-out;
}

.dashboard .UE_1202_DashboardTile {
  /* display: none; */
}
.dashboard {
  padding: 0;
  width: 100%;
}
.UE_0801_PageIndicator {
  position: absolute;
  bottom: 0px;
  width: calc(100vw - var(--directAccessWidth));
  height: 28px;
  display: flex;
  z-index: 1;
  background-color: rgba(var(--audiBlack), 0.7);
}

.T_0101_Dashboard.AIScreen .UE_0801_PageIndicator {
  left: -119px;
  right: 0;
  width: auto;
}

.UE_1202_DashboardTile {
  position: absolute;
  height: 100%;
  width: var(--tileWidth);
}

.UE_1202_DashboardTile.centerTile {
  margin-left: calc(var(--tileWidth) + var(--dashboardTileSpacing));
}
.UE_1202_DashboardTile.rightTile {
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * 2);
}

/* Push away unselected Tiles*/
/* left tile selected others pushed to right*/
.centerDetail .UE_1202_DashboardTile:not(.detailView),
.leftDetail .UE_1202_DashboardTile:not(.detailView),
.rightDetail .UE_1202_DashboardTile:not(.detailView) {
  pointer-events: none;
}

/* stretch right tile to 2/3 & hide center tile */
.UE_1202_DashboardTile.medium {
  width: calc(var(--tileWidth) * 2 + var(--dashboardTileSpacing));
}

.UE_1202_DashboardTile.medium.rightTile {
  margin-left: calc(var(--tileWidth) + var(--dashboardTileSpacing));
}

.T_0101_Dashboard .hidden {
  display: none;
}

/* dashboardtile detailview */
.UE_1202_DashboardTile.detailView {
  width: var(--dashboardWidth);
  margin-left: 0;
}

.UE_0801_PageIndicator.hidden {
  display: none;
}

.SE_0048_AI_Screen {
  position: absolute;
  width: 100%;
  height: 100%;
  display: inline;
  right: 0;
}

/* CLASSIFICATION: CONFIDENTIAL */

.UE_1202_DashboardTile .detailContent {
  transition: var(--AG_0202_FadeOut);
  opacity: 0;
}
.detailView .detailContent {
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.2s;
  opacity: 1;
}

.UE_1202_DashboardTile {
  opacity: 1;
  transition: var(--AG_0201_FadeIn), var(--AG_0303_ScaleResize);
}

.detailView {
  transition: var(--AG_0303_ScaleResize);
}

/* Push away unselected Tiles*/
/* left tile selected others pushed to right*/
.leftDetail .centerTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(var(--dashboardWidth) + var(--dashboardTileSpacing));
  opacity: 0 !important;
}
.leftDetail .rightTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(
    var(--dashboardWidth) + (var(--dashboardTileSpacing) * 2) + var(--tileWidth)
  );
  opacity: 0 !important;
}

/* center tile selected left tile pushed to left, right tile pushed to right*/
.centerDetail .leftTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * -1);
  opacity: 0 !important;
}
.centerDetail .rightTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(
    var(--tileWidth) * 3 + var(--dashboardTileSpacing) * 3 + 105px
  );
  opacity: 0 !important;
}

/* right tile selected others pushed to left*/
.rightDetail .leftTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc(
    ((var(--tileWidth) * 2) + (var(--dashboardTileSpacing) * 2)) * -1
  );
  opacity: 0 !important;
}
.rightDetail .centerTile {
  transition: var(--AG_0202_FadeOut), var(--AG_0101_Move);
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * -1);
  opacity: 0 !important;
}

/* stretch right tile to 2/3 & hide center tile */
.UE_1202_DashboardTile.medium.rightTile {
  width: calc(var(--tileWidth) * 2 - var(--dashboardTileSpacing));
}

.UE_1202_DashboardTile.medium.rightTile {
  width: calc(var(--tileWidth) * 2 + var(--dashboardTileSpacing));
  margin-left: calc(var(--tileWidth) + var(--dashboardTileSpacing));
}

.UE_1202_DashboardTile.centerTile.hidden {
  display: none;
}

.UE_1202_DashboardTile.medium.rightTile .dashboardTile {
  border-width: 0 0 var(--dashboardMarginBottom) 0 !important;
}

/* ------------------------------- EDIT MODE ------------------------------- */
.T_0101_Dashboard .dashboard .UE_1202_DashboardTile .dashboardTile {
  transition: all 0.1s ease-in-out;
}

.T_0101_Dashboard.editMode
  .dashboard
  .UE_1202_DashboardTile.dragging
  .dashboardTile {
  background-color: rgba(var(--interactionBackground), 0.2);
  border-radius: var(--standardBorderRadius);
}

.T_0101_Dashboard.editMode
  .dashboard
  .UE_1202_DashboardTile:not(.dragging)
  .dashboardTile {
  transform: scale(0.9);
}

.T_0101_Dashboard .dashboard .UE_1202_DashboardTile:not(.dragging)::before {
  position: absolute;
  content: '';
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 0.1s ease-in-out;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 1),
    rgba(var(--audiBlack), 0.3)
  );
  clip-path: polygon(
    0% 0%,
    0% 100%,
    5% 100%,
    5% 5%,
    95% 5%,
    95% 95%,
    5% 95%,
    5% 100%,
    100% 100%,
    100% 0%
  );
}

.T_0101_Dashboard.editMode
  .dashboard
  .UE_1202_DashboardTile:not(.dragging)::before {
  transition: all 0.07s ease-in-out;
  opacity: 1;
}

.T_0101_Dashboard.editMode
  .dashboard
  .UE_1202_DashboardTile:not(.dragging)
  .dashboardTile
  > * {
  opacity: var(--touchDisabledState);
}

.T_0101_Dashboard.editMode .dashboard .UE_1202_DashboardTile.dragging {
  z-index: 1;
}

.T_0101_Dashboard.editMode .dashboard .tileEditBackground {
  position: absolute;
  height: 100%;
  width: var(--tileWidth);
  margin-left: var(--tileWidth);
  background-color: rgba(var(--audiBlack), 0.4);
}

/* AppSelection */

.T_0101_Dashboard .appSelection {
  position: absolute;
  width: var(--dashboardWidth);
  height: 100%;
  top: 0px;
  z-index: 1;
}

.T_0101_Dashboard .appSelection .scroller {
  position: absolute;
  width: 100%;
  top: var(--minTouchSize);
  height: calc(100% - var(--minTouchSize));
  overflow-y: auto;
  overflow-x: hidden;
}

.T_0101_Dashboard .appSelection .scroller .inner {
  position: relative;
  width: 100%;
}

.T_0101_Dashboard .appSelection .appSelectionHeadline {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.T_0101_Dashboard .appSelection .appSelectionHeadline::before,
.T_0101_Dashboard .appSelection .appSelectionHeadline .button::before {
  position: absolute;
  left: calc(var(--opticalTouchReduction) / 2);
  bottom: calc(var(--opticalTouchReduction) / 2 + 1px);
  content: '';
  height: 2px;
  width: calc(100% - var(--opticalTouchReduction));
  background-color: var(--tabbarSeparatorColor);
}

.T_0101_Dashboard .appSelection .appSelectionHeadline::before {
  left: calc(var(--opticalTouchReduction) / 2 + var(--minTouchSize));
  width: calc(100% - var(--opticalTouchReduction) - var(--minTouchSize));
}

.T_0101_Dashboard .appSelection .appSelectionHeadline .folderTitle {
  position: relative;
  display: flex;
  width: calc(100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2));
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  justify-content: center;
  align-items: center;
}

.T_0101_Dashboard .appSelection .appSelectionHeadline .folderTitle .textPrio1 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.T_0101_Dashboard .appSelection .appSelectionHeadline .button {
  position: relative;
  min-width: var(--minTouchSize);
  height: 100%;
  width: var(--minTouchSize);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding-bottom: var(--heightBleed);
  box-sizing: border-box;
}

