/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0402_Tabbarmenu {
  position: absolute;
  height: var(--minTouchSize);
  background-color: rgb(var(--bodyColorGrey));
  width: calc(100% - 20px);
  margin: 10px;
}

:root {
  --sourceWidth: 280px;
  --bluetoothNameWidth: 100px;
  --tabMaxWidth: 784px; /*Width of 24 "W"s*/
  --heightBleed: 0px;
  --iconShift0402: 5px;
}

.UE_0402_Tabbarmenu {
}

.tabbar {
  position: absolute;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

/*---------------------------- BACKGROUND STYLING -----------------------------------*/
.background {
  position: absolute;
  height: 100%;
  width: 100%;
}

/*---------------------------- BUTTON PARAMETERS -----------------------------------*/
.button {
  position: relative;
  height: 100%;
  width: var(--minTouchSize);
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  flex-shrink: 0;
  padding-bottom: var(--heightBleed);
  box-sizing: border-box;
}

.buttonContainer.left .button:before,
.buttonContainer.right .button:before,
.tabs:before,
.UE_0404_Titleline:before,
.UE_0501_InputField:before,
.SE_0058_TitleLineWithArrows:before,
.fullBackgroundTitle .separator,
.zdctp::before,
.UE_0402_Tabbarmenu .tabbar .headlineIcon::before,
.appSelectorTitle::before {
  position: absolute;
  left: calc(var(--opticalTouchReduction) / 2);
  bottom: calc(var(--opticalTouchReduction) / 2 + 1px);
  content: '';
  height: 2px;
  width: calc(100% - var(--opticalTouchReduction));
  background-color: var(--tabbarSeparatorColor);
}

.textButton {
  position: relative;
  height: 100%;
  width: var(--minTouchSize);
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  flex-shrink: 0;
  padding-bottom: 10px;
  box-sizing: border-box;
}

.tabbar
  :not(.opsHeadline):not(.vpsHeadline):not(.garageDoor)
  .button
  .touchIcon {
  transform: translate(-50%, calc(-50% - var(--iconShift0402)));
}

/*---------------------------- OPENED SOURCE SELECTION -----------------------------------*/
.sourceSelectionOpen .UE_0402_Tabbarmenu .tabbar .background {
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.7) var(--minTouchSize),
    black var(--minTouchSize),
    black calc(var(--minTouchSize) + 283px),
    rgba(0, 0, 0, 0.7) calc(var(--minTouchSize) + 283px),
    rgba(0, 0, 0, 0.7) 100%
  );
}

/*--------- HEADLINE ICON CONTAINER ------------*/
.UE_0402_Tabbarmenu .tabbar .headlineIcon {
  position: relative;
  display: flex;
  height: calc(100% - 7px);
  width: var(--minTouchSize);
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.UE_0402_Tabbarmenu .tabbar .headlineIcon::before {
  left: calc(var(--opticalTouchReduction) / 2);
  bottom: -2px;
  width: 100%;
}

/*--------- BUTTON CONTAINER ------------*/
.buttonContainer {
  position: relative;
  height: 100%;
  width: auto;
  display: flex;
}

.buttonContainer.left {
  align-self: flex-start;
}
.buttonContainer.right {
  align-self: flex-end;
}

.npsSourceIcon {
  position: relative;
  height: calc(100% - 7px);
  width: var(--minTouchSize);
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  flex-shrink: 0;
}

.button p {
  position: relative;
  white-space: nowrap;
  text-align: center;
  /* font-family: var(--fontNormal); */
}

.button.textButton {
  width: fit-content;
  max-width: 260px;
}

.button .textcontainer {
  position: relative;
  white-space: nowrap;
  text-align: center;
  margin: 0 var(--textButtonMargin);
  /* font-family: var(--fontNormal); */
}

.buttonContainer .button .listIcon {
  position: absolute;
  right: 5px;
  bottom: 14px;
}

.googleBoundingBox {
  width: 75px;
  height: 75px;
  background-color: rgba(var(--boundingBoxGreenFull));
}

.googleBoundingBox .touchIcon {
  width: 75px;
  height: 75px;
}

/*---------------------------- SOURCE SELECTION BUTTON -----------------------------------*/
.arrow {
  position: absolute;
  top: 20px;
  left: 12px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid rgb(var(--audiWhite));
}

.phoneToggleText {
  width: unset;
}

.phoneToggleTextContainer {
  max-width: 466px;
  min-width: var(--minTouchSize);
  display: flex;
  align-items: center;
  padding: 0 var(--iA1);
  box-sizing: border-box;
}

.button .phoneToggleTextContainer > .listIcon {
  position: unset;
  right: unset;
  bottom: unset;
}

.phoneToggleText .arrow {
  position: unset;
  left: unset;
  top: unset;
}

.phoneToggleText .textPrio2 {
  max-width: calc(100% - var(--listIconSize) - 20px);
  padding: 0 var(--iA1);
}

/*---------------------------- TABS -----------------------------------*/

.tabs {
  position: relative;
  flex: 1;
  height: 100%;
  overflow: hidden;
}

.tabCluster {
  height: 100%;
  white-space: nowrap;
  overflow-x: scroll;
}

.tabCluster::-webkit-scrollbar {
  display: none;
}

.tabCluster .button {
  display: inline-flex;
  height: 100%;
  min-width: var(--minTouchSize);
  width: auto;
  justify-content: center;
}

.tabCluster .button .textWrapper {
  display: flex;
  position: relative;
  width: 100%;
  align-items: center;
  justify-content: center;
}
.tabCluster .button .textWrapper .textPrio2 {
  margin: 0 var(--textButtonMargin);
}

.tabCluster .button:not(.activated) .textWrapper {
  opacity: var(--secondaryTextOpacity);
}

.tabCluster .button .textWrapper .listIcon {
  margin-right: var(--iA1);
}

.tabCluster .button .textWrapper .listIcon:not(.hidden) + .textPrio2 {
}

.tabCluster .button .tab {
  max-width: var(--tabMaxWidth);
}

.tabbar.overflowLeft.overflowRight .tabs .tabCluster {
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(var(--audiBlack), 0) 10px,
    rgba(var(--audiBlack), 1) 25px,
    rgba(var(--audiBlack), 1) calc(100% - 25px),
    rgba(var(--audiBlack), 0) calc(100% - 10px)
  );
}

.tabbar.overflowLeft .tabs .tabCluster {
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(var(--audiBlack), 0) 10px,
    rgba(var(--audiBlack), 1) 25px
  );
}

.tabbar.overflowRight .tabs .tabCluster {
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(var(--audiBlack), 1) calc(100% - 25px),
    rgba(var(--audiBlack), 0) calc(100% - 10px)
  );
}

.appSelectorTitle {
  position: relative;
  align-items: center;
  display: flex;
  flex: 1;
}
.appSelectorTitle p {
  margin-bottom: 10px;
  margin-left: var(--cA1);
  margin-right: var(--cA1);
}
/*---------------------------- TITLELINE WITH ARROWS --------------------------------------*/
.SE_0058_TitleLineWithArrows {
  position: relative;
  flex: 1;
  overflow-x: hidden;
  height: calc(100%);
}

/*---------------------------- CLIMATE SPECIAL BEHAVIOR -----------------------------------*/

.tabbar.climate .tabs .tabCluster {
  position: absolute;
  max-width: 100%;
  left: 50%;
  transform: translateX(-50%);
}
/*---------------------------- LOGBOOK SPECIAL BEHAVIOR -----------------------------------*/

/*----------------------------- UE_0404_TITLELINE --------------------------------------*/
.UE_0404_Titleline {
}

.UE_0404_Titleline .button {
  padding-bottom: unset;
  min-height: calc(100%);
  top: var(--iconShift0402);
}

.UE_0404_Titleline .button .fullSize {
  height: calc(100%);
}

.UE_0404_Titleline .providerIcon.large {
  width: 200px;
}

/*--------------------------- UE_0501_INPUTFIELD -----------------------------------------*/
.UE_0501_InputField {
  position: relative;
  flex: 1;
  overflow-y: hidden;
  height: calc(100%);
}

.UE_0501_InputField.firstElement {
  margin-left: -5px;
}
.UE_0501_InputField.lastElement {
  margin-right: -5px;
}

.UE_0501_InputField .inputField {
  background-color: unset;
}
.UE_0501_InputField .inputField .interactionBackground {
  background-color: unset;
  border: 0;
}

/*--------------------------- STAND ALONE LOGIK -----------------------------------------*/

.UE_0404_Titleline .textPrio2 {
  flex: none;
}

/*----------------------FULL BACKGROUND VERSION-----------------------------*/
.fullBackgroundButton {
  display: inline;
  width: var(--minTouchSize);
}
.tabbar .fullBackgroundButton.driveSelect {
  display: inline;
  margin-left: 30px;
}

.fullBackgroundTitle {
  width: calc(100% - (var(--minTouchSize) + 4));
  position: relative;
  height: 100%;
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  box-sizing: border-box;
}
.fullBackgroundTitle.driveSelect_smallTile {
  justify-content: center;
}
.fullBackgroundTitle.driveSelect p {
  max-width: 435px;
}
.fullBackgroundTitle.driveSelect_smallTile p {
  max-width: 380px;
}

.fullBackgroundTitle p {
  margin: 0 20px;
  width: fit-content;
}

/*----------------------- zweiDrtittelCockpitTilePID---------------------------*/

.zdctp {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 var(--hA3);
}

.temperature,
.uber,
.hour {
  display: flex;
  align-items: baseline;
}

.zdctp .textPrio3 {
  margin-left: var(--hA1);
}

@media only screen and (min-width: 2219px) {
  :root {
    --heightBleed: 10px;
  }
}

/*--------------------------- SETTINGS -----------------------------------------*/
.settings {
  top: 150px;
  position: relative;
  height: calc(100vh - 150px);
  overflow: scroll;
}
