/* SELECTION SCREEN */
.selectionScreen {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
}

.decoratorContainer {
  height: 70%;
  width: 70%;
  position: absolute;
  left: 33%;
  top: 16%;
}

.decoratorContainer .decoratorCarColor {
  position: absolute;
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-size: contain;
  z-index: 1;
  pointer-events: none;
}

.decoratorWrapper {
  height: 100%;
  width: 100%;
  position: relative;
}
.decorator,
.lighting {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.lightingWrapper {
  height: 200px;
  width: 820px;
  position: absolute;
  left: 356px;
  top: 238px;
  pointer-events: none;
}

/* CONNECTORS AND BUTTONS */

.lightingConnector {
  position: absolute;
  top: 260px;
  left: 570px;
}

.decoratorConnector {
  position: absolute;
  top: 242px;
  left: 1080px;
}
