/* CLASSIFICATION: CONFIDENTIAL */

/**-----------------adjustable values--------------------**/
.UE_1401_Vertical_Bargraph.oil {
  --barWidth: 25px;
  --barHeight: 288px;
  --topSegmentHeight: 0px;
  --segmentSpacing: 6px;
  --fillingTopBorder: 6px;
  --fillingOuterBorder: 1px;
  --markLabelHeight: 40px;
  --markLaneOffset: 4px;
  --bottomSegmentHeight: calc(
    (var(--barHeight) - var(--topSegmentHeight) - var(--segmentSpacing)) / 3
  );
  --centerSegmentHeight: calc(var(--bottomSegmentHeight) * 2);
}
.UE_1401_Vertical_Bargraph.oil.small {
  --barWidth: 19px;
  --barHeight: 224px;
}

.UE_1401_Vertical_Bargraph.aroma {
  --barWidth: 80px;
  --barHeight: 304px;
  --segmentSpacing: 2px;
  --fillingTopBorder: 5px;
  --fillingOuterBorder: 1px;
  --separatorWidth: 50px;
  --aromaLabelWidth: 112px;
}

/**-----------------body--------------------**/
:global(#root) > .UE_1401_Vertical_Bargraph {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgb(var(--audiBlack));
}

:global(#root) > .UE_1401_Vertical_Bargraph .bodyWrapper {
  padding: 20px;
  width: 270px;
  height: 320px;
}

:global(#root) > .UE_1401_Vertical_Bargraph.light .bodyWrapper {
  background-color: rgb(var(--audiLightGrey));
  background-size: 100%;
}

/**-----------------bargraph elements--------------------**/
.UE_1401_Vertical_Bargraph .verticalBargraph {
  display: flex;
  flex-direction: column;
  position: relative;
  top: 0px;
  left: 0px;
  justify-content: space-between;
  height: calc(var(--barHeight) + 2px);
}
.UE_1401_Vertical_Bargraph.light .verticalBargraph {
  height: calc(var(--barHeight) + 4px);
}

.UE_1401_Vertical_Bargraph .verticalBargraph .bar {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  top: 0px;
  left: 0px;
  height: calc(var(--barHeight) - 2px);
  width: calc(var(--barWidth) - 2px);
  border: 2px solid rgba(var(--audiWhite), 0.8);
  background-color: transparent;
  -webkit-mask-image: linear-gradient(
    to top,
    rgb(var(--audiBlack)) 0px,
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) + 1px - var(--segmentSpacing) / 2),
    transparent
      calc(var(--bottomSegmentHeight) + 1px - var(--segmentSpacing) / 2),
    transparent
      calc(var(--bottomSegmentHeight) + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) + 1px + var(--centerSegmentHeight)),
    transparent
      calc(var(--bottomSegmentHeight) + 1px + var(--centerSegmentHeight)),
    transparent
      calc(
        var(--bottomSegmentHeight) + 1px + var(--centerSegmentHeight) +
          var(--segmentSpacing)
      ),
    rgb(var(--audiBlack))
      calc(
        var(--bottomSegmentHeight) + 1px + var(--centerSegmentHeight) +
          var(--segmentSpacing)
      ),
    rgb(var(--audiBlack)) 100%
  );
}

.UE_1401_Vertical_Bargraph.oil.light .verticalBargraph .bar,
.UE_1401_Vertical_Bargraph.aroma.light .verticalBargraph .bar {
  height: calc(var(--barHeight));
  width: calc(var(--barWidth));
  border: 2px solid rgb(var(--audiBlack));
  background-color: rgb(var(--audiBlack));
  -webkit-mask-image: unset;
}

.UE_1401_Vertical_Bargraph .verticalBargraph .bar .filling {
  position: absolute;
  top: unset;
  bottom: -1px;
  left: -1px;
  height: calc(50% - 7px);
  width: calc(var(--barWidth) - 2px);
  border: 1px solid rgb(var(--audiWhite));
  border-top: var(--fillingTopBorder) solid rgb(var(--audiWhite));
  background-color: rgba(var(--audiWhite), 0.8);
}

.UE_1401_Vertical_Bargraph.light .verticalBargraph .bar .filling {
  bottom: 0px;
  left: 0px;
  -webkit-mask-image: linear-gradient(
    to top,
    rgb(var(--audiBlack)) 0px,
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) - var(--segmentSpacing) / 2),
    transparent calc(var(--bottomSegmentHeight) - var(--segmentSpacing) / 2),
    transparent calc(var(--bottomSegmentHeight) + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc(var(--bottomSegmentHeight) + var(--centerSegmentHeight)),
    rgb(var(--audiBlack))
      calc(
        var(--bottomSegmentHeight) + var(--centerSegmentHeight) + 2px +
          var(--segmentSpacing)
      ),
    rgb(var(--audiBlack)) 100%
  );
}

.UE_1401_Vertical_Bargraph .verticalBargraph .mark {
  position: relative;
  display: flex;
  flex-direction: row;
  width: min-content;
  height: var(--markLabelHeight);
  justify-content: flex-end;
  align-items: center;
  margin-left: 1px;
}
.UE_1401_Vertical_Bargraph.light .verticalBargraph .mark {
  margin-left: 2px;
}

.UE_1401_Vertical_Bargraph .verticalBargraph .mark .label .textPrio2 {
  margin-left: var(--zA8);
}
.UE_1401_Vertical_Bargraph.light .verticalBargraph .mark .label p {
  color: rgb(var(--audiBlack));
  font-family: var(--fontNormal);
}

.UE_1401_Vertical_Bargraph .verticalBargraph .mark.max {
  margin-top: -18px;
}
.UE_1401_Vertical_Bargraph.light .verticalBargraph .mark.max {
  margin-top: -19px;
}

.UE_1401_Vertical_Bargraph .verticalBargraph .mark.min {
  margin-bottom: calc(
    var(--bottomSegmentHeight) - (var(--markLabelHeight) / 2) +
      var(--markLaneOffset) - 1px
  );
}
.UE_1401_Vertical_Bargraph.light .verticalBargraph .mark.min {
  margin-bottom: calc(
    var(--bottomSegmentHeight) - (var(--markLabelHeight) / 2) +
      var(--markLaneOffset)
  );
}

.UE_1401_Vertical_Bargraph .verticalBargraph .mark .separator {
  background-color: rgb(var(--audiWhite));
  height: 2px;
  width: var(--barWidth);
  margin-top: var(--markLaneOffset);
}

/**----------------- Aroma --------------------**/
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .bar {
  margin-left: calc(
    var(--aromaLabelWidth) + var(--zA8) + var(--separatorWidth)
  );
  -webkit-mask-image: linear-gradient(
    to top,
    rgb(var(--audiBlack)) 0px,
    rgb(var(--audiBlack))
      calc(var(--barHeight) / 4 + 1px - var(--segmentSpacing) / 2),
    transparent calc(var(--barHeight) / 4 + 1px - var(--segmentSpacing) / 2),
    transparent calc(var(--barHeight) / 4 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc(var(--barHeight) / 4 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 2 + 1px - var(--segmentSpacing) / 2),
    transparent
      calc((var(--barHeight) / 4) * 2 + 1px - var(--segmentSpacing) / 2),
    transparent
      calc((var(--barHeight) / 4) * 2 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 2 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 3 + 1px - var(--segmentSpacing) / 2),
    transparent
      calc((var(--barHeight) / 4) * 3 + 1px - var(--segmentSpacing) / 2),
    transparent
      calc((var(--barHeight) / 4) * 3 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 3 + 1px + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack)) var(--barHeight)
  );
}
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .bar .filling {
  -webkit-mask-image: linear-gradient(
    to top,
    rgb(var(--audiBlack)) 0px,
    rgb(var(--audiBlack)) calc(var(--barHeight) / 4 - var(--segmentSpacing) / 2),
    transparent calc(var(--barHeight) / 4 - var(--segmentSpacing) / 2),
    transparent calc(var(--barHeight) / 4 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack)) calc(var(--barHeight) / 4 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 2 - var(--segmentSpacing) / 2),
    transparent calc((var(--barHeight) / 4) * 2 - var(--segmentSpacing) / 2),
    transparent calc((var(--barHeight) / 4) * 2 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 2 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 3 - var(--segmentSpacing) / 2),
    transparent calc((var(--barHeight) / 4) * 3 - var(--segmentSpacing) / 2),
    transparent calc((var(--barHeight) / 4) * 3 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack))
      calc((var(--barHeight) / 4) * 3 + var(--segmentSpacing) / 2),
    rgb(var(--audiBlack)) var(--barHeight)
  );
}
.UE_1401_Vertical_Bargraph .verticalBargraph .mark.aroma,
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .mark {
  display: none;
}
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .mark.aroma {
  display: flex;
  position: absolute;
}
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .mark.aroma .label {
  margin-right: var(--zA8);
  width: var(--aromaLabelWidth);
}
.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .mark.aroma p {
  text-align: right;
}

.UE_1401_Vertical_Bargraph.aroma .verticalBargraph .mark .separator {
  width: var(--separatorWidth);
}
.UE_1401_Vertical_Bargraph.light.aroma .verticalBargraph .mark .separator {
  background-color: rgb(var(--audiBlack));
}

/**-----------------Configuration--------------------**/

.settings {
  top: 400px;
  position: absolute;
}
