/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --pwmPPELeftBar6013Width: 113px;
  --pwmPPERightBar6013Width: 112px;
  --pwmPPEBar6013CenterGap: 361px;
}

:global(#root) > .UE_6013_Powermeter_PPE_mini {
}

.UE_6013_Powermeter_PPE_mini {
  position: absolute;
  width: calc(
    var(--pwmPPELeftBar6013Width) + var(--pwmPPEBar6013CenterGap) +
      var(--pwmPPERightBar6013Width) + 1px
  );
  height: 70px;
}

/*--------------------------- Labels ----------------------------------*/

.powermeterKlein .textLabels {
  position: absolute;
  top: -24px;
  width: 100%;
}

.textLabels .chargeLbl,
.textLabels .powerLbl {
  position: absolute;
  background-color: rgb(var(--audiWhite));
  height: 16px;
}

.textLabels .chargeLbl {
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/CHARGElblBig.png);
  width: 94px;
}
.textLabels .lblLeft {
  left: 9px;
}

.textLabels .powerLbl {
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/POWERlblBig.png);
  width: 86px;
}
.textLabels .lblRight {
  left: 14px;
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6013_Powermeter_PPE_mini.UE_6013_00_state_1
  .powermeterKlein
  .textLabels
  > * {
  background-color: rgb(var(--bargraphInactive));
}

/*--------------------------- powermeter ----------------------------------*/

.powermeterKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.powermeterKlein .leftbar {
  position: absolute;
  width: var(--pwmPPELeftBar6013Width);
  top: 34px;
  left: 1px;
}

.powermeterKlein .rightbar {
  position: absolute;
  width: calc(var(--pwmPPERightBar6013Width));
  top: 34px;
  left: calc(var(--pwmPPELeftBar6013Width) + var(--pwmPPEBar6013CenterGap) + 1px);
}

/*---------- Config --------*/

.pwrConfig {
  position: absolute;
  margin-top: 100px;
  width: 100%;
  color: white;
}

.sliderDemo {
  margin-top: 100px;
  width: var(--gaugeBarWidth);
  color: white;
}

.sliderDemo .pwrslider {
  width: 100%;
}

.ThrottleLimitedDemo {
  color: white;
}

.RecuLimitedDemo {
  color: white;
}

.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
