/* CLASSIFICATION: CONFIDENTIAL */

/* Standalone */

:global(#root) > .SE_0031_AppSelector {
  position: absolute;
  padding: 8px;
  background-color: rgb(var(--bodyColorGrey));
  box-sizing: border-box;
  width: 100%;
  height: 80vh;
  margin: 0;
}

:global(#root)
  > .SE_0031_AppSelector
  .appSelector
  .appList::-webkit-scrollbar-track {
  display: none;
}

/* .SE_0031_AppSelector */

.SE_0031_AppSelector {
}

.appSelector {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  z-index: 20;
}

/*----------------------- APP SELECTOR LIST-----------------------*/
.appList {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding-left: calc(var(--uiStateMargin) * 2 + var(--hA1));
  padding-right: calc(var(--uiStateMargin) * 2 - var(--zA1) + var(--hA1));
  padding-bottom: var(--uiStateMargin);
  margin-bottom: 2px;
  overflow-y: scroll;
  box-sizing: border-box;
}

.appLine {
  display: grid;
  grid-gap: var(--uiStateMargin);
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
}

.appButton {
  height: calc(100% - var(--uiStateMargin));
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: var(--uiStateMargin);
  min-width: 0;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiWhite), 0.06),
    rgba(var(--audiWhite), 0.03)
  );
  box-sizing: border-box;
}

.appButton > div:last-of-type {
  flex-direction: column;
}

.appLine:last-child .appButton {
  margin-bottom: 0;
  height: 100%;
}

.appButton.disabled {
  opacity: var(--touchDisabledState);
}

.textContainer {
  overflow: hidden;
  width: 100%;
  z-index: 1;
  padding: 0 var(--textButtonMargin);
  margin-bottom: var(--iA1);
  box-sizing: border-box;
  text-align: center;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.textContainer .textPrio2 {
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: rgb(var(--audiWhite));
}

.iconContainer {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.iconContainer .daIcon {
  background-size: 100%;
}

/* Audi App Icon: DirectAccess size 72px */
.iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A5_apple_car_play.svg);
}

/* Add button / Bluetooth Icon normal touchIcon size: 50px*/
.appLine.firstRow .appButton:nth-child(1) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5AC_connect_device.svg);
}
.appLine.firstRow .appButton:nth-child(2) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5AA_media_bluetooth.svg);
}

.appLine.firstRow .appButton:nth-child(3) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A6_android_auto.svg);
}

.appLine.secondRow .appButton:nth-child(1) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A7_main_menue_Radio.svg);
}
.appLine.secondRow .appButton:nth-child(2) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A8_media_ami_usb1.svg);
}
.appLine.secondRow .appButton:nth-child(3) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A9_media_ami_usb2.svg);
}
.appLine.secondRow .appButton:nth-child(4) .iconContainer .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5AB_radio_sirius.svg);
}

.appLine.firstRow .appButton:last-child .iconContainer .daIcon {
  background-image: url(../../../../../Templates/02_Overlay/T_0201_AppGrid/01_HTML/img/spotify-icon.png);
}

.Button__button {
  width: var(--minTouchSize);
  height: 100%;
  position: relative;
}

.watchface {
  display: flex;
  height: 100%;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 1),
    rgba(var(--audiBlack), 0.3)
  );
}

.watchface .UE_0101_ActionButton {
  height: 80px;
}

.clockTile {
  height: 60%;
  width: 250px;
  margin-left: 100px;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  background-size: 100vw 100vh;
  background-attachment: fixed;
}

.SE_0093_01_Clock_DashboardTile {
  height: calc(100% - 80px);
}

.settings {
  position: absolute;
  top: 81vh;
  height: 19vh;
}

@media only screen and (max-width: 1768px) {
  .clockTile {
    height: 80%;
  }
}
