/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_6021_TachoTube_PPE {
  position: absolute;
  background-color: black;
  margin: 0;
  width: var(--screenWidthFPK);
  height: var(--screenHeightFPK);
}

/*-------------------- .T_0112_InputMethods --------------------*/

.UE_6021_TachoTube_PPE {
}

.testTube {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-image: url('../img/test_tube.png');
  background-repeat: no-repeat;
}

.UE_6021_TachoTube_PPE.hideText .testTube {
  background-image: url('../img/tachoTubeWithoutText.png');
  background-position: center top;
}

.settings {
  position: absolute;
  top: 600px;
}
