/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --conbarLeftBarWidth: 149px;
  --conbarRightBarWidth: 252px;
  --conbarRightBarCenterGap: 3px;
}

:global(#root) > .UE_6042_Verbrauchsbalken_klein {
  left: 15px;
}

.UE_6042_Verbrauchsbalken_klein {
  position: absolute;
  width: calc(
    var(--conbarLeftBarWidth) + var(--conbarRightBarCenterGap) +
      var(--conbarRightBarWidth)
  );
  height: 70px;
}

.UE_6042_Verbrauchsbalken_klein.UE_6042_03_inactive,
.UE_6042_Verbrauchsbalken_klein.UE_6042_07,
.UE_6042_Verbrauchsbalken_klein.UE_6042_01_inactive,
.UE_6042_Verbrauchsbalken_klein.UE_6042_05 {
  width: calc(var(--conbarRightBarWidth) + 1px);
}

/*--------------------------- Labels ----------------------------------*/

.consumptionKlein .valueLabels {
  position: absolute;
  top: 10px;
  left: -2px;
  width: 100%;
}

.valueLabels .SpritesheetDigits {
  position: absolute;
}
.UE_6042_00_inactive .valueLabels .SpritesheetDigits.digit1,
.UE_6042_04 .valueLabels .SpritesheetDigits.digit1 {
  left: 38px;
}
.UE_6042_00_inactive .valueLabels .SpritesheetDigits.digit2,
.UE_6042_04 .valueLabels .SpritesheetDigits.digit2 {
  left: 143px;
}
.UE_6042_00_inactive .valueLabels .SpritesheetDigits.digit3,
.UE_6042_04 .valueLabels .SpritesheetDigits.digit3 {
  left: 239px;
}
.UE_6042_00_inactive .valueLabels .SpritesheetDigits.digit4,
.UE_6042_04 .valueLabels .SpritesheetDigits.digit4 {
  right: 34px;
}
.UE_6042_00_inactive .valueLabels .minus,
.UE_6042_04 .valueLabels .minus {
  width: 7px;
  height: 2px;
  top: 8px;
  left: 31px;
  position: absolute;
  background-color: rgb(var(--audiWhite));
}

/*--------------------------- type 05 ----------------------------------*/
.UE_6042_03_inactive,
.UE_6042_07 {
  --conbarLeftBarWidth: 0px;
  --conbarRightBarWidth: 403px;
  --conbarRightBarCenterGap: 0px;
}

.UE_6042_03_inactive .valueLabels .SpritesheetDigits.digit1,
.UE_6042_07 .valueLabels .SpritesheetDigits.digit1 {
  left: 0px;
}
.UE_6042_03_inactive .valueLabels .SpritesheetDigits.digit2,
.UE_6042_07 .valueLabels .SpritesheetDigits.digit2 {
  left: 57px;
}
.UE_6042_03_inactive .valueLabels .SpritesheetDigits.digit3,
.UE_6042_07 .valueLabels .SpritesheetDigits.digit3 {
  left: 118px;
}
.UE_6042_03_inactive .valueLabels .SpritesheetDigits.digit4,
.UE_6042_07 .valueLabels .SpritesheetDigits.digit4 {
  left: 193px;
}
.UE_6042_03_inactive .valueLabels .SpritesheetDigits.digit5,
.UE_6042_07 .valueLabels .SpritesheetDigits.digit5 {
  left: 270px;
}
.UE_6042_03_inactive .valueLabels .maxLbl,
.UE_6042_07 .valueLabels .maxLbl {
  position: absolute;
  background-color: rgb(var(--audiWhite));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/MAXlbl.png);
  width: 52px;
  height: 14px;
  top: 1px;
  right: 11px;
}
.UE_6042_03_inactive .consumptionKlein .rightbar,
.UE_6042_07 .consumptionKlein .rightbar {
  left: calc(var(--conbarLeftBarWidth) + var(--conbarRightBarCenterGap));
}

/*--------------------------- type 06 ----------------------------------*/
.UE_6042_02_inactive,
.UE_6042_06 {
  --conbarLeftBarWidth: 65px;
  --conbarRightBarWidth: 336px;
  --conbarRightBarCenterGap: 3px;
}

.UE_6042_02_inactive .valueLabels .SpritesheetDigits.digit1,
.UE_6042_06 .valueLabels .SpritesheetDigits.digit1 {
  left: 59px;
}
.UE_6042_02_inactive .valueLabels .SpritesheetDigits.digit2,
.UE_6042_06 .valueLabels .SpritesheetDigits.digit2 {
  left: 125px;
}
.UE_6042_02_inactive .valueLabels .SpritesheetDigits.digit3,
.UE_6042_06 .valueLabels .SpritesheetDigits.digit3 {
  left: 189px;
}
.UE_6042_02_inactive .valueLabels .SpritesheetDigits.digit4,
.UE_6042_06 .valueLabels .SpritesheetDigits.digit4 {
  left: 269px;
}
.UE_6042_02_inactive .valueLabels .SpritesheetDigits.digit5,
.UE_6042_06 .valueLabels .SpritesheetDigits.digit5 {
  left: 348px;
}

/*--------------------------- type 07 ----------------------------------*/
.UE_6042_01_inactive,
.UE_6042_05 {
  --conbarLeftBarWidth: 0px;
  --conbarRightBarWidth: 403px;
  --conbarRightBarCenterGap: 0px;
}

.UE_6042_01_inactive .valueLabels .SpritesheetDigits.digit1,
.UE_6042_05 .valueLabels .SpritesheetDigits.digit1 {
  left: 0px;
}
.UE_6042_01_inactive .valueLabels .SpritesheetDigits.digit2,
.UE_6042_05 .valueLabels .SpritesheetDigits.digit2 {
  left: 158px;
}
.UE_6042_01_inactive .valueLabels .SpritesheetDigits.digit3,
.UE_6042_05 .valueLabels .SpritesheetDigits.digit3 {
  left: 335px;
}

.UE_6042_01_inactive .consumptionKlein .rightbar,
.UE_6042_05 .consumptionKlein .rightbar {
  left: calc(var(--conbarLeftBarWidth) + var(--conbarRightBarCenterGap));
}

/*--------------------------- legend ----------------------------------*/
.consumptionKlein .legend {
  position: absolute;
  display: flex;
  align-items: baseline;
  justify-content: center;
  top: 47px;
  width: 100%;
}

.consumptionKlein .legend .legendValue {
  text-align: right;
  margin-left: 8px;
  margin-right: 5px;
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6042_Verbrauchsbalken_klein.UE_6042_01_inactive
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6042_Verbrauchsbalken_klein.UE_6042_02_inactive
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6042_Verbrauchsbalken_klein.UE_6042_03_inactive
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6042_Verbrauchsbalken_klein.UE_6042_00_inactive
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6042_Verbrauchsbalken_klein.UE_6042_03_inactive .valueLabels .maxLbl,
.UE_6042_Verbrauchsbalken_klein.UE_6042_00_inactive .valueLabels .minus {
  background-color: rgb(var(--bargraphInactive));
}

.UE_6042_Verbrauchsbalken_klein.UE_6042_01_inactive .legend > *,
.UE_6042_Verbrauchsbalken_klein.UE_6042_02_inactive .legend > *,
.UE_6042_Verbrauchsbalken_klein.UE_6042_03_inactive .legend > *,
.UE_6042_Verbrauchsbalken_klein.UE_6042_00_inactive .legend > * {
  color: rgb(var(--bargraphInactive));
}

/*--------------------------- powermeter ----------------------------------*/

.consumptionKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.consumptionKlein .leftbar {
  position: absolute;
  width: var(--conbarLeftBarWidth);
  top: 34px;
  left: -1px;
}

.consumptionKlein .rightbar {
  position: absolute;
  width: calc(var(--conbarRightBarWidth));
  top: 34px;
  left: calc(var(--conbarLeftBarWidth) + var(--conbarRightBarCenterGap) - 1px);
}

/*---------- Config --------*/

.pwrConfig {
  position: absolute;
  margin-top: 100px;
  width: 100%;
  color: white;
}

.sliderDemo {
  margin-top: 100px;
  width: var(--gaugeBarWidth);
  color: white;
}

.sliderDemo .pwrslider {
  width: 100%;
}

.ThrottleLimitedDemo {
  color: white;
}

.RecuLimitedDemo {
  color: white;
}

.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
