/* ------------------- CURSOR ELEMENT ------------------- */

.cursorEntry {
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 45px;
  z-index: 22;
  max-width: calc(100% - 90px);
  margin-right: 60px;
}

.cursorEntry:not(:last-of-type) {
  border-bottom: 1px solid rgba(var(--audiWhite), 0.1);
}

.UE_1009_C2Cursor {
  position: absolute;
  height: var(--minTouchSize);
  left: 0;
}

.cursorEntry p,
.cursorEntry .uiState {
  z-index: 1;
}
