/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_9001_Abdecker_IFA_links {
  position: absolute;
  width: 620px;
  height: var(--fidMainContentHeight);
}

.UE_9001_Abdecker_IFA_links {
  position: absolute;
  width: 620px;
  height: var(--fidMainContentHeight);
}

.UE_9001_Abdecker_IFA_links .textLine {
  position: absolute;
  display: flex;
  width: 300px;
  height: 49px;
  right: 210px;
  justify-content: flex-end;
  align-items: baseline;
}

.UE_9001_Abdecker_IFA_links .textLine.top {
  top: 92px;
}
.UE_9001_Abdecker_IFA_links .textLine.bottom {
  top: 160px;
}

.UE_9001_Abdecker_IFA_links .textLine .digit.speed,
.UE_9001_Abdecker_IFA_links .textLine .digit.kWh {
  margin-left: var(--zA8);
  margin-right: var(--zA8);
  width: 90px;
  text-align: right;
}
.UE_9001_Abdecker_IFA_links.UE_9001_00 .textLine.bottom .digit {
  width: 155px;
}

.UE_9001_Abdecker_IFA_links .textLine .consumptionUnit {
  text-align: left;
  align-self: center;
  width: 80px;
}
.UE_9001_Abdecker_IFA_links .textLine .consumptionUnit.singleLine {
  align-self: unset;
}

.UE_9001_Abdecker_IFA_links .textLine .consumptionUnit.singleLine .textUnits {
  text-align: left;
}
.UE_9001_Abdecker_IFA_links .textLine .textUnits.speed {
  width: var(--boardCompUnitWidth);
  text-align: left;
}
.UE_9001_Abdecker_IFA_links .textLine .textUnits {
  text-align: center;
}
.UE_9001_Abdecker_IFA_links .textLine .consumptionUnit .divider {
  width: 100%;
  height: 1px;
  background-color: rgb(var(--fpkGrey));
}

.UE_9001_Abdecker_IFA_links.UE_9001_01 .textLine .listIcon {
  align-self: center;
  margin-right: var(--zA8);
}
.UE_9001_Abdecker_IFA_links.UE_9001_01 .textLine.top .listIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/FID-Icons/List/E10C_fuel.png);
}
.UE_9001_Abdecker_IFA_links.UE_9001_01 .textLine.bottom .listIcon {
  background-color: rgb(var(--FIDtelltaleGreen));
  background-image: unset;
  -webkit-mask-image: url(../../../../../Globals/icons/IconMaster/FID-Icons/List/E151_phev_range_electric.png);
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

.settings {
  position: absolute;
  top: 400px;
  width: 1200px;
}
