/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_0401_Notification {
  font-size: 0;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--ambientBackground1));
  width: 100%;
  height: 100%;
}

.T_0401_Notification {
}

.notification {
  height: auto; /*336px;*/
  margin-bottom: 15px;
  position: relative;
  display: block;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.07),
    rgba(var(--interactionBackground), 0)
  );
  box-shadow: none;
}

.notification .handle {
  position: absolute;
  bottom: 20px;
  height: 4px;
  width: 220px;
  background-color: rgb(var(--audiWhite));
  outline: 3px solid rgba(var(--audiBlack), 0.6);
  border-radius: 4px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.notification .notiDivider {
  margin-left: 80px;
  margin-right: 72px;
  border-top: 2px solid var(--tabbarSeparatorColor);
}

.notification .UE_0401_OperationPanel {
  width: 100%;
  height: var(--minTouchSize);
  position: relative;
}

.UE_0401_OperationPanel .interactionBackground {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

/*-------------------------- HEADLINE -----------------------------*/

.notification .notiHeadline {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  margin-bottom: 9px;
  margin-right: 80px;
  margin-left: 23px;
  align-items: center;
}

.notification .notiHeadline .notiTitle {
  margin-left: 20px;
}

.notification .notiHeadline .notiAddInfo p,
.notification .notiHeadline .notiTime p {
  color: rgba(var(--audiWhite), 0.5);
}

.notification .notiHeadline .notiExpand {
  width: var(--minTouchSize);
  height: 56px;
  position: relative;
  margin-left: auto;
}

.notification .notiHeadline .notiExpand .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  top: -14px;
  margin-left: 8px;
}

.notification .notiHeadline .notiExpand .button .uiState {
  height: 79px;
  margin-top: 0px;
}

.notification .notiHeadline .notiExpand .button .touchIcon {
  top: 41px;
}

/*-------------------------- CONTENT -----------------------------*/

.notification .notiContent {
  margin-top: var(--iA1);
  margin-bottom: 15px;
  margin-left: 80px;
  margin-right: 80px;
  display: flex;
  flex-direction: row;
  flex: 1;
  justify-content: space-between;
}

.notification .notiContent .notiContentCol1 {
  display: flex;
  flex: 1;
  flex-direction: column;
}

.notification .notiContent .notiContentCol1 .notiTitle p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.notification .notiContent .notiContentCol1 .notiText {
  margin-top: 10px;
  display: flex;
  max-height: calc(2 * var(--lineHeightPrio2));
}

.notification .notiContent .notiContentCol1 .notiText p + p {
  margin-top: 0px;
  margin-left: var(--iA2);
  flex-shrink: 0;
  align-self: flex-end;
  color: rgb(
    var(--ambientcolor1)
  ); /*this is the icon color as defined in AHCP3-31181. If iconColor would be a white or grey color then use ambientcolor*/
}

.notification .notiContent .notiContentCol1 .notiText p {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break-spaces;
  color: rgba(var(--audiWhite), 0.5);
}

.notification .notiContent .notiContentCol2 {
  display: flex;
  align-items: center;
  margin-left: var(--iA2);
}

.notification .notiContent .notiContentCol2 .notiImage {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg);
  width: var(--coverSizeList);
  height: var(--coverSizeList);
  border-radius: 50%;
  background-size: cover;
  background-repeat: no-repeat;
}

.notification.singleLine .notiTitleSingleLine {
  position: relative;
  width: 100%;
}
.notification.singleLine .notiTitleSingleLine p {
  position: relative;
  width: calc(100% - 160px);
  margin: 45px;
  margin-left: 80px;
  margin-right: 80px;
}

/*-------------------------- GROUP OPEN -----------------------------*/

.notification.groupOpen {
  margin-bottom: 0px;
}

.T_0401_Notification:first-of-type
  .notification.groupOpen
  .notiHeadline
  .notiAddInfo,
.T_0401_Notification:first-of-type
  .notification.groupOpen
  .notiHeadline
  .notiTime {
  display: none;
}

.T_0401_Notification:not(:first-of-type) .notification.groupOpen .notiHeadline {
  display: none;
}

.notification.stacked .UE_0401_OperationPanel {
  display: none;
}

/*-------------------------- STACKED -----------------------------*/

.notification.stacked {
  height: auto; /*218px;*/
  margin-bottom: 50px;
}

.notification .stackedcontainer {
  top: calc(27px + var(--popupOutlineWidth));
  position: relative;
  width: 100%;
  display: none;
}

.notification.stacked .stackedcontainer {
  display: block;
}

.notification .stackedcontainer .leerborder1 {
  width: calc(100% - 8px);
  height: 9px;
  position: relative;
  left: 3px;
  outline: var(--popupOutlineWidth) solid rgba(var(--audiBlack), 0.9);
  border-left: 1px solid rgba(var(--interactionBackground), 0.3);
  border-right: 1px solid rgba(var(--interactionBackground), 0.3);
  border-bottom: 1px solid rgba(var(--interactionBackground), 0.3);
  border-radius: var(--standardBorderRadius);
  background-color: rgba(var(--audiBlack), 1);
  clip-path: polygon(
    -5px 0px,
    -5px calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) 0px
  );
}

.notification .stackedcontainer .leerborder2 {
  width: calc(100% - 16px);
  height: 8px;
  position: relative;
  left: 7px;
  outline: var(--popupOutlineWidth) solid rgba(var(--audiBlack), 0.9);
  border-left: 1px solid rgba(var(--interactionBackground), 0.25);
  border-right: 1px solid rgba(var(--interactionBackground), 0.25);
  border-bottom: 1px solid rgba(var(--interactionBackground), 0.25);
  border-radius: var(--standardBorderRadius);
  background-color: rgba(var(--audiBlack), 1);
  clip-path: polygon(
    -5px 0px,
    -5px calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) 0px
  );
}

.notification .stackedcontainer .leerborder3 {
  width: calc(100% - 24px);
  height: 7px;
  position: relative;
  left: 11px;
  outline: var(--popupOutlineWidth) solid rgba(var(--audiBlack), 0.9);
  border-left: 1px solid rgba(var(--interactionBackground), 0.2);
  border-right: 1px solid rgba(var(--interactionBackground), 0.2);
  border-bottom: 1px solid rgba(var(--interactionBackground), 0.2);
  border-radius: var(--standardBorderRadius);
  background-color: rgba(var(--audiBlack), 1);
  clip-path: polygon(
    -5px 0px,
    -5px calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) calc(100% - -5px),
    calc(100% - -5px) 0px
  );
}

/*-------------------------- OPERATIONPANEL BUTTONS COLORED -----------------------------*/

.operationPanel.colored .button:nth-child(2) .touchIcon {
  background-color: rgb(var(--fpkGreen));
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
  background-image: none;
}

.operationPanel.colored .button:nth-child(3) .touchIcon {
  background-color: rgb(var(--fpkRed));
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
  background-image: none;
}

.operationPanel.colored .button:nth-child(2) .textPrio2 {
  color: rgb(var(--fpkGreen));
}

.operationPanel.colored .button:nth-child(3) .textPrio2 {
  color: rgb(var(--fpkRed));
}

/*-------------------------- SETTINGS -----------------------------*/
.settings {
  position: absolute;
  top: 400px;
}
