/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0049_ExtendedPlaybackControls {
  margin: 10px 0;
  height: var(--minTouchSize);
  width: var(--dashboardWidth);
  background-color: rgb(var(--bodyColorGrey));
}

.epcContainer {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.epcBackground {
  position: absolute;
  background-color: var(--buttonBackgroundColor);
  top: calc(var(--opticalTouchReduction) / 2);
  left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
  height: calc(100% - var(--opticalTouchReduction));
  border-radius: var(--standardBorderRadius);
}

/*- for transition purposes, follow the container height but do not exceed minTouchSize -*/
.button {
  position: relative;
  max-height: var(--minTouchSize);
  height: 100%;
  width: calc(100% / 5);
  opacity: 0;
  transition: all var(--animationDuration) ease;
}

/* first line just for standalone purpose */
.epcOn .button,
.epcOn .button {
  opacity: 1;
}
