:global(#root) > .SE_0077_UpdateRunning {
  width: 100%;
  height: 100%;
  margin: 0px;
}
.updateRunning {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: url(../img/SE_0077_UpdateRunning.webp);
  box-sizing: border-box;
  justify-content: center;
  align-items: flex-end;
}

@media only screen and (max-width: 1768px) {
  .updateRunning {
    background-image: url(../img/SE_0077_UpdateRunning_LOW.webp);
  }
}
