/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --laneIconSize: 44px;
}

:global(#root) > .SE_5702_Navigation_GB {
  position: absolute;
  height: 325px;
  width: 1920px;
}

.SE_5702_01_detailed_Guidance_right {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 620px;
  height: 100%;
}
.SE_5702_01_detailed_Guidance_right.cardVariant {
  position: absolute;
  width: 100%;
  height: 100%;
}

.SE_5702_01_detailed_Guidance_right .background {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-mask-image: url(../../../../../UE_Elemente/90_FIDAbdecker/UE_9000_Abdecker_IFA_rechts/01_HTML/img/UE_9000_Abdecker_IFA_rechts_background.png);
  background-image: radial-gradient(
    170% 80% at -75% 105%,
    rgba(var(--ambientcolor1_background), 0.26) 0%,
    rgba(var(--audiGrey), 0.05)
  );
  background-color: rgba(var(--audiBlack), 0.7);
}
.SE_5702_01_detailed_Guidance_right .kontur {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../../../../../UE_Elemente/90_FIDAbdecker/UE_9000_Abdecker_IFA_rechts/01_HTML/img/UE_9000_Abdecker_IFA_rechts_kontur.png);
  opacity: 0.2;
}
.SE_5702_01_detailed_Guidance_right.cardVariant .background,
.SE_5702_01_detailed_Guidance_right.cardVariant .kontur {
  display: none;
}

.SE_5702_01_detailed_Guidance_right .OFA {
  position: absolute;
  top: 36px;
  left: 85px;
  width: 170px;
  height: 170px;
  background-color: rgba(var(--boundingBoxGreen));
  background-image: url(../img/OFA_placeholder.png);
  background-position: center;
  background-repeat: no-repeat;
}
.SE_5702_01_detailed_Guidance_right.cardVariant .OFA {
  top: 10px;
  left: 60px;
}

.SE_5702_01_detailed_Guidance_right .streetText {
  position: absolute;
  top: 70px;
  left: 273px;
  width: 335px;
}
.SE_5702_01_detailed_Guidance_right.cardVariant .streetText {
  top: 50px;
  left: 240px;
  width: 310px;
}

.SE_5702_01_detailed_Guidance_right .nextManeuverBox {
  position: absolute;
  top: 165px;
  left: 273px;
  width: 280px;
  display: flex;
  align-items: baseline;
}
.SE_5702_01_detailed_Guidance_right.cardVariant .nextManeuverBox {
  position: absolute;
  top: 140px;
  left: 240px;
  width: 280px;
}

.SE_5702_01_detailed_Guidance_right .nextManeuverBox p:last-child {
  margin-left: 5px;
}

/*----------------------- LANE GUIDANCE -----------------------*/
.SE_5702_01_detailed_Guidance_right .laneGuidance {
  position: absolute;
  top: 228px;
  left: 145px;
  display: flex;
  justify-content: center;
  height: var(--laneIconSize);
  width: calc((var(--laneIconSize) + 11px) * 6 + 1px);
}
.SE_5702_01_detailed_Guidance_right.cardVariant .laneGuidance {
  position: absolute;
  top: 205px;
  left: 113px;
}

.SE_5702_01_detailed_Guidance_right .laneGuidance .lane {
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(var(--laneIconSize) + 10px);
  height: 100%;
  border-left: 1px dashed rgba(var(--audiWhite), 0.5);
  position: relative;
}

.SE_5702_01_detailed_Guidance_right .laneGuidance .lane:last-child {
  border-right: 1px dashed rgba(var(--audiWhite), 0.5);
}

.SE_5702_01_detailed_Guidance_right .laneGuidance .lane .laneIcon {
  height: var(--laneIconSize);
  width: var(--laneIconSize);
  background-color: rgba(var(--boundingBoxGreen));
}

.SE_5702_00_Signpost_left {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 620px;
  height: 100%;
}

.SE_5702_00_Signpost_left .signpost {
  position: absolute;
  width: 536px;
  height: 277px;
  left: 25px;
  top: 25px;
  background-color: rgba(var(--boundingBoxGreen));
  background-image: url(../img/signpost-placeholder.png);
  background-position: center;
  background-repeat: no-repeat;
}

.settings {
  position: absolute;
  top: 400px;
  width: 1200px;
}
