.opsBanner {
  position: absolute;
  top: 0;
  left: 524px;
  width: 302px;
  height: 42px;
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.opsBanner--centered {
  left: 50%;
  transform: translateX(-50%);
}

.opsBanner--rhd {
  left: unset;
  right: 524px;
}

.opsBanner .redText {
  color: rgba(255, 0, 0, 1);
}

.opsBanner.yellowBackground {
  background-color: rgba(255, 255, 0, 0.25);
}

.opsBanner p + p {
  margin-left: 2px;
}

.opsContainer {
  position: absolute;
  left: 433px;
  top: 38px;
  width: 484px;
  height: 314px;
}

.opsContainer--centered {
  left: 50%;
  transform: translateX(-50%);
}

.opsContainer--rhd {
  left: unset;
  right: 433px;
}

.opsBB {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 0, 0, 0.5);
}

.opsCid {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url('../../../01_HTML/assets/OPS\ CID\ \(dummy\).png');
}

.opsCarTopview {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 190px;
  height: 208px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: bottom;
}

.opsAU416_2 {
  background-image: url('../../../01_HTML/assets/AU416_2_Topview.png');
}

.opsAU416_3 {
  background-image: url('../../../01_HTML/assets/AU416_3_Topview.png');
}
