:global(#root) > .SE_0025_DriveSelectDashboardTile {
  background: rgb(var(--audiBlack));
  position: absolute;
  height: 816px;
  width: 590px;
  margin: 0;
}

.driveSelect_DashboardTile {
  position: absolute;
  height: 100%;
  width: 100%;
}

.driveSelect_DashboardTile .UE_0402_Tabbarmenu {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
}

.driveSelect_DashboardTile.ai .UE_0402_Tabbarmenu .fullBackgroundTitle {
  justify-content: flex-start;
}

.driveSelect_DashboardTile .scrollContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}
.driveSelect_DashboardTile .scrollContainer {
  height: calc(100% - 119px);
}

.T_0001_BasicLayout.driveSelectDashboard .driveSelectBackground {
  display: inline;
}

/*-----List CarToggle-------*/
.UE_1009_C2Cursor {
  position: absolute;
}

.driveSelect_DashboardTile .listCarToggle {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
}

.driveSelect_DashboardTile .listCarToggle .carToggleElement {
  position: relative;
  min-height: var(--minTouchSizeDashboardList);
  flex: 1;
  width: 100%;
  display: flex;
  box-sizing: border-box;
}

.driveSelect_DashboardTile .listCarToggle .carToggleElement:not(:last-of-type) {
  border-bottom: 1px solid var(--listSeparatorLineColor);
}

.driveSelect_DashboardTile .listCarToggle .carToggleElement .UE_1009_C2Cursor {
  width: 100%;
  height: 100%;
  position: absolute;
}

.driveSelect_DashboardTile
  .listCarToggle
  .carToggleElement
  .UE_0101_ActionButton {
  flex: 1;
  height: 100%;
}

/*-------List element-------*/
.listElement {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-wrap: nowrap;
  position: relative;
  padding-left: var(--lA1);
  width: calc(100% - var(--lA1));
}

.listElement .uiStateContainer {
  display: none;
  position: absolute;
  height: 100%;
  width: calc(100% + 3px);
  left: -3px;
  z-index: 1;
}

.listElement .uiStateContainer.uiList {
  display: block;
}

.listElement .uiStateContainer.uiList ~ .buttonContainer {
  z-index: 2;
}

.listElement .textContainer {
  display: inline-flex;
  flex: 1;
  min-width: 0;
  position: relative;
  margin-right: var(--iA1);
}

.listElement .textContainer .textColumn {
  position: relative;
  flex: 1;
  align-self: center;
  overflow: hidden;
  text-overflow: ellipsis;
}

.listElement .textContainer .textColumn .textRow {
  position: relative;
  flex: 1;
  display: flex;
  align-items: baseline;
}

.listElement .textContainer .textColumn .textPrio1,
.listElement .textContainer .textColumn .textPrio2 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.buttonContainer {
  height: 100%;
  width: fit-content;
  display: flex;
  flex-wrap: nowrap;
}

.buttonContainer .button {
  display: inline-block;
  width: var(--minTouchSize);
  height: 100%;
  position: relative;
}

.headerSwitch {
  position: absolute;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  top: 0px;
  right: 0px;
  z-index: 1;
}

.dSelectAssistantIcon {
  transform: translateY(-3%);
  width: 21px;
  height: 21px;
  margin-left: 16px;
  background-image: url(../../../ST_0005_DriveSelect/01_HTML/img/driveSelectAssistant-Icon.png);
}

.dSelectAssistantIcon.top {
  position: absolute;
  top: 50px;
  right: var(--minTouchSize);
}

.headerSwitch .UE_1103_Switch label {
  z-index: -1;
}

.settings {
  position: absolute;
  top: 850px;
  width: 600px;
}
