/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --socBarWidth: 351px;
  --socstatusIconStartX: 115px;
}

.SOCBar.UE_6000_01_Fuel_DE,
.SOCBar.UE_6000_02_Fuel_US,
.SOCBar.UE_6000_04_Fuel_US_Fehler,
.SOCBar.UE_6000_03_Fuel_DE_Fehler {
  --socstatusIconStartX: 117px;
}

.SOCBar.UE_6003_00_Fuel_DE,
.SOCBar.UE_6003_01_Fuel_US,
.SOCBar.UE_6003_02_Fuel_DE_Fehler,
.SOCBar.UE_6003_03_Fuel_US_Fehler {
  --socBarWidth: 228px;
  --socstatusIconStartX: 110px;
}

.SOCBar.UE_6004_00_SOC_PHEV,
.SOCBar.UE_6004_01_SOC_PHEV_Marker,
.SOCBar.UE_6004_02_SOC_PHEV_Fehler {
  --socBarWidth: 118px;
  --socstatusIconStartX: -1px;
}

:global(#root) > .UE_6000_Fuellstand_klein {
}

.UE_6000_Fuellstand_klein {
  position: absolute;
  width: var(--socBarWidth);
  height: 90px;
}

.SOCBar {
  position: absolute;
  width: 100%;
  height: 100%;
}

.SOCBar .soc {
  position: absolute;
  width: calc(var(--socBarWidth) - 1px);
  top: 34px;
  left: 1px;
}

.SpritesheetDigits {
  position: absolute;
  display: flex;
  height: 14px;
  width: fit-content;
  top: 10px;
  left: calc(50% - 24px);
}

.UE_6004_00_SOC_PHEV .SpritesheetDigits,
.UE_6004_01_SOC_PHEV_Marker .SpritesheetDigits,
.UE_6004_02_SOC_PHEV_Fehler .SpritesheetDigits {
  left: 36px;
}

/* upper assets */
.scale {
  position: absolute;
  height: 16px;
  width: var(--socBarWidth);
  top: 10px;
}

.upperAssets {
  position: absolute;
  background-color: rgb(var(--audiWhite));
}

.reserveAsset {
  height: 16px;
  width: 14px;
  left: 0px;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/zeroAsset.png');
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

.oneSlashTwoAsset {
  height: 15px;
  width: 36px;
  left: 50%;
  transform: translateX(-50%);
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/oneSlashTwoAsset.png');
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

.oneAsset {
  height: 14px;
  width: 7px;
  right: 1px;
  top: 1px;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/oneAsset.png');
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

.emptyAsset {
  height: 14px;
  width: 12px;
  left: 0px;
  top: 1px;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/emptyAsset.png');
  -webkit-mask-position: bottom;
  -webkit-mask-repeat: no-repeat;
}

.fullAsset {
  height: 14px;
  width: 11px;
  right: 0px;
  top: 1px;
  -webkit-mask-image: url('../../../../../components/NewBargraph/img/fullAsset.png');
  -webkit-mask-position: bottom;
  -webkit-mask-repeat: no-repeat;
}

/* Inactive State */
.inactive .reserveAsset,
.inactive .oneSlashTwoAsset,
.inactive .oneAsset,
.inactive .emptyAsset,
.inactive .fullAsset,
.inactive .fpkStatusIcon,
.inactive .fpkStatusFitSizeIcon {
  background-color: rgb(var(--bargraphInactive));
}

.inactive .SpritesheetDigits {
  opacity: 0;
}

/*  fpk icons  */

.fpkStatusIcon {
  position: absolute;
  top: 39px;
}

.E12E_Pfeil_links {
  left: var(--socstatusIconStartX);
  -webkit-mask-image: url('../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E12E_Pfeil_links.png');
  top: 41px;
}
.UE_6000_04_Fuel_US_Fehler .E12E_Pfeil_links,
.UE_6000_03_Fuel_DE_Fehler .E12E_Pfeil_links,
.UE_6000_02_Fuel_US .E12E_Pfeil_links,
.UE_6000_01_Fuel_DE .E12E_Pfeil_links {
  left: calc(var(--socstatusIconStartX));
}

.SOCBar.UE_6003_00_Fuel_DE .fpkStatusIcon.E12E_Pfeil_links,
.SOCBar.UE_6003_01_Fuel_US .fpkStatusIcon.E12E_Pfeil_links,
.SOCBar.UE_6003_02_Fuel_DE_Fehler .fpkStatusIcon.E12E_Pfeil_links,
.SOCBar.UE_6003_03_Fuel_US_Fehler .fpkStatusIcon.E12E_Pfeil_links {
  left: 55px;
}

.E194_car_battery_low {
  left: calc(var(--socstatusIconStartX) + 32px);
  -webkit-mask-image: url('../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E194_car_battery_low.png');
}

.E10C_fuel {
  left: calc(var(--socstatusIconStartX) + 43px);
  top: 50px;
  -webkit-mask-image: url('../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E10C_fuel.png');
}

.SOCBar.UE_6003_00_Fuel_DE .fpkStatusFitSizeIcon.E10C_fuel,
.SOCBar.UE_6003_01_Fuel_US .fpkStatusFitSizeIcon.E10C_fuel,
.SOCBar.UE_6003_02_Fuel_DE_Fehler .fpkStatusFitSizeIcon.E10C_fuel,
.SOCBar.UE_6003_03_Fuel_US_Fehler .fpkStatusFitSizeIcon.E10C_fuel {
  left: 98px;
}

.E12F_Pfeil_rechts {
  left: calc(var(--socstatusIconStartX) + 63px);
  -webkit-mask-image: url('../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E12F_Pfeil_rechts.png');
  top: 41px;
}
.UE_6000_04_Fuel_US_Fehler .E12F_Pfeil_rechts,
.UE_6000_03_Fuel_DE_Fehler .E12F_Pfeil_rechts,
.UE_6000_02_Fuel_US .E12F_Pfeil_rechts,
.UE_6000_01_Fuel_DE .E12F_Pfeil_rechts {
  left: calc(var(--socstatusIconStartX) + 54px);
}

.SOCBar.UE_6003_00_Fuel_DE .fpkStatusIcon.E12F_Pfeil_rechts,
.SOCBar.UE_6003_01_Fuel_US .fpkStatusIcon.E12F_Pfeil_rechts,
.SOCBar.UE_6003_02_Fuel_DE_Fehler .fpkStatusIcon.E12F_Pfeil_rechts,
.SOCBar.UE_6003_03_Fuel_US_Fehler .fpkStatusIcon.E12F_Pfeil_rechts {
  left: 109px;
}

.SOCBar.UE_6004_00_SOC_PHEV .fpkStatusIcon.E12F_Pfeil_rechts,
.SOCBar.UE_6004_01_SOC_PHEV_Marker .fpkStatusIcon.E12F_Pfeil_rechts,
.SOCBar.UE_6004_02_SOC_PHEV_Fehler .fpkStatusIcon.E12F_Pfeil_rechts {
  left: 62px;
}

/*---------- Bordcomputer Config ---------- */
.settings {
  position: absolute;
  top: 200px;
  color: white;
  width: 100%;
  background-color: rgb(var(--audiBlack));
}

.socBarConfig .bartype_config,
.socBarConfig .scale_content_config,
.socBarConfig .level {
  margin-top: 20px;
}

#socSlider,
#socMarker {
  width: var(--socWidth);
}
