

:root {
  --aioWidth: 162px;
  --aioHeight: 61px;
  --aioIcon: 54px;
  --aioVrSubContWidth: 93px;
  --aioHrSubContWidth: 106px;
  --aioVrTrafficLightWidth: 23px;
  --aioVrTrafficLightHeigth: 55px;
  --aioHrTrafficLightWidth: 55px;
  --aioHrTrafficLightHeigth: 23px;
}

.SE_5103_AIO {
  width: var(--aioWidth);
  height: var(--aioHeight);
}

.T_5400_02_AIO_vertikal_1_zeilig {
  width: 125px;
  height: 56px;
  margin-left: 0px;
}

.T_5400_03_AIO_vertikal_2_zeilig {
  width: 88px;
  height: 56px;
  margin-left: 0px;
}

.T_5400_04_AIO_horizontal_1_zeilig {
  display: flex;
  flex-direction: column;
  width: 100px;
  height: 82px;
  margin-left: 11px;
}

.T_5400_05_AIO_horizontal_2_zeilig {
  width: 92px;
  height: 82px;
  margin-left: 14px;
}

.AIO_Container {
  height: 100%;
  display: flex;
  align-items: center;
}

.AIO_icon {
  width: var(--aioIcon);
  height: var(--aioIcon);
  background-color: rgba(235, 0, 0, 0.5);
  background-position: center;
  background-repeat: no-repeat;
}

.AIO_icon.hidden {
  visibility: hidden;
}

.iconContainer {
  position: absolute;
  width: var(--aioIcon);
  height: var(--aioIcon);
  image-rendering: pixelated;
}

.AIO_SubContainer {
  display: flex;
  align-items: center;
}

.AIO_SubContainer.vertikal {
  width: 125px;
}

.vertikal .AIO_trafficLight {
  width: var(--aioVrTrafficLightWidth);
  height: var(--aioVrTrafficLightHeigth);
}

.textContainer {
  display: flex;
  flex-direction: column;
}

.horizontalTextContainer {
  display: flex;
  flex-direction: column;
  height: 56px;
  text-align: center;
}

.longTextContainer {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  height: 100%;
  width: 100px;
}

.longTextContainerHorizontal {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  height: 75%;
}

.greyText {
  color: rgba(var(--audiWhite), 0.7);
}

.numberContainer {
  width: 75px;
}

.horizontalNumberContainer {
  text-align: center;
}

.AIO_SubContainer.horizontal {
  width: var(--aioHrSubContWidth);
  flex-direction: column;
  margin-top: 5px;
}

.horizontal .AIO_trafficLight {
  width: var(--aioHrTrafficLightWidth);
  height: var(--aioHrTrafficLightHeigth);
}

.AIO_vertical_trafficLight {
  width: 25px;
  height: 54px;
  background-color: rgba(var(--boundingBoxGreen));
}

.AIO_horizontal_trafficLight {
  width: 54px;
  height: 25px;
  background-color: rgba(var(--boundingBoxGreen));
}

.vertikal .textContainer {
  flex: 1;
}

.textContainer.sec {
  display: flex;
  margin: 0;
  justify-content: flex-end;
  align-items: baseline;
  max-width: calc(var(--aioVrSubContWidth) - var(--aioVrTrafficLightWidth));
}

.vertikal .seconds {
  max-width: var(--hA2);
}

.horizontal .textContainer {
  display: flex;
  align-items: baseline;
}

.textContainer.hidden {
  visibility: hidden;
}

.settings {
  height: 500px;
  overflow: scroll;
}

.settings > div:first-child > div:first-child {
  display: flex;
  flex-wrap: wrap;
}

.settings > div:first-child > div:first-child > p {
  width: 100%;
}
.settings > div:first-child > div:first-child > div:nth-child(3) {
  margin-left: 5rem;
  margin-right: 5rem;
}

.settings > div:first-child > div:first-child div div label > div:first-child {
  width: 100%;
}

.settings > div:first-child > div:first-child div div label > div:nth-child(2),
.settings > div:first-child > div:first-child div label > div:nth-child(2) {
  display: none;
}
