:global(#root) > .SE_5013_Motordaten_RS {
  background-color: rgb(var(--bodyColorGrey));
}

.SE_5013_Motordaten_RS {
  width: var(--cardContentWidthKB);
  height: var(--cardContentHeight);
  margin: 0;
}

.engineData {
  width: 100%;
  height: 100%;
  padding: 20px 0px 0px 15px;
  box-sizing: border-box;
  position: relative;
}

.chartContainer {
  position: relative;
  width: 512px;
  height: 100%;
}

.chartContainer svg {
  position: absolute;
  left: 0px;
  width: 500px;
}

#path {
  fill: url(#fill-gradient-default);
  stroke: url(#stroke-gradient-default);
}

#pathPHEV1,
#pathPHEV2,
#pathPHEV3 {
  fill: url(#fill-gradient-phev);
  stroke: url(#stroke-gradient-phev);
}

.engineData.phev #pathPHEV1,
.engineData.phev #pathPHEV2,
.engineData.phev #pathPHEV3 {
  display: inline;
}

.engineData.short path {
  clip-path: inset(-2px -2px -2px 325px);
}

.engineData.short #pathPHEV3 {
  clip-path: inset(-2px -2px -2px 38px);
}

.chartContainer .currentValuePointer {
  width: 10px;
  height: 2px;
  position: absolute;
  background-color: rgb(var(--audiWhite));
  top: 108px;
  right: 0px;
}

.chartContainer #boostSVGPath {
  clip-path: inset(0px 186px 0px 280px);
}
