.T_10000_AR_HUD_BasicLayout {
  position: absolute;
  width: 1200px;
  height: 400px;
  background-color: rgba(var(--audiBlack), 1);
  overflow: hidden;
}

.InteractionConcept ::-webkit-scrollbar-track {
  background: #282726e0;
  border: 1px solid #353332e0 !important;
}

.T_10000_AR_HUD_BasicLayout.streetBg {
  background-image: url(../../01_HTML/assets/street.png);
}

.withUneceArea {
  display: grid;
  grid-template-rows: 236px 1fr;
}

.uneceContainer {
}

.arHudWithUneceContainer {
  position: relative;
  overflow: hidden;
}

.T_10000_AR_HUD_BasicLayout .uneceArea {
  width: 100%;
  height: 236px;
  background-color: rgba(var(--audiWhite), 0.2);
  position: absolute;
  top: 0;
}

.T_10000_AR_HUD_BasicLayout .powermeterPlaceholder {
  width: 403px;
  height: 162px;
  background-image: url(../assets/Powermeter_placeholder.png);
  background-repeat: no-repeat;
  position: absolute;
  bottom: 0;
  left: 470px;
}

.T_10000_AR_HUD_BasicLayout .powermeterPlaceholder--rhd {
  left: unset;
  right: 470px;
}

.SE_5001_DigitalerTacho--default {
  position: absolute;
  bottom: 5px;
  left: 611px;
  z-index: 1;
}

.SE_5001_DigitalerTacho--rhd {
  right: 611px;
  left: unset;
}

.SE_5001_DigitalerTacho--centered {
  left: 50%;
  transform: translateX(-50%);
  right: unset;
}

.T_10000_AR_HUD_BasicLayout .SE_5001_DigitalerTacho .digit.boundingBox {
  background-color: rgba(255, 0, 0, 0.3);
}

.popUp {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin-top: 60px;
}

.UE_1002_ListItemStandard .HDradioFID .radioFID p {
  font-size: var(--fontSizeARHUD);
}
.UE_1002_ListItemStandard p {
  font-size: var(--fontSizeARHUD);
}

.UE_1002_ListItemStandard .Sirius p:first-of-type {
  width: 70px !important;
}

.UE_1002_ListItemStandard
  .HDradioFID
  .listElement
  .textContainer
  .iconContainer {
  margin-left: 18px;
  flex: 0.65;
  margin-right: 8px;
}
.UE_1002_ListItemStandard
  .HDradioFID
  .listElement
  .textContainer
  .iconContainer
  p.textPrio1 {
  margin-left: var(--zA5);
}

.iconAndText .UE_1002_ListItemStandard .listElement .textContainer .listIcon,
.UE_1002_ListItemStandard.singleLine .listElement .textContainer .listIcon {
  align-self: center;
}
.iconAndText .UE_1002_ListItemStandard .listElement .textContainer .textColumn {
  margin-left: 6px;
}

.twoColumnsText .listElement .textContainer:last-child {
  flex: 4.5;
  margin-left: 19px;
}

.twoColumnsRadio .listElement .textContainer:first-child {
  margin-right: 24px;
}

.twoColumnsRadio .listElement .textContainer:last-child {
  flex: 1.75;
  margin-left: 0;
}

.UE_1002_ListItemStandard
  .HDradioFID
  .listElement
  .textContainer
  .textColumn.textBox1 {
  flex: 0.75;
}

.UE_1002_ListItemStandard
  .HDradioFID
  .listElement
  .textContainer
  .textColumn.textBox2 {
  flex: 2.1;
}

.T_10000_AR_HUD_BasicLayout p {
  font-family: var(--fontNormal);
}
