/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --chipsDistance: 55px;
}

:global(#root) > .UE_0501_InputField {
  height: var(--minTouchSize);
  width: 100%;
  margin: 100px 0px 0px 0px;
}

.UE_0501_InputField {
}

.inputField {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  min-width: 500px;
  height: 100%;
  justify-content: left; /* align horizontal */
  align-items: center; /* align vertical */
}

.popupStylingInputField {
  background-color: unset;
  background-image: linear-gradient(
    rgba(var(--audiBlack), 0.5),
    rgba(var(--audiBlack), 0.5)
  );
  background-size: calc(100% - 4px);
  background-repeat: no-repeat;
  background-position-y: 4px;
}

.inputField.appGridInputField .interactionBackground {
  background-color: unset;
}

.inputField:after {
  content: '';
  position: absolute;
  width: calc(100% - var(--cA1) * 2);
  left: var(--cA1);
  height: 1px;
  bottom: calc(50% - var(--lineHeightPrio2) / 2 - 11px);
  background-color: var(--tabbarSeparatorColor);
}

.inputCursorContainer,
.inputSelectionContainer {
  display: none;
}
.inputField.inputCursor .inputCursorContainer,
.inputField.inputSelection .inputSelectionContainer {
  display: block;
}

/*--------------------------- TEXTFIELD -----------------------------------------*/
.input {
  display: inline-block;
  height: 100%;
  position: relative;
  flex-grow: 1;
  caret-color: transparent;
}
.input :focus {
  outline: none;
}

input.textfield {
  width: calc(100% - (var(--cA1) * 2));
  height: 100%;
  margin-left: var(--cA1);
  margin-right: var(--cA1);
  background: transparent;
  border: none;
  position: absolute;
}

input.textfield::selection {
  width: calc(100% + 4px);
  background: rgba(var(--ambientcolor1), 0.3);
}

input.textfield::placeholder {
  color: rgb(var(--audiWhite));
  opacity: var(--touchDisabledState);
}

.buttonContainer {
  display: flex;
  width: fit-content;
  height: 100%;
}

.passwordMeterBox {
  display: flex;
  width: fit-content;
  max-width: 305px;
  height: 100%;
  margin-right: var(--cA1);
  align-items: center;
  justify-content: flex-start;
}

.passwordMeterBox p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/*------------------------- PASSWORD METER -------------------------------------*/
.passwordMeterBox.strong p {
  color: rgb(var(--fpkGreen));
}

.passwordMeterBox.weak p {
  color: rgb(var(--fpkYellow));
}

.passwordMeterBox.veryWeak p {
  color: rgb(var(--fpkOrange));
}

.passwordMeterBox.tooWeak p {
  color: rgb(var(--fpkRed));
}

/*--------------------------- BUTTONS -----------------------------------------*/
.inputField .button {
  margin-left: auto;
  height: 100%;
  width: var(--minTouchSize);
  position: relative;
  left: 0px;
}

.inputField.tabbar .button {
  height: 111px;
}

/*--------------------------- SELECTION MARKER -----------------------------------------*/
.marker {
  position: absolute;
  top: 90px;
  left: 141px;
  width: 18px;
  height: 36px;
}

.skewContainer {
  position: absolute;
  top: 5px;
  width: 100%;
  height: 100%;
  background: rgba(var(--ambientcolor1), 1);
}

/*INPUT CURSOR*/
.marker.centered .skewContainer {
  height: calc(100% - 9px);
  top: 9px;
}

.marker.centered .skewContainer:after {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
  top: -9px;
  left: 0;
  border-width: 0 9px 9px;
  border-style: solid;
  border-color: transparent transparent rgb(var(--ambientcolor1));
}

/*SELECTION AREA MARKER*/
.left.marker {
  left: 79px;
}

.right.marker {
  left: 222px;
}

.left.marker:before,
.right.marker:before {
  content: '';
  position: absolute;
  top: -47px;
  width: 2px;
  height: var(--lineHeightPrio2);
  background-color: rgb(var(--ambientcolor1));
}

.left.marker:before {
  left: 16px;
}

.overflowContainer {
  position: absolute;
  height: 36px;
  width: 18px;
  overflow: hidden;
}

.overflowContainer .skewContainer {
  transform: skewY(-26deg);
}

.right.marker .overflowContainer .skewContainer {
  transform: skewY(26deg);
}

/*--------------------------- TABBAR USE CASE -----------------------------------------*/
.UE_0402_Tabbarmenu .tabbar .UE_0501_InputField .inputField,
.inputField.tabbar {
  height: calc(100% - 8px);
  border-bottom: none;
}

.textPrio2 {
  color: rgb(var(--audiWhite));
  font-family: var(--fontLight);
  font-size: var(--fontSizePrio2);
  line-height: var(--lineHeightPrio2);
}

.UE_0501_InputField .inputField.tabbar .marker {
  top: 86px;
}

/*--------------------------- INPUT CURSOR -----------------------------------------*/
.caret {
  width: 2px;
  height: var(--lineHeightPrio2);
  background-color: rgb(var(--audiWhite));
  position: absolute;
  top: calc(50% + 1px);
  left: 149px;
  transform: translateY(-50%);
}

.inputCursorContainer > .caret {
  animation: blinker 1.2s linear infinite;
}

@keyframes blinker {
  from {
    visibility: hidden;
  }
  50% {
    visibility: hidden;
  }
  to {
    visibility: visible;
  }
}

/*--------------------------- CHIP -----------------------------------------*/
.chipBlock {
  position: absolute;
  left: 30px;
  top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SE_0094_Chip:not(:first-of-type) {
  margin-left: var(--chipsDistance);
}

/********************** Long Press Extension ---------------------------------*/
.longPressExtension {
  position: absolute;
  top: 55px;
  left: 10px;
}

.settings {
  position: absolute;
  top: 327px;
  height: calc(100% - 370px);
  overflow: scroll;
}
