/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --numkeyCount: 3;
  --numSeparatorCount: calc(
    (var(--numkeyCount) - 1) * var(--keyboardSeparatorWidth)
  );
  --numkeyWidth: calc((100% - var(--numSeparatorCount)) / var(--numkeyCount));
}

:global(#root) > .UE_0506_PhoneNumericKeypad {
  margin: 0;
  height: 400px;
}

.keypad {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: row;
  padding: 0 8px 8px 8px;
}

.mainButtons {
  display: flex;
  flex-direction: column;
  flex: calc(var(--numkeyWidth) * 3);
}

.inputLine {
  display: none;
}

.keyboardLine {
  height: var(--keyboardLineHeight);
  display: flex;
  border-bottom: 1px solid rgb(var(--audiGrey));
  box-sizing: border-box;
}

.keypad .sideButtons .keyboardLine:last-of-type {
  height: calc(var(--keyboardLineHeight) * 3);
}

.keyboardLine:last-of-type {
  border-bottom: none;
}

.button {
  position: relative;
  width: var(--numkeyWidth);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  min-width: var(--minTouchSize);
  flex: 1;
}

.keypad .sideButtons .keyboardLine .button {
  width: 100%;
}

.keyContentWrapper {
  height: 75px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: fit-content;
}

.letters {
  display: none;
}

.keypad.showLetters .keyboardLine .button .letters {
  display: block;
}

.UE_0506_PhoneNumericKeypad .keypad .sideButtons {
  display: none;
}
.keypad.sideButtonsTelephone .sideButtons,
.keypad.sideButtons .sideButtons {
  display: flex;
  flex-direction: column;
  flex: var(--numkeyWidth);
}

.keyboardLine:last-of-type .touchIcon,
.keypad.sideButtonsTelephone
  .sideButtons
  .keyboardLine:last-of-type
  .textPrio1 {
  display: none;
}

.keypad.sideButtonsTelephone
  .sideButtons
  .keyboardLine:last-of-type
  .touchIcon {
  display: block;
}

.keyboardLine:first-of-type .button:first-of-type .keyContentWrapper .mailbox {
  min-width: 24px;
  min-height: 24px;
  width: 24px;
  height: 24px;
  margin-left: 0;
}

.button p {
  text-align: center;
  align-self: center;
}

.button:first-child:before {
  background: none;
}

.button:before {
  content: '';
  position: absolute;
  width: var(--keyboardSeparatorWidth);
  height: calc(100% - (var(--numKeyboardSeparatorMargin) * 2));
  top: var(--numKeyboardSeparatorMargin);
  background: rgb(var(--audiGrey));
  left: 0;
}

.keypad.sideButtons .mainButtons .keyboardLine .button:last-of-type:after,
.keypad.sideButtonsTelephone
  .mainButtons
  .keyboardLine
  .button:last-of-type:after {
  content: '';
  position: absolute;
  width: var(--keyboardSeparatorWidth);
  height: calc(100% - (var(--numKeyboardSeparatorMargin) * 2));
  top: var(--numKeyboardSeparatorMargin);
  background: rgb(var(--audiGrey));
  right: 0;
}

.mainButtons .keyboardLine .button .bottomButton,
.keypad.bottomButtons
  .mainButtons
  .keyboardLine
  .button
  .bottomButton
  + .textPrio1 {
  display: none;
}

.keypad.bottomButtons .mainButtons .keyboardLine .button .bottomButton {
  display: block;
}

/*------------------ settings -----------------*/

.UE_0506_PhoneNumericKeypad .settings {
  position: absolute;
  top: calc(100% + 100px);
}

.UE_0506_PhoneNumericKeypad .settings p {
  color: white;
  width: auto;
}

.UE_0506_PhoneNumericKeypad .settings .textPrio2,
.UE_0506_PhoneNumericKeypad .settings .textSettingLabel {
  margin-bottom: 10px;
}
