/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --mapLabel_marginText: 10px;
  --mapLabel_marginTextBold: 13px;
  --mapLabel_borderBold: 6px;
}

/* Standalone */

:global(#root) > .SE_0074_MapLabel {
  position: absolute;
  height: 600px;
  width: 100%;
  min-width: 550px;
  margin: 20px;
  background-color: rgb(var(--bodyColorGrey));
}

/* .SE_0074_MapLabel */

.SE_0074_MapLabel {
}

.navMapContainer {
  position: relative;
  display: flex;
  flex: 1;
  justify-content: flex-start;
  box-sizing: border-box;
  width: fit-content;
  box-shadow: 0 0 0 1px rgba(var(--ambientBackground1), 0.9);
}

.navMapLabel {
  position: relative;
  display: flex;
  width: fit-content;
  box-sizing: border-box;
  flex-direction: column;
  overflow: hidden;
  background-color: rgba(var(--ambientBackground1), 0.9);
  border: 1px solid rgb(var(--audiGrey));
}

.altOne.navMapLabel {
  border: 3px solid rgb(var(--routeAlternativeOneBorder));
}
.altTwo.navMapLabel {
  border: 3px solid rgb(var(--routeAlternativeTwoBorder));
}

.navMapLabelContainer {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: center;
  margin-top: 1px;
  margin-bottom: 1px;
}

.altOne .navMapLabelContainer p {
  color: rgb(var(--routeAlternativeOneFont));
}
.altTwo .navMapLabelContainer p {
  color: rgb(var(--routeAlternativeTwoFont));
}

.navMapLabelContainer p:first-of-type {
  margin-left: var(--mapLabel_marginText);
}
.navMapLabelContainer p:last-of-type {
  margin-right: var(--mapLabel_marginText);
}

.SE_0074_MapLabel.traffic .navMapLabelContainer p:first-of-type {
  margin-left: var(--mapLabel_marginTextBold);
}
.SE_0074_MapLabel.traffic .navMapLabelContainer p:last-of-type {
  margin-right: var(--mapLabel_marginTextBold);
}

.lineVertical {
  position: absolute;
  height: 0;
}

.traffic.lineVertical {
  height: 100%;
}

.left {
  left: 0px;
}
.right {
  right: 0px;
}

.some.lineVertical {
  border-left: var(--mapLabel_borderBold) solid rgba(var(--routeTrafficSome));
}
.more.lineVertical {
  border-left: var(--mapLabel_borderBold) solid rgba(var(--routeTrafficMore));
}
.jam.lineVertical {
  border-left: var(--mapLabel_borderBold) solid rgba(var(--routeTrafficJam));
}
.none.lineVertical {
  border-left: var(--mapLabel_borderBold) solid rgba(var(--routeTrafficNone));
}

.navMapLabelBackground {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: calc(100% - 6px);
  height: var(--fontSizePrio2);
  background-repeat: no-repeat;
  background-position: center;
}

.altOne.navMapLabelBackground {
  background-color: rgba(var(--routeAlternativeOneInner));
}

.altTwo.navMapLabelBackground {
  background-color: rgba(var(--routeAlternativeTwoInner));
}

.settings {
  position: absolute;
  top: 150px;
  width: 50%;
}
