/* CLASSIFICATION: CONFIDENTIAL */
:global(#root) > .ST_0015_PreHeating {
  margin: auto;
  width: 100%;
  height: 588px;
  background-color: rgb(var(--audiBlack));
}

.ST_0015_PreHeating .preHeating {
  height: 100%;
  width: 100%;
  display: flex;
}

.ST_0015_PreHeating .preHeating .preHeating_left {
  height: 100%;
  width: calc(40% - 2 * var(--climateSideMargin));
  margin: 0 var(--climateSideMargin);
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
}

.ST_0015_PreHeating .preHeating .preHeating_right {
  position: relative;
  height: 100%;
  margin: 0 var(--climateSideMargin);
  display: flex;
  align-items: flex-start;
  flex: 1;
  flex-direction: column;
  justify-content: center;
}

.ST_0015_PreHeating .preHeating .preHeating_right .sliderContainer,
.ST_0015_PreHeating .preHeating .preHeating_left .UE_0201_ToggleButtonGroup {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 55px;
  margin-top: 65px;
}

.ST_0015_PreHeating .preHeating .preHeating_left .departureTimer .button {
  width: 100%;
}
.ST_0015_PreHeating .preHeating .preHeating_left .UE_0201_ToggleButtonGroup {
  width: 100%;
}

/* .ST_0015_PreHeating .preHeating.oneLine .departureTimer .textbox .secondRow {
  display: none;
} */

.ST_0015_PreHeating .preHeating .preHeating_separatorLineVertical {
  position: relative;
  width: 2px;
  background-color: rgba(var(--audiWhite), 0.15);
  margin-top: calc(var(--minTouchSize) / 2);
  margin-bottom: calc(var(--minTouchSize) / 2);
  flex-shrink: 0;
}

.ST_0015_PreHeating .preHeating .preHeating_right .preHeating_buttons {
  display: flex;
  position: relative;
  width: 100%;
}

.ST_0015_PreHeating .preHeating .preHeating_right .preHeating_ProgressState {
  display: flex;
  position: relative;
  flex-direction: column;
}
.ST_0015_PreHeating
  .preHeating
  .preHeating_right
  .preHeating_ProgressState
  > p {
  width: 278px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ST_0015_PreHeating .preHeating .preHeating_right p.textPrio1 {
  margin-bottom: var(--hA1);
}
.ST_0015_PreHeating .preHeating .preHeating_right .preHeating_progressHeadline {
  display: flex;
  align-items: flex-end;
  height: 130px;
}

.ST_0015_PreHeating
  .preHeating
  .preHeating_right
  .preHeating_buttons
  .UE_0101_ActionButton {
  position: relative;
  width: 278px;
  height: var(--minTouchSize);
}
.ST_0015_PreHeating
  .preHeating
  .preHeating_right
  .UE_0101_ActionButton
  .buttonContent
  .touchIcon {
  position: relative;
  top: 45px;
}

.ST_0015_PreHeating
  .preHeating
  .preHeating_right
  .UE_0101_ActionButton
  .buttonContent
  .textPrio3 {
  position: relative;
  top: 30px;
  width: 100%;
  text-align: center;
}
