:root {
  --routeOverviewWidth: 702px;
  --elementHeight: calc(669px - var(--hA1) - var(--zA1));
  --topContainerIconHeight: 40px;
  --topContainerBigIconWidth: 175px;
  --topContainerBigIconMinWidth: 79px;
}

:global(#root) > .SE_0053_ExitJunctionView {
  height: 500px;
  width: var(--routeOverviewWidth);
}

.NAVExitJunctionView {
  height: 100%;
  width: var(--routeOverviewWidth);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.NAVExitJunctionViewContainer {
  position: relative;
  display: flex;
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  box-sizing: border-box;
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: var(--animationFadeDuration);
  flex-direction: column;
  border: solid 1px rgba(var(--audiLightGrey), 0.5);
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--ambientBackground1), 1);
  top: 0;
  left: 0;
}

.exitJunctionTopContainer {
  position: relative;
  display: flex;
  align-items: center;
}

.exitJunctionTopContainer::after {
  position: absolute;
  content: '';
  width: calc(100% - 22px);
  margin-left: 11px;
  margin-right: 11px;
  background-color: rgba(var(--audiGrey), 0.8);
  height: 1px;
  bottom: 4px;
}

.exitJunctionIconTextContainer {
  position: relative;
  display: flex;
  width: calc(100% - var(--minTouchSize));
  height: calc(var(--minTouchSize) - (var(--uiStateMargin) * 2));
  align-items: center;
  justify-content: flex-end;
  margin-left: 2px;
}

.exitJunctionFirstBox {
  background-color: rgba(var(--boundingBoxGreenFull));
  width: var(--topContainerBigIconWidth);
  height: var(--topContainerIconHeight);
  margin-right: calc(var(--zA1) + var(--hA3));
  min-width: var(--topContainerBigIconMinWidth);
}

.ExitJunctionImage {
  background-image: url(../img/placeholder.webp);
  width: 100%;
  height: calc(100% - var(--minTouchSize));
  z-index: 1;
  background-size: contain;
  background-position: bottom;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 1768px) {
  :root {
    --elementHeight: calc(542px - var(--zA6));
  }
}
