/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_0202_Settings {
  margin: 5px;
  background-color: rgb(var(--bodyColorGrey));
  height: 673px;
  width: 840px;
  position: relative;
}

.settings {
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
}

/*---------------- Titleline--------------*/

.settingsTitleLine {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
}

.settingsTitleLine .UE_0101_ActionButton {
  position: relative;
  height: 100%;
  width: 119px;
  align-self: center;
}

.settingsTitleLine .headline {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  width: calc(100% - var(--minTouchSize) - 2 * var(--lA1));
}

.settingsTitleLine .headline p.textPrio1 {
  position: relative;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
/*---------------- T_0103_ListVertical--------------*/

.T_0103_ListVertical {
  height: calc(100% - var(--popupInsetWidth) * 3 - var(--minTouchSize));
  width: 100%;
  position: relative;
  margin-top: var(--popupInsetWidth);
  margin-bottom: var(--popupInsetWidth);
}

.settings.right .T_0103_ListVertical {
  margin-right: var(--minTouchSize);
  margin-left: 0px;
}

.settings .T_0103_ListVertical .list::-webkit-scrollbar-track,
.settings .T_0103_ListVertical .list::-webkit-scrollbar-track {
  display: none;
}

.settings.right .T_0103_ListVertical .list::-webkit-scrollbar-track {
  display: initial;
  margin-top: var(--popupInsetWidth);
}

/* Info Popup + Dropdown */

.infoPopupDropdownItem .divisor {
  height: 100px;
  position: absolute;
  top: 8px;
  left: 79px;
  width: 1px;
  background-color: #393939;
}

.infoPopupDropdownItem .divisor.listFoldingDivisor {
  left: 129px;
}

.infoPopupDropdownFoldingHeader > div {
  margin-right: 15px;
}

.infoPopupDropdownItem .foldingWidget.maxCategories .foldingContent {
  height: unset;
}

.btnWrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.row {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  border-bottom: 1px solid var(--listSeparatorLineColor);
  margin-bottom: 20px;
  margin-top: 20px;
}

.row:last-child {
  border-bottom: unset;
  margin-bottom: unset;
  margin-top: unset;
}
.btnGroup {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
  width: 100%;
}
.heading {
  align-self: flex-start;
  margin-bottom: var(--hA1);
  width: 100%;
}

.row .btnGroup,
.row .heading {
  padding-left: var(--lA1);
  width: calc(100% - var(--lA1));
}

.buttonGroup {
  width: 100%;
}

.UE_0201_ToggleButtonGroup {
  width: 100%;
}

.row.indentation {
  margin-left: calc(var(--minTouchSize) + 10px);
  width: calc(100% - (var(--minTouchSize) + 10px));
}

/*------------options---------*/

.options {
  position: absolute;
  top: 680px;
}
