/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --routeOverviewWidth: calc(var(--etaBoxWidth) + var(--minTouchSize) * 2);
  --burgerMenuShadow: 3px;
  --vzeOnMapSize: 75px;
  --googleOnMapSize: 75px;
  --speedCameraOverlayWidth: 150px;
  --speedCameraOverlayHeight: 150px;
}

:global(#root) > .T_0204_MapOverlay {
  font-size: 0;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--audiBlack));
  /*background-image: url("../img/MIB-3 Nacht.png"); remove comment for opacity visualization*/
  width: 1920px;
  height: 697px;
  margin: 0px;
}

.T_0204_MapOverlay {
}

.mapOverlay {
  position: relative;
  width: 100%;
  height: 100%;
}

.mapOverlay .mapBackground {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url('../img/day1.5km.webp');
  background-repeat: no-repeat;
  background-size: cover;
}
.mapOverlay .mapBackground.nightMapTilted {
  background-image: url('../img/night1.5km.webp');
}

.mapOverlay .SE_0074_MapLabel {
  position: absolute;
}

.mapOverlay .day.traffic .SE_0074_MapLabel:first-of-type {
  display: block;
  left: 891px;
  top: 515px;
}
.mapOverlay .day.traffic .SE_0074_MapLabel:last-of-type {
  display: block;
  left: 1193px;
  top: 266px;
}
.mapOverlay .day.traffic .SE_0074_MapLabel:nth-of-type(2) {
  display: block;
  left: 941px;
  top: 631px;
}

.mapOverlay .night.traffic .SE_0074_MapLabel:first-of-type {
  display: block;
  left: 932px;
  top: 566px;
}
.mapOverlay .night.traffic .SE_0074_MapLabel:last-of-type {
  display: block;
  left: 297px;
  top: 338px;
}
.mapOverlay .night.traffic .SE_0074_MapLabel:nth-of-type(2) {
  display: block;
  left: 1062px;
  top: 546px;
}

.mapOverlay .full.day .SE_0074_MapLabel:first-of-type {
  display: block;
  left: 656px;
  top: 343px;
}
.mapOverlay .full.day .SE_0074_MapLabel:last-of-type {
  display: block;
  left: 373px;
  top: 397px;
}

.mapOverlay .full.night .SE_0074_MapLabel:first-of-type {
  display: block;
  left: 704px;
  top: 402px;
}
.mapOverlay .full.night .SE_0074_MapLabel:last-of-type {
  display: block;
  left: 406px;
  top: 397px;
}

.mapOverlay .T_0203_BurgerMenu {
  position: absolute;
  width: calc(50% - var(--buttonBorderDist));
  height: calc(100% - 2 * var(--buttonBorderDist));
  left: calc(var(--buttonBorderDist) - 1px);
  top: calc(var(--buttonBorderDist) - 1px);
}

.mapOverlay .T_0203_BurgerMenu.small {
  width: 35%;
}

.T_0203_BurgerMenu.small .mapOverlay .bottomLeftRow {
  left: calc(35% + (2 * var(--buttonBorderDist)) + 6px);
}

.searchAgainRow {
  position: absolute;
  width: 50%;
  height: var(--minTouchSize);
  left: 50%;
  top: var(--buttonBorderDist);
}
.topLeftRow {
  position: absolute;
  height: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  top: var(--buttonBorderDist);
  left: var(--buttonBorderDist);
}
.topRightRow {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: var(--buttonBorderDist);
  right: var(--buttonBorderDist);
}
.topRightRow .subRowRev {
  display: flex;
  flex-direction: row-reverse;
}
.topRightRow .subRowRev:not(:first-of-type) {
  margin-top: var(--buttonBorderDist);
}
.bottomLeftRow {
  position: absolute;
  height: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  bottom: var(--buttonBorderDist);
  left: var(--buttonBorderDist);
}
.bottomRightRow {
  position: absolute;
  bottom: var(--buttonBorderDist);
  right: var(--buttonBorderDist);
}

.searchAgainRow > .button,
.topLeftRow > .button,
.topRightRow .subRowRev > .button,
.bottomLeftRow > .button,
.bottomRightRow > .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}
.searchAgainRow .button.searchAgain {
  position: absolute;
  width: unset;
  left: 50%;
  max-width: calc(100% - 14px);
  transform: translateX(-50%);
  top: 0;
}

.topRightRow .button:not(:first-of-type) {
  margin-right: 8px;
}

.topLeftRow .searchButton {
  position: absolute;
  width: 280px;
}

.searchAgainRow .button > p {
  margin: 0 45px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.topLeftRow .searchButton .iconTextContainer {
  position: absolute;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
}

.topLeftRow .searchButton .iconTextContainer .searchIcon {
  width: var(--minTouchSize);
  height: 100%;
}

.topLeftRow .searchButton .iconTextContainer .searchIcon .touchIcon {
  position: relative;
}

.topLeftRow .searchButton .iconTextContainer .searchText {
  flex: 1;
}

.topLeftRow .searchButton .iconTextContainer .searchText p {
  margin-right: var(--textButtonMargin);
  margin-left: var(--iA1);
}

.topLeftRow .searchButton,
.topLeftRow.mapBox .SE_0006_MapBox,
.topLeftRow.topLeftRow.navEtaBox .SE_0038_Nav_ETA_Box {
  display: block;
}

.SE_0038_Nav_ETA_Box .etaBox {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}

/*---------- T_0203_BurgerMenu visible ----------*/
/*---------- T_0203_BurgerMenu searchAgain ----------*/
.T_0203_BurgerMenu.searchAgain .searchAgainRow {
  display: block;
}

/*---------- T_0203_BurgerMenu visible ----------*/
.T_0203_BurgerMenu .mapOverlay .T_0203_BurgerMenu {
  display: block;
}
.T_0203_BurgerMenu .mapOverlay .bottomLeftRow {
  left: calc(50% + 8px + 5px);
}

/*---------- SE_0056_NextManeuverBox ----------*/
.T_0204_MapOverlay .mapOverlay .topLeftRow .SE_0056_NextManeuverBox {
  position: absolute;
  top: -8px;
  left: 288px;
}

.T_0204_MapOverlay
  .mapOverlay
  .topLeftRow
  .SE_0056_NextManeuverBox
  .SE_0067_LaneGuidance {
  width: fit-content;
  padding: 20px var(--cA1);
}

.nextManeuverBox .mapOverlay > .SE_0056_NextManeuverBox {
  position: absolute;
  top: var(--buttonBorderDist);
  left: calc(
    var(--etaBoxWidth) + 80px + var(--minTouchSize) * 2 +
      var(--buttonBorderDist) * 2
  );
  max-width: calc(
    100% - var(--etaBoxWidth) - 80px - var(--minTouchSize) * 4 -
      var(--buttonBorderDist) * 5
  );
  display: block;
}

.nextManeuverBox.T_0203_BurgerMenu .mapOverlay > .SE_0056_NextManeuverBox {
  position: absolute;
  top: var(--buttonBorderDist);
  left: calc(50% + var(--buttonBorderDist));
  max-width: calc(50% - var(--minTouchSize) * 2 - var(--buttonBorderDist) * 4);
  display: block;
}

.secondaryLaneMarginTop {
  margin-top: var(--buttonBorderDist);
}
/* ---------------------- SE_0072_RouteCriteriaOverlay ---------------------- */

.routeCriteria .mapOverlay > .SE_0072_RouteCriteriaOverlay {
  display: block;
}

.routeOverviewOn .SE_0072_RouteCriteriaOverlay .routeCriteria {
  width: calc(
    100% - var(--etaBoxWidth) - var(--minTouchSize) * 3 -
      var(--buttonBorderDist) * 4
  );
  left: calc(
    var(--etaBoxWidth) + var(--minTouchSize) * 2 + var(--buttonBorderDist) * 2
  );
}

.T_0203_BurgerMenu .SE_0072_RouteCriteriaOverlay .routeCriteria {
  width: calc(50% - var(--minTouchSize) * 2 - var(--buttonBorderDist) * 4);
  left: calc(50% + var(--minTouchSize) + var(--buttonBorderDist) * 2);
}

/* ------------------------------ offline Info ------------------------------ */
.bottomLeftRow .offlineInfo {
  display: flex;
  background-color: rgba(var(--audiBlack), 0.85);
  margin-left: var(--buttonBorderDist);
  height: var(--listIconSize);
  margin-top: auto;
  max-width: 200px;
  align-items: center;
}

.routeOverviewOn .mapOverlay .bottomLeftRow .offlineInfo {
  margin-left: calc(
    var(--routeOverviewWidth) - var(--minTouchSize) + var(--buttonBorderDist)
  );
}

.bottomLeftRow .offlineInfo p {
  margin-right: var(--iA1);
  margin-left: var(--iA1);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.routeCriteria .mapOverlay .bottomLeftRow .offlineInfo {
  margin-top: 0px;
}

/*---------- copyrightGoogle ----------*/

.mapOverlay > .copyrightGoogle {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: fit-content;
  position: absolute;
  bottom: var(--buttonBorderDist);
  left: 50%;
  transform: translateX(-50%);
  padding: 0px var(--cA1);
  box-sizing: border-box;
  overflow: hidden;
  width: 1070px;
}

.routeCriteria .mapOverlay > .copyrightGoogle {
  bottom: calc(var(--buttonBorderDist) + 60px);
}
.T_0203_BurgerMenu .mapOverlay > .copyrightGoogle {
  left: 1173px;
  width: 419px;
}

.routeOverviewOn .mapOverlay > .copyrightGoogle {
  left: 1046px;
  width: 672px;
}
.mapOverlay > .copyrightGoogle p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  text-align: center;
  color: rgb(var(--audiBlack));
  font-family: var(--fontNormal);
}

.mapOverlay > .copyrightGoogle.night p {
  color: rgb(var(--audiWhite));
  font-family: var(--fontLight);
}

.mapOverlay > .copyrightGoogle p:not(:first-child) {
  margin-top: var(--zA2);
}
/*---------- SE_0007_ScaleInfo ----------*/
.bottomRightRow .button .SE_0007_ScaleInfo {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.SE_0007_ScaleInfo .Text:last-of-type {
  height: unset;
}

/*---------- SE_0047_RouteOverview ----------*/

.SE_0047_RouteOverview {
  position: absolute;
  display: flex;
  width: var(--routeOverviewWidth);
  height: calc(100% - var(--minTouchSize) - (var(--buttonBorderDist) * 3));
  margin: 0;
  top: calc(var(--minTouchSize) + (var(--buttonBorderDist) * 2));
  left: var(--buttonBorderDist);
}
.bottomLeftRow .button {
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: var(--animationFadeDuration);
}
.routeOverviewOn .mapOverlay .bottomLeftRow .button {
  opacity: 0;
  transition-delay: 0;
}

.routeOverviewOn .mapOverlay .bottomLeftRow {
  pointer-events: none;
}

/*---------- Google ON MAP ----------*/
.googleOnMapContainer {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 8px;
  right: calc(var(--minTouchSize) + 8px * 2);
  height: var(--minTouchSize);
  width: var(--googleOnMapSize);
}

.routeCriteria .mapOverlay .googleOnMapContainer {
  bottom: calc(60px + 8px);
}

.googleIconPlacehoder {
  height: var(--googleOnMapSize);
  width: var(--googleOnMapSize);
  background-color: rgb(var(--boundingBoxGreen));
  background-size: contain;
}

/*---------- VZE ON MAP ----------*/
.vzeOnMapContainer {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 8px;
  right: calc(var(--googleOnMapSize) + var(--minTouchSize) + 8px * 3);
  height: var(--minTouchSize);
  width: var(--vzeOnMapSize);
}

.vzeOnMapContainer.googleCopyOn {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 8px;
  right: calc(var(--minTouchSize) + 8px * 3);
}

.vzeIcon {
  height: var(--vzeOnMapSize);
  width: var(--vzeOnMapSize);
  background-image: url(../img/dummyIconVZE.png);
  background-size: contain;
}

/*---------- SE_0053_Exit_Junction_View ----------*/

.NAVExitJunctionView .mapOverlay .SE_0053_ExitJunctionView {
  position: absolute;
  display: flex;
  margin: 0;
  justify-content: center;
  top: var(--buttonBorderDist);
  height: calc(100% - 16px);
  left: var(--buttonBorderDist);
}

.T_0204_MapOverlay .settingConfig .textSettingLabel.sub {
  margin-left: 80px;
}

/* ------------------------- SE_0034_OnstreetLegend ------------------------- */
.T_0204_MapOverlay .mapOverlay .SE_0034_OnstreetLegend {
  position: absolute;
  margin: 0;
  justify-content: center;
  top: 50%;
  transform: translateY(-50%);
  right: var(--buttonBorderDist);
  display: block;
}

/* --------------------------- .speedCameraOverlay -------------------------- */
/* ----------------- not in da zone ----------------- */
.T_0204_MapOverlay .mapOverlay .speedCameraOverlay.approach {
  width: var(--minTouchSize);
  height: 116px;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay.approach .textPrio3 {
  padding: 0;
}

.T_0204_MapOverlay
  .mapOverlay
  .speedCameraOverlay.approach
  .centerInfoLine
  .stats,
.T_0204_MapOverlay .mapOverlay .speedCameraOverlay.approach .topInfoLine {
  display: none;
}

/* ----------------- in da zone ----------------- */
.T_0204_MapOverlay .mapOverlay .speedCameraOverlay {
  display: flex;
  margin-bottom: 8px;
  width: calc(var(--minTouchSize) * 2 + 8px);
  height: 149px;
  position: absolute;
  right: 8px;
  bottom: calc(var(--minTouchSize) + 8px);
  background-color: rgba(var(--audiBlack), 0.85);
  flex-direction: column;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .textPrio3 {
  width: 100%;
  padding: 0 var(--lA1);
  box-sizing: border-box;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .topInfoLine,
.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .bottomInfoLine {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: fit-content;
  width: calc(100% - (var(--buttonBorderDist) * 2));
  margin: 0 var(--buttonBorderDist);
  box-sizing: border-box;
  padding: 4px 0;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .topInfoLine {
  border-bottom: 1px solid rgba(var(--audiWhite), 0.3);
}

.T_0204_MapOverlay
  .mapOverlay
  .speedCameraOverlay
  .centerInfoLine
  .stats
  .textPrio3
  + .textPrio3 {
  margin-top: var(--zA2);
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .bottomInfoLine {
  border-top: 1px solid rgba(var(--audiWhite), 0.3);
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .centerInfoLine {
  position: relative;
  flex: 1;
  display: flex;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .centerInfoLine .stats {
  position: relative;
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}

.T_0204_MapOverlay
  .mapOverlay
  .speedCameraOverlay
  .centerInfoLine
  .stats
  .textPrio3 {
  text-align: right;
}

.T_0204_MapOverlay
  .mapOverlay
  .speedCameraOverlay
  .centerInfoLine
  .scoIconContainer {
  position: relative;
  max-width: var(--minTouchSize);
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .scoright {
  right: 8px;
  width: 100px;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.T_0204_MapOverlay .mapOverlay .speedCameraOverlay .scoIcon {
  width: 75px;
  height: 75px;
  background-image: url('../img/Verkehrszeichen_Example.png');
  background-repeat: no-repeat;
  background-size: cover;
}

/*---------- Small Display ----------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0204_MapOverlay {
    width: 1768px;
    height: 569px;
  }

  .mapOverlay > .copyrightGoogle {
    width: 918px;
  }
  .T_0203_BurgerMenu .mapOverlay > .copyrightGoogle {
    left: 1059px;
    width: 343px;
  }
  .routeOverviewOn .mapOverlay > .copyrightGoogle {
    left: 970px;
    width: 520px;
  }

  .mapOverlay .topRightRow .subRowRev:last-of-type {
    display: none;
  }
}

.mapOverlay .darkenScreenConnector {
  position: absolute;
  background-color: rgba(var(--audiBlack), 0.7);
  height: 100%;
  width: 100%;
  z-index: 11;
}

.mapOverlay .SE_0106_Button_with_Connector {
  position: absolute;
  top: 30%;
  left: 50%;
}
