/* CLASSIFICATION: CONFIDENTIAL */

/* ------------------------ Standalone ------------------------ */

:global(#root) > .ST_0005_DriveSelect {
  margin: 1px;
  width: 1800px;
  height: 459px;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

:global(#root) > .ST_0005_DriveSelect .background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--audiBlack), 0.5);
  /* z-index: -10; */
}

/* ------------------------ .ST_0005_DriveSelect ------------------------ */

.ST_0005_DriveSelect {
}

.driveSelect {
  position: relative;
  width: 100%;
  height: 100%;
}

/* -------------------------------- decorator ------------------------------- */

.decorator {
  width: 100%;
  height: 100%;
  pointer-events: none;
  display: inline;
}

.driveSelect .decoratorWrapper {
  opacity: 0;
  transition: var(--AG_0202_FadeOut);
  transition-delay: 0.1s;
}

.driveSelect.balance .decoratorWrapper.balance,
.driveSelect.comfort .decoratorWrapper.comfort,
.driveSelect.dynamic .decoratorWrapper.dynamic,
.driveSelect.efficiency .decoratorWrapper.efficiency,
.driveSelect.offroad .decoratorWrapper.offroad {
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.1s;
}

.decorator .decoratorWheels {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  pointer-events: none;
  background-size: contain;
}

.decorator .decoratorCar {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  z-index: 1;
  pointer-events: none;
}

.decorator .decoratorCarColor {
  position: absolute;
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-size: contain;
  z-index: 1;
  pointer-events: none;
}

.decorator .decoratorWheels,
.decorator .decoratorCar,
.decorator .decoratorCarColor {
  transition: transform 2s;
  bottom: 40px;
  height: var(--carDecoratorHeight);
  width: calc(100% - 120px);
}

.decorator.lift .decoratorCar,
.decorator.lift .decoratorCarColor {
  transition: transform 2s;
  transform: translateY(-4px);
}

.decorator.low .decoratorCar,
.decorator.low .decoratorCarColor {
  transition: transform 2s;
  transform: translateY(4px);
}

/* --------------------------------- buttons -------------------------------- */
.btnOffroadContainer {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  right: 21px;
  bottom: 80px;
  display: inline;
}

.driveSelect .plusButtonContainer {
  position: absolute;
  width: 600px;
  height: var(--minTouchSize);
  right: 160px;
  bottom: 80px;
  display: inline;
}

.driveSelect .plusButtonContainer .button .text {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
}

.driveSelect .plusButtonContainer .button p {
  width: calc(100% - var(--textButtonMargin) * 2);
  margin: auto;
  margin-left: var(--textButtonMargin);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: right;
}

.driveSelect .driveSelectEditContainer {
  /* final position depending on the final decorator */
  position: absolute;
  height: fit-content;
  width: fit-content;
  display: flex;
  align-items: center;
  top: 139px;
  left: 390px;
  opacity: 0;
  transition: var(--AG_0202_FadeOut);
  transition-delay: 0.1s;
}
.driveSelect.balance .driveSelectEditContainer {
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.1s;
}

.driveSelect .driveSelectEditContainer .button {
  position: relative;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  display: flex;
  justify-content: center;
}

/* -------------------------------- connector ------------------------------- */

.driveSelect .driveSelectEditContainer .SE_0106_Button_with_Connector {
  position: relative;
  transform: translateY(-50%);
  margin-left: 100px;
  z-index: 1;
}

/* --------------------------- floating Container --------------------------- */

.driveSelect .SE_0039_Lift_Bargraph {
  top: 21px;
  left: 0px;
  z-index: 1;
  position: absolute;
}

/*------------------ SMALL DISPLAY ------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0005_DriveSelect {
    width: 1648px;
    height: 331px;
  }

  .driveSelect .driveSelectEditContainer {
    /* final position depending on the final decorator */
    top: 70px;
    left: 337px;
  }

  .decorator .decoratorWheels,
  .decorator .decoratorCar,
  .decorator .decoratorCarColor {
    bottom: 15px;
  }
}
.driveSelectButtonsContainer {
  position: absolute;
  display: flex;
  top: -119px;
  transform: translateX(-50%);
  left: 50%;
  z-index: 2;
  height: var(--minTouchSize);
}

.driveSelectButtonsContainer.tabbarVersionDS {
  display: none;
}

.driveSelectButtonsContainer .interactionBackground.leftBorderRadius {
  width: calc(100% - (var(--opticalTouchReduction) / 2));
}

.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .driveSelectButton.toggle.activated
  .uiState,
.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .driveSelectButton.toggle.activated
  .borderBottom,
.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .driveSelectButton.button:active
  .uiState,
.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .driveSelectButton.button:active
  .borderBottom {
  width: calc(100% - var(--uiStateMargin));
}

.ST_0005_DriveSelect .driveSelectButtonsContainer .driveSelectButton .state {
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
}

.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .interactionBackground.leftBorderRadius {
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
}

.ST_0005_DriveSelect
  .driveSelectButtonsContainer
  .interactionBackground.rightBorderRadius,
.ST_0005_DriveSelect .driveSelectButtonsContainer .state {
  border-radius: 0 var(--standardBorderRadius) var(--standardBorderRadius) 0;
}

.driveSelectButton {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 61px 0px 61px;
  max-width: 476px;
}
.driveSelectButton.das {
  padding: 0px 45px 0px 45px;
}

.driveSelectButton .textPrio2 {
  max-width: 405px;
}

.dSelectAssistantIcon {
  width: 21px;
  height: 21px;
  margin: 2px 0 0 11px;
  background-image: url(../../../../../Templates/SpecialElements/ST_0005_DriveSelect/01_HTML/img/driveSelectAssistant-Icon.png);
}

.dasPopupContainer {
  position: absolute;
  left: 0px;
  top: -119px;
  width: calc(var(--dashboardWidth) - (var(--popupOutlineWidth)));
  height: calc(var(--dashboardHeight) - (var(--popupOutlineWidth)));
  z-index: 2;
}
.splitContainer {
  display: flex;
  position: relative;
  width: 100%;
  flex-direction: column;
  flex-grow: 2;
}

.dasTitleLine {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.dasTitleLine .button.close {
  margin-left: 2px;
}
.dasTitleLine .button.right {
  margin-right: 2px;
}
.dasTitleLine > .headline {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  width: calc(100% - (2 * var(--minTouchSize) + 2 * var(--lA1)));
}

.dasContent {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: var(--popupInsetWidth);
  width: calc(100% - var(--popupInsetWidth));
  overflow: hidden;
}

.driveSelectAssistantContainer {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: calc(100% - 2 * var(--lA1));
  height: 100%;
  margin: 0 var(--lA1) 0 var(--lA1);
}

.driveSelectAssistantContainer .textPrio1 {
  text-align: center;
  margin-bottom: calc(var(--minTouchSize) + 30px);
}

.driveSelectAssistantButton .UE_0101_ActionButton {
  position: absolute;
  height: var(--minTouchSize);
  width: fit-content;
  bottom: 30px;
  transform: translateX(-50%);
  left: 50%;
  max-width: calc(100% - 2 * var(--lA1));
}

@media only screen and (min-width: 2219px) {
  :global(#root) > .ST_0005_DriveSelect {
    height: 578px;
  }
  .driveSelect .driveSelectEditContainer {
    top: 318px;
    left: 460px;
  }

  .decorator .decoratorWheels,
  .decorator .decoratorCar,
  .decorator .decoratorCarColor {
    width: 100%;
  }
}
