/* CLASSIFICATION: CONFIDENTIAL */

.triangle {
  --topMoreMenuTrianglePosition: 46px;
  --leftMoreMenuTrianglePosition: 980px;
}

:global(#root) > .UE_0405_MoreMenu {
  position: absolute;
  height: 600px;
  width: 100%;
  min-width: 550px;
  margin: 60px;
  background-color: rgb(var(--bodyColorGrey));
}

.popupStyling {
  position: relative;
  width: fit-content;
  max-width: calc(100vw - var(--dashboardMarginSide) * 2 - 24px);
  padding: 6px;
}
.moreMenuBackground {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
}

/*---------------------- CONTENT CONTAINER -------------------*/

.moreMenuExternalContainer {
  display: flex;
  flex-direction: column;
}

.moreMenuHeaderContainer {
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}
.moreMenuHeader {
  width: calc(100% - (2 * var(--textButtonMargin)));
  height: 74px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 var(--textButtonMargin);
}
.moreMenuHeader p {
  margin-left: 15px;
}
.moreMenuLine {
  background-color: rgba(var(--audiWhite), 0.6);
  height: 1px;
  width: 100%;
}

.moreMenuContainer {
  display: flex;
  flex-direction: row;
  padding: 2px;
}
.button {
  position: relative;
  min-width: var(--minTouchSize);
  width: auto;
  height: var(--minTouchSize);
  z-index: 1;
}

.moreMenu:not(.textButton) .button p {
  display: -webkit-box;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: var(--textButtonMargin);
  margin-right: var(--textButtonMargin);
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

/* ------------------ TEXT BUTTON VARIANT ------------------ */

.textButton {
}

.moreMenuTextButton {
  display: flex;
  flex-direction: column;
  width: 450px;
}

.moreMenuTextButton > .button {
  position: relative;
  min-width: var(--minTouchSize);
  height: var(--minTouchSize);
  width: 100%;
}

.moreMenuTextButton > .button:not(:last-of-type)::after {
  content: '';
  position: absolute;
  margin-left: 15px;
  height: 1px;
  width: calc(100% - 30px);
  background-color: rgba(var(--audiWhite), 0.2);
}

.moreMenuTextButton .button .text {
  height: 100%;
  display: flex;
  width: calc(100% - 2 * var(--iA2));
  margin: 0 var(--iA2);
  align-items: center;
}

.moreMenuTextButton .button .touchIcon {
  position: relative;
  left: 0px;
  top: 0px;
  flex-shrink: 0;
  transform: unset;
}

.moreMenuTextButton .button p {
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.moreMenuTextButton .button .text .touchIcon + .textPrio2 {
  margin-left: var(--iA1);
  text-align: left;
}

.settings {
  position: relative;
  top: 700px;
  margin-left: 16px;
}
