/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --calendarListTitleMargin: 90px;
  --calendarListAppointmentBarWidth: 3px;
  --halfTouchIcon: 25px;
  --windowHeight: 545px;
  --windowWidth: 1676px;
  --scrollingAnimationDuration: 0.5s;
}

:global(#root) > .SE_0059_CalendarList {
  margin: 0px;
  width: var(--windowWidth);
  height: var(--windowHeight);
  overflow: hidden;
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  z-index: -1;
}

.dark-rectangle {
  background-color: rgba(var(--audiBlack), 0.5);
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.calendarList {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
}

.calendarList .leftPanel {
  flex: 1;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
}

.leftPanel .calendarListContainer.fadeOutBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.leftPanel .calendarListContainer.fadeOutTop {
  -webkit-mask-image: var(--listStartFadeout);
}

.leftPanel .calendarListContainer.fadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.rightPanel {
  flex: 1;
  height: 100%;
  overflow-x: hidden;
  box-sizing: border-box;
  display: flex;
  position: relative;
}

/*---------------Tob Bar------------------*/
.calendarTitleContainer {
  height: var(--minTouchSize);
  width: calc(100% - (var(--opticalTouchReduction) / 2));
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 0 var(--listVerticalPadding);
  margin-left: calc(var(--opticalTouchReduction) / 2);
  box-sizing: border-box;
}

.calendarTitleItem {
  min-height: calc(var(--minTouchSize) - (var(--cA1) * 2));
  width: calc(100% - (var(--iA1) * 2));
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  position: relative;
  padding: var(--cA1) 0;
  margin: 0 var(--iA1);
}

.calendarListItem:not(:last-child)::after {
  content: '';
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: var(--listSeparatorLineColor);
}

.calendarTitleItem .UE_1009_C2Cursor {
  position: absolute;
  display: block;
  height: 100%;
  width: calc(100% + 2 * var(--iA1) + 2 * var(--listVerticalPadding));
  left: calc(-1 * (var(--iA1) + var(--listVerticalPadding)));
}

.calendarTitleIcon {
  position: relative;
  margin-left: var(--iA2);
}

.calendarTitle {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.calendarTitle p.textPrio1 {
  width: 100%;
  text-align: center;
  /* overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; */
}

/*---------------List element------------------*/
.calendarListContainer {
  position: relative;
  top: var(--zA2);
  height: calc(100% - var(--minTouchSize) - var(--zA2));
  width: 100%;
  display: flex;
  align-content: flex-start;
  box-sizing: border-box;
  flex-direction: column;
  padding: 0 var(--listVerticalPadding);
  overflow-y: scroll;
}

.calendarListContainer.headerActive {
  height: calc(100% - var(--minTouchSize) - var(--zA2));
}

.calendarListItem {
  min-height: calc(var(--minTouchSize) - (var(--cA1) * 2));
  width: calc(100% - (var(--iA1) * 2));
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: space-between;
  position: relative;
  padding: var(--cA1) 0;
  margin: 0 var(--iA1);
  overflow: hidden;
}

.calendarListItem .UE_1009_C2Cursor {
  position: absolute;
  height: 100%;
  width: 100%;
}

.calendarListItem.selectedAppointment .UE_1009_C2Cursor {
  display: block;
}

.appointmentTime {
  position: relative;
  margin-right: var(--iA2);
  box-sizing: border-box;
  overflow: hidden;
}

.appointmentRow {
  display: flex;
  justify-content: flex-end;
  width: 133px;
}
.appointmentRow.am_pm {
  width: 164px;
}

.appointmentTime p.textPrio1,
.appointmentTime p.textPrio2 {
  margin-left: var(--lA1);
  /* overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; */
}

.appointmentRow .time_pm,
.appointmentRow .time_am {
  display: none;
}
.appointmentRow .time_am {
  margin-top: 5px;
}
.appointmentRow .time_pm {
  margin-top: 19px;
  margin-left: 1px;
}
.appointmentRow.secondRow .time_pm {
  margin-top: 10px;
  margin-left: 1px;
}
.appointmentRow.am_pm.pm .time_pm,
.appointmentRow.am_pm.am .time_am {
  display: inline-block;
}

.appointmentTitle {
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
  box-sizing: border-box;
}

.appointmentTitle.yellow {
  border-left: var(--calendarListAppointmentBarWidth) solid
    rgb(var(--audiYellow));
}

.appointmentTitle.blue {
  border-left: var(--calendarListAppointmentBarWidth) solid
    rgb(var(--climateBlue));
}

.appointmentTitle p.textPrio1,
.appointmentTitle p.textPrio2 {
  margin-left: var(--iA2);
  /* overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis; */
  opacity: 0.4;
}

.appointmentImage {
  position: relative;
  display: flex;
  width: 110px;
  justify-content: center;
  align-items: center;
  opacity: 0.4;
}

.appointmentImage .touchIcon {
  position: relative;
  top: 0;
  left: 0;
  transform: none;
}

.calendarListItem.default {
}

.calendarListItem.declined .appointmentTitle p {
  text-decoration: line-through;
}

.calendarListItem.accepted .appointmentTitle p,
.calendarListItem.accepted .appointmentImage {
  opacity: 1;
}

/*---------------Day View------------------*/

.SE_0059_CalendarList
  .calendarList
  .rightPanel
  .SE_0055_DayDetailView::-webkit-scrollbar {
  display: none;
}

.SE_0059_CalendarList
  .calendarList
  .rightPanel
  .SE_0055_DayDetailView
  .dayDetailView::-webkit-scrollbar-track {
  display: none;
}

.SE_0055_DayDetailView .dayDetailView.appointment {
  overflow-y: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
}

.SE_0055_DayDetailView {
  overflow-x: hidden;
  overflow-y: visible;
}

.SE_0055_DayDetailView .UE_0202_FloatingContainer .floatingContainer {
  max-width: calc((var(--windowWidth) / 2) - (var(--textButtonMargin) * 2));
}

/* ------------- List Header ------------- */

.header {
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  width: calc(100% - var(--opticalTouchReduction) / 2);
  margin-left: calc(var(--opticalTouchReduction) / 2);
  animation: restoreHeight var(--scrollingAnimationDuration);
}

.today {
  display: flex;
  align-items: center;
  animation: reverseButtonExpanding var(--scrollingAnimationDuration);
}

.buttonContainer {
  height: 100%;
  display: flex;
  flex: 1;
  overflow: hidden;
}

.extensibleBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  min-width: unset;
  animation: reverseButtonCollapsing var(--scrollingAnimationDuration);
}

.prevDay,
.nextDay {
  min-width: unset;
  width: var(--minTouchSize);
  animation: reverseSmallButtonCollapsing var(--scrollingAnimationDuration);
}

.buttonContainer .button {
  height: 100%;
}

.buttonContainer .button div:last-of-type {
  overflow: hidden;
}

.buttonContainer .button p {
  position: relative;
  margin: 0 var(--textButtonMargin);
}

@media only screen and (max-width: 1768px) {
  :root {
    --windowWidth: 1524px;
    --windowHeight: 416px;
  }

  .appointmentTitle {
    flex: 3;
  }

  .SE_0055_DayDetailView .dayDetailView.appointment::-webkit-scrollbar {
    display: block;
  }
}

/* --------------- Scrolling behavior --------------- */

.scrolling .calendarListContainer {
  height: calc(100% - 52px - var(--zA2));
}

.scrolling .calendarListContainer.headerActive {
  height: calc(100% - 52px - var(--zA2));
}

.scrolling .header {
  height: 50px;
  animation: shrinkHeight var(--scrollingAnimationDuration);
}

.scrolling .buttonContainer {
  padding: 2px 0;
}

.scrolling .today {
  width: 100%;
  animation: buttonExpanding var(--scrollingAnimationDuration);
}

.scrolling .prevDay,
.scrolling .nextDay {
  width: 0;
  min-width: 0;
  animation: smallButtonCollapsing var(--scrollingAnimationDuration);
}

.scrolling .buttonContainer .touchIcon {
  animation: hideContent 0.2s;
  animation-fill-mode: both;
}

.buttonContainer .touchIcon {
  animation: showContent 0.2s;
  animation-delay: 0.3s;
  animation-fill-mode: both;
}

.scrolling .extensibleBtn {
  width: 0;
  min-width: 0;
  flex: unset;
  animation: buttonCollapsing var(--scrollingAnimationDuration);
}

.extensibleBtn p {
  animation: showContent 0.2s;
  animation-delay: 0.3s;
  animation-fill-mode: both;
}

.scrolling .extensibleBtn p {
  animation: hideContent 0.2s;
  animation-fill-mode: both;
}

@keyframes hideContent {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes showContent {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes shrinkHeight {
  from {
    height: 119px;
  }

  to {
    height: 50px;
  }
}

@keyframes restoreHeight {
  from {
    height: 50px;
  }

  to {
    height: 119px;
  }
}

@keyframes buttonExpanding {
  from {
    width: 271px;
  }

  to {
    width: 100%;
  }
}

@keyframes reverseButtonExpanding {
  from {
    width: 100%;
  }

  to {
    width: 271px;
  }
}

@keyframes smallButtonCollapsing {
  from {
    width: 119px;
  }

  to {
    width: 0;
  }
}

@keyframes reverseSmallButtonCollapsing {
  from {
    width: 0;
  }

  to {
    width: 119px;
  }
}

@keyframes buttonCollapsing {
  from {
    width: 270px;
  }

  to {
    width: 0;
  }
}

@keyframes reverseButtonCollapsing {
  from {
    width: 0;
  }

  to {
    width: 270px;
  }
}

.settings {
  position: absolute;
  top: 600px;
}
