/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0401_OperationPanel {
  margin: 10px 0;
  height: var(--operationPanelHeight);
  background-color: rgb(var(--bodyColorGrey));
}

.operationPanel {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.button {
  flex: 1;
  position: relative;
}

.loaderContainer {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

/* handle in source selecetion */
.sourceSelectionOpen .UE_0401_OperationPanel .operationPanel .button {
  opacity: 0.4;
}

.button .Text {
  position: relative;
  white-space: nowrap;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  text-overflow: ellipsis;
  overflow: hidden;
  margin-left: var(--textButtonMargin);
  margin-right: var(--textButtonMargin);
}
.UE_0401_OperationPanel .operationPanel.fiveButtons .button {
  max-width: calc(100% / 5);
}
.UE_0401_OperationPanel .operationPanel.sevenButtons .button {
  max-width: calc(100% / 7);
}
