.TiltAngle__Wrapper {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 100%;
}

.TiltAngle__sideTiltContainer {
  width: 100%;
  height: 100%;
  mask: linear-gradient(
    180deg,
    #0000 27%,
    rgba(0, 0, 0, 1) 38%,
    rgba(0, 0, 0, 1) 64%,
    #0000 71%
  );
  -webkit-mask: linear-gradient(
    180deg,
    #0000 1%,
    rgba(0, 0, 0, 1) 3%,
    rgba(0, 0, 0, 1) 99%,
    #0000 97%
  );
  position: absolute;
  /* bottom: 34px; */
  left: 50%;
  transform: translateX(-50%);
}

