.TiltAngle__sideTiltImg {
  position: absolute;
  top: -226.5px;
  left: 174px;
}

.TiltAngle__sideTilt__tag {
  position: absolute;
  top: 50%;
  left: 191px;
  transform: translateY(-50%);
}

.TiltAngle__lengthTiltContainer {
  width: 100%;
  height: 100%;
  grid-area: 1/1;
  mask: linear-gradient(
    180deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 40%,
    rgba(0, 0, 0, 1) 57%,
    #0000 60%
  );
  -webkit-mask: linear-gradient(
    180deg,
    #0000 -13%,
    rgba(0, 0, 0, 1) 16%,
    rgba(0, 0, 0, 1) 90%,
    #0000 60%
  );
  position: absolute;
  bottom: 0px;
  left: 50%;
  /* transform: translateX(-50%); */
  transform: translateX(-600px);
}

.TiltAngle__lengthTilt {
  position: absolute;
  top: 1.87px;
  background-image: url('../../img/TiltAngle_line_big.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  height: 100%;
  width: 100%;
  transform: scale(4.5);
}

.TiltAngle__lengthTilt__calibrators {
  position: relative;
  height: 100%;
}

.TiltAngle__lengthTilt__center_short {
  height: 2px;
  width: 55px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-82%, -50%);
}

.TiltAngle__lengthTilt__right_short {
  height: 2px;
  width: 55px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  right: 174px;
  transform: translateY(-50%);
}

.TiltAngle__lengthTilt__wings {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 2px;
  width: 73%;
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.TiltAngle__lengthTilt__wings_left {
  height: 100%;
  width: 100%;
  background-color: #fff;
  mask: linear-gradient(
    90deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 40%,
    rgba(0, 0, 0, 1) 57%,
    #0000 60%
  );
  -webkit-mask: linear-gradient(
    90deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 59%,
    rgba(0, 0, 0, 1) 64%,
    #0000 98%
  );
  opacity: 50%;
}

.TiltAngle__lengthTilt__wings_left {
  height: 100%;
  width: 100%;
  background-color: #fff;
}

.TiltAngle__lengthTilt__wings_right {
  height: 100%;
  width: 100%;
  background-color: #fff;
  mask: linear-gradient(
    90deg,
    #0000 30%,
    rgba(0, 0, 0, 1) 40%,
    rgba(0, 0, 0, 1) 57%,
    #0000 60%
  );
  -webkit-mask: linear-gradient(
    90deg,
    #0000 33%,
    rgba(0, 0, 0, 1) 69%,
    rgba(0, 0, 0, 1) 64%,
    #0000 98%
  );
  opacity: 50%;
}

.TiltAngle__lengthTiltImg {
  position: absolute;
  top: -445px;
  left: 590.5px;
}

.TiltAngle__lengthTilt__tag {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(7%, -50%);
}

.TiltAngle__tag {
  display: flex;
  align-items: center;
}

.TiltAngle__tag__text {
  box-sizing: border-box;
  border: 2px solid rgba(var(--ambientcolor1));
  width: 95px;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.TiltAngle__tag__tip {
  box-sizing: border-box;
  height: 2px;
  width: 33px;
  background-color: rgba(var(--ambientcolor1));
}

.TiltAngle__tag__text p {
  font-family: var(--fontLight);
}
