/* CLASSIFICATION: CONFIDENTIAL */

/* Standalone */

:global(#root) > .SE_0029_Quicksettings {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  font-size: 0;
  overflow: hidden;
  background-color: black;
}

/* .SE_0029_Quicksettings */

.SE_0029_Quicksettings {
}
.closed {
  width: 0 !important;
}

:global(#root) > .SE_0029_Quicksettings .wallpaper {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--climateBarHeight));
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

:global(#root) > .SE_0029_Quicksettings .bodyWrapper {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--statusBarHeight) - var(--climateBarHeight));
  margin-top: var(--statusBarHeight);
}

.quickSettings {
  width: calc(100% - var(--minTouchSize));
  height: 100%;
}

.decorator {
  position: absolute;
  width: 1163px;
  height: 395px;
  top: calc(var(--minTouchSize) + 40px);
  margin-left: -20px;
}

.decoratorBasic {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.decoratorColor_Ambient {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  background-color: rgb(var(--ambientcolor1));
}

.decoratorColor_Line,
.decoratorColor_ICL {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  background-color: rgb(var(--ambientcolor1));
}

.settingButtons {
  position: absolute;
  display: flex;
  flex-direction: column;
  padding-top: 37px;
  padding-bottom: 66px;
  padding-left: 30px;
  width: calc(100% - 149px);
  height: calc(100% - 37px - 60px);
}

.settingButtonGroup {
  position: relative;
  display: flex;
  /* justify-content: space-between; */
}

.settingButtonGroup.top .button:not(:last-child) {
  margin-right: 22px;
}

.settingButtonGroup.top {
  flex-direction: row;
  height: var(--minTouchSize);
  width: 100%;
  flex-shrink: 0;
}

.left {
  flex-direction: row;
  height: var(--minTouchSize);
  width: 965px;
}

.right {
  width: 573px;
  height: 542px;
  flex-direction: column;
  margin-left: 84px;
}

.settingButtonGroup .button {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

.right .button {
  width: 100%;
}

.settingButtonGroup .button .buttonContent {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  flex-direction: row;
}

.settingButtonGroup .button .buttonContent .icon {
  position: relative;
  width: var(--minTouchSize);
  height: 100%;
}

.settingButtonGroup .button .buttonContent .text {
  height: 100%;
  width: calc(100% - var(--minTouchSize));
}

.settingButtonGroup .button .buttonContent .text .textPrio1 {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-right: var(--lA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.handle {
  height: 178px;
  width: 2px;
  top: 50%;
  transform: translateY(-50%);
  right: 50px;
  position: absolute;
}

.moveHandle {
  left: 1px;
  position: absolute;
}

.triangle {
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-right: 8px solid rgb(var(--audiWhite));
  border-bottom: 15px solid transparent;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.upperLine {
  width: 3px;
  height: 82px;
  position: absolute;
  top: 0;
  background-color: rgb(var(--borderGrey));
  border-radius: 10px;
  left: 0;
  clip-path: polygon(0 0, 100% 0, 100% 83%, 0 90%);
}

.lowerLine {
  width: 3px;
  height: 82px;
  position: absolute;
  bottom: 0;
  background-color: rgb(var(--borderGrey));
  border-radius: 30px;
  left: 0;
  clip-path: polygon(0 10%, 100% 17%, 100% 100%, 0 100%);
}

.moveHandle .triangle {
  border-right: unset;
  border-top: 10px solid transparent;
  border-left: 5px solid rgb(var(--audiWhite));
  border-bottom: 10px solid transparent;
}

.moveHandle .upperLine {
  height: 88px;
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0 83%);
  left: 2px;
}

.moveHandle .lowerLine {
  height: 88px;
  clip-path: polygon(0 17%, 100% 10%, 100% 100%, 0 100%);
  left: 2px;
}

.quickSettingsConfig {
  position: absolute;
  bottom: 0;
}

.quickSettingsClosed {
  width: 4px;
}

.lowerGroup {
  position: relative;
  display: flex;
  height: calc(100% - 72px);
  margin-top: 72px;
  justify-content: space-between;
}
.settingButtonGroup.lowerLeft {
  width: 100%;
  margin: 0 186px 0 100px;
}

.settingButtonGroup.lowerLeft > div {
  width: 100%;
  margin-top: auto !important;
  margin-bottom: 0 !important;
}

.settingButtonGroup.lowerLeft .UE_0601_Slider {
  width: 100%;
  margin-top: auto;
}
.settingButtonGroup.lowerLeft .UE_0601_Slider .absoluteSlider {
  position: relative;
  top: 0px;
}
.settingButtonGroup.lowerRight {
  width: 515px;
  height: 100%;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: flex-end;
}
.settingButtonGroup .buttonGroupRightTop {
  display: flex;
  height: var(--minTouchSize);
  position: relative;
  margin-left: auto;
}

.buttonGroupRightTop .button {
  margin-right: 0 !important;
}

.switchContainer {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  flex-shrink: 0;
}

.switchContainer .UE_1103_Switch {
  width: var(--minTouchSize);
  margin-left: auto;
}

.settingButtonGroup .switchContainer + .switchContainer {
  margin-top: 22px;
}
.settingButtonGroup .switchContainer p {
  align-self: center;
  margin: 0 var(--cA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: calc(100% - 2 * var(--cA1) - var(--minTouchSize));
}

.settingButtonGroup .buttonGroupRightTop .button {
  margin-right: 0 !important;
}

.settingButtonGroup .buttonGroupRightTop {
  display: flex;
  height: var(--minTouchSize);
  position: relative;
  margin-left: auto;
}

.button.switchButton {
  padding-left: 0px;
  padding-right: 0px;
}

.button.switchButton .switch {
  position: relative;
}

/*-------------------closed-----------------*/

.SE_0029_Quicksettings.closed {
  z-index: 9;
}

.SE_0029_Quicksettings.closed .quickSettings .handle {
  left: calc(1px - var(--directAccessWidth));
  position: absolute;
}

.SE_0029_Quicksettings.closed .quickSettings {
  width: 4px;
}

/*-------------------two columns list--------------------------*/

.SE_0029_Quicksettings.twoColumnsList .quickSettings .settingButtons {
  height: calc(100% - 40px);
}

.SE_0029_Quicksettings.twoColumnsList
  .quickSettings
  .settingButtons
  .lowerGroup {
  height: calc(100% - var(--iA1) - var(--minTouchSize));
  margin-top: var(--iA1);
}

.SE_0029_Quicksettings.twoColumnsList
  .quickSettings
  .settingButtons
  .lowerGroup
  .SE_0080_TwoColumnsList {
  display: block;
}

.SE_0029_Quicksettings.twoColumnsList
  .quickSettings
  .settingButtons
  .settingButtonGroup.top
  .quickSettingsTitle {
  display: flex;
  align-items: center;
}

/* ------------------------------- edit mode -------------------------------- */

.SE_0029_Quicksettings.editMode .quickSettings .textBlock,
.SE_0029_Quicksettings.editMode
  .quickSettings
  .settingButtonGroup.lowerLeft
  .UE_0101_ActionButton {
  display: block;
}

.SE_0029_Quicksettings.editMode .quickSettings .textBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 0 var(--iA1) !important;
  margin-bottom: 120px !important;
  max-width: calc(100% - var(--iA2));
}

.SE_0029_Quicksettings.editMode .quickSettings .button.notReplaceable {
  opacity: var(--touchDisabledState);
}

.SE_0029_Quicksettings.editMode
  .quickSettings
  .settingButtonGroup.lowerLeft
  .UE_0101_ActionButton {
  width: fit-content;
  position: absolute;
  bottom: 0;
  max-width: calc(100% - (var(--textButtonMargin) * 4));
}

.SE_0029_Quicksettings.editMode
  .quickSettings
  .settingButtonGroup.lowerLeft
  .UE_0101_ActionButton
  .btnContainerText
  p {
  margin: 0 calc(var(--textButtonMargin) * 2) !important;
  width: calc(100% - (var(--textButtonMargin) * 4));
}

.SE_0029_Quicksettings.editMode .quickSettings .settingButtonGroup.lowerLeft {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: unset;
  margin-top: var(--iA1) !important;
  width: 1023px;
}

.SE_0029_Quicksettings.editMode .quickSettings .settingButtonGroup.lowerLeft p {
  max-width: calc(100% - var(--iA2));
}

/* .SE_0029_Quicksettings.editMode .quickSettings .settingButtonGroup.lowerRight {
  margin-left: 84px;
} */

/* ---------------------------- edit mode active ----------------------------- */
.SE_0029_Quicksettings .quickSettings {
  transition: all 0.1s ease-in-out;
}

.SE_0029_Quicksettings.editMode.active
  .quickSettings
  .settingButtonGroup.top
  .UE_0101_ActionButton:not(.rightTop),
.SE_0029_Quicksettings.editMode.active
  .quickSettings
  .settingButtonGroup
  .switchContainer.actionButtonWrapper:not(.dragging) {
  transform: scale(0.9);
  opacity: 0.4;
}

.SE_0029_Quicksettings.editMode.active
  .switchContainer.actionButtonWrapper.dragging
  .uiState {
  width: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}

