.UE_1103_Switch .switch {
  pointer-events: none;
}

.UE_0101_ActionButton {
  cursor: pointer;
}

/*------------------- Settings -----------------*/

.settings {
  position: absolute;
  top: 800px;
}

/* ------------------------------ small display ----------------------------- */

@media only screen and (max-width: 1768px) {
  .decorator {
    position: absolute;
    width: 920px;
    top: 90px;
    margin-left: 20px;
  }

  .settingButtons .settingButtonGroup.top .button:nth-of-type(9) {
    display: none;
  }

  .settingButtons {
    padding-top: 0px;
    padding-bottom: 26px;
    height: calc(100% - 26px);
  }

  .settingButtons .lowerGroup {
    height: calc(100% - 22px);
    margin-top: 22px;
  }

  /* .handle {
    left: calc(100% - 92px);
  } */

  .SE_0029_Quicksettings.twoColumnsList .quickSettings .settingButtons {
    height: calc(100% - 5px);
  }

  .SE_0029_Quicksettings.editMode .quickSettings .settingButtonGroup.lowerLeft {
    width: 871px;
  }
}

@media only screen and (min-width: 2219px) {
  .decorator {
    width: 1200px;
    height: 400px;
    margin-left: -40px;
    top: calc(var(--minTouchSize) + 90px);
  }
}
