:global(#root) > .SE_0092_InteriorExperienceSettings {
  margin: auto;
  width: 1678px;
  height: 553px;
}
.SE_0092_InteriorExperienceSettings {
}
.containerInteriorExperienceSettings {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.interiorExperienceSettings_DataPicker {
  display: flex;
}

.UE_1301_DataPicker {
  position: relative;
  height: var(--dataPickerHeight);
}

.UE_1301_DataPicker .dataPicker .list {
  width: calc(100% - 10px);
  margin-right: 5px;
}

.dataPickerCenter {
  width: 340px;
}

.dataPickerLeft,
.dataPickerRight {
  width: 520px;
}

.dataPicker .selectionFrame .statictext {
  margin-left: unset;
  right: 5px;
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0092_InteriorExperienceSettings {
    width: 1526px;
    height: 425px;
  }
  .dataPickerCenter {
    width: 310px;
  }

  .dataPickerLeft,
  .dataPickerRight {
    width: 490px;
  }
}
