.launchDashboardScreen > * {
  box-sizing: border-box;
}

.launchDashboardScreen {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.header {
  position: relative;
  display: flex;
  align-items: center;
}

.body_left p:first-child {
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break-spaces;
}

.body_left p,
.body_left p:first-child,
.checkbox_container p {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break-spaces;
}

.body_left p:first-child {
  -webkit-line-clamp: 4;
}
.checkbox_container p {
  -webkit-line-clamp: 1;
}

.body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 80px 0;
}

.body_left {
  display: flex;
  flex-direction: column;
  gap: 40px;

  padding-left: 117px;
  width: 65%;
}

.checkbox_container {
  display: flex;
  gap: 30px;
}

.body_right {
  display: flex;
  flex-direction: column;
  gap: 50px;
  align-items: center;
  margin-top: 67px;
}

