:global(#root) > .SE_0091_Recipients_screen {
  margin: 0px;
  width: 1682px;
  height: 487px;
  overflow: hidden;
  position: absolute;
  background: rgb(var(--bodyColorGrey));
}

.SE_0091_Recipients_screen .recipientsScreen {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.recipientsScreen .UE_0201_ToggleButtonGroup {
  padding: 0 var(--lA1);
  margin: 0 30px;
}

.UE_0101_ActionButton .btnContainerText {
  width: 100%;
}

.SE_0091_Recipients_screen .recipientsScreen .recipientInputLine {
  padding: var(--lA1);
  margin: 0 30px;
}
.SE_0091_Recipients_screen .recipientsScreen .recipientInputLine:nth-child(2) {
  padding-top: 0;
  padding-bottom: 15px;
}

.SE_0091_Recipients_screen
  .recipientsScreen
  .recipientInputLine
  .recipientInputField
  .UE_0501_InputField {
  height: var(--minTouchSize);
  flex: 1;
}

.SE_0091_Recipients_screen
  .recipientsScreen
  .recipientInputLine
  .recipientInputField {
  display: flex;
}

.SE_0091_Recipients_screen
  .recipientsScreen
  .recipientInputLine
  .recipientInputField
  .UE_0101_ActionButton {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.SE_0091_Recipients_screen
  .recipientsScreen
  .recipientInputLine
  .recipientLabel
  .textPrio2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-bottom: var(--hA1);
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0091_Recipients_screen {
    width: 1530px;
    height: 308px;
  }
}

@media only screen and (min-width: 2219px) {
  .SE_0091_Recipients_screen .recipientsScreen {
    justify-content: center;
  }
  .recipientsScreen .UE_0201_ToggleButtonGroup {
    padding: 0 15px;
  }
  .SE_0091_Recipients_screen .recipientsScreen .recipientInputLine {
    padding: 15px 15px 10px 15px;
  }

  .SE_0091_Recipients_screen
    .recipientsScreen
    .recipientInputLine:nth-child(3) {
    padding-bottom: 0;
  }
}
