:global(#root) > .SE_0002_SOCBargraph {
  padding: 0px 10px;
  position: relative;
  width: calc(100% - 20px);
}
.SE_0002_SOCBargraph {
  display: flex;
  align-items: center;
  width: 100%;
  height: 250px;
  box-sizing: border-box;
}

.barContainer {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.barContainer.withButtons {
  left: 124px;
  width: calc(100% - (var(--minTouchSize) + 5px) * 2);
}

.noInteractionEmpty,
.noInteractionFilling,
.firstNotInteractable,
.firstNotInteractableFilling,
.firstEmptyInteractable,
.firstInteractableFilling,
.lastEmptyInteractable,
.lastInteractableFilling,
.lastNotInteractable,
.lastNotInteractableFilling {
  position: absolute;
  width: 100%;
}

.lastEmptyInteractable,
.lastNotInteractable {
  background-color: rgba(var(--fpkGreen), 0.3);
  height: 2px;
}

.lastInteractableFilling,
.lastNotInteractableFilling {
  background-color: rgb(var(--fpkGreen));
  height: 2px;
}

.noInteractionEmpty,
.firstNotInteractable,
.firstEmptyInteractable {
  background-color: rgba(var(--fpkGreen), 0.3);
  height: 6px;
}

.firstNotInteractableFilling,
.noInteractionFilling,
.firstInteractableFilling {
  background-color: rgb(var(--fpkGreen));
  height: 6px;
}

.range {
  position: absolute;
  display: block;
  appearance: none;
  -webkit-appearance: none;
  width: 100%;
  height: 100%;
  background-color: transparent;
  cursor: pointer;
  margin: 0;
}

.range:focus {
  outline: none;
}
.range::-webkit-slider-runnable-track {
  width: 100%;
  height: 26px;
  box-shadow: none;
  border: none;
  -webkit-appearance: none;
  background-color: transparent;
}

.range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2px;
  height: 26px;
  background: rgb(var(--audiWhite));
  cursor: pointer;
}

.barContainer.withButtons .range::-webkit-slider-thumb {
  height: 14px;
  transform: translateY(6px);
}

.valueContainer {
  position: absolute;
  width: auto;
  height: 73px;
  top: 15px;
}

.positiveButton {
  position: absolute;
  right: 0;
  margin-left: 5px;
  visibility: hidden;
}

.positiveButton.visible {
  visibility: visible;
}

.negativeButton {
  position: absolute;
  left: 0;
  margin-right: 5px;
  visibility: hidden;
}

.negativeButton.visible {
  visibility: visible;
}
/*----------------------------- More Menu ----------------------------------*/

.SE_0002_SOCBargraph .socBargraph .pinContainer {
  position: absolute;
  top: 0px;
  width: calc(100% - var(--sliderAbsoluteMargin) * 2);
  height: var(--minTouchSize);
  margin: 0 var(--sliderAbsoluteMargin);
}
.SE_0002_SOCBargraph.pin .socBargraph .pinContainer {
  display: block;
}

.SE_0002_SOCBargraph .socBargraph .boundingBox {
  position: absolute;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  left: 402px;
  /* background-color: rgba(var(--boundingBoxGreen)); */
}

.SE_0002_SOCBargraph .socBargraph .UE_0405_MoreMenu {
  position: absolute;
  top: var(--popupOutlineWidth);
  left: 449px;
  z-index: 0;
}
.SE_0002_SOCBargraph .socBargraph .UE_0405_MoreMenu .moreMenu {
  z-index: 0;
}
.SE_0002_SOCBargraph
  .socBargraph
  .UE_0405_MoreMenu
  .moreMenu
  .moreMenuContainer
  .button {
  min-width: 72px;
  height: 72px;
}

.socTextContainer {
  position: absolute;
  top: 0px;
  height: 75px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.socTextContainer .socTargetLabel {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.socTextContainer .socTargetLabel p:nth-child(2) {
  margin-top: 0;
  margin-left: 5px;
}
.socTextContainer > p {
  margin-top: var(--zA2);
}

.batteryLabelContainer {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-between;
  top: 150px;
}

.batteryLabelContainer p {
  margin: unset !important;
}

/* ----------------------------- destinationFlag ---------------------------- */

.SE_0002_SOCBargraph .destinationPosition {
  position: absolute;
  top: 55px;
  left: 200px;
  width: 40px;
  height: 50px;
  transform: translateX(-50%);
}

.SE_0002_SOCBargraph.navi .destinationPosition {
  display: block;
}

.SE_0002_SOCBargraph .destinationPosition .destinationFlag {
  position: absolute;
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E762_specialflag_20x20.svg');
  width: 40px;
  height: 40px;
  left: 7px;
}

.SE_0002_SOCBargraph .destinationPosition .minVariant {
  width: 40px;
  top: 19px;
  position: relative;
  text-align: center;
}

.SE_0002_SOCBargraph .destinationPosition .destinationArrow {
  position: absolute;
  width: 9px;
  height: 5px;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url('../img/SE_0002_SOCBargraph_arrow.png');
  background-size: contain;
  display: block;
}

/*-------------------- Distance Labels --------------------*/
.SE_0002_SOCBargraph .distanceLabelWrapper {
  position: absolute;
  width: 75%;
  height: fit-content;
  left: 0;
  top: 185px;
}

.SE_0002_SOCBargraph .distanceLabelWrapper p {
  position: relative;
  text-align: center;
  width: 100%;
  left: 0px;
}

.SE_0002_SOCBargraph .distanceLabelWrapper .line {
  position: relative;
  width: 100%;
  height: 10px;
  left: 0px;
  border-right: 1px solid var(--tabbarSeparatorColor);
  border-bottom: 1px solid var(--tabbarSeparatorColor);
  border-left: 1px solid var(--tabbarSeparatorColor);
  box-sizing: border-box;
}
