/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --vpsBorderWidth: 3px;
  --opsWidth: 500px;
  --opsViewWidth: calc(var(--opsWidth) + var(--minTouchSize) * 2);
  --opsCarHeight: 445px;
  --opsCarWidth: 210px;
  --opsCarHeightSmall: 190px;
  --opsCarWidthSmall: 90px;
  --opsParkManeuverShift: 154px;
  --opsParkManeuverOut: 467px;
  --opsButtonLineBorderWidth: 1px;
  --opsTabbarButtonHeight: 109px;
  --opsTabbarHeight: calc(
    var(--opsTabbarButtonHeight) + var(--uiStateMargin) - 1px
  );
  --opsSettingsWidth: calc(
    100vw - var(--opsViewWidth) - var(--vpsBorderWidth) -
      var(--buttonBorderDist) - 6px
  );
  --opsSecondRowOffset: 20px;
  --opsMirrorDist: calc(
    50% - var(--opsCarWidthSmall) / 2 - var(--minTouchSize)
  );
  --opsTrailerHeight: 250px;
  --opsTrailerWidth: 200px;
  --opsBackgroundOpacity: 0.5;
}

:global(#root) > .SE_0015_Parking_OPS {
  position: relative;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--ambientBackground1));
  width: 1920px;
  height: 956px;
  margin: 0;
}

:global(#root) > .SE_0015_Parking_OPS.parking_OPS_b10Screen {
  margin-left: var(--directAccessWidth);
}

:global(#root) > .SE_0015_Parking_OPS .opsParking {
  position: relative;
}

.SE_0015_Parking_OPS {
}

.vpsWide {
}

.hideTopRightButton {
  visibility: hidden;
}

.opsParking {
  width: var(--opsViewWidth);
  height: 100%;
  box-sizing: border-box;
  position: absolute;
  z-index: 10;
}

.parking_OPS_b10Screen,
.parking_OPS_b10Screen .opsParking,
.parking_OPS_b10Screen .opsView {
  width: var(--opsWidth);
}

.parking_OPS_b10Screen .opsParking {
  box-sizing: unset;
}

.parking_OPS_b10Screen .opsView {
  left: 0;
}

.opsParkingBackground {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--audiBlack));
  background-size: 100vw 100vh;
  background-position: 0 0;
}

.opsParking.vpsWide .opsParkingBackground {
  clip-path: polygon(
    0% 0%,
    0% 100%,
    var(--minTouchSize) 100%,
    var(--minTouchSize) var(--opsTabbarHeight),
    100% var(--opsTabbarHeight),
    100% 0%
  );
}

.parking_OPS_b10Screen .opsParking.vpsWide .opsParkingBackground {
  clip-path: polygon(
    0% 0%,
    0% 100%,
    0 100%,
    0 var(--opsTabbarHeight),
    100% var(--opsTabbarHeight),
    100% 0%
  );
}

/*-------------------------------- T_0202_Settings --------------------------------*/
.T_0202_Settings {
  position: absolute;
  width: 0;
  opacity: 0;
  transition: var(--AG_0202_FadeOut), var(--AG_0303_ScaleResize);
  pointer-events: none;
  height: calc(100% - 2 * var(--buttonBorderDist) + 2px);
  top: calc(var(--buttonBorderDist) - 1px);
  left: calc((var(--opsViewWidth) + var(--vpsBorderWidth)) + 7px);
}
.T_0202_Settings .infoPopupDropdownItem .btnGroup .UE_0101_ActionButton {
  width: 278px;
}

.parking_OPS_b10Screen .T_0202_Settings {
  left: calc((var(--opsWidth) + var(--vpsBorderWidth) + 7px));
}

.opsParking.settingsOpen .T_0202_Settings {
  opacity: 1;
  width: var(--opsSettingsWidth);
  transition: var(--AG_0201_FadeIn), var(--AG_0303_ScaleResize);
}

.opsParking.vpsWide .T_0202_Settings,
.opsParking.settingsOpen .T_0202_Settings {
  pointer-events: all;
}

.opsParking.vpsWide .ipaPopupContainer,
.opsParking.ipaOpen.vpsWide .ipaPopupContainer,
.opsParking.vpsWide .T_0202_Settings,
.opsParking.settingsOpen.vpsWide .T_0202_Settings {
  left: calc(var(--minTouchSize) * 2 + var(--buttonBorderDist) - 2px);
}

.parking_OPS_b10Screen .opsParking.vpsWide .T_0202_Settings {
  left: calc(var(--buttonBorderDist) - 1px);
}

.T_0202_Settings .settings {
  border: 1px solid rgba(var(--audiWhite), 0.3);
  box-sizing: border-box;
}

.opsParking .opsContent .T_0202_Settings .settings .T_0103_ListVertical {
  width: calc(100% + 1px); /* in order to set the scrollbar on the border */
}

.opsParking .opsContent .T_0202_Settings .settings .indent {
  width: 93%;
}

/*-------------------------------- settings overlay --------------------------------*/
.opsParking.ipaOpen.vpsOn .settingsOverlay,
.opsParking.settingsOpen.vpsOn .settingsOverlay,
.settingsOverlay {
  position: absolute;
  height: 100vh;
  width: calc(100vw - var(--opsViewWidth));
  top: calc(-1 * var(--opsTabbarHeight));
  left: var(--opsViewWidth);
  opacity: 1;
  background-color: rgba(var(--audiBlack), 0.7);
}

.parking_OPS_b10Screen .opsParking.settingsOpen.vpsOn .settingsOverlay,
.parking_OPS_b10Screen .settingsOverlay {
  width: calc(100vw - var(--opsWidth) - var(--directAccessWidth));
  left: var(--opsWidth);
}
.parking_OPS_b10Screen .opsParking.settingsOpen.vpsWide .settingsOverlay,
.parking_OPS_b10Screen .opsParking.ipaOpen.vpsOn .settingsOverlay {
  width: calc(100vw - var(--directAccessWidth));
  left: 0;
}

.opsParking.vpsOn .settingsOverlay {
  width: 0;
}

.opsParking.vpsWide .settingsOverlay {
  width: 0;
  left: var(--minTouchSize);
}

.opsParking.ipaOpen.vpsWide .settingsOverlay,
.opsParking.settingsOpen.vpsWide .settingsOverlay {
  width: calc(100vw - var(--minTouchSize) - var(--directAccessWidth) - 1px);
  left: calc(var(--minTouchSize) * 2);
}

.opsParking.vpsWide .settingsOverlay,
.opsParking.ipaOpen.vpsWide .settingsOverlay,
.opsParking.settingsOpen.vpsWide .settingsOverlay,
.opsParking.vpsOn .settingsOverlay,
.opsParking.ipaOpen.vpsOn .settingsOverlay,
.opsParking.settingsOpen.vpsOn .settingsOverlay {
  top: unset;
  height: 100%;
}

/*----------------- tabBar --------------------*/

.UE_0402_Tabbarmenu {
  position: relative;
  height: 120px;
  z-index: 1;
}

.parking_OPS_b10Screen .UE_0402_Tabbarmenu {
  width: calc(var(--directAccessWidth) + var(--opsWidth) + 1px);
  left: calc((var(--directAccessWidth) + 1px) * -1);
  background-color: rgb(var(--audiBlack));
  background-size: 100vw 100vh;
  background-position: 0 0;
  clip-path: polygon(
    0% 0%,
    0% 100%,
    0 100%,
    0 var(--opsTabbarHeight),
    100% var(--opsTabbarHeight),
    100% 0%
  );
}

.tabbar {
  width: 100%;
  padding: unset;
  position: absolute;
  display: flex;
  flex-direction: row;
  height: calc(100% - 1px);
  box-sizing: border-box;
}
.tabbar .background {
  position: absolute;
  background-size: 100vw 100vh;
  background-position: 0 0;
  -webkit-clip-path: polygon(
    0% 0%,
    0 calc(100% - 8px),
    100% calc(100% - 8px),
    100% 100%,
    100% 0%
  );
  width: 100%;
  height: 100%;
  left: 0px;
}

.separator {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--tabbarSeparatorColor);
  bottom: 8px;
  z-index: 1;
}

.opsHeadline {
  height: var(--opsTabbarButtonHeight);
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.opsHeadline .button {
  height: var(--opsTabbarButtonHeight);
}
.opsHeadline .textPrio2 {
  margin-left: var(--iA1);
  margin-right: var(--iA1);
}
.opsHeadline .listIcon {
  margin-left: var(--iA2);
}

.opsHeadlineSubContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: var(--opsWidth);
}

.opsHeadlineSubContainer.rbfIcon {
  justify-content: flex-start;
}

.opsHeadlineSubContainer .listIcon {
  margin-left: 45px;
}

.opsHeadlineSubContainer.rbfIcon .listIcon {
  margin-left: 5px;
}

.opsHeadlineSubContainer.rbfIcon .listIcon:first-of-type {
  margin-left: 0;
}
/*-------------------- OPS Parking Content ---------------------------*/
.opsContent {
  position: relative;
  height: calc(100% - var(--opsTabbarHeight));
  width: 100%;
  top: -8px;
  display: flex;
}

.opsParking.vpsWide .opsContent {
  width: var(--minTouchSize);
}

.buttonsBackgrtound {
  position: absolute;
  width: calc(var(--minTouchSize) * 2);
  height: 100%;
  background-color: rgb(var(--audiBlack));
}

.opsButton {
  background-color: var(--audiBlack);
  top: var(--buttonBorderDist);
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
}

.parking_OPS_b10Screen .opsButton {
  width: var(--directAccessWidth);
  left: calc(var(--directAccessWidth) * -1);
}

.opsButton_container {
  position: relative;
  width: calc(var(--minTouchSize) * 2);
  display: flex;
  flex-wrap: wrap;
}

.parking_OPS_b10Screen .opsButton_containerBackground {
  border: 1px solid var(--tabbarSeparatorColor);
  border-radius: var(--standardBorderRadius);
}

.opsButton_containerBackground {
  width: calc(100% - 2px);
  height: calc(100% - 6px);
  top: 3px;
  position: absolute;
}

.opsButton_bottomButtons {
  margin-top: var(--iA2);
  display: flex;
  flex-wrap: wrap;
}

.boundingBoxContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.opsContent:not(.ipaPopupContainer) > .button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: var(--minTouchSize);
}

.opsView .boundingBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--touchIconSize);
  height: var(--touchIconSize);
  background-color: rgb(var(--boundingBoxGreen));
}

.opsView {
  position: absolute;
  left: calc(var(--minTouchSize) * 2);
  height: 100%;
  width: var(--opsWidth);
  background-color: rgba(var(--interactionBackground), 0.07);
}

.opsView .UE_0101_ActionButton {
  background-color: rgba(var(--audiBlack), var(--opsBackgroundOpacity));
  border-radius: var(--standardBorderRadius);
}

.contentArea {
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.cameraBackground {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../img/OPS-background.webp);
  background-position: center;
}

.carPlaceholder {
  position: absolute;
  width: var(--opsCarWidth);
  height: var(--opsCarHeight);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.trailer {
  position: absolute;
  width: var(--opsTrailerWidth);
  height: var(--opsTrailerHeight);
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(../img/SE_0015_OPS_Trailer.png);
  background-position: center;
  bottom: 0px;
}

.carPlaceholderSmall,
.ghostCar {
  position: absolute;
  width: var(--opsCarWidthSmall);
  height: var(--opsCarHeightSmall);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.rearWheelHub {
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: rgb(var(--fpkRed));
  top: 50%;
}

.opsParking.parkOut .carPlaceholderSmall {
  top: var(--opsParkManeuverOut);
}

.carPlaceholderSmall {
  top: var(--opsParkManeuverShift);
}

.ghostCar {
  opacity: 0.3;
}

.parkingLot {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: flex-end;
  right: var(--buttonBorderDist);
}

.parkingLot.diagonally {
  right: 0;
}

.parkingLot.diagonally .ghostCar {
  transform: rotate(90deg);
}

.parkingLot .button {
  height: var(--opsCarHeightSmall);
  width: var(--minTouchSize);
}
.parkingLot.diagonally .button {
  height: var(--minTouchSize);
  width: var(--opsCarHeightSmall);
}

.carArea {
  display: flex;
  position: relative;
  height: var(--opsCarHeight);
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.parkOut {
}

.opsParking.parkOut .carArea {
  position: absolute;
  top: calc(
    (var(--opsCarHeightSmall) / 2 + var(--opsParkManeuverOut)) -
      (var(--opsCarHeight) + 1px) / 2
  );
}

.contentArea .carArea .distance {
  position: absolute;
  left: 50%;
  top: 52%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  height: calc(var(--listIconSize) + var(--lineHeightPrio2));
  width: var(--minTouchSize);
}
.contentArea .carArea .distance.full {
  background-color: rgba(var(--audiBlack), var(--opsBackgroundOpacity));
  border-radius: var(--standardBorderRadius);
}
.contentArea .carArea .distance.noCamera {
  background-color: var(--buttonBackgroundColor) !important;
}

.contentArea .carArea .distance .backgroundbox {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background-color: rgba(var(--audiBlack), var(--opsBackgroundOpacity));
  border-radius: var(--standardBorderRadius);
}
.contentArea .carArea .distance.full .backgroundbox {
  background-color: unset;
  border-radius: unset;
}

.carArea.parkManeuvre {
  position: absolute;
  top: calc(
    (var(--opsCarHeightSmall) / 2 + var(--opsParkManeuverShift)) -
      (var(--opsCarHeight) + 1px) / 2
  );
}

.carArea .left {
  position: absolute;
  left: var(--buttonBorderDist);
}

.carArea .right {
  position: absolute;
  right: var(--buttonBorderDist);
}

.reverse {
  display: flex;
  position: absolute !important;
  left: 50%;
  top: 98%;
  transform: translate(-50%, -50%);
}

.carArea .boundingBoxContainer.mirror.left {
  left: var(--opsMirrorDist);
}
.carArea .boundingBoxContainer.mirror.right {
  right: var(--opsMirrorDist);
}

.carArea .forwardBoundingBox,
.carArea .backwardBoundingBox {
  position: absolute;
  height: 50%;
  width: 53px;
  background-color: rgba(var(--boundingBoxGreen));
  left: 50%;
  transform: translateX(-50%);
}

.carArea .forwardBoundingBox {
  top: 0;
}

.carArea .backwardBoundingBox {
  bottom: 0;
}

/*---------------- button lines ------------------*/
.forbidden {
  position: absolute;
  display: flex;
  height: 105px; /*105px = 110cm*/
  width: var(--opsCarWidth);
  bottom: calc(86px + var(--buttonBorderDist));
  background-color: rgb(var(--FIDtelltaleRed), 0.4);
  left: 50%;
  transform: translateX(-50%);
}

.opsViewButtonLine {
  position: absolute;
  height: var(--minTouchSize);
  width: calc(100% - (var(--buttonBorderDist) * 2));
  display: flex;
  flex-direction: row;
  margin-left: var(--buttonBorderDist);
  margin-right: var(--buttonBorderDist);
  justify-content: space-between;
  align-items: flex-end;
}

.opsViewButtonLine.topCenter {
  top: var(--buttonBorderDist);
  justify-content: center;
  align-items: flex-start;
}
.opsViewButtonLine.topCenterDist {
  bottom: var(--buttonBorderDist);
  justify-content: center;
  align-items: flex-start;
}

.opsViewButtonLine.topSecondRow {
  top: calc(calc(var(--touchIconSize) + (2 * var(--buttonBorderDist)) - 3px));
  justify-content: center;
  align-items: flex-start;
}

.opsViewButtonLine.center {
  justify-content: center;
}

.opsViewButtonLine.top {
  top: var(--buttonBorderDist);
}

.opsViewButtonLine.left {
  justify-content: flex-start;
}

.opsViewButtonLine.right {
  justify-content: flex-end;
}

.boundingboxForInfoButton {
  position: absolute;
  height: calc(var(--minTouchSize) + 25px);
  width: calc(var(--minTouchSize) + 25px);
  bottom: 0px;
  right: 0px;
  background-color: rgba(var(--boundingBoxGreen));
}

.opsViewButtonLine.bottom {
  bottom: var(--buttonBorderDist);
}
.opsViewButtonLine.bottomSecondRow {
  bottom: calc(var(--touchIconSize) + (2 * var(--buttonBorderDist)) - 3px);
}

.opsView .opsViewButtonLine .distance {
  position: relative;
  height: var(--lineHeightPrio1);
  min-width: 131px;
  max-width: calc(100% - 2 * var(--minTouchSize) - 2 * var(--buttonBorderDist));
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  align-self: flex-start;
}
.opsView .opsViewButtonLine.opsViewButtonLine.topCenterDist .distance {
  align-self: flex-end;
}

.distance .background {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  background-color: rgba(var(--audiBlack), var(--opsBackgroundOpacity));
  border-radius: var(--standardBorderRadius);
}
.distance .background.noCamera {
  background-color: var(--buttonBackgroundColor) !important;
}

.distance .valueCol {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin-right: var(--zA1);
  overflow: hidden;
  height: var(--lineHeightPrio2);
  width: 68px;
}

.distance .unitCol {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  overflow: hidden;
  width: 45px;
  height: var(--lineHeightPrio2);
}

.opsViewButtonLine p {
  display: block;
  position: relative;
  align-self: center;
  width: 100%;
}

.distance .textPrio2 {
  text-align: right;
}

.distance .textPrio1 {
  font-family: var(--fontNormal);
  color: rgb(var(--fpkRed));
  text-align: center;
}

.unitCol .textPrio2 {
  color: rgba(var(--audiWhite), 0.7);
  text-align: left;
}

.opsViewButtonLine .button.left {
  margin-right: var(--buttonBorderDist);
}

.opsViewButtonLine.top.full .text {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.opsViewButtonLine.top.full .text p {
  margin: 0 var(--textButtonMargin);
}

.opsViewButtonLine.top.full.text .button.full,
.opsViewButtonLine.top.full.buttons .button {
  flex: 1;
}

/* Use the KASS-Pfeile svg in 02_PSD*/
/* The "PlaceholderArrowxxx" is just a bounding box */
.arrowFrame {
  width: calc(var(--opsCarWidth) + 2 * var(--minTouchSize));
  height: calc(var(--opsCarHeight) + 100px);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.arrowFrame .arrowContainer {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
}

.placeholderArrowFlex {
  width: 110px;
  height: 94px;
  background-color: rgb(var(--sideAssistRed));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
}

.placeholderArrowFlex.left {
  -webkit-mask-image: url(../img/SE_0015_Parking_OPS_small_left.png);
}

.placeholderArrowFlex.right {
  -webkit-mask-image: url(../img/SE_0015_Parking_OPS_small_right.png);
}

/*---------------------- Car Touchpoints --------------------*/
.carTouchpoints {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
}

.carTouchpoints .button {
  position: relative;
  display: flex;
  width: 100%;
  height: calc(100% / 4 - var(--buttonBorderDist));
  margin-bottom: var(--buttonBorderDist);
}

.carTouchpoints .button.front.center .SE_0106_Button_with_Connector {
  top: 131px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.carTouchpoints .button.frontWheels .SE_0106_Button_with_Connector {
  top: 57px;
}

.carTouchpoints .leftSide .SE_0106_Button_with_Connector {
  left: 82px;
}

.carTouchpoints .rightSide .SE_0106_Button_with_Connector {
  left: calc(100% - 118px);
}

.carTouchpoints .button.roofcenter {
  position: absolute;
  width: 170px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -30%);
  margin-bottom: 0;
}
.carTouchpoints .button.roofcenter .SE_0106_Button_with_Connector {
  top: 94px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.carTouchpoints .button.rearWheels .SE_0106_Button_with_Connector {
  top: 102px;
}

.carTouchpoints .button.rear.center .SE_0106_Button_with_Connector {
  top: 80px;
  left: 50%;
  transform: translate(-50%, -50%);
}

/*-------------------------------- IPA Popup --------------------------------*/

.SE_0015_Parking_OPS .ipaPopupContainer {
  position: absolute;
  width: 0;
  height: calc(100% - 2 * var(--buttonBorderDist) + 2px);
  top: calc(var(--buttonBorderDist) - 1px);
  left: calc((var(--opsViewWidth) + var(--vpsBorderWidth) + 7px));
  pointer-events: none;
  transition:
    var(--AG_0201_FadeIn),
    width var(--animationDuration) cubic-bezier(0, 0, 0.53, 1);
  opacity: 0;
}

.SE_0015_Parking_OPS.parking_OPS_b10Screen .ipaPopupContainer {
  left: calc((var(--opsWidth) + var(--vpsBorderWidth)) + 7px);
}

.SE_0015_Parking_OPS .opsParking.ipaOpen .ipaPopupContainer {
  opacity: 1;
  width: var(--opsSettingsWidth);

  pointer-events: all;
}

.SE_0015_Parking_OPS.parking_OPS_b10Screen
  .opsParking.ipaOpen
  .ipaPopupContainer {
  width: var(--opsSettingsWidth);
}

.SE_0015_Parking_OPS.parking_OPS_b10Screen
  .opsParking.vpsWide
  .ipaPopupContainer,
.SE_0015_Parking_OPS.parking_OPS_b10Screen
  .opsParking.ipaOpen.vpsWide
  .ipaPopupContainer,
.parking_OPS_b10Screen .opsParking.settingsOpen.vpsWide .T_0202_Settings {
  left: calc(var(--buttonBorderDist) - 1px);
}

.SE_0015_Parking_OPS .opsParking .SE_0086_ParkingSpecialPopup {
  width: 100%;
  height: 100%;
}

.SE_0015_Parking_OPS
  .opsParking
  .opsContent
  .SE_0086_ParkingSpecialPopup
  .UE_0405_MoreMenu
  .moreMenuTextButton
  .button {
  width: unset;
}

.separatorLine {
  width: var(--minTouchSize);
  height: 1px;
  background-color: rgba(var(--audiWhite), 0.2);
  position: absolute;
  top: calc(var(--minTouchSize) + var(--iA1));
  left: 50%;
  transform: translateX(-50%);
}

.separatorLine.bottom {
  top: calc(var(--minTouchSize) * 2 + var(--iA1));
}

/*-------------------------------- settingsConfig --------------------------------*/
.settingsConfig {
  width: 100%;
}

.settingsConfig .textSettingLabel {
  position: relative;
  top: 10px;
  margin-bottom: 10px;
}

.settingsConfig select {
  border: 0;
  background-color: white;
  height: 30px;
  width: 170px;
  border-radius: 4px;
  margin-right: 20px;
}

/*-------------------------------- B10 screen --------------------------------*/
@media only screen and (min-width: 2219px) {
  :root {
    --opsSettingsWidth: calc(
      100vw - var(--opsWidth) - var(--vpsBorderWidth) - var(--directAccessWidth) -
        var(--buttonBorderDist) - 6px
    );
  }
}

/*-------------------------------- small screen --------------------------------*/
@media only screen and (max-width: 1768px) {
  :root {
    --opsWidth: 480px;
    --opsCarHeight: 384px;
    --opsCarWidth: 181px;
    --opsTrailerHeight: 215px;
    --opsTrailerWidth: 172px;
    --opsSecondRowOffset: 37px;
  }

  :global(#root) > .SE_0015_Parking_OPS {
    width: 1768px;
    height: 828px;
  }

  .boundingBoxContainer.mirror {
    top: 115px;
  }

  .carTouchpoints .button.front.center .SE_0106_Button_with_Connector {
    top: 110px;
  }

  .carTouchpoints .button.frontWheels .SE_0106_Button_with_Connector {
    top: 51px;
  }

  .carTouchpoints .leftSide .SE_0106_Button_with_Connector {
    left: 75px;
  }

  .carTouchpoints .rightSide .SE_0106_Button_with_Connector {
    left: calc(100% - 112px);
  }

  .carTouchpoints .button.rearWheels .SE_0106_Button_with_Connector {
    top: 89px;
  }

  .carTouchpoints .button.roofcenter {
    width: 160px;
  }
  .carTouchpoints .button.roofcenter .SE_0106_Button_with_Connector {
    top: 80px;
  }

  .carTouchpoints .button.rear.center .SE_0106_Button_with_Connector {
    top: 71px;
  }
}
