/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --slidingSwitchWidth: 264px;
  --slidingSwitchMargin: calc(
    (
        var(--minTouchSize) - var(--switchTrackWidth) - 2 *
          var(--switchTrackBorderWidth)
      ) / 2
  );
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_1104_SlidingSwitch {
  background-color: rgb(var(--bodyColorGrey));
  height: 150px;
  width: 400px;
}

/*-------------------- UE_1104_SlidingSwitch --------------------*/

.UE_1104_SlidingSwitch {
}

.switch {
  position: absolute;
  height: var(--minTouchSize);
  padding: 0 var(--slidingSwitchMargin);
  display: flex;
  justify-content: center;
  align-items: center;
}

.switchCheckbox {
  display: none;
}

.switchTrack {
  width: var(--slidingSwitchWidth);
  height: 28px;
  display: flex;
  align-items: center;
  overflow: hidden;
  cursor: pointer;
  border: var(--switchTrackBorderWidth) solid rgb(var(--audiWhite));
  border-radius: calc(var(--minTouchSize) * 2);
}

.light .switchTrack {
  border: 2px solid rgb(var(--audiBlack));
}

/* switch off */
.switchThumb {
  width: 16px;
  height: 16px;
  position: relative;
  left: 4px;
  border-radius: 50%;
  opacity: var(--disabledState);
  background-color: rgb(var(--audiWhite));
  transition: var(--AG_0303_ScaleResize),
    left var(--animationDuration) var(--animationCurve),
    opacity var(--animationDuration) var(--animationCurve);
}

.description {
  position: absolute;
  width: var(--slidingSwitchWidth);
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  padding: 0 var(--iA2);
}

.light .switchThumb {
  background-color: rgb(var(--audiBlack));
}

/* switch on */
.switchCheckbox:checked + .switchTrack .switchThumb {
  width: 20px;
  height: 20px;
  left: 240px;
  opacity: 1;
}

.settings {
  width: 100%;
  margin-top: 100px;
  position: absolute;
}
