:root {
  --stepperTopDistDashboard: 3px;
  --pageIndicatorWidth: 109px;
}

:global(#root) > .SE_0088_PlayEmbedded {
  background: rgb(var(--bodyColorGrey));
  position: absolute;
  height: calc(816px - (var(--minTouchSize) * 2));
  width: 1800px;
  margin: 0;
  overflow: hidden;
}

.playEmbeddedBackground {
  position: absolute;
  background-color: rgb(var(--audiBlack));
  width: 100%;
  height: calc(100% + (var(--minTouchSize) * 2));
  top: calc(-1 * (var(--minTouchSize)));
  display: flex;
  justify-content: center;
}
.immersiveMode .playEmbeddedBackground {
  width: 100%;
  height: 100%;
  top: 0px;
}

.playEmbeddedConetent {
  position: relative;
  width: 1800px;
  height: 816px;
  top: 0px;
  background-image: url('../img/01_NaturalTheater/Calming/se_0088_wellbeing_calming_1_content.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.backgroundDarken {
  position: absolute;
  width: 100%;
  height: calc(100% + (var(--minTouchSize) * 2));
  top: calc(-1 * (var(--minTouchSize)));
  background-color: rgba(var(--audiBlack), 0.5);
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
}
.immersiveMode .backgroundDarken {
  opacity: 0;
  height: 100%;
  top: 0;
  transition: var(--AG_0202_FadeOut);
}

.playEmbedded {
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
}
.immersiveMode .playEmbedded {
  top: var(--minTouchSize);
  height: calc(100% - var(--minTouchSize) * 2);
  opacity: 0;
  transition: var(--AG_0202_FadeOut);
}

.playEmbedded .UE_1303_Stepper_Decorator {
  height: 100%;
  position: relative;
}

.UE_0703_TimeBar {
  width: calc(100% - var(--minTouchSize) * 4);
  position: absolute;
  bottom: 1px;
  left: calc(var(--minTouchSize) * 2);
}

.settings {
  position: absolute;
  top: 600px;
}
