:global(#root) > .UE_6081_RS_PushToPass {
  position: relative;
  padding: 16px;
  height: 68px;
}

.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS {
}

.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS.UE_6081_00,
.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS.UE_6081_01,
.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS.UE_6081_02,
.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS.UE_6081_03,
.UE_6045_Powermeter_Bargraph_Dynamic_PHEV_RS.UE_6081_04 {
}

.Bargraph {
  position: relative;
  width: 404px;
  display: flex;
}

/* Text Labels */
.textLabels {
  position: relative;
  height: 26px;
}
.textLabel {
  position: absolute;
}
.lower {
  margin-top: 13px;
}
.max {
  top: 4px;
  left: 0px;
  width: 53px;
  height: 14px;
  background-image: url(../img/UE_6081_-MAX.png);
}
.status {
  left: 126px;
  top: 1px;
  width: 39px;
  height: 18px;
  background-image: url(../img/UE_6081_-0Percent.png);
}
.maxPower {
  top: 4px;
  left: 301px;
  background-image: url(../img/UE_6081_-MAX.png);
  width: 53px;
  height: 14px;
}
.charge {
  top: 0px;
  left: 21px;
  width: 96px;
  height: 14px;
  background-image: url(../img/UE_6081_-CHARGE.png);
}
.power {
  top: 0px;
  left: 199px;
  margin-top: 0;
  width: 88px;
  height: 14px;
  background-image: url(../img/UE_6081_-Power.png);
}

/* Boost label */
.boost {
  position: absolute;
  top: 2px;
  left: 357px;
  width: 89px;
  height: 20px;
  background-repeat: no-repeat;
}

.boost.red {
  background-image: url(../img/UE_6081_-Boost_red.png);
}

.boost.gray {
  background-image: url(../img/UE_6081_-Boost_white.png);
  opacity: 0.6;
}

.boost.white {
  background-image: url(../img/UE_6081_-Boost_white.png);
}

/* Inactive State (state_0) */
.UE_6081_00 .textLabel {
  opacity: var(--disabledState);
}

/* Boost available (state_4) */
.UE_6081_02 .segment.availableBoostSegment,
.UE_6081_03 .segment.availableBoostSegment {
  border-top-color: rgba(var(--audiWhite), 0.6);
  background-color: rgba(var(--audiWhite), 0.1);
}

