.popup {
  outline: var(--popupOutlineWidth) solid rgba(var(--audiBlack), 0.8);
  background-color: rgb(var(--audiBlack));
  border: 1px solid rgb(var(--borderGreyDark));
  box-shadow: inset 0 0 0 var(--popupInsetWidth) rgba(var(--audiWhite), 0.07);
  background-size: 100%;
  background-repeat: no-repeat;
  background-position: center;
  z-index: 1;
  display: flex;
}

.valueContainer,
.valuePopup {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: auto;
  height: 72px;
  padding-left: 10px;
  padding-right: 10px;
  align-items: center;
  text-align: center;
  background-color: rgb(var(--audiBlack));
  background-repeat: no-repeat;
  background-position: center;
  background-size: calc(100% - var(--popupInsetWidth) * 2)
    calc(100% - var(--popupInsetWidth) * 2);
  border: 1px solid rgb(var(--borderGreyDark));
  z-index: 1;
}

.value {
  width: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: calc(100% - var(--sliderValueInsetWidth) * 2)
    calc(100% - var(--sliderValueInsetWidth) * 2);
  z-index: 1;
}
