/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --callStackTileWidth: 537px;
  --callStackTileHeight: 192px;
  --callStackAnimatedTrackSizeStart: 34px;
  --callStackAnimatedTrackSizeEnd: 44px;
}

.UE_1203_CustomizableTile {
  position: absolute;
  top: 0;
  margin: 0;
  width: 585px;
  height: var(--callStackTileHeight);
  box-sizing: border-box;

  .textContainer {
    max-width: 450px;
  }

  .rightIcon {
    max-width: 330px;
  }

  .buttonDimension {
    height: 98%;
  }

  .story_container {
    width: 1800px;
    height: 192px;
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 0 10px;
    overflow: hidden;
    box-sizing: border-box;
  }

  .iconContainer {
    display: flex;
    width: var(--minTouchSize);
    height: 100%;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
  }

  .customizableTile {
    position: relative;
    display: flex;
    width: 100%;
    height: 0;
    align-items: center;
    overflow: hidden;

    .background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(var(--interactionBackground), 0.15);
      border-radius: var(--standardBorderRadius);
    }

    .lineHighlite {
      position: absolute;
      width: calc(100% - 4px);
      height: 1px;
      background-color: rgba(var(--ambientcolor1), 0.8);
      bottom: 5px;
      left: 2px;
    }

    .bluredHighlite {
      position: absolute;
      width: calc(100%);
      height: 18px;
      background-color: rgba(var(--ambientcolor1), 0.1);
      bottom: 0px;
      left: 0px;
      filter: blur(20px);
    }

    .contactDetails {
      display: grid;
      flex-direction: column;
      height: 100%;
      margin-left: var(--iA1);
      margin-right: 0;
      overflow: hidden;
      align-items: center;

      .textPrio1,
      .textPrio2 {
        max-width: 340px !important;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      p:nth-child(2) {
        opacity: var(--secondaryTextOpacity);
      }
    }

    .listIcon {
      position: absolute;
      top: 0;
      right: 0;
    }

    .animatedIconContainer {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: var(--callStackAnimatedTrackSizeStart);
      background: rgba(var(--tabbarBackground), 0.1);
      opacity: 0;
    }

    .track {
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 100%;
      background-color: rgba(var(--audiWhite), 0.4);
    }

    .coverContainer {
      display: flex;
      height: 100%;
      align-items: center;

      .cover {
        width: var(--coverSizeList);
        height: var(--coverSizeList);
        margin-left: var(--lA1);
        background-image: url(../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg);

        .rounded {
          border-radius: 50%;
        }
      }
    }
  }

  .animated {
    opacity: 1;
    height: 100%;
  }
}

.UE_1203_CustomizableTile .customizableTile.animated {
  opacity: 1;
  height: 100%;
}

.UE_1203_CustomizableTile .customizableTile.animated .animatedIconContainer {
  opacity: 1;
}

.UE_1203_CustomizableTile .customizableTile.animated .track {
  opacity: 1;
}

.UE_1203_CustomizableTile .customizableTile.animated .cover,
.UE_1203_CustomizableTile .customizableTile.animated .contactDetails,
.UE_1203_CustomizableTile .customizableTile.animated .listIcon {
  opacity: 1;
}

.UE_1203_CustomizableTile .customizableTile.animated.noIcon .cover,
.UE_1203_CustomizableTile .customizableTile.animated.noIcon .contactDetails,
.UE_1203_CustomizableTile .customizableTile.animated.noIcon .listIcon {
  animation-delay: 0.6s;
}

/* ---- animations ---- */
@keyframes customizableTile_opacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes customizableTile_height {
  from {
    height: 0;
  }

  to {
    height: var(--callStackTileHeight);
  }
}

@keyframes customizableTile_brightness {
  0% {
    opacity: 0;
  }

  75% {
    opacity: 0.6;
  }

  80% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes customizableTile_animatedContainer {
  0% {
    left: 0;
    right: auto;
    width: 100%;
  }

  75% {
    left: 0;
    width: 100%;
  }

  75.001% {
    left: auto;
    right: 0;
    height: var(--callStackAnimatedTrackSizeStart);
  }

  100% {
    left: auto;
    right: 0;
    width: var(--callStackAnimatedTrackSizeEnd);
    height: var(--callStackAnimatedTrackSizeEnd);
  }
}

@keyframes customizableTile_progress {
  0% {
    left: 0;
    width: 0;
  }

  75% {
    left: 0;
    width: 100%;
  }

  75.001% {
    left: auto;
    right: 0;
  }

  100% {
    left: auto;
    right: 0;
    width: 100%;
  }
}

/* ---- settings ---- */
.settings {
  position: relative;
  width: 70%;
  top: 250px;
}

.settings button {
  font-size: 20px;
  margin-left: 10px;
}
