:global(#root) > .SE_0102_MDK_SmartcardScreen {
  margin: auto;
  width: 1678px;
  height: 553px;
}

.SE_0102_MDK_SmartcardScreen {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.smartcardScreen {
  display: flex;
  align-items: center;
  height: 100%;
}

.SmartcardScreenContainer {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 30px;
  box-sizing: border-box;
}

.SE_0102_MDK_SmartcardScreen .smartcardScreen .textLines {
  height: calc(100% - 150px);
  width: 100%;
}

.textContainer {
  margin-left: var(--zA4);
  margin-right: var(--zA4);
  width: 60%;
  height: 100%;
  position: relative;
}

.textLines p {
  white-space: normal;
}

.bottomPanel {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 33px;
  width: calc(100% - var(--zA4));
  margin-left: var(--zA4);
  justify-content: space-between;
}

.bottomPanel div {
  display: flex;
  align-items: center;
}

.bottomPanel div .listIcon {
  margin-right: 10px;
}

.bottomPanel .activated {
  margin-left: auto;
}

/* --------------------- Full decorator -------------------------------*/
.SE_0102_MDK_SmartcardScreen .smartcardScreen .fullDecorator {
  display: flex;
  position: relative;
  flex: 1;
  height: 100%;
  justify-content: center;
}

.SE_0102_MDK_SmartcardScreen .smartcardScreen .mdkBackground,
.SE_0102_MDK_SmartcardScreen .smartcardScreen .mdkDecorator {
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.SE_0102_MDK_SmartcardScreen .smartcardScreen .mdkBackground {
  display: flex;
  background: radial-gradient(
    circle at 100%,
    rgba(var(--audiBlack), 0.7) 20%,
    transparent 65%
  );
  right: -250px;
  background-position: right;
}

.SE_0102_MDK_SmartcardScreen.smartCard .smartcardScreen .mdkDecorator {
  background-image: url(../img/MDK_Phonebox_Smartcard.png);
}

.settings {
  position: absolute;
  top: 800px;
}
