/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0026_FAS_Profiles {
  margin: 1px;
  width: 1800px;
  height: 459px;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.fasProfiles {
  position: relative;
  width: 100%;
  height: 100%;
}

/* -------------------------------- decorator ------------------------------- */

.decorator {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.decoratorBackground,
.decoratorCar {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  pointer-events: none;
}

.decoratorBackground {
  background-image: url('../img/decorator_background_placeholder.webp');
}
.decoratorCar {
  background-image: url('../img/decorator_car_placeholder.webp');
}

.decoratorCarColor,
.decoratorSensor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  pointer-events: none;
}

.decoratorCarColor {
  -webkit-mask-image: url('../img/decorator_carcolor_placeholder.webp');
}
.decoratorSensor {
  -webkit-mask-image: url('../img/decorator_sensor__placeholder.webp');
}

/* -------------------------------- connector ------------------------------- */

.fasEditContainer {
  /* final position depending on the final decorator */
  position: absolute;
  height: fit-content;
  width: fit-content;
  display: flex;
  top: 170px;
  left: 95px;
}

.fasEditContainer .button {
  position: relative;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  display: flex;
  justify-content: center;
}

.SE_0106_Button_with_Connector {
  position: relative;
  margin-top: 134px;
  margin-left: 100px;
}

/*------------------ SMALL DISPLAY ------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0026_FAS_Profiles {
    width: 1648px;
    height: 331px;
  }

  .fasEditContainer {
    /* final position depending on the final decorator  */
    top: 62px;
    left: 180px;
  }
}
