/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --tiltAngle_groundHeight: 53%;
  --tiltAngle_instrumentDiameter: 679px;
  --tiltAngle_valueBoxWidth: 80px;
  --tiltAngle_valueBoxHeight: 50px;
  --tiltAngle_frontTiltScaleHeight: 362px;
  --tiltAngle_maxScaleValue: 30;
  --tiltAngle_maxScaleValueExtended: 40;
  --tiltAngle_zeroOpacityValue: 47;
  --tiltAngle_tiltInstrumentHeight: 362px;
  --tiltAngle_frontTiltScaleWidth: 15px;
  --tiltAngle_sideTiltScaleWidth: 13px;
  --tiltAngle_markerWidth: 34px;
  --tiltAngle_scaleMargin: 4px;
  --tiltAngle_tiltBargraphOpacity: 0.4;
  --tiltAngle_infoLineContainerWidth: 250px;
}

:global(#root) > .ST_0004_TiltAngleDisplay {
  position: absolute;
  width: 1800px;
  height: 697px;
  margin: 0;
}

:global(#root) > .ST_0004_TiltAngleDisplay > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay {
  height: 100%;
  width: 100%;
  position: absolute;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .UE_1303_Stepper_Decorator {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltContent {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  overflow: hidden;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .horizonDecorator {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  width: 1920px;
  left: -70px;
  top: calc(50% - 100vh / 2 - 75px);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .horizonDecorator.base {
  background-image: url(../img/ST_0004_TiltAngleDisplay_Background.png);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .backgroundCover {
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  height: 100%;
  width: 100%;
  left: 0px;
  top: 0px;
  background-image: linear-gradient(
    to right,
    rgba(var(--audiBlack), 1) 0%,
    rgba(var(--audiBlack), 0) 40%,
    rgba(var(--audiBlack), 0) 40%,
    rgba(var(--audiBlack), 1) 100%
  );
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .carDecoratorWrapper {
  position: absolute;
  height: var(--carDecoratorHeight);
  width: 100%;
  bottom: var(--tiltAngle_groundHeight);
  opacity: 0.75;
  left: 50%;
  transform: translate(-50%, 50%);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .carDecoratorWrapper .carDecorator {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  -webkit-mask-size: contain;
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .carDecoratorWrapper
  .carDecorator.color {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
}

/*---Instrument---*/
.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument {
  position: absolute;
  width: var(--tiltAngle_instrumentDiameter);
  left: 50%;
  transform: translate(-50%, 50%);
  bottom: var(--tiltAngle_groundHeight);
  height: calc(var(--tiltAngle_tiltInstrumentHeight));
  overflow: hidden;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .background {
  background-color: rgb(var(--borderGrey));
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .sideTilt {
  position: absolute;
  width: 100%;
  clip-path: polygon(0px 20%, 50% 50%, 100% 20%, 100% 80%, 50% 50%, 0px 80%);
  height: var(--tiltAngle_instrumentDiameter);
  top: calc(
    -1 * (
        var(--tiltAngle_instrumentDiameter) -
          var(--tiltAngle_tiltInstrumentHeight)
      ) / 2
  );
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .sideTilt .scale {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-image: url(../img/ST_0004-InstrumentSideTilt_HIGH.png);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .sideTilt .filling {
  width: calc(100% - 2 * var(--tiltAngle_sideTiltScaleWidth));
  height: calc(100% - 2 * var(--tiltAngle_sideTiltScaleWidth));
  border-radius: 50%;
  border: solid var(--tiltAngle_sideTiltScaleWidth)
    rgba(var(--audiWhite), var(--tiltAngle_tiltBargraphOpacity));
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .rotatingElementsContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .sideTiltLine {
  position: absolute;
  width: 100%;
  height: 2px;
  top: calc(50% - 1px);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .valueBox {
  left: calc(
    50% + var(--tiltAngle_markerWidth) + 0.5 *
      var(--tiltAngle_frontTiltScaleWidth) + var(--tiltAngle_scaleMargin)
  );
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .valueBox {
  position: absolute;
  width: var(--tiltAngle_valueBoxWidth);
  height: var(--tiltAngle_valueBoxHeight);
  transform: translateY(-50%);
  top: calc(2px - 50%);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .valueBox
  .textPrio2 {
  margin: 8px;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .frontTilt {
  position: absolute;
  width: calc(
    var(--tiltAngle_frontTiltScaleWidth) + 2 * var(--tiltAngle_scaleMargin)
  );
  height: calc(var(--tiltAngle_frontTiltScaleHeight) + 6px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, calc(-50%));
  /*background-color: rgba(var(--audiBlack), 0.5);*/
  background-color: rgba(var(--audiBlack), 0.7);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .frontTilt .scale {
  width: var(--tiltAngle_frontTiltScaleWidth);
  height: var(--tiltAngle_frontTiltScaleHeight);
  margin: 3px var(--tiltAngle_scaleMargin);
  background-image: url(../img/ST_0004-InstrumentLengthTilt_HIGH.png);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .frontTilt
  .filling {
  position: absolute;
  width: calc(100% - 2 * var(--tiltAngle_scaleMargin));
  margin: 3px var(--tiltAngle_scaleMargin);
  background-color: rgba(
    var(--audiWhite),
    var(--tiltAngle_tiltBargraphOpacity)
  );
  top: 180px;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .carHorizonLine {
  position: absolute;
  width: calc(
    100% - 2 *
      (var(--tiltAngle_sideTiltScaleWidth) + var(--tiltAngle_scaleMargin))
  );
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 17px;
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .carHorizonLine
  .valueBox {
  left: var(--tiltAngle_markerWidth);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .landscapeHorizonLine {
  position: absolute;
  width: 254px;
  height: 2px;
  left: 50%;
  top: calc(50% - 1px);
  transform: translateX(-50%);
  background-color: rgb(var(--borderGrey));
}

/*---Infos---*/
.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoLine {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoLine.top {
  top: var(--iA1);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoLine.top .textPrio2.value {
  margin-top: 0px;
  margin-left: 6px;
  width: 71px;
  text-align: right;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoLine.bottom {
  bottom: 11px;
}
.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltContent.chinaVersion
  .infoLine.bottom {
  bottom: 28.5px;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  position: relative;
  align-items: center;
  justify-content: center;
  width: fit-content;
  overflow: hidden;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoContainer.row {
  flex-direction: row;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoContainer .textPrio2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoLine .infoContainer {
  margin-left: var(--cA1);
  width: var(--tiltAngle_infoLineContainerWidth);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .infoLine
  .infoContainer:first-child {
  margin-left: 0;
}

.ST_0004_TiltAngleDisplay.hiddenStepperDecorator .tiltAngleDisplay .infoColumn {
  right: 10px;
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoColumn {
  position: absolute;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  right: calc(var(--minTouchSize) + 10px);
  min-width: var(--minTouchSize);
}

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoColumn .infoContainer {
  margin-top: 24px;
  max-width: 72px;
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .infoColumn
  .infoContainer
  .textPrio2 {
  margin-left: 0;
}
/*-----markers----*/

.ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .marker {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 4px;
  background-color: rgb(var(--audiWhite));
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .carHorizonLine
  .marker {
  background-color: rgb(var(--ambientcolor1));
  width: var(--tiltAngle_markerWidth);
  /*box-shadow: 0 0 2px;*/
  height: 5px;
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .landscapeHorizonLine
  .marker {
  width: calc(var(--tiltAngle_markerWidth) * 2);
  left: 50%;
  transform: translate(-50%, -50%);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.left {
  left: 0;
  width: var(--tiltAngle_markerWidth);
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.center {
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(
    var(--tiltAngle_frontTiltScaleWidth) + 2 *
      (var(--tiltAngle_markerWidth) + var(--tiltAngle_scaleMargin))
  );
  background-image: linear-gradient(
    to right,
    rgb(var(--ambientcolor1))
      calc(
        50% - var(--tiltAngle_frontTiltScaleWidth) / 2 -
          var(--tiltAngle_scaleMargin)
      ),
    transparent
      calc(
        50% - var(--tiltAngle_frontTiltScaleWidth) / 2 -
          var(--tiltAngle_scaleMargin)
      ),
    transparent
      calc(
        50% + var(--tiltAngle_frontTiltScaleWidth) / 2 +
          var(--tiltAngle_scaleMargin)
      ),
    rgb(var(--ambientcolor1))
      calc(
        50% + var(--tiltAngle_frontTiltScaleWidth) / 2 +
          var(--tiltAngle_scaleMargin)
      )
  );
  background-color: transparent;
  /*box-shadow: 0 0 2px;*/
  height: 5px;
}

.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.right {
  right: 0;
  width: var(--tiltAngle_markerWidth);
}

.ST_0004_TiltAngleDisplay.hiddenStepperDecorator
  .tiltAngleDisplay
  .tiltContent
  .UE_0202_FloatingContainer.liftBargraphFloatingContainer
  .floatingContainer {
  left: 6px;
}
.ST_0004_TiltAngleDisplay
  .tiltAngleDisplay
  .tiltContent
  .UE_0202_FloatingContainer.liftBargraphFloatingContainer
  .floatingContainer {
  top: calc(50% + 2px);
  transform: translateY(-50%);
  left: calc(var(--minTouchSize) + 6px);
  z-index: 1;
}
/*---Settings---*/
.ST_0004_TiltAngleDisplay .displaySettingsConfig {
  position: absolute;
  top: 570px;
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0004_TiltAngleDisplay {
    height: 425px;
    width: 1526px;
  }
  :root {
    --tiltAngle_instrumentDiameter: 513px;
    --tiltAngle_frontTiltScaleHeight: 272px;
    --tiltAngle_tiltInstrumentHeight: 272px;
  }

  .ST_0004_TiltAngleDisplay .tiltAngleDisplay .tiltInstrument .sideTilt .scale {
    background-image: url(../img/ST_0004-InstrumentSideTilt_LOW.png);
  }

  .ST_0004_TiltAngleDisplay
    .tiltAngleDisplay
    .tiltInstrument
    .frontTilt
    .scale {
    background-image: url(../img/ST_0004-InstrumentLengthTilt_LOW.png);
  }

  .ST_0004_TiltAngleDisplay .tiltAngleDisplay .infoColumn .infoContainer {
    margin-top: 18px;
  }

  .ST_0004_TiltAngleDisplay .tiltAngleDisplay .horizonDecorator {
    top: calc(50% - 100vh / 2 - 35px);
  }
}

.settings {
  position: absolute;
  top: 700px;
  width: 50%;
  z-index: 100;
}
