/* CLASSIFICATION: CONFIDENTIAL */

#root > .SE_0007_ScaleInfo {
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  border: 1px solid yellow;
}

.SE_0007_ScaleInfo {
  &__scaleInfo {
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;

    &__scaleLine {
      width: 100px;
      height: 2px;
      background-image: url('../img/scaleLine.png');
    }
  }

  &__settings {
    position: absolute;
    width: 1000px;
    top: 200px;
  }
}
