/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_5300_LoadingScreen {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthGB);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}

.T_5300_LoadingScreen {
}

.loadingScreen {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  flex-direction: column;
}

.UE_0701_Loader {
  position: relative;
  display: initial;
  width: calc(100% - 2 * var(--hA3) - 40px);
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 30px;
  height: auto;
}

.UE_0702_ProgressBar {
  position: relative;
  display: initial;
  margin-top: 10px;
  width: calc(100% - 2 * var(--hA3));
  height: auto;
}

.UE_0702_ProgressBar .topLine {
  margin-bottom: 0;
}

.T_0104_TextAndButtons {
  position: relative;
  width: 100%;
  height: fit-content;
}

.settings {
  position: absolute;
  margin-top: 450px;
  width: 1200px;
}
