/* CLASSIFICATION: CONFIDENTIAL */

/*------------------------------- standalone properties --------------------------------*/

:global(#root) > .UE_1003_ListItemButtons {
  position: relative;
  background-color: rgb(var(--bodyColorGrey));
  height: var(--minTouchSize);
}

/*------------------------------- LIST ELEMENTS --------------------------------*/

.UE_1003_ListItemButtons {
  display: block;
}

.listItemButtons {
  display: flex;
  position: relative;
  height: 100%;
  width: 100%;
  overflow: hidden;
  flex-wrap: nowrap;
}

.button {
  position: relative;
  display: inline-block;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}
