.ST_0136_DashcamImageView {
  width: 100%;
  height: 100%;
  padding: 25px;
  box-sizing: border-box;
  overflow-y: scroll;
  overflow-x: hidden;

  &__row {
    display: flex;
    margin: 30px 0 50px;

    &__imageContainer {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;

      &__image {
        width: 418px;
        height: 235px;
        background-color: rgba(var(--boundingBoxGreen));
        margin-bottom: 20px;

        &__iconContainer {
          float: right;
          margin: 5px;
        }
      }

      &__textIconContainer {
        display: flex;
        gap: var(--iA1);
      }
    }
  }
}
