/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --dropDownWidth: 310px;
}

:global(#root) > .UE_0407_Dropdown {
  margin: 50px 0 0 50px;
  max-width: 500px;
  max-height: 500px;
}

:global(#root) > .UE_0407_Dropdown.showContent {
  height: 500px;
}

.UE_0407_Dropdown {
  position: absolute;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
}

.UE_0407_Dropdown.radio {
  width: fit-content;
}

.UE_0407_Dropdown.cursor {
  width: 100%;
}

.UE_0407_Dropdown.showContent {
  height: 100%;
}

/* ------------------- CLOSED DROPDOWN ------------------- */

.closed,
.closed .button {
  position: absolute;
  top: 0;
  min-width: 100%;
  max-width: 500px;
  height: 100%;
}

.radio .closed {
  width: fit-content;
}

.cursor .closed {
  width: 100%;
}

.arrow {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid rgb(var(--audiWhite));
  margin-left: 45px;
}

.closedTextContainer {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 45px;
  box-sizing: border-box;
}

.hidden {
  visibility: hidden;
  pointer-events: none;
}

/* ------------------- OPEN DROPDOWN ------------------- */

.open {
  height: 100%;
  max-width: 100%;
}

.radio .open {
  width: fit-content;
}

.cursor .open {
  width: 100%;
}

.clickableOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 21;
}

.dropDown {
  display: block;
  max-width: 500px;
  min-width: var(--minTouchSize);
  max-height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
}

.radio .dropDown {
  width: fit-content;
}

.cursor .dropDown {
  width: 100%;
}

/* ------------------- SETTINGS ------------------- */

.settings {
  position: absolute;
  top: 600px;
}
