/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --imageSizeSmall: 170px;
}

:global(#root) > .SE_0024_Contact_Card {
  margin: 0px;
  width: 100%;
  height: 956px;
}

:global(#root) > .SE_0024_Contact_Card > .background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.SE_0024_Contact_Card .contactCard {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 100%;
}

.SE_0024_Contact_Card .contactCard .leftPanel {
  flex: 2;
  height: 100%;
  overflow: hidden;
}

.SE_0024_Contact_Card .contactCard .leftPanel .T_0103_ListVertical {
  height: 100%;
}

.SE_0024_Contact_Card .contactCard .rightPanel {
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.SE_0024_Contact_Card .rightPanel .contactDetails {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

/*--------------COVER--------------*/
.SE_0024_Contact_Card .rightPanel .contactDetails .cover {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg');
}

.SE_0024_Contact_Card .rightPanel .contactDetails .cover {
  background-size: cover;
  background-repeat: no-repeat;
  height: var(--imageSize);
  width: var(--imageSize);
}

.SE_0024_Contact_Card .rightPanel .contactDetails .cover {
  border-radius: 50%;
  /*width: calc(100% - 20px);*/
  /*height: calc(var(--imageSize) - 20px);*/
  margin: 10px;
}

/*------------TEXT-----------*/

.SE_0024_Contact_Card .rightPanel .contactDetails .text {
  margin-top: 20px;
  width: 100%;
}

.SE_0024_Contact_Card .rightPanel .contactDetails .text .textPrio1,
.SE_0024_Contact_Card .rightPanel .contactDetails .text .textPrio2 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
}

/*---------SMALL SCREEN-------*/
@media only screen and (max-width: 1768px) {
  .SE_0024_Contact_Card .rightPanel .contactDetails .cover {
    background-size: cover;
    background-repeat: no-repeat;
    height: var(--imageSizeSmall);
    width: var(--imageSizeSmall);
  }
}
