:global(#root) > .SE_0072_RouteCriteriaOverlay {
  height: var(--dashboardHeight);
  width: var(--dashboardWidth);
  background-color: black;
}

.routeCriteria {
  display: flex;
  align-items: center;
  height: fit-content;
  position: absolute;
  width: calc(100% - var(--minTouchSize) * 2 - var(--zA8) * 4);
  bottom: var(--zA8);
  left: calc(var(--minTouchSize) + var(--zA8) * 2);
  padding: 10px var(--cA1);
  background-color: rgba(var(--audiBlack), 0.85);
  box-sizing: border-box;
  overflow: hidden;
}

.routeCriteria .textPrio2 {
  flex: 1;
}

.iconContainer {
}

.listIcon + .listIcon {
  margin-left: 4px;
  padding-left: 5px;
}

/*---- Move to BurgerMenu ----*/
.T_0203_BurgerMenu .SE_0072_RouteCriteriaOverlay .routeCriteria {
  width: calc(50% - var(--minTouchSize) * 2 - var(--zA8) * 4);
  left: calc(50% + var(--minTouchSize) + var(--zA8) * 2);
}

/*---- Move to RouteOverview ----*/
.routeOverviewOn .SE_0072_RouteCriteriaOverlay .routeCriteria {
  width: calc(
    100% - var(--etaBoxWidth) - var(--minTouchSize) * 3 - var(--zA8) * 4
  );
  left: calc(var(--etaBoxWidth) + var(--minTouchSize) * 2 + var(--zA8) * 2);
}
