:root {
  --glowIntensity: 0.25;
  --colorPopupBackground: rgb(255, 255, 255);

  --sliderAbsoluteMargin: 20px;
  --sliderAbsoluteShowButtonsMargin: calc(
    var(--sliderAbsoluteMargin) - var(--uiStateMargin)
  );

  --sliderLabelIconSize: 22px;
  --sliderThumbWidth: 2px;
  --sliderThumbGapWidth: 4px;
  --sliderThumbHeight: 26px;
  --sliderTrackHeight: 6px;
  --sliderValueInsetWidth: 4px;
}

:global(#root) > .UE_0601_Slider {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
}

.UE_0601_Slider {
  position: relative;
  width: 100%;
  display: flex;
  box-sizing: border-box;
}

.slider {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
  /* padding: 20px; */
  box-sizing: border-box;
}

.controls {
  position: absolute;
  top: 0;
  left: 20px;
  right: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.slider .touchArea {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(var(--boundingBoxGreen));
}

.standardSlider .touchArea {
  left: 45px;
  width: calc(100% - 90px);
}

.canvas {
  position: relative;
  width: 100%;
  height: var(--sliderTrackHeight);
  clip-path: inset(0px 0px 0px 0px);
  opacity: 0.8;
}

.background {
  position: absolute;
  top: calc(var(--opticalTouchReduction) / 2);
  left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
  height: calc(100% - var(--opticalTouchReduction));
  border-radius: var(--standardBorderRadius);
  box-sizing: border-box;
}

/* Common */
.headline {
  margin-bottom: var(--hA1);
}

.subText {
  opacity: var(--secondaryTextOpacity);
  margin-bottom: var(--hA1);
}

.subText.twoLines {
  margin-bottom: 0;
}

.subText2 {
  opacity: var(--secondaryTextOpacity);
  margin-bottom: var(--hA1);
}

.textLabels {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0 45px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
}

.textLabels p {
  margin: 0;
  margin-top: 0 !important;
}

.textLabels.greyText p {
  color: rgb(var(--borderGrey));
}

/* Standard slider */
.standardSlider .slider {
  padding: 20px 45px;
}

.standardSlider .interactionBackground {
  left: calc(var(--minTouchSize) - 1px);
  width: calc(100% - var(--minTouchSize) * 2 + 2px);
  border-radius: 0;
  border-left-width: 0;
  border-right-width: 0;
}

.standardSlider .range {
  width: 100%;
  margin: 0;
  height: var(--sliderTrackHeight);
  position: relative;
  top: 27px;
  background-color: transparent;
  background-image: linear-gradient(
    to right,
    rgb(var(--audiWhite)),
    rgb(var(--audiWhite))
  );
  background-repeat: no-repeat;
  cursor: pointer;
  outline: none;
  user-select: none;
  -webkit-appearance: none;
}

.sliderDecoration {
  position: relative;
  height: 16px;
  width: 8px;
  top: 5px;
}

.sliderThumb {
  margin-left: 0px;
  position: relative;
  height: var(--sliderThumbHeight);
  width: var(--sliderThumbWidth);
  background-color: rgb(var(--audiWhite));
}

.standardSlider .range::-webkit-slider-thumb {
  height: 100%;
  opacity: 0;
}

.progressBackground {
  position: relative;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 2px;
  background-image: linear-gradient(rgb(var(--audiGrey)), rgb(var(--audiGrey)));
  background-repeat: no-repeat;
  background-position: right;
}

.standardSlider .progressBackground {
  top: 19px;
}

/* Relative Slider */
.relativeSlider {
  width: 100%;
  position: absolute;
}

.relativeSlider .sliderBox {
  position: absolute;
  margin-top: 135px;
  height: 42px;
  width: calc(100% - 20px);
  margin-left: 10px;
  background-color: rgba(var(--audiBlack));
  -webkit-clip-path: polygon(
    calc(100% - 7px) 0,
    100% 50%,
    calc(100% - 7px) 100%,
    calc(0% + 7px) 100%,
    0 50%,
    calc(0% + 7px) 0
  );
}

.relativeSlider .sliderBox .left {
  position: absolute;
  width: 11px;
  height: 42px;
  background-color: rgb(var(--audiWhite));
  -webkit-clip-path: polygon(
    100% 0,
    calc(100% - 4px) 0,
    0 50%,
    calc(100% - 4px) 100%,
    100% 100%,
    calc(100% - 7px) 50%
  );
}

.relativeSlider .sliderBox .right {
  position: absolute;
  width: 11px;
  height: 42px;
  margin-left: calc(100% - 11px);
  background-color: rgb(var(--audiWhite));
  -webkit-clip-path: polygon(
    0 0,
    calc(0% + 4px) 0,
    100% 50%,
    calc(0% + 4px) 100%,
    0% 100%,
    7px 50%
  );
}

.relativeSlider .sliderBox .center {
  width: calc(100% - 8px);
  height: 42px;
  position: absolute;
  -webkit-clip-path: polygon(
    calc(100% - 7px) 0,
    100% 50%,
    calc(100% - 7px) 100%,
    calc(0% + 7px) 100%,
    0 50%,
    calc(0% + 7px) 0
  );
  margin-left: 4px;
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .slidermask {
  width: calc(100% - 8px);
  height: 42px;
  position: absolute;
  -webkit-clip-path: polygon(
    calc(100% - 7px) 0,
    100% 50%,
    calc(100% - 7px) 100%,
    calc(0% + 7px) 100%,
    0 50%,
    calc(0% + 7px) 0
  );
  background-color: rgba(var(--audiBlack), 0.3);
  margin-left: 4px;
}

.UE_0601_Slider.gradient .relativeSlider .sliderBox .left {
  background-color: rgb(var(--climateBlue));
}

.UE_0601_Slider.gradient .relativeSlider .sliderBox .right {
  background-color: rgb(var(--audiRed));
}

.UE_0601_Slider.gradient .relativeSlider .sliderBox .center {
  background-image: linear-gradient(
    to right,
    rgb(var(--climateBlue)) -30%,
    rgb(var(--audiRed)) 130%
  );
}

.sliderBox .textLabels,
.iconLabels {
  display: flex;
  position: absolute;
  width: 100%;
  flex-direction: row;
}

.iconLabels {
  justify-content: space-between;
}

.standardSlider .iconLabels {
  width: auto;
  left: 45px;
  right: 45px;
}

.sliderBox .textLabels {
  bottom: 4px;
}

.sliderBox .iconLabels {
  bottom: 6px;
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .mask {
  position: absolute;
  width: 100%;
  height: 100%;
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .left .mask {
  background-color: rgba(var(--audiBlack), 0.4);
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .center .mask {
  width: 100%;
  height: 100%;
  background-color: rgba(var(--audiWhite), 0.9);
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .left {
  background-color: rgba(var(--audiWhite), 0.9);
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .right {
  background-color: rgba(var(--audiWhite), 0.7);
}

.UE_0601_Slider.uni .relativeSlider .sliderBox .center .triangle {
  width: 100%;
  height: 100%;
  background-color: rgba(var(--audiBlack), 0.4);
  -webkit-clip-path: polygon(0 0, 100% 0, 0 100%);
}

.relativeSlider .valuebox.centerflex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.relativeSlider .sliderGlow {
  position: absolute;
  width: 100%;
  height: calc(var(--climateBarHeight) - 13px);
  top: 94px;
}

.UE_0601_Slider.gradient .relativeSlider .sliderGlow {
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0),
    rgba(var(--audiBlack), var(--glowIntensity)) 50%,
    rgba(var(--audiBlack), 0)
  );
  background-image: linear-gradient(
    to right,
    rgba(var(--audiBlack), 0),
    rgb(var(--climateBlue)) calc(0% + 20px),
    rgb(var(--audiRed)) calc(100% - 20px),
    rgba(var(--audiBlack), 0)
  );
}

.UE_0601_Slider.uni .relativeSlider .sliderGlow {
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0),
    rgba(var(--audiBlack), var(--glowIntensity)) 50%,
    rgba(var(--audiBlack), 0)
  );
  background-image: linear-gradient(
      to right,
      rgba(var(--audiBlack), 0),
      rgba(var(--audiBlack), 0.4) calc(0% + 20px),
      rgba(var(--audiBlack), 0) calc(100% - 20px),
      rgba(var(--audiBlack), 0)
    ),
    linear-gradient(
      to right,
      rgba(var(--audiBlack), 0),
      rgba(var(--audiWhite), 0) calc(0% + 20px),
      rgba(var(--audiWhite), 0.9) calc(100% - 40px),
      rgba(var(--audiWhite), 0.9) calc(100% - 20px),
      rgba(var(--audiBlack), 0)
    ),
    linear-gradient(
      to right,
      rgba(var(--audiBlack), 0),
      rgba(var(--audiBlack), 0.4) calc(0% + 20px),
      rgba(var(--audiBlack), 0) calc(100% - 20px),
      rgba(var(--audiBlack), 0)
    );
}

.plusMinusSigns {
  display: flex;
  position: absolute;
  width: calc(100% - (var(--cA1) * 2));
  height: fit-content;
  align-items: center;
  justify-content: space-between;
  margin: 0 var(--cA1);
  top: 7px;
}

/* Absolute slider */

.absoluteSlider .absSliderThumbDecorator {
  position: absolute;
  width: var(--sliderThumbWidth);
  height: var(--sliderTrackHeight);
}

.absoluteSlider.brightness .absSliderThumbDecorator {
  height: 2px;
}

.absoluteSlider.brightness .absSliderThumbDecorator .absSliderThumb {
  transform: translateY(calc(-1 / 2 * (var(--sliderThumbHeight) - 2px)));
}

.absoluteSlider .absSliderThumb {
  position: absolute;
  width: 100%;
  height: var(--sliderThumbHeight);
  background-color: rgb(var(--audiWhite));
  transform: translateY(
    calc(-1 / 2 * (var(--sliderThumbHeight) - var(--sliderTrackHeight)))
  );
}

.absoluteSlider .absSliderGap {
  position: absolute;
  width: var(--sliderThumbGapWidth);
  height: 100%;
  background-color: rgb(var(--audiBlack));
}

.absoluteSlider .range {
  position: absolute;
  top: 0;
  left: 0px;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

.absoluteSlider .range:focus {
  outline: none;
}

.absoluteSlider .range::-webkit-slider-runnable-track {
  width: 100%;
  height: var(--sliderThumbHeight);
  background-color: transparent;
  border: none;
  box-shadow: none;
  -webkit-appearance: none;
}

.absoluteSlider .range::-webkit-slider-thumb {
  visibility: hidden;
}

/*----------------POPUP FRAME DESIGN--------------*/
.relativeSlider .sliderValueContainer,
.absoluteSlider .sliderValueContainer,
.dataCollection .sliderValueContainer {
  width: auto;
  height: 65px;
  top: 5px;
  position: absolute;
  z-index: 1;
  display: flex;
}

.dataCollection .sliderValueContainer {
  top: -75px;
}

.absoluteSlider .sliderValueContainer {
  top: -35px;
}
.absoluteSlider.color .sliderValueContainer {
  width: 65px;
  background-color: var(--colorPopupBackground);
}

.absoluteSlider .sliderValueContainer .sliderValueColor {
  position: relative;
  width: 100%;
  height: 100%;
}
.color .absoluteSlider .sliderValueContainer .sliderValueColor {
  display: block;
}

.relativeSlider .sliderValueContainer .temperature {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  align-items: baseline;
  position: relative;
  width: 100%;
  height: fit-content;
  margin: 15px;
}

.relativeSlider .sliderValueContainer .temperature .temperatureDecimal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  height: fit-content;
}

.relativeSlider .sliderValueContainer .temperature .temperatureDecimal .degree {
  margin-bottom: 7px;
  opacity: 0.7;
}

/* Brightness slider */

.iconLabels {
  display: flex;
  position: absolute;
  width: 100%;
  flex-direction: row;
}

.iconLabels {
  justify-content: space-between;
}

.sliderValueContainer .brightnessValue {
  position: relative;
  margin: 0 6px;
  margin-top: 13px;
}

.brightnessTrackGradient,
.brightnessTrackScale {
  position: relative;
  width: 100%;
  height: 100%;
}
.brightnessTrackGradient {
  background-image: linear-gradient(
    to right,
    rgba(var(--audiWhite), 0.3),
    rgba(var(--audiWhite), 0.8) 100%
  );
}
.brightnessTrackScale {
  top: -40px;
}

.UE_0601_Slider.brightness .iconLabels {
  position: absolute;
  width: calc(100% + 100px);
  left: -50px;
  bottom: 47px;
}
.UE_0601_Slider.brightness.buttons .iconLabels {
  position: absolute;
  width: calc(100% - 20px);
  left: 10px;
  bottom: 5px;
}

.UE_0601_Slider.brightness .sliderLabelIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  min-width: var(--listIconSize);
  min-height: var(--listIconSize);
  display: inline-block;
  background-position: center;
  background-repeat: no-repeat;
}
.UE_0601_Slider.brightness .sliderLabelIcon:first-child {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E790_slider_dark.svg');
}
.UE_0601_Slider.brightness .sliderLabelIcon:last-child {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E791_slider_light.svg');
}

.brightnessSlider {
  width: 100%;
  height: 100%;
  position: relative;
}

.brightnessActiveTrackMask {
  position: absolute;
  left: var(--sliderAbsoluteMargin);
  right: var(--sliderAbsoluteMargin);
  top: 50%;
  transform: translateY(-50%);
  height: var(--sliderTrackHeight);
  background-color: rgb(var(--audiWhite));
}

.brightnessActiveTrackMask .brightnessTrackGradient,
.brightnessActiveTrackMask .brightnessTrackScale {
  position: relative;
  width: 100%;
  height: 100%;
}

.brightnessActiveTrackMask .brightnessTrackScale {
  top: -36px;
}

.scrolling .brightnessActiveTrackMask .brightnessTrackScale {
  height: 40px;
  top: -40px;
}

.toastContent {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 4px;
  margin-left: 15px;
  margin-right: 15px;
}

/* TODO move to T_0302_Dialog */
.T_0302_Dialog
  .UE_0601_Slider
  .absoluteSlider
  .sliderWrapper
  .slider.brightness
  .iconLabels
  .sliderLabelIcon {
  -webkit-filter: invert(1);
}

/*----------------TOAST CONTENT--------------*/
.relativeSlider .toastContent,
.dataCollection .toastContent {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0px;
  margin-left: 15px;
  margin-right: 15px;
}

.relativeSlider .dialogText {
  flex: 1;
  overflow: hidden;
  max-width: 100%;
}

.UE_0601_Slider.standardSlider:not(.dataCollection) .sliderValueContainer {
  position: absolute;
  top: 0;
  border: 0;
  background: unset;
}

.UE_0601_Slider.standardSlider:not(.dataCollection) .toastContent {
  margin-left: 0;
  margin-right: 0;
}

.settings {
  position: absolute;
  top: 400px;
  width: 50%;
}
