:root {
  --toastMinHeight: 80px;
  --toastWidht: 513px;
}

:global(#root) > .SE_5014_Toast {
  position: absolute;
  height: 200px;
  width: 600px;
}

.SE_5014_Toast .toastPopup {
  position: absolute;
  min-height: var(--toastMinHeight);
  width: var(--toastWidht);
  margin: var(--popupInsetWidth);
  top: 50%;
  transform: translateY(-50%);
}

.SE_5014_Toast.darken .toastPopup .darkenOverlay {
  position: absolute;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  top: -1px;
  left: -1px;
  background-color: rgba(var(--audiBlack), 0.6);
  z-index: 1;
}

.SE_5014_Toast .toastPopup .textGroup {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: calc(100% - var(--lA1) * 2);
  margin: var(--zA6) var(--lA1) var(--zA6) var(--lA1);
}

.SE_5014_Toast .toastPopup .textGroup p {
  text-align: center;
  flex-shrink: 0;
}

.SE_5014_Toast .contentInfo {
  width: 800px;
  position: absolute;
  top: 300px;
}
