.warningContainer {
  position: absolute;
  width: 160px;
  height: 80px;
  top: 210px;
  left: 595px;
  z-index: 1;
}

.warningContainer--rhd {
  left: unset;
  right: 595px;
}

.warningContainer--centered {
  left: 50%;
