:global(#root) > .SE_0096_VoiceMemoScreen {
  width: 1678px;
  height: 526px;
  position: absolute;
  margin: 0px;
  overflow: hidden;
}

.SE_0096_VoiceMemoScreen {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.voiceMemoScreen {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  padding: 0 40px;
  box-sizing: border-box;
}

.voiceMemoScreen_Text {
  display: flex;
  height: calc(100% - var(--minTouchSize));
}
.voiceMemoScreen_Text .textPrio2 {
  margin-top: 0;
}
.voiceMemoScreen_TextFirstColumn {
  max-width: 30%;
  display: flex;
  flex-direction: column;
  height: var(--minTouchSize);
  justify-content: center;
}
.voiceMemoScreen_TextSecondColumn {
  max-width: calc(100% - var(--cA1));
  display: flex;
  flex-direction: column;
  height: var(--minTouchSize);
  justify-content: center;
  margin-left: var(--cA1);
  overflow: hidden;
}

.voiceMemoScreen_TimeBar {
  margin-top: -77px;
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0096_VoiceMemoScreen {
    height: 526px;
  }
}
