.Text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
}

.normalFont {
  font-family: var(--fontNormal);
}

.lightFont {
  font-family: var(--fontLight);
}

.extendedFont {
  font-family: var(--fontExtendedNormal) !important;
}

.boldFont {
  font-weight: bold;
}

p {
  cursor: default;
  user-select: none;
  margin: 0px;
}

.arHUD {
  font-size: var(--fontSizeARHUD);
  color: rgb(var(--audiWhite));
}

.textPrioBIG {
  font-size: var(--fontSizePrioBIG);
  color: rgb(var(--audiWhite));
  font-family: var(--fontLight);
  line-height: var(--lineHeightPrioBIG);
}

.textPrioBIG.boldFont {
  line-height: 73px;
  font-family: var(--fontNormal);
}

.textPrioA {
  font-size: var(--fontSizePrioA);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrioA);
}

.textPrio1 {
  font-size: var(--fontSizePrio1);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio1);
}

.textPrio1.headline {
  margin-bottom: var(--hA1);
  margin-top: var(--hA2);
}

.textPrio2 {
  font-size: var(--fontSizePrio2);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio2);
}
.textPrio2.grey {
  color: rgb(var(--fpkGrey));
}

.textPrio1 + .textPrio1 {
  margin-top: var(--zA1);
}

.textPrio1 + .textPrio2 {
  margin-top: var(--zA1);
}

.textPrio1.flow + .textPrio2 {
  margin-top: var(--iA4);
}

.textPrio2 + .textPrio2 {
  margin-top: var(--zA2);
}

.textPrio2.left + .textPrio2.right {
  margin-top: 0px;
}

.icon {
  margin-left: var(--iA1);
}

.textPrio3 {
  font-size: var(--fontSizePrio3);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio3);
}

.textPrio4 {
  font-size: var(--fontSizePrio4);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio4);
}

.textPrio5 {
  font-size: var(--fidFontSizePrio5);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--fidLineHeightPrio5);
}

.textClimateLarge {
  font-size: 51px;
  font-family: var(--fontExtendedNormal);
  color: rgb(var(--audiWhite));
  line-height: 48px;
}
.textClimateSmall {
  font-size: 35px;
  font-family: var(--fontExtendedNormal);
  color: rgb(var(--audiWhite));
  line-height: 27px;
}

.textBadge {
  font-size: var(--fontSizePrio4);
  font-family: var(--fontNormal);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio4);
  border: 1px solid rgb(var(--audiWhite));
  height: 24px;
  text-transform: uppercase;
  padding: 0 6px;
}

.inputFieldHeader {
  font-size: var(--fontSizePrio3);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  line-height: var(--lineHeightPrio2);
  text-transform: uppercase;
  margin-bottom: 5px;
}

.hyperlink {
  text-decoration: underline;
  color: rgb(var(--hyperlinkColor));
}

/******** Black Lables *********/
.blackText {
  color: rgb(var(--audiBlack));
  font-family: var(--fontNormal);
}

/****** Text for synonyms ***************/
.synonymText {
  color: rgb(var(--borderGrey));
}

/****** Text for FPK***************/

.textUnits {
  font-size: var(--fontSizePrio3);
  font-family: var(--fontLight);
  color: rgba(var(--audiWhite), 0.7);
  line-height: var(--lineHeightPrio3);
}

p.grayLabel {
  color: rgba(var(--audiWhite), 0.7);
}

.textStatus {
  font-size: 25px;
  font-family: var(--fontLight);
  color: rgb(var(--fpkGrey));
}

/* FID new text sizes */

.fpk.textPrio1 {
  font-size: var(--fidFontSizePrio1) !important;
  line-height: var(--fidLineHeightPrio1) !important;
}

.fpk.textPrio2 {
  font-size: var(--fidFontSizePrio2) !important;
  line-height: var(--fidLineHeightPrio2) !important;
}

.fpk.textPrio3 {
  font-size: var(--fidFontSizePrio3) !important;
  line-height: var(--fidLineHeightPrio3) !important;
}

.fpk.textUnits {
  font-size: var(--fidFontSizePrio3) !important;
  line-height: var(--fidLineHeightPrio3) !important;
}

/****** Text for Settings***************/
.textSettingLabel {
  font-size: var(--fontSizePrio3);
  font-family: var(--fontLight);
  color: rgb(var(--audiWhite));
  margin-left: 50px;
}

.disabled,
.cssDocu.disabled,
.disabled .cssDocu {
  color: rgb(var(--fpkGrey));
}

.cssDocu {
  color: greenyellow;
}

.textPrioA.statusInfoBoxStyling {
  margin: var(--statusInfoBoxMarginPrioA) var(--statusInfoBoxMargin);
}

.textPrio1.statusInfoBoxStyling {
  margin: var(--statusInfoBoxMarginPrio1) var(--statusInfoBoxMargin);
}

.textPrio2.statusInfoBoxStyling {
  margin: var(--statusInfoBoxMarginPrio2) var(--statusInfoBoxMargin);
}

.textPrio3.statusInfoBoxStyling {
  margin: var(--statusInfoBoxMarginPrio3) var(--statusInfoBoxMargin);
}
