.laptimerContainer {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
}

.laptimerContainer.left.laptimerFormat_1,
.laptimerContainer.left.laptimerFormat_2,
.laptimerContainer.left.laptimerFormat_3,
.laptimerContainer.left.laptimerFormat_4 {
  left: 190px;
}

.laptimerContainer.right.laptimerFormat_1,
.laptimerContainer.right.laptimerFormat_2,
.laptimerContainer.right.laptimerFormat_3,
.laptimerContainer.right.laptimerFormat_4 {
  left: unset;
  right: 190px;
}

.laptimerIcon {
  background-color: rgba(255, 0, 0, 0.3);
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 8px;
}

.laptimerContainer.laptimerFormat_1 .laptimerIcon,
.laptimerContainer.laptimerFormat_3 .laptimerIcon {
  /* margin-left: 33px; */
}

.stopuhrIcon {
  width: 40px;
  height: 41px;
  background-image: url('../../../01_HTML/assets/Stopuhr-Icon (dummy).png');
}

.trackPositionIcon {
  width: 53px;
  height: 41px;
  background-image: url('../../../01_HTML/assets/TrackPosition-IconSequenz  (dummy).png');
}
