/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --se0030SeparatorMargin: 25px;
  --se0030ButtonWidth: 515px;
  --se0030CarDecoratorWidth: 760px;
  --se0030TyresOffset: 210px;
}

/* Standalone */

:global(#root) > .ST_0008_CarService {
  position: absolute;
  height: 578px;
  width: 1800px;
  margin: 0;
}

/* ST_0008_CarService  */

.ST_0008_CarService {
}

:global(#root) > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  margin: 0;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

:global(#root) > .darkBackground {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(var(--audiBlack), 0.5);
}

.carService {
  height: 100%;
  width: 100%;
  position: absolute;
}

.carDecorator {
  position: absolute;
  width: var(--se0030CarDecoratorWidth);
  height: 100%;
  left: calc(
    (100% - var(--se0030ButtonWidth) - var(--se0030CarDecoratorWidth)) / 2 +
      16px + var(--se0030ButtonWidth)
  );
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.textBlock,
.rdkIcon {
  display: flex;
  position: absolute;
  width: var(--se0030CarDecoratorWidth);
  height: 100%;
  left: calc(
    (100% - var(--se0030ButtonWidth) - var(--se0030CarDecoratorWidth)) / 2 +
      16px + var(--se0030ButtonWidth)
  );
  justify-content: center;
  align-items: center;
}

.textBlock .paragraphGroup {
  display: flex;
  width: calc(100% - var(--textButtonMargin) * 2);
  margin: 0 var(--textButtonMargin);
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-self: center;
}

.textBlock .paragraphGroup .textPrio2 {
  width: 100%;
  text-align: center;
}

.rdkIcon .fpkCommonIcon {
  position: absolute;
  left: calc(100% / 2 - (var(--fpkCommonIconSize) / 2));
  top: calc(100% / 2 - (var(--fpkCommonIconSize) / 2));
  height: var(--fpkCommonIconSize);
  width: var(--fpkCommonIconSize);
  background-color: rgba(var(--boundingBoxGreenFull));
}

/*--------LEFT SECTION WRAPPER---------*/

.leftSectionWrapper {
  position: absolute;
  height: calc(100% - 90px);
  width: var(--se0030ButtonWidth);
  left: 16px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 21px;
}

/*--------BUTTONS---------*/

.buttons {
  position: relative;
  width: 100%;
  flex: 1;
  margin-bottom: var(--se0030SeparatorMargin);
}

.carService.variant_1 .leftSectionWrapper .buttons {
  height: calc(2 * var(--minTouchSize));
}

.actionButtonWrapper {
  position: relative;
  height: fit-content;
}

.actionButtonWrapper .button,
.statusInfo {
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  max-width: 100%;
  width: calc(100% - 90px);
  position: relative;
  display: flex;
  justify-content: flex-start;
  padding-left: 45px;
  padding-right: 45px;
}

.actionButtonWrapper .btnContainerText {
  align-items: unset;
}

.statusInfo .touchIcon {
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
  background-color: rgb(var(--audiGreen));
  background-image: none;
}

.actionButtonWrapper .button .touchIcon,
.statusInfo .touchIcon {
  position: relative;
  left: 0px;
  transform: translate(0%, -50%);
  min-width: var(--touchIconSize);
}

.actionButtonWrapper .button .textPrio2,
.statusInfo .textPrio2 {
  position: relative;
  line-height: var(--minTouchSize);
  z-index: 1;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  flex: 1;
  max-width: calc(
    var(--se0030ButtonWidth) - (var(--textButtonMargin) * 2) -
      var(--touchIconSize)
  );
}

.leftSectionWrapper .touchIcon + .textPrio2,
.leftSectionWrapper .touchIcon + .buttonWithIcon {
  margin-left: var(--iA1);
}

.btnContainerText .Text {
  margin: unset;
}

.UE_0101_ActionButton.text .btnContainerText p {
  margin: unset;
  width: 100%;
  max-width: 100%;
  text-align: left;
}
/*-----PRESSURE INDICATOR------*/

.pressureIndicator {
  position: absolute;
  width: fit-content;
  height: fit-content;
}

.pressureIndicator.top {
  top: calc(50% - var(--se0030TyresOffset) - 12px);
}
.pressureIndicator.left {
  left: 755px;
}
.pressureIndicator.right {
  left: 1435px;
}
.pressureIndicator.bottom {
  top: calc(50% + var(--se0030TyresOffset) - 23px);
}

.pressureIndicator .SE_0106_Button_with_Connector {
  position: relative;
}
.pressureIndicator.bottom .SE_0106_Button_with_Connector,
.pressureIndicator.top .SE_0106_Button_with_Connector {
  top: calc(-49px / 2);
}
.pressureIndicator.right .SE_0106_Button_with_Connector,
.pressureIndicator.left .SE_0106_Button_with_Connector {
  left: calc(-49px / 2);
}

.pressureIndicator.top .squareIssue {
  top: 73px;
}
.pressureIndicator.left .squareIssue {
  left: 135px;
}
.pressureIndicator.right .squareIssue {
  left: -116px;
}
.pressureIndicator.bottom .squareIssue {
  top: -86px;
}

/*-------CAR CONNECTORS---------*/

.pressureIndicator .tireInfoBox {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 129px;
  height: 87px;
}

.pressureIndicator.top .tireInfoBox {
  top: -146px;
}
.pressureIndicator.bottom .tireInfoBox {
  top: 100px;
}
.pressureIndicator.right .tireInfoBox,
.pressureIndicator.left .tireInfoBox {
  left: 52%;
}

.pressureIndicator .tireInfoBox .numberColumn {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-right: var(--zA1);
  overflow: hidden;
  height: 100%;
  width: 76px;
}
.pressureIndicator.bottom .tireInfoBox .numberColumn {
  justify-content: flex-end;
  margin-top: 5px;
}

.pressureIndicator .tireInfoBox .unitColumn {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  overflow: hidden;
  width: 49px;
  height: 100%;
}

.pressureIndicator .tireInfoBox .unitColumn .textPrio2 {
  width: 100%;
}
.pressureIndicator.bottom .tireInfoBox .numberColumn .textPrio2:first-child {
  padding-bottom: 4px;
}
.pressureIndicator.bottom .tireInfoBox .unitColumn .textPrio2:first-child {
  padding-bottom: 10px;
}
.pressureIndicator .tireInfoBox .unitColumn .textPrio2:first-child {
  padding-bottom: 3px;
}

.pressureIndicator .SE_0106_Button_with_Connector .squareContainer {
  border: none;
  background-color: unset;
}

.pressureIndicator
  .SE_0106_Button_with_Connector
  .squareContainer
  .squareBlack {
  display: none;
}

.pressureIndicator p {
  position: relative;
}

.pressureIndicator p.value.none {
  color: rgb(var(--fpkGrey));
}

.pressureIndicator p.value.ok {
  color: rgb(var(--FIDtelltaleGreen));
}

.pressureIndicator p.value.warning {
  color: rgb(var(--fpkOrange));
}

.pressureIndicator p.value.failure {
  color: rgb(var(--fpkRed));
}

.pressureIndicator p.degrees {
  color: rgb(var(--fpkGrey));
}

.pressureIndicator .squareIssue {
  display: flex;
  width: 122px;
  height: 44px;
  position: absolute;
  align-items: center;
  justify-content: center;
}

.pressureIndicator .Text {
  overflow: hidden;
  text-overflow: unset;
}

.squareIssue.none {
  background-color: rgba(var(--fpkGrey), 0.3);
  border: 2px solid rgb(var(--fpkGrey));
}

.squareIssue.ok {
  background-color: rgba(var(--FIDtelltaleGreen), 0.3);
  border: 2px solid rgb(var(--FIDtelltaleGreen));
}

.squareIssue.warning {
  background-color: rgba(var(--fpkOrange), 0.3);
  border: 2px solid rgb(var(--fpkOrange));
}

.squareIssue.failure {
  background-color: rgba(var(--fpkRed), 0.3);
  border: 2px solid rgb(var(--fpkRed));
}

/*-----------NEXT INSPECTION---------*/

.nextInspection {
  position: relative;
  width: 100%;
  display: flex;
}

.separatorLine {
  position: absolute;
  width: var(--se0030ButtonWidth);
  height: 2px;
  background-color: rgba(var(--audiWhite), 0.15);
}

.textLine {
  position: relative;
  padding-left: 45px;
  align-self: center;
  width: 100%;
  overflow: hidden;
  margin-top: var(--se0030SeparatorMargin);
}
.textLine .textPrio2 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.textLine .textPrio2:nth-child(2) {
  margin-top: var(--zA8);
}

/*-------VARIANTS--------*/

.firstButton.variant_2 .touchIcon {
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
  background-color: rgb(var(--warningOrange));
  background-image: none;
}

.firstButton.variant_3 .touchIcon {
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
  background-color: rgb(var(--fpkRed));
  background-image: none;
}

/*-------SETTINGS--------*/

.settingConfig {
  position: absolute;
  height: 20%;
  width: 50%;
  top: 560px;
}

/*------SMALL VERSION-------*/

@media only screen and (max-width: 1768px) {
  :root {
    --se0030CarDecoratorWidth: 477px;
    --se0030TyresOffset: 156px;
  }

  :global(#root) > .ST_0008_CarService {
    height: 450px;
    width: 1648px;
  }

  .ST_0008_CarService .carService .textBlock .paragraphGroup {
    width: 100%;
    margin: unset;
  }

  .ST_0008_CarService .carService .leftSectionWrapper .buttons {
    height: calc(2 * var(--minTouchSize));
  }
  .ST_0008_CarService .carService.variant_1 .leftSectionWrapper .buttons {
    height: var(--minTouchSize);
  }

  .ST_0008_CarService .carService .pressureIndicator.left {
    left: 786px;
  }
  .ST_0008_CarService .carService .pressureIndicator.right {
    left: 1306px;
  }

  .ST_0008_CarService .carService .pressureIndicator .squareIssue {
    width: 68px;
    height: 25px;
  }

  .ST_0008_CarService .carService .pressureIndicator.bottom .squareIssue {
    top: -70px;
  }
  .ST_0008_CarService
    .carService
    .pressureIndicator.bottom
    .SE_0106_Button_with_Connector {
    margin-top: -145px;
  }
  .ST_0008_CarService .carService .pressureIndicator.bottom .tireInfoBox {
    bottom: 14px;
  }

  .ST_0008_CarService
    .carService
    .pressureIndicator.left
    .SE_0106_Button_with_Connector {
    margin-left: 158px;
  }
  .ST_0008_CarService .carService .pressureIndicator.left .tireInfoBox {
    left: 1px;
  }

  .ST_0008_CarService
    .carService
    .pressureIndicator.right
    .SE_0106_Button_with_Connector {
    margin-left: -101px;
  }
  .ST_0008_CarService .carService .pressureIndicator.right .tireInfoBox {
    right: 39px;
  }
}
