/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --climateBarSliderWidth: 614px;
  --climateBarBlackSpacing: 8px;
  --climateBarBorderStrength: 1px;
  --minimizedClimateWidth: calc(var(--minTouchSize) + 1px);
  --minimizedClimateHeight: calc(
    var(--buttonBorderDist) + 3 * var(--minTouchSize)
  );
}

@media only screen and (max-width: 1768px) {
  :root {
    --climateBarSliderWidth: 557px;
    --minimizedClimateHeight: calc(
      var(--buttonBorderDist) + 3 * var(--minTouchSize) - 3px
    );
  }

  .SE_0120_ClimateBar > .climateBar.minimized .button.defroster,
  .SE_0120_ClimateBar > .climateBar.minimized .button.maximize {
    height: calc(var(--minTouchSize) - 1px);
  }
}

:global(#root) > .SE_0120_ClimateBar {
  margin: 0px;
  position: absolute;
  top: 380px;
  height: var(--climateBarHeight);
  width: 1920px;
}
:global(#root) > .SE_0120_ClimateBar.b10Screen {
  width: 2220px;
}

.SE_0120_ClimateBar > .climateBar {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  background-clip: content-box;
  background-color: rgb(var(--audiBlack));
  transition: var(--AG_0303_ScaleResize);
  transition-delay: 0.2s;
}

.SE_0120_ClimateBar > .climateBar.minimized {
  overflow: visible;
}

.SE_0120_ClimateBar.opsReduced {
  pointer-events: none;
}

.SE_0120_ClimateBar
  > .climateBar
  > div:not(.maximize):not(.miniButtonLine):not(.defroster) {
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.35s;
}

.climateBarContainer {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.SE_0120_ClimateBar.noSeatheating
  .climateBarContainer
  .buttonGroup:global(#temperatureLeft) {
  margin-left: 64px;
}
.SE_0120_ClimateBar.noSeatheating
  .climateBarContainer
  .buttonGroup:global(#temperatureRight) {
  margin-right: 64px;
}

.SE_0120_ClimateBar .climateBar .button {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  margin-top: 13px;
}

.SE_0120_ClimateBar .climateBar .button.maximize {
  position: relative;
  opacity: 0;
  transition: var(--AG_0202_FadeOut);
  margin-bottom: var(--buttonBorderDist);
}

.SE_0120_ClimateBar .climateBar .buttonGroup,
.SE_0120_ClimateBar .climateBar .seatButtonGroup {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.SE_0120_ClimateBar .climateBar .buttonGroup > .button:not(:first-child),
.SE_0120_ClimateBar .climateBar .seatButtonGroup > .button:not(:first-child),
.SE_0120_ClimateBar .climateBar .jokerButtons > .button:not(:first-child) {
  margin-left: 7px;
}

.SE_0120_ClimateBar .climateBar .jokerButtons {
  position: relative;
  width: calc(var(--directAccessWidth) - 35px);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.SE_0120_ClimateBar .climateBar .buttonGroup .textBox {
  position: absolute;
  top: 12px;
  margin-left: auto;
  margin-right: auto;
}

.SE_0120_ClimateBar .climateBar .buttonGroup.textBoxHidden .textBox {
  display: none;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) {
  width: 128px;
  justify-content: center;
  margin: 8px 6px 4px 6px;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .button {
  margin-top: 5px;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .textBox {
  top: 4px;
  width: calc(100% + 44px);
}

@media only screen and (min-width: 2219px) {
  .SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .textBox {
    width: calc(100% + 41px);
  }
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .text.center {
  width: 100%;
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.SE_0120_ClimateBar
  .climateBar
  .buttonGroup:global(#mode)
  .footnote.text.center {
  margin-top: 92px;
  height: auto;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .menu {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .textPrio3.realIcon {
  opacity: 0.25;
}
.SE_0120_ClimateBar
  .climateBar
  .buttonGroup.textBoxHidden:global(#mode)
  .footnote {
  display: none;
}

.SE_0120_ClimateBar .climateBar .textBox .content {
  display: flex;
  flex-direction: row;
  position: absolute;
  left: 0;
  right: 0;
  height: calc(100% - 5px);
  width: 100%;
  align-items: center;
  justify-content: center;
}

.SE_0120_ClimateBar .climateBar .textBox {
  width: var(--minTouchSize);
  height: 24px;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.SE_0120_ClimateBar .climateBar .textBox .hidden {
  display: none;
}

.SE_0120_ClimateBar .climateBar .textBox .textBackground {
  position: absolute;
  width: 100%;
  height: calc(100% - 6px);
}

.SE_0120_ClimateBar
  .climateBar
  .buttonGroup:global(#mode)
  .textBox
  .textBackground {
  width: 100%;
  height: calc(100% - 5px);
  border-bottom: 0px;
  top: 0px;
  opacity: 0.5;
  background-image: none;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .touchIcon {
  pointer-events: none;
}

.SE_0120_ClimateBar .climateBar .buttonGroup:global(#mode) .centerText {
  pointer-events: none;
}

.SE_0120_ClimateBar .climateBar .temperatureSlider {
  position: absolute;
  height: calc(100% + 79px);
  width: var(--climateBarSliderWidth);
  margin-top: -81px;
  display: none;
  z-index: 3;
}
.SE_0120_ClimateBar .climateBar .temperatureSlider:global(#tempSliderLeft) {
  left: 0;
}
.SE_0120_ClimateBar .climateBar .temperatureSlider:global(#tempSliderRight) {
  right: 0;
}

.SE_0120_ClimateBar .climateBar .temperatureSlider.ventilation {
  width: 520px;
}
.SE_0120_ClimateBar
  .climateBar
  .temperatureSlider.ventilation:global(#tempSliderLeft) {
  left: calc(var(--minTouchSize) + 5px);
}
.SE_0120_ClimateBar
  .climateBar
  .temperatureSlider.ventilation:global(#tempSliderRight) {
  right: calc(var(--minTouchSize) + 5px);
}

.SE_0120_ClimateBar.noSeatheating .climateBar .temperatureSlider {
  width: 452px;
}
.SE_0120_ClimateBar.noSeatheating
  .climateBar
  .temperatureSlider:global(#tempSliderLeft) {
  left: 0;
}
.SE_0120_ClimateBar.noSeatheating
  .climateBar
  .temperatureSlider:global(#tempSliderRight) {
  right: 0;
}

.SE_0120_ClimateBar.b10Screen .climateBar .temperatureSlider {
  width: 400px;
}
.SE_0120_ClimateBar.b10Screen .climateBar .temperatureSlider.ventilation {
  width: 328px;
}

.SE_0120_ClimateBar.b10Screen
  .climateBar
  .temperatureSlider:global(#tempSliderLeft) {
  left: calc(var(--minTouchSize) + 10px);
}
.SE_0120_ClimateBar.b10Screen
  .climateBar
  .temperatureSlider:global(#tempSliderRight) {
  right: calc(var(--minTouchSize) + 10px);
}

.SE_0120_ClimateBar.b10Screen
  .climateBar
  .temperatureSlider.ventilation:global(#tempSliderLeft) {
  left: calc(var(--minTouchSize) * 2 + 11px);
}
.SE_0120_ClimateBar.b10Screen
  .climateBar
  .temperatureSlider.ventilation:global(#tempSliderRight) {
  right: calc(var(--minTouchSize) * 2 + 11px);
}

.SE_0120_ClimateBar.b10Screen.noSeatheating
  .climateBar
  .temperatureSlider:global(#tempSliderLeft) {
  left: 28px;
}
.SE_0120_ClimateBar.b10Screen.noSeatheating
  .climateBar
  .temperatureSlider:global(#tempSliderRight) {
  right: 28px;
}

.SE_0120_ClimateBar .climateBar .temperatureSlider .centeredContent {
  display: flex;
  align-items: center;
  justify-content: center;
}

.SE_0120_ClimateBar .climateBar .temperatureSlider .backgroundLayer {
  position: absolute;
  width: 100%;
  height: calc(var(--climateBarHeight) - 13px);
  top: 89px;
  background-color: rgb(var(--audiBlack));
}

.SE_0120_ClimateBar .climateBar .temperatureSlider .UE_0601_Slider.relative {
  width: 100%;
  position: absolute;
}

.SE_0120_ClimateBar
  .climateBar
  .temperatureSlider
  .UE_0601_Slider.relative
  .relativeSlider {
  width: 100%;
}

.SE_0120_ClimateBar .climateBar .temperatureSlider.open {
  display: initial;
}

.SE_0120_ClimateBar .climateBar .buttonGroup .temperature .digits {
  text-align: center;
  display: flex;
  justify-content: center;
  margin-top: 35px;
}

.SE_0120_ClimateBar .climateBar .centerText {
  text-align: center;
  line-height: 26px;
  position: absolute;
  top: 50%;
}

.SE_0120_ClimateBar .climateBar .buttonGroup .dragLine {
  top: 0;
  height: 1px;
  width: 100%;
  background: linear-gradient(
    to right,
    rgb(var(--audiWhite)),
    rgb(var(--climateLightBlue))
  );
  position: absolute;
}

/*----Stepper----*/

.SE_0120_ClimateBar .climateBar .buttonGroup.temp,
.SE_0120_ClimateBar .climateBar .buttonGroup.fan {
  width: fit-content;
}

.SE_0120_ClimateBar .climateBar .climateBarStepperType {
  width: fit-content;
}

.SE_0120_ClimateBar
  .climateBar
  .buttonGroup.temp
  .UE_1302_Stepper
  .stepper
  .button,
.SE_0120_ClimateBar
  .climateBar
  .buttonGroup.fan
  .UE_1302_Stepper
  .stepper
  .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  margin-top: 0;
}

.SE_0120_ClimateBar .climateBar .buttonGroup.temp .textBox,
.SE_0120_ClimateBar .climateBar .buttonGroup.fan .textBox {
  z-index: 1;
}

.SE_0120_ClimateBar .climateBar .temperatureStepper,
.SE_0120_ClimateBar .climateBar .fanOff,
.SE_0120_ClimateBar .climateBar .fanStepper {
  margin-top: 13px;
  width: fit-content;
  height: var(--minTouchSize);
  position: relative;
}

.SE_0120_ClimateBar .climateBar .temperatureStepper .UE_1302_Stepper,
.SE_0120_ClimateBar .climateBar .fanOff .UE_1302_Stepper,
.SE_0120_ClimateBar .climateBar .fanStepper .UE_1302_Stepper {
  width: fit-content;
  position: relative;
  height: fit-content;
}

/*----Zone reduction-----*/

.SE_0120_ClimateBar
  .climateBar.reduced
  .buttonGroup:global(#temperatureRight)
  .textBox
  .textBackground,
.SE_0120_ClimateBar
  .climateBar.efficiency
  .buttonGroup:global(#temperatureRight)
  .textBox
  .textBackground {
  background-image: none;
}

.SE_0120_ClimateBar
  .climateBar.reduced
  .buttonGroup:global(#temperatureRight)
  .textPrio3,
.SE_0120_ClimateBar
  .climateBar.reduced
  .buttonGroup:global(#temperatureRight)
  .UE_1302_Stepper,
.SE_0120_ClimateBar .climateBar.reduced .seatClimate.right,
.SE_0120_ClimateBar
  .climateBar.efficiency
  .buttonGroup:global(#temperatureRight)
  .textPrio3,
.SE_0120_ClimateBar
  .climateBar.efficiency
  .buttonGroup:global(#temperatureRight)
  .UE_1302_Stepper,
.SE_0120_ClimateBar .climateBar.efficiency .seatClimate.right,
.SE_0120_ClimateBar
