  > div.seatClimate.right:not(.maximize) {
  opacity: var(--touchDisabledState);
}

.SE_0120_ClimateBar .climateBar.reduced .seatClimate.right {
  pointer-events: none;
}

.SE_0120_ClimateBar .climateBar.reduced .seatClimate.right .uiState {
  display: none;
}

/*---SeatClimateButtons--*/

.SE_0120_ClimateBar .climateBar .seatClimate {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  margin-top: 13px;
}

.SE_0120_ClimateBar .climateBar .seatClimate .button {
  position: absolute;
  margin: 0;
}

.SE_0120_ClimateBar .climateBar .seatClimate .button.close .touchIcon.seatIcon,
.SE_0120_ClimateBar .climateBar .seatClimate .button.open .touchIcon.closeIcon {
  opacity: 0;
}
.SE_0120_ClimateBar .climateBar .seatClimate .button.open .touchIcon.seatIcon,
.SE_0120_ClimateBar
  .climateBar
  .seatClimate
  .button.close
  .touchIcon.closeIcon {
  opacity: 1;
}

.SE_0120_ClimateBar
  .climateBar
  .seatClimate
  .button.close
  .touchIcon.closeIcon {
  transition: var(--AG_0202_FadeOut);
  transition-delay: 0.1s;
  background-image: url(../img/dummyCloseIcon.png);
}
.SE_0120_ClimateBar .climateBar .seatClimate .button.close .touchIcon.seatIcon {
  transition: var(--AG_0202_FadeOut);
  transition-delay: unset;
}
.SE_0120_ClimateBar .climateBar .seatClimate .button.open .touchIcon.closeIcon {
  transition: var(--AG_0201_FadeIn);
  transition-delay: unset;
  background-image: url(../img/dummyCloseIcon.png);
}
.SE_0120_ClimateBar .climateBar .seatClimate .button.open .touchIcon.seatIcon {
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.2s;
}

.SE_0120_ClimateBar .climateBar .seatClimate .button.heating,
.SE_0120_ClimateBar .climateBar .seatClimate .button.ventilation {
  display: none;
}
.SE_0120_ClimateBar .climateBar .seatClimate .button.close,
.SE_0120_ClimateBar .climateBar .seatClimate .button.open {
  z-index: 2;
  pointer-events: none;
}

.SE_0120_ClimateBar .climateBar .seatClimate.heating.ventilation .button.close,
.SE_0120_ClimateBar .climateBar .seatClimate.heating.ventilation .button.open {
  pointer-events: initial;
}

.SE_0120_ClimateBar .climateBar .seatClimate.heating .button.heating,
.SE_0120_ClimateBar .climateBar .seatClimate.ventilation .button.ventilation {
  display: block;
}

.SE_0120_ClimateBar .climateBar .seatClimate .stateButtonWrapper {
  position: absolute;
  width: inherit;
  bottom: 0px;
  height: var(--minTouchSize);
  box-shadow: none;
  background-color: rgb(var(--audiBlack));
}

.SE_0120_ClimateBar
  .climateBar
  .seatClimate.opened.heating.ventilation
  .button
  .uiState {
  height: 100%;
  margin: 0;
}

.SE_0120_ClimateBar .climateBar .seatClimate.opened .stateButtonWrapper {
  bottom: 132px;
  height: 253px;
  box-shadow: 0px 0px 40px 3px rgb(var(--audiBlack));
  animation: seatClimateExpand var(--animationDuration);
  z-index: 1;
}

.SE_0120_ClimateBar .climateBar .seatClimate.closed .stateButtonWrapper {
  animation: seatClimateCollapse var(--animationDuration);
}

.SE_0120_ClimateBar
  .climateBar
  .seatClimate
  .stateButtonWrapper
  .button
  .touchIcon,
.SE_0120_ClimateBar
  .climateBar
  .seatClimate.closed
  .stateButtonWrapper
  .button
  .touchIcon {
  opacity: 0;
  transition: var(--AG_0202_FadeOut);
  transition-delay: unset;
}
.SE_0120_ClimateBar
  .climateBar
  .seatClimate.opened
  .stateButtonWrapper
  .button
  .touchIcon {
  opacity: 1;
  transition: opacity 1s ease-in-out;
  transition-delay: 0.02s;
}

.SE_0120_ClimateBar .climateBar .seatClimate .button.ventilation {
  top: 0px;
}

.SE_0120_ClimateBar .climateBar .seatClimate .button.heating {
  bottom: 0px;
}

/*------------------------------ CLIMATEBAR MINIMIZED ------------------------------------*/
.SE_0120_ClimateBar > .climateBar.minimized {
  position: relative;
  width: var(--minimizedClimateWidth);
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  background-clip: content-box;
  transition-delay: 0s;
}
.SE_0120_ClimateBar > .climateBar.minimized.b10Screen {
  width: var(--directAccessWidth);
}

.SE_0120_ClimateBar .climateBar > .minimizedOverlay {
  position: absolute;
  bottom: 0;
  transition: var(--AG_0303_ScaleResize);
  transition-delay: 0.3s;
}
.SE_0120_ClimateBar .climateBar.minimized > .minimizedOverlay.vertical {
  height: var(--minimizedClimateHeight);
  opacity: 1;
  border-top: var(--climateBarBorderStrength) solid rgba(var(--audiWhite), 0.35);
}
.SE_0120_ClimateBar .climateBar > .minimizedOverlay.vertical {
  width: var(--minimizedClimateWidth);
  height: 0;
  opacity: 0;
  background-color: rgba(var(--ambientBackground1), 1);
  box-shadow: 0px -5px 0px 0px rgb(var(--audiBlack));
  box-sizing: border-box;
  transition: var(--AG_0201_FadeIn);
  transition-delay: 0.3s;
}
.SE_0120_ClimateBar .climateBar.ops > .minimizedOverlay.vertical {
  border-top: unset;
  transition: none;
  transition-delay: 0s;
}
.SE_0120_ClimateBar .climateBar.minimized > .minimizedOverlay.vertical {
  display: block;
  transition: var(--AG_0202_FadeOut);
  transition-delay: 0.2s;
}

.SE_0120_ClimateBar .climateBar .miniButtonLine {
  position: absolute;
  height: fit-content;
  width: var(--minimizedClimateWidth);
  bottom: 0;
  flex-direction: column;
  justify-content: flex-end;
  opacity: 0 !important;
  pointer-events: none;
  z-index: 1;
  transition: unset;
}
.SE_0120_ClimateBar .climateBar.b10Screen .miniButtonLine {
  width: var(--directAccessWidth);
  height: 100%;
  flex-direction: row;
  justify-content: flex-end;
}

.SE_0120_ClimateBar .climateBar.minimized.ops .miniButtonLine {
  transition: unset;
}

.SE_0120_ClimateBar .climateBar.minimized .miniButtonLine {
  display: flex;
  opacity: 1 !important;
  pointer-events: initial;
}
.SE_0120_ClimateBar
  .climateBar.minimized:not(.b10Screen)
  .miniButtonLine
  .button {
  width: 100%;
}

.SE_0120_ClimateBar .miniButtonLine.opsLargeRes {
  width: calc(var(--minTouchSize) * 2);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
}

.defrostBtnContainer.opsClimate,
.b10Screen .defrostBtnContainer {
  display: flex;
}

.SE_0120_ClimateBar > .climateBar.minimized .button.defroster,
.SE_0120_ClimateBar > .climateBar.minimized .button.maximize {
  opacity: 1;
}
.SE_0120_ClimateBar > .climateBar.minimized.vertical .button.defroster,
.SE_0120_ClimateBar > .climateBar.minimized.vertical .button.maximize {
  margin-top: 0;
}

.SE_0120_ClimateBar > .climateBar.minimized .button.maximize {
  z-index: 1;
}
.SE_0120_ClimateBar > .climateBar.minimized.ops .button.maximize {
  opacity: 0.4;
  /* z-index: 0; */
}

.SE_0120_ClimateBar
  > .climateBar.minimized
  > div:not(.minimizedOverlay):not(.miniButtonLine):not(.maximize):not(
    .defroster
  ):not(.seatClimate) {
  opacity: 0;
  transition-delay: 0s;
}

