.SE_0120_ClimateBar.b10Screen .climateBar .separatorLineLeft {
  position: absolute;
  width: 448px;
  height: 1px;
  top: 2px;
  left: 0px;
  background-color: var(--tabbarSeparatorColor);
}

.climateBar:not(.ops) .separatorLineCenter {
  position: absolute;
  width: 100%;
