  top: 2px;
  left: 0px;
  background-color: rgba(var(--audiWhite), 1);
}

.SE_0120_ClimateBar.b10Screen .climateBar .separatorLineCenter {
  width: 1743px;
  left: 451px;
}

.SE_0120_ClimateBar.b10Screen .climateBar .separatorLineRight {
  position: absolute;
  width: 23px;
  height: 1px;
  top: 2px;
  right: 0px;
  background-color: var(--tabbarSeparatorColor);
}

.SE_0120_ClimateBar.b10Screen .climateBar .miniButtonLine .separatorLine {
  position: absolute;
  width: var(--directAccessWidth);
  height: 1px;
  top: 2px;
  left: 0px;
