}

.climateBar .buttonGroup .button.text .btnContainerText p {
  margin-left: 5px;
  margin-right: 5px;
  width: calc(100% - 10px);
}

@keyframes seatClimateExpand {
  from {
    bottom: 0px;
    height: var(--minTouchSize);
  }
  to {
    height: 253px;
    bottom: 132px;
  }
}

@keyframes seatClimateCollapse {
  from {
    height: 253px;
    bottom: 132px;
  }
  to {
    bottom: 0px;
    height: var(--minTouchSize);
  }
}

