  display: none;
}

body.ST_0066_ClimateMenu.joker :global(#closeOperation) {
  display: flex;
}

body.ST_0066_ClimateMenu.joker :global(#closeOperation) {
  width: 128px;
  justify-content: center;
}

.settings {
  position: absolute;
  top: 550px;
