/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --imageSize: 240px;
  --imageSizeMini: 76px;
  --imageSizePhone: 190px;
  --ueListTileHeaderHeight: 100px;
  --textCoverMargin: 15px;
}

:global(#root) > .UE_1006_ListTile {
  background: rgb(var(--bodyColorGrey));
  height: 560px;
  position: relative;
  margin: 0;
}

.listIcon + .listIcon {
  margin-left: 0 !important;
  padding-left: 5px;
}

.UE_1006_ListTile .listTile {
  height: 100%;
}

.UE_1006_ListTile .horizontalList {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}

.UE_1006_ListTile .horizontalList.UE_1006_01,
.UE_1006_ListTile .horizontalList.UE_1006_02,
.UE_1006_ListTile .horizontalList.UE_1006_03 {
  width: calc(100% - 2 * var(--listVerticalPadding));
  padding: 0 var(--listVerticalPadding);
}

.UE_1006_ListTile .horizontalList.listTileFadeOutBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.UE_1006_ListTile .horizontalList.listTileFadeTopBottom {
  -webkit-mask-image: var(--listStartFadeout);
}

.UE_1006_ListTile .horizontalList.listTileFadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.UE_1006_ListTile .horizontalList .listWrapper {
  position: relative;
  width: calc(100% - 24px);
  margin-left: var(--iA1);
}

.UE_1006_ListTile .horizontalList.fas .listWrapper {
  height: 100%;
}

.UE_1006_ListTile .horizontalList.UE_1006_00 .listWrapper {
  width: calc(100% - var(--minTouchSize) * 2);
  padding: 0 var(--minTouchSize);
  margin-left: 0;
}

.UE_1006_ListTile .horizontalList.UE_1006_01 .listWrapper + .listWrapper,
.UE_1006_ListTile .horizontalList.UE_1006_02 .listWrapper + .listWrapper,
.UE_1006_ListTile .horizontalList.UE_1006_03 .listWrapper + .listWrapper {
  margin-top: var(--lA1);
}

.UE_1006_ListTile .horizontalList.UE_1006_00 .listWrapper:before {
  display: block;
  content: '';
  position: relative;
  height: 1px;
  background-color: rgba(var(--audiGrey), 0.8);
  margin-top: 5px;
  margin-bottom: 10px;
}
.UE_1006_ListTile .horizontalList.UE_1006_00 .listWrapper.header:before,
.UE_1006_ListTile .horizontalList.UE_1006_00 .listWrapper:first-child:before {
  display: none;
}

.UE_1006_ListTile .horizontalList .listWrapper .UE_1007_ListHeader {
  display: none;
  position: relative;
  width: 100%;
  height: calc(var(--ueListTileHeaderHeight) - var(--zA4));
}
.UE_1006_ListTile
  .horizontalList
  .listWrapper
  .UE_1007_ListHeader.firstListHeader {
  height: var(--ueListTileHeaderHeight);
}

.listTileHeader {
}

.UE_1006_ListTile .horizontalList .listWrapper.header .UE_1007_ListHeader {
  display: block;
}

.UE_1006_ListTile .horizontalList .listWrapper .list {
  position: relative;
  width: 100%;
  flex: 1;
  display: flex;
  justify-content: space-between;
}

.UE_1006_ListTile .horizontalList.UE_1006_01 .listWrapper .list,
.UE_1006_ListTile .horizontalList.UE_1006_03 .listWrapper .list {
  padding: 0 var(--lA1);
  box-sizing: border-box;
}

.UE_1006_ListTile .horizontalList.UE_1006_02 .listWrapper .list {
  padding: 0 calc(var(--lA1) - var(--iA1));
  box-sizing: border-box;
}

.UE_1006_ListTile .horizontalList.fas .listWrapper .list {
  height: 100%;
  align-items: center;
}

.UE_1006_ListTile .horizontalList .listWrapper .listElement {
  position: relative;
  top: 0px;
  width: var(--imageSize);
  display: flex;
  flex-direction: column;
}

.UE_1006_ListTile .horizontalList.media .listWrapper .listElement,
.UE_1006_ListTile .horizontalList.mini .listWrapper .listElement {
  width: calc(100% - var(--iA1) * 2);
}

.UE_1006_ListTile .horizontalList.media .listWrapper .listElement {
  align-items: center;
  padding: 0 var(--iA1);
}

.UE_1006_ListTile
  .horizontalList.media
  .listWrapper
  .listElement
  .UE_1009_C2Cursor,
.UE_1006_ListTile .horizontalList.media .listWrapper .listElement .cover,
.UE_1006_ListTile
  .horizontalList.media
  .listWrapper
  .listElement.button:active
  .uiState {
  width: var(--imageSize);
}

.UE_1009_C2Cursor {
  position: absolute;
  height: 100%;
  width: 100%;
}

.UE_1006_ListTile .horizontalList.media .UE_1009_C2Cursor {
  height: var(--imageSize);
}

.UE_1006_ListTile .horizontalList .listWrapper .list .listElement .textPrio2 {
  width: 100%;
  margin-top: var(--textCoverMargin);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.UE_1006_ListTile .horizontalList .listWrapper .listElement .cover {
  background-size: cover;
  background-repeat: no-repeat;
  height: var(--imageSize);
  width: 100%;
}

.UE_1006_ListTile
  .horizontalList.UE_1006_03
  .listWrapper
  .listElement.button
  .button {
  height: var(--imageSize);
}

.UE_1006_ListTile .horizontalList.UE_1006_03 .listWrapper .listElement .cover {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--standardBorderRadius);
}

.UE_1006_ListTile
  .horizontalList
  .listWrapper
  .list
  .listElement
  .textPrio2:nth-child(2) {
  margin-top: var(--zA1);
}

.UE_1006_ListTile
  .horizontalList
  .listWrapper
  .list
  .listElement
  .textPrio2.title {
  margin-top: var(--textCoverMargin);
}

.uiState.primary.fullSize.borderLeft {
  z-index: 1;
}

.UE_1006_ListTile .C2Cursor {
  position: unset;
}

