/* CLASSIFICATION: CONFIDENTIAL */

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_0801_PageIndicator {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  background-color: rgb(var(--bodyColorGrey));
  margin: 0;
}

/*-------------------- .UE_0801_PageIndicator --------------------*/

.UE_0801_PageIndicator {
}

.UE_0801_PageIndicator.b10Screen .pageIndicator {
  padding-right: unset;
}
.pageIndicator {
  display: flex;
  flex-direction: row;
  margin: auto;
  padding-top: 3px;
  padding-right: var(--directAccessWidth);
}

.segment {
  width: 30px;
  height: 2px;
  margin-right: 8px;
  background-color: rgb(var(--borderGrey));
  margin-bottom: 0;
}

.segment:last-child {
  margin-right: 0;
}

.active {
  height: 5px;
  background-color: rgb(var(--audiWhite));
  margin-top: -3px;
}
