/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0703_TimeBar {
  background-color: rgb(var(--bodyColorGrey));
  height: 193px;
  margin: 20px;
}

.UE_0703_TimeBar {
}

.timeBar {
  position: relative;
  width: calc(100% - 3px);
  margin-right: 3px;
  height: 193px;
}

.time {
  position: absolute;
  top: 79px;
  right: 0px;
}

.time .timeSeparator {
  margin-left: var(--cA1);
  margin-right: var(--cA1);
  padding-left: 8px;
  padding-right: 8px;
}

.progressContainer {
  width: 100%;
  position: absolute;
  bottom: 0px;
  height: var(--minTouchSize);
}

/*----------------POPUP FRAME DESIGN--------------*/
.seekPopupContainer {
  width: auto;
  height: 72px;
  padding-left: 10px;
  padding-right: 10px;
  left: 50px;
  position: absolute;
  display: flex;
  align-items: center;
}

.seekPopupContainer.hidden {
  display: none;
}

/*----------------TOAST CONTENT--------------*/
.seekPopupContainer .Toast-Content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.seekPopupContainer .Toast-Content .dialogText {
  flex: 1;
  overflow: hidden;
  max-width: 100%;
}

.seekPopupContainer .Toast-Content .dialogText p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*----------------Slider--------------*/
.slider {
  appearance: none;
  width: 100%;
  height: 6px;
  position: absolute;
  top: 48px;
  background-color: transparent;
  background-image: linear-gradient(
    to right,
    rgb(var(--audiLightGrey)),
    rgb(var(--audiWhite))
  );
  background-repeat: no-repeat;
  cursor: pointer;
  outline: none;
}

.slider::-webkit-slider-runnable-track {
  box-shadow: none;
  border: none;
  background: transparent;
  -webkit-appearance: none;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2px;
  height: 26px;
  background: rgb(var(--audiWhite));
  cursor: pointer;
}

.progressBackground {
  position: absolute;
  top: 52px;
  left: 0px;
  width: 100%;
  height: 2px;
  background-color: transparent;
  background-image: linear-gradient(rgb(var(--audiGrey)), rgb(var(--audiGrey)));
  background-repeat: no-repeat;
  background-position: right;
}
