/* CLASSIFICATION: CONFIDENTIAL */

$climateFlyoutWidth: 380px;

#root > .T_0208_Dynamic_Overlay {
  margin: 50px;
}

.T_0208_Dynamic_Overlay {
  max-width: $climateFlyoutWidth;
  box-sizing: border-box;

  @mixin buttonLine {
    width: $climateFlyoutWidth;
    height: fit-content;
    display: flex;
    justify-content: space-evenly;
  }

  &__container {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: rgb(var(--audiBlack));

    &__firstButtonLine {
      @include buttonLine;
      margin-top: 7px;
    }

    &__stepperContainer {
      border-radius: var(--standardBorderRadius);
      background-color: var(--buttonBackgroundColor);
      margin: 6px;
    }

    &__lastButtonLine {
      @include buttonLine;
      margin-bottom: 6px;
    }
  }
}
