/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --UE_6054_barWidth: 300px;
  --UE_6054_barHeight: 17px;
  --UE_6054_rootHeight: 61px;
  --UE_6054_barSegmentSpacing: 4px;
  --UE_6054_barSegmentWidth: calc(
    (var(--UE_6054_barWidth) - 2px - var(--UE_6054_barSegmentSpacing)) / 2
  );
}

:global(#root) > .UE_6054_Fahrerzustandsschaetzung {
  background-color: rgb(var(--audiBlack));
  margin: 10px;
  width: 528px;
}

.fahrerzustandsschaetzung {
  position: relative;
  width: 528px;
  height: var(--UE_6054_rootHeight);
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------- SCALE ----------------------------------*/
.textLabels {
  position: absolute;
  width: 100%;
  height: 36px;
  top: 4px;
  left: 0px;
}
.inactive .textLabels {
  opacity: var(--disabledState);
}

.textLabels .textPrio2 {
  position: absolute;
  margin: 0;
  width: 230px;
  text-align: center;
  overflow: hidden;
}

.textLabels .textPrio2:first-child {
  left: 0px;
}

.textLabels .textPrio2:nth-child(2) {
  left: 298px;
}

/*-------------------------- MARKER --------------------------*/

.Bargraph .markerContainer {
  position: absolute;
  width: var(--UE_6054_barWidth);
  height: var(--barHeight);
  bottom: 0px;
}

.Bargraph .marker {
  position: absolute;
  background-color: rgb(var(--audiGrey));
  width: 2px;
  height: var(--barHeight);
  left: calc(var(--UE_6054_barWidth) / 2 - 1px);
}

.inactive .Bargraph .marker {
  opacity: 0.4;
}

/*--------------------------- BAR SEGMENTS ----------------------------------*/
.Bargraph {
  position: absolute;
  height: var(--UE_6054_barHeight);
  width: var(--UE_6054_barWidth);
  top: 44px;
  left: 114px;
}
/*--------------------------- CONTROL ELEMENT ----------------------------------*/
.settings {
  position: absolute;
  width: 100%;
  top: 150px;
}
