/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_5001_WelcomeGoodbye {
  position: absolute;
  top: var(--upperStatusBarHeightFPK);
  left: 0px;
  width: var(--screenWidthFPK);
  height: var(--fidMainContentHeight);
}

.T_5001_WelcomeGoodbye {
}

.welcomeGoodbye {
  width: 100%;
  height: 100%;
}

.decoratorContainer {
  position: absolute;
  width: 445px;
  height: 250px;
  left: 737px;
  top: 35px;
  /* background-color: rgb(var(--audiGrey)); */
}
.decoratorBasis {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
.decoratorColor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(
    var(--ambientcolor1)
  ); /*  remove comment when mask image is set */
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center center;
}

.distanceInfos {
  margin-left: 25px;
}

.textLine {
  position: absolute;
  width: calc(670px - var(--zA4) * 2);
  display: flex;
  justify-content: center;
  align-items: baseline;
  margin-left: var(--zA4);
  margin-right: var(--zA4);
}

.textLine:nth-child(1) {
  top: 111px;
}

.textLine:nth-child(2) {
  top: 185px;
}

.name {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.digit {
  margin-left: var(--zA8);
  margin-right: var(--zA8);
}

.distanceInfos .textLine:nth-child(1) .textPrio2 {
  max-width: 295px;
}

.distanceInfos .textLine:nth-child(2) .textPrio2 {
  max-width: 308px;
}

/********  TransportMode  **********/

.transportMode {
  position: absolute;
  width: 670px;
  height: 285px;
  top: 0px;
  right: 25px;
}

.headLine,
.textWrapper {
  position: relative;
  width: 450px;
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 110px;
}

.headLine p,
.textLines {
  position: relative;
  width: 345px;
  margin-left: 15px;
}

.headLine {
  margin-top: 20px;
}
.textWrapper:nth-child(2) {
  top: 20px;
}
.textWrapper:nth-child(3) {
  top: 42px;
}

.piktogrammRollIcon {
  position: relative;
  flex-shrink: 0;
}

.separatorLine,
.separatorLineRight {
  position: absolute;
  width: 502px;
  height: 1px;
  top: 172px;
  background-color: rgba(var(--audiGrey), 0.8);
}

.separatorLine {
  left: 110px;
}

.separatorLineRight {
  right: 85px;
}

.settings {
  width: 50%;
  position: absolute;
  top: 450px;
}
