/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --widthIconLadestationen: 40px;
  --heightIconLadestationen: 40px;
  --burgerMenuBargraphWidth: 24px;
  --listHeight: 549px;
}

:global(#root) > .T_0203_BurgerMenu {
  font-size: 0;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--audiGrey));
  /*background-image: url("../img/MIB-3 Nacht.png"); remove comment for opacity visualization*/
  width: 50%;
  height: 700px;
  margin: 20px;
}

.T_0203_BurgerMenu {
}

.burgerMenu {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  z-index: 10;
  border-color: rgba(var(--audiWhite), 0.3);
  /*border-right: 1px solid rgba(var(--audiWhite), 0.3);
  box-shadow: 0px 0px 0px 0px transparent,
    5px 0px 0px 0px rgba(var(--audiBlack), 0.75);*/
  background-color: rgb(var(--audiBlack));
}

.burgerMenu .tabbackground {
  position: absolute;
  top: 0;
  height: 110px;
  width: 100%;
}

.burgerMenu .burgerbackground {
  position: relative;
  display: flex;
  flex-direction: column;
  top: -5px;
  height: calc(100% - 114px);
  width: 100%;
}

.burgerMenu .burgerGoogleLogo {
  position: absolute;
  /* display: none; */
  flex-direction: column;
  background-color: rgba(var(--ambientBackground1), 0.9);
  height: fit-content;
  position: absolute;
  width: calc(100% - var(--listVerticalPadding) - 28px);
  bottom: var(--zA8);
  left: calc(var(--listVerticalPadding) + var(--iA1));
  padding: 10px var(--cA1);
  box-sizing: border-box;
}

.T_0203_BurgerMenu.showGoogle .burgerMenu .burgerGoogleLogo {
  display: flex;
}

.keyboardVisible .T_0203_BurgerMenu.showGoogle .burgerMenu .burgerGoogleLogo {
  bottom: calc(
    var(--inputMethodsHeight) - var(--climateBarHeight) -
      var(--dashboardMarginBottom) + var(--zA8)
  );
}

.burgerMenu .burgerGoogleLogo p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/*--------------- FILTERBAR ---------------*/

.burgerMenu .buttonLine {
  height: var(--minTouchSize);
  width: calc(100% - var(--opticalTouchReduction));
  margin-left: calc(var(--opticalTouchReduction) / 2);
}

.UE_1003_ListItemButtons .listItemButtons .button {
  min-width: var(--minTouchSize);
}

.burgerMenu .buttonLine .UE_1003_ListItemButtons .button .uiState {
  height: calc(100% + 1px);
}

.burgerMenu .buttonLine .moreButton {
  height: 100%;
  width: var(--minTouchSize);
  text-align: center;
  float: right;
  position: relative;
}

.SE_0104_Nav_More_Overlay {
  position: relative;
  float: right;
  width: calc(100% - 35px);
  top: 29px;
  right: var(--lA1);
}

.burgerMenu .buttonLine .UE_1003_ListItemButtons {
  height: 100%;
  width: calc(var(--minTouchSize) * 6);
  float: left;
  position: relative;
}

.burgerMenu .buttonLine .filterSorting {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.burgerMenu.poi .buttonLine .filterSorting {
  display: flex;
}

.burgerMenu.poi .buttonLine .filterSorting .button,
.burgerMenu.poi .buttonLine .filterSorting .actionButtonGroup,
.burgerMenu.poi .buttonLine .filterSorting .button .textIconWrapper {
  position: relative;
  display: flex;
  flex: 1;
  height: var(--minTouchSize);
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}

.burgerMenu.poi .buttonLine .filterSorting .button,
.burgerMenu.poi .buttonLine .filterSorting .actionButtonGroup,
.burgerMenu.poi
  .buttonLine
  .filterSorting
  .button
  .textIconWrapper
  .UE_0101_ActionButton {
  height: 100%;
}

.burgerMenu.poi .buttonLine .filterSorting .actionButtonGroup {
  padding: var(--uiStateMargin);
  box-sizing: border-box;
}

.burgerMenu.poi .buttonLine .filterSorting .actionButtonGroup .button.checkbox {
  width: var(--minTouchSize);
  flex: none;
  align-items: center;
  justify-content: center;
}

.burgerMenu.poi
  .buttonLine
  .filterSorting
  .actionButtonGroup
  .button.checkbox
  .UE_1101_Checkbox
  .checkboxContainer,
.burgerMenu.poi
  .buttonLine
  .filterSorting
  .actionButtonGroup
  .button.checkbox
  .UE_1101_Checkbox
  .checkboxContainer
  .checkbox {
  left: 0;
  top: 0;
  transform: none;
}

.burgerMenu.poi
  .buttonLine
  .filterSorting
  .actionButtonGroup
  .button.checkbox:before {
  content: '';
  position: absolute;
  height: calc(100% - 50px);
  width: 1px;
  background-color: rgba(var(--audiWhite), 0.5);
  left: 0;
}

.burgerMenu.poi
  .buttonLine
  .filterSorting
  .actionButtonGroup
  .button
  .textPrio2,
.burgerMenu.poi .buttonLine .filterSorting .button .textIconWrapper {
  margin: 0 var(--textButtonMargin);
}

.burgerMenu.poi .buttonLine .filterSorting > .button {
  padding: 0 var(--uiStateMargin);
}

.burgerMenu.poi .buttonLine .filterSorting .button .textIconWrapper {
  display: flex;
  flex: 1;
}

.burgerMenu.poi .buttonLine .filterSorting .button .textIconWrapper .touchIcon {
  position: relative;
  transform: none;
  left: 0;
  top: 0;
  margin-left: var(--iA1);
}

.burgerMenu.poi .buttonLine .filterSorting .button .textPrio2,
.burgerMenu.poi .buttonLine .filterSorting .button .textIconWrapper {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
  flex: 1;
}

/*--------------- T_0103_LISTVERTICAL ---------------*/

.burgerMenu .T_0103_ListVertical {
  height: calc(100% - var(--minTouchSize) - 2px);
  width: calc(100% + 1px);
  margin: var(--popupInsetWidth) 0;
  position: relative;
  flex: 1;
  display: flex;
}

.burgerMenu .T_0103_ListVertical,
.burgerMenu.detail .T_0103_ListVertical {
  flex: 1;
  overflow: hidden;
}

.burgerMenu.detail
  .UE_1002_ListItemStandard
  .textContainer
  .listIcon
  + .listIcon {
  margin-left: 0;
  padding-left: 5px;
}

/*--------------- UE_0402_TABBARMENU ---------------*/

.UE_0402_Tabbarmenu {
  height: var(--minTouchSize);
  position: relative;
  width: 100%;
  z-index: 1;
}

.burgerMenu .UE_0402_Tabbarmenu .tabbar .background {
  clip-path: polygon(
    0% 0%,
    0 calc(100% - 8px),
    calc(100% - 0px) calc(100% - 8px),
    calc(100% - 0px) 100%,
    100% 100%,
    100% 0%
  );
}

.burgerMenu .UE_0402_Tabbarmenu .tabbar .buttonContainer .button.text {
  width: fit-content;
}

.burgerMenu
  .UE_0402_Tabbarmenu
  .tabbar
  .buttonContainer
  .button.text
  .textPrio2 {
  margin: 0 var(--textButtonMargin);
}

.burgerMenu
  .UE_0402_Tabbarmenu
  .tabbar
  .buttonContainer.right
  .button:first-of-type {
  display: none;
}

.burgerMenu.alternativeRoute .SE_0037_AlternativeRoute {
  display: block;
  flex: 1;
  height: calc(100% - var(--minTouchSize) - var(--popupInsetWidth));
  width: calc(100% + 1px);
}

.burgerMenu.alternativeRoute .UE_0401_OperationPanel {
  display: block;
  width: calc(100% - 1px);
  height: var(--minTouchSize);
}

.burgerMenu.tourPlan .UE_1002_ListItemStandard .textBox1 {
  flex: unset;
}

.burgerMenu.tourPlan .UE_1002_ListItemStandard .textBox2.rightalign:last-child {
  flex: 1 1 1px;
}

.settingsConfig {
  position: absolute;
  margin-top: 50px;
  overflow-y: auto;
  height: 700px;
}

.settingsConfig .textSettingLabel.sub {
  margin-left: 80px;
}

/*--------------- Speech Diolog Detail Screen -----------------*/
.Speech_Dialog_Detail_Screen {
  width: calc(100% + 1px);
  margin: var(--popupInsetWidth) 0;
  flex: 1;
  overflow: hidden;
}

.burgerMenu .Speech_Dialog_Detail_Screen {
  width: calc(100% + 1px);
  margin: var(--popupInsetWidth) 0;
  flex: 1;
  overflow: hidden;
}

.burgerMenu.speechDialogDetailScreen .Speech_Dialog_Detail_Screen {
  display: block;
}

.Speech_Dialog_Detail_Screen .elementWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.Speech_Dialog_Detail_Screen .listContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  flex: 1;
}

.Speech_Dialog_Detail_Screen .listWrapper {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.Speech_Dialog_Detail_Screen .listContainer .list {
  display: inline-block;
  width: calc(100% - 18px);
  height: calc(100% - 1px);
  list-style-type: none;
  padding: 0 var(--listVerticalPadding);
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
.listContainer .list li:last-child {
  border-bottom: unset;
}
.line2 .textBox1 {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.ratingContainer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.ratingBoundingBoxContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 80%;
}
.ratingBoundingBoxContainer .ratingBoundingBox {
  width: 100px;
  height: 40px;
  background-color: rgb(var(--audiGreen), 0.5);
}
.ratingText {
  margin: 0 var(--hA1);
  max-width: calc(100% - 180px);
}

.ratingText + .ratingBoundingBox {
  width: 60px;
}
.Speech_Dialog_Detail_Screen .textBox,
.Speech_Dialog_Detail_Screen .textBox1 {
  max-width: 100%;
}
.lineWrapper {
  display: flex;
  width: calc(100% - 40px);
  height: 100%;
  align-items: center;
  justify-content: flex-start;
}
.lineWrapper .listIcon {
  margin-right: 20px;
}

/*--------------- NAV ONLINE INFORMATION (DOPI) ---------------*/

.itemListContainer {
  display: flex;
  width: 100%;
  height: var(--minTouchSize);
  border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
}

.items {
  flex: 1;
  max-width: calc(100% / 3);
  height: 100%;
}
.navButton {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navButton p {
  margin: 0 15px;
  max-width: calc(100% - 30px);
}

.multipleIcons {
  display: inline-block;
  align-self: center;
}

.burgerMenu .NAV_Online_Information {
  width: calc(100% + 1px);
  margin: var(--popupInsetWidth) 0;
  flex: 1;
  overflow: hidden;
}

.burgerMenu.navOnlineInformation .NAV_Online_Information {
  display: block;
}

.NAV_Online_Information .elementWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.NAV_Online_Information .listContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  flex: 1;
}

.NAV_Online_Information .listWrapper {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.NAV_Online_Information .listContainer .list {
  display: inline-block;
  width: calc(100% - 18px);
  height: calc(100% - 1px);
  list-style-type: none;
  padding: 0 var(--listVerticalPadding);
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
}

.NAV_Online_Information .listContainer .list .NAV_provider_logo {
  width: 100%;
  display: flex;
  justify-content: center;
  height: fit-content;
  margin-top: 10px;
}

.NAV_Online_Information .listContainer .list .NAV_provider_logo .logo {
  width: 164px;
  height: 40px;
  background-color: rgba(var(--boundingBoxGreenFull));
}

.NAV_Online_Information .listContainer .list .navOnlineInformationItem,
.Speech_Dialog_Detail_Screen .listContainer .list .navOnlineInformationItem,
.NAV_Online_Information .list > .UE_1002_ListItemStandard.navi {
  border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
}

.NAV_Online_Information
  .listContainer
  .list
  .navOnlineInformationItem.borderTop {
  border-top: 1px solid rgba(var(--audiGrey), 0.8);
}

.NAV_Online_Information .listContainer.fadeOutTopBottom,
.Speech_Dialog_Detail_Screen .listContainer.fadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.NAV_Online_Information .UE_1005_ListFolding .foldingWidget:not(.activated) {
  max-height: calc(var(--minTouchSize));
  will-change: max-height;
}

.burgerMenu.navOnlineInformation .yellow,
.burgerMenu.speechDialogDetailScreen .yellow {
  color: rgb(var(--audiYellow));
}

.burgerMenu.navOnlineInformation .green,
.burgerMenu.speechDialogDetailScreen .green {
  color: rgb(var(--audiGreen));
}

.burgerMenu.navOnlineInformation .red,
.burgerMenu.speechDialogDetailScreen .red {
  color: rgb(var(--audiRed));
}

.NAV_Online_Information .UE_1005_ListFolding .content,
.Speech_Dialog_Detail_Screen .UE_1005_ListFolding .content {
  height: fit-content;
}

/*--------------- CHARGING STATION ---------------*/

.NAV_Online_Information .informationLadestationen {
  display: flex;
  padding-top: var(--iA1);
  box-sizing: border-box;
  margin-right: var(--iA4);
  margin-bottom: var(--iA1);
  margin-left: var(--lA1);
  position: relative;
  justify-content: space-between;
}

.NAV_Online_Information .informationLadestationen.variant2 {
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.NAV_Online_Information .informationLadestationen p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.NAV_Online_Information
  .informationLadestationen.variant2
  .textLadestationenLeft {
  margin-left: var(--iA1);
}

.NAV_Online_Information
  .informationLadestationen
  .textLadestationenLeft
  .listIcon {
  margin-right: var(--iA1);
}

.NAV_Online_Information .informationLadestationen .textLadestationenLeft {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.textLadestationenLeft.lastOne {
  margin-bottom: 10px;
}

.NAV_Online_Information .informationLadestationen .textLadestationenRight {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  overflow: hidden;
}

.NAV_Online_Information
  .informationLadestationen.variant2
  .textLadestationenLeft {
  display: block;
  overflow: hidden;
}

.NAV_Online_Information
  .informationLadestationen.variant2
  .textLadestationenLeft
  p:last-of-type {
  margin-top: 39px;
}

.NAV_Online_Information .informationLadestationen .textLadestationen {
  width: 100%;
  display: flex;
  justify-content: center;
}

/*--------------- PICTURE VIEWER ---------------*/
.occupancyPredictionChartViewer,
.pictureViewer {
  display: flex;
  padding: var(--iA2);
  margin-left: calc(var(--iA1) + var(--lA1));
  box-sizing: border-box;
}
.pictureViewer {
  flex-direction: row;
  align-items: center;
}
.occupancyPredictionChartViewer {
  flex-direction: column;
  align-items: center;
}

.occupancyPredictionChartViewer .arrowLeft,
.occupancyPredictionChartViewer .arrowRight,
.pictureViewer .arrowLeft,
.pictureViewer .arrowRight {
  min-width: var(--minTouchSize);
  height: var(--minTouchSize);
}

.occupancyPredictionChartViewer .arrowLeft,
.NAV_Online_Information .pictureViewer .arrowLeft {
  margin-right: auto;
}

.occupancyPredictionChartViewer .arrowLeft .button,
.occupancyPredictionChartViewer .arrowRight .button,
.NAV_Online_Information .pictureViewer .arrowLeft .button,
.NAV_Online_Information .pictureViewer .arrowRight .button {
  position: relative;
  height: 100%;
}

.occupancyPredictionChartViewer .arrowRight,
.NAV_Online_Information .pictureViewer .arrowRight {
  margin-left: auto;
}

.NAV_Online_Information .pictureViewer .pictureCenter {
  width: calc(var(--listHeight) - var(--minTouchSize) - var(--iA3));
  height: calc(var(--listHeight) - var(--minTouchSize) - var(--iA3));
  background-color: rgba(var(--boundingBoxGreenFull));
  justify-self: center;
}

/*--------------- OCCUPANCY PREDICTION CHART ONLY ---------------*/

.timeControlContainer {
  display: flex;
  position: relative;
  width: 100%;
  align-items: flex-start;
}

.occupancyPredictionChart {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: calc(var(--listHeight) - 2 * var(--minTouchSize) - var(--iA3));
  justify-self: center;
  margin-bottom: var(--iA1);
}

.occupancyPredictionChartViewer .textPrio1,
.occupancyPredictionChartViewer .textPrio2,
.occupancyPredictionChartViewer .textPrio4 {
  width: 100%;
  text-align: center;
}

.arrivalTime {
  display: flex;
  width: 100%;
  height: calc(var(--lineHeightPrio1) + var(--lineHeightPrio2) + var(--zA1));
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
  padding: 0 var(--iA1);
}

.arrivalDetail {
  display: flex;
  width: 100%;
  margin-bottom: var(--zA1);
}

.arrivalDetail .textPrio2 {
  text-align: right;
  margin: 0 var(--zA1);
  width: 50%;
}

.arrivalTime .eta {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  width: 50%;
}

.arrivalTime .eta .textPrio2.time {
  text-align: right;
  margin-top: unset;
  width: 90px;
}

.arrivalTime .eta .textPrio3.am {
  margin-top: 3px;
}

.arrivalTime .eta .textPrio3.pm {
  margin-top: 10px;
  margin-left: 1px;
}

.chart {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  overflow: hidden;
}

.bargraph {
  width: var(--burgerMenuBargraphWidth);
  max-height: 100%;
  border: 1px solid rgb(var(--ambientcolor1));
  padding: 3px;
  box-sizing: border-box;
  margin: 0 calc(var(--zA1) / 2);
}

.bargraph.arrival {
  border: 1px solid rgb(var(--audiWhite));
}

.bargraph.disabled {
  opacity: var(--disabledState);
}

.bargraph .background {
  width: 100%;
  height: 100%;
  background-color: rgba(var(--ambientcolor1), 0.25);
}

.bargraph.arrival .background {
  background-color: rgba(var(--audiWhite), 0.75);
}

.timeAxis {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin-top: var(--zA8);
}

.bargraph:first-child {
  margin-left: 0;
}

.bargraph:last-child {
  margin-right: 0;
}

.timeAxis .textPrio4:first-child {
  margin-left: calc(var(--burgerMenuBargraphWidth) * 3 + var(--zA1) * 2.5);
}

.timeAxis .textPrio4:last-child {
  margin-right: calc(var(--burgerMenuBargraphWidth) * 2 + var(--zA1) * 1.5);
}
.timeAxis .textPrio4 {
  width: var(--burgerMenuBargraphWidth);
  margin: 0 calc(var(--burgerMenuBargraphWidth) + var(--zA1) * 1.5); /* half of distance between two bargraphs that have a scale (two bargraphs and three spaces between)*/
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/*--------------- OPENING HOURS ---------------*/
.NAV_Online_Information .openingHours,
.Speech_Dialog_Detail_Screen .openingHours {
  box-sizing: border-box;
  margin: var(--iA1);
  margin-left: calc(var(--hA3) + var(--iA1) + var(--hA1));
}

.NAV_Online_Information .openingHours .openingHoursLine,
.Speech_Dialog_Detail_Screen .openingHours .openingHoursLine {
  display: flex;
  justify-content: space-between;
  height: calc((var(--lineHeightPrio2) * 2) + (var(--zA2) * 2));
}

.NAV_Online_Information .openingHours .openingHoursLine p,
.Speech_Dialog_Detail_Screen .openingHours .openingHoursLine p {
  margin-top: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: var(--iA1);
  max-width: 620px;
}

/*--------------- PARKING SLOTS ---------------*/
.NAV_Online_Information .parkingSlots {
  box-sizing: border-box;
  margin: var(--iA1);
  margin-left: calc(var(--hA3) + var(--iA1) + var(--hA1));
}

.NAV_Online_Information .parkingSlots .parkingSlotsLine {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: calc((var(--lineHeightPrio2) * 2) + (var(--zA2) * 2));
}

.NAV_Online_Information .parkingSlots .parkingSlotsColumn {
  display: flex;
  flex-direction: column;
}

.NAV_Online_Information .parkingSlots .parkingSlotsColumn:nth-of-type(2n) {
  margin-left: var(--iA1);
}

.NAV_Online_Information .parkingSlots .parkingSlotsColumn p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 620px;
}

/*--------------- PRICES ---------------*/
.NAV_Online_Information .prices {
  box-sizing: border-box;
  padding: var(--iA1);
  margin-left: calc(var(--zA4) + var(--lA1));
}

.NAV_Online_Information .prices .pricesLine {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: calc((var(--lineHeightPrio2) * 2) + (var(--zA2) * 2));
  box-sizing: border-box;
}

.NAV_Online_Information .prices .pricesColumn {
  display: flex;
  flex-direction: column;
}

.NAV_Online_Information .prices .pricesColumn:nth-of-type(2n) {
  margin-left: var(--iA1);
}

.NAV_Online_Information .prices .pricesColumn p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 620px;
}

/*---------------- DETAIL SUMMARY ----------------*/

.detailSummary {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
  width: 100%;
  margin-top: var(--iA1);
  width: 100%;
}

.textIcon .listIcon {
  margin-right: var(--iA1);
}

