/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --ftwMainAreaLeftMargin: 194px; /* Android layout*/
  --ftwMainAreaRightMargin: 48px; /* Android layout*/
  --ftwLoaderHeight: 3px;
  --ftwContentWidth: 49%; /*fixed values: 936px/862px */
}

/* Standalone */

:global(#root) > .SE_0068_FirstTimeWizard {
  position: absolute;
  width: 2220px;
  height: 956px;
  overflow-y: scroll;
  overflow-x: hidden;
  margin: 0px;
}

/* SE_0068_FirstTimeWizard */

.SE_0068_FirstTimeWizard {
}

.firstTimeWizard {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--climateBarHeight));
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  background-size: 100vw 100vh;
  box-sizing: border-box;
}

/* ------------------------------ topButtonLine ----------------------------- */

.firstTimeWizard > .topButtonLine {
  display: flex;
  flex-direction: row;
  height: var(--toolbarHeightGAS);
  background-color: rgb(var(--ambientBackground1));
  align-items: center;
  padding-left: 74px; /* Android layout*/
  padding-right: var(--ftwMainAreaRightMargin); /* Android layout*/
}

.firstTimeWizard .topButtonLine .UE_0101_ActionButton {
  position: relative;
  min-width: var(--minTouchSize);
  height: var(--buttonHeightGAS);
  max-height: var(--buttonHeightGAS);
  display: flex;
  align-items: center;
  justify-content: center;
}

.firstTimeWizard .topButtonLine .UE_0101_ActionButton.back {
  min-width: var(--backButtonSizeGAS);
  width: var(--backButtonSizeGAS);
  height: var(--backButtonSizeGAS);
  max-height: var(--backButtonSizeGAS);
}

.firstTimeWizard
  .topButtonLine
  .UE_0101_ActionButton:not(.back):not(:last-of-type) {
  margin-right: var(--opticalTouchReduction);
}

.firstTimeWizard .topButtonLine .headline {
  align-self: center;
  flex: 1;
  margin-left: 75px;
  margin-right: var(--textButtonMargin);
}

/* -------------------------------- loader Gas -------------------------------- */
.firstTimeWizard .loaderGas {
  position: relative;
  width: 100%;
  top: calc(var(--ftwLoaderHeight) * -1);
  height: var(--ftwLoaderHeight);
  background-color: rgba(var(--audiWhite), 0.4);
  background-image: linear-gradient(
    to right,
    rgba(var(--ambientcolor1), 1),
    rgba(var(--ambientcolor1), 1) 35%,
    transparent 35%
  );
}

/* ----------------------------- center content ----------------------------- */

.firstTimeWizard .centerContent {
  position: relative;
  display: flex;
  flex-direction: column;
  top: calc(var(--ftwLoaderHeight) * -1);
  width: 100%;
  height: calc(100% - var(--toolbarHeightGAS));
  justify-content: center;
  align-items: center;
}

.scrollableText {
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  -webkit-mask-image: var(--listStartEndFadeout);
}
.scrollableText::-webkit-scrollbar-track {
  border: 1px solid rgba(var(--audiWhite), 0.15);
  background-clip: content-box;
  margin-top: 4px;
  margin-bottom: 4px;
}

.scrollableText p {
  margin-left: 0px;
  margin-right: var(--zA4);
  width: calc(100% - var(--zA4));
}

.scrollableText p:first-child {
  margin-top: var(--zA4);
}

.scrollableText p:last-child {
  margin-bottom: var(--zA4);
}

/* ---------------------------- welcome content ---------------------------- */

.noPrimaryUser .firstTimeWizard .centerContent,
.privacyScreen .firstTimeWizard .centerContent,
.speechScreen .firstTimeWizard .centerContent,
.settingsEULA .firstTimeWizard .centerContent {
  width: 100%;
  box-sizing: border-box;
  padding-left: var(--ftwMainAreaLeftMargin); /* Android layout*/
  padding-right: var(--ftwMainAreaRightMargin); /* Android layout*/
}
.securitySettings .centerContent,
.securitySettingsSelected .centerContent,
.bluetoothDeactivated .centerContent,
.deviceSearch .centerContent,
.sendPhoneNumber .centerContent,
.ASI .centerContent,
.deviceConnected .centerContent {
  width: 100%;
  box-sizing: border-box;
  padding-left: var(--ftwMainAreaLeftMargin); /* Android layout*/
  padding-right: var(--ftwMainAreaRightMargin); /* Android layout*/
}

.noPrimaryUser .containerNoPrimaryUser,
.privacyScreen .containerNoPrimaryUser,
.containerSettingsEULA {
