  height: 100%;
  width: 100%;
  overflow-y: auto;
}

.containerSettingsEULA .textPrio1:not(:first-of-type) {
  margin-top: var(--textButtonMargin);
}

.securitySettingsSelected .containerSecuritySettingsSelected {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.securitySettingsSelected .containerSecuritySettingsSelected p:first-child {
