/* ---------------------------- language selection content ---------------------------- */

.firstTimeWizard .centerContent .languageSelectionContent {
  display: flex;
  width: calc(
    100% - var(--ftwMainAreaLeftMargin) - var(--ftwMainAreaRightMargin)
  );
  margin-left: var(--ftwMainAreaLeftMargin); /* Android layout*/
  margin-right: var(--ftwMainAreaRightMargin); /* Android layout*/
