:global(#root) > .T_0207_InputScalableOverlay {
  position: absolute;
  height: 496px;
  width: 960px;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--ambientBackground1));
  margin: 10px;
}

:global(#root) > .T_0207_InputScalableOverlay .inputScalableOverlay {
  top: 5px;
}

.inputScalableOverlay {
  display: flex;
  position: absolute;
  width: 100%;
  height: 496px;
  flex-direction: column;
  left: 0px;
  top: 0px;
}

/* -------------------------------- titleline ------------------------------- */

.overlayTitleline {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.overlayTitleline .button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  min-width: var(--minTouchSize);
  align-self: center;
}

.overlayTitleline .button.close {
  margin-left: var(--popupInsetWidth);
}

.overlayTitleline .UE_0501_InputField {
  display: flex;
  flex: 1;
  align-items: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  overflow: hidden;
  height: var(--minTouchSize);
}

.overlayTitleline .UE_0501_InputField .interactionBackground {
  background-color: unset;
}

/* -------------------------------- UE_0506_PhoneNumericKeypad ----------------------------------------------------------------------------------------------------- */

.UE_0506_PhoneNumericKeypad .keypad {
  padding: var(--popupInsetWidth);
}

.UE_0506_PhoneNumericKeypad .keypad .keyboardLine:last-of-type {
  border-bottom: none;
}

/* ------------------------------ small screen ------------------------------ */
@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0207_InputScalableOverlay {
    width: 884px;
    height: 506px;
  }
}
