:root {
}

:global(#root) > .SE_0103_CockpitDashboardTilePID {
  background-color: rgb(var(--ambientBackground1));

  width: 100%;
  height: 400px;
}
.SE_0103_CockpitDashboardTilePID {
}

.zweiDrittel {
  background-image: url('../img/AHCP3_city-background.png');
  background-repeat: repeat-x;
  background-position: left 0px bottom 75%;
}

.dashboardCockpitTile {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.mainContainer {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  margin-left: 210px;
}

.zweiDrittel .mainContainer {
  margin-left: var(--iA2);
}

.mainContainer .textPrio1 {
  margin-top: 0px;
  margin-left: 20px;
}

.row {
  display: flex;
  justify-content: flex-start;
  width: 350px;
  margin-top: 55px;
  margin-left: 10px;
  align-items: center;
}

.zweiDrittel .row,
.zweiDrittel .row .textPrio1 {
  width: unset;
}

.row .textPrio1 {
  text-align: right;
  margin: 0 var(--hA1) 0px var(--hA1);
  align-self: baseline;
}

.row .textPrio2 {
  text-align: left;
  align-self: baseline;
}
.row .listIcon {
  position: relative;
  transform: none;
  left: 0;
  top: 0;
}

.bottomContentContainer {
  position: relative;
  display: flex;
  width: 100%;
}

.bottomContentContainer .bottomButton {
  width: 100%;
}

.bottomContentContainer .bottomButton .bottomBottonContentContainer {
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  max-width: 100%;
  width: calc(100% - 90px);
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 45px;
  padding-right: 45px;
}

.zweiDrittel
  .bottomContentContainer
  .bottomButton
  .bottomBottonContentContainer {
  justify-content: center;
}

.bottomContentContainer .bottomButton .bottomBottonContentContainer .touchIcon {
  position: relative;
  left: unset;
  top: unset;
  transform: unset;
  min-width: var(--touchIconSize);
}

.textContainer {
  position: relative;
  max-width: calc(100% - var(--textButtonMargin) - var(--iA1));
  margin-left: var(--iA1);
}

.textContainer .row {
  justify-content: flex-start;
  margin-top: var(--zA2);
  margin-left: unset;
}

.textContainer .row .textPrio2 {
  text-align: right;
  width: 87px;
  align-self: baseline;
}

.textContainer .row .textPrio2:nth-child(3) {
  margin-left: var(--iA1);
}

.row .unit {
  color: rgba(var(--audiWhite), 0.7);
  align-self: baseline;
  width: 38px;
  margin-bottom: 2px;
  margin-left: var(--zA8);
}

.SpritesheetDigits {
  margin-left: var(--hA1);
}

.row .listIcon {
  margin-right: var(--iA2);
}

.carImg {
  position: absolute;
  bottom: 22%;
  right: 10%;
  width: 777px;
  height: 310px;
}

.decWheels,
.decCar,
.decColor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
}

.decColor {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
}

.dashboardCockpitTile > .gradient {
  position: absolute;
  bottom: 90px;
  height: 120px;
  width: 100%;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0.7),
    transparent 100%
  );
}

.settings {
  position: absolute;
  top: 500px;
  width: 100%;
  color: white;
}

@media only screen and (max-width: 1768px) {
  .zweiDrittel {
    background-position: unset;
  }
}
