/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_5002_Ladescreen {
  background-color: rgb(var(--bodyColorGrey));
  width: calc(var(--screenWidthFPK) - 2px);
  height: var(--screenHeightFPK);
  border: 1px solid yellow;
  margin: 0;
  overflow: hidden;
}

.chargingScreen,
.fullContent,
.reducedContent {
  position: absolute;
  left: 0px;
  right: 0px;
  width: var(--screenWidthFPK);
  height: var(--screenHeightFPK);
}

.chargingScreen p,
.chargingScreen .listIcon {
  margin: 0px;
}

/*********  fullContent **********/

.chargingTime {
  position: absolute;
  top: 21px;
  left: 789px;
  display: flex;
  width: 342px;
  justify-content: center;
  align-items: end;
}

.chargingTime .textPrio2 {
  margin-left: 8px;
}
.chargingTime .textPrio3 {
  margin-left: 4px;
}
.chargingTime .textPrio3.time_am {
  margin-bottom: 6px;
}
.chargingTime .textPrio3.time_pm {
  margin-bottom: 2px;
  margin-left: 5px;
}

.chargingTime .textPrio2.hourMins {
  margin-top: 2px;
  margin-left: 1px;
  max-width: 100px;
}

.time_am {
  display: inline-block;
}

.time_pm {
  display: inline-block;
}
.hourMins {
  display: inline-block;
}

.UE_6041_Ladeleistung,
.UE_6041_01_Bidirektionales_Laden {
  left: 50%;
  transform: translateX(-50%);
  top: 98px;
}

.chargingCarDecorator {
  position: absolute;
  width: 400px;
  height: 120px;
  top: 266px;
  left: 760px;
}

.decoratorBasis {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
.decoratorColor {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center center;
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center center;
}

.SE_5000_Linien_Charging {
  position: absolute;
  width: 430px;
  height: 34px;
  top: 322px;
  left: 745px;
  background-image: url(../../../../SpecialElements/SE_5000_Linien_Charging/PNG/SE_5000_Linien_Charging_placeholder.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.SE_5000_Linien_Charging.AG_0204_Pulsate {
  opacity: 0;
  animation: var(--AG_0204_Pulsate);
}
.SE_5000_Linien_Charging.AG_0205_Flash {
  animation: var(--AG_0205_Flash);
}

.fpkStatusIcon.E68B_Laden_IT_Netz {
  position: absolute;
  background-image: url(../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E68B_Laden_IT-Netz.png);
  background-position: center;
  background-repeat: no-repeat;
  left: 938px;
  top: 397px;
  height: 45px;
  width: 45px;
  z-index: 1;
}

/*********  reducedContent **********/
.item {
  display: flex;
  flex-direction: column;
  width: calc(100% / 3);
  height: 100%;
  justify-content: space-between;
  align-items: center;
}
.reducedContent.showTime .item {
  width: 25%;
}
.multiline {
  width: 100%;
  height: 115px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.textLine {
  width: 100%;
  text-align: center;
}

.textLine {
  display: flex;
  justify-content: center;
  align-items: baseline;
}

.reducedContent .listIcon {
  width: 60px;
  height: 60px;
  min-width: 60px;
  min-height: 60px;
  background-image: url(../assets/defaultListIcon.png);
}

.item .textLine .textPrioA,
.item .textLine .textPrio2 {
  display: inline-block;
}

.item:not(.timeWrapper) .textLine .textPrioA,
.item.timeWrapper.am_pm .textLine .textPrioA {
  margin-right: var(--zA1);
}

.item.hour .multiline {
  height: unset;
}

.item.am_pm .textLine:last-of-type {
  align-items: unset;
}

.reducedContent {
  display: block;
}

.contentWrapper {
  top: 147px;
  position: relative;
  width: calc(100% - 130px);
  margin: 0 auto;
  /*padding: 0 65px;*/
  height: 200px;
  display: flex;
  justify-content: center;
}

.iconContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.timeWrapper .time_am {
  width: 47px;
  height: var(--lineHeightPrioA);
}
.timeWrapper .time_pm {
  width: 47px;
  height: var(--lineHeightPrioA);
  align-items: baseline;
}
.timeWrapper .hourMins {
  width: auto;
  max-width: 100px;
}
.timeWrapper.am_pm.am .time_am {
  display: inline-block;
  vertical-align: top;
  position: relative;
  top: 8px;
}
.timeWrapper.am_pm.pm .time_pm {
  display: inline-block;
  position: relative;
  top: 30px;
}
.timeWrapper.hourMins .hourMins {
  display: inline-block;
}

.chargingScreen .textPrio2.status {
  position: absolute;
  text-align: center;
  width: 750px;
  max-width: 750px;
  left: 584px;
  top: 444px;
  z-index: 1;
}

.chargingScreen .textPrio2.status.short {
  width: 580px;
  max-width: 580px;
  text-align: left;
}

/*---------- ChargingScreen Config ---------- */
.chargingScreenConfig {
  position: absolute;
  top: 600px;
  color: white;
}

.chargingBargraphConfig,
.reducedContentConfig {
  margin-top: 20px;
}

.chargingScreenConfig .reducedContentConfig .textSettingLabel {
  margin-left: unset;
}

.textPrioA + .textPrio2 {
  margin-top: 0;
}

.textPrio2 + .textPrio2 {
  margin-top: 0;
}

.textPrio1 + .textPrio2 {
  margin-top: 0;
}

.textPrio2 + .hourMins {
  margin-bottom: 2px;
}

.listIcon.power.right {
  padding-left: 5px;
  margin-left: 4px;
}

.settings {
  position: absolute;
  width: 50%;
  top: 600px;
  height: calc(100% - 620px);
  overflow: scroll;
}
