.scroller {
  position: relative;
  width: calc(100% - var(--popupInsetWidth) + 1px);
  overflow-y: hidden;
  overflow-x: hidden;
  margin-left: var(--popupInsetWidth);
  margin-bottom: var(--popupInsetWidth);
}

.inner {
  position: relative;
  width: 100%;
}

.inner.input {
  opacity: 0.4;
}

.scroller::-webkit-scrollbar-track {
  display: none;
}

.folderName {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-shrink: 0;
  flex-grow: 0;
}

.popupNoBefore::before {
  content: none !important;
  height: 0px !important;
}

.handle {
  height: 8px;
  width: 178px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.triangle {
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-right: 8px solid rgb(var(--audiWhite));
  border-bottom: 15px solid transparent;
  -webkit-transform: rotate(90deg);
  margin-right: 10px;
  margin-left: 10px;
}

.upperLine {
  width: 82px;
  height: 3px;
  top: 0;
  background-color: rgb(var(--borderGrey));
  border-radius: 10px;
  left: 0;
  clip-path: polygon(0 10%, 100% 17%, 90% 100%, 0 100%);
}

.lowerLine {
  width:82px;
  height: 3px;
  bottom: 0;
  background-color: rgb(var(--borderGrey));
  border-radius: 30px;
  left: 0;
  clip-path: polygon(0 10%, 100% 17%, 100% 100%, 10% 100%);
}

.folderTitle {
  position: relative;
  display: flex;
  width: calc(100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2));
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  justify-content: center;
  align-items: center;
  text-align: center;
}

.button {
  position: relative;
  height: 100%;
  min-width: var(--minTouchSize);
  align-self: center;
}

.close {
  margin-left: var(--popupInsetWidth);
}

.right {
  margin-right: var(--popupInsetWidth);
}

.UE_0501_InputField {
  position: relative;
  width: calc(100% - var(--minTouchSize) - (var(--popupInsetWidth) * 2));
  height: calc(var(--minTouchSize) - 1px);
}

.unHighlight {
  height: fit-content;
  width: fit-content;
}

.unHighlightSelected {
  opacity: 0.2;
}

.backgroundHighlight {
  background-color: rgba(var(--interactionBackground), 0.2);
  border-radius: var(--standardBorderRadius);
}

.background {
  z-index: 2 !important;
}

.T_0305_Intent_Choser_Popup_elementsMax2 .iconWrapper {
  width: var(--daIconSize) !important;
  height: var(--daIconSize) !important;
}

.settings {
  position: absolute;
  top: 750px;
  width: 50%;
}

@media only screen and (max-width: 1768px) {
  .settings {
    top: 682px;
    width: 50%;
  }

  .inner {
    width: calc(100% - 1px);
  }
}
