:root {
  --scoreWidth: 364px;
  --mainSportsContainerHeight: 265px;
  --mainSportsChannelNumberWidth: 142px;
  --centerContainerWidth: calc(
    2 * (var(--scoreWidth) + var(--listIconSize)) + var(--iA4)
  );
}

/*  Standalone */

:global(#root) > .SE_0008_SportsNowPlaying {
  background-color: rgb(var(--bodyColorGrey));
  height: 526px;
  width: 100%;
  position: absolute;
  margin: 8px;
}

:global(#root) > .SE_0008_SportsNowPlaying .settings {
  margin-top: 96px;
}

/* SE_0008_SportsNowPlaying */

.sportsNowPlaying {
  display: flex;
  flex-direction: column;
}

.mainSportsContainer {
  width: 100%;
  height: var(--mainSportsContainerHeight);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: var(--hA3);
}

.touchIconContainer {
  display: flex;
  flex-direction: column;
  width: var(--minTouchSize);
  height: var(--mainSportsContainerHeight);
  justify-content: flex-start;
  margin-top: calc((var(--opticalTouchReduction)) * -1);
}

.touchIconContainer .button {
  position: absolute;
  width: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: var(--minTouchSize);
}

.coverContainer {
  width: var(--mainSportsContainerHeight);
  height: var(--mainSportsContainerHeight);
  display: flex;
  justify-content: center;
  align-items: center;
}

.coverContainer .Text {
  margin-bottom: var(--iA1);
}

.coverContainer .button {
  position: absolute;
  bottom: 0%;
}
.coverContainer .button {
  height: var(--minTouchSize);
}

.coverContainer .button .btnContainerText {
  width: var(--siriusSatButtonWidth);
}

.coverContainer .playPandoraBtn .btnContainerText {
  width: 100%;
  min-width: var(--siriusSatButtonWidth);
  max-width: 390px;
}

.coverContainer .btnContainerText p {
  max-width: calc(100% - 2 * var(--textButtonMargin));
}

.cover {
  position: relative;
  height: 100%;
  width: 100%;
  justify-self: center;
  align-self: center;
  background-size: contain;
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg);
  transition: all var(--animationDuration) ease;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
}

.listIconContainer {
  width: var(--listIconSize);
  display: flex;
  flex-direction: column;
}

.centerContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: var(--centerContainerWidth);
  margin-bottom: var(--iA1);
  margin-left: var(--cA1);
  margin-right: var(--cA1);
  margin-top: -110px;
}

.lineChannel {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: var(--centerContainerWidth);
}

.channelNumber {
  max-width: var(--mainSportsChannelNumberWidth);
  text-align: right;
}

.channelName.textPrio2 {
  margin-top: unset;
  margin-left: var(--cA1);
  flex: 1;
}

.gameScore {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: var(--zA4) 0;
}

.gameTimeContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  max-width: 100%;
}

.gameTime {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: calc(100% - (var(--listIconSize) + var(--iA1)) * 2);
  margin-right: calc(var(--listIconSize) + var(--iA1));
  margin-left: var(--iA1);
}

.scoreleft {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: calc(var(--scoreWidth) + var(--listIconSize));
}

.scoreleft .scoreHome {
  margin-right: var(--iA1);
  margin-left: var(--iA1);
  display: flex;
  justify-content: flex-end;
  max-width: calc(var(--scoreWidth) - 2 * var(--iA1));
  box-sizing: border-box;
}

.scoreright {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: calc(var(--scoreWidth) + var(--listIconSize));
}

.scoreAway {
  margin-right: var(--iA1);
  margin-left: var(--iA1);
  display: flex;
  justify-content: flex-start;
  max-width: calc(var(--scoreWidth) - 2 * var(--iA1));
  box-sizing: border-box;
}

.separator {
  width: var(--iA4);
  display: flex;
  justify-content: center;
}

.teams .separator {
  width: 70px;
}

.gameScore.pending {
  max-width: 100%;
}
.pending .scoreleft {
  width: 100%;
}

.pending .scoreleft .scoreHome {
  max-width: 100%;
  margin: auto;
  width: fit-content;
}

/*--------------------------------- Satellite Button --------------------------------->*/
.satelliteContainer {
  display: flex;
  justify-content: center;
  position: relative;
  top: calc(-1 * (var(--minTouchSize) + var(--iA1) + 75px));
  flex-direction: column;
  width: 100%;
  align-items: center;
}

.satelliteContainer .playPandoraBtn {
  margin-top: 11px;
}
.satelliteContainer .button {
  position: relative;
  height: var(--minTouchSize);
}

.satelliteContainer .button .btnContainerText {
  width: var(--siriusSatButtonWidth);
}

.satelliteContainer .playPandoraBtn .btnContainerText {
  width: 100%;
  max-width: 390px;
}

.satelliteContainer .btnContainerText p {
  max-width: calc(100% - 2 * var(--textButtonMargin));
}

.liveTextContainer {
  display: flex;
  position: relative;
  height: var(--minTouchSize);
  width: var(--centerContainerWidth);
  justify-content: flex-end;
  align-self: center;
  align-items: center;
  margin-right: var(--cA1);
  top: calc(-1 * var(--iA1));
}

.satelliteContainer.oneBtnSNP .button .btnContainerText {
  width: 354px;
}

/*------------------------------------------- Extended Playback Controls ---------------------------------------------*/

.sportsNowPlaying div.SE_0049_ExtendedPlaybackControls {
  position: absolute;
  height: 0;
  width: 100%;
  bottom: 0px;
  transition: var(--AG_0202_FadeOut), var(--AG_0303_ScaleResize);
  pointer-events: none;
}

.sportsNowPlaying .epcLayer {
  position: absolute;
  display: none;
  top: -10px;
  height: calc(100% + 10px);
  width: 100%;
  pointer-events: none;
  background-color: rgba(var(--audiBlack), 0.5);
}

.T_0303_FullScreenDialog .sportsNowPlaying .epcLayer {
  top: 0px;
  height: 100%;
}

.settings {
  position: absolute;
  top: 550px;
  width: 50%;
}

@media only screen and (max-width: 1768px) {
  :root {
    --mainSportsContainerHeight: 189px;
  }

  .coverContainer .Text {
    margin-bottom: calc(var(--zA7) + var(--iA1));
  }

  .centerContainer {
    margin-top: calc(-1 * var(--zA7));
  }

  .score {
    margin: unset;
    flex: unset;
  }

  .liveTextContainer {
    margin-top: calc(-1 * var(--hA3));
  }
}

@media only screen and (min-width: 2219px) {
  .sportsNowPlaying {
    top: calc(
      50% -
        (var(--minTouchSize) + var(--hA3) + var(--mainSportsContainerHeight)) /
        2
    );
    position: relative;
  }
  .satelliteContainer {
    margin-top: 30px;
  }
  .coverContainer .button {
    position: absolute;
    bottom: -17%;
  }
}
