/* ------------------- RADIO ELEMENT ------------------- */

.radioEntry {
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 45px;
  box-sizing: border-box;
  z-index: 22;
}

.radioEntry:not(:last-of-type) {
  border-bottom: 1px solid rgba(var(--audiWhite), 0.1);
}

.UE_1102_RadioButton {
  height: 100%;
  margin-left: 45px;
}
