:global(#root) > .SE_0084_Etron_Energy_Flow {
  width: 1682px;
  height: 720px;
}

:global(#root) > .SE_0084_Etron_Energy_Flow .btnContainer {
  top: 152px;
  left: 145px;
}
:global(#root) > .SE_0084_Etron_Energy_Flow .SE_0106_Button_with_Connector {
  left: 650px;
  top: 270px;
}

.SE_0084_Etron_Energy_Flow {
  width: 100%;
  height: 100%;
}

.etronEFlowContainer {
  position: relative;
  width: 100%;
  height: 100%;
}

.btnContainer {
  position: absolute;
  height: fit-content;
  width: fit-content;
  display: flex;
  top: 90px;
  left: 188px;
  max-width: 480px;
  min-width: var(--minTouchSize);
  z-index: 1;
  visibility: hidden;
}

.SE_0106_Button_with_Connector {
  position: relative;
  top: 210px; /*200px;*/
  left: 695px; /*670px;*/
  z-index: 0;
}
.carDecorator,
.energyFlowPlaceholder,
.bodyLights,
.carDecoratorColor,
.carWheels {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
.SE_0084_Etron_Energy_Flow .etronEFlowContainer .carDecoratorColor {
  background-color: rgb(var(--ambientcolor1));
  position: absolute;
  width: 100%;
  height: 100%;

  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: contain;
  background-repeat: no-repeat;
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  background-image: unset;
}

@media only screen and (max-width: 1768px) {
  .btnContainer {
    top: 25px;
    left: 145px;
  }
  .SE_0106_Button_with_Connector {
    top: 145px;
    left: 650px;
  }
}

@media only screen and (min-width: 2219px) {
  .btnContainer {
    top: 150px;
    left: 195px;
  }
  .SE_0106_Button_with_Connector {
    top: 270px; /*238px; */
    left: 700px; /*638px;*/
  }
}

.settings {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
}
