/* CLASSIFICATION: CONFIDENTIAL */

.UE_6100_C1Cursor {
  width: 100%;
  height: 100%;
}

:global(#root) > .UE_6100_C1Cursor {
  margin: 0;
  width: 100%;
  height: 119px;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.C1Cursor {
  position: relative;
  width: 100%;
  height: calc(100% + 1px);
  border-top: 1px solid rgb(var(--borderGrey));
  border-bottom: 1px solid rgb(var(--borderGrey));
  border-right: 2px solid rgb(var(--borderGrey));
  border-left: 5px solid transparent;
  box-sizing: border-box;
  box-shadow: inset 0 0 0 3px rgba(var(--audiBlack), 1);
  z-index: 1;
  border-radius: 0px var(--standardBorderRadius) var(--standardBorderRadius) 0px;
}

.C1Cursor::before {
  position: absolute;
  content: '';
  left: -5px;
  top: -1px;
  width: 5px;
  height: calc(100% + 2px);
  background-color: rgba(var(--audiWhite), 1);
  box-shadow: 3px 0px 5px 1px rgba(var(--audiWhite), 0.2);
  border-radius: var(--standardBorderRadius) 0px 0px var(--standardBorderRadius);
}
