/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0040_Native_User_Switch {
  position: absolute;
  width: 1920px;
  height: calc(956px - var(--climateBarHeight));
  overflow-x: hidden;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  margin: 0px;
  margin-top: 1px;
}

:global(#root) > .SE_0040_Native_User_Switch .pinPad .centerContent {
  height: calc(100% - var(--minTouchSize));
}

:global(#root) > .SE_0040_Native_User_Switch .infoText + .buttonHandle {
  margin-bottom: 60px;
}

.SE_0040_Native_User_Switch .nativeUserSwitch {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.SE_0040_Native_User_Switch .nativeUserSwitch.blackBackground {
  background-color: rgb(var(--audiBlack));
}

/* ------------------------------ topButtonLine ----------------------------- */

.topButtonLine {
  display: flex;
  flex-direction: row;
}

.UE_0101_ActionButton {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

.topButtonLine .button:first-of-type {
  margin-right: var(--zA8);
}

.userConsent .topButtonLine .button:first-of-type {
  margin-left: -90px;
}

.actionButtonWrapper {
  display: flex;
  height: 100%;
  width: fit-content;
  position: relative;
  justify-self: center;
}

.button .icon {
  position: relative;
}

/* ----------------------------- center content ----------------------------- */

.centerContent {
  position: relative;
  display: flex;
  flex-direction: column;
  top: calc(50% - var(--minTouchSize));
  left: 50%;
  transform: translate(-50%, -50%);
  width: 65%;
}

.waitingScreen .textline {
  align-self: center;
  height: var(--minTouchSize);
  width: 100%;
}

.waitingScreen .textline p {
  text-align: center;
}

.waitingScreen .textPrio1 {
  align-self: center;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.waitingScreen .user {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
}

.waitingScreen .user.withIcon {
  margin-left: -60px;
}

.waitingScreen .user .image {
  border-radius: 50%;
  width: var(--coverSizeList);
  height: var(--coverSizeList);
  margin: auto 10px auto 10px;
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg);
  background-size: cover;
  background-repeat: no-repeat;
  align-self: center;
  flex-shrink: 0;
}

.waitingScreen .UE_0701_Loader {
  margin-top: 30px;
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  margin-left: 18px;
  margin-right: 18px;
}

/* ------------------------- TileList User Selection ------------------------ */

.user .userSelection {
  display: block;
  height: 100%;
  text-align: center;
}

.user .UE_1006_ListTile {
  width: 100%;
}

.user .UE_1006_ListTile .horizontalList.UE_1006_00 .listWrapper {
  padding: 0;
  width: 100%;
}

.user .userSelection > p:last-child {
  position: absolute;
  bottom: 30px;
}
.user .userSelection > p {
  width: calc(100% - 60px);
  margin-left: 30px;
}

.user .contactContainer {
  display: flex;
  justify-content: center;
  width: calc(100%);
  margin-top: 80px;
}

.user .centerContent {
  height: calc(100% - var(--minTouchSize));
  top: 0px;
  left: 0px;
  transform: translateY(0);
  transform: translateX(0);
  width: calc(100% - 60px);
  margin-left: 30px;
}

/* ---------------------------- text and buttons ---------------------------- */

.textAndButton .textAndButtonsScreen {
  display: block;
  width: 100%;
  height: 100%;
}

.textAndButton .centerContent {
  position: relative;
  display: flex;
  flex-direction: column;
  top: 0px;
  left: 50%;
  transform: translate(-50%, 0%);
  width: 65%;
  height: calc(100% - var(--minTouchSize));
}

.textAndButton .T_0104_TextAndButtons {
  position: relative;
  width: 100%;
  height: 100%;
}

.textAndButton .topButtonLine .headline {
  align-self: center;
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

/* ----------------------------- welcome screen ----------------------------- */

.welcome .centerContent {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.welcome .welcomeScreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
}

.welcome .welcomeAnimation {
  background-image: url(../img/welcome_animation_placeholder.webp);
  width: 1920px;
  height: 503px;
  position: relative;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%);
}

.welcome .privacyDisclaimer {
  width: 70%;
  bottom: 25px;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  left: 50%;
  transform: translateX(-50%);
}

.welcome .privacyDisclaimer p {
  align-self: center;
  text-align: center;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  width: calc(100% - 2 * var(--iA1));
}

.welcome .privacyDisclaimer p:not(:first-child) {
  margin-top: var(--zA2);
}

.welcome .topButtonLine .actionButtonWrapper {
  margin-left: calc(var(--minTouchSize) + var(--zA8));
}

/* ------------------------------- ok screen -------------------------------- */

.SE_0040_Native_User_Switch .nativeUserSwitch.r2fOk {
  box-sizing: border-box;
  padding: 0 calc(var(--zA4) * 3);
  background-color: rgb(var(--audiBlack));
}
.r2fOk .centerContent {
  top: 50%;
  width: 100%;
}

.r2fOk .okScreen {
  display: block;
  text-align: center;
}

/* ------------------------------- error screen -------------------------------- */

.SE_0040_Native_User_Switch .nativeUserSwitch.r2fError {
  box-sizing: border-box;
  padding: 0 calc(var(--zA4) * 3);
  background-color: rgb(var(--audiBlack));
}
.r2fError .centerContent {
  top: 50%;
  width: 100%;
}

.r2fError .errorScreen {
  display: block;
  text-align: center;
}

.r2fError .errorButton .UE_0101_ActionButton .textPrio2 {
  padding: 0 var(--textButtonMargin);
}

.r2fError .errorButton {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 50px;
}

.r2fError .errorButton .UE_0101_ActionButton {
  display: flex;
  width: auto;
  justify-content: center;
  align-items: center;
  margin-right: unset;
}

/* -------------------- android pin pad ------------------- */

.pinPad .pinPadScreen {
  width: 100%;
  height: calc(100% - 45px);
  display: flex;
  margin-top: 45px;
}

.pinPad .centerContent {
  display: flex;
  flex-direction: column;
  top: 0px;
  left: 0%;
  height: 100%;
  width: 100%;
  transform: unset;
}

.lockScreen .centerContent {
  top: var(--minTouchSize);
}

.pinPadScreen .UE_0503_NumericKeypad {
  margin-left: 60px;
  min-width: 735px;
  max-width: 735px;
  display: flex;
  flex-direction: column;
}

.pinPadScreen .UE_0503_NumericKeypad .keypad {
  height: calc(5 * var(--keyboardLineHeight));
  max-height: 100%;
}

.pinPadScreen .UE_0503_NumericKeypad .keypad .inputLine {
  height: calc(var(--keyboardLineHeight) - 1px);
  display: flex;
  /* border-bottom: 1px solid rgb(var(--audiGrey)); */
  background-color: rgba(var(--audiBlack), 0.1);
  align-items: center;
  justify-content: space-evenly;
}

.pinPadScreen .UE_0503_NumericKeypad.inactive .keyboardLine {
  opacity: var(--touchDisabledState);
}

.pinPadScreen .rightContent {
  display: flex;
  flex-flow: column;
  overflow: hidden;
  margin: 0 60px;
  flex-grow: 1;
}

.pinPadScreen .infoText {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  justify-self: center;
  flex-shrink: 0;
  height: 200px;
}

.pinPadScreen .lines.pinError {
  margin-bottom: var(--zA4);
  margin-top: var(--iA4);
}

.pinPadScreen .lines.pinError p {
  color: rgb(var(--fpkRed));
}

.pinPadScreen .infoText .lines {
  overflow: hidden;
}

.pinPadScreen .rightContent.error .infoText {
  opacity: var(--touchDisabledState);
}

.pinPadScreen .lines,
.pinPadScreen .statusText {
  padding-right: var(--cA1);
  box-sizing: border-box;
}

.pinPadScreen .statusText > p:first-of-type,
.pinPadScreen .pinError > p:first-of-type {
  margin-top: var(--zA1);
}

.pinPadScreen .infoButton {
  min-width: var(--minTouchSize);
}

.pinPadScreen .statusText {
  margin-top: var(--lA1);
  margin-bottom: var(--zA4);
  justify-self: center;
  flex-direction: column;
}

.pinPadScreen .statusText .iconLine,
.pinPadScreen .lines.pinError .iconLine {
  display: flex;
  margin-left: var(--iA1);
  align-items: center;
}

.pinPadScreen .statusText .iconLine {
  align-items: unset;
}

.pinPadScreen .statusText .iconLine .listIcon {
  margin-top: 3px;
}

.pinPadScreen .statusText p,
.pinPadScreen .lines.pinError p {
  margin-left: calc(2 * var(--iA1) + 40px);
}

.pinPadScreen .statusText .iconLine p,
.pinPadScreen .lines.pinError .iconLine p {
  margin-left: var(--iA1);
}

.pinPadScreen .loadingIndicator {
  position: relative;
  margin-top: 60px;
  margin-bottom: var(--zA4);
  height: var(--minTouchSize);
  margin-right: calc(var(--minTouchSize) + var(--cA1));
}

.pinPadScreen .loadingIndicator .currentProgress {
  margin: 0;
}

.pinPadScreen .UE_0501_InputField {
  flex: 1;
  min-height: var(--minTouchSize);
  max-height: var(--minTouchSize);
  margin-top: 60px;
  margin-bottom: var(--iA1);
  margin-right: 0px;
}

.pinPadScreen .actionButton {
  width: fit-content;
  min-height: var(--minTouchSize);
  margin-top: auto;
  margin-left: auto;
  justify-content: center;
  align-items: center;
}

.pinPadScreen .actionButton .textPrio2 {
  padding: 0 var(--textButtonMargin);
}

.buttonHandle {
  position: absolute;
  bottom: 10px;
  display: flex;
  height: fit-content;
  width: calc(
    100% - (60px * 3 + 735px)
  ); /* 735px--> pinPad width, 60px--> margins */
  margin: 0 60px;
  justify-content: flex-end;
  align-self: end;
}

.infoText + .buttonHandle {
  margin-top: var(--iA1);
}

.UE_0101_ActionButton.actionButtonBottom {
  position: relative;
  min-width: var(--minTouchSize);
  height: var(--minTouchSize);
  width: fit-content;
}

.actionButtonSpinOi {
  width: fit-content;
  margin-top: var(--cA1);
}

/* ------------------------------- user consent screen -------------------------------- */

.userConsent {
  box-sizing: border-box;
  padding: 0 calc(var(--zA4) * 3);
}
.userConsent .centerContent {
  height: calc(100% - var(--minTouchSize));
  top: 0;
  left: 0px;
  transform: translateY(0);
  transform: translateX(0);
  width: 100%;
}

.userConsent .userConsentScreen {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.userConsent .infoWrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  margin-top: 30px;
  margin-left: calc(var(--minTouchSize) - (var(--zA4) * 3) + var(--lA1));
}

.userConsent .infoText,
.userConsent .infoBullets {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}

.userConsent .infoText .lines {
  padding-right: var(--cA1);
  box-sizing: border-box;
  overflow: hidden;
}

.userConsent .lines .textPrio2 {
  text-align: left;
}

.userConsent .infoBullets .lines {
  height: 100%;
  max-height: 441px;
  width: 100%;
  overflow: auto;
  -webkit-mask-image: var(--listStartEndFadeout);
  padding-top: 10px;
  padding-bottom: 10px;
}

.userConsent .infoBullets .lines.scrollbarWithTrack::-webkit-scrollbar-track {
  border: 1px solid rgba(var(--audiWhite), 0.15);
  background-clip: content-box;
  margin-top: 4px;
  margin-bottom: 4px;
}

.userConsent .infoBullets .lines .textPrio2.bulletpoint:before {
  content: '\2022 \00a0';
}

.userConsent .infoBullets .lines .textPrio2 {
  margin-bottom: var(--iA1);
}

.userConsent .infoBullets .lines .textPrio2.bulletpoint {
  margin-bottom: 0;
}

.userConsent .infoText .infoButton {
  min-width: var(--minTouchSize);
  margin-top: calc(var(--iA4) + var(--lineHeightPrio1));
}

.userConsent .userConsentScreen .okButton {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  margin-top: 30px;
}

.userConsent .okButton .UE_0101_ActionButton {
  display: flex;
  width: auto;
  justify-content: center;
  align-items: center;
  width: 600px;
}

.userConsent .languageSelection {
  position: absolute;
  top: 0px;
  right: calc(var(--zA4) * 3);
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  width: calc(100% - var(--minTouchSize) - (var(--zA4) * 3) - var(--cA1));
}

.userConsent .languageSelectionText {
  margin-right: var(--cA1);
  align-self: center;
  justify-content: flex-end;
  width: 100%;
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
}

/* ---------------------------- language selection content ---------------------------- */

.langSelect .centerContent {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: var(--minTouchSize);
  width: calc(100% - 2 * var(--minTouchSize));
  height: calc(100% - var(--minTouchSize) - var(--ftwLoaderHeight));
  justify-content: center;
  align-items: center;
  transform: unset;
  left: unset;
  top: unset;
}

.langSelect .languageSelectionContent {
  display: flex;
  width: calc(100%);
  margin-top: 1px;
  overflow: hidden;
}

.langSelect .languageSelectionContent .T_0103_ListVertical {
  width: 100%;
}

.langSelect .topButtonLine .headline {
  align-self: center;
  flex: 1;
  margin: 0 var(--iA2);
}

/* -------------------------------- settings -------------------------------- */

.settings {
  top: 820px;
  position: absolute;
}
/*-------------------------------------------------------------------- LOW DISPLAY ----------------------------------------------------------------------*/

@media only screen and (max-width: 1768px) {
  :global(#root) > .pinPad {
    width: 1768px;
  }

  .pinPadScreen .statusText {
    margin-bottom: 20px;
  }

  .lockScreen .pinPad .centerContent {
    top: 30px;
  }
}

.is_ST_0082_ORU_Update_Running {
  visibility: hidden;
  pointer-events: none;
}
