.UE_0101_ActionButton {
  width: auto;
  min-width: var(--minTouchSize);
}

.button.groupLeft .interactionBackground {
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
  border-right-width: 0;
}
.button.groupRight .interactionBackground {
  border-radius: 0 var(--standardBorderRadius) var(--standardBorderRadius) 0;
  border-left-width: 0;
}

/* -- icon -- */

.icon {
  width: 100%;
  height: 100%;
  border: 0;
  position: unset;
  position: absolute;
}

/* -- btnContainerText -- */
/* ------------------------------- text button ------------------------------ */
.button .btnContainerText {
  /* display: none; */
}

.button.text {
  /* display: inline-table !important; */
  /* min-width: unset !important; */
}

.button.text .btnContainerText {
  position: relative;
  display: flex;
  min-width: var(--minTouchSize);
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.button.text .btnContainerText p {
  position: relative;
  margin: 0 var(--textButtonMargin);
  width: calc(100% - var(--textButtonMargin) * 2);
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.button.text.smallTextMargin .btnContainerText p {
  margin: 0 15px;
  width: calc(100% - 30px);
}
.button.text .btnContainerText .textPrio2 + .textPrio3 {
  margin-top: var(--zA2);
}
.button.text .touchIcon {
  /* display: none; */
}
.button.text.subline .btnContainerText .textPrio3 {
  /* display: block; */
}

.button.text.subline .btnContainerText {
  margin-top: 14px;
}

.button.text .btnContainerText .textPrio3 {
  /* display: none; */
}

.button.fadeOutLeft.text .btnContainerText {
  align-items: flex-end;
}

.button.fadeOutRight.text .btnContainerText {
  align-items: flex-start;
}
.topText {
  position: absolute;
  top: 11px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 100%;
}
/* -- loader -- */

.UE_0701_Loader {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 1px;
}

.UE_0701_Loader .circleProgressContainer {
  transform: scale(var(--AG_0503_Loader_Waiting_Circle_Scale));
  animation: var(--AG_0503_Loader_Waiting_Circle_Rotate_Scale);
}

/* -- sizes -- */

.UE_0101_ActionButton.small {
  width: var(--minTouchSize);
}

.UE_0101_ActionButton.medium {
  width: 300px;
}

.UE_0101_ActionButton.large,
.UE_0101_ActionButton.largeFol,
.UE_0101_ActionButton.largeFor {
  width: 600px;
}

/* -- fade out -- */

.fadeOutLeft .icon {
  left: auto;
  right: 0;
  width: var(--minTouchSize);
  position: absolute;
}

.fadeOutRight .icon {
  right: auto;
  left: 0;
  width: var(--minTouchSize);
  position: absolute;
}

.fadeOutLeft .interactionBackground,
.fadeOutLeft .uiState.borderRight {
  -webkit-mask-image: -webkit-gradient(
    linear,
    right top,
    left top,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
}

.fadeOutLeft .uiState.borderRight {
  right: var(--uiStateMargin);
  width: calc(var(--minTouchSize));
  right: var(--uiStateMargin);
  margin-right: unset;
}

.fadeOutLeft .uiState.borderRight.fullSize {
  right: 0;
}
.fadeOutRight .interactionBackground,
.fadeOutRight .uiState.borderLeft {
  -webkit-mask-image: -webkit-gradient(
    linear,
    right top,
    left top,
    color-stop(0, rgba(0, 0, 0, 0)),
    color-stop(1, rgba(0, 0, 0, 1))
  );
}
.fadeOutRight .uiState.borderLeft {
  width: var(--minTouchSize);
  left: var(--uiStateMargin);
  margin-left: unset;
}
.fadeOutRight .uiState.borderLeft.fullSize {
  left: 0;
}

.fadeOutLeft .btnContainerText {
  align-items: flex-end;
}

/* -- disabled -- */

.disabled .interactionBackground,
.disabled .touchIcon,
.disabled p,
.disabled .uiState {
  opacity: var(--touchDisabledState) !important;
}

/* -- Showcase -- */

.UE_0101_ActionButtonShowcase {
}

.UE_0101_ActionButtonShowcase .previewContainer > .UE_0101_ActionButton {
  position: relative;

  transition: width 0.2s ease;
  border: none;
  margin: 40px;
}

.previewContainer {
  display: flex;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.UE_0101_ActionButtonShowcase .UE_0101_ActionButton {
  margin: 40px;
}

.buttonContainer {
  position: relative;
  margin-left: 150px;
  display: inline-flex;
  justify-content: space-between;
  background-color: rgba(var(--audiBlack), 0.5);
  border: 1px solid rgb(var(--borderGrey));
  width: calc(100% - 350px);
}
.buttonContainer .UE_0101_ActionButton {
  margin-bottom: 0;
}
.buttonTextContainer {
  display: inline-block;
  flex-grow: 0;
}

.buttonContainer p {
  margin-top: 8px;
  margin-bottom: 10px;
  text-align: center;
}
.backgroundOverlay {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}
