/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .T_0402_Notification_Extended {
  width: 100%;
  height: 816px;
}

.notiCenter {
  width: 100%;
  height: 100%;
  position: relative;
}

.backgroundOverlay {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  height: 100%;
  background-color: rgba(var(--audiBlack), 0.9);
}

.notifications {
  width: 60%;
  height: calc(100% - 2px);
  left: 50%;
  transform: translateX(-50%);
  position: relative;
  overflow-y: scroll;
  overflow-x: hidden;
}
.notiCenter.expanded .notifications {
  background-color: rgba(var(--audiBlack), 1);
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.1),
    rgba(var(--interactionBackground), 0)
  );
  border: 1px solid rgba(var(--interactionBackground), 0.35);
  outline: var(--popupOutlineWidth) solid rgba(var(--audiBlack), 0.9);
  border-radius: var(--standardBorderRadius);
  box-sizing: border-box;
  background-repeat: no-repeat;
}

/* margins are just to see the outline of a notification*/
.T_0401_Notification {
  width: 100%;
  position: relative;
  height: fit-content;
  overflow-x: hidden;
}

.notiCenter.expanded .notification,
.T_0401_Notification .notification.groupOpen {
  background-color: unset;
  background-image: none;
  border: none;
  border-radius: unset;
  outline: none;
}

.notiCenter.expanded
  .T_0401_Notification:not(:last-of-type)
  .notification.singleLine::after {
  position: absolute;
  content: '';
  height: 1px;
  width: calc(100% + 0px);
  left: 0px;
  background-color: var(--tabbarSeparatorColor);
  bottom: -45px;
}

.notiCenter.expanded .T_0401_Notification:not(:first-of-type) .notiDivider {
  display: none;
}

.T_0401_Notification:last-of-type {
  margin-bottom: 5px;
}
/* <!--handle not defined yet, needs to be styled globally-->   */
.handle {
  position: absolute;
  bottom: 21px;
  height: 4px;
  width: 220px;
  background-color: rgb(var(--audiWhite));
  outline: 3px solid rgba(var(--audiBlack), 0.6);
  border-radius: 4px;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
}

.T_0402_Notification_Extended ::-webkit-scrollbar-thumb {
  display: none; /* Safari and Chrome */
}

.T_0402_Notification_Extended ::-webkit-scrollbar-track {
  display: none; /* Safari and Chrome */
}

.settings {
  position: absolute;
}

@media only screen and (min-width: 2219px) {
  .notifications,
  .handle {
    left: calc(50% + var(--directAccessWidth) / 2);
  }
}
