.overviewContainer {
  box-sizing: border-box;

  display: flex;
  justify-content: space-between;
  gap: 3px;
  text-align: center;
  height: 100%;
  width: 100%;
  position: absolute;
  padding: 10px 60px;
}

.content {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.overviewSeat {
  background-repeat: no-repeat;
  background-position: center center;

  width: 100%;
  height: 100%;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  z-index: 10;
}

.singleSeat {
  background-image: url(../../img/SEAT.png);
}

