/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --contentBoxWidth: 497px;
  --contentBoxHeight: 364px;
}

:global(#root) > .SE_0051_MapPopup {
  position: relative;
  height: 900px;
  width: 100vw;
  background-color: rgb(var(--audiGrey));
  margin: 8px;
}

:global(#root) > .SE_0051_MapPopup .mapPopup {
  top: 5px;
}

.SE_0051_MapPopup {
}

.SE_0051_MapPopup .mapPopup {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: calc(var(--dashboardWidth) / 2);
  height: calc(var(--dashboardHeight) - 2 * var(--buttonBorderDist) + 2px);
  left: calc(var(--directAccessWidth) + var(--buttonBorderDist) - 1px);
  top: calc(var(--statusBarHeight) + var(--buttonBorderDist) - 1px);
  border-color: rgba(var(--audiWhite), 0.3);
  box-sizing: border-box;
  overflow: hidden;
}

.contentBox,
.contentBox .mapPopup {
  width: var(--contentBoxWidth);
  height: calc(var(--contentBoxHeight) + var(--minTouchSize) + 5px);
  left: unset;
}

.contentBox.stepper,
.contentBox.stepper .mapPopup {
  width: calc(
    var(--contentBoxWidth) + (var(--minTouchSize) * 2) -
      var(--opticalTouchReduction)
  );
}

/* -------------------------------- WIDTHS AND POSITIONING ACCORDING TO CONTENT ----------------------------------------------------------------------------------------------------- */

.mapPopup.countryInfo {
  right: calc(var(--buttonBorderDist) - 1px);
  left: auto;
}

/* -------------------------------- TRAFFIC EVENTS ----------------------------------------------------------------------------------------------------- */

.mapPopup .textPrio1,
.mapPopup .textPrio2,
.mapPopup .textPrio3 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

/* -------------------------------- titleline ------------------------------- */

.mapPopup .overlayTitleline {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.overlayTitleline .button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: var(--minTouchSize);
  min-width: var(--minTouchSize);
  align-self: center;
}

.mapPopup .overlayTitleline .button.close {
  margin-left: var(--popupInsetWidth);
}

.mapPopup .overlayTitleline .button.right {
  margin-right: var(--popupInsetWidth);
}

.mapPopup .overlayTitleline .button.text {
  width: fit-content;
}

.mapPopup .overlayTitleline .titelLine {
  display: flex;
  flex: 1;
  align-items: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  width: calc(
    100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2) -
      (var(--popupInsetWidth) * 2)
  );
  overflow: hidden;
}

/* --------------------------------- Content -------------------------------- */

.trafficEvents {
  width: calc(100% - var(--zA4) * 2);
  height: calc(100% - var(--zA4) - var(--popupInsetWidth));
  padding-top: var(--zA4);
  padding-left: var(--zA4);
  padding-right: var(--zA4);
  padding-bottom: var(--popupInsetWidth);
  display: flex;
  flex-direction: column;
}

.trafficEvents .listIcon {
  width: 50px;
  height: 50px;
}

.mapPopupHeadline {
  display: flex;
  margin-bottom: var(--iA4);
}

.mapPopupHeadline p {
  margin-left: var(--iA1);
}

.mapPopupSecondHeadline {
  display: flex;
  margin-bottom: var(--zA6);
}

.laneInfo {
  height: 40px;
  background-color: grey;
  margin-top: var(--zA2);
  flex-grow: 0;
  flex-shrink: 0;
}

.mapPopupDivider {
  border-top: 1px solid rgba(var(--audiWhite), 0.3);
  margin-top: var(--zA8);
  margin-bottom: var(--zA8);
}

.mapPopupSource {
  position: absolute;
  bottom: var(--popupInsetWidth);
  display: flex;
  align-items: center;
  align-self: flex-end;
  max-width: calc(50% - var(--zA4));
}

.icon1,
.icon3,
.arrow {
  margin-right: var(--iA1);
}

.icon2 {
  margin-left: var(--iA1);
}
.mapPopupSecondHeadline,
.mapPopupSecondHeadlineExtended {
  align-items: center;
}

/* --------------------------------- layout1 -------------------------------- */

.mapPopup
  .trafficEvents.layout1
  .mapPopupSecondHeadline
  .textPrio2:nth-of-type(2) {
  margin-left: 0px;
}

/* --------------------------------- layout2 -------------------------------- */

.mapPopup
  .trafficEvents.layout2
  .mapPopupSecondHeadline
  .listIcon:nth-of-type(1) {
  margin-left: 0px;
}

/* --------------------------------- layout21 -------------------------------- */

.trafficEvents.layout21 .mapPopupSecondHeadlineExtended {
  display: flex;
  margin-left: 55px;
  margin-bottom: var(--zA6);
}

.trafficEvents.layout21 .mapPopupSecondHeadline {
  margin-bottom: initial;
}

.trafficEvents.layout21 .mapPopupSecondHeadline .icon1 {
  margin-left: 0px;
}

/* --------------------------------- layout3 -------------------------------- */

.trafficEvents.layout3 .mapPopupSecondHeadline .icon1 {
  margin-left: 0px;
}

.trafficEvents.layout3 .icon3 {
  padding-left: 5px;
  margin-left: 0;
}

/* --------------------------------- layout4 -----1-------------------------- */
.trafficEvents.layout4 .icon1 {
  margin-left: var(--iA1);
}

/* -------------------------------- T_0103_ListVertical ----------------------------------------------------------------------------------------------------- */

.T_0103_ListVertical {
  position: relative;
  height: calc(100% - var(--minTouchSize) - var(--popupInsetWidth) * 2);
  width: calc(100% + 1px);
  overflow: hidden;
}

.mapPopup > .T_0103_ListVertical .listWrapper .elementWrapper .list {
  padding-left: var(--popupInsetWidth);
  padding-right: calc(var(--popupInsetWidth) + 4px);
  width: calc(100% - var(--popupInsetWidth) * 2);
}

.mapPopup
  > .T_0103_ListVertical
  .listWrapper
  .elementWrapper
  .list::-webkit-scrollbar-track {
  display: none;
}

/* --------------------------------- Content Box -------------------------------- */

.contentBox .mapPopup .contentBoxContainer {
  display: flex;
  position: relative;
  width: calc(100% - 2 * var(--popupInsetWidth));
  height: 100%;
  justify-content: center;
  align-items: flex-start;
  left: var(--popupInsetWidth);
}

.SE_0051_MapPopup.contentBox .mapPopup .contentBoxContainer .contentBox {
  background-color: rgba(var(--boundingBoxGreen));
  width: calc(100% - var(--opticalTouchReduction));
  height: calc(100% - (var(--opticalTouchReduction) / 2));
}

.SE_0051_MapPopup.contentBox.stepper
  .mapPopup
  .contentBoxContainer
  .contentBox {
  width: calc(100% - 2 * var(--minTouchSize));
}

.contentBox.stepper .mapPopup .contentBoxContainer .UE_1303_Stepper_Decorator {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
}

.contentBox.stepper
  .mapPopup
  .contentBoxContainer
  .UE_1303_Stepper_Decorator
  .stepperDecorator
  .imagesContainer {
  display: none;
}

.SE_0051_MapPopup.contentBox.stepper
  .mapPopup
  .contentBoxContainer
  .UE_1303_Stepper_Decorator
  .UE_0202_FloatingContainer
  .fullHeight {
  height: calc(100% - 2px);
}

/* ------------------------------ small screen ------------------------------ */

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0051_MapPopup {
    width: 1524px;
    height: 542px;
  }
}

/* --------------------------------- SETTINGS -------------------------------- */

.settings {
  position: absolute;
  top: 700px;
  width: 100vw;
}

.settings .sub {
  margin-left: 90px;
}
