/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --seatMassageButtonAreaSpace: 40px;
  --bckgrndPicWidthBig: 780px;
  --sideMarginSeats: 370px;
  --sideMarginSeatsSmall: 210px;
  --bckgrndPicWidthSmall: 543px;
  --floatingButtonWrapperMinWidth: 794px;
  --floatingButtonWrapperMaxWidth: 1598px;
  --floatingButtonWrapperMinWidthLowRes: 722px;
  --floatingButtonWrapperMaxWidthLowRes: 892px;
  --minThreeStateButtonWidth: 216px;
  --seatButtonAreaSpace: var(--buttonBorderDist);
  --SeatButtonWrapperHeight: calc(357px + var(--seatButtonAreaSpace));
}

/*  Standalone */

:global(#root) > .ST_0012_SeatMenu {
  position: absolute;
  width: 1800px;
  height: 697px;
  margin: 0;
  /* background-color: rgb(var(--audiBlack)); */
  /* background-image: var(--backgroundSpot); */
}

:global(#root) > .ST_0012_SeatMenu .popupWrapper {
  left: 0px;
  top: 0;
  height: 100%;
}

/* ST_0012_SeatMenu */

/** root **/
.seatMenu {
  height: 100%;
  width: 100%;
  position: absolute;
}

.ST_0012_SeatMenu .isSeatSelected .square {
  background-color: rgb(var(--ambientcolor1));
}

@media only screen and (max-width: 1920px) {
  :global(#root) > .ST_0012_SeatMenu {
    width: 1800px;
    height: 578px;
  }
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0012_SeatMenu {
    height: 450px;
    width: 1648px;
  }
}

@media only screen and (min-width: 2219px) {
  :global(#root) > .ST_0012_SeatMenu {
    height: 697px;
    width: 1800px;
  }
}

.settings {
  position: absolute;
  top: 700px;
  height: 257px;
  width: 800px;
  overflow: scroll;
}
