/* ----------------------- Decorators ----------------------- */

.seatContainer {
  display: flex;
  justify-content: space-between;
  height: 571px;
  top: 64px;
  position: absolute;
  pointer-events: none;
  margin: 0 300px;
  width: calc(100% - 2 * 300px);
}

.seatLeft {
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  width: 488px;
  position: relative;
  margin-left: 40px;
}

.seatRight {
  height: 100%;
  width: 488px;
  position: relative;
  top: 50%;
  transform: scaleX(-1) translateY(-50%);
  margin-right: 40px;
}

.seatDecorator {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  position: absolute;
  background-image: url(../../img/Decorator_SingleSeat.png);
  background-size: contain;
}

/* ----------------------- Hatchings and Highlights ----------------------- */

.highlightsContainer .seatHatching {
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 1;
  transition: opacity 0.1s ease-in-out;
}

.highlightsContainer .seatHighlight {
  background-color: rgb(var(--ambientcolor1));
  mask-size: contain;
  -webkit-mask-size: contain;
  mask-repeat: no-repeat;
  -webkit-mask-repeat: no-repeat;
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 1;
  transition: opacity 0.1s ease-in-out;
}

.notSelectable .seatHighlight {
  background-color: unset;
}

.selectable .back .seatHatching {
  background-image: url(../../img/ST_0012_SeatMenu_hatching_seat_backrest.png);
}

.selectable .seat .seatHatching {
  background-image: url(../../img/ST_0012_SeatMenu_hatching_seat_side.png);
}

.selectable .central .seatHatching {
  background-image: url(../../img/ST_0012_SeatMenu_hatching_seat.png);
}

.active .back .seatHighlight {
  mask-image: url(../../img/SeatAdjustment_back_active.png);
  -webkit-mask-image: url(../../img/SeatAdjustment_back_active.png);
}

.active .seat .seatHighlight {
  mask-image: url(../../img/SeatAdjustment_seat-side_active.png);
  -webkit-mask-image: url(../../img/SeatAdjustment_seat-side_active.png);
}

.active .central .seatHighlight {
  mask-image: url(../../img/SeatAdjustment_central_active.png);
  -webkit-mask-image: url(../../img/SeatAdjustment_central_active.png);
}

/* ----------------------- Touchpoints ----------------------- */

.touchPoint {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
  align-items: center;
}

.touchPoint.boundingBox {
  background-color: rgba(var(--boundingBoxGreen));
}

.back .touchPoint {
  top: 36%;
  left: 37%;
}

.seat .touchPoint {
  top: 64%;
  left: 30%;
}

.central .touchPoint {
  top: 60%;
  left: 59%;
}

.touchPoint.restricted {
  opacity: 0.4;
}

.SE_0106_Button_with_ConnectorBody .SE_0106_Button_with_Connector {
  transform: translate(-50%, -50%);
}

/* ----------------------- Arrows ----------------------- */

.arrowContainer {
  position: absolute;
  width: 660px;
  height: 571px;
  z-index: 1;
  left: -80px;
}

.arrow,
.arrowShadow {
  position: absolute;
  width: 100%;
  height: 100%;
}

.active .arrow {
  background-color: rgb(var(--ambientcolor1));
}

.selectable .back .arrow.enlarge {
  background-image: url(../../img/Back_selectable_enlarge.png);
}

.selectable .back .arrow.reduce {
  background-image: url(../../img/Back_selectable_reduce.png);
}

.active .back .arrow.enlarge {
  mask-image: url(../../img/Back_active_enlarge-fill.png);
  -webkit-mask-image: url(../../img/Back_active_enlarge-fill.png);
}

.active .back .arrowShadow.enlarge {
  background-image: url(../../img/Back_active_enlarge-shadow.png);
}

.active .back .arrow.reduce {
  mask-image: url(../../img/Back_active_reduce_fill.png);
  -webkit-mask-image: url(../../img/Back_active_reduce_fill.png);
}

.active .back .arrowShadow.reduce {
  background-image: url(../../img/Back_active_reduce_shadow.png);
}

.selectable .seat .arrow.enlarge {
  background-image: url(../../img/seat_selectable_enlarge.png);
}

.selectable .seat .arrow.reduce {
  background-image: url(../../img/seat_selectable_reduce.png);
}

.active .seat .arrow.enlarge {
  mask-image: url(../../img/seat_active_enlarge_fill.png);
  -webkit-mask-image: url(../../img/seat_active_enlarge_fill.png);
}

.active .seat .arrowShadow.enlarge {
  background-image: url(../../img/seat_active_enlarge_shadow.png);
}

.active .seat .arrow.reduce {
  mask-image: url(../../img/seat_active_reduce_fill.png);
  -webkit-mask-image: url(../../img/seat_active_reduce_fill.png);
}

.active .seat .arrowShadow.reduce {
  background-image: url(../../img/seat_active_reduce_shadow.png);
}

.selectable .central .arrow.enlarge {
  background-image: url(../../img/cuschion_selectable_enlarge.png);
}

.selectable .central .arrow.reduce {
  background-image: url(../../img/cuschion_selectable_reduce.png);
}

.active .central .arrow.enlarge {
  mask-image: url(../../img/cuschion_active_enlarge_fill.png);
  -webkit-mask-image: url(../../img/cuschion_active_enlarge_fill.png);
}

.active .central .arrowShadow.enlarge {
  background-image: url(../../img/cuschion_active_enlarge_shadow.png);
}

.active .central .arrow.reduce {
  mask-image: url(../../img/cuschion_active_reduce_fill.png);
  -webkit-mask-image: url(../../img/cuschion_active_reduce_fill.png);
}

.active .central .arrowShadow.reduce {
  background-image: url(../../img/cuschion_active_reduce_shadow.png);
}

/* ----------------------- Media Query ----------------------- */

@media only screen and (max-width: 1920px) {
  .seatContainer {
    margin: 0 200px;
    width: calc(100% - 2 * 200px);
  }

  .back .touchPoint {
    left: 38%;
  }

  .seat .touchPoint {
    left: 35%;
  }

  .central .touchPoint {
    left: 61%;
  }
}
