/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --paddingLineItems: 9px;
  --borderThickness: 1px;
  --textInputHeight: calc(
    (var(--keyboardLineHeight) * 5) + (var(--paddingLineItems) * 2) +
      (var(--borderThickness) * 10)
  );
  --numericKeypadStandaloneWidth: 973px;
  --numericKeypadStandaloneHeight: 392px;
  --minWidth_KeyboardButtonStandalone: 95px;
  --keyboardGap: 28px;
  --keyboardWidth: calc(
    (100% - var(--keyboardGap) - 12px - 32px - 40px) / 14 * 11 + 10px + 16px
  );
  --numpadWidth: calc(
    (100% - var(--keyboardGap) - 12px - 32px) / 14 * 3 + 2px + 16px
  );
  --inputMethodsHeight: calc(5 * var(--keyboardLineHeight));
  --inputMethodsReducedHeight: calc(
    var(--inputMethodsHeight) - var(--keyboardLineHeight)
  );
  --lpeKeyWidth: 132px;
}

:global(#root) > .T_0112_InputMethods {
  margin: 0;
  width: 1800px;
  height: fit-content;
  position: absolute;
}

.T_0112_InputMethods .hidden {
  display: none !important;
}

.T_0112_InputMethods .inputMethods {
  position: relative;
  width: calc(100% - (var(--popupOutlineWidth) * 2) - 2px);
  height: fit-content;
  flex-direction: column;
  margin-left: var(--popupOutlineWidth);
  margin-right: var(--popupOutlineWidth);
  border-bottom: 0px;
}

.T_0112_InputMethods .inputMethods .textInputWrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: auto;
}

.wrapper {
  background-color: rgba(var(--audiWhite), 0.17);
  height: calc(var(--touchIconSize));
  width: calc(var(--touchIconSize));
  position: relative;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  border-radius: 50%;
}
.wrapper.wrapperClicked {
  background-color: rgba(var(--audiWhite), 1);
}
.circularIcons {
  height: 26px;
  width: 26px;
  position: relative;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  border: 2px solid rgb(var(--audiWhite));
  border-radius: 50%;
}
.circularIcons.activated {
  border: 2px solid rgb(var(--audiBlack));
}

.T_0112_InputMethods .inputMethods > .keyboard {
  position: relative;
  width: 100%;
  height: 100%;
}

.T_0112_InputMethods .inputMethods.keyboard > .keyboard,
.T_0112_InputMethods .inputMethods.numPad > .numPad,
.T_0112_InputMethods .inputMethods.hwr > .hwr {
  display: inherit;
}

/*----------------- KEYBOARD -----------------*/
.T_0112_InputMethods .inputMethods .textInputWrapper .UE_0502_Keyboard {
  width: 100%;
}
.T_0112_InputMethods.numericKeypad
  .inputMethods
  .textInputWrapper
  .UE_0502_Keyboard {
  width: var(--keyboardWidth);
}

.T_0112_InputMethods .UE_0503_NumericKeypad {
  width: var(--numpadWidth);
}

.oneInfotainmentTopBar {
  margin-top: 15px;
}

.oneInfotainmentTopBar .oneInfotainmentIcons {
  width: calc(100% - 90px);
  height: 100%;
  margin: 0 45px 15px 45px;
}

.oneInfotainmentTopBar .oneInfotainmentIcons .toggleBar {
  justify-content: space-between;
}

.oneInfotainmentTopBar .inputFieldWrapper {
  position: relative;
  height: var(--minTouchSize);
  width: calc(100% - 8px);
  margin-left: 4px;
  margin-right: 4px;
}
.oneInfotainmentTopBar .inputFieldWrapper .UE_0501_InputField {
  position: absolute;
  height: 100%;
  width: calc(100% - 82px);
  margin: 0 41px 0px 41px;
}

.oneInfotainmentTopBar
  .inputFieldWrapper
  .UE_0501_InputField
  .interactionBackground {
  background-color: unset;
}

/*----------------- NUMPAD -----------------*/
.T_0112_InputMethods .inputMethods.numPad {
  justify-content: center;
  flex-direction: row;
}
.T_0112_InputMethods .inputMethods.numPad > .numPad {
  height: calc(var(--inputMethodsHeight) - var(--keyboardLineHeight));
}

.T_0112_InputMethods .inputMethods.numPad .textInputWrapper {
  justify-content: center;
}

.T_0112_InputMethods .inputMethods .numPad .UE_0503_NumericKeypad {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--numericKeypadStandaloneWidth);
}

.T_0112_InputMethods .inputMethods .numPad .UE_0503_NumericKeypad .keypad {
  width: 100%;
}

/*----------------- HWR -----------------*/
.T_0112_InputMethods .inputMethods .hwr {
  position: relative;
  height: calc(var(--inputMethodsHeight));
  width: 100%;
  display: flex;
  justify-content: center;
}

.T_0112_InputMethods .inputMethods .hwr .UE_0503_NumericKeypad {
  height: fit-content;
  border-left: 1px solid rgb(var(--audiGrey));
}

.T_0112_InputMethods .inputMethods .hwr .UE_0504_HWR {
  width: calc(100% - var(--numpadWidth));
  position: relative;
}

.T_0112_InputMethods .inputMethods .hwr .UE_0504_HWR .buttonsWrapper {
  width: calc(100% - 200px);
}

.T_0112_InputMethods .inputMethods .hwr .handWritingContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
}

.T_0112_InputMethods .inputMethods .hwr .UE_0505_Wordproposals {
  width: 100%;
  background-color: rgb(var(--audiBlack));
}

.T_0112_InputMethods
  .inputMethods
  .hwr
  .UE_0202_FloatingContainer
  .floatingContainer {
  min-height: var(--keyboardLineHeight);
  width: 100%;
}

.T_0112_InputMethods .inputMethods .hwr .UE_0505_Wordproposals .listContainer,
.T_0112_InputMethods
  .inputMethods
  .keyboard
  .UE_0505_Wordproposals
  .listContainer {
  border-left: none;
  border-right: none;
}

.SE_0094_Chip .chip .text {
  padding: 15px 20px !important;
}

/*----------------- WORDPROPOSALS -----------------*/
.T_0112_InputMethods .inputMethods .UE_0505_Wordproposals {
  position: relative;
}

