/*----------------- SAFETY ALERT -----------------*/
.T_0112_InputMethods .inputMethods .safetyAlert {
  position: absolute;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  background-color: rgba(var(--audiBlack), 0.98);
  overflow: hidden;
  z-index: 1;
}

.T_0112_InputMethods .inputMethods .safetyAlert p {
  max-width: calc(100% - (var(--textButtonMargin) * 2));
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0 var(--textButtonMargin);
}

.T_0112_InputMethods .inputMethods .safetyAlert .button {
  position: relative;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  width: fit-content;
  margin-top: 50px;
}

.T_0112_InputMethods .inputMethods.safetyAlert .safetyAlert {
  display: flex;
}

/*-----LPE-----*/

.T_0112_InputMethods.LPE .UE_0502_Keyboard .keyboardLine,
.T_0112_InputMethods.LPE .UE_0503_NumericKeypad,
.T_0112_InputMethods.LPE .UE_0505_Wordproposals {
  opacity: var(--touchDisabledState);
}

.SE_0081_LongPressExtension .button {
  width: var(--lpeKeyWidth) !important;
}

.SE_0081_LongPressExtension.O_Extension {
  right: 266px;
  top: -163px;
}

.SE_0081_LongPressExtension.O_Extension.twoLines {
  top: -282px;
}

/*---------------------- Cursor Control --------------------------*/

.cc_overlay {
  position: absolute;
  bottom: 0;
  height: calc(5 * var(--keyboardLineHeight));
  width: 100%;
  background-color: rgba(var(--audiBlack), 0.8);
  clip-path: polygon(
    0% 0%,
    100% 0%,
    100% 100%,
    47.6% 100%,
    47.6% 80.4%,
    20.8% 80.4%,
    20.8% 100%,
    0% 100%
  );
  z-index: 1;
}

.ccCenter {
  position: absolute;
  top: 28px;
  left: 470px;
  max-width: 600px;
}

.ccLeft {
  position: absolute;
  top: 0;
  left: 8px;
  width: 148px;
  height: calc(5 * var(--keyboardLineHeight));
  background-color: var(--buttonBackgroundColor);
}

.ccRight {
  position: absolute;
  top: 0;
  right: 8px;
  width: 440px;
  height: calc(5 * var(--keyboardLineHeight));
  background-color: var(--buttonBackgroundColor);
}

.cc_overlay.cc_SpacebarPressed .ccLeft,
.cc_overlay.cc_ContinuousScroll_Right .ccLeft {
  border-right: 3px dashed rgba(var(--audiWhite), 0.2);
}

.cc_overlay.cc_SpacebarPressed .ccRight,
.cc_overlay.cc_ContinuousScroll_Left .ccRight {
  border-left: 3px dashed rgba(var(--audiWhite), 0.2);
}

.cc_overlay.cc_ContinuousScroll_Left .ccLeft {
  border-right: 4px solid rgb(var(--audiWhite));
  border-radius: var(--standardBorderRadius);
  background-image: -webkit-linear-gradient(
    right,
    rgba(var(--audiWhite), var(--uiStateGradientOpacity)) 0%,
    rgba(var(--audiWhite), 0) 100%
  );
}
.cc_overlay.cc_ContinuousScroll_Right .ccRight {
  border-left: 4px solid rgb(var(--audiWhite));
  border-radius: var(--standardBorderRadius);
  background-image: -webkit-linear-gradient(
    left,
    rgba(var(--audiWhite), var(--uiStateGradientOpacity)) 0%,
    rgba(var(--audiWhite), 0) 100%
  );
}

.ccLeft .touchIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E514_keyboard_swipe_left.svg);
  height: 75px;
  width: 75px;
  left: 60px;
}

.ccRight .touchIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E515_keyboard_swipe_right.svg);
  height: 75px;
  width: 75px;
  left: 129px;
}

@media only screen and (max-width: 1768px) {
  :root {
    --lpeKeyWidth: 122px;
  }

  .SE_0081_LongPressExtension.O_Extension {
    right: 243px;
  }
}

/*------------------------------------------- SETTINGS ---------------------------------------------*/
.settings {
  position: absolute;
  top: 750px;
  width: 100%;
  height: 400px;
  overflow-y: scroll;
}
