#root > .SE_0083_ReadAndReplyScreen {
  width: 1682px;
  height: 487px;
  background: rgb(var(--bodyColorGrey));
}

.SE_0083_ReadAndReplyScreen {
  height: 100%;
  width: 100%;
  &__mail {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;

    &__UE_1005_ListFolding {
      width: calc(
        100% - (var(--listVerticalPadding) * 4) - var(--opticalTouchReduction) -
          2px
      );
      height: fit-content;
      border-bottom: 1px solid rgba(var(--audiWhite), 0.4);
      background-color: rgba(var(--interactionBackground), 0.15);
      box-sizing: border-box;
      margin-left: calc(
        var(--listVerticalPadding) * 2 + (var(--opticalTouchReduction) / 2) +
          2px
      );
      margin-right: calc(var(--listVerticalPadding) * 2);
      border-radius: var(--standardBorderRadius);
    }

    &__UE_0501_InputField {
      width: calc(100% - (var(--listVerticalPadding) * 4 + 2px));
      height: var(--minTouchSize);
      margin-left: calc(var(--listVerticalPadding) * 2 + 2px);
      margin-right: calc(var(--listVerticalPadding) * 2);
      margin-top: 4px;
    }
    &__textBlock {
      margin: var(--iA2)
        calc(var(--lA1) + var(--zA5) + var(--listVerticalPadding)) var(--iA2)
        var(--textButtonMargin);
    }
  }

  &__settings {
    display: flex;
    flex-direction: column;
    margin-top: 20px;
  }
}

@media only screen and (max-width: 1768px) {
  #root > .SE_0083_ReadAndReplyScreen {
    width: 1530px;
    height: 308px;
  }
}
