.interactiveLine {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -100%);
  width: 900px;
  height: 41px;
  background-color: rgba(255, 255, 0, 0.25);
}

.interactiveLine .cidIcon {
  width: 42px;
  min-width: 42px;
  height: 41px;
  background-image: url('./img/CID-Icons_40x40\ _\ 34x34\ \(dummy\).png');
}

.interactiveLine p {
  margin: 0 0 2px 0;
}

.interactiveLine .twoLineText {
  margin-left: -3px;
}

.interactiveLine .gapBox {
  width: 7px;
  height: 41px;
  background-color: rgb(32, 117, 128);
}

.interactiveLine .gapBox2 {
