  margin-top: var(--iA1);
  width: 900px;
  height: 41px;
  background-color: rgba(255, 255, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
}

.bcIconsGroup {
  display: flex;
}

.bcIcon {
  width: 63px;
  height: 63px;
  background-color: rgba(var(--boundingBoxGreen));
}

.msgBorderWrapper .bcIcon:nth-child(odd) {
