/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --vpsCarWidth: 760px;
  --vpsCarHeight: 355px;
  --vpsBorderWidth: 3px;
  --vpsCurbViewWidth: 360px;
  --vpsScreenHeight: 956px;
  --vpsScreenWidth: 1920px;
  --vpsIpaCarWidth: 977px;
  --vpsIpaCarHeight: 388px;
  --rardarOffset: 75px;
}

:global(#root) > .SE_0016_Parking_VPS {
  position: absolute;
  width: var(--vpsScreenWidth);
  height: var(--vpsScreenHeight);
  margin: 0;
}

.bodyWrapper {
  position: absolute;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--audiBlack));
  width: 100%;
  height: 100%;
}

.vpsParking {
  width: calc(100% - var(--opsViewWidth));
  margin-left: var(--opsViewWidth);
  height: 100%;
  position: absolute;
  z-index: 10;
  border-left: var(--vpsBorderWidth) solid rgb(var(--audiBlack));
  background-color: rgb(var(--audiBlack));
  background-size: 100vw 100vh;
  background-position: calc(-1 * var(--opsViewWidth)) 0;
}

.parking_VPS_b10Screen,
.parking_VPS_b10Screen .vpsParking {
  width: calc(
    100vw - var(--opsWidth) - var(--directAccessWidth) - var(--vpsBorderWidth)
  );
}
.parking_VPS_b10Screen .vpsParking {
  margin-left: calc(var(--opsWidth) + var(--directAccessWidth));
}

.vpsParking.wide {
  width: calc(
    100% - var(--minTouchSize) - var(--directAccessWidth) +
      var(--opsButtonLineBorderWidth)
  );
  margin-left: calc(
    var(--minTouchSize) + var(--directAccessWidth) -
      var(--opsButtonLineBorderWidth)
  );
  height: 100%;
  position: relative;
  z-index: 10;
  border-left: none;
  background-position: 0px;
}

.parking_VPS_b10Screen .vpsParking.wide {
  width: calc(
    100vw - var(--directAccessWidth) - var(--opsButtonLineBorderWidth)
  );
  margin-left: calc(var(--directAccessWidth) + var(--opsButtonLineBorderWidth));
}

/*--------------- tabbar ---------------*/
.vpsParking .UE_0402_Tabbarmenu {
  position: relative;
  height: 120px;
}

.vpsParking.wide .UE_0402_Tabbarmenu {
  margin-left: calc(
    var(--opsViewWidth) - (var(--minTouchSize) * 2) -
      var(--opsButtonLineBorderWidth)
  );
  width: calc(
    100% - (var(--opsViewWidth) - (var(--minTouchSize)) * 2) -
      var(--vpsBorderWidth) + var(--opsButtonLineBorderWidth)
  );
  border-left: var(--vpsBorderWidth) solid rgb(var(--audiBlack));
}

.vpsParking .UE_0402_Tabbarmenu .tabbar {
  width: 100%;
  padding: unset;
}

.vpsParking .UE_0402_Tabbarmenu .tabbar .background {
  position: absolute;
  background-size: 100vw 100vh;
  background-position: calc(-1 * var(--opsViewWidth)) 0;
  -webkit-clip-path: polygon(
    0% 0%,
    0 calc(100% - 8px),
    100% calc(100% - 8px),
    100% 100%,
    100% 0%
  );
  width: 100%;
  height: 100%;
  left: 0px;
}

.vpsParking .UE_0402_Tabbarmenu .tabbar .separator {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--tabbarSeparatorColor);
  bottom: 9px;
}

.vpsHeadline {
  height: var(--opsTabbarButtonHeight);
  width: calc(100% - var(--buttonBorderDist) * 2);
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  overflow: hidden;
  margin-left: var(--buttonBorderDist);
  margin-right: var(--buttonBorderDist);
}

.infoIcon {
  position: relative;
  height: var(--opsTabbarButtonHeight);
  width: var(--minTouchSize);
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  box-sizing: border-box;
  padding-bottom: 10px;
  display: flex;
}

.vpsHeadline .button {
  height: var(--opsTabbarButtonHeight);
  display: flex;
}

.trajectoryRecordingContainer {
  height: var(--opsTabbarButtonHeight);
  width: var(--minTouchSize);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}
.trajectoryRecordingContainer p {
  width: fit-content !important;
}

.vpsHeadline .textWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
  min-width: var(--minTouchSize);
  flex: 1;
}

.vpsHeadline .textWrapper .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  max-width: calc(100% - (var(--iA1) * 2));
  min-width: var(--minTouchSize);
  height: 100%;
  margin: 0 var(--iA1);
}

.vpsHeadline .textWrapper p {
  width: 100%;
}

.UE_0402_Tabbarmenu .progressBar {
  background-color: unset;
  position: absolute;
  height: 6px;
  top: 106px;
}

.vpsView .buttonLine.top .btnTextWrapper {
  display: flex;
  flex-direction: row;
}

.buttonLine.top .text.distance {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background-color: rgba(var(--audiBlack), 0.4);
  margin-left: var(--buttonBorderDist);
  padding-left: var(--zA1);
  padding-right: var(--zA1);
  height: var(--touchIconSize);
  border-radius: var(--standardBorderRadius);
}

.buttonLine.top .text.distance .listIcon {
  margin-left: var(--zA1);
}

.buttonLine.top p.distance {
  text-align: right;
  flex-shrink: 0;
  margin-left: var(--zA1);
  width: 59px;
}

.buttonLine.top p.parkUnit {
  text-align: left;
  flex-shrink: 0;
  margin-left: var(--zA1);
  margin-right: var(--zA1);
  margin-top: 0;
  width: 34px;
}

.vpsHeadline .textPrio2.secondLine {
  display: block;
}

/*--------------------- UGDO -----------------*/
.vpsParking.garage .UE_0402_Tabbarmenu .tabbar .vpsHeadline {
  justify-content: flex-start;
}

.garageButtonWrapper {
  display: flex;
  width: 100%;
}

.vpsParking.garage .UE_0402_Tabbarmenu .tabbar .vpsHeadline .garageDoor {
  display: flex;
  flex: 1 1 0;
  width: 0;
}
.vpsParking.garage
  .UE_0402_Tabbarmenu
  .tabbar
  .vpsHeadline
  .garageDoor
  .button {
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  padding-bottom: unset;
}

.vpsParking.garage
  .UE_0402_Tabbarmenu
  .tabbar
  .vpsHeadline
  .garageDoor
  .touchIcon {
  left: calc(var(--minTouchSize) / 2 - var(--touchIconSize) / 2);
  top: unset;
  transform: unset;
}

.vpsParking.garage
  .UE_0402_Tabbarmenu
  .tabbar
  .vpsHeadline
  .garageDoor
  .textBlock {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  overflow: hidden;
  width: calc(100% - var(--textButtonMargin) - var(--minTouchSize));
  margin-right: var(--textButtonMargin);
  margin-left: var(--minTouchSize);
}

.vpsParking.garage
  .UE_0402_Tabbarmenu
  .tabbar
  .vpsHeadline
  .garageDoor
  .textBlock
  p {
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: left;
  font-family: var(--fontLight);
}

/*--------------------- content -----------------*/
.vpsContent,
.noCamera {
  display: flex;
  position: relative;
  height: calc(100% - var(--opsTabbarHeight));
  width: 100%;
  top: -8px;
  justify-content: center;
  align-items: center;
}

.vpsParking .boundingBox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--touchIconSize);
  height: var(--touchIconSize);
  background-color: rgb(var(--boundingBoxGreen));
}

.vpsParking .boundingBoxFull {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(var(--boundingBoxGreenFull));
}

.vpsParking .boundingBoxCar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(var(--bodyColorGrey), 0.3);
}

.vpsContent .button {
  position: relative;
  height: var(--minTouchSize);
  /* width: var(--minTouchSize); */
  min-width: var(--minTouchSize);
}

.vpsContent .boundingBoxContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.noCameraBackground {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  background-color: rgb(var(--audiBlack));
}
.noCamera .boundingBox {
  position: absolute;
  width: calc(var(--touchIconSize) * 2);
  height: calc(var(--touchIconSize) * 2);
}

.vpsCamera {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  background-image: url(../img/VPS-background.webp);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.carPlaceholder,
.birdsEyeBackground {
  display: block;
}
.birdsEyeBackground {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  background-image: url(../img/VPS-background-2.webp);
  background-position: center;
  background-size: cover;
}

.carPlaceholder {
  position: absolute;
  width: var(--vpsCarWidth);
  height: var(--vpsCarHeight);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.vpsView {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* Use the KASS-Pfeile svg from SE_0015_Parking_OPS/02_PSD */
/* The "PlaceholderArrowxxx" is just a bounding box */
.placeholderArrow {
  position: absolute;
  width: 142px;
  height: 121px;
  top: 176px;
  background-color: rgb(var(--sideAssistRed));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
}

.placeholderArrow.left {
  -webkit-mask-image: url(../img/SE_0016_Parking_VPS_arrowLeft.png);
  left: 20px;
}

.placeholderArrow.right {
  -webkit-mask-image: url(../img/SE_0016_Parking_VPS_arrowRight.png);
  right: 20px;
}

.vpsParking.kassActive .vpsContent .vpsView .placeholderArrow {
  display: block;
}
.vpsParking.kassInactive .vpsContent .vpsView .placeholderArrow {
  display: block;
}
.vpsParking.kassInactive .vpsContent .vpsView .placeholderArrow.left {
  /* Use the KASS-Pfeile svg from SE_0015_Parking_OPS/02_PSD */
  -webkit-mask-image: url(../img/SE_0016_Parking_VPS_arrowLeftInactiv.png);
}
.vpsParking.kassInactive .vpsContent .vpsView .placeholderArrow.right {
  /* Use the KASS-Pfeile svg from SE_0015_Parking_OPS/02_PSD */
  -webkit-mask-image: url(../img/SE_0016_Parking_VPS_arrowRightInactiv.png);
}

/*--------------------------- Car Touchpoints ---------------------------*/
.carTouchpoints {
  width: calc(var(--vpsCarWidth) + 10px + 2 * var(--minTouchSize));
  height: calc(var(--vpsCarHeight) + 2 * var(--minTouchSize));
  position: absolute;
}

.carTouchpoints .boundingBoxContainer {
  position: absolute;
}

.carTouchpoints .boundingBoxContainer.left {
  bottom: 0%;
}

.carTouchpoints .boundingBoxContainer.center {
  top: 50%;
  transform: translate(0%, -50%);
}

.carTouchpoints .boundingBoxContainer.right {
  top: 0%;
}

.carTouchpoints .boundingBoxContainer.front {
  left: 0%;
}

.carTouchpoints .boundingBoxContainer.roof {
  top: 50%;
  left: 58%;
  transform: translate(-50%, -50%);
}

.carTouchpoints .boundingBoxContainer.rear {
  right: 0%;
}

.carTouchpoints .boundingBoxContainer.door {
  left: 45%;
}

/*--------------------------- button lines ---------------------------*/
.vpsView .buttonLine {
  position: absolute;
  height: var(--minTouchSize);
  width: calc(100% - 2 * var(--buttonBorderDist));
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-left: var(--buttonBorderDist);
  margin-right: var(--buttonBorderDist);
}
.vpsView .buttonLine.top {
  top: var(--buttonBorderDist);
}
.vpsView .buttonLine.top .cleaningButton {
  margin-left: auto;
  margin-right: var(--buttonBorderDist);
}
.vpsView .buttonLine.bottom {
  bottom: var(--buttonBorderDist);
  justify-content: unset;
}
.bottom__btn {
  bottom: var(--buttonBorderDist);

  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-57%);
}
.bottom__btn__width {
  width: 430px;
}

.vpsView .buttonLine.parkingStepper {
  height: calc(3 * var(--listItemHeightDataPicker));
  width: 120px;
  align-items: flex-start;
}
.vpsView .buttonLine.vpsDwnCtrBtn {
  justify-content: center;
  column-gap: var(--buttonBorderDist);
}

.vpsView .buttonLine .button {
  background-color: rgba(var(--audiBlack), 0.5);
  border-radius: var(--standardBorderRadius);
}

.vpsView .egoCar {
  display: block;
  position: relative;
  height: var(--opsCarHeightSmall);
  width: var(--opsCarWidthSmall);
}

.vpsView .egoSplines {
  position: absolute;
  bottom: 207px;
  left: 0px;
  height: 335px;
  width: 210px;
}

.vpsView .buttonLine .button.continue {
  display: flex;
  min-width: calc(var(--minTouchSize) - 2 * var(--textButtonMargin));
  width: unset;
  padding: 0 var(--textButtonMargin);
}

.vpsView .buttonLine .button .text {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.vpsView .buttonLine .button .text p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/*---------------------- curbview -------------------*/
.vpsParking.curbView .vpsContent .vpsCurbView {
  display: flex;
}
.vpsParking .vpsContent .vpsCurbView {
  position: absolute;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.vpsParking .vpsContent .vpsCurbView .vpsCurbCamera {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../img/vpsCurbLeft.webp);
  background-position: center;
}
.vpsParking .vpsContent .vpsCurbView .vpsCurbCamera.right {
  background-image: url(../img/vpsCurbRight.webp);
}

.vpsParking .vpsContent .vpsCurbView .vpsCurbCar {
  position: relative;
  height: 100%;
  min-width: var(--vpsCurbViewWidth);
  max-width: var(--vpsCurbViewWidth);
  display: flex;
  align-items: center;
  justify-content: center;
}

.vpsCurbView .vpsCurbCar .curbCarPlaceholder {
  position: relative;
  width: var(--opsCarWidth);
  height: var(--opsCarHeight);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/*---------------------- IPA ReplacementView -------------------*/

.vpsIPARepl {
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.radarCar {
  height: 100%;
  width: 1177px;
  position: absolute;
}

.vpsIPARepl .parkingspace {
  position: absolute;
  height: 469px;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(../img/SE_0016_Parking_VPS_NAR_background.png);
  bottom: 0px;
}

.vpsIPARepl .decoratorWheels,
.vpsIPARepl .decoratorCar {
  position: absolute;
  width: var(--vpsIpaCarWidth);
  height: var(--vpsIpaCarHeight);
  right: 0px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position-x: center;
  pointer-events: none;
  top: 50%;
  transform: translateY(-50%);
}

.vpsIPARepl .radarSensor {
  display: inline;
  position: absolute;
  background-repeat: no-repeat;
  background-size: contain;
  pointer-events: none;
  left: 0px;
}

.vpsCapaParkout {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.vpsCapaParkout .carPlaceholderSmall {
  position: absolute;
  width: var(--opsCarWidthSmall);
  height: var(--opsCarHeightSmall);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.vpsCapaParkout .toggleButtonContainer {
  display: flex;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  height: calc(
    var(--opsCarHeightSmall) + 2 *
      (var(--minTouchSize) + var(--buttonBorderDist))
  );
  flex-direction: column;
}

.vpsCapaParkout .toggleButtonRow {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
}

.vpsIPARepl .radarSensor.behind {
  width: 588px;
  height: 171px;
  top: calc(50% - 171px + var(--rardarOffset));
  background-image: url(../img/SE_0016_Parking_VPS_NAR_Sensor_Lbehind.png);
}

.vpsIPARepl .radarSensor.front {
  width: 588px;
  height: 218px;
  top: calc(50% + var(--rardarOffset));
  background-image: url(../img/SE_0016_Parking_VPS_NAR_Sensor_Lfront.png);
}

/*---------------------- settings -------------------*/
.settings {
  position: absolute;
  top: 1000px;
  width: 100%;
}

/*-------------------------------- small screen --------------------------------*/
@media only screen and (max-width: 1768px) {
  :root {
    --vpsCarWidth: 655px;
    --vpsCarHeight: 306px;
    --vpsScreenHeight: 828px;
    --vpsScreenWidth: 1768px;
    --vpsIpaCarWidth: 842px;
    --vpsIpaCarHeight: 334px;
    --rardarOffset: 62px;
  }

  .placeholderArrow {
    top: 135px;
  }

  .vpsIPARepl .parkingspace {
    height: 416px;
  }

  .vpsIPARepl .decoratorWheels,
  .vpsIPARepl .decoratorCar {
    right: 20px;
  }

  .vpsIPARepl .radarSensor.behind {
    width: 522px;
    height: 152px;
    top: calc(50% - 152px + var(--rardarOffset));
  }

  .vpsIPARepl .radarSensor.front {
    width: 522px;
    height: 193px;
    top: calc(50% + var(--rardarOffset));
  }
}

/*-------------------------------- small screen --------------------------------*/
@media only screen and (min-width: 2219px) {
  :root {
    --vpsScreenWidth: 2220px;
  }
}
