:global(#root) > .SE_5011_RDK_RS {
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthKB);
  height: var(--cardContentHeight);
  margin: 0;
}

.SE_5011_RDK_RS {
}

.RDK {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  position: relative;
}

.RDK .UE_5011_Topview {
  height: 250px;
  width: 200px;
  top: 10px;
  position: absolute;
}

.RDK .leftPart,
.RDK .rightPart {
  flex-direction: column;
  height: 100%;
  position: relative;
  width: 156px;
}

.RDK .centerPart {
  position: relative;
  height: 100%;
  width: 200px;
}

.RDK .frontLeftValues,
.RDK .backLeftValues,
.RDK .frontRightValues,
.RDK .backRightValues {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
}

.RDK .frontLeftValues,
.RDK .frontRightValues {
  margin-top: 18px;
}
.RDK .backLeftValues,
.RDK .backRightValues {
  margin-top: 66px;
}

.RDK .numberColumn {
  width: 88px;
  overflow: hidden;
}
.RDK .unitColumn {
  width: 54px;
  overflow: hidden;
}
.RDK .numberColumn,
.RDK .unitColumn {
  display: flex;
  flex-direction: column;
}

.RDK .numberColumn {
  align-items: flex-end;
  margin-right: var(--zA1);
}

.RDK .unitColumn {
  align-items: flex-start;
}

.RDK .unitColumn p:first-of-type {
  padding-top: 8px;
}

.RDK .unitColumn p {
  padding-top: 2px;
}

.RDK .textPrio1.grey,
.RDK .textPrio2.grey {
  color: rgb(var(--fpkGrey));
}

.RDK .centerPart .greyArrow {
  position: absolute;
  background-image: url('../img/Pfeil_vorne_rechts.png');
  background-position: center;
  background-repeat: no-repeat;
  width: 18px;
  height: 20px;
}

.RDK .centerPart .greyArrow.left {
  transform: scaleX(-1);
}

.RDK .centerPart .greyArrow.front.left,
.RDK .centerPart .greyArrow.front.right {
  top: 51px;
}
.RDK .centerPart .greyArrow.back.left,
.RDK .centerPart .greyArrow.back.right {
  top: 199px;
}
.RDK .centerPart .greyArrow.front.left,
.RDK .centerPart .greyArrow.back.left {
  left: 7px;
}
.RDK .centerPart .greyArrow.front.right,
.RDK .centerPart .greyArrow.back.right {
  right: 7px;
}

.RDK .centerPart .wheelCheck {
  position: absolute;
  width: 14px;
  height: 34px;
  display: inline;
  background-color: rgba(var(--fpkGrey), 0.3);
  border: solid 1px rgb(var(--fpkGrey));
}

.RDK .centerPart .wheelCheck.front.left,
.RDK .centerPart .wheelCheck.front.right {
  top: 43px;
}
.RDK .centerPart .wheelCheck.back.left,
.RDK .centerPart .wheelCheck.back.right {
  top: 190px;
}
.RDK .centerPart .wheelCheck.front.left,
.RDK .centerPart .wheelCheck.back.left {
  left: 51px;
}
.RDK .centerPart .wheelCheck.front.right,
.RDK .centerPart .wheelCheck.back.right {
  right: 51px;
}

.RDK .centerPart .fpkCommonIcon {
  position: absolute;
  left: calc(100% / 2 - (var(--fpkCommonIconSize) / 2));
  top: calc(100% / 2 - (var(--fpkCommonIconSize) / 2));
  height: var(--fpkCommonIconSize);
  width: var(--fpkCommonIconSize);
  background-color: rgba(var(--boundingBoxGreenFull));
}

.RDK.frontLeftGreen .centerPart .wheelCheck.front.left,
.RDK.frontRightGreen .centerPart .wheelCheck.front.right,
.RDK.backLeftGreen .centerPart .wheelCheck.back.left,
.RDK.backRightGreen .centerPart .wheelCheck.back.right {
  background-color: rgba(var(--FIDtelltaleGreen), 0.3);
  border: solid 1px rgb(var(--FIDtelltaleGreen));
}
.RDK.frontLeftOrange .centerPart .wheelCheck.front.left,
.RDK.frontRightOrange .centerPart .wheelCheck.front.right,
.RDK.backLeftOrange .centerPart .wheelCheck.back.left,
.RDK.backRightOrange .centerPart .wheelCheck.back.right {
  background-color: rgba(var(--fpkOrange), 0.3);
  border: solid 1px rgb(var(--fpkOrange));
}
.RDK.frontLeftRed .centerPart .wheelCheck.front.left,
.RDK.frontRightRed .centerPart .wheelCheck.front.right,
.RDK.backLeftRed .centerPart .wheelCheck.back.left,
.RDK.backRightRed .centerPart .wheelCheck.back.right {
  background-color: rgba(var(--fpkRed), 0.3);
  border: solid 1px rgb(var(--fpkRed));
}

.RDK.frontLeftGreen p.front.left,
.RDK.frontRightGreen p.front.right,
.RDK.backLeftGreen p.back.left,
.RDK.backRightGreen p.back.right {
  color: rgb(var(--FIDtelltaleGreen));
}
.RDK.frontLeftOrange p.front.left,
.RDK.frontRightOrange p.front.right,
.RDK.backLeftOrange p.back.left,
.RDK.backRightOrange p.back.right {
  color: rgb(var(--fpkOrange));
}
.RDK.frontLeftRed p.front.left,
.RDK.frontRightRed p.front.right,
.RDK.backLeftRed p.back.left,
.RDK.backRightRed p.back.right {
  color: rgb(var(--fpkRed));
}

.settings {
  margin-top: 50px;
  width: 1000px;
}
