/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --Dialog_minWidth: 500px;
  --Dialog_maxWidth: 1300px;
  --Dialog_height: 474px;
}

:global(#root) > .T_0302_Dialog {
  width: var(--Dialog_maxWidth);
  height: var(--Dialog_height);
  margin: 8px;
}

:global(#root) > .T_0302_Dialog.increasedHeight,
:global(#root) > .T_0302_Dialog.increasedHeight .dialogPopup {
  height: fit-content;
  position: relative;
}

:global(#root) > .T_0302_Dialog .dialogPopup {
  position: absolute;
  width: var(--Dialog_maxWidth);
  height: var(--Dialog_height);
}

.dialogPopup {
  position: relative;
  width: 100%;
  height: 100%;
}

.T_0302_Dialog.increasedHeight {
  height: fit-content;
}

/*----------------POPUP FRAME DESIGN--------------*/
.dialogPopupContainer {
  width: 100%;
  height: 100%;
  flex-direction: column;
}

/*----------------POPUP CONTENT--------------*/
.dialogContent {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--popupContentPadding);
  height: calc(100% - var(--minTouchSize));
  box-sizing: border-box;
}

.dialogText {
  flex: 1;
  overflow: hidden;
  max-width: 100%;
}

.dialogText.textCenter p {
  text-align: center;
}

.variant1 > .decorator {
  width: calc(var(--coverSize) / 2);
  height: calc(var(--coverSize) / 2);
  margin-left: 80px;
  border-radius: 50%;
  background-color: greenyellow;
  border: 1px solid rgb(var(--audiWhite));
  box-shadow: 0 0 0 6px rgba(var(--ambientBackground1), 0.2);
  align-self: center;
}

.UE_0401_OperationPanel {
  width: 100%;
  height: var(--minTouchSize);
  position: relative;
}

.naviDashboardImage {
  margin-left: 60px;
}

/*------------------ settings -----------------*/

.settings {
  position: absolute;
  top: calc(var(--Dialog_height) + 100px);
}

