/*-------------------- VARIANTS --------------------*/
/*---------- variant1 ----------*/
.variant1:not(.T_0302_Dialog) {
  width: 100%;
  display: flex;
  align-items: center;
}

/*---------- variant2 ----------*/
.T_0302_Dialog.variant2 .dialogContent {
  padding: unset;
}
.variant2 {
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.titleMarginVariant2 {
  display: flex;
  justify-content: center;
  margin: 0 100px;
}

.variant2.largeIcon .titleMarginVariant2 {
  margin: 0 140px;
}

.variant2 .textPrio1 {
  text-align: center;
  margin-top: calc(var(--popupInsetWidth) + 14px);
  margin-bottom: 14px;
}
.providerIconContainer {
  width: fit-content;
  position: absolute;
  top: 0;
  right: 0;
  padding-left: var(--cA1);
  padding-right: var(--cA1);
  padding-top: calc(var(--popupInsetWidth) + 17px);
  padding-bottom: 18px;
}
.providerIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  background-color: rgba(var(--boundingBoxGreenFull));
}
.variant2.largeIcon .providerIcon {
  width: 200px;
}
.textContainer {
  flex: 1;
}
.variant2 .textContainer {
  padding: var(--popupContentPadding);
  display: flex;
  align-items: center;
  justify-content: center;
}
.dialogDivider {
  position: relative;
  left: 0;
  height: 1px;
  width: calc(100%);
  background-color: rgba(var(--audiWhite), 0.6);
}
.textContainer .textPrio2,
.variant4 .textContainer .textPrio1 {
  position: relative;
  text-align: center;
}

.UE_0401_OperationPanel .interactionBackground {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-radius: 0px;
}

/*---------- variant3 ----------*/
.variant3 {
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.variant3 .textContainer {
  position: relative;
  flex: none;
}
.variant3 .textContainer > .textPrio1 {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-align: center;
}

.UE_1101_Checkbox {
  position: relative;
  height: var(--minTouchSize);
  margin-top: 20px;
}

/*---------- variant4 ----------*/
.variant4 {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.titleWrapper {
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
}

.variant4 .listIcon {
  display: inline-block;
  position: relative;
  top: calc(50% - 3px);
  transform: translateY(-50%);
  margin-right: var(--iA1);
}

.UE_0601_Slider {
  width: 100%;
}
.UE_0601_Slider .sliderBox {
  height: var(--minTouchSize);
  background-color: unset;
  background-image: none;
}
.UE_0601_Slider .headline,
.UE_0601_Slider .button,
.UE_0601_Slider .textLabels {
  display: none;
}

/*--------------- Variant 5 ------------------*/

.T_0302_Dialog.variant5 .dialogContent {
  padding: unset;
}
.variant5 {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.titleMarginVariant5 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 100px;
  height: 75px;
  flex-shrink: 0;
}

.dialogDividerVariant5 {
  position: relative;
  left: 0;
  height: 2px;
  width: calc(100%);
  background-color: var(--tabbarSeparatorColor);
}
.redSeperator {
  position: relative;
  left: calc(var(--opticalTouchReduction));
  height: 100%;
  width: calc(100% - (var(--opticalTouchReduction) * 2));
  background-color: rgba(var(--audiRed), 0.7);
}

.textArea {
  box-sizing: border-box;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 30px 60px;
  gap: 30px;
}
.bulletTextContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.bullet {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 5px 0;
}
.bullet:first-child {
  margin-top: 0;
}
.bullet:last-child {
  margin-bottom: 0;
}
.bulletText {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 50%;
}
.bullet .textPrio2 {
  margin: 0 10px;
}

.paraText p {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: break-spaces;
}

.UE_0401_OperationPanel .Text {
  top: unset;
  transform: unset;
}

/*---------- Anzeigelogik Varianten ----------*/

.variant1,
.variant2,
.variant3,
.variant4 {
  display: flex;
  justify-content: center;
}

:global(#root) > .T_0302_Dialog .increasedHeight {
  height: 530px;
}
