.folSubContainer {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.folContainer.tts.lg .folSubContainer {
  gap: 4px;

  flex-direction: row;
  max-width: 100%;

  margin-bottom: 2px;
  align-items: end;
}

/* .fol-speedometer {
  transform: translateY(-5.5px);
} */

.folContainer {
  position: absolute;
  bottom: 0;
}

.folContainer.left {
  left: 150px;
}
.folContainer.left--fol {
  left: 253px;
}
.folContainer.left--lg {
  left: 192px;
}

.folContainer.right {
  right: 150px;
}
.folContainer.right--fol {
  right: 253px;
}
.folContainer.right--lg {
  right: 192px;
}

.folContainer.lg {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.folDefault {
  display: flex;
  width: 143px;
  height: 45px;
  background-color: rgba(255, 255, 0, 0.5);
  justify-content: center;
}

.folContainer.tts.lg .folDefault {
  margin-left: 10px;
  min-width: 143px;
  margin-right: 10px;
}

.folTexts {
  display: flex;
  align-items: end;
  height: 100%;
  margin-left: 2px;
}

.folTexts p + p {
  margin-left: 3px;
}
.folTexts p {
  line-height: 0.7;
  overflow: visible;
  margin-bottom: 5px;
}

.followIconBB {
  display: flex;
  width: 49px;
  height: 100%;
  justify-content: end;
  align-items: end;
  background-color: rgba(255, 0, 0, 0.5);
  margin-left: -9px;
}

.followIcon {
  width: 45px;
  height: 45px;
  margin-right: 4px;
  background: url('../../assets/follow_arrows\ \(dummy\).png') no-repeat;
  background-position: bottom;
  background-size: 45px 45px;
  align-self: flex-end;
}

.folTts {
  display: flex;
  align-self: flex-end;
  /*  overflow: hidden; */
  background-color: rgba(255, 255, 0, 0.5);
  width: 357px;
  justify-content: center;
}
.tts.lg .folTts {
  width: 202px;
}

.roadIconBB {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 88px;
  height: 38px;
  background-color: rgba(255, 0, 0, 0.5);
  z-index: 1;
  margin-left: 9px;
}
.tts.lg .roadIconBB {
  margin-left: 9px;
}

.folTts p {
  padding-left: 6px;
  margin-left: -13px;
}

.folContainer.tts.lg .folTts p {
  padding-left: 78px;
  margin-left: -84px;
}

.roadIcon {
  width: 65px;
  height: 36px;
  background-image: url('../../assets/roadIcon\ \(dummy\).png');
  background-position: center;
  background-repeat: no-repeat;
}

.folLg {
  display: flex;
  height: 35px;
  background-color: rgba(255, 255, 0, 0.5);
}

.laneGuidanceIcon,
.laneGuidanceIcon2 {
  height: 100%;
  background-repeat: no-repeat;
}

.laneGuidanceIcon {
  width: 40px;
  background-image: url('../../assets/laneguidance\ \(dummy\).png');
}

.laneGuidanceIcon2 {
  width: 44px;
  background-image: url('../../assets/laneguidance2\ \(dummy\).png');
}

.laneGuidanceIcon::after,
.laneGuidanceIcon2::after {
  content: '';
  position: absolute;
  width: 44px;
  height: 35px;
  background-color: rgba(255, 0, 0, 0.5);
}

.folTlc {
  position: absolute;
  display: flex;
  left: 250px;
  align-items: baseline;
  bottom: 0;
}

.folContainer.lc_fol.left {
  left: 150px;
  transform: unset;
}

.folContainer.lc_fol.right {
  left: unset;
  transform: unset;
  right: 150px;
}
