/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --alphabeticalScrollbar_indexWidth: 55px;
  --alphabeticalScrollbar_cursorSize: 64px;
  --alphabeticalScrollbar_popoverWidth: 96px;
  --alphabeticalScrollbar_indexFontSize: 14px;
  --alphabeticalScrollbar_cursorBorderWidth: 4px;
  --alphabeticalScrollbar_verticalMargin: 6px;
}

:global(#root) > .UE_0803_AlphabeticalScrollbar {
  position: absolute;
  display: flex;
  width: 100%;
  height: 554px;
  margin: 0;
  margin-top: 50px;
  padding: 0;
  align-items: flex-start;
  justify-content: center;
  overflow-y: scroll;
}

:global(#root) > .UE_0803_AlphabeticalScrollbar .indexPopover {
  opacity: 1;
}

.UE_0803_AlphabeticalScrollbar {
  position: absolute;
  right: 4px;
  top: 0;
  pointer-events: none;
  user-select: none;
  height: 73px;
}

.UE_0803_AlphabeticalScrollbar .alphabeticalScrollbar {
  position: relative;
  display: flex;
  width: var(--minTouchSize);
  height: 100%;
  /* height: calc(100% - var(--alphabeticalScrollbar_verticalMargin) * 2); */
  flex-direction: column;
  align-items: flex-end;
  user-select: none;
  margin-top: var(--alphabeticalScrollbar_verticalMargin);
  margin-bottom: var(--alphabeticalScrollbar_verticalMargin);
  /* margin-top: var(--alphabeticalScrollbar_verticalMargin);
  margin-bottom: var(--alphabeticalScrollbar_verticalMargin); */
}

.UE_0803_AlphabeticalScrollbar .indexPopover {
  position: absolute;
  top: 0;
  right: var(--iA1);
  width: var(--alphabeticalScrollbar_popoverWidth);
  user-select: none;
  pointer-events: none;
  /* opacity: 0; */
}

.UE_0803_AlphabeticalScrollbar .indexPopover .index {
  /*display:flex*/
  height: 100%;
  padding: 0 var(--zA1);
}

.textPrioA {
  margin: var(--statusInfoBoxMarginPrioA) var(--statusInfoBoxMargin);
}
