:root {
  --chargingScreenBarWidth: 392px;
  --barSegmentSpacing: 4px;
  --chargingScreenBarSegmentNum: 3;
  --chargingScreenBarSegmentWidth: calc(
    (
        var(--chargingScreenBarWidth) - var(--barSegmentSpacing) *
          (var(--chargingScreenBarSegmentNum) - 1)
      ) / var(--chargingScreenBarSegmentNum)
  );
}

:global(#root) > .UE_6041_01_Bidirektionales_Laden {
  left: 0px;
  top: 0px;
}

.UE_6041_01_Bidirektionales_Laden {
  width: var(--chargingScreenBarWidth);
  height: 150px;
  position: absolute;
  z-index: 1;
}

.chargingBargraph.scale {
  position: absolute;
  bottom: 0px;
  width: 66px;
  max-width: 66px;
  text-align: center;
}

.chargingBargraph.scale.left {
  left: 0px;
  text-align: left;
}

.chargingBargraph.scale.center {
  left: 99px;
  text-align: center;
}

.chargingBargraph.scale.right {
  right: 0px;
  text-align: right;
}

.valueContainer {
  position: absolute;
  display: flex;
  align-items: baseline;
  left: 50%;
  transform: translateX(-50%);
  top: 50px;
}
.valueContainer .value {
  margin-right: 5px;
}

.chargingIconsContainer {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0px;
}
.chargingIconsContainer .listIcon:last-child {
  margin-left: 5px;
}

/*-------------------------- Bargraph -------------------------*/
.Bargraph {
  position: absolute;
  top: 110px;
  left: 0px;
  width: var(--chargingScreenBarWidth);
  height: var(--barHeight);
}

.textPrio2 + .textPrio2 {
  margin-top: 0;
}

.settings {
  position: absolute;
  top: 600px;
  width: 800px;
}
