.SE_0073_ConsumptionStatistic {
  height: 554px;
  width: 1682px;
  position: absolute;
  margin: 0;
}

.SE_0073_ConsumptionStatistic path {
  fill: transparent;
}

.consumptionStatistic {
  height: 100%;
}

.consumptionStatisticContent {
  height: 100%;
  width: calc(100% - (var(--minTouchSize) + 2px) * 2) !important;
  position: absolute;
  left: calc(var(--minTouchSize) + 2px);
}

.yMetrics {
  width: fit-content;
  height: fit-content;
  position: absolute;
  left: 25px;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}

.yMetrics p:first-of-type::after {
  content: '';
  display: block;
  position: absolute;
  width: calc(100% - 4px);
  left: 2px;
  height: 1px;
  background-color: rgb(var(--audiWhite));
  margin-top: 1px;
}

.yMetrics p:last-of-type {
  margin-top: var(--zA2);
}

.xMetrics {
  position: absolute;
  bottom: 20px;
  left: 730px;
}

.chartContainer {
  position: relative;
  float: right;
  right: 120px;
  top: var(--zA4);
  width: 80%;
  height: 90%;
}

.buttonContainer {
  position: absolute;
  right: 32px;
  bottom: 103px;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

.indicatorContainer {
  position: absolute;
  background-color: rgb(var(--audiGrey));
  transform: translateY(-50%);
  left: -10px;
  padding: 3px;
}

.indicator {
  transform: translateY(-50%);
  left: -37px;
}

/* BASIC LAYOUT ADAPTATION */

.stroke.tile_0 {
  stroke: url(#stroke-gradient_0);
}

.fill.tile_0 {
  fill: url(#fill-gradient_0);
}

.stroke.tile_1 {
  stroke: url(#stroke-gradient_1);
}

.fill.tile_1 {
  fill: url(#fill-gradient_1);
}

.stroke.tile_2 {
  stroke: url(#stroke-gradient_2);
}

.fill.tile_2 {
  fill: url(#fill-gradient_2);
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0073_ConsumptionStatistic {
    height: 427px;
    width: 1530px;
  }
  .chartContainer {
    top: var(--iA1);
    right: 100px;
  }

  .indicatorContainer {
    top: 182.346px;
  }

  .buttonContainer {
    bottom: 74px;
    right: 20px;
  }

  .yMetrics {
    left: 10px;
  }

  .xMetrics {
    bottom: 10px;
    left: 658px;
  }
}
