.container > * {
  box-sizing: border-box;
}

.story__container {
  background-color: black;

  width: 1800px;
  height: 956px;
}

.container {
  box-sizing: border-box;
  background-color: black;
  width: 100%;
  height: 100%;

  padding: 20px;

  display: grid;
  grid-template-rows: 1fr max-content;
}

.container__center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
}

.container__center--text {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 880px;
}

.container__center--slider {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 900px;
  gap: 20px;
}

.container__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
}

.container__bottom--text {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
