/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --mergeDuration: 0.3s;
  --tabIndentation: 4px;
}

.tab.tabFocus {
  width: var(--tabFocusWidth);
  transition: width var(--mergeDuration) var(--animationCurve),
    transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.right .tab.tabFocus {
  /* add 2px because of separator line*/
  width: calc(var(--tabFocusWidth) + 2px);
}

.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards .tab.tabFocus {
  width: var(--tabFocusWidthRS);
  transition: width var(--mergeDuration) var(--animationCurve),
    transform var(--mergeDuration) var(--animationCurve);
}

.card.fix .tab.tabFocus {
  width: 100%;
  transition: width var(--mergeDuration) var(--animationCurve),
    transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.left .card.temp .tab.tabFocus {
  transform: translateX(
    calc(var(--tabIndentation) + var(--tabCollapsedWidth) * 2)
  );
}
.UE_6200_Cards.left .card.fix .tab.tabFocus {
  transform: translateX(0);
}

.UE_6200_Cards.left .card .tabs .tab.tabFocus .wrapper {
  transform: translateX(0);
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.left .card.fix .tabs .tab.tabFocus .wrapper {
  transform: translateX(
    calc(var(--tabIndentation) + var(--tabCollapsedWidth) * 2)
  );
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.left .card.fix .tabs .tab.tabFocus .wrapper.alignLeft {
  transform: translateX(108px);
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.right .card.fix .tab.tabFocus {
  transform: translateX(0);
}

.UE_6200_Cards.right .card .tabs .tab.tabFocus .wrapper {
  transform: translateX(0);
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.right .card.fix .tabs .tab.tabFocus .wrapper {
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.right .card.fix .tabs .tab.tabFocus .wrapper.alignLeft {
  transform: translateX(25px);
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.card.fix .tabs .tab::after {
  opacity: 0;
  transition: opacity var(--mergeDuration) var(--animationCurve);
}
.card.temp .tabs .tab::after {
  transition: opacity var(--mergeDuration) var(--animationCurve);
}

.card.temp .tab.tabFocus .tabHighlight {
  opacity: 1;
  transition: opacity var(--mergeDuration) var(--animationCurve);
  transition-delay: 0.2s;
}
.card.fix .tab.tabFocus .tabHighlight {
  opacity: 0;
  transition: opacity calc(var(--mergeDuration) * 0.5) var(--animationCurve);
}

.UE_6200_Cards.right .card.temp .tab.tabNext1 {
  /* add 2px because of separator line*/
  transform: translateX(calc(var(--tabFocusWidth) + 2px));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards.right .card.temp .tab.tabNext1 {
  transform: translateX(var(--tabFocusWidthRS));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.right .card.fix .tab.tabNext1 {
  transform: translateX(var(--cardWidth));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards.right .card.fix .tab.tabNext1 {
  transform: translateX(var(--cardWidth_RS));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.left .card.temp .tab.tabNext1 {
  transform: translateX(calc(var(--tabIndentation) + var(--tabCollapsedWidth)));
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.left .card.fix .tab.tabNext1 {
  transform: translateX(calc(-1 * var(--tabCollapsedWidth)));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.right .card.temp .tab.tabNext2 {
  /* add 2px because of separator line*/
  transform: translateX(
    calc(var(--tabFocusWidth) + var(--tabCollapsedWidth) + 2px)
  );
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards.right .card.temp .tab.tabNext2 {
  transform: translateX(
    calc(var(--tabFocusWidthRS) + var(--tabCollapsedWidth))
  );
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.right .card.fix .tab.tabNext2 {
  transform: translateX(calc(var(--cardWidth) + var(--tabCollapsedWidth)));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards.right .card.fix .tab.tabNext2 {
  transform: translateX(calc(var(--cardWidth_RS) + var(--tabCollapsedWidth)));
  transition: transform var(--mergeDuration) var(--animationCurve);
}

.UE_6200_Cards.left .card.temp .tab.tabNext2 {
  transform: translateX(var(--tabIndentation));
  transition: transform var(--mergeDuration) var(--animationCurve);
}
.UE_6200_Cards.left .card.fix .tab.tabNext2 {
  transform: translateX(calc(-2 * var(--tabCollapsedWidth)));
  transition: transform var(--mergeDuration) var(--animationCurve);
}
