/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --callStackTileWidth: 537px;
  --callStackTileHeight: 192px;
  --callStackAnimatedTrackSizeStart: 34px;
  --callStackAnimatedTrackSizeEnd: 44px;
}

:global(#root) > .SE_0054_IntelligentCallStack {
  position: absolute;
  margin: 0;
  width: var(--callStackTileWidth);
  height: var(--callStackTileHeight);
  box-sizing: border-box;
}

.SE_0054_IntelligentCallStack .intelligentCallStack {
  position: relative;
  display: flex;
  width: 100%;
  height: 0;
  align-items: center;
  overflow: hidden;
}

.SE_0054_IntelligentCallStack .intelligentCallStack .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--interactionBackground), 0.15);
  border-radius: var(--standardBorderRadius);
}

.SE_0054_IntelligentCallStack
  .intelligentCallStack.productRec
  .background
  .lineHighlite {
  position: absolute;
  width: calc(100% - 4px);
  height: 1px;
  background-color: rgba(var(--ambientcolor1), 0.8);
  bottom: 5px;
  left: 2px;
}
.SE_0054_IntelligentCallStack
  .intelligentCallStack.productRec
  .background
  .bluredHighlite {
  position: absolute;
  width: calc(100%);
  height: 18px;
  background-color: rgba(var(--ambientcolor1), 0.1);
  bottom: 0px;
  left: 0px;
  filter: blur(20px);
}

.SE_0054_IntelligentCallStack .intelligentCallStack.productRec .coverContainer {
  display: block;
}
.SE_0054_IntelligentCallStack .intelligentCallStack .coverContainer .cover {
  width: var(--coverSizeList);
  height: var(--coverSizeList);
  margin-left: var(--lA1);
}
.SE_0054_IntelligentCallStack
  .intelligentCallStack
  .coverContainer
  .cover.rounded {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg);
}

.SE_0054_IntelligentCallStack .intelligentCallStack .contactDetails {
  margin: 0 var(--iA1) 0 var(--lA1);
  overflow: hidden;
}

.SE_0054_IntelligentCallStack .intelligentCallStack .contactDetails .textPrio1,
.SE_0054_IntelligentCallStack .intelligentCallStack .contactDetails .textPrio2 {
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.SE_0054_IntelligentCallStack
  .intelligentCallStack
  .contactDetails
  p:nth-child(2) {
  opacity: var(--secondaryTextOpacity);
}

.SE_0054_IntelligentCallStack .intelligentCallStack .listIcon {
  position: absolute;
  top: 0;
  right: 0;
}

.SE_0054_IntelligentCallStack .intelligentCallStack .animatedIconContainer {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: var(--callStackAnimatedTrackSizeStart);
  background: rgba(var(--tabbarBackground), 0.1);
  opacity: 0;
}

.SE_0054_IntelligentCallStack .track {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background-color: rgba(var(--audiWhite), 0.4);
}

.SE_0054_IntelligentCallStack .intelligentCallStack.animated {
  animation: intelligentCallStack_opacity 0.5s, intelligentCallStack_height 0.6s;
  animation-fill-mode: forwards;
}

.SE_0054_IntelligentCallStack
  .intelligentCallStack.animated
  .animatedIconContainer {
  animation: intelligentCallStack_brightness 3s,
    intelligentCallStack_animatedContainer ease-in-out 1s 1.5s;
  animation-fill-mode: forwards;
}

.SE_0054_IntelligentCallStack .intelligentCallStack.animated .track {
  opacity: 0;
  animation: intelligentCallStack_brightness 3s,
    intelligentCallStack_progress ease-in-out 2.5s 0.5s;
  animation-fill-mode: forwards;
}

.SE_0054_IntelligentCallStack .intelligentCallStack.animated .cover,
.SE_0054_IntelligentCallStack .intelligentCallStack.animated .contactDetails,
.SE_0054_IntelligentCallStack .intelligentCallStack.animated .listIcon {
  opacity: 0;
  animation: intelligentCallStack_opacity 0.3s;
  animation-delay: 2.4s;
  animation-fill-mode: forwards;
}

.SE_0054_IntelligentCallStack .intelligentCallStack.animated.noIcon .cover,
.SE_0054_IntelligentCallStack
  .intelligentCallStack.animated.noIcon
  .contactDetails,
.SE_0054_IntelligentCallStack .intelligentCallStack.animated.noIcon .listIcon {
  animation-delay: 0.6s;
}

/* ---- animations ---- */
@keyframes intelligentCallStack_opacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes intelligentCallStack_height {
  from {
    height: 0;
  }

  to {
    height: var(--callStackTileHeight);
  }
}

@keyframes intelligentCallStack_brightness {
  0% {
    opacity: 0;
  }

  75% {
    opacity: 0.6;
  }

  80% {
    opacity: 0.6;
  }

  100% {
    opacity: 1;
  }
}

@keyframes intelligentCallStack_animatedContainer {
  0% {
    left: 0;
    right: auto;
    width: 100%;
  }

  75% {
    left: 0;
    width: 100%;
  }

  75.001% {
    left: auto;
    right: 0;
    height: var(--callStackAnimatedTrackSizeStart);
  }

  100% {
    left: auto;
    right: 0;
    width: var(--callStackAnimatedTrackSizeEnd);
    height: var(--callStackAnimatedTrackSizeEnd);
  }
}

@keyframes intelligentCallStack_progress {
  0% {
    left: 0;
    width: 0;
  }

  75% {
    left: 0;
    width: 100%;
  }

  75.001% {
    left: auto;
    right: 0;
  }

  100% {
    left: auto;
    right: 0;
    width: 100%;
  }
}

/* ---- settings ---- */
.settings {
  position: relative;
  width: 70%;
  top: 250px;
}

.settings button {
  font-size: 20px;
  margin-left: 10px;
}
