/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --appGridScrollbarWidth: 2px;
  --appGridFolderSideSpace: 163px;
  --appGridFolderBottomSpace: 29px;
  --thirdPartyAppIconSize: 80px;
}

:global(#root) > .T_0201_AppGrid {
  background: rgb(var(--bodyColorGrey));
  position: absolute;
  height: 700px;
  width: var(--appGridWidth);
  margin: 0;
}
.T_0201_AppGrid {
}

.appGridOpen {
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.folderOpen {
  margin-left: auto;
  margin-right: auto;
}

.appgrid {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}

.appgrid.scrollbarWithTrack::-webkit-scrollbar-track {
  margin-top: 25px;
  margin-bottom: 25px;
}

.row {
  position: relative;
  width: 98%;
}

.innerRow {
  width: calc(var(--ue1201AppGridTileWidth) * 5);
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin: 0 auto;
}

.appRow {
  clip-path: polygon(0 16%, 100% 16%, 100% 100%, 0% 100%);
  transform: translateY(-252px);
}

.folderRow {
  width: calc(var(--ue1201AppGridTileWidth) * 5);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin: 0 auto;
}

.predictionRow {
  display: relative;
  z-index: 10;
  padding-bottom: 80px;
}

.predictionIconWrapper {
  position: absolute;
  left: 50%;
  bottom: -25px;
  transform: translateX(-50%);
  opacity: 30%;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 30px;
}

.predictionIconLeft,
.predictionIconRight {
  background-color: rgb(255, 255, 255, 0.4);
  height: 2px;
  width: 100%;
}

.folderDetail .row {
  width: auto;
}

.folderDetail .UE_1201_AppGridTile {
  flex: 0 0 calc(100% / 4);
}

.appgridDetailView {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--dashboardMarginBottom));
}

.folderDetail {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: var(--appGridTileBorderWidth);
  left: 50%;
  width: calc(100% - var(--appGridFolderSideSpace) * 2);
  height: calc(100% - var(--appGridFolderBottomSpace));
  box-sizing: border-box;
  transform: translateX(-50%);
  background-color: rgba(var(--audiBlack), 0.5);
}

