.scroller {
  position: relative;
  width: calc(100% - var(--popupInsetWidth) + 1px);
  overflow-y: auto;
  overflow-x: hidden;
  margin-left: var(--popupInsetWidth);
  margin-bottom: var(--popupInsetWidth);
}

.inner {
  position: relative;
  width: calc(100% - 3px);
}

.inner.input {
  opacity: 0.4;
}

.scroller::-webkit-scrollbar-track {
  display: none;
}

.folderName {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.folderTitle {
  position: relative;
  display: flex;
  width: calc(100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2));
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  justify-content: center;
  align-items: center;
  text-align: center;
}

.button {
  position: relative;
  height: 100%;
  min-width: var(--minTouchSize);
  align-self: center;
}

.close {
  margin-left: var(--popupInsetWidth);
}

.right {
  margin-right: var(--popupInsetWidth);
}

.UE_0501_InputField {
  position: relative;
  width: calc(100% - var(--minTouchSize) - (var(--popupInsetWidth) * 2));
  height: calc(var(--minTouchSize) - 1px);
}

.unHighlight {
  height: fit-content;
  width: fit-content;
}

.unHighlightSelected {
  opacity: 0.2;
}

.backgroundHighlight {
  background-color: rgba(var(--interactionBackground), 0.2);
  border-radius: var(--standardBorderRadius);
}

.settings {
  position: absolute;
  top: 750px;
  width: 50%;
}

@media only screen and (max-width: 1768px) {
  .settings {
    top: 682px;
    width: 50%;
  }

  .inner {
    width: calc(100% - 1px);
  }
}
