/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --barWidth: 404px;
  --barSegmentNum: 4;
  --barSegmentWidth: calc(
    (var(--barWidth) - var(--barSegmentSpacing) * (var(--barSegmentNum) - 1)) /
      var(--barSegmentNum)
  );
  --laptimerWidth: 490px;
  --laptimerPadding: 15px;
  --boardCompMemoryTopFirstLine: 44px;
  --boardCompMemoryTopSecondLine: 115px;
  --boardCompAvgWidth: 70px;
  --boardCompMaxDigitWidthSmall: 82px;
  --boardCompMaxDigitWidthLarge: 147px;
  --boardCompUnitWidth: 80px;
  --boardCompMemoryTextLineLeftWidth: calc(
    var(--boardCompAvgWidth) + 2 * var(--zA8) +
      var(--boardCompMaxDigitWidthSmall) + var(--boardCompUnitWidth)
  );
  --boardCompShortLongTermLine: calc(
    (
      var(--boardCompMemoryTextLineLeftWidth) + var(--zA8) +
        var(--boardCompMaxDigitWidthLarge) + var(--zA8) + 31px
    )
  );
  --boardCompLongTermPhevLine: calc(
    var(--listIconSize) + var(--zA8) + var(--boardCompMemoryTextLineLeftWidth) +
      var(--zA8) + var(--boardCompAvgWidth) + 2 * var(--zA8) +
      var(--boardCompAvgWidth) + var(--zA8) + 52px
  );
}

:global(#root) > .T_5100_Boardcomputer {
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthGB);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}
:global(#root) > .T_5100_Boardcomputer.kb {
  width: var(--cardContentWidthKB);
}

.boardcomputer {
  position: relative;
  width: 100%;
  height: 100%;
}

/* Track */
.T_5100_Boardcomputer ::-webkit-scrollbar-track {
  border: 1px solid rgba(var(--scrollbarTrackColor));
