/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --listHeaderExtendedButtons_height: 47px;
}

:global(#root) > .SE_0061_ListHeaderExtendedButtons {
  margin: 0;
}

.SE_0061_ListHeaderExtendedButtons {
}

.SE_0061_ListHeaderExtendedButtons {
  height: var(--listHeaderExtendedButtons_height);
}

.listHeaderExtendedButtons {
  /* height: var(--listHeaderExtendedButtons_height); */
  height: 100%;
  box-sizing: border-box;
  margin-left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
}

.SE_0061_ListHeaderExtendedButtons.sticky .listHeaderExtendedButtons {
  border-top: none;
  height: var(--minTouchSize);
  background-color: rgba(var(--audiBlack), 0.98);
}

.container {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
}

.tripDate,
.tripInfo {
  flex: 1;
  padding-right: var(--textButtonMargin);
  box-sizing: border-box;
  overflow: hidden;
}
.tripDate {
  padding-left: var(--lA1);
}

.tripInfo {
  margin-right: auto;
}

.SE_0061_ListHeaderExtendedButtons .tripInfo,
.SE_0061_ListHeaderExtendedButtons .actions {
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
}

.SE_0061_ListHeaderExtendedButtons.sticky .tripInfo,
.SE_0061_ListHeaderExtendedButtons.sticky .actions {
  opacity: 1;
}

.SE_0061_ListHeaderExtendedButtons .tripDate .textPrio2,
.SE_0061_ListHeaderExtendedButtons .tripInfo .textPrio2 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.actions {
  display: flex;
  position: relative;
  flex: 2;
  height: 100%;
  overflow: hidden;
}

.SE_0061_ListHeaderExtendedButtons .actions .UE_0101_ActionButton.button {
  position: relative;
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.textIconWrapper {
  display: flex;
  flex: 1;
  margin: 0 var(--textButtonMargin);
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.textIconWrapper p {
  margin: 0;
  flex: 1;
}

.textIconWrapper div:first-child {
  width: 100%;
  text-align: center;
}

.touchIcon {
  position: relative;
  margin-left: var(--iA1);
  transform: none;
  left: 0;
  top: 0;
  flex-shrink: 0;
}

.SE_0061_ListHeaderExtendedButtons .button {
  height: var(--minTouchSize);
}
