/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --newbarHeight: 7px;
  --barBorderRadius: 3px;
}

.bargraph {
  height: var(--newbarHeight);
}

/** Powermeter **/

/* Charge */
.bargraph.charge .chargeLimitBackground,
.bargraph.charge .chargeBackground {
  position: absolute;
  top: 1px;
  width: 100%;
  height: calc(100% - 2px);
  -webkit-mask-image: url(img/bargraph_charge_clipmask.png);
  -webkit-mask-repeat: no-repeat;
}
.bargraph.charge .chargeLimitBackground,
.bargraph.charge.inactive .chargeBackground {
  background-color: rgb(var(--bargraphInactive));
}
.bargraph.charge .chargeBackground {
  background-color: rgb(var(--fpkChargeBackground));
}

.bargraph.charge .chargeLimitWrapper,
.bargraph.charge .chargeWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  right: 0;
  -webkit-mask-image: linear-gradient(to right, rgb(0, 0, 0), rgb(0, 0, 0));
  -webkit-mask-repeat: no-repeat;
}

.bargraph.charge .fill {
  position: absolute;
  height: calc(100% - 2px);
  width: 100%;
  right: 0;
  background-color: rgb(var(--fpkGreen));
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}

/* Power */
.bargraph.power .powerLimitBackground,
.bargraph.power .powerBackground {
  position: absolute;
  top: 1px;
  left: -1px;
  width: 100%;
  height: calc(100% - 4px);
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}
.bargraph.power .powerLimitBackground {
  background-color: rgb(var(--bargraphInactive));
}
.bargraph.power .powerBackground {
  background-color: rgb(var(--fpkGreenInactive));
}
.bargraph.power.throttleLimit .powerBackground {
  border-radius: var(--barBorderRadius) 0px 0px var(--barBorderRadius);
}
.bargraph.power.yellow .powerBackground {
  background-color: rgb(var(--fpkYellowInactive));
}
.bargraph.power.yellow .fill {
  background-color: rgb(var(--fpkYellow));
}
.bargraph.power .fill {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--fpkGreen));
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}

.bargraph.power .glow {
  position: absolute;
  left: 0px;
  top: -3px;
  height: 13px;
  filter: blur(5px);
}
.bargraph.power .glowClip {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(var(--fpkGreen), 0.75);
  clip-path: polygon(0px 4px, 100% 0%, 100% 100%, 0% 9px);
}

/* SOC */
.bargraph.soc .socBackground {
  position: absolute;
  top: 1px;
  left: -1px;
  width: 100%;
  height: calc(100% - 4px);
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
  background-color: rgb(var(--bargraphInactive));
}
.bargraph.soc .fill {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--fpkWhite));
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}
.bargraph.soc.red .socBackground {
  background-color: rgb(var(--warningRedInactive));
}
.bargraph.soc.red .fill {
  background-color: rgb(var(--warningRed));
}
.bargraph.soc.green .socBackground {
  background-color: rgb(var(--fpkGreenInactive));
}
.bargraph.soc.green .fill {
  background-color: rgb(var(--fpkGreen));
}

.bargraph .marker {
  position: absolute;
  top: -4px;
  height: 15px;
  width: 17px;
  background-color: rgb(var(--audiBlack));
  clip-path: polygon(
    0px 100%,
    0px calc(100% - 7px),
    50% 0%,
    100% calc(100% - 7px),
    100% 100%
  );
}
.bargraph .marker .markerFill {
  position: absolute;
  top: 3px;
  left: 2px;
  height: calc(100% - 5px);
  width: calc(100% - 4px);
  background-color: rgb(var(--fpkWhite));
  clip-path: polygon(
    0px 100%,
    0px calc(100% - 4px),
    50% 0%,
    100% calc(100% - 4px),
    100% 100%
  );
}
.bargraph .marker .markerFill.green {
  background-color: rgb(var(--fpkGreen));
}

/* RPM */
.bargraph.rpm .rpmBackground {
  position: absolute;
  top: 1px;
  left: -1px;
  height: calc(100% - 4px);
  border: 1px solid rgb(var(--audiBlack));
  border-top-left-radius: var(--barBorderRadius);
  border-bottom-left-radius: var(--barBorderRadius);
  background-color: rgb(var(--bargraphInactive));
}
.bargraph.rpm.inactive .rpmBackground {
  border-radius: var(--barBorderRadius);
}

.bargraph.rpm .rpmBackgroundLimit {
  position: absolute;
  top: 1px;
  left: -1px;
  width: 100%;
  height: calc(100% - 4px);
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
  background-color: rgb(var(--warningRedInactive));
}
.bargraph.rpm .rpmBackgroundLimitActive {
  background-color: rgb(var(--warningRed));
}

.bargraph.rpm .fill {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--fpkWhite));
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}
.bargraph.rpm .fill.end {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.bargraph.rpm.red .fill {
  background-color: rgb(var(--warningRed));
}

.bargraph.rpm .fillLimit {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--warningRed));
  border: 1px solid rgb(var(--audiBlack));
  border-top-right-radius: var(--barBorderRadius);
  border-bottom-right-radius: var(--barBorderRadius);
}

.bargraph.rpm .line {
  position: absolute;
  height: 100%;
  width: 1px;
  background-color: rgb(var(--audiBlack));
}

/* CONSUMPTION */
.bargraph.consumption .consumptionBackground {
  position: absolute;
  top: 1px;
  left: -1px;
  height: calc(100% - 4px);
  width: 100%;
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
  background-color: rgb(var(--bargraphInactive));
}

.bargraph.consumption .fill {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--fpkWhite));
  border: 1px solid rgb(var(--audiBlack));
  border-radius: var(--barBorderRadius);
}
.bargraph.consumption .fill.end {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.bargraph.consumption .fillLimit {
  position: absolute;
  left: -1px;
  height: calc(100% - 2px);
  background-color: rgb(var(--fpkGreen));
  border: 1px solid rgb(var(--audiBlack));
  border-top-right-radius: var(--barBorderRadius);
  border-bottom-right-radius: var(--barBorderRadius);
}

.bargraph.consumption .line {
  position: absolute;
  height: 100%;
  width: 2px;
  background-color: rgb(var(--audiBlack));
}
