:root {
}

:global(#root) > .SE_0105_PID_Digital_Decors {
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  width: 1920px;
  height: 720px;
  position: absolute;
}
.SE_0105_PID_Digital_Decors {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgb(var(--audiBlack));
}

.digitalDecorator {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  aspect-ratio: 1920/720;
}

.digitalDecorator .systemUIBackground {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  transform: scaleX(-1);
  background-position-x: -100px;
}

.digitalDecorator .digitalDecoratorImage {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.digitalDecorator.A_Linie .digitalDecoratorImage {
  background-image: url(../img/SE_0105_PID_Digital_Decors_A_Linie.png);
}
.digitalDecorator.Sport .digitalDecoratorImage {
  background-image: url(../img/SE_0105_PID_Digital_Decors_Sport.png);
}
.digitalDecorator.RS .digitalDecoratorImage {
  background-image: url(../img/SE_0105_PID_Digital_Decors_RS.png);
}

.stepperDecorator .UE_0202_FloatingContainer .button {
  display: flex;
  justify-content: center;
}

.goodbyeDecoratorContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  padding: 90px 60px;
  box-sizing: border-box;
  display: flex;
}
.goodbyeDecoratorLeft {
  display: flex;
  align-items: flex-end;
  width: 80%;
  z-index: 1;
}

.goodbyeDecoratorRight {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;
  width: 20%;
  z-index: 1;
}

.settings {
  position: absolute;
  top: 750px;
  width: 100%;
  color: white;
}
