:root {
  --topLongPressExtensionTrianglePosition: 46px;
  --leftLongPressExtensionTrianglePosition: 550px;
}

:global(#root) > .SE_0081_LongPressExtension {
  position: absolute;
  height: 600px;
  width: 100%;
  min-width: 550px;
  margin: 60px;
  background-color: rgb(var(--bodyColorGrey));
}

.SE_0081_LongPressExtension .longPressExtension {
  position: relative;
  width: fit-content;
  max-width: calc(100vw - var(--dashboardMarginSide) * 2 - 24px);
}

/*---------------------- EXTENSION CONTAINER -------------------*/

.SE_0081_LongPressExtension .longPressExtension .extensionContainer {
  display: flex;
  flex-direction: column;
  padding: 2px;
}

.SE_0081_LongPressExtension .longPressExtension .extensionContainer .firstLine,
.SE_0081_LongPressExtension.twoLines
  .longPressExtension
  .extensionContainer
  .secondLine {
  display: flex;
  flex-direction: row;
  position: relative;
}

.SE_0081_LongPressExtension.twoLines
  .longPressExtension
  .extensionContainer
  .firstLine::before {
  content: '';
  position: absolute;
  width: calc(100% - var(--cA1));
  height: 1px;
  left: calc(var(--cA1) / 2);
  background-color: rgba(var(--audiWhite), 0.3);
  top: -1px;
}

.SE_0081_LongPressExtension.A_Extension
  .longPressExtension
  .extensionContainer
  .A_Text,
.SE_0081_LongPressExtension.O_Extension
  .longPressExtension
  .extensionContainer
  .O_Text {
  display: block;
}

.SE_0081_LongPressExtension .longPressExtension .extensionContainer .button {
  position: relative;
  min-width: var(--minTouchSize);
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}
.SE_0081_LongPressExtension
  .longPressExtension
  .extensionContainer
  .button.text::before {
  content: '';
  position: absolute;
  width: 1px;
  height: calc(100% - var(--cA1));
  bottom: calc(var(--cA1) / 2);
  background-color: rgba(var(--audiWhite), 0.3);
  z-index: 2;
}
.SE_0081_LongPressExtension .longPressExtension .extensionContainer .button p {
  display: -webkit-box;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: var(--textButtonMargin);
  margin-right: var(--textButtonMargin);
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}

.SE_0081_LongPressExtension
  .longPressExtension
  .extensionContainer
  .inputFieldButtons {
  width: auto;
}
.settings {
  position: relative;
  top: 400px;
}
