:global(#root) > .arrowPopup {
  margin-top: 50px;
  margin-left: 50px;
}

.arrowPopup {
  width: fit-content;
  position: relative;
  padding: var(--popupInsetWidth);
}

.placeHolderContainer {
  display: flex;
  flex-direction: column;
}

.placeHolder {
  display: flex;
  height: var(--minTouchSize);
  width: 600px;
}

.placeHolder p {
  display: flex;
  margin: auto;
}

.triangleBackground {
  background-color: rgba(var(--audiBlack), 1);
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.1),
    rgba(var(--interactionBackground), 0)
  );
  /* background-color: red; */
}

.triangle {
  width: 0;
  pointer-events: none;
}

/*----------------- TRIANGLE BOTTOM -------------------*/
.lower {
  top: 0px;
}
.lower .triangleBackground {
  position: absolute;
  z-index: 0;
  bottom: -21px;
  left: -6px;
  width: 100%;
  height: calc(100% + 13px);
  clip-path: polygon(
    calc(var(--leftTrianglePosition) + 7px) calc(100% - 21px),
    calc(var(--leftTrianglePosition) + 49px) calc(100% - 21px),
    calc(var(--leftTrianglePosition) + 28px) 100%
  );
}

.lower .lowerTriangle {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.lower .borderTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-bottom: 0;
  z-index: 2;
  bottom: -22.5px;
  left: var(--leftTrianglePosition);
  border-top-color: rgb(48, 54, 59);
  border-width: 22px;
  clip-path: polygon(
    0% 0%,
    50% 100%,
    100% 0%,
    calc(100% - 1.5px) 0,
    50% calc(100% - 1.5px),
    1.5px 0
  );
}

.lower .blackTriangle {
  position: absolute;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-bottom: 0;
  bottom: -25px;
  left: calc(var(--leftTrianglePosition) - 2px);
  border-top-color: rgba(var(--audiBlack), 0.8);
  border-width: 24px;
  clip-path: polygon(
    0% 0%,
    50% 100%,
    100% 0%,
    calc(100% - 8px) 0,
    50% calc(100% - 8px),
    8px 0
  );
}

/*----------------- TRIANGLE TOP -------------------*/

.upper .triangleBackground {
  position: absolute;
  top: -22px;
  left: 0px;
  width: 100%;
  height: calc(100% + 14px);
  clip-path: polygon(
    calc(var(--leftTrianglePosition) + 0px) calc(0% + 22px),
    calc(var(--leftTrianglePosition) + 22px) calc(0% + 0px),
    calc(var(--leftTrianglePosition) + 44px) calc(0% + 22px)
  );
}

.upper .blackTriangle {
  position: relative;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-top: 0;
  top: -27px;
  left: calc(var(--leftTrianglePosition) - 2px);
  border-bottom-color: rgba(var(--audiBlack), 0.8);
  border-width: 24px;
  clip-path: polygon(
    0 100%,
    50% 0,
    100% 100%,
    calc(100% - 8px) 100%,
    50% 8px,
    8px 100%
  );
}

.upper .borderTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-top: 0;
  z-index: 2;
  top: -23px;
  left: calc(var(--leftTrianglePosition) - 1px);
  border-bottom-color: rgba(var(--interactionBackground), 0.4);
  border-width: 23px;
  z-index: 1;
  clip-path: polygon(
    0 100%,
    50% 0,
    100% 100%,
    calc(100% - 2px) 100%,
    50% 2px,
    2px 100%
  );
}

/*----------------- TRIANGLE LEFT -------------------*/

.left .triangleBackground {
  position: absolute;
  z-index: 1;
  left: -20px;
  z-index: 0;
  width: 23px;
  height: 100%;
  clip-path: polygon(
    0 var(--topTrianglePosition),
    21px calc(var(--topTrianglePosition) - 21px),
    21px calc(var(--topTrianglePosition) + 21px)
  );
}

.left .blackTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-left: 0;
  top: var(--topTrianglePosition);
  transform: translateY(-50%);
  left: -28px;
  border-right-color: rgba(var(--audiBlack), 0.8);
  border-width: 24px;
  clip-path: polygon(
    100% 0%,
    0 50%,
    100% 100%,
    100% calc(100% - 8px),
    8px 50%,
    100% 8px
  );
}

.left .borderTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-left: 0;
  z-index: 2;
  top: var(--topTrianglePosition);
  transform: translateY(-50%);
  left: -23px;
  border-right-color: rgba(var(--interactionBackground), 0.4);
  border-width: 23px;
  z-index: 1;
  clip-path: polygon(
    100% 0%,
    0 50%,
    100% 100%,
    100% calc(100% - 2px),
    2px 50%,
    100% 2px
  );
}

/*----------------- TRIANGLE RIGHT -------------------*/
.right .triangleBackground {
  position: absolute;
  width: 23px;
  left: calc(100% - 2px);
  height: 100%;
  clip-path: polygon(
    calc(100% - 21px) calc(var(--topTrianglePosition) - 21px),
    100% var(--topTrianglePosition),
    calc(100% - 21px) calc(var(--topTrianglePosition) + 21px)
  );
}

.right .blackTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-right: 0;
  top: var(--topTrianglePosition);
  transform: translateY(-50%);
  right: -27px;
  border-left-color: rgba(var(--audiBlack), 0.8);
  border-width: 24px;
  clip-path: polygon(
    0% 0%,
    100% 50%,
    0% 100%,
    0% calc(100% - 8px),
    calc(100% - 8px) 50%,
    0% 8px
  );
}

.right .borderTriangle {
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: transparent;
  border-right: 0;
  z-index: 2;
  top: var(--topTrianglePosition);
  transform: translateY(-50%);
  right: -23px;
  border-left-color: rgba(var(--interactionBackground), 0.4);
  border-width: 23px;
  z-index: 1;
  clip-path: polygon(
    0% 0%,
    100% 50%,
    0% 100%,
    0% calc(100% - 2px),
    calc(100% - 2px) 50%,
    0% 3px
  );
}

/*----------------- Settings -------------------*/

.settings {
  position: absolute;
  width: 100%;
  top: 400px;
}

.settings input[type='number'] {
  width: 100px;
  margin-left: 60px;
  margin-right: 30px;
}
