:root {
  --imageWidth: 300px;
  --imageHeight: 215px;
}

:global(#root) > .SE_0089_GlobalSearch {
  width: 1666px;
  height: 555px;
  overflow: hidden;
}

.globalSearch {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}

.searchImage {
  height: 360px;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.5;
}

.searchIcon {
  background-image: url(../img/SE_0089_GlobalSearch_Icon.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  width: var(--imageWidth);
  height: var(--imageHeight);
  position: absolute;
}

.textContainer {
  margin: var(--iA1);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
  height: calc(var(--minTouchSize) - var(--iA3));
}

.textContainer p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: calc(100% - var(--iA2));
}

@media only screen and (max-width: 1768px) {
  :root {
    --imageWidth: 182px;
    --imageHeight: 130px;
  }

  :global(#root) > .SE_0089_GlobalSearch {
    width: 1514px;
    height: 427px;
  }

  .searchImage {
    height: 163px;
  }
}
