/* CLASSIFICATION: CONFIDENTIAL */
:global(#root) > .UE_0001_DirectAccess {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: var(--directAccessWidth);
  height: 696px;
  margin: 8px;
}

.directAccess {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: rgb(var(--audiBlack));
}

.directAccess .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  left: 0px;
}

.directAccess .button .uiState {
  z-index: 1;
}

.directAccess .button .touchIcon {
  position: absolute;
  width: 50px;
  height: 50px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.directAccess .button .touchIcon:nth-of-type(2) {
  position: absolute;
  width: 50px;
  height: 50px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  background-image: inherit;
}
.button:global(#appGridButton) {
  margin: auto 0;
}

.directAccess .button:global(#appGridButton) .daIcon {
  background-color: transparent;
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E428_appgrid_DA.svg);
  background-size: 100%;
}

.directAccess .daIconWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.directAccess .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E721_main_menue_Navigation_DA.svg);
}

.directAccess .dashboardIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E775_main_menue_home_DA.svg);
  background-size: 100%;
}

:global(#directAccessTwo) .daIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E724_main_menue_media_DA.svg);
}

/*------------------------------------------- B10 ---------------------------------------------*/
.UE_0001_DirectAccess.b10Screen,
:global(#root) > .UE_0001_DirectAccess.b10Screen {
  position: absolute;
  background-color: unset;
  width: calc(var(--minTouchSize) * 2);
  height: calc(var(--minTouchSize) * 4);
}

.b10Screen .directAccess .daBorder {
  width: calc(100% - 6px);
  height: calc(100% - 6px);
  top: 0px;
  left: 0px;
  position: absolute;
  border: 3px solid var(--buttonBackgroundColor);
  border-radius: var(--standardBorderRadius);
}
.b10Screen .directAccess .daBackground {
  width: calc(100% - 6px);
  height: calc(var(--minTouchSize) - 3px);
  top: 3px;
  left: 3px;
  position: absolute;
  background-color: var(--buttonBackgroundColor);
}
.b10Screen .directAccess .separatorLine {
  width: calc(100%);
  height: 2px;
  top: 116px;
  left: 0px;
  position: absolute;
  background-color: rgb(var(--ambientcolor1));
}

.b10Screen .directAccess {
  width: 100%;
  height: 100%;
  display: inline-grid;
  grid-template-columns: auto auto;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 1920px) {
  .directAccess::after {
    position: absolute;
    left: 114px;
    top: -115px;
    content: '';
    width: 1px;
    height: calc(100% + 111px);
    background-color: var(--tabbarSeparatorColor);
  }
}

@media only screen and (max-width: 1768px) {
  .directAccess {
    max-width: 120px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  :global(#directAccessOne) {
    top: unset;
  }

  :global(#directAccessTwo) {
    top: unset;
  }

  :global(#appGridButton) {
    top: unset;
    display: none;
  }

  :global(#directAccessThree) {
    bottom: unset;
  }

  :global(#directAccessFour) {
    bottom: unset;
  }
}

/*------------------------------------------- PID ---------------------------------------------*/
.pidScreen .directAccess::after {
  position: absolute;
  left: 114px;
  top: -115px;
  content: '';
  width: 1px;
  height: calc(100% + 111px);
  background-color: var(--tabbarSeparatorColor);
}
