/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --rekuBarWidth: 500px;
  --rekuRootHeight: 187px;
  --rekuBarHeight: 30px;
  --rekuBarSegmentSpacing: 2px;

  --rekuMarkerPosition: 120px;
  --rekuMarkerWidth: 4px;
  --rekuMarkerClipLeft: calc(var(--rekuMarkerPosition));
  --rekuMarkerClipRight: calc(
    var(--rekuMarkerPosition) + var(--rekuMarkerWidth) + 1px
  );

  /* --------------------------------- colors --------------------------------- */
  --rekuBackgroundGrey: rgba(var(--audiWhite), 0.2);
  --rekuFillingColor: rgba(var(--fpkGreen), 0.4);

  /* --------------------------- Allradantrieb 4m/s2 -------------------------- */
  --rekuLeftFirstSegment4ms2: 24px;
  --rekuLeftReku1Segment4ms2: 72px;
  --rekuLeftReku2Segment4ms2: 72px;
  --rekuLeftRekuBSegment4ms2: 72px;
  --rekuLeftLastSegment4ms2: 246px;

  /* ------------------------------- Heckantrieb ------------------------------ */
  --rekuLeftFirstSegmentHeck: 59px;
  --rekuLeftReku1SegmentHeck: 136px;
  --rekuLeftReku2SegmentHeck: 116px;
  --rekuLeftRekuBSegmentHeck: 103px;
  --rekuLeftLastSegmentHeck: 72px;

  /* ------------------------------- Hybrid ------------------------------ */

  --rekuLeftLastSegmentHybrid: 494px;

  /* ------------------------------ upperSpacing ------------------------------ */

  --rekuUpperFillingSpacing: calc(
    var(--rekuBarSegmentSpacing) * 4 + 1px + var(--rekuLeftFirstSegmentHeck) +
      var(--rekuLeftReku1SegmentHeck) + var(--rekuLeftReku2SegmentHeck) +
      var(--rekuLeftRekuBSegmentHeck)
  );
}

:global(#root) > .UE_6052_Rekuperation_Intensitaet {
  background-color: rgb(var(--audiBlack));
  margin: 10px;
}

.rekuIntens {
  position: relative;
  width: var(--rekuBarWidth);
  height: var(--rekuRootHeight);
  top: 0px;
  left: 0px;
}

.rekuIntens .textLabels {
  position: relative;
  width: var(--rekuBarWidth);
  height: var(--rekuRootHeight);
  top: 0px;
  left: 0px;
}

.rekuIntens .textLabels .zero {
  position: absolute;
  left: 0px;
  top: 12px;
  height: 18px;
  width: 19px;
  background-image: url(../img/UE_6052_Rekuperation_Intensitaet_Zero.png);
}

.rekuIntens .textLabels .max {
  position: absolute;
  top: 15px;
  right: 0px;
  width: 53px;
  height: 14px;
  background-image: url(../img/UE_6052_Rekuperation_Intensitaet_Reku_MAX.png);
}

.rekuIntens .textLabels .labelText {
  position: absolute;
  top: 2px;
  left: 53px;
  width: 334px;
  height: 37px;
  margin: 0 var(--cA1);
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  white-space: nowrap;
}

.rekuIntens .upperBar {
  position: absolute;
  width: var(--rekuBarWidth);
  height: 3px;
  top: 41px;
  left: 0px;
  border-top: 1px solid rgba(var(--audiWhite), 0.4);
  background-color: rgb(var(--FIDtelltaleGrey));
  -webkit-mask-image: linear-gradient(
    to right,
    black 0%,
    black var(--rekuUpperFillingSpacing),
    transparent var(--rekuUpperFillingSpacing),
    transparent
      calc(var(--rekuBarSegmentSpacing) + var(--rekuUpperFillingSpacing)),
    black calc(var(--rekuBarSegmentSpacing) + var(--rekuUpperFillingSpacing)),
    black 100%
  );
}

.rekuIntens .upperBar .upperFilling {
  position: absolute;
  width: 333px;
  height: 3px;
  top: -1px;
  left: 0px;
  border-top: 1px solid rgba(var(--audiWhite), 0.2);
  background-color: rgb(var(--fpkGreen));
}

.rekuIntens .lowerBar {
  position: absolute;
  width: calc(var(--rekuBarWidth) - 2px);
  height: calc(var(--rekuBarHeight) + 3px);
  top: 48px;
  left: 0px;
  border-bottom: 1px solid rgba(var(--audiWhite), 0.6);
  border-left: 1px solid rgba(var(--audiWhite), 0.6);
  border-right: 1px solid rgba(var(--audiWhite), 0.6);
  clip-path: polygon(
    calc(var(--rekuMarkerClipLeft) + 1px) calc(100% - 2px),
    var(--rekuMarkerClipLeft) calc(100% - 1px),
    var(--rekuMarkerClipLeft) 0%,
    0% 0%,
    0% 100%,
    100% 100%,
    100% 0%,
    calc(var(--rekuMarkerClipRight) + 1px) 0%,
    calc(var(--rekuMarkerClipRight) + 1px) calc(100% - 2px),
    var(--rekuMarkerClipRight) calc(100% - 1px),
    var(--rekuMarkerClipRight) 0%,
    calc(var(--rekuMarkerClipLeft) + 1px) 0%
  );
}

.rekuIntens .lowerBar .segment {
  position: absolute;
  height: var(--rekuBarHeight);
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--audiWhite), 0.1) 2px,
    rgba(var(--audiWhite), 0.1) 3px
  );
  background-repeat: no-repeat;
  overflow: hidden;
  background-color: var(--rekuBackgroundGrey);
}

.rekuIntens .lowerBar .segment.first {
  left: var(--rekuBarSegmentSpacing);
  width: var(--rekuLeftFirstSegment4ms2);
}

.rekuIntens .lowerBar .segment.reku1 {
  left: calc(
    var(--rekuBarSegmentSpacing) + var(--rekuLeftFirstSegment4ms2) +
      var(--rekuBarSegmentSpacing)
  );
  width: var(--rekuLeftReku1Segment4ms2);
}
.rekuIntens .lowerBar .segment.reku2 {
  left: calc(
    var(--rekuBarSegmentSpacing) + var(--rekuLeftFirstSegment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftReku1Segment4ms2) +
      var(--rekuBarSegmentSpacing)
  );
  width: var(--rekuLeftReku2Segment4ms2);
}
.rekuIntens .lowerBar .segment.rekuB {
  left: calc(
    var(--rekuBarSegmentSpacing) + var(--rekuLeftFirstSegment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftReku1Segment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftReku2Segment4ms2) +
      var(--rekuBarSegmentSpacing)
  );
  width: var(--rekuLeftRekuBSegment4ms2);
}

.rekuIntens .lowerBar .segment.last {
  left: calc(
    var(--rekuBarSegmentSpacing) + var(--rekuLeftFirstSegment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftReku1Segment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftReku2Segment4ms2) +
      var(--rekuBarSegmentSpacing) + var(--rekuLeftRekuBSegment4ms2) +
      var(--rekuBarSegmentSpacing)
  );
  width: var(--rekuLeftLastSegment4ms2);
}

.rekuIntens .lowerBar .segment .rekuicon {
  width: 44px;
  height: 21px;
  top: 5px;
  left: 50%;
  position: relative;
  transform: translateX(-50%);
  background-color: rgb(var(--audiBlack));
  z-index: 1;
}

.rekuIntens .lowerBar .segment .rekuicon.reku1 {
  -webkit-mask-image: url('../img/UE_6052_Rekuperation_Intensitaet_1.png');
}

.rekuIntens .lowerBar .segment .rekuicon.reku2 {
  -webkit-mask-image: url('../img/UE_6052_Rekuperation_Intensitaet_2.png');
}

.rekuIntens .lowerBar .segment .rekuicon.rekuB {
  width: 38px;
  height: 31px;
  top: 0px;
  -webkit-mask-image: url('../img/UE_6052_Rekuperation_Intensitaet_B.png');
}

.rekuIntens .lowerBar .marker {
  position: absolute;
  left: var(--rekuMarkerPosition);
  height: var(--rekuBarHeight);
  width: var(--rekuMarkerWidth);
  background-color: rgb(var(--audiWhite));
  z-index: 1;
}

.rekuIntens .tail {
  position: absolute;
  left: calc(var(--rekuBarSegmentSpacing) + 1px);
  top: 48px;
  height: 64px;
  width: calc(var(--rekuMarkerPosition) + 2px);
  z-index: 1;
}

.rekuIntens .tailBottom {
  position: absolute;
  top: calc(var(--rekuBarHeight) + 4px);
  left: 0px;
  height: 25px;
  width: 100%;
  background: linear-gradient(
    180deg,
    rgba(var(--audiWhite), 0.13) 0%,
    transparent 100%
  );
}

.rekuIntens .tailRight {
  position: absolute;
  top: 0px;
  left: 0px;
  height: var(--rekuBarHeight);
  width: 100%;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(var(--audiWhite), 0.2) 90%
  );
}

.rekuIntens .lowerBar .filling {
  position: absolute;
  left: var(--rekuBarSegmentSpacing);
  height: var(--rekuBarHeight);
  width: 330px;
  background-color: var(--rekuFillingColor);
  clip-path: polygon(
    0 100%,
    0% 0%,
    var(--rekuLeftFirstSegment4ms2) 0,
    var(--rekuLeftFirstSegment4ms2) 100%,
    calc(var(--rekuLeftFirstSegment4ms2) + var(--rekuBarSegmentSpacing)) 100%,
    calc(var(--rekuLeftFirstSegment4ms2) + var(--rekuBarSegmentSpacing)) 0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2)
      )
      0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 2 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 2 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2)
      )
      0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 2 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2)
      )
      0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 2 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 3 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 3 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2)
      )
      0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 3 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2) +
          var(--rekuLeftRekuBSegment4ms2)
      )
      0,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 3 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2) +
          var(--rekuLeftRekuBSegment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 4 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2) +
          var(--rekuLeftRekuBSegment4ms2)
      )
      100%,
    calc(
        var(--rekuLeftFirstSegment4ms2) + 4 * var(--rekuBarSegmentSpacing) +
          var(--rekuLeftReku1Segment4ms2) + var(--rekuLeftReku2Segment4ms2) +
          var(--rekuLeftRekuBSegment4ms2)
      )
      0,
    100% 0,
    100% 100%
  );
}

.rekuIntens .currentTextContainer {
  width: var(--rekuBarWidth);
  height: 100px;
  top: 90px;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.rekuIntens .currentTextContainer .statusText {
  margin-left: var(--cA1);
  margin-right: var(--cA1);
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  white-space: nowrap;
}
.UE_6052_Rekuperation_Intensitaet
  .rekuIntens
  .currentTextContainer
  .statusText:not(:first-child) {
  margin-top: var(--zA2);
}

