.lanes * {
  box-sizing: border-box;
}

.lanes {
  width: 273px;
  height: 42px;
  background-color: #646464;
  box-sizing: border-box;

  padding: 0 3px;

  position: relative;
}
.lanes__background {
  top: 0;
  position: absolute;
  background-color: rgba(255, 255, 0, 0.5);
  height: 100%;
}
.lanes__background--left {
  left: 3px;
  border-top-right-radius: 5px;
  width: calc(50% - 3px);
}
.lanes__background--right {
  right: 3px;
  border-top-left-radius: 5px;
  width: calc(50% - 3px);
}

.lanes__icons {
  display: flex;
  align-items: center;
  transform: translateY(0px);
  height: 100%;
}
.lanes__icons__icon {
  width: 42px;
  height: 42px;
  display: grid;
  place-items: center;
}

.lanes-dividers {
  height: 100%;
  display: flex;
  gap: 42px;

  position: absolute;
  top: 0;
}
.lanes-dividers__divider {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 2px;
}
.lanes-dividers__divider div {
  background-color: #646464;
  width: 3px;
}

