:root {
  --LiveSportList_marginCover: 20px;
  --LiveSportList_marginSeparator: 10px;
}

:global(#root) > .SE_0009_LiveSportList {
  width: 100%;
  margin: 0px;
  border: 1px solid yellow;
}

.SE_0009_LiveSportList .liveSportList {
  width: 100%;
  height: var(--minTouchSize);
  display: flex;
}

.SE_0009_LiveSportList.fpk_LiveSportList .liveSportList .leftLiveSportList,
.SE_0009_LiveSportList.fpk_LiveSportList .liveSportList .rightLiveSportList {
  display: none;
}

.SE_0009_LiveSportList .liveSportList .fpkLiveSportList {
  display: none;
}

.SE_0009_LiveSportList .liveSportList .leftLiveSportList,
.SE_0009_LiveSportList .liveSportList .rightLiveSportList {
  display: flex;
}

.SE_0009_LiveSportList .liveSportList .leftLiveSportList,
.SE_0009_LiveSportList.textCover_LiveSportList
  .liveSportList
  .leftLiveSportList {
  width: 60%;
}

.SE_0009_LiveSportList .liveSportList .rightLiveSportList,
.SE_0009_LiveSportList.textCover_LiveSportList
  .liveSportList
  .rightLiveSportList {
  width: 40%;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .containerCoverLiveSportList:first-child {
  margin-left: var(--LiveSportList_marginCover);
}

.miniPlayerCoverLeft {
  margin-left: 3px !important;
  width: 101px !important;
}

.miniPlayerCoverRight {
  margin-left: 0px !important;
  width: 101px !important;
}

.textContainerLiveSportListMiniPlayer {
  max-width: 790px !important;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .containerCoverLiveSportList:last-child {
  margin-left: 150px;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .containerCoverLiveSportList,
.SE_0009_LiveSportList.fpk_LiveSportList
  .liveSportList
  .fpkLiveSportList
  .containerCoverLiveSportList {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%;
  width: fit-content;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .containerCoverLiveSportList {
  width: 252px;
  flex-shrink: 0;
}

.SE_0009_LiveSportList .liveSportList .leftLiveSportList .coverLiveSportList {
  background-image: url(../../../../../Globals/assets/defaultCover.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: var(--coverSizeList);
  width: var(--coverSizeList);
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: left;
}

.SE_0009_LiveSportList.textCover_LiveSportList
  .liveSportList
  .leftLiveSportList
  .coverLiveSportList {
  background-image: none;
  width: 252px;
  min-width: var(--coverSizeList);
}

.SE_0009_LiveSportList .liveSportList .leftLiveSportList .coverLiveSportList p {
  display: none;
}

.SE_0009_LiveSportList.textCover_LiveSportList
  .liveSportList
  .leftLiveSportList
  .coverLiveSportList
  p {
  display: inline-block;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList {
  width: calc(100% - (var(--coverSizeList) * 2));
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList,
.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .bottomTextLiveSportList {
  display: flex;
  justify-content: center;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList {
  width: 100%;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList
  .boxTextLiveSportList:nth-child(1),
.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList
  .boxTextLiveSportList:nth-child(3) {
  width: 50%;
}
.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList
  .boxTextLiveSportList:nth-child(1) {
  text-align: right;
}
.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList
  .boxTextLiveSportList:nth-child(3) {
  text-align: left;
}

.SE_0009_LiveSportList
  .liveSportList
  .leftLiveSportList
  .textContainerLiveSportList
  .upperTextLiveSportList
  .boxTextLiveSportList:nth-child(2) {
  margin: 0 var(--LiveSportList_marginSeparator);
}

.SE_0009_LiveSportList .liveSportList .rightLiveSportList {
  justify-content: center;
  align-items: center;
}

.SE_0009_LiveSportList
  .liveSportList
  .rightLiveSportList
  .textContainerLiveSportList {
  overflow: hidden;
  margin: 0 var(--zA4);
}

.SE_0009_LiveSportList.fpk_LiveSportList .liveSportList {
  width: 100%;
  height: 63px;
  display: flex;
}

.SE_0009_LiveSportList.fpk_LiveSportList .liveSportList .fpkLiveSportList {
  width: 100%;
  height: 100%;
  display: flex;
}

.SE_0009_LiveSportList.fpk_LiveSportList
  .liveSportList
  .fpkLiveSportList
  .fpkBoxTextLiveSportList {
  width: calc(50% - var(--listIconSize) / 2);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  margin: 0 var(--zA4);
}
.SE_0009_LiveSportList.fpk_LiveSportList
  .liveSportList
  .fpkLiveSportList
  .fpkBoxTextLiveSportList:first-child {
  margin-right: var(--iA1);
}
.SE_0009_LiveSportList.fpk_LiveSportList
  .liveSportList
  .fpkLiveSportList
  .fpkBoxTextLiveSportList:last-child {
  margin-left: var(--iA1);
}

.favoriteContainer {
  position: relative;
  min-width: var(--minTouchSize);
}

.SE_0009_LiveSportList .settings {
  position: absolute;
  top: 50%;
}
