:global(#root) > .T_0205_FilterOverlay {
  margin: 5px;
  background-color: rgb(var(--bodyColorGrey));
  height: 673px;
  width: 840px;
  position: relative;
}

.T_0205_FilterOverlay .filter {
  width: 100%;
  height: 100%;
  position: absolute;
}

/*---------------- Titleline--------------*/

.T_0205_FilterOverlay .filter .filterTitleLine {
  height: var(--minTouchSize);
  width: 100%;
  display: none;
}

.T_0205_FilterOverlay .filter .filterTitleLine {
  position: relative;
  display: flex;
}

.T_0205_FilterOverlay .filter .filterTitleLine .UE_0101_ActionButton {
  position: relative;
  height: 100%;
  width: 119px;
  align-self: center;
}

.T_0205_FilterOverlay
  .filter
  .filterTitleLine
  .UE_0101_ActionButton:last-of-type {
  margin-left: auto;
}

.T_0205_FilterOverlay .filter .filterTitleLine p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.T_0205_FilterOverlay .filter .filterTitleLine .headline {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  width: calc(100% - 2 * var(--minTouchSize) - 2 * var(--lA1));
}

.T_0205_FilterOverlay .filter .filterTitleLine > .headline p.textPrio1 {
  position: relative;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
/*---------------- T_0103_ListVertical--------------*/

.T_0205_FilterOverlay .filter .T_0103_ListVertical {
  height: calc(100% - var(--popupInsetWidth) * 3 - var(--minTouchSize));
  width: 100%;
  position: relative;
  margin-top: var(--popupInsetWidth);
  margin-bottom: var(--popupInsetWidth);
}

.T_0205_FilterOverlay .filter .T_0103_ListVertical .list {
  padding: 0 13px 0 9px;
}

.T_0205_FilterOverlay .filter.right .T_0103_ListVertical {
  margin-right: var(--minTouchSize);
  margin-left: 0px;
}

.T_0205_FilterOverlay
  .filter
  .T_0103_ListVertical
  .list::-webkit-scrollbar-track,
.T_0205_FilterOverlay
  .filter
  .T_0103_ListVertical
  .list::-webkit-scrollbar-track {
  display: none;
}

.T_0205_FilterOverlay
  .filter.right
  .T_0103_ListVertical
  .list::-webkit-scrollbar-track {
  display: initial;
  margin-top: var(--popupInsetWidth);
}

/*------------options---------*/

.T_0205_FilterOverlay .options {
  position: absolute;
  top: 100%;
}
