/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --etaBoxWidth: 464px;
}

:global(#root) > .SE_0038_Nav_ETA_Box {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  margin: 20px;
  width: calc(var(--etaBoxWidth) + var(--minTouchSize) * 2);
  height: var(--minTouchSize);
  border-radius: var(--standardBorderRadius);
}

:global(#root) > .SE_0038_Nav_ETA_Box .etaBox {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}

.UE_0101_ActionButton {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}

.etaContent {
  position: relative;
  display: flex;
  flex-direction: row;
  width: var(--etaBoxWidth);
  flex-grow: 0;
  flex-shrink: 0;
}

.unit {
  color: rgba(var(--audiWhite), 0.7);
  margin-left: var(--zA8);
}

.etaCol1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-left: var(--iA2);
  margin-top: var(--iA1);
  margin-bottom: var(--iA1);
  width: 89px;
}

.etaCol2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin-left: var(--iA1);
  margin-top: var(--iA1);
  margin-bottom: var(--iA1);
  flex: 1;
}

.etaCol2_1,
.etaCol3_1 {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  margin-bottom: 5px;
}

.etaCol3 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin-left: var(--iA1);
  margin-right: var(--iA2);
  margin-top: var(--iA1);
  margin-bottom: var(--iA1);
  flex: 1;
}

/* --------------------------------- colors --------------------------------- */

.etaBox.green .etaContent .etaCol2 p {
  color: rgb(var(--fpkGreen));
}

.etaBox.orange .etaContent .etaCol2 p {
  color: rgb(var(--fpkOrange));
}

.etaBox.red .etaContent .etaCol2 p {
  color: rgb(var(--fpkRed));
}
