/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0052_CarToggleBar {
  margin: 10px 0;
  height: var(--carToggleHeight);
  background-color: rgb(var(--bodyColorGrey));
}

.SE_0052_CarToggleBar {
  position: relative;
}

.carToggle {
  position: relative;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  background-clip: content-box;
}

.carToggle.tabbarVersionDS {
  width: calc(100% - 139px);
}

.carToggle .button {
  flex: 1;
  position: relative;
  height: var(--minTouchSize);
  overflow: hidden;
}

.rsContainer {
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: row;
  max-width: calc(100% - var(--touchIconSize));
}

.carToggle.rs .button .textRS {
  font-family: var(--fontExtendedBold);
  margin: 0;
}

.carToggle.rs .button.rs .touchIcon {
  position: unset;
  transform: unset;
  min-width: var(--touchIconSize);
}

.carToggle.rs .button.rs {
  display: flex;
}

.carToggle .button.rs {
  display: none;
}

.carToggle.fas .button:nth-of-type(n + 5) {
  display: none;
}

.carToggle .button .textPrio2 {
  white-space: nowrap;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  margin-left: var(--textButtonMargin);
  margin-right: var(--textButtonMargin);
}

.dSelectAssistantIcon,
.dSelectAssistantIconDSTV {
  position: absolute;
  transform: translateY(-50%);
  width: 21px;
  height: 21px;
  top: calc(50% + 2px);
  right: 35px;
  background-image: url(../../../ST_0005_DriveSelect/01_HTML/img/driveSelectAssistant-Icon.png);
}
.dSelectAssistantIcon.isEfficiency {
  right: 28px;
}

.dSelectAssistantIcon.isBalanceOffroad {
  right: 39px;
}

.dSelectAssistantIcon.isRs2 {
  right: 30px;
}

.tabbarVersionDS .dSelectAssistantIcon {
  right: 25px;
}

.tabbarVersionDS .dSelectAssistantIcon.isEfficiency {
  right: 18px;
}

.tabbarVersionDS .dSelectAssistantIcon.isBalanceOffroad {
  right: 29px;
}

.tabbarVersionDS .dSelectAssistantIcon.isRs2 {
  right: 20px;
}

.dSelectAssistantIconDSTV {
  right: 25px;
}

.buttonDSTabbarVersion {
  width: 139px;
  position: absolute;
  right: 0;
  top: 0;
}

.textDSTabbarVersion {
  background-image: url(../../../../../UE_Elemente/02_ButtonGroup/UE_0201_ToggleButtonGroup/01_HTML/assets/driveSelect.png);
  width: 60px;
  height: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
}

.textDSTabbarVersion.activeAssistantDSTV {
  left: 25px;
  transform: translateY(-50%);
}
