/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --checkboxSize: 28px; /* adjust together with radioButtonSize */
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_1101_Checkbox {
  background-color: rgb(var(--bodyColorGrey));
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  padding: 0 20px;
}

:global(#root) > .UE_1101_Checkbox.showLabel {
  width: 319px;
}

/*-------------------- UE_1101_Checkbox --------------------*/

.UE_1101_Checkbox {
}

.checkboxContainer {
  position: relative;
  display: inline-flex;
  height: 100%;
  max-width: 100%;
  left: 50%;
  transform: translateX(-50%);
}
.checkbox {
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: var(--checkboxSize);
  height: var(--checkboxSize);
  cursor: pointer;
  -webkit-user-select: none;
}
.text {
  position: relative;
  width: calc(100% - 40px);
  margin-left: var(--iA1);
  margin-top: auto;
  margin-bottom: auto;
  top: 1px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: none;
}
.checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  height: calc(var(--checkboxSize) - 2px);
  width: calc(var(--checkboxSize) - 2px);
  background-color: transparent;
  border: 2px solid rgb(var(--audiWhite));
}

.checkbox input:checked ~ .checkmark {
  border: 2px solid;
  border-image: linear-gradient(
      25deg,
      rgba(var(--audiWhite), 1) 0%,
      rgba(var(--audiWhite), 1) 86%,
      rgba(var(--audiWhite), 0) 86%
    )
    1;
}

.checkbox input:checked ~ .checkmark:after {
  display: block;
}

.checkmark:after {
  position: absolute;
  top: -17px;
  left: 10px;
  width: 11px;
  height: 31px;
  border: solid rgb(var(--audiWhite));
  border-width: 0 2px 2px 0;
  transform: rotate(35deg) skewY(19deg);
  content: '';
  display: none;
}

/*---------- showLabel ----------*/
.showLabel .text {
  display: block;
}
.showLabel .checkbox {
  left: unset;
  transform: translateY(-50%);
}

/*---------- settings ----------*/
.settings {
  position: absolute;
  top: 200px;
}
