:global(#root) > .SE_0079_01_LogbookEdit_DashboardTile {
  width: 590px;
  height: 460px;
  margin: 0px;
  overflow: hidden;
}

.SE_0079_01_LogbookEdit_DashboardTile {
  height: 100%;
  width: 100%;
}

.logbookDashboardTile {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--iA1);
  box-sizing: border-box;
}

.logbookDashboardTile_containerText {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logbookDashboardTile_centralContainer {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: var(--iA1) 0;
  position: relative;
}

.logbookDashboardTile_bigBox,
.logbookDashboardTile_smallBox {
  display: flex;
  border: 1px solid rgba(var(--audiWhite), 0.2);
  align-items: center;
  position: relative;
}

.logbookDashboardTile_bigBox {
  padding: 20px var(--iA1);
}

.logbookDashboardTile_smallBox {
  padding: 8px var(--iA1);
}

.logbookDashboardTile_containerStop {
  display: flex;
}

.logbookDashboardTile_time,
.logbookDashboardTile_containerStop,
.ldt_am_pm_start {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.logbookDashboardTile_unitHour {
  margin-top: 18px;
  margin-left: 5px;
  position: absolute;
  left: 113px;
}

.ldt_am_pm {
  display: flex;
}

.time_am {
  margin-top: 2px;
}

.time_pm {
  margin-top: 10px;
  margin-left: 1px;
}

.listIcon {
  opacity: 0.4;
}

.ldt_am_pm_start .time_am,
.ldt_am_pm_start .time_pm {
  position: absolute;
  right: -31px;
}

.logbookDashboardTile_arrowContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logbookDashboardTile_line {
  width: 4px;
  background-color: rgb(var(--audiWhite));
}

.lineStop1 {
  height: 93px;
}
.lineStop1.withStop {
  height: 10px;
}

.lineStop2 {
  height: 20px;
}

@media only screen and (max-height: 720px) {
  :global(#root) > .SE_0079_01_LogbookEdit_DashboardTile {
    height: 482px;
  }

  .lineStop1 {
    height: 109px;
  }
  .lineStop1.withStop {
    height: 15px;
  }

  .lineStop2 {
    height: 25px;
  }
}

@media only screen and (min-width: 2219px) {
  :global(#root) > .SE_0079_01_LogbookEdit_DashboardTile {
    height: 579px;
  }

  .lineStop1 {
    height: 204px;
  }
  .lineStop1.withStop {
    height: 60px;
  }

  .lineStop2 {
    height: 73px;
  }
}
