/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --buttonWidth: 600px;
}

/* ------------------------ Standalone ------------------------ */

:global(#root) > .SE_0023_Display_ON_OFF {
  position: absolute;
  height: 80%;
  width: 100%;
  margin: 0;
}

/* ------------------------ .SE_0023_Display_ON_OFF ------------------------ */
.SE_0023_Display_ON_OFF {
}

.background {
  position: absolute;
  height: 100%;
  width: 100%;
  margin: 0;
  background-color: rgb(var(--audiBlack));
}

.container {
  position: absolute;
  height: calc(100% - var(--climateBarHeight));
  width: 100%;
  z-index: 500;
  display: flex;
  flex-direction: column;
}

.topBar {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-self: flex-start;
  height: var(--minTouchSize);
  width: 100%;
}

.topBar .button:first-of-type {
  position: absolute;
  align-self: flex-start;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.topBar .startCleaningButton {
  position: relative;
  align-self: center;
  height: var(--minTouchSize);
  width: var(--buttonWidth);
  margin-top: 0;
}

.UE_0101_ActionButton {
  width: var(--buttonWidth);
}

.button p {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: fit-content;
}

.actionButtonWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: fit-content;
  width: fit-content;
  margin: auto;
}

/* ------------------------ Variants ------------------------ */

.variant {
  background: rgb(var(--audiBlack));
  display: flex;
  height: 100%;
  width: 100%;
  z-index: 500;
  margin: 0;
  position: relative;
  flex-direction: column;
  align-items: center;
}

.tapReactivate .variant {
  justify-content: center;
  background-color: rgb(var(--audiBlack));
}

.stopCleaning .variant {
  height: 100%;
  background-color: rgb(var(--audiBlack));
}

/* ------------------------ Settings ------------------------ */

.settings {
  position: absolute;
  background-color: rgb(var(--audiBlack));
  height: 20%;
  width: 100%;
  top: 670px;
}
