/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --Dialog_maxWidth: 1300px;
  --Dialog_minWidth: 500px;
}

:global(#root) > .T_0301_Toast {
  margin: 8px;
}

.T_0301_Toast {
  height: 100%;
  max-width: var(--Dialog_maxWidth);
  min-width: var(--Dialog_minWidth);
}

/*------------------------------------------- Darken screen ------------------------------------------------*/
.T_0101_Dashboard.toastShown {
  opacity: 0.4;
}

/*----------------POPUP FRAME DESIGN--------------*/
.Toast_Container {
  width: 100%;
  height: auto;
  flex-direction: column;
}

/*----------------TOAST CONTENT--------------*/
.Toast_Content {
  align-items: center;
  justify-content: left;
  padding: var(--popupContentPadding);
  overflow: hidden;
  max-width: 100%;
}
