/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --hvTempBarWidth: 403px;
  /* --hvTempLabelOffsetLeft: 21px;
  --hvTempLabelOffsetRight: 24px; */
}

:global(#root) > .UE_6051_HV_Temperatur {
}

.UE_6051_HV_Temperatur {
  position: absolute;
  width: calc(var(--hvTempBarWidth));
  height: 90px;
}

/*--------------------------- Labels ----------------------------------*/

.hvTemperatur .valueLabels {
  position: absolute;
  top: 0px;
  width: 100%;
}

.valueLabels .SpritesheetDigits {
  position: absolute;
  top: 10px;
}
.valueLabels .SpritesheetDigits.digit1 {
  position: absolute;
  left: 8px;
}
.valueLabels .SpritesheetDigits.digit2 {
  position: absolute;
  left: 127px;
}
.valueLabels .SpritesheetDigits.digit3 {
  position: absolute;
  left: 249px;
}
.valueLabels .SpritesheetDigits.digit4 {
  position: absolute;
  right: -1px;
}
.valueLabels .SpritesheetDigits.digit4 .digit {
  background-color: rgb(var(--warningRed));
}

.hvTemperatur .valueLabels .minus {
  width: 7px;
  height: 2px;
  top: 18px;
  left: 0px;
  position: absolute;
  background-color: rgb(var(--audiWhite));
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6051_HV_Temperatur.UE_6051_00_state_1
  .hvTemperatur
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6051_HV_Temperatur.UE_6051_00_state_1
  .hvTemperatur
  .valueLabels
  .SpritesheetDigits
  .unit,
.UE_6051_HV_Temperatur.UE_6051_00_state_1 .hvTemperatur .fpkStatusIcon,
.UE_6051_HV_Temperatur.UE_6051_00_state_1 .hvTemperatur .minus {
  background-color: rgb(var(--bargraphInactive));
}

/*--------------------------- powermeter ----------------------------------*/

.hvTemperatur {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.hvTemperatur .hvTempBargraph {
  position: absolute;
  width: var(--hvTempBarWidth);
  top: 34px;
  left: 0;
}

.hvTemperatur .fpkStatusIcon.E355_HV_Batterie_Temp {
  position: absolute;
  top: 37px;
  left: 170px;
  -webkit-mask-image: url(../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E355_HV_Batterie_Temp.png);
}

/*---------- Bordcomputer Config ---------- */
.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
