/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --conbarBCWidth: 500px;
  --conbarBCLeftBarWidth: 191px;
  --conbarBCRightBarCenterGap: 3px;
  --conbarBCRightBarWidth: calc(var(--conbarBCWidth) - var(--conbarBCLeftBarWidth) - var(--conbarBCRightBarCenterGap));
  --conbarBCLabelHeight: var(--lineHeightPrio2);
  --conbarBCGapHeight: 17px;
  --conbarBCAvgHeight: 49px;
}

:global(#root) > .UE_6040_Verbrauchsbalken {
  left: 15px;
}

.UE_6040_Verbrauchsbalken {
  position: absolute;
}

.consumptionLoc {
  position: relative;
  top: 0px;
  left: 0px;
  width: var(--conbarBCWidth);
  height: calc(var(--conbarBCLabelHeight) + (2 * var(--conbarBCGapHeight)) + var(--newbarHeight) + var(--conbarBCAvgHeight));
}

.UE_6040_Verbrauchsbalken.UE_6040_01,
.UE_6040_Verbrauchsbalken.UE_6040_03,
.UE_6040_Verbrauchsbalken.UE_6040_04,
.UE_6040_Verbrauchsbalken.UE_6040_05 {
  width: calc(var(--conbarBCRightBarWidth) + 1px);
}

.UE_6040_03 .consumptionLoc .rightbar,
.UE_6040_04 .consumptionLoc .rightbar,
.UE_6040_05 .consumptionLoc .rightbar {
  left: calc(var(--conbarBCLeftBarWidth) + var(--conbarBCRightBarCenterGap));
}

.UE_6040_01,
.UE_6040_03,
.UE_6040_04,
.UE_6040_05 {
  --conbarBCLeftBarWidth: 0px;
  --conbarBCRightBarWidth: 499px;
  --conbarBCRightBarCenterGap: 0px;
}


/*--------------------------- Labels ----------------------------------*/
.consumptionLoc .valueLabels {
  position: absolute;
  top: 6px;
  left: 0px;
  width: 100%;
}

.consumptionLoc .valueLabels > *{
  margin-top: 0px;
}


.valueLabels .textPrio2 {
  position: absolute;
  color: rgba(var(--audiWhite), 0.7);
}
.UE_6040_00 .valueLabels .digit1 {
  left: calc(73px - 25px);
}
.UE_6040_00 .valueLabels .digit2 {
  left: calc(var(--conbarBCLeftBarWidth) - 9px);
}
.UE_6040_00 .valueLabels .digit3 {
  left: calc(var(--conbarBCLeftBarWidth) + 118px - 19px);
}
.UE_6040_00 .valueLabels .digit4 {
  left: calc(var(--conbarBCLeftBarWidth) + (118px * 2) - 19px);
}


.UE_6040_01 .valueLabels .digit1 {
  left: 0px;
}
.UE_6040_01 .valueLabels .digit2 {
  left: calc(45% - 19px);
}
.UE_6040_01 .valueLabels .digit3 {
  left: calc(90% - 19px);
}
.UE_6040_01 .consumptionLoc .rightbar {
  left: calc(var(--conbarBCLeftBarWidth) + var(--conbarBCRightBarCenterGap));
}


.UE_6040_02 {
  --conbarBCLeftBarWidth: 77px;
  --conbarBCRightBarWidth: calc(var(--conbarBCWidth) - var(--conbarBCLeftBarWidth) - var(--conbarBCRightBarCenterGap));
}

.UE_6040_02 .valueLabels {
  width: calc(100% - var(--conbarBCLeftBarWidth));
  left: calc(var(--conbarBCLeftBarWidth) - 9px);
}

.UE_6040_02 .valueLabels .digit1 {
  left: 0px;
}
.UE_6040_02 .valueLabels .digit2 {
  left: 87px;/* calc(23% - 10px); */
}
.UE_6040_02 .valueLabels .digit3 {
  left: 167px;/* calc(44% - 19px); */
}
.UE_6040_02 .valueLabels .digit4 {
  left: 264px;/* calc(67% - 19px); */
}
.UE_6040_02 .valueLabels .digit5 {
  left: 362px;/* calc(90% - 19px); */
}


.UE_6040_03 .valueLabels .digit1 {
  left: 0px;
}
.UE_6040_03 .valueLabels .digit2 {
  left: calc(23% - 10px);
}
.UE_6040_03 .valueLabels .digit3 {
  left: calc(44% - 19px);
}
.UE_6040_03 .valueLabels .digit4 {
  left: calc(67% - 19px);
}
.UE_6040_03 .valueLabels .digit5 {
  left: calc(90% - 19px);
}


.UE_6040_04 .valueLabels .digit1 {
  left: 0px;
}
.UE_6040_04 .valueLabels .digit2 {
  left: calc(18% - 10px);
}
.UE_6040_04 .valueLabels .digit3 {
  left: calc(36% - 19px);
}
.UE_6040_04 .valueLabels .digit4 {
  left: calc(54% - 19px);
}
.UE_6040_04 .valueLabels .digit5 {
  left: calc(72% - 19px);
}
.UE_6040_04 .valueLabels .maxLbl {
  left: calc(90% - 31px);
}


.UE_6040_05 .valueLabels .digit1 {
  left: 0px;
}
.UE_6040_05 .valueLabels .digit2 {
  left: calc(30% - 10px);
}
.UE_6040_05 .valueLabels .digit3 {
  left: calc(60% - 19px);
}
.UE_6040_05 .valueLabels .digit4 {
  left: calc(90% - 19px);
}


/*--------------------------- legend ----------------------------------*/
.consumptionLoc .legend {
  position: absolute;
  display: flex;
  align-items: baseline;
  justify-content: center;
  top: calc(var(--conbarBCLabelHeight) + (2 * var(--conbarBCGapHeight)) + var(--newbarHeight));
  width: 100%;
}

.consumptionLoc .legend .consumptionUnit {
  text-align: center;
  align-self: center;
}

.consumptionLoc .legend .consumptionUnit .divider {
  width: 100%;
  height: 1px;
  background-color: rgb(var(--fpkGrey));
}

.consumptionLoc .legend .legendValue {
  text-align: right;
  margin-left: 8px;
  margin-right: 5px;
  margin-top: 5px;
}

.consumptionLoc .textPrio2,
.consumptionLoc .textPrio3 {
  color: rgba(var(--audiWhite), 0.7);
}

.consumptionLoc
  .legend
  .consumptionUnit.singleLine {
  align-self: unset;
  width: auto;
}

.consumptionLoc
  .legend
  .consumptionUnit.singleLine
  p:first-child {
  text-align: left;
  margin-top: 13px;
}

.consumptionLoc
  .legend
  .consumptionUnit.singleLine
  p:last-child,
.consumptionLoc
  .legend
  .consumptionUnit.singleLine
  .divider {
  display: none;
}

/*--------------------------- powermeter ----------------------------------*/
.consumptionLoc .leftbar {
  position: absolute;
  width: var(--conbarBCLeftBarWidth);
  top: calc(var(--conbarBCLabelHeight) + var(--conbarBCGapHeight));
  left: -1px;
}

.consumptionLoc .rightbar {
  position: absolute;
  width: calc(var(--conbarBCRightBarWidth));
  top: calc(var(--conbarBCLabelHeight) + var(--conbarBCGapHeight));
  left: calc(var(--conbarBCLeftBarWidth) + var(--conbarBCRightBarCenterGap) - 1px);
}

/*---------- Config --------*/

.pwrConfig {
  position: absolute;
  margin-top: 100px;
  width: 100%;
  color: white;
}

.sliderDemo {
  margin-top: 100px;
  width: var(--gaugeBarWidth);
  color: white;
}

.sliderDemo .pwrslider {
  width: 100%;
}

.ThrottleLimitedDemo {
  color: white;
}

.RecuLimitedDemo {
  color: white;
}

.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
