/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --relSliderWidth: 280px;
  --seatsTopviewWidth: 480px;
  --seatsTopviewHeight: 490px;
  --seatsMarginTop: 20px;
  --seatsHeightDiff: 120px;
  --climateSideMargin: 80px;
}

:global(#root) > .ST_0066_ClimateMenu {
  position: absolute;
  width: 100%;
  height: 100%;
  margin-left: 0;
  overflow: hidden;
}

:global(#root) > .ST_0066_ClimateMenu .bodyWrapper {
  position: absolute;
  width: 1800px;
  height: 816px;
  background-color: rgb(var(--audiBlack));
}

.ST_0066_ClimateMenu {
}

.horizontalAlign {
