  height: calc(100% - 109px);
  width: 100%;
  overflow: hidden;
  position: absolute;
}

.main .bgTouchButtons {
  left: 50%;
  top: 50%;
  transform: translateY(calc(-50% - var(--minTouchSize) / 2)) translateX(-50%);
  display: flex;
  position: absolute;
  width: calc(100% - 2 * (318px));
  height: calc(var(--minTouchSize) * 3);
  justify-content: space-between;
  z-index: 3;
}

.main .bgTouchButtons.isB10 {
  width: calc(100% - 2 * (308px));
}

.main .bgTouchButtons.oneInfo {
  top: 48%;
}
.main .bgTouchButtons.isOneInfoZone {
  top: 48%;
  left: 43%;
}

.content.rear .main .bgTouchButtons {
  width: calc(100% - 2 * (350px));
}

.main .button {
  width: 100%;
}

.main .bgTouchButtons .left,
.main .bgTouchButtons .right {
  height: 100%;
  width: 220px;
  flex-direction: column;
  display: flex;
  justify-content: center;
}

.main .bgTouchButtons .left {
  margin-right: 89px;
}

.main .bgTouchButtons .right {
  margin-left: 89px;
}

.main .bgTouchButtons .button {
  background-color: unset;
}

.main .bgTouchButtons .button {
