}

.main .bgTouchButtons .button .icon,
.buttons3row .icon {
  position: absolute;
  width: var(--minTouchSize);
  height: 100%;
}

.buttons3row .icon {
  right: 0;
}

.main .bgTouchButtons .left .button {
  float: right;
}

.main .bgTouchButtons .left .button .icon {
  border: 0;
}

.main .bgTouchButtons .right .button {
  float: left;
}

.main .bgTouchButtons .right .button .icon {
  border: 0;
  right: 0;
}
.main .bgTouchButtons .left .button .sectionLining,
.main .bgTouchButtons .left .button .buttonBackground,
.main .bgTouchButtons .left .button .uiState {
  -webkit-mask-image: -webkit-gradient(
    linear,
    right top,
    left top,
    color-stop(0, rgba(0, 0, 0, 0.1)),
    color-stop(1, rgba(0, 0, 0, 1))
