    linear,
    right top,
    left top,
    color-stop(0, rgba(0, 0, 0, 1)),
    color-stop(1, rgba(0, 0, 0, 0.1))
  );
}

.main .bgTouchButtons .left .button .uiState,
.button.fadeOutRight .uiState {
  width: var(--minTouchSize);
  left: var(--uiStateMargin);
  margin-left: unset;
}

.main .bgTouchButtons .right .button .uiState,
.buttons3row .button .uiState,
.button.fadeOutLeft .uiState {
