/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --tabBarHeight: 63px;
  --tabFocusWidth: 526px;
  --tabFocusWidthRS: 631px;
  --tabCollapsedWidth: 65px;
  --cardHeight: calc(var(--cardContentHeight) + var(--tabBarHeight));
  --cardWidth: 665px;
  --cardWidth_RS: 770px;
  --sectionLiningWhite: repeating-linear-gradient(
    to bottom,
    rgb(var(--audiWhite)),
    rgb(var(--audiWhite)) 1px,
    transparent 1px,
    transparent 3px
  );
}
