/*---------- content ----------*/
/*------ container ------*/
.UE_6200_Cards .card .contentContainer {
  position: absolute;
  height: var(--cardContentHeight);
  width: var(--cardContentWidthGB);
  top: var(--tabBarHeight);
  overflow: hidden;
}

.UE_6200_Cards.left .card .contentContainer {
  right: 0px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    right top,
    left top,
    color-stop(0, rgba(0, 0, 0, 0)),
    color-stop(0.03, rgba(0, 0, 0, 1)),
    color-stop(0.99, rgba(0, 0, 0, 1)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  transition:
    width var(--animationDuration) var(--animationCurve),
    right var(--animationDuration) var(--animationCurve);
}
.UE_6200_Cards.right .card .contentContainer {
  left: 0px;
  -webkit-mask-image: -webkit-gradient(
    linear,
    left top,
    right top,
    color-stop(0, rgba(0, 0, 0, 0)),
    color-stop(0.03, rgba(0, 0, 0, 1)),
    color-stop(0.99, rgba(0, 0, 0, 1)),
    color-stop(1, rgba(0, 0, 0, 0))
  );
  transition:
    width var(--animationDuration) var(--animationCurve),
    left var(--animationDuration) var(--animationCurve);
}
.UE_6200_Cards.left .card.kb .contentContainer {
  right: 45px;
  width: var(--cardContentWidthKB);
  transition:
    width var(--animationDuration) var(--animationCurve),
    right var(--animationDuration) var(--animationCurve);
}
.UE_6200_Cards.right .card.kb .contentContainer {
  left: 45px;
  width: var(--cardContentWidthKB);
  transition:
    width var(--animationDuration) var(--animationCurve),
    left var(--animationDuration) var(--animationCurve);
}

.UE_6200_Cards.right .card .contentContainer.fullWidth {
  position: absolute;
  height: var(--cardContentHeight);
  width: calc(100% - 1px);
  top: var(--tabBarHeight);
  overflow: hidden;
  -webkit-mask-image: unset;
  transition: unset;
  left: unset;
}

