/*-------- animation --------*/
/*------ tabBar ------*/
.UE_6200_Cards .tab {
  width: var(--tabCollapsedWidth);
}

.UE_6200_Cards .tab.tabFocus .text {
  opacity: 1;
}

.UE_6200_Cards .tab.tabNext1 .tabHighlight,
.UE_6200_Cards .tab.tabNext2 .tabHighlight,
.UE_6200_Cards .tab.tabNext3 .tabHighlight,
.UE_6200_Cards .tab.tabNext1 .text,
.UE_6200_Cards .tab.tabNext2 .text,
.UE_6200_Cards .tab.tabNext3 .text {
  opacity: 0;
}

.UE_6200_Cards.left .card.temp .tab.tabFocus::after,
.UE_6200_Cards.right .card.temp .tab.tabNext2::after {
  opacity: 0;
}

.UE_6200_Cards .card.anim.temp .tab::after {
  transition: opacity var(--animationDuration) var(--animationCurve);
}

.UE_6200_Cards.right .tabs .tab.tabNext3 {
  transform: translateX(calc(var(--cardWidth) - 4px));
}
.UE_6200_Cards.left .tabs .tab.tabNext3 {
  transform: translateX(calc(-1 * var(--tabCollapsedWidth)));
}
.T_0002_01_FPKBasicLayoutRS.UE_6200_Cards.right .card.kb .tabs .tab.tabNext3 {
  transform: translateX(calc(var(--cardWidth_RS) - 4px));
}
/* card.anim.temp and card.anim.fix are used for the AS_0902_CardsToggle animation*/

/** Tab Focus Out **/

.UE_6200_Cards.right .card.anim.temp .tab.tabNext3 {
  width: var(--tabFocusWidth);
  animation: slideTabOutToLeft calc(var(--animationDuration))
    var(--animationCurve) forwards;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabNext3 {
  width: var(--tabFocusWidth);
  animation: slideTabOutToRight calc(var(--animationDuration))
    var(--animationCurve) forwards;
}
.UE_6200_Cards .card.anim.temp .tab.tabNext3 .tabHighlight {
  animation: tabFocusContentHide calc(var(--animationDuration) * 1)
    var(--animationCurve) forwards;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabNext3 .text,
.UE_6200_Cards .card.anim.temp .tab.tabNext3 .listIcon {
  animation: tabFocusContentHide calc(var(--animationDuration) * 0.5)
    var(--animationCurve) forwards;
}
.UE_6200_Cards.right .card.anim.temp .tab.tabNext3 .text {
  animation:
    tabFocusTextOut calc(var(--animationDuration)) var(--animationCurve)
      forwards,
    tabFocusContentHide calc(var(--animationDuration) * 0.5)
      var(--animationCurve) forwards;
}

/** Tab Focus In **/

.UE_6200_Cards.right .card.anim.temp .tab.tabFocus {
  animation: slideTabInAsFocusToLeft calc(var(--animationDuration))
    var(--animationCurve) forwards;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabFocus {
  animation: slideTabInAsFocusToRight calc(var(--animationDuration))
    var(--animationCurve) forwards;
}
.UE_6200_Cards .card.anim.temp .tab.tabFocus .tabHighlight {
  animation: tabFocusContentShow calc(var(--animationDuration) * 1)
    var(--animationCurve) calc(var(--animationDuration) * 0) both;
}
.UE_6200_Cards.right .card.anim.temp .tab.tabFocus .line {
  animation: tabFocusLine calc(var(--animationDuration)) var(--animationCurve)
    forwards;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabFocus .text {
  animation: tabFocusContentShow calc(var(--animationDuration) * 0.5)
    var(--animationCurve) calc(var(--animationDuration) * 0.5) both;
}
.UE_6200_Cards.right .card.anim.temp .tab.tabFocus .line .text {
  animation:
    tabFocusTextIn calc(var(--animationDuration)) var(--animationCurve) forwards,
    tabFocusContentShow calc(var(--animationDuration) * 0.5)
      var(--animationCurve) calc(var(--animationDuration) * 0.5) both;
}

/** Tab Next1 In **/

.UE_6200_Cards.right .card.anim.temp .tab.tabNext1 {
  animation: slideTabInAsNext1ToLeft calc(var(--animationDuration) * 0.75)
    var(--animationCurve) calc(var(--animationDuration) * 0.25) both;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabNext1 {
  animation: slideTabInAsNext1ToRight calc(var(--animationDuration) * 0.75)
    var(--animationCurve) calc(var(--animationDuration) * 0.25) both;
}

/** Tab Next2 In **/

.UE_6200_Cards.right .card.anim.temp .tab.tabNext2 {
  animation: slideTabInAsNext2ToLeft calc(var(--animationDuration) * 0.5)
    var(--animationCurve) calc(var(--animationDuration) * 0.5) both;
}
.UE_6200_Cards.left .card.anim.temp .tab.tabNext2 {
  animation: slideTabInAsNext2ToRight calc(var(--animationDuration) * 0.5)
    var(--animationCurve) calc(var(--animationDuration) * 0.5) both;
}
.UE_6200_Cards .card.anim.temp .tab.tabNext2 .listIcon {
  animation: listIconAsNext2Show calc(var(--animationDuration) * 0.5)
    var(--animationCurve) calc(var(--animationDuration) * 0.5) both;
}

