/* CLASSIFICATION: CONFIDENTIAL */

@keyframes slideOutLeft {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  99% {
    transform: translateX(-85%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes slideInLeft {
  0% {
    transform: translateX(65%);
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

@keyframes slideOutRight {
  0% {
    opacity: 1;
  }
  40% {
    opacity: 0;
  }
  99% {
    transform: translateX(85%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@keyframes slideInRight {
  0% {
    transform: translateX(-65%);
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  100% {
    transform: translateX(0%);
    opacity: 1;
  }
}

/** Tab Focus Out **/

@keyframes slideTabOutToLeft {
  0% {
    opacity: 1;
    transform: translateX(0px);
  }
  100% {
    opacity: 0;
    transform: translateX(calc(var(--tabFocusWidth) * -1 - 4px));
  }
}

@keyframes slideTabOutToRight {
  0% {
    opacity: 1;
    transform: translateX(
      calc(var(--tabIndentation) + var(--tabCollapsedWidth) * 2)
    );
  }
  100% {
    opacity: 0;
    transform: translateX(var(--cardWidth));
  }
}

@keyframes tabFocusContentHide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes tabFocusTextOut {
  0% {
    margin-right: 10px;
  }
  100% {
    margin-right: 0px;
  }
}

/** Tab Focus In **/

@keyframes slideTabInAsFocusToLeft {
  0% {
    width: var(--tabCollapsedWidth);
    transform: translateX(var(--tabFocusWidth));
  }
  100% {
    /* add 2px because of separator line*/
    width: calc(var(--tabFocusWidth) + 2px);
    transform: translateX(0px);
  }
}

@keyframes slideTabInAsFocusToRight {
  0% {
    width: var(--tabCollapsedWidth);
    transform: translateX(
      calc(var(--tabIndentation) + var(--tabCollapsedWidth))
    );
  }
  100% {
    width: var(--tabFocusWidth);
    transform: translateX(
      calc(var(--tabIndentation) + var(--tabCollapsedWidth) * 2)
    );
  }
}

@keyframes tabFocusContentShow {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes tabFocusLine {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 26px;
  }
}
@keyframes tabFocusTextIn {
  0% {
    margin-right: 0px;
  }
  100% {
    margin-right: 10px;
  }
}

/** Tab Next1 In **/

@keyframes slideTabInAsNext1ToLeft {
  0% {
    transform: translateX(
      calc(var(--tabFocusWidth) + var(--tabCollapsedWidth))
    );
  }
  100% {
    /* add 2px because of separator line*/
    transform: translateX(calc(var(--tabFocusWidth) + 2px));
  }
}

@keyframes slideTabInAsNext1ToRight {
  0% {
    transform: translateX(var(--tabIndentation));
  }
  100% {
    transform: translateX(
      calc(var(--tabIndentation) + var(--tabCollapsedWidth))
    );
  }
}

/** Tab Next2 In **/

@keyframes slideTabInAsNext2ToLeft {
  0% {
    transform: translateX(calc(var(--cardWidth) - 4px));
  }
  100% {
    transform: translateX(
      /* add 2px because of separator line*/
        calc(var(--tabFocusWidth) + var(--tabCollapsedWidth) + 2px)
    );
  }
}

@keyframes slideTabInAsNext2ToRight {
  0% {
    transform: translateX(calc(-1 * var(--tabCollapsedWidth)));
  }
  100% {
    transform: translateX(var(--tabIndentation));
  }
}

@keyframes listIconAsNext2Show {
  30% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
