/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --progressTrackHeight: 2px;
  --progressIndicatorHeight: 6px;
  --progressIndicatorGapWidth: 5px;
  --circleProgressContainerSize: 50px;
  --circleProgressStrokeWidth: 5;
  --strokeDashArray: 125;
}

.UE_0701_Loader {
}

/*-------------------- standalone properties --------------------*/
:global(#root) > .UE_0701_Loader {
  margin: 0 var(--cA1);
}

:global(#root) > .UE_0701_Loader .UE_0701_00_Line {
  margin: 40px 0;
  width: 290px;
}
:global(#root) > .UE_0701_Loader .UE_0701_01_Circle {
  margin: 40px 32px;
}

/*-------------------- UE_0701_00_Line --------------------*/
.UE_0701_00_Line {
  position: relative;
  width: 100%;
  height: var(--progressIndicatorHeight);
  overflow: hidden;
}

.progressTrack {
  position: absolute;
  width: 100%;
  height: var(--progressTrackHeight);
  top: 50%;
  transform: translateY(-50%);
  background-color: rgb(var(--audiGrey));
}

.progressIndicator {
  position: absolute;
  width: 0%;
  height: var(--progressIndicatorHeight);
  top: 50%;
  transform: translateY(-50%);
  background: -webkit-linear-gradient(
    left,
    rgb(var(--audiLightGrey)),
    rgb(var(--audiWhite))
  );
}

.animation .primary {
  animation: var(--AG_0502_Loader_Waiting_Indicator_Primary);
}
.primary {
  animation: unset;
}

.animation .secondary {
  animation: var(--AG_0502_Loader_Waiting_Indicator_Secondary);
}

.secondary {
  animation: unset;
}

.gap {
  position: absolute;
  width: var(--progressIndicatorGapWidth);
  height: 2px;
  top: 2px;
  right: calc(var(--progressIndicatorGapWidth) * -1);
  background-color: black;
}

/*-------------------- UE_0701_01_Circle --------------------*/

.animation .circleProgressContainer {
  animation: var(--AG_0503_Loader_Waiting_Circle_Rotate);
}

.circleProgressContainer {
  height: var(--circleProgressContainerSize);
  width: var(--circleProgressContainerSize);
  animation: unset;
}

.animation .circleProgress {
  animation: var(--AG_0503_Loader_Waiting_Circle_Lines);
}

.circleProgress {
  r: calc(
    (
        var(--circleProgressContainerSize) - var(--circleProgressStrokeWidth) *
          2px
      ) / 2
  );
  cx: calc(var(--circleProgressContainerSize) / 2);
  cy: calc(var(--circleProgressContainerSize) / 2);
  fill: none;
  stroke: rgb(var(--audiWhite));
  stroke-width: var(--circleProgressStrokeWidth);
  stroke-dasharray: 126;
  stroke-dashoffset: 0;
  transform-origin: center;
  animation: unset;
}

/*-------------------- UE_0701_01_Circle_Scale --------------------*/
.scale {
  position: relative;
  display: flex;
  width: fit-content;
  height: fit-content;
  justify-content: center;
  align-items: center;
}

.scale .circleProgressContainer {
  transform: scale(var(--AG_0503_Loader_Waiting_Circle_Scale));
}

.animation .scale .circleProgressContainer {
  animation: var(--AG_0503_Loader_Waiting_Circle_Rotate_Scale);
}

.scale .circleProgress {
  stroke-width: 2px;
}

/*-------------------- settings --------------------*/
.settings {
  position: absolute;
  top: 150px;
  margin: var(--iA1);
}

.loaderSelect {
  border: 0;
  background-color: white;
  height: 30px;
  border-radius: 4px;
  margin: 0 0 20px 20px;
}
option {
  padding: 500px;
}
