/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --dtmfHeight: calc(
    var(--minTouchSize) + var(--keyboardLineHeight) * 4 + var(--popupInsetWidth) *
      3 + 4px
  );
}

:global(#root) > .T_0206_ScalableOverlay {
  position: absolute;
  height: 670px;
  width: 1100px;
  background-image: var(--backgroundSpot);
  background-color: rgb(var(--ambientBackground1));
  margin: 8px;
}

:global(#root) > .T_0206_ScalableOverlay .scalableOverlay {
  top: 5px;
}

.scalableOverlay {
  position: absolute;
  width: 100%;
  height: 100%;
  flex-direction: column;
  left: 0px;
  top: 0px;
}

.overlayTitleline .button:first-of-type {
  margin-left: var(--popupInsetWidth);
}

.overlayTitleline .button:last-of-type {
  margin-right: var(--popupInsetWidth);
}

/* -------------------------------- WIDTHS AND POSITIONING ACCORDING TO CONTENT ----------------------------------------------------------------------------------------------------- */

.scalableOverlay.dtmf {
  width: 50%;
  height: var(--dtmfHeight);
}

.scalableOverlay.contactCard,
.scalableOverlay.dateSelector {
  width: calc(var(--dashboardWidth) - 2px);
}

.SE_0095_CalendarDateSelector {
  height: calc(100% - var(--minTouchSize) - 5px);
}

.T_0206_ScalableOverlay
  .scalableOverlay
  > .SE_0095_CalendarDateSelector
  .dateSelector {
  align-items: center;
}

.T_0206_ScalableOverlay
  .scalableOverlay
  > .SE_0095_CalendarDateSelector
  .dateSelector
  .header {
  width: calc(100% - var(--opticalTouchReduction));
  margin: unset;
}

/* -------------------------------- titleline ------------------------------- */

.overlayTitleline {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.overlayTitleline .button {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: var(--minTouchSize);
  min-width: var(--minTouchSize);
  align-self: center;
}

.titelLine {
  display: flex;
  flex: 1;
  align-items: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  overflow: hidden;
}

.overlayTitleline .button.text {
  width: fit-content;
  display: flex;
}

.overlayTitleline .button.text p {
  display: block;
  margin: 0 var(--textButtonMargin);
  z-index: 1;
}
/* -------------------------------- T_0103_ListVertical ----------------------------------------------------------------------------------------------------- */
.T_0206_ScalableOverlay .scalableOverlay > .T_0103_ListVertical {
  position: relative;
  height: calc(100% - var(--minTouchSize) - var(--popupInsetWidth) * 2);
  width: calc(100% - 1px);
  overflow: hidden;
}

.T_0206_ScalableOverlay
  .scalableOverlay
  > .T_0103_ListVertical
  .listWrapper
  .elementWrapper
  .list {
  padding-left: var(--popupInsetWidth);
  padding-right: calc(var(--popupInsetWidth) + 4px);
  padding-top: var(--iA1);
  width: calc(100% - var(--popupInsetWidth) * 2);
}

.T_0206_ScalableOverlay
  .scalableOverlay
  > .T_0103_ListVertical
  .listWrapper
  .elementWrapper
  .list::-webkit-scrollbar-track {
  display: none;
}

/* -------------------------------- Exit Junction View ----------------------------------------------------------------------------------------------------- */
.exitJunction {
  flex: 1;
  background-color: rgba(var(--boundingBoxGreen));
  margin: var(--popupInsetWidth);
}

/* -------------------------------- UE_0506_PhoneNumericKeypad ----------------------------------------------------------------------------------------------------- */
.UE_0506_PhoneNumericKeypad .keypad {
  padding: var(--popupInsetWidth);
}

.UE_0506_PhoneNumericKeypad .keypad .keyboardLine:last-of-type {
  border-bottom: none;
}
/* -------------------------------- SE_0024_Contact_Card ----------------------------------------------------------------------------------------------------- */

.SE_0024_Contact_Card {
  height: calc(100% - var(--minTouchSize) - var(--popupInsetWidth) * 2);
  overflow: hidden;
  padding-top: var(--popupInsetWidth);
  padding-bottom: var(--popupInsetWidth);
}

.SE_0024_Contact_Card .T_0103_ListVertical {
  height: 100%;
}

.SE_0024_Contact_Card .T_0103_ListVertical .listWrapper .elementWrapper .list {
  padding-top: calc(var(--iA1) - var(--popupInsetWidth));
  padding-left: var(--popupInsetWidth);
  padding-right: calc(var(--listVerticalPadding) * 2 - var(--popupInsetWidth));
}

/* ------------------------------ small screen ------------------------------ */
@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0206_ScalableOverlay {
    height: 542px;
  }
}

/* ------------------------------ B10 screen ------------------------------ */
@media only screen and (min-width: 2219px) {
  .scalableOverlay.contactCard,
  .scalableOverlay.dateSelector {
    width: calc(var(--dashboardWidth) - (var(--popupOutlineWidth) + 1px) * 2);
  }
}

/* --------------------------------- SETTINGS -------------------------------- */
.settings {
  position: absolute;
  top: 700px;
}
