/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --callSeparatorGap: calc(15px + var(--activeStateStrength) * 2);
  --callMargin: 11px;
  --activeCallWidth: calc((100% - var(--callMargin) * 2) / 12 * 7);
  --inactiveCallWidth: calc((100% - var(--callMargin) * 2) / 12 * 5);
  --activeStateStrength: 4px;
  --coverSizeActiveCall: 220px;
  --coverSizeInactiveCall: 170px;
}

/* ------------------------ Standalone ------------------------ */

:global(#root) > .SE_0011_TwoActiveCalls {
  width: var(--dashboardWidth);
  height: 459px;
  margin: 8px;
}

/* ------------------------ SE_0011_TwoActiveCalls ------------------------ */

.SE_0011_TwoActiveCalls {
}

.twoActiveCalls {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
}

.twoActiveCalls .T_0102_DetailScreen {
  position: relative;
  transition: all 0.4s ease-in-out;
}

.twoActiveCalls .T_0102_DetailScreen div {
  transition: all 0.4s ease-in-out;
}

.twoActiveCalls .T_0102_DetailScreen:first-child {
  margin-right: 11px;
}

.twoActiveCalls .T_0102_DetailScreen:nth-child(2) {
  margin-left: 11px;
}

.twoActiveCalls
  .T_0102_DetailScreen
  .detailScreen
  .musicInfoContainer
  .infoText {
  margin-top: 0px;
}

/*------------------------- CALL SEPARATOR -------------------------*/
.twoActiveCalls .T_0102_DetailScreen:nth-child(2):before {
  content: '';
  position: absolute;
  height: calc(100% - var(--callSeparatorGap) * 2);
  width: 1px;
  background-color: rgba(var(--audiWhite), 0.4);
  left: -11px;
  top: var(--callSeparatorGap);
}

/*------------------------- ACTIVE STATE -------------------------*/
.activeStateContainer {
  position: absolute;
  width: 100%;
  height: var(--activeStateStrength);
  bottom: 3px;
}

.activeState {
  position: absolute;
  width: calc(var(--activeCallWidth) - 35px);
  height: 100%;
  background-color: rgb(var(--audiWhite));
  transition: left 0.2s ease-in-out;
}

.leftCall {
  left: 30px;
}

.rightCall {
  left: calc(100% - var(--activeCallWidth) + 10px);
}

/*------------------------- ACTIVE CALL -------------------------*/
.active {
  width: var(--activeCallWidth);
}

.twoActiveCalls .T_0102_DetailScreen.active .titleLine .textWrapper .textPrio2 {
  color: rgb(var(--audiGreen));
}

.twoActiveCalls .T_0102_DetailScreen.active .cover {
  height: var(--coverSizeActiveCall);
  width: var(--coverSizeActiveCall);
}

/*------------------------- INACTIVE CALL -------------------------*/

.inactive {
  width: var(--inactiveCallWidth);
}

.twoActiveCalls .T_0102_DetailScreen.inactive .cover {
  height: var(--coverSizeInactiveCall);
  width: var(--coverSizeInactiveCall);
}

.twoActiveCalls .T_0102_DetailScreen.inactive .hdVoice {
  opacity: 0 !important;
  transition: opacity 0.1s ease-in-out !important;
}

.twoActiveCalls .T_0102_DetailScreen.inactive .titleLine,
.twoActiveCalls .T_0102_DetailScreen.inactive .infoText {
  margin-left: 0px;
}

.twoActiveCalls
  .T_0102_DetailScreen.inactive
  .detailScreen
  .musicInfoContainer {
  width: calc(100% - var(--coverSizeInactiveCall) - 180px);
}

.settings {
  position: relative;
  width: var(--dashboardWidth);
  height: var(--minTouchSize);
}

.twoActiveCalls .T_0102_DetailScreen.inactive .detailScreen .coverContainer {
  margin: 0px;
  position: absolute;
  height: 100%;
  right: 100px;
  width: fit-content;
}

.twoActiveCalls .T_0102_DetailScreen.inactive .detailScreen {
  margin-left: 60px;
  width: calc(100% - 60px);
  opacity: 0.4;
}

.T_0102_DetailScreen .detailScreen .titleLine {
  grid-column-end: 4;
  margin-right: 0px;
}

.T_0102_DetailScreen .detailScreen .infoText {
  margin-right: unset;
}

@media only screen and (max-width: 1768px) {
  .twoActiveCalls .T_0102_DetailScreen.inactive .detailScreen .coverContainer {
    margin-right: 113px;
  }

  .twoActiveCalls .T_0102_DetailScreen.inactive .detailScreen {
    margin-left: 80px;
  }

  .twoActiveCalls
    .T_0102_DetailScreen.inactive
    .detailScreen
    .titleLine
    .lineTwo {
    max-width: 140px;
  }
}
