.AIO {
  position: absolute;
  height: 100%;
  width: 108px;
  display: flex;
  flex-direction: column;
}

.trafficLight,
.textContainer {
  display: flex;
  justify-content: center;
}

.textContainer {
  align-items: center;
}

.trafficLight {
  height: 108px;
  align-items: flex-end;
}

.trafficlight_vertical_green {
  width: 19px;
  height: 42px;
  background-image: url('./img/trafficlight_vertical_green.png');
}

.trafficlight_horizontal_green_left {
  width: 42px;
  height: 18px;
  background-image: url('./img/trafficlight_horizontal_green_left.png');
  margin-bottom: 3px;
}

.trafficlight_horizontal_green_right {
  width: 42px;
  height: 18px;
  background-image: url('./img/trafficlight_horizontal_green_right.png');
  margin-bottom: 3px;
}

.trafficlight_vertical_red {
  width: 19px;
  height: 42px;
  background-image: url('./img/trafficlight_vertical_red.png');
}

.trafficlight_horizontal_red {
  width: 42px;
  height: 18px;
  background-image: url('./img/trafficlight_horizontal_red.png');
  margin-bottom: 3px;
}
