:root {
  --sliceSE0050StandaloneWidth: 760px;
  --sliceSE0050StandaloneHeight: 230px;
}

:global(#root) > .SE_0050_SliceTemplate {
  height: 100%;
  width: 100%;
  margin: 10px;
}

:global(#root) > .SE_0050_SliceTemplate .slice {
  width: var(--sliceSE0050StandaloneWidth);
  height: var(--sliceSE0050StandaloneHeight);
}

.SE_0050_SliceTemplate .slice .rowOne {
  display: flex;
  width: calc(100% - 20px);
  height: calc(var(--minTouchSize) - 7px);
  align-items: center;
  margin-left: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
}

.SE_0050_SliceTemplate .slice .rowOne .textContainer {
  position: relative;
  flex: 1;
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  overflow: hidden;
}

.SE_0050_SliceTemplate .slice .rowOne .cover + .textContainer {
  margin-left: var(--lA1);
}

.SE_0050_SliceTemplate .slice .rowOne .textContainer p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.SE_0050_SliceTemplate .slice .rowTwo {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: var(--minTouchSize);
}

.SE_0050_SliceTemplate .slice .rowTwo .button {
  position: relative;
  width: calc(100% / 4);
}

/* ---------------------------------- logik --------------------------------- */

.SE_0050_SliceTemplate.noSubTitle .rowOne .textContainer .textPrio2 {
  display: none;
}

.SE_0050_SliceTemplate.twoBtn .slice .rowTwo .button {
  position: relative;
  width: calc(100% / 2);
}

.SE_0050_SliceTemplate.threeBtn .slice .rowTwo .button {
  position: relative;
  width: calc(100% / 3);
}

.SE_0050_SliceTemplate.twoBtn .slice .rowFour .button {
  position: relative;
  width: calc(100% / 4);
}

/* ---------------------------------- LAST VISIBLE --------------------------------- */
.slice.lastvisible .SE_0050_SliceTemplate .slice,
.SE_0050_SliceTemplate.lastvisible .slice {
  opacity: 0.4;
}

/* ---------------------------------- LIST ELEMENT --------------------------------- */

.SE_0050_SliceTemplate .settings {
  z-index: 2;
  margin-top: 50px;
}
