:root {
  --QRCodeScreen_infoRightWidth: 255px;
  --QRCodeScreen_boundingBoxBigSize: 250px;
  --QRCodeScreen_boundingBoxSmallSize: 200px;
  --QRCodeScreen_textLinesMarginTop: 10px;
}

:global(#root) > .T_0113_QRCodeScreen {
  margin: auto;
  width: 1678px;
  height: 553px;
}

.textContainer {
  margin-left: var(--zA4);
  width: 60%;
  height: inherit;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.T_0113_QRCodeScreen.QRCodeScreen_ProgressBar
  .QRCodeScreen
  .textContainer
  .QRCodeScreen_ProgressBarContainer {
  display: flex;
  flex-direction: column;
  margin-left: var(--zA4);
  margin-right: var(--zA4);
}

.T_0113_QRCodeScreen.QRCodeScreen_ProgressBar
  .QRCodeScreen
  .textContainer
  .QRCodeScreen_ProgressBarContainer
  .UE_0702_ProgressBar
  .progressBar
  .centerLine
  .currentProgress {
  margin: 0;
}

.T_0113_QRCodeScreen
  .QRCodeScreen
  .textContainer
  .QRCodeScreen_ProgressBarContainer
  > p {
  width: 100%;
  margin: 15px 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.T_0113_QRCodeScreen
  .QRCodeScreen
  .textContainer
  .QRCodeScreen_ProgressBarContainer
  > p:first-child {
  margin-top: 0;
}
.T_0113_QRCodeScreen
  .QRCodeScreen
  .textContainer
  .QRCodeScreen_ProgressBarContainer
  > p:last-child {
  margin-bottom: 40px;
}

.T_0113_QRCodeScreen
  .QRCodeScreen
  .textContainer
  .qrcodeContainerButton
  .UE_0101_ActionButton
  p {
  position: relative;
  text-align: center;
  align-self: center;
  padding: 0 var(--textButtonMargin);
}
.T_0113_QRCodeScreen.infoRight .infoTextRight,
.T_0113_QRCodeScreen.infoLeft .infoTextLeft {
  display: flex;
  align-items: center;
  margin-top: calc(var(--zA4) / 2);
}
.T_0113_QRCodeScreen.infoRight .infoTextRight {
  margin-top: 0;
}
.T_0113_QRCodeScreen.infoRight.QRCodeScreen_QRCode .infoTextRight {
  margin-top: calc(var(--zA4) / 2);
}

.T_0113_QRCodeScreen.infoRight .infoTextRight p,
.T_0113_QRCodeScreen.infoLeft .infoTextLeft p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.T_0113_QRCodeScreen.infoLeft .infoTextLeft {
  margin-left: var(--zA4);
  width: calc(100% - var(--zA4));
  justify-content: center;
}

.T_0113_QRCodeScreen.infoRight .infoTextRight p {
  max-width: var(--QRCodeScreen_infoRightWidth);
}
.T_0113_QRCodeScreen.infoLeft .infoTextLeft p {
  max-width: calc(100% - var(--listIconSize) - var(--zA4) * 2);
  margin-right: var(--zA4);
}

.T_0113_QRCodeScreen.infoRight .infoTextRight .listIcon,
.T_0113_QRCodeScreen.infoLeft .infoTextLeft .listIcon {
  margin-right: var(--zA4);
}
.T_0113_QRCodeScreen .QRCodeScreen .textLines {
  height: calc((var(--lineHeightPrio1) + var(--zA1)) * 6 + var(--zA4));
  overflow-y: auto;
  width: 100%;
}

.T_0113_QRCodeScreen.QRCodeScreen_ProgressBar .QRCodeScreen .textLines {
  height: calc((var(--lineHeightPrio1) + var(--zA1)) * 5 + var(--zA4));
}

.T_0113_QRCodeScreen.QRCodeScreen_ProgressBar.infoLeft
  .QRCodeScreen
  .textLines {
  height: calc((var(--lineHeightPrio1) + var(--zA1)) * 4 + var(--zA4));
}

.T_0113_QRCodeScreen .QRCodeScreen .textLines > p {
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 100%;
  text-align: left;
}

.T_0113_QRCodeScreen .QRCodeScreen .textLines > p:first-child {
  margin-top: 0;
}

.T_0113_QRCodeScreen .QRCodeScreen {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  height: 100%;
}
.T_0113_QRCodeScreen .QRCodeScreen .qrCodeDecorator {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.T_0113_QRCodeScreen .QRCodeScreen .UE_1402_QRCode.boundingBox {
  height: var(--QRCodeScreen_boundingBoxBigSize);
  width: var(--QRCodeScreen_boundingBoxBigSize);
  background-color: rgba(var(--boundingBoxGreen));
  margin-left: calc(var(--listIconSize) + var(--zA4));
  display: inline-block;
}

/* --------------------- Full decorator -------------------------------*/
.T_0113_QRCodeScreen .QRCodeScreen .fullDecorator {
  display: flex;
  position: relative;
  flex: 1;
  height: 100%;
  justify-content: center;
}

.T_0113_QRCodeScreen .QRCodeScreen .mdkBackground,
.T_0113_QRCodeScreen .QRCodeScreen .mdkDecorator {
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.T_0113_QRCodeScreen .QRCodeScreen .mdkBackground {
  display: flex;
  background: radial-gradient(
    circle at 100%,
    rgba(var(--audiBlack), 0.7) 20%,
    transparent 65%
  );
  right: -250px;
  background-position: right;
}

.T_0113_QRCodeScreen.smartPhone .QRCodeScreen .mdkDecorator {
  background-image: url(../img/MDK_Phonebox_Smartphone.png);
}
.T_0113_QRCodeScreen.carPhone .QRCodeScreen .mdkDecorator {
  background-image: url(../img/MDK_Car_Phone.png);
  background-position: bottom;
}

/* --------------------- QRCodeScreen_disabled -------------------------------*/

.T_0113_QRCodeScreen.QRCodeScreen_disabled
  .QRCodeScreen
  .QRCodeScreen_ProgressBarContainer,
.T_0113_QRCodeScreen.QRCodeScreen_disabled .QRCodeScreen .textLines,
.T_0113_QRCodeScreen.QRCodeScreen_disabled .QRCodeScreen .UE_1402_QRCode {
  opacity: var(--touchDisabledState);
}

@media only screen and (max-width: 1768px) {
  body.T_0113_QRCodeScreen {
    width: 1526px;
    height: 425px;
  }
  .T_0113_QRCodeScreen.infoRight.QRCodeScreen_QRCode .infoTextRight {
    margin-top: var(--zA6);
  }
  .T_0113_QRCodeScreen .QRCodeScreen .UE_1402_QRCode.boundingBox {
    width: var(--QRCodeScreen_boundingBoxSmallSize);
    height: var(--QRCodeScreen_boundingBoxSmallSize);
  }
  .T_0113_QRCodeScreen .QRCodeScreen .textLines {
    height: calc((var(--lineHeightPrio1) + var(--zA1)) * 5 + var(--zA4));
  }

  .T_0113_QRCodeScreen.infoLeft .QRCodeScreen .textLines {
    height: calc((var(--lineHeightPrio1) + var(--zA1)) * 3 + var(--zA4));
  }

  .T_0113_QRCodeScreen.QRCodeScreen_ProgressBar .QRCodeScreen .textLines {
    height: calc((var(--lineHeightPrio1) + var(--zA1)) * 4 + var(--zA4));
  }

  .T_0113_QRCodeScreen.QRCodeScreen_ProgressBar.infoLeft
    .QRCodeScreen
    .textLines {
    height: calc((var(--lineHeightPrio1) + var(--zA1)) * 3 + var(--zA4));
  }

  .T_0113_QRCodeScreen
    .QRCodeScreen
    .textContainer
    .QRCodeScreen_ProgressBarContainer
    > p {
    margin: 10px 0;
  }
  .T_0113_QRCodeScreen
    .QRCodeScreen
    .textContainer
    .QRCodeScreen_ProgressBarContainer
    > p:first-child {
    margin-top: 0;
  }

  .T_0113_QRCodeScreen.QRCodeScreen_ProgressBar
    .QRCodeScreen
    .textContainer
    .QRCodeScreen_ProgressBarContainer
    > p:last-child {
    margin-bottom: 20px;
    margin-top: 0;
  }

  .T_0113_QRCodeScreen.withOperationPanel.smartPhone
    .QRCodeScreen
    .mdkDecorator,
  .T_0113_QRCodeScreen.withOperationPanel.smartCard
    .QRCodeScreen
    .mdkDecorator {
    background-position: center 30%;
  }
}
