:root {
  --radioButtonSize: 28px; /* adjust together with radioButtonSize */
}

:global(#root) > .UE_1102_RadioButton {
  background-color: rgb(var(--bodyColorGrey));
  height: 119px;
  width: 119px;
}

.radioButton {
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: var(--radioButtonSize);
  height: var(--radioButtonSize);
  cursor: pointer;
  -webkit-user-select: none;
}

.radioButton input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.checkmark {
  position: absolute;
  height: calc(var(--radioButtonSize) - 2px);
  width: calc(var(--radioButtonSize) - 2px);
  background-color: transparent;
  border: 2px solid rgb(var(--audiWhite));
  border-radius: 50%;
}

.checkmark:after {
  content: '';
  position: absolute;
  display: none;
  top: 8px;
  left: 8px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgb(var(--audiWhite));
}

input:checked ~ .checkmark:after {
  display: block;
}
