:global(#root) > .ST_0067_DialTab_Screen {
  position: absolute;
  height: 697px;
  width: 1800px;
  margin: 0;
}

.ST_0067_DialTab_Screen {
  height: 100%;
  width: 100%;
  position: absolute;
}

.dtsContainer {
  display: flex;
  width: 100%;
  height: 100%;
}

.dtsSeparator {
  width: 1px;
  height: 100%;
  background-color: rgba(var(--audiGrey), 1);
  flex-shrink: 0;
}

.dtsLeft,
.dtsRight {
  height: 100%;
  width: calc(50% - 1px);
  flex-shrink: 0;
}

.dtsLeft {
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.dtsInputContainer {
  display: flex;
  width: 100%;
  box-sizing: border-box;
  /* padding: 0 8px; */
}

.dtsField {
  width: calc(100% - var(--minTouchSize));
  display: flex;
  flex-shrink: 0;
}

.dtsField .UE_0501_InputField {
  position: relative;
  height: var(--minTouchSize);
  width: calc(100% - var(--numkeyWidth) + var(--minTouchSize) + 4px);
}

.dtsField .UE_0501_InputField .interactionBackground {
  background-color: unset;
}

.dtsInputContainer .dtsField .button {
  width: var(--numkeyWidth);
  margin-right: 8px;
}
.dtsInputContainer > .button {
  width: var(--minTouchSize);
}
.dtsInputContainer > .button .uiState.borderBottom {
  height: 100%;
}

.dtsSeparatorContainerLeft {
  width: calc(100% - 12px);
  height: 1px;
  background-color: rgba(var(--audiGrey), 1);
  flex-shrink: 0;
  margin-left: 4px;
}

.dtsLeft .UE_0506_PhoneNumericKeypad {
  width: 100%;
  height: calc(100% - 1px - var(--minTouchSize));
}

.dtsLeft .UE_0506_PhoneNumericKeypad .keyboardLine {
  height: calc(100% / 4);
}

.dtsLeft .UE_0506_PhoneNumericKeypad .keypad {
  padding-left: 4px;
}

.dtsLeft
  .UE_0506_PhoneNumericKeypad
  .keypad
  .sideButtons
  .keyboardLine:last-of-type {
  height: calc((100% / 4) * 3);
}

.dtsRight .T_0103_ListVertical {
  height: 100%;
  overflow: hidden;
}

@media only screen and (max-width: 1768px) {
  .dtsField .UE_0501_InputField {
    width: calc(100% - var(--numkeyWidth) + var(--minTouchSize) - 22px);
  }

  .dtsField .UE_0501_InputField .inputField {
    min-width: unset;
  }
}
