/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0055_DayDetailView {
  width: 957px;
  height: 570px;
  margin: 0;
}

:root {
  --halfTouchIcon: 25px;
}

.SE_0055_DayDetailView {
}

.background {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  z-index: -1;
}

.darkRectangle {
  background-color: rgba(var(--audiBlack), 0.5);
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.dayDetailView {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.dayDetailView.appointment.fadeOutBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.dayDetailView.appointment.fadeOutTop {
  -webkit-mask-image: var(--listStartFadeout);
}

.dayDetailView.appointment.fadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.appointmentInformation {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

/*-----------Day General Information-----------*/
.dayGeneralInformation {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.textBlock {
  width: calc(100% - (var(--textButtonMargin) * 2));
  padding: 0 var(--uiStateMargin);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.textBlock p {
  text-align: center;
  width: 100%;
}

.SE_0055_DayDetailView {
  position: relative;
}

/*-------------Appointment Information----------------*/

.appointmentInformation {
  flex-direction: column;
  padding: 0 var(--listVerticalPadding);
}

.appointmentInformation .touchIcon {
  margin: 0 var(--lA1);
  position: relative;
  top: calc(
    ((var(--minTouchSize) - (var(--cA1) * 2)) / 2) - var(--halfTouchIcon)
  );
  left: 0;
  transform: none;
  margin-top: -6px;
}

.appointmentLine:first-child .touchIcon {
  margin-top: -2px;
}
.appointmentLine {
  width: calc(100% - (var(--iA1) * 2));
  min-height: calc(var(--minTouchSize) - (var(--cA1) * 2));
  height: fit-content;
  padding: var(--cA1) 0;
  margin: 0 var(--iA1);
  position: relative;
  align-items: center;
  display: flex;
  justify-content: flex-start;
}

.appointmentLine:not(:last-of-type):after {
  content: '';
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: rgba(var(--audiGrey), 0.8);
}

.informationText {
  max-width: calc(
    100% - (var(--listVerticalPadding) * 2) - (var(--iA1) * 2) -
      (var(--lA1) * 2) - (var(--halfTouchIcon) * 2)
  );
  box-sizing: border-box;
  overflow: hidden;
  margin: 0 var(--iA1);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.clickedHyperlink:active,
p.clickedAllHyperlinks {
  color: rgba(138, 180, 248, 0.8);
}

.green {
  color: rgb(var(--audiGreen));
}

.settings {
  position: relative;
  margin-top: 50px;
}
