/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0058_TitleLineWithArrows {
  position: absolute;
  margin: 0;
  width: 100%;
  height: var(--minTouchSize);
  background-color: rgb(var(--bodyColorGrey));
}

.SE_0058_TitleLineWithArrows {
}

/*------------------------ RELEVANT CONTENT --------------------------*/
.titleLineWithArrows {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.button.arrowLeft,
.button.arrowRight {
  position: relative;
  height: 100%;
  width: var(--minTouchSize);
  padding-bottom: unset;
}

.textContainer {
  height: 100%;
  width: 100%;
  flex: 1;
  margin: 0 var(--iA1);
  overflow: hidden;
  text-align: center;
}

.touchIcon {
  margin-top: 5px;
}
