.demoModeBg {
  background-image: url('../../assets/DemoMode-Porsche.png');
}

.textLinesContainer {
  display: flex;
  flex-direction: column;
  background-color: unset;
  bottom: 0;
}

.textLine {
  margin-bottom: 3px;
  background-color: rgba(255, 255, 0, 0.25);
}

.textLine {
  height: 40px;
}

.textLinesContainer {
  position: absolute;
  width: 900px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  background-color: rgba(255, 255, 0, 0.25);
}
