:global(#root) > .SE_5010_Gmeter_RS {
  background-color: rgb(var(--bodyColorGrey));
  width: 598px;
  height: 262px;
  margin: 0;
}
.SE_5010_Gmeter_RS .Gmeter_RS {
  height: 100%;
  width: 100%;
  position: relative;
}

.SE_5010_Gmeter_RS .Gmeter_RS .container_SE_5010_00,
.SE_5010_Gmeter_RS .Gmeter_RS .container_SE_5010_01 {
  display: none;
}
.SE_5010_Gmeter_RS .Gmeter_RS.SE_5010_00 .container_SE_5010_00,
.SE_5010_Gmeter_RS .Gmeter_RS.SE_5010_01 .container_SE_5010_01 {
  display: block;
  height: 100%;
  width: 100%;
}

.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine {
  height: 180px;
  width: 410px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  z-index: 0;
}
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_value {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 82px;
  border: 2px solid rgb(92, 105, 112);
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  box-sizing: border-box;
  background-color: rgb(var(--audiBlack));
}

.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine p {
  margin-top: 0;
}
.SE_5010_Gmeter_RS
  .Gmeter_RS
  .Gmeter_firstLine
  .Gmeter_SpritesheetDigitsContainer,
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_value .Gmeter_SpritesheetDigitsContainer {
  display: flex;
  align-items: baseline;
  justify-content: center;
}

.SE_5010_Gmeter_RS
  .Gmeter_RS
  .Gmeter_firstLine
  .Gmeter_SpritesheetDigitsContainer
  .Gmeter_dot,
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_value .Gmeter_dot {
  width: 3px;
  height: 18px;
  background-image: url(../../../../51_BoardcomputerSportanzeige/T_5100_Boardcomputer/01_HTML/img/T_5100_19_Laptimer_Dot.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_value .Gmeter_dot {
  height: 20px;
}
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine .Gmeter_topValue {
  position: absolute;
  top: -30px;
  left: 50%;
  transform: translateX(-50%);
}
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine .Gmeter_rightValue {
  position: absolute;
  top: 50%;
  right: -55px;
  transform: translateY(-50%);
}
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine .Gmeter_leftValue {
  position: absolute;
  top: 50%;
  left: -55px;
  transform: translateY(-50%);
}
.SE_5010_Gmeter_RS .Gmeter_RS .Gmeter_firstLine .Gmeter_bottomValue {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}

