/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --soundVolumeOverlay_width: 716px;
  --soundVolumeOverlay_widthPID: 849px;
}

/* ------------------------ Standalone ------------------------ */

:global(#root) > .SE_0014_SoundVolumeOverlay {
  height: fit-content;
  width: var(--soundVolumeOverlay_width);
  position: absolute;
  margin: 8px;
}

:global(#root) > .SE_0014_SoundVolumeOverlay.standalonePIDVolumePopup {
  width: var(--soundVolumeOverlay_widthPID);
}

/* ------------------------ .SE_0014_SoundVolumeOverlay ------------------------ */

.SE_0014_SoundVolumeOverlay {
}

.soundVolumeOverlay {
  height: 152px;
  width: 100%;
  position: relative;
}

.pidSelected .soundVolumeOverlay {
  height: fit-content;
}

.soundVolumeContainer {
  width: 100%;
  height: auto;
  flex-direction: column;
  padding-top: var(--iA1);
  padding-bottom: var(--iA1);
  position: relative;
}

.soundVolumeContent {
  width: calc(100% - var(--iA1));
  display: flex;
  align-items: center;
  height: var(--minTouchSize);
}
.soundVolumeContent.soundVolumeContentPID {
  width: calc(100% - 2 * var(--iA1));
  margin-left: var(--iA1);
}

.soundVolumeContent .button {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

/******************* Volume Info **************************/

.volumeInfo {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  height: var(--minTouchSize);
  pointer-events: none;
  width: calc(100% - var(--minTouchSize));
  transform: translateY(-5px);
}

.soundVolumeContentPID .volumeInfo {
  width: calc(100% - (2 * var(--minTouchSize)));
}

.volumeText {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
}

.volumeText .Text {
  margin-left: var(--iA1);
  margin-right: var(--iA1);
}

.volumeText .listIcon {
  margin-left: var(--iA1);
}

.volumeInfo .UE_0601_Slider {
  width: calc(100% - 10px);
  top: -40px;
  margin-left: 2px;
}

.volumeInfo .UE_0601_Slider .slider {
  padding: 0 45px;
  height: 0;
}

.volumeInfo .UE_0601_Slider .sliderBox {
  height: var(--minTouchSize);
  background-color: unset;
  background-image: none;
}

.volumeInfo .UE_0601_Slider .range::-webkit-slider-thumb {
  display: none;
}

.volumeInfo .UE_0601_Slider .headline,
.volumeInfo .UE_0601_Slider .button,
.volumeInfo .UE_0601_Slider .textLabels,
.volumeInfo .UE_0601_Slider .sliderBox .segmentContainer .sliderDecoration {
  display: none;
}

.headset {
  height: var(--minTouchSize);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 10px 0 25px;
  max-width: 100%;
}

.UE_1103_Switch {
  min-width: var(--minTouchSize);
  height: var(--minTouchSize);
}

/*-------------------- config -------------------*/
.settings {
  position: absolute;
  top: 300px;
  width: 100%;
  color: white;
}
