:global(#root) > .T_0105_DisclaimerTemplate {
  width: 1678px;
  height: 554px;
  position: absolute;
  margin: 0px;
  overflow: hidden;
}

.disclaimerTemplate {
  height: calc(100% - 119px);
  position: absolute;
  width: 100%;
}

.UE_0402_Tabbarmenu {
  height: var(--minTouchSize);
  position: relative;
  width: 100%;
  z-index: 1;
}
.scrollableText p {
  margin-left: var(--zA4);
  margin-right: var(--zA4);
}
.scrollableText p:first-child {
  margin-top: var(--zA4);
}
.scrollableText p:last-child {
  margin-bottom: var(--zA4);
}

.scrollableText .infoContainer p:first-child,
.scrollableText .infoContainer p:last-child {
  margin-top: unset;
  margin-bottom: unset;
  margin-left: var(--iA1);
}

.scrollableText {
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  -webkit-mask-image: var(--listStartEndFadeout);
}

.iconInfos {
  margin: var(--zA8) var(--zA4) var(--zA4) var(--zA4);
}

.iconInfoParagraph {
}

.subheadline {
}

.infoContainer {
  position: relative;
  display: inline-flex;
  height: 100%;
  max-width: 100%;
  margin-top: var(--zA2);
}

.infoContainer .textPrio2 {
  margin-bottom: 0;
}

.settings {
  position: absolute;
  top: 600px;
}

