:root {
  --viewAreaWidth: 720px;
  --safeAreaWidth: 546px;
  --safeAreaLeftPos: 103px;
}

:global(#root) > .T_5305_ViewAreaVideostream {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: calc(var(--cardWidth) - 4px);
  height: var(--cardContentHeight);
  border: 1px solid yellow;
  margin: 0;
}

.carPlay {
  position: absolute;
  height: 100%;
  width: var(--viewAreaWidth);
  right: 0px;
}

.carPlay .viewArea {
  position: absolute;
  height: 100%;
  width: var(--viewAreaWidth);
  right: 0px;
  background-color: rgb(0, 0, 255);
}

.carPlay .safeArea {
  position: absolute;
  height: 100%;
  width: var(--safeAreaWidth);
  left: var(--safeAreaLeftPos);
  background-color: rgb(var(--boundingBoxGreenFull));
}

.carPlay .viewLabel {
  position: absolute;
  top: 20px;
  right: 0px;
  width: 60px;
  overflow: visible;
  white-space: unset;
}
.carPlay .safeLabel {
  position: absolute;
  top: 20px;
  right: 100px;
  color: rgb(var(--audiBlack));
  font-family: var(--fontNormal);
}
