.StatusLine {
  width: 700px;
  height: 70px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.StatusLine.boundingBox {
  background-color: rgba(255, 255, 0, 0.3);
}

.SpritesheetDigits {
  position: absolute;
  width: fit-content;
  top: 3px;
  left: 195px;
}

.SpritesheetDigits.kursiv {
  top: 2px;
}

.SpritesheetDigits .unit.kmh.custom,
.SpritesheetDigits .unit.kmh.italic.custom,
.SpritesheetDigits .unit.mph {
  bottom: -21px;
  left: 50%;
  transform: translateX(-50%);
}

.SpritesheetDigits.kursiv .unit.kmh.custom,
.SpritesheetDigits.kursiv .unit.kmh.italic.custom,
.SpritesheetDigits.kursiv .unit.mph {
  bottom: -18px;
}

.SpritesheetDigits .unit.mph {
  width: 50px;
}

.SpritesheetDigits .unit.kmh {
  width: 58px;
}

