  position: absolute;
  height: 70px;
  width: 51px;
  right: 59px;
  background-color: rgba(var(--boundingBoxGreen));
}

.tsv {
  background-image: url('./img/MEB_AR-HUD_VZE_LINK.png');
  position: absolute;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
}

.NAV_LaneGuidance {
  display: flex;
  flex-direction: column;
  position: absolute;
  height: 70px;
  width: 175px;
  overflow: hidden;
}

.NAV_LaneGuidance_top {
  display: flex;
  width: 100%;
  position: absolute;
  top: 0;
  transition: all 0.1s linear;
}

.NAV_LaneGuidanceArrowsShow .NAV_LaneGuidance_top {
  transform: translateY(0);
}

.NAV_LaneGuidanceArrows .NAV_LaneGuidance_top {
  transform: translateY(34px);
}

.NAV_LaneGuidance_bottom {
  /* background-image: url("./img/MEB_AR-HUD_lane_dividing_right_V001_19-04-05.png"); */
  /*  background-repeat: no-repeat;
  background-position-x: 33px; */
  position: absolute;
  top: 39px;
  transition: all 0.1s linear;
}

.NAV_LaneGuidanceArrowsShow .NAV_LaneGuidance_bottom {
  opacity: 1;
  transform: translateY(0px);
}

.NAV_LaneGuidanceArrows .NAV_LaneGuidance_bottom {
  opacity: 0;
  transform: translateY(34px);
}

.NAV_LaneGuidance_top_arrow {
  background-image: url('./img/MEB_AR-HUD_follow_arrows_small_V004_20-12-08.png');
  height: 36px;
  width: 44px;
}
.NAV_LaneGuidance_top_arrow_boundingBox {
  background-color: rgba(255, 255, 0, 0.3);
  height: 36px;
  width: 43px;
  position: absolute;
  left: 13px;
}

.NAV_LaneGuidance_bottom_arrows {
  background-image: url('./img/MEB_AR-HUD_lane_V001_19-04-05.png');
  height: 32px;
  width: 25px;
}
.NAV_LaneGuidance_bottom_arrows_boundingBox {
  background-color: rgba(255, 255, 0, 0.3);
  height: 31px;
  width: 175px;
}

.textBox {
