:global(#root) > .SE_0087_PNAV_Overlay {
  margin: 20px;
  background: rgb(var(--bodyColorGrey));
}

.pnavBox {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-direction: row;
}

.pnavBox .UE_0101_ActionButton {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.pnavBox > :first-child {
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}

.routeSelectionBox {
  margin-left: var(--buttonBorderDist);
  display: flex;
  flex-direction: row;
}

.pnavBox .routeSelectionBox .UE_0101_ActionButton {
  background-color: rgba(var(--audiBlack), 0.85);
}

.pnavBox .routeSelectionBox > .UE_0101_ActionButton:first-child {
  border-top-left-radius: var(--standardBorderRadius);
  border-bottom-left-radius: var(--standardBorderRadius);
}
.pnavBox .routeSelectionBox > .UE_0101_ActionButton:last-child {
  border-top-right-radius: var(--standardBorderRadius);
  border-bottom-right-radius: var(--standardBorderRadius);
}

.routeSelectionBox .routeSelection {
  padding-left: 4px;
  display: flex;
  flex-direction: column;
  background-color: rgba(var(--audiBlack), 0.85);
  width: 690px;
  position: relative;
  border-bottom-right-radius: var(--standardBorderRadius);
  border-bottom-left-radius: var(--standardBorderRadius);
}

.routeSelectionBox .routeSelection.extended {
  height: calc(3 * var(--minTouchSize));
}

.routeSelectionBox .routeSelection .routeLine {
  height: var(--minTouchSize);
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.routeSelectionBox .routeSelection .routeLine.one {
  border-left: 7px solid rgb(var(--routeActiveColor));
  padding-left: var(--iA1);
  margin-right: var(--iA1);
}

.routeSelectionBox .routeSelection .routeLine.two {
  display: none;
  border-left: 7px solid rgb(var(--routeAlternativeOneBorder));
  padding-left: var(--iA1);
  margin-right: var(--iA1);
}

.routeSelectionBox .routeSelection .routeLine.three {
  display: none;
  border-left: 7px solid rgb(var(--routeAlternativeTwoBorder));
  padding-left: var(--iA1);
  margin-right: var(--iA1);
}

.routeSelectionBox .routeSelection.extended .routeLine.two {
  display: inline-flex;
}

.routeSelectionBox .routeSelection.extended .routeLine.three {
  display: inline-flex;
}

.routeSelectionBox .routeSelection .secondLinePnav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.SE_0087_PNAV_Overlay .routeSelectionBox .routeSelection .secondLinePnav.nodtd {
  justify-content: unset;
}

.routeSelectionBox .routeSelection .routeLine .textPrio2 {
  margin-right: var(--iA1);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.SE_0087_PNAV_Overlay
  .routeSelectionBox
  .routeSelection.nodtd
  .routeLine
  .textPrio2 {
  margin-right: var(--iA2);
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .textPrio2
  + .textPrio2 {
  margin-top: 0px;
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .textPrio2:nth-of-type(1) {
  width: 134px;
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .textPrio2:nth-of-type(2) {
  width: 126px;
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .textPrio2:nth-of-type(3) {
  width: 123px;
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .textPrio2:nth-of-type(4) {
  width: 87px;
  margin-left: 0px;
}

.routeSelectionBox .routeSelection .routeLine .secondLinePnav .capacity {
  display: flex;
  align-items: center;
  justify-content: baseline;
  flex-direction: row;
  right: 0px;
}

.routeSelectionBox
  .routeSelection
  .routeLine
  .secondLinePnav
  .capacity
  .textPrio2 {
  margin-left: 0px;
  margin-right: 0px;
  width: 87px;
}

.routeSelectionBox .routeSelection.extended .separatorLine {
  height: calc(var(--zA1) - var(--zA2));
  margin: 0 var(--iA1);
  background-color: rgba(var(--audiWhite), 0.2);
}

.settings {
  position: absolute;
  top: 400px;
}
