:root {
  --airQualityClimateSegmentWidth: 300px;
  --airQualityClimateSegmentHeight: 20px;
  --segmentWidth: 175px;
  --centralBoxWidth: calc(3 * var(--minTouchSize) + 2px);
  --segmentAfterRight: var(--iA1);
  --segmentBeforeLeft: var(--iA1);
  --airQualityValueWidth: 150px;
  --airQualityColor: var(--airQualityColor1);
  --airQualityStandaloneWidth: 1900px;
}

:global(#root) > .ST_0014_AirQuality {
  width: var(--airQualityStandaloneWidth);
  overflow-x: hidden;
  position: relative;
}

:global(#root) > .ST_0014_AirQuality .airQuality .operationLine {
  width: var(--airQualityStandaloneWidth);
}

:global(#root) > .ST_0014_AirQuality .airQuality {
  height: 697px;
}

.airQuality {
  width: 100%;
  height: 100%;
  position: relative;
}

.airQuality .airQualityContent {
  display: flex;
  width: 100%;
  height: calc(100% - 255px);
  position: relative;
  top: 120px;
}

.AQI_Particles {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}

.outsideLoop {
  position: absolute;
  width: 950px;
  height: 280px;
  top: 45px;
  left: 300px;
  /* background-image: url(../../../../../Animations/SpecificAnimations/40_CID/AS_4014_AQIParticles/PNG%20Sequenzen/outside%20loop/Outside_particles00.png); */
  -webkit-mask-image: url(../../../../../Animations/SpecificAnimations/40_CID/AS_4014_AQIParticles/PNG%20Sequenzen/outside%20loop/Outside_particles00.png);
  background-color: rgb(var(--airQualityColor3));
}

.insideLoop {
  position: absolute;
  width: 415px;
  height: 140px;
  top: 112px;
  left: 1223px;
  z-index: 1;
  /* background-image: url(../../../../../Animations/SpecificAnimations/40_CID/AS_4014_AQIParticles/PNG%20Sequenzen/inside%20loop/Inside_particles00.png); */
  -webkit-mask-image: url(../../../../../Animations/SpecificAnimations/40_CID/AS_4014_AQIParticles/PNG%20Sequenzen/inside%20loop/Inside_particles00.png);
  background-color: rgb(var(--airQualityColor1));
}

/*---------Left and Right Parts------------*/
.firstValue,
.secondValue {
  flex: 1;
  width: calc(50% - (var(--centralBoxWidth) / 2));
}

.airQuality .airQualityContent .textBlock {
  width: 100%;
  height: calc(100% - 75px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.paragraphGroup {
  width: 100%;
  height: calc(var(--lineHeightPrioA) + (var(--lineHeightPrioA) * 2));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-top: 100px;
  z-index: 1;
}

.paragraphGroup.sensorActive {
  height: calc(
    var(--lineHeightPrioA) + (var(--lineHeightPrioA) * 2) -
      var(--lineHeightPrio2)
  );
}

.alternativeParagraphGroup {
  width: 100%;
  height: calc(var(--lineHeightPrioA) + (var(--lineHeightPrioA) * 2));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.alternativeParagraphGroup.sensorActive {
  margin-top: var(--lineHeightPrio2);
}

.sensorVariant {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: var(--touchDisabledState);
}

.sensorVariant.alternativeActive {
  position: relative;
  top: 14px;
}

.carDecorator {
  background-repeat: no-repeat;
  background-position: center;
  width: 700px;
  height: calc(100% - 75px);
  background-size: 700px;
  position: absolute;
  right: 35px;
}

.airQuality .airQualityContent .textBlock .textPrioA,
.airQuality .airQualityContent .textBlock .textPrio2 {
  z-index: 2;
}

.airQuality .airQualityContent .textBlock p {
  max-width: calc(100% - 90px);
}

.airQuality .airQualityContent .segmentSection {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  height: calc(var(--textButtonMargin) + var(--iA2));
  bottom: 35px;
}

.airQuality .airQualityContent .firstValue .segmentSection::after {
  content: '';
  position: absolute;
  height: 2px;
  width: var(--segmentWidth);
  bottom: 31px;
  right: var(--segmentAfterRight);
  background-color: rgba(var(--audiWhite), 0.5);
}

.airQuality .airQualityContent .secondValue .segmentSection::before {
  content: '';
  position: absolute;
  height: 2px;
  width: var(--segmentWidth);
  bottom: 31px;
  left: var(--segmentBeforeLeft);
  background-color: rgba(var(--audiWhite), 0.5);
}

.airQuality .airQualityContent .segmentSection .segment {
  width: var(--airQualityClimateSegmentWidth);
  height: var(--airQualityClimateSegmentHeight);
  border: 2px solid rgba(var(--audiBlack), 0.3);
  margin-right: 1px;
  margin-top: 12px;
}

.ST_0014_AirQuality.firstAlternative
  .airQuality
  .airQualityContent
  .firstValue
  .segmentSection
  .segment,
.ST_0014_AirQuality.secondAlternative
  .airQuality
  .airQualityContent
  .secondValue
  .segmentSection
  .segment {
  margin-top: 32px;
}

.airQuality .airQualityContent .segment .filling {
  position: relative;
  width: 100%;
  height: 100%;
}

.segment .filling.primary {
  background-color: rgba(var(--audiWhite), 0.15);
}

.airQuality .airQualityContent .segmentSection .segmentText {
  display: flex;
  flex-direction: row;
  width: 300px;
  position: relative;
  top: var(--zA1);
}

.airQuality .airQualityContent .segmentSection .segmentText p {
  flex: 1;
  display: flex;
}

.airQuality .airQualityContent .segmentSection .segmentText p:nth-of-type(2) {
  justify-content: center;
}

.airQuality .airQualityContent .segmentSection .segmentText p:last-of-type {
  justify-content: flex-end;
}

/*-------------------- segmentOverlay --------------------*/

.segmentOverlayMask {
  position: relative;
  width: var(--airQualityValueWidth);
  height: 28px;
  top: 34px;
  right: calc(150px - (var(--airQualityValueWidth) / 2));
}
.segmentOverlayMask .segmentOverlay {
  position: absolute;
  width: var(--airQualityValueWidth);
  height: 15px;
  top: 0;
  opacity: 0.7;
  background-color: rgb(var(--airQualityColor));
}

/*-------------------- chargingGlow --------------------*/

.chargingGlowContainer {
  position: absolute;
  height: calc(var(--airQualityClimateSegmentHeight) * 4);
  overflow: hidden;
  width: var(--airQualityValueWidth);
  left: calc(50% - (var(--airQualityClimateSegmentWidth) / 2));
  top: 2px;
}

.chargingGlowContainer .chargingGlow {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    to right,
    transparent 0%,
    rgb(var(--airQualityColor)) 100%
  );
  left: 0;
  top: 0%;
  opacity: 0.2;
}

/*-----------Central Box--------------*/
.airQuality .airQualityContent .centralBox {
  width: var(--centralBoxWidth);
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.ST_0014_AirQuality
  .airQuality
  .airQualityContent
  .centralBox
  .centralButtonContainer {
  position: relative;
  width: var(--centralBoxWidth);
  height: var(--minTouchSize);
  z-index: 1;
  display: flex;
  margin-bottom: var(--hA1);
}

.ST_0014_AirQuality
  .airQuality
  .airQualityContent
  .centralBox
  .centralButtonContainer
  .textWrapper {
  width: calc(100% - var(--opticalTouchReduction) / 2 - var(--minTouchSize));
  border: 2px solid var(--buttonBackgroundColor);
  top: calc(var(--opticalTouchReduction) / 2);
  left: calc(var(--opticalTouchReduction) / 2);
  border-radius: var(--standardBorderRadius) 0 0 var(--standardBorderRadius);
  display: flex;
  margin: calc(var(--opticalTouchReduction) / 2);
  margin-right: 0;
  border-right: 0;
}

.ST_0014_AirQuality
  .airQuality
  .airQualityContent
  .centralBox
  .centralButtonContainer
  .interactionBackground,
.ST_0014_AirQuality
  .airQuality
  .airQualityContent
  .centralBox
  .centralButtonContainer
  .state {
  border-radius: 0 var(--standardBorderRadius) var(--standardBorderRadius) 0;
}

.ST_0014_AirQuality
  .airQuality
  .airQualityContent
  .centralBox
  .centralButtonContainer
  p {
  margin: auto;
  width: 158px;
  text-align: center;
}

/*------------Bottom Operation Line-----------*/
.airQuality .operationLine {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 120px;
  justify-content: center;
}

.airQuality .operationLine.bottom {
  bottom: 0;
  position: absolute;
}

.operationLine.bottom .airBottomButtonContainer {
  position: relative;
  width: fit-content;
  height: 100%;
  bottom: 0px;
  z-index: 1;
  display: flex;
}

.operationLine.bottom .airBottomButtonContainer .UE_0101_ActionButton.button {
  position: relative;
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  display: flex;
  justify-content: center;
  width: 100%;
}

/*--------Green--------*/

.green .textBlock .paragraphGroup p {
  color: rgb(var(--airQualityColor1));
  transition: color var(--animationDuration) var(--animationCurve);
}
/*--------Second Value--------*/

.orange .textBlock .paragraphGroup p {
  color: rgb(var(--airQualityColor3));
  transition: color var(--animationDuration) var(--animationCurve);
}

.ST_0014_AirQuality.firstAlternative
  .airQuality
  .airQualityContent
  .firstValue.green
  .textBlock
  .textPrioA,
.ST_0014_AirQuality.firstAlternative
  .airQualityContent
  .firstValue.green
  .textBlock
  .textPrio2,
.ST_0014_AirQuality.secondAlternative
  .airQuality
  .airQualityContent
  .secondValue.green
  .textBlock
  .textPrioA,
.ST_0014_AirQuality.secondAlternative
  .airQualityContent
  .secondValue.green
  .textBlock
  .textPrio2,
.ST_0014_AirQuality.firstAlternative
  .airQuality
  .airQualityContent
  .firstValue.orange
  .textBlock
  .textPrioA,
.ST_0014_AirQuality.firstAlternative
  .airQuality
  .airQualityContent
  .firstValue.orange
  .textBlock
  .textPrio2,
.ST_0014_AirQuality.secondAlternative
  .airQuality
  .airQualityContent
  .secondValue.orange
  .textBlock
  .textPrioA,
.ST_0014_AirQuality.secondAlternative
  .airQuality
  .airQualityContent
  .secondValue.orange
  .textBlock
  .textPrio2 {
  color: rgb(var(--audiWhite));
}

/*----------Settings-----------*/
.settings {
  display: flex;
  flex-direction: column;
  width: 40%;
}

@media only screen and (max-width: 1768px) {
  :root {
    --segmentWidth: 150px;
  }

  :global(#root) > .ST_0014_AirQuality {
    width: 1748px;
    overflow-x: hidden;
  }

  :global(#root) > .ST_0014_AirQuality .airQuality .operationLine {
    width: 1748px;
  }

  :global(#root) > .ST_0014_AirQuality .airQuality {
    height: 828px;
  }

  .airQuality .airQualityContent .secondValue .carDecorator {
    background-size: 95%;
  }

  .outsideLoop {
    top: 38px;
    left: 170px;
  }

  .insideLoop {
    top: 107px;
    left: 1088px;
  }
}

@media only screen and (min-width: 1920px) and (max-width: 1920px) {
  .sensorText {
    margin-bottom: 40px;
  }
}

@media only screen and (min-width: 2219px) {
  :root {
    --airQualityStandaloneWidth: 1800px;
  }

  :global(#root) > .ST_0014_AirQuality .airQuality {
    height: 816px;
  }

  .outsideLoop {
    top: 105px;
    left: 185px;
  }

  .insideLoop {
    top: 172px;
    left: 1100px;
  }
}
