/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --gridRowFullscreen4Size: calc(
    956px - var(--statusBarHeight) - var(--climateBarHeight) -
      (var(--minTouchSize) * 2) - var(--dashboardMarginBottom)
  );
  --gridRowFullscreen5Size: calc(
    956px - var(--statusBarHeight) - var(--climateBarHeight) -
      var(--minTouchSize) - var(--dashboardMarginBottom)
  );
}

:global(#root) > .UE_1202_DashboardTile {
  position: absolute;
  margin: 10px;
  background-color: rgb(var(--bodyColorGrey));
  width: 550px; /*var(--tileWidth);*/
  height: calc(956px - var(--statusBarHeight) - var(--climateBarHeight));
  border: 1px solid yellow; /* Not part of the Design! */
}

.leftLiveSportList {
  width: 100% !important;
}

.UE_1202_DashboardTile .dashboardTile {
