/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --pwmPHEVLeftBarWidth: 131px;
  --pwmPHEVRightBarWidth: 270px;
  --pwmPHEVBarCenterGap: 3px;
}

:global(#root) > .UE_6015_Powermeter_PHEV_klein {
}

.UE_6015_Powermeter_PHEV_klein {
  position: absolute;
  width: calc(
    var(--pwmPHEVLeftBarWidth) + var(--pwmPHEVBarCenterGap) +
      var(--pwmPHEVRightBarWidth) + 1px
  );
  height: 70px;
}

/*--------------------------- Labels ----------------------------------*/

.powermeterKlein .valueLabels {
  position: absolute;
  top: 5px;
  width: 100%;
}

.valueLabels .maxLbl {
  position: absolute;
  top: 6px;
  background-color: rgb(var(--audiWhite));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/MAXlbl.png);
  width: 52px;
  height: 14px;
}

.valueLabels .lblLeft {
  left: 0px;
}
.valueLabels .lblRight {
  right: 1px;
}

.valueLabels .SpritesheetDigits {
  position: absolute;
  top: 5px;
  left: calc(var(--pwmPHEVLeftBarWidth) + var(--pwmPHEVBarCenterGap) - 11px);
}

.powermeterKlein .textLabels {
  position: absolute;
  top: 48px;
  width: 100%;
}

.textLabels .chargeLbl {
  position: absolute;
  top: 5px;
  background-color: rgb(var(--fpkUnit));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/CHARGElbl.png);
  width: 80px;
  height: 14px;
}
.textLabels .lblLeft {
  left: 24px;
}

.textLabels .powerLbl {
  position: absolute;
  top: 5px;
  background-color: rgb(var(--fpkUnit));
  -webkit-mask-image: url(../../../../../components/NewBargraph/img/POWERlbl.png);
  width: 74px;
  height: 14px;
}
.textLabels .lblRight {
  left: 232px;
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6015_Powermeter_PHEV_klein.UE_6015_00_state_1
  .powermeterKlein
  .valueLabels
  .maxLbl,
.UE_6015_Powermeter_PHEV_klein.UE_6015_00_state_1
  .powermeterKlein
  .valueLabels
  .SpritesheetDigits
  .digit,
.UE_6015_Powermeter_PHEV_klein.UE_6015_00_state_1
  .powermeterKlein
  .valueLabels
  .SpritesheetDigits
  .percent,
.UE_6015_Powermeter_PHEV_klein.UE_6015_00_state_1
  .powermeterKlein
  .textLabels
  > *,
.UE_6015_Powermeter_PHEV_klein.chargeReduced
  .powermeterKlein
  .valueLabels
  .maxLbl.lblLeft,
.UE_6015_Powermeter_PHEV_klein.throttleReduced
  .powermeterKlein
  .valueLabels
  .maxLbl.lblRight {
  background-color: rgb(var(--bargraphInactive));
}

/*--------------------------- powermeter ----------------------------------*/

.powermeterKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.powermeterKlein .leftbar {
  position: absolute;
  width: var(--pwmPHEVLeftBarWidth);
  top: 34px;
  left: -1px;
}

.powermeterKlein .rightbar {
  position: absolute;
  width: calc(var(--pwmPHEVRightBarWidth));
  top: 34px;
  left: calc(var(--pwmPHEVLeftBarWidth) + var(--pwmPHEVBarCenterGap) - 1px);
}

/*-------------------- glow --------------------*/
.UE_6015_Powermeter_PHEV_klein .powermeterKlein .UE_6015_02_boost_glow {
  position: absolute;
  height: var(--powerMeterHeight);
  width: auto;
  left: 140px;
  right: 0;
  top: 24px;
  z-index: 0;
}

.UE_6015_Powermeter_PHEV_klein
  .powermeterKlein
  .UE_6015_02_boost_glow
  .fillingGlowMask {
  position: relative;
  width: calc(100% + 25px);
  height: calc(100% + 30px);
  top: -15px;
  left: -10px;
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 1) 100%
  );
}

.UE_6015_Powermeter_PHEV_klein
  .powermeterKlein
  .UE_6015_02_boost_glow
  .fillingGlowMask
  .fillingGlow {
  position: relative;
  width: calc(100% - 19px);
  height: calc(var(--barHeight) + 7px);
  top: calc((100% - var(--barHeight)) / 2 - 3px);
  margin-left: 9px;
  opacity: 0.8;
  background-color: rgb(var(--audiWhite));
  filter: blur(5.8px);
}

/*---------- Config --------*/

.pwrConfig {
  position: absolute;
  margin-top: 100px;
  width: 100%;
  color: white;
}

.sliderDemo {
  margin-top: 100px;
  width: var(--gaugeBarWidth);
  color: white;
}

.sliderDemo .pwrslider {
  width: 100%;
}

.ThrottleLimitedDemo {
  color: white;
}

.RecuLimitedDemo {
  color: white;
}

.settings {
  top: 150px;
  position: absolute;
  width: 1000px;
}
