:global(#root) > .T_0001_BasicLayout {
  width: 1920px;
  height: 956px;
}

.T_0001_BasicLayout {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.T_0001_BasicLayout > .background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
}

.T_0001_BasicLayout > .intentChoserBackground {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
}

.T_0001_BasicLayout .driveSelectBackground {
  position: absolute;
  width: 1800px;
  height: 816px;
  right: 0px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  opacity: 0;
  transition: var(--AG_0203_CrossFade);
}

.T_0001_BasicLayout .driveSelectBackgroundCover {
  position: absolute;
  width: 100%;
