  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_backgroundCover.png);
}
.T_0001_BasicLayout.driveSelect.b10Screen .driveSelectBackgroundCover {
  width: calc(100% - var(--directAccessWidth));
  left: var(--directAccessWidth);
  background-position: top;
}

