  transition: var(--AG_0203_CrossFade);
}

.T_0001_BasicLayout.driveSelect.detailViewOpen .driveSelectBackground.balance {
  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_background_Individual.png);
}
.T_0001_BasicLayout.driveSelect.detailViewOpen .driveSelectBackground.comfort {
  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_background_Comfort.png);
}
.T_0001_BasicLayout.driveSelect.detailViewOpen .driveSelectBackground.dynamic {
  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_background_Dynamic.png);
}
.T_0001_BasicLayout.driveSelect.detailViewOpen
  .driveSelectBackground.efficiency {
  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_background_Efficiency.png);
}
.T_0001_BasicLayout.driveSelect.detailViewOpen .driveSelectBackground.offroad {
  background-image: url(../../../../SpecialElements/ST_0005_DriveSelect/01_HTML/img/Backgrounds/ST_0005_DriveSelect_background_Offroad.png);
}

.T_0001_BasicLayout.pidScreen .dashboardIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E775_main_menue_home_DA.svg);
