.b10Screen.immersiveMode .UE_0001_DirectAccess {
  left: calc(((var(--minTouchSize) * 2 + 8px) + 40px) * -1);
  transition: var(--AG_0101_Move);
}

.b10Screen .UE_0002_StatusBar {
  position: relative;
  height: calc(100% - var(--climateBarHeight));
  width: var(--directAccessWidth);
  overflow: hidden;
  box-sizing: border-box;
  background-color: rgb(var(--audiBlack));
  left: 0;
  transition: var(--AG_0101_Move);
}
.b10Screen.immersiveMode .UE_0002_StatusBar {
  left: calc(var(--directAccessWidth) * -1);
  transition: var(--AG_0101_Move);
}

.b10Screen .statusBarOverlay,
.T_0001_BasicLayout.driveSelect .statusBarOverlay {
  display: none;
}

.T_0402_Notification_Extended {
  z-index: 9;
}

.b10Screen .SE_0029_Quicksettings.closed .quickSettings .handle {
  left: calc((100vw - 10px) * -1);
  right: unset;
}

.b10Screen .SE_0029_Quicksettings {
  position: relative;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 1;
  transition: var(--AG_0101_Move), var(--AG_0202_FadeOut);
}

.b10Screen .SE_0029_Quicksettings .quickSettings .handle {
  right: 50px;
}

.T_0001_BasicLayout.b10Screen > .center {
  width: var(--dashboardWidth);
  height: calc(100% - var(--climateBarHeight));
  left: var(--directAccessWidth);
  position: absolute;
}

.T_0001_BasicLayout.b10Screen > .center .dashboard {
  padding-left: 0;
  transition: var(--AG_0303_ScaleResize), var(--AG_0101_Move);
}

.T_0001_BasicLayout.b10Screen .T_0201_AppGrid {
  top: 0;
  height: calc(100% - var(--climateBarHeight));
}

.T_0001_BasicLayout.b10Screen .T_0305_Intent_Choser_Popup {
  top: 0;
  height: calc(100% - var(--climateBarHeight));
}

.T_0001_BasicLayout.b10Screen .T_0201_AppGrid .topRow {
  border-top: unset;
}

.T_0001_BasicLayout.b10Screen .T_0305_Intent_Choser_Popup .topRow {
  border-top: unset;
}

.immersiveMode .T_0101_Dashboard {
  width: 100vw;
  margin-left: calc(var(--directAccessWidth) * -1);
  transition: var(--AG_0303_ScaleResize), var(--AG_0101_Move);
}

.immersiveMode .T_0101_Dashboard .UE_1202_DashboardTile.detailView {
  width: 100vw;
}

.T_0001_BasicLayout.immersiveMode > .center .dashboard {
  width: 100vw;
  transition: var(--AG_0303_ScaleResize);
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0001_BasicLayout {
    width: 1768px;
    height: 828px;
  }

  .SE_0015_Parking_OPS {
    width: calc(var(--opsViewWidth) + var(--minTouchSize) + 1px);
  }
}

@media only screen and (max-height: 720px) {
  :global(#root) > .T_0001_BasicLayout {
    width: 1920px;
    height: 720px;
  }
}

@media only screen and (max-width: 1920px) {
  /************** UTILITY ************/
  .hide {
    display: none;
  }
}

@media only screen and (min-width: 2219px) {
  :global(#root) > .T_0001_BasicLayout {
    width: 2220px;
    height: 956px;
  }

  .T_0206_ScalableOverlay,
  .T_0207_InputScalableOverlay {
    top: var(--popupOutlineWidth);
    left: calc(var(--directAccessWidth) + var(--popupOutlineWidth));
    height: calc(var(--dashboardHeight) - var(--popupOutlineWidth) * 2);
  }

  .T_0303_FullScreenDialog.variantOne,
  .T_0303_FullScreenDialog.variantThree,
  .T_0303_FullScreenDialog.variantFour,
  .T_0303_FullScreenDialog.T_0303_01,
  .T_0303_FullScreenDialog.T_0303_02,
  .T_0303_FullScreenDialog.burgerMenuDetailScreen,
  .T_0303_FullScreenDialog.variantFive {
    top: var(--popupOutlineWidth);
  }

  .ST_0066_ClimateMenu_Wrapper {
    top: calc(100% - var(--climateBarHeight));
    height: calc(100% - var(--climateBarHeight));
  }

  .ST_0066_ClimateMenu_Wrapper.open {
    top: 0;
  }

  .SE_0051_MapPopup.contentBox {
    transform: translateX(calc(-50% + var(--directAccessWidth) / 2));
  }

  .T_0001_BasicLayout.b10Screen > .SE_0040_Native_User_Switch {
    position: absolute;
    top: 0px;
    height: calc(100% - var(--climateBarHeight));
    left: var(--nativeScreenOffset);
    width: calc(100% - var(--nativeScreenOffset));
  }
  .T_0108_SplashScreen {
    position: absolute;
    top: 0px;
    height: calc(100% - var(--climateBarHeight));
    left: var(--directAccessWidth);
    width: calc(100% - var(--directAccessWidth));
  }

  .T_0301_Toast {
    bottom: 13px;
  }

  .SE_0014_SoundVolumeOverlay,
  .T_0302_Dialog {
    left: calc(50% + var(--directAccessWidth) / 2);
  }

  .SE_0023_Display_ON_OFF.stopCleaning .container .variant,
  .SE_0023_Display_ON_OFF.tapReactivate .container .variant {
    left: var(--nativeScreenOffset);
    width: calc(100% - var(--nativeScreenOffset));
  }
  .SE_0023_Display_ON_OFF.stopCleaning .container,
  .SE_0023_Display_ON_OFF.tapReactivate .container {
    background-color: rgb(var(--audiBlack));
  }
}
