/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0201_ToggleButtonGroup {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  margin-top: 140px;
}

:global(#root) > .bodyWrapper {
  margin-top: 40px;
}

.UE_0203_ToggleTabGroup {
}

.bodyWrapper {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.toggleButtonGroup .headline {
  margin-top: 0;
}
.textPrio1.noSubText {
  margin-bottom: var(--hA1);
}
.textPrio2.subText {
  margin-bottom: var(--hA1);
  opacity: var(--secondaryTextOpacity);
}
.subText2 {
  opacity: var(--secondaryTextOpacity);
  margin-bottom: var(--hA1);
}
.subText.twoLines {
  margin-bottom: 0;
}

.toggleButtonGroup .headline.hidden {
  /* display: none; */
}

.toggleButtonGroup .toggleBar {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
}

.toggleButtonGroup .toggleBar .UE_0101_ActionButton {
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  position: relative;
  display: flex;
  justify-content: center;
}

.toggleButtonGroup .toggleBar .button.large {
  flex: 1;
}

.UE_0201_ToggleButtonGroup
  .toggleButtonGroup
  .toggleBar
  .button.floatingButton {
  position: absolute;
  display: flex;
  top: -90px;
}

.UE_0201_ToggleButtonGroup
  .toggleButtonGroup
  .toggleBar
  .button.floatingButton
  :global(.interactionBackground) {
  background-image: linear-gradient(
    var(--buttonBackgroundColor),
    var(--buttonBackgroundColor)
  );
  background-color: rgba(var(--audiBlack), 0.95);
}

.toggleBar #togglebutton6,
.toggleBar #togglebutton7 {
  max-width: var(--minTouchSize);
}

.toggleButtonGroup .toggleBar .button p {
  line-height: var(--minTouchSize);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-align: center;
  margin: 0 var(--textButtonMargin);
  z-index: 1;
}

/* ----------------------- interior toggle buttongroup ---------------------- */

.interiorLightToggleGroup {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.bodyWrapper.intLight .interiorLightToggleGroup {
  display: flex;
}

.interiorLightToggleGroup .UE_0101_ActionButton {
  height: var(--minTouchSize);
  min-width: var(--minTouchSize);
  max-width: var(--minTouchSize);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.UE_0101_ActionButton .btnContainerText {
  width: 100%;
}

.tbgIconAndText .UE_0101_ActionButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-left: 30px;
  padding-right: 30px;
  width: auto;
}

.UE_0101_ActionButton .tbgIconAndText_Text {
  max-width: 150px;
  margin-left: 15px;
  order: 2;
}

.tbgIconAndText .toggleBar .button .tbgIconAndText_Text p {
  margin: unset;
}

.tbgIconAndText .UE_0101_ActionButton .icon {
  order: 1;
  position: relative;
}

.tbgIconAndText .UE_0101_ActionButton .touchIcon {
  left: unset;
  transform: translateY(-50%);
  position: relative;
}

.interiorLightToggleGroup .colorgroup {
  display: flex;
  position: relative;
}

.driveSelectButton {
  display: flex;
  align-items: center;
  justify-content: center;
}
.driveSelectIcon {
  background-image: url(../assets/driveSelect.png);
  width: 79px;
  height: 46px;
}

.interiorLightToggleGroup .button p {
  line-height: var(--minTouchSize);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  text-align: center;
  margin: auto;
  z-index: 1;
}

.preset {
  position: absolute;
  margin: auto;
  width: 36px;
  height: 36px;
}
.preset.line {
  -webkit-mask-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E39E_preset_ambientlight_line.svg);
}
.preset.gradient {
  -webkit-mask-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E785_preset_ambientlight_gradient.svg);
  /* transform: rotate(180deg); */
}

.preset.line.SE_5006_00_neutral {
  background-color: rgb(var(--LED_neutral));
}
.preset.gradient.SE_5006_00_neutral {
  background-color: rgb(var(--LED_neutral_background));
}

.preset.line.SE_5006_02_orange {
  background-color: rgb(var(--LED_orange));
}
.preset.gradient.SE_5006_02_orange {
  background-color: rgb(var(--LED_orange_background));
}

.preset.line.SE_5006_04_rot {
  background-color: rgb(var(--LED_rot));
}
.preset.gradient.SE_5006_04_rot {
  background-color: rgb(var(--LED_rot_background));
}

.preset.line.SE_5006_06_violett {
  background-color: rgb(var(--LED_violett));
}
.preset.gradient.SE_5006_06_violett {
  background-color: rgb(var(--LED_violett_background));
}

.preset.line.SE_5006_08_blau {
  background-color: rgb(var(--LED_blau));
}
.preset.gradient.SE_5006_08_blau {
  background-color: rgb(var(--LED_blau_background));
}

.preset.line.SE_5006_10_gruen {
  background-color: rgb(var(--LED_gruen));
}
.preset.gradient.SE_5006_10_gruen {
  background-color: rgb(var(--LED_gruen_background));
}

.preset.line.SE_5006_12_rs {
  background-color: rgb(var(--LED_rs));
}
.preset.gradient.SE_5006_12_rs {
  background-color: rgb(var(--LED_rs_background));
}

