:root {
  --fpkCommonIconSize: 60px;
}

:global(#root) > .SE_5012_DriveSelectTeilnehmerRS {
  background-color: rgb(var(--bodyColorGrey));
  width: var(--cardContentWidthKB);
  height: var(--cardContentHeight);
  margin: 0;
}

.SE_5012_DriveSelectTeilnehmerRS {
}

/* EDITED */
.driveSelectStatus {
  height: 100%;
  width: var(--cardContentWidthKB);
}

.SE_5012_00_DriveSelect5Teilnehmer,
.SE_5012_01_DriveSelect4Teilnehmer {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  width: var(--cardContentWidthKB);
  align-items: center;
}

.driveSelectStatus .lineDriveSelect {
  display: flex;
  width: 100%;
  padding-left: var(--zA4);
  box-sizing: border-box;
}

.SE_5012_01_DriveSelect4TeilnehmerDriveSelect .lineDriveSelect:first-child {
  max-height: fit-content;
  margin-top: var(--zA4);
}
.SE_5012_01_DriveSelect4TeilnehmerDriveSelect .lineDriveSelect:last-child {
  max-height: fit-content;
  margin-bottom: var(--zA4);
}
.driveSelectStatus {
  max-height: fit-content;
  margin-bottom: var(--zA4);
}

.driveSelect {
  display: flex;
  align-items: center;
  width: 50%;
  padding-right: var(--lA1);
  box-sizing: border-box;
}
.driveSelectStatus
  .SE_5012_01_DriveSelect4TeilnehmerDriveSelect
  .lineDriveSelect
  .driveSelect {
  max-height: fit-content;
}

/* EDITED */
.iconDriveSelect {
  margin-right: var(--iA1);
  flex-shrink: 0;
}

.fpkCommonIcon {
  height: var(--fpkCommonIconSize);
  width: var(--fpkCommonIconSize);
  background-color: rgba(var(--boundingBoxGreenFull));
}

/* EDITED */
.settings {
  position: absolute;
  top: 500px;
}
