/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --maneuverArrowSize: 50px;
  --maneuverArrowSizeSmall: 40px;
  --laneWidth: 40px;
  --laneHeight: 30px;
  --laneBorderStrength: 1px;
  --laneGuidanceMaxWidth: calc(
    var(--laneWidth) * 8 + var(--laneBorderStrength) * 9 +
      (var(--maneuverArrowMargin) - var(--iA1)) * 2 + var(--iA1) * 2
  );
}

:global(#root) > .SE_0056_NextManeuverBox {
  position: absolute;
  max-width: calc(
    100% - var(--etaBoxWidth) - var(--minTouchSize) * 4 - var(--zA8) * 5
  );
}

.SE_0056_NextManeuverBox {
}

.nextManeuverBox {
  display: flex;
  flex-direction: column;
  position: relative;
}
/*----------------------- DISTANCE TO MANEUVER -----------------------*/
.dtm {
  display: none;
  align-items: center;
  height: var(--minTouchSize);
}

.maneuverArrow {
  width: var(--maneuverArrowSize);
  height: var(--maneuverArrowSize);
  background-color: red;
  margin-right: var(--cA1);
}

.streetIcon {
  width: var(--maneuverArrowSize);
  height: var(--maneuverArrowSize);
  background-color: red;
  margin-left: var(--textButtonMargin);
}
.textWrapper {
  display: flex;
  align-items: baseline;
}

.unit {
  margin-left: var(--zA8);
}

/*----------------------- DTM DOUBLE -----------------------*/
.dtm.double {
  height: fit-content;
  padding: var(--iA1) 0;
}

.dtm.double .maneuverArrow {
  width: var(--maneuverArrowSizeSmall);
  height: var(--maneuverArrowSizeSmall);
  background-color: red;
  margin-right: calc(var(--cA1) + 6px);
  margin-left: calc(
    (var(--maneuverArrowSize) - var(--maneuverArrowSizeSmall)) / 2
  );
}
/*----------------------- TOPBOX -----------------------*/
.topBox {
  display: flex;
  align-items: center;
  background-color: rgba(var(--audiBlack), 0.85);
  padding: 0 var(--cA1);
  box-sizing: border-box;
}
/*----------------------- BOTTOMBOX -----------------------*/
.bottomBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  width: fit-content;
  margin-top: var(--zA8);
  background-color: rgba(var(--audiBlack), 0.85);
  padding: 0 var(--cA1);
  box-sizing: border-box;
}

.bottomBox > * {
  position: relative;
}

/*----------------------- LANE GUIDANCE -----------------------*/
.SE_0067_LaneGuidance {
  background-color: rgba(var(--audiBlack), 0.85);
  padding: 0px var(--cA1) 35px;
  display: flex;
  justify-content: center;
  height: var(--laneWidth);
  width: calc(100% - 60px);
}

.SE_0067_LaneGuidance .lane {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 100%;
  border-left: var(--laneBorderStrength) dashed rgba(var(--audiWhite), 0.5);
  position: relative;
}

.SE_0067_LaneGuidance .lane:last-child {
  border-right: var(--laneBorderStrength) dashed rgba(var(--audiWhite), 0.5);
}

.SE_0067_LaneGuidance .lane .laneIcon {
  height: var(--laneWidth);
  width: var(--laneWidth);
  background-color: rgba(var(--boundingBoxGreen));
}

.SE_0067_LaneGuidance .lane .laneIcon.secondaryLaneIcon {
  position: absolute;
  top: 35px;
}

/*----------------------- STREETNAME -----------------------*/

.streetname {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  padding-left: var(--textButtonMargin);
}

/* -------------------------------- settings -------------------------------- */

.settings {
  position: absolute;
  top: 400px;
  width: 1000px;
}
