:global(#root) > .T_0107_SkeletonScreen {
  width: 1800px;
  height: 698px;
}

:global(#root) > .T_0107_SkeletonScreen .db {
  height: 698px;
}

.dark {
  background-color: rgba(var(--audiGrey), 0.45);
  border-radius: var(--standardBorderRadius);
}

.textPlaceholder {
  background-color: rgba(var(--audiGrey), 0.8);
}

.T_0107_SkeletonScreen.animation .dark,
.T_0107_SkeletonScreen.animation .textPlaceholder {
  animation-duration: 1000ms;
  animation-timing-function: ease-in-out;
  animation-direction: reverse;
  animation-direction: alternate;
  animation-iteration-count: infinite;
}

.T_0107_SkeletonScreen.animation .dark {
  animation-name: animateArea;
}
.T_0107_SkeletonScreen.animation .textPlaceholder {
  animation-name: animateTextPlaceholder;
}

@keyframes animateArea {
  0% {
    background-color: rgba(var(--audiGrey), 0.45);
  }
  100% {
    background-color: rgba(var(--audiDarkGrey), 0.45);
  }
}

@keyframes animateTextPlaceholder {
  0% {
    background-color: rgba(var(--audiGrey), 0.8);
  }
  100% {
    background-color: rgba(var(--audiGrey), 0.2);
  }
}

.navi,
.list,
.halfList,
.radio,
.calendar,
.splitList,
.db,
.assistant,
.etron,
.carService {
  height: 100%;
  width: 100%;
  display: flex;
}

.naviMap {
  position: relative;
  display: flex;
  height: 100%;
  width: 100%;
  top: 0px;
  left: 0px;
}

.list {
  flex-direction: column;
}

.listItem,
.halfListItem,
.fullListItem {
  flex: 1;
  width: calc(100% - 56px);
  margin: 5px 0 0 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.list .smallHeader_LR:first-child,
.list .smallHeader_LR:first-child + .listItem,
.list .smallHeader_LR:first-child + .listItem + .smallHeader_LR {
  margin-bottom: 10px;
}

.detailView.splitList .halfListItem {
  width: calc(100% - 5px);
  margin-left: 0;
}

.listRecommendations.splitList .fullListItem,
.detailView.splitList .halfListItem,
.store.splitList .halfListItem {
  height: var(--minTouchSize);
  flex: none;
}

.listItem:first-of-type,
.halfListItem:first-of-type,
.fullListItem:first-of-type {
  margin-top: 0;
}

.listUpperText {
  height: var(--fontSizePrio1);
  margin-left: var(--lA1);
  width: 20%;
}

.listLowerText {
  height: var(--fontSizePrio2);
  margin-top: 10px;
  margin-left: var(--lA1);
  width: 40%;
}

.radio {
  flex-direction: column;
  justify-content: space-between;
}

.upper,
.lower {
  height: var(--minTouchSize);
  width: 100%;
}

.halfList {
  width: calc(50% - 2 * var(--listVerticalPadding));
  flex-direction: column;
}
.detailView .halfList {
  width: 60%;
}

.detailViewBox {
  width: 40%;
  height: 100%;
}

.fullList {
  width: 100%;
  flex-direction: column;
}

.recommendation {
  margin-bottom: 10px;
  height: var(--callStackTileHeight) !important;
}
.detailView .recommendation {
  margin-bottom: 0;
}

.smallHeader_LR {
  height: 56px;
  width: calc(100% - 56px);
  margin: 5px 0 15px 30px;
}

.headerListContainer {
  display: flex;
  flex-direction: row;
  margin-bottom: 15px;
}

.headerListItem {
  height: 100%;
  width: 33%;
}

.headerListItem:nth-child(2) {
  margin: 0 15px;
}

.headerListContainer.recommendation_two .headerListItem {
  width: 50%;
}

.headerListContainer.recommendation_two .headerListItem:nth-child(2) {
  margin: 0 0 0 15px;
}

.calendar {
  display: grid;
  grid-template-columns:
    [line1] calc(var(--zA4) * 3) [line2] calc(var(--zA4) * 3)
    [line4] 1fr [line3] 1fr [line4] 1fr [line5] 1fr [line6] 1fr [line7] 1fr [line8] 1fr [line9] 1fr [line10] 1fr [line11] 1fr [line12] 1fr [line13] 1fr [line14] 1fr [line15] 1fr;
  padding: 0 var(--cA1);
  width: calc(100% - var(--cA1) * 2);
}

.calendarItem {
  margin: 0 2px;
  grid-column-end: span 2;
}

.calendarItem:first-of-type {
  margin-left: calc(var(--cA1) * -1);
}

.calendarItem:last-of-type {
  margin-right: 0;
}

.calendarItem .textPlaceholder {
  width: calc(100% - var(--lA1) * 2);
  margin-left: var(--lA1);
  margin-right: var(--lA1);
  height: var(--fontSizePrio2);
}

.calendarItem .textPlaceholder:first-of-type {
  margin-top: 10px;
}

.calendarItem .textPlaceholder:last-of-type {
  margin-top: 22px;
}

.db {
  position: absolute;
}

.dbTile {
  position: absolute;
  width: var(--tileWidth);
  height: 100%;
}

.dbTile::after {
  content: '';
  position: absolute;
  top: 0;
  height: 100%;
  width: var(--dashboardTileSpacing);
  right: calc(var(--dashboardTileSpacing) * (-1));
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 1),
    rgba(var(--audiBlack), 0.3)
  );
}

.separator {
  position: absolute;
  top: 110px;
  width: 100%;
  height: 2px;
  background-color: rgba(var(--audiGrey), 1);
}

.tile0 .list {
  overflow: hidden;
}

.tile0 .listItem {
  width: 100%;
  margin-left: 0;
}

.tile1 {
  margin-left: calc(var(--tileWidth) + var(--dashboardTileSpacing));
}

.tile2 {
  margin-left: calc((var(--tileWidth) + var(--dashboardTileSpacing)) * 2);
}

.dbTile.extended {
  width: calc(var(--tileWidth) * 2 + var(--dashboardTileSpacing));
}

.dbTile:first-of-type {
  margin-left: 0;
}

.dbTile:last-of-type {
  margin-right: var(--directAccessWidth);
}

.dbTile:last-of-type::after {
  right: calc(var(--directAccessWidth) * (-1));
  width: var(--directAccessWidth);
}

.dbTile .title {
  height: var(--fontSizePrio1);
  width: 156px;
  margin: 15px auto 0 auto;
}

.dbTile .subTitle {
  height: var(--fontSizePrio2);
  width: 510px;
  margin: 10px auto 0 auto;
}

.assistant .dark {
  position: absolute;
  width: 100%;
  height: 100%;
}

.assistant .dots {
  position: relative;
  bottom: 12px;
  right: 60px;
  width: var(--assistantWidth);
  height: 100px;
  margin: auto;
}

.etron {
  justify-content: space-between;
}

.etron .carDecorator {
  position: absolute;
  top: 120px;
  left: calc(100% / 2 - 780px / 2 - 60px);
  width: 780px;
  height: 250px;
}

.stepper {
  position: relative;
  height: calc(100% - 6px);
  width: var(--minTouchSize);
}

.carService .left {
  width: var(--se0030ButtonWidth);
  margin-left: 30px;
}

.carService .separator {
  top: 426px;
  width: var(--se0030ButtonWidth);
}

.carService .carDecorator {
  position: absolute;
  top: 145px;
  left: 825px;
  width: 690px;
  height: 300px;
}

.settings {
  height: 280px;
  overflow: scroll;
}

@media only screen and (min-width: 2219px) {
  .assistant .dots {
    right: 0px;
  }

  .etron .carDecorator {
    left: calc(100% / 2 - 780px / 2);
  }

  .carService .separator {
    top: 486px;
  }
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .T_0107_SkeletonScreen {
    width: 1648px;
    height: 569px;
  }

  :global(#root) > .T_0107_SkeletonScreen .db {
    height: 569px;
  }

  .listItem:last-of-type,
  .halfListItem:nth-of-type(n + 5),
  .fullListItem:nth-of-type(n + 5) {
    display: none;
  }

  .carService .separator {
    top: 300px;
  }
  .carService .carDecorator {
    top: 145px;
    left: 870px;
    width: 430px;
    height: 190px;
  }

  .etron .carDecorator {
    top: 100px;
    left: calc(100% / 2 - 520px / 2 - 60px);
    width: 520px;
    height: 160px;
  }
}

.fullcontentText {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 31px 43px 0 33px;
}

.fctTitle {
  height: var(--fontSizePrio1);
  width: 371px;
  margin-bottom: 36px;
}

.fctTextline {
  height: var(--fontSizePrio2);
  width: 100%;
  margin-bottom: 10px;
}
