.speedometer {
  width: 151px;
  height: 54px;

  display: flex;
  position: relative;
}

.speedometer__background {
  position: absolute;
  background-color: rgba(255, 255, 0, 0.5);
  width: 50%;
  height: 100%;
}
.speedometer__background--left {
  left: 0;
  border-top-right-radius: 5px;
}
.speedometer__background--right {
  right: 0;
  border-top-left-radius: 5px;
}

.speedometer__icons {
  background-color: rgba(255, 0, 0, 0.5);
  height: 100%;
  width: 54px;

  position: relative;
}
.speedometer__icons--noBgColor {
  background-color: unset;
}

.speedometer__icons__icon {
  position: absolute;
  left: 50%;
  top: 4%;

  transform: translateX(-50%);
}

.speedometer__icons__icon--arrow-curve-right {
  top: 8%;
  left: 70%;
}

.speedometer__icons__icon--arrow-right {
  left: 28%;
  transform: rotate(90deg);
}

.speedometer__text-container {
  display: flex;
  width: 97px;
  align-items: flex-end;
  z-index: 1;
}
.speedometer__text-container p {
  margin-left: -1.3px;
  margin-right: -1.2px;
  line-height: 1;
}
.speedometer__text-container p:not(:first-of-type) {
  margin-bottom: 1.5px;
}

.speedometer__text-container__divider {
  background-color: rgba(0, 255, 255, 0.5);
  height: 100%;
  width: 5px;
}
.speedometer__text-container__divider--2 {
  width: 6px;
}

.hideElement {
  visibility: hidden;
  pointer-events: none;
}
