:global(#root) > .SE_0079_LogbookEdit {
  margin: 0px;
  width: 1682px;
  height: 555px;
  overflow: hidden;
  position: absolute;
}

.SE_0079_LogbookEdit {
}

.SE_0079_LogbookEdit .logbookEdit {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 100%;
}

.SE_0079_LogbookEdit .logbookEdit .contentArea {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
}

.SE_0079_LogbookEdit .logbookEdit .leftPanel,
.SE_0079_LogbookEdit .logbookEdit .rightPanel,
.SE_0079_LogbookEdit .logbookEdit .mischfahrtLeft,
.SE_0079_LogbookEdit .logbookEdit .mischfahrtRight {
  display: none;
}

.SE_0079_LogbookEdit.logbook .logbookEdit .leftPanel {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
}

.SE_0079_LogbookEdit.logbook .logbookEdit .rightPanel {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
  justify-content: center;
}

.SE_0079_LogbookEdit .logbookEdit .leftPanel .UE_0201_ToggleButtonGroup {
  margin-top: 10px;
}
.SE_0079_LogbookEdit .logbookEdit .leftPanel .T_0103_ListVertical {
  height: calc(100% - var(--minTouchSize) - 2px);
  padding: 0 var(--popupInsetWidth);
  margin: var(--popupInsetWidth) 0;
  position: relative;
  flex: 1;
  display: flex;
  overflow: hidden;
}

.SE_0079_LogbookEdit .logbookEdit .rightPanel .rightPanelContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-content: flex-start;
  box-sizing: border-box;
  flex-direction: column;
  padding: 0 var(--iA2);
  justify-content: center;
}

.SE_0079_LogbookEdit .logbookEdit .rightPanel .rightPanelText {
  margin-bottom: var(--iA4);
}

.SE_0079_LogbookEdit .logbookEdit p {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.SE_0079_LogbookEdit .logbookEdit .UE_0501_InputField {
  height: var(--minTouchSize);
}

.SE_0079_LogbookEdit .logbookEdit .UE_0501_InputField:not(:last-of-type) {
  margin-bottom: var(--iA4);
}

.SE_0079_LogbookEdit.mischfahrt .logbookEdit .mischfahrtLeft {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: inherit;
  box-sizing: border-box;
}

.SE_0079_LogbookEdit .logbookEdit .mischfahrtLeft .mischfahrtLeftSlider {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: inherit;
  box-sizing: border-box;
}

.SE_0079_LogbookEdit .logbookEdit .mischfahrtLeft .T_0103_ListVertical {
  height: 100%;
  padding: var(--popupInsetWidth);
  box-sizing: border-box;
}

.SE_0079_LogbookEdit.mischfahrt .logbookEdit .mischfahrtRight {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
  align-items: center;
  justify-content: center;
  padding: 0 var(--zA4);
  box-sizing: border-box;
}

.SE_0079_LogbookEdit .logbookEdit .mischfahrtRight .mischfahrtRightText {
  overflow: hidden;
  width: 100%;
}

.SE_0079_LogbookEdit
  .logbookEdit
  .mischfahrtRight
  .mischfahrtRightText
  .mischfahrtValue {
  display: flex;
  align-items: baseline;
  justify-content: center;
  margin-top: 80px;
  position: absolute;
  top: 0;
  width: 100%;
}

.SE_0079_LogbookEdit
  .logbookEdit
  .mischfahrtRight
  .mischfahrtRightText
  .mischfahrtValue
  p:first-child {
  margin-right: var(--zA8);
}

.SE_0079_LogbookEdit .logbookEdit .mischfahrtRight .mischfahrtRightText p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
}

.SE_0079_LogbookEdit .logbookEdit .mischfahrtRight .UE_0101_ActionButton {
  position: absolute;
  width: fit-content;
  max-width: calc(100% - 60px);
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
  bottom: 0;
  margin-bottom: var(--zA4);
}
/*-------noCalender-------*/
.SE_0079_LogbookEdit.noCalendar .logbookEdit .leftPanel .noCalendarContainer {
  display: flex;
}

.SE_0079_LogbookEdit.noCalendar .logbookEdit .rightPanel {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
}

.SE_0079_LogbookEdit.noCalendar .logbookEdit .leftPanel {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50%;
  box-sizing: border-box;
}
.SE_0079_LogbookEdit.noCalendar .logbookEdit .leftPanel .T_0103_ListVertical {
  display: none;
}

.SE_0079_LogbookEdit .logbookEdit .leftPanel .noCalendarContainer {
  width: 100%;
  height: calc(100% - var(--minTouchSize));
  display: none;
  flex-direction: column;
  align-items: center;
}

.SE_0079_LogbookEdit
  .logbookEdit
  .leftPanel
  .noCalendarContainer
  .textContainer {
  width: 100%;
  height: calc(100% - var(--minTouchSize));
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.SE_0079_LogbookEdit .logbookEdit .leftPanel .noCalendarContainer .textPrio2 {
  margin: 0 var(--cA1);
  width: calc(100% - 2 * var(--cA1));
  text-align: center;
}

.SE_0079_LogbookEdit
  .logbookEdit
  .leftPanel
  .noCalendarContainer
  .buttonWrapper,
.buttonWrapper_right {
  width: 100%;
  display: flex;
  flex-direction: row;
  bottom: 0px;
  margin-top: auto;
}

.buttonWrapper_right {
  margin-top: unset;
  bottom: unset;
}

.SE_0079_LogbookEdit
  .logbookEdit
  .leftPanel
  .noCalendarContainer
  .buttonWrapper
  .button,
.buttonWrapper_right .button {
  width: 50%;
  height: var(--minTouchSize);
  position: relative;
}

.settings {
  position: absolute;
  top: 60%;
}
@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0079_LogbookEdit {
    width: 1530px;
    height: 427px;
  }
}
