:global(#root) > .SE_0101_PIDLockscreen {
  position: relative;
  width: 1920px;
  height: 720px;
  margin: 0px;
  overflow: hidden;
}
.SE_0101_PIDLockscreen {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgb(var(--audiBlack));
}

.SE_0101_PIDLockscreen .backgroundSpot {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  background-repeat: no-repeat;
  transform: scaleX(-1);
  background-position-x: -100px;
}

.PIDLockscreenContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 0 30px;
  box-sizing: border-box;
}

.touchIcon {
  position: unset;
  left: unset;
  top: unset;
  transform: none;
  margin-bottom: 15px;
}

/*------------------------------------- Decor background ------------------------------------------*/

.digitalDecorator {
  position: absolute;
  height: 100%;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  aspect-ratio: 1920/720;
}

.digitalDecorator .systemUIBackground {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
  background-image: var(--backgroundSpot);
  transform: scaleX(-1);
  background-position-x: -100px;
}

.digitalDecorator .digitalDecoratorImage {
  position: absolute;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.digitalDecorator.A_Linie .digitalDecoratorImage {
  background-image: url(../img/SE_0101_PIDLockscreen_A_Linie.png);
}
.digitalDecorator.Sport .digitalDecoratorImage {
  background-image: url(../img/SE_0101_PIDLockscreen_Sport.png);
}
.digitalDecorator.RS .digitalDecoratorImage {
  background-image: url(../img/SE_0101_PIDLockscreen_RS.png);
}

.PID_LOCK {
  width: 100%;
  height: 100%;
}

.pidContainer {
  display: flex;
  height: 100%;
  flex-direction: column;

  justify-content: space-between;
}

.iconContainer {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  align-self: flex-end;
}

.iconContainer .touchIcon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.UE_0101_ActionButton {
  align-self: center;
  margin-bottom: 5%;
  width: calc(3 * var(--minTouchSize));
}
