/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0033_MiniPlayer {
  background-color: rgb(var(--bodyColorGrey));
  background-image: none;
  position: absolute;
  margin: 0;
  height: calc(var(--minTouchSize) * 2);
  width: var(--tileWidth);
}

:global(#root) > .SE_0033_MiniPlayer.extended {
  height: 700px;
}

:global(#root) > .SE_0033_MiniPlayer.detailView {
  height: var(--minTouchSize);
  width: 100%;
  position: absolute;
}

:root {
  --controlWidth: calc(33% - var(--miniPlayerMargin) * 2);
  --textContainerMargin: calc(var(--iA1) + 9px);
  --miniPlayerMargin: 8px;
}

.SE_0033_MiniPlayer {
  position: absolute;
  height: calc(var(--minTouchSize) * 2);
  width: 100%;
}

.appSourceBtn .touchIcon {
  width: var(--daIconSize);
  height: var(--daIconSize);
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Special/E5A7_main_menue_Radio.svg);
}

.SE_0033_MiniPlayer .miniPlayerBackground {
  position: absolute;
  height: calc(100%);
  background-color: rgba(var(--interactionBackground), 0.25);
  border-radius: var(--standardBorderRadius);
  width: 100%;
}
.SE_0033_MiniPlayer.detailView .miniPlayerBackground {
  height: calc(100% - var(--opticalTouchReduction));
  width: calc(100% - var(--opticalTouchReduction));
  top: calc(var(--opticalTouchReduction) / 2);
  left: calc(var(--opticalTouchReduction) / 2);
}

.SE_0033_MiniPlayer.detailView {
  height: var(--minTouchSize);
  position: absolute;
}

.miniPlayer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: stretch;
  flex-direction: column-reverse;
}

.liveSportsPlayer {
  margin-right: 150px;
}

.controls {
  position: relative;
  display: flex;
  align-items: center;
  height: var(--minTouchSize);
  width: var(--controlWidth);
  min-width: 563px;
}

.controls .button {
  position: relative;
  flex: 1;
  height: 100%;
}

.controls .button .UE_0701_Loader {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}

.button:nth-of-type(3) {
  display: flex;
  align-items: center;
  justify-content: center;
}

.trackTitle {
  flex: 1;
  margin-left: var(--textContainerMargin);
  margin-right: var(--textContainerMargin);
  align-self: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.trackTitle p {
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.subtitle {
  margin-top: var(--zA2);
}

.miniPlayer .trackTitle .trackTitleContainer {
  display: flex;
  align-items: center;
  height: var(--listIconSize);
}

.radioHdContainer {
  display: flex;
  align-items: center;
}
.radioHdContainer .Text:first-of-type {
  width: unset;
  overflow: unset;
}
.radioHdContainer .listIcon,
.radioHdContainer .Text:last-of-type {
  margin-left: var(--iA1);
}

/*------------------------------------------- CONTENT DASHBOARD ---------------------------------------------*/

.controls.dashboardMiniPlayer {
  width: 100%;
  min-height: var(--minTouchSize);
}
.controls.dashboardMiniPlayer::before {
  position: absolute;
  content: '';
  background-color: rgba(var(--audiBlack), 0.5);
  height: 2px;
  width: calc(100% - 52px);
  left: 28px;
  top: -1px;
}

.trackInfo {
  position: relative;
  height: var(--minTouchSize);
  min-height: var(--minTouchSize);
  display: flex;
  align-items: center;
  flex: 1;
  overflow: hidden;
}

.trackInfo .button {
  position: relative;
  height: 100%;
  width: var(--minTouchSize);
}

.arrow {
  position: absolute;
  top: 20px;
  left: 12px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid rgb(var(--audiWhite));
}

.SE_0033_MiniPlayer.extended {
  height: unset;
}

.SE_0033_MiniPlayer.extended .miniPlayer {
  justify-content: space-between;
}

.SE_0033_MiniPlayer.extended .trackInfo {
  max-height: var(--minTouchSize);
}
.SE_0033_MiniPlayer.extended .trackInfo::before {
  position: absolute;
  content: '';
  background-color: rgba(var(--audiBlack), 0.5);
  height: 2px;
  width: calc(100% - 52px);
  left: 28px;
  bottom: calc(var(--opticalTouchReduction) / 2 + 1px);
}

.SE_0033_MiniPlayer.grow .trackInfo {
  height: var(--minTouchSize);
  max-height: var(--minTouchSize);
  flex: unset;
}

.SE_0033_MiniPlayer .miniPlayerCoverContainer,
.SE_0033_MiniPlayer .playlistContainer {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}
.SE_0033_MiniPlayer .playlistContainer {
  margin-top: -5px;
}

.SE_0033_MiniPlayer .playlistContainer .T_0103_ListVertical {
  position: relative;
  height: 100%;
  width: 100%;
}

/*------------------------------------------- CONTENT DETAILVIEW ---------------------------------------------*/

.SE_0033_MiniPlayer.detailView .miniPlayer {
  flex-direction: row;
}
.SE_0033_MiniPlayer.detailView .controls {
  width: var(--controlWidth);
  height: var(--minTouchSize);
}

.trackInfo {
  height: 100%;
}

.cover {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  height: calc(100% - 60px);
  width: calc(100% - 60px);
  max-height: var(--coverSize);
  max-width: var(--coverSize);
  margin-top: 30px;
  margin-bottom: 30px;
}

.SE_0033_MiniPlayer.detailView .cover {
  height: var(--coverSizeList);
  width: var(--coverSizeList);
  margin: 0px;
}

.time {
  align-self: center;
  text-align: right;
  justify-self: flex-end;
  margin-right: var(--textContainerMargin);
  width: 300px;
}

/*--------------------------------- SETTINGS ---------------------------------------*/
.settings {
  position: absolute;
  top: 750px;
  width: 80vw;
}
