/* Start: MASSAGE SIDE LIST */
.listMassageContainer {
  position: absolute;
  width: 100%;
  height: calc(100% - var(--minTouchSize) - var(--iA1));
  display: flex;
  justify-content: space-between;
}

.leftListMassage,
.rightListMassage {
  position: absolute;
  width: 415px;
  height: 100%;
}

.leftListMassage {
  left: 12px;
}
.rightListMassage {
  right: 12px;
}

.UE_0407_Dropdown {
  width: 100%;
  max-width: 415px;
}

.massageIconContainer {
  position: absolute;
  left: 430px;
  width: calc(100% - (415px + 12px + 15px) * 2);
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: var(--minTouchSize);
  margin: 0 var(--iA1);
}

.massageIconLeft,
.massageIconRight {
  height: 54px;
  width: 54px;
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
  background-color: rgb(var(--ambientcolor1));
}

.activation {
  mask-image: url(../../img/activation.png);
  -webkit-mask-image: url(../../img/activation.png);
}

.back {
  mask-image: url(../../img/back.png);
  -webkit-mask-image: url(../../img/back.png);
}

.balance {
  mask-image: url(../../img/balance.png);
  -webkit-mask-image: url(../../img/balance.png);
}

.mobilization {
  mask-image: url(../../img/mobilization.png);
  -webkit-mask-image: url(../../img/mobilization.png);
}

.relax {
  mask-image: url(../../img/relax.png);
  -webkit-mask-image: url(../../img/relax.png);
}

.shoulder {
  mask-image: url(../../img/shoulder.png);
  -webkit-mask-image: url(../../img/shoulder.png);
}

.stretch {
  mask-image: url(../../img/stretch.png);
  -webkit-mask-image: url(../../img/stretch.png);
}

.vitalization {
  mask-image: url(../../img/vitalization.png);
  -webkit-mask-image: url(../../img/vitalization.png);
}

/* End: MASSAGE SIDE BUTTONS */

.seatContainer {
  display: flex;
  justify-content: center;
  height: 497px;
  top: 164px;
  position: absolute;
  pointer-events: none;
  margin: 0 var(--sideMarginSeats);
  width: calc(100% - 2 * var(--sideMarginSeats));
}

.seatLeft {
  top: 50%;
  transform: translateY(-50%);
  height: 100%;
  width: 425px;
  position: absolute;
  left: 59px;
}

.seatRight {
  height: 100%;
  width: 425px;
  position: absolute;
  right: 59px;
  top: 50%;
  transform: scaleX(-1) translateY(-50%);
}

.seatDecorator {
  width: 425px;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url('../../img/Decorator_SingleSeat.png');
  position: absolute;
}

.seatHighlightAll,
.seatHighlightUp,
.seatHighlightLow,
.seatHighlightSeat {
  -webkit-mask-size: contain;
  mask-size: contain;

  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;

  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 1;
}

