/** seat button container **/

.seatButtonContainer {
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
  bottom: var(--seatButtonAreaSpace);
  position: absolute;
  margin-left: var(--seatButtonAreaSpace);
  margin-right: var(--seatButtonAreaSpace);
  width: calc(100% - (2 * var(--seatButtonAreaSpace)));
  text-overflow: ellipsis;
}

.seatButtonWrapper {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 423px;
  overflow: hidden;
}

.onoffButton {
  width: 203px;
  text-transform: uppercase;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.seatButtonContainer .threeStatesButton {
  min-width: var(--minThreeStateButtonWidth);
}

.seatButtonContainer .button {
  height: var(--minTouchSize);
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  max-width: calc(100% - var(--minTouchSize));
}

@media only screen and (max-width: 1768px) {
  .button .btnContainerText p {
    width: calc(
      100% - ((var(--bckgrndPicWidthSmall) / 2) + (2 * var(--textButtonMargin)))
    );
  }

  .seatButtonContainer {
    justify-content: center;
    height: var(--SeatButtonWrapperHeight);
    top: 0;
    pointer-events: none;
  }

  .seatButtonContainer .seatButtonWrapper {
    flex-direction: column;
    justify-content: center;
    max-width: 300px;
    pointer-events: all;
  }

  .seatButtonContainer .seatButtonWrapper.right {
    flex-direction: column-reverse;
    margin-left: 60px;
  }

  .seatButtonContainer .button {
    width: auto;
    max-width: unset;
  }

  .seatButtonContainer .seatButtonWrapper p {
    width: auto;
  }

  .colorpattern {
    width: 350px;
    left: 20px;
    transform: none;
  }

  .seatContainer {
    height: 95%;
    margin: 0 var(--sideMarginSeatsSmall);
    width: calc(100% - 2 * var(--sideMarginSeatsSmall));
  }

  .seatLeft .connectorMassageV1.cmv1 .SE_0106_Button_with_Connector,
