/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0035_Disambiguation_List {
  background: rgb(var(--bodyColorGrey));
  height: 600px;
  width: 100%;
  margin: 10px;
}

.disListContainer {
  position: relative;
  width: calc(100% - 22px);
  height: calc(100% + 6px);
  top: -6px;
  padding: 0px 0px;
  margin-left: calc(var(--opticalTouchReduction) / 2);
  display: flex;
  flex-direction: row;
  align-items: stretch;
}

.disList {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 8px 0px 0px 0px;
  margin: 0;
  overflow-y: hidden;
  overflow-x: hidden;
}

.disList.numbers {
  width: calc(var(--minTouchSize) - var(--opticalTouchReduction));
}

.disList.numbers.icons .touchIcon {
  position: relative;
  transform: none;
  left: 0;
}

.disList.text {
  flex: 1;
}

.disListItem {
  width: 100%;
  display: flex;
  flex: 1;
  flex-direction: row;
  overflow: hidden;
}

.disListItemBorder {
  border-bottom: 1px solid rgba(var(--audiWhite), 0.1);
}

.indexContainer {
  position: relative;
  width: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  overflow: hidden;
  justify-content: center;
  margin-top: auto;
  margin-bottom: auto;
  flex-shrink: 0;
}

.iconTextContainer {
  flex-direction: column;
  justify-content: center;
  margin-top: unset;
  margin-bottom: unset;
}

.textDisIconText {
  align-self: center;
  margin: var(--zA1) var(--iA1);
  max-width: calc(100% - 30px);
  position: relative;
  top: 0;
  left: 0;
  transform: initial;
}

.listItemContainer {
  display: flex;
  align-items: center;
  width: 100%;
  margin-left: 15px;
  border-bottom: 1px solid rgba(var(--audiWhite), 0.1);
}

.disListItem:last-of-type .listItemContainer {
  border-bottom: none;
}

.UE_1002_ListItemStandard {
  width: 100%;
}

.disListIconText .navi .iconContainer.iconFront {
  display: none !important;
}

@media only screen and (max-width: 1768px) {
  .disListItem:nth-child(4) {
    display: none;
  }
  .disListItem:nth-child(3) .listItemContainer {
    border-bottom: none;
  }
}
