/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) {
  background-color: unset;
}
:global(#root) > .UE_1302_Stepper {
  background-color: rgb(var(--audiBlack));
}
:global(#root) > .UE_1302_Stepper {
  width: fit-content;
}

/*----------------------------  .UE_1302_Stepper default ------------------------------------*/

.stepper {
  position: relative;
  height: var(--minTouchSize);
  width: fit-content;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.UE_1302_Stepper .stepper .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.UE_1302_Stepper .stepper .center {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.UE_1302_Stepper .stepper .center > div {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.UE_1302_Stepper .stepper .center .default {
  justify-content: center;
}

.UE_1302_Stepper .stepper .center .textPrio2 {
  text-align: center;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.UE_1302_Stepper .stepper .center .offText {
  text-align: center;
  width: 160px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.UE_1302_Stepper .stepper .center .offText p {
  font-size: var(--fontSizePrio1);
  margin-top: 5px;
}

.UE_1302_Stepper .stepper .center .iconContainer {
  height: 75px;
  position: relative;
}

.UE_1302_Stepper .stepper .center .iconContainer .listIcon {
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.UE_1302_Stepper:not(.temperature):not(.fan):not(.text):not(.default)
  .stepper
  .UE_0101_ActionButton:last-child {
  margin-left: var(--minTouchSize);
}

.UE_1302_Stepper.default .center,
.UE_1302_Stepper.text .center {
  position: unset;
  min-width: var(--minTouchSize);
  max-width: 192px;
  width: fit-content;
}

/*---------------------------- .UE_1302_Stepper temperature ------------------------------------*/
:global(#root) > .UE_1302_Stepper.temperature {
  width: 310px;
}
:global(#root) > .UE_1302_Stepper.temperature.climateBar {
  width: 328px;
}

.UE_1302_Stepper.temperature .stepper {
  justify-content: space-between;
  width: 100%;
}
.UE_1302_Stepper.temperature.climateBar .stepper {
  justify-content: space-between;
  width: 328px;
}
.UE_1302_Stepper.temperature.climateBar.reduced .stepper {
  width: 278px;
}
.UE_1302_Stepper.temperature .stepper .center > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-self: center;
  align-items: baseline;
  height: fit-content;
}

.UE_1302_Stepper.temperature .stepper .center .temperature .temperatureDecimal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  height: fit-content;
}
.UE_1302_Stepper.temperature.fahrenheit
  .stepper
  .center
  .temperature
  .temperatureDecimal {
  align-self: flex-start;
}
.UE_1302_Stepper.temperature.fahrenheit
  .stepper
  .center
  .temperature
  .temperatureDecimal
  .textClimateSmall {
  display: none;
}

.UE_1302_Stepper.temperature
  .stepper
  .center
  .temperature
  .temperatureDecimal
  .textPrio3 {
  margin-bottom: 7px;
  opacity: 0.7;
}

.UE_1302_Stepper.temperature.fahrenheit
  .stepper
  .center
  .temperature
  .temperatureDecimal
  .textPrio3 {
  margin-top: 2px;
  margin-bottom: 0px;
}

.UE_1302_Stepper.temperature .stepper .UE_0101_ActionButton .touchIcon {
  background-image: unset !important;
  -webkit-mask-image: url(../../../../../Globals/assets/defaultTouchIcon.png);
}
.UE_1302_Stepper.temperature
  .stepper
  .UE_0101_ActionButton.minusButton
  .touchIcon {
  background-color: rgb(var(--climateBlue));
}
.UE_1302_Stepper.temperature
  .stepper
  .UE_0101_ActionButton.plusButton
  .touchIcon {
  background-color: rgb(var(--audiRed));
}
.UE_1302_Stepper.temperature.off .stepper .UE_0101_ActionButton .touchIcon {
  background-color: rgb(var(--audiWhite));
}

.UE_1302_Stepper.temperature .stepper .temperatureColorLine {
  position: absolute;
  height: 4px;
  width: 240px;
  bottom: 0px;
  background-image: linear-gradient(
    to right,
    rgb(var(--climateBlue)),
    rgb(var(--audiAssistantRed))
  );
  left: 50%;
  transform: translateX(-50%);
}

/*---------------------------- .UE_1302_Stepper fan ------------------------------------*/
:global(#root) > .UE_1302_Stepper.fan {
  width: 278px;
}
:global(#root) > .UE_1302_Stepper.fan.climateBar {
  width: 328px;
}

.UE_1302_Stepper.fan .stepper {
  justify-content: space-between;
  width: 100%;
}
.UE_1302_Stepper.fan.climateBar .stepper {
  width: 328px;
}

.UE_1302_Stepper.fan.climateBar.reduced .stepper {
  width: 278px;
}
.UE_1302_Stepper.fan .stepper .center > div {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.UE_1302_Stepper.fan .stepper .center .fan .textClimateLarge {
  width: fit-content;
}

.UE_1302_Stepper.fan .stepper .center .fan .touchIcon {
  position: relative;
  margin-left: var(--iA1);
  left: unset;
  top: unset;
  transform: none;
}
.UE_1302_Stepper.fan.reduced .stepper .center .fan .touchIcon {
  margin-left: unset;
}

/*---------------------------- .UE_1302_Stepper climateBar ------------------------------------*/
/* .UE_1302_Stepper.climateBar .stepper .UE_0101_ActionButton.plusButton {
  margin-left: 40px;
}
.UE_1302_Stepper.temperature .stepper .UE_0101_ActionButton.plusButton {
  margin-left: 90px;
} */
.UE_1302_Stepper.climateBar .stepper .touchIcon {
  margin-left: 0px;
}

/*---------------------------- .UE_1302_Stepper Fan Reduced ------------------------------------*/
.UE_1302_Stepper.reduced .stepper {
  width: 100%;
}

/*---------- settings ----------*/
.Settings {
  position: relative;
  top: 170px;
}

/*---------- Anzeigelogik ----------*/

.UE_1302_Stepper.fan .stepper .UE_0101_ActionButton.minusButton .touchIcon {
  background-image: url('../img/Pfeil_links.svg');
  width: 40px;
  height: 40px;
}
.UE_1302_Stepper.fan .stepper .UE_0101_ActionButton.plusButton .touchIcon {
  background-image: url('../img/Pfeil_rechts.svg');
  width: 40px;
  height: 40px;
}

.UE_1302_Stepper.temperature
  .stepper
  .UE_0101_ActionButton.minusButton
  .touchIcon {
  -webkit-mask-image: url('../img/Pfeil_links.svg');
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
  width: 40px;
  height: 40px;
}
.UE_1302_Stepper.temperature.reduced
  .stepper
  .UE_0101_ActionButton.minusButton
  .touchIcon {
  transform: translate(-40px, -50%);
}

.UE_1302_Stepper.fan .stepper .UE_0101_ActionButton.plusButton .touchIcon,
.UE_1302_Stepper.temperature
  .stepper
  .UE_0101_ActionButton.plusButton
  .touchIcon {
  -webkit-mask-image: url('../img/Pfeil_rechts.svg');
  -webkit-mask-position: center;
  -webkit-mask-repeat: no-repeat;
  width: 40px;
  height: 40px;
}
.UE_1302_Stepper.temperature.reduced
  .stepper
  .UE_0101_ActionButton.plusButton
  .touchIcon {
  transform: translate(0px, -50%);
}

.UE_1302_Stepper.temperature .stepper .UE_0101_ActionButton .temperature {
  display: block;
}
.UE_1302_Stepper.fan .stepper .UE_0101_ActionButton,
.UE_1302_Stepper.temperature .stepper .UE_0101_ActionButton {
  z-index: 1;
}
.UE_1302_Stepper.temperature .stepper .center {
  width: fit-content;
  display: flex;
  justify-content: center;
  left: 50%;
  transform: translateX(calc(-50% + 8px));
}
.UE_1302_Stepper.temperature.showText .stepper .center {
  transform: translateX(-50%);
}
.UE_1302_Stepper.fan .stepper .center {
  left: 50%;
  transform: translateX(calc(-50%));
}
