.demoMode,
.demoModeBg {
  position: absolute;
  width: 100%;
  height: 100%;
}

.demoModeBg {
  background-image: url('../../assets/DemoMode-Porsche.png');
}

.textLinesContainer,
.demoLine1,
.demoLine2,
.demoLine3,
.demoBanner {
  position: absolute;
  width: 900px;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  background-color: rgba(255, 255, 0, 0.25);
}

.demoBanner {
  top: 154px;
  height: 80px;
}

.textLine,
.demoLine1,
.demoLine2,
.demoLine3 {
  height: 40px;
}

.demoLine1 {
  bottom: 83px;
}

.demoLine2 {
  bottom: 42px;
}

.demoLine3 {
  bottom: 1px;
}
