}

:global(#root) > .T_5200_PopUpTeil {
  position: relative;
  margin: 8px;
}

.T_5200_PopUpTeil .dialogPopup {
  position: absolute;
  height: var(--popupTeilHeight);
  min-height: var(--popupTeilMinHeight);
  max-height: var(--popupTeilHeight);
  width: calc(var(--popupTeilWidth) - 2px);
  left: 0px;
}

.T_5200_PopUpTeil.darken .dialogPopup .darkenOverlay {
  position: absolute;
  width: calc(100% + 2px);
  height: calc(100% + 2px);
  top: -1px;
  left: -1px;
  background-color: rgba(var(--audiBlack), 0.6);
  z-index: 1;
}

/*----------------POPUP FRAME DESIGN--------------*/
.T_5200_PopUpTeil .dialogPopup .dialogPopupContainer {
  position: absolute;
  width: 100%;
  min-height: var(--popupTeilMinHeight);
