/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --tiltAngle_groundHeight_Dashboard: 53%;
  --tiltAngle_instrumentDiameter_Dashboard: 442px;
  --tiltAngle_valueBoxWidth_Dashboard: 54px;
  --tiltAngle_valueBoxHeight_Dashboard: 34px;
  --tiltAngle_frontTiltScaleHeight_Dashboard: 234px;
  --tiltAngle_maxScaleValue_Dashboard: 90;
  --tiltAngle_maxScaleValueExtended_Dashboard: 90;
  --tiltAngle_zeroOpacityValue_Dashboard: 47;
  --tiltAngle_tiltInstrumentHeight_Dashboard: 362px;
  --tiltAngle_frontTiltScaleWidth_Dashboard: 9px;
  --tiltAngle_sideTiltScaleWidth_Dashboard: 14px;
  --tiltAngle_markerWidth_Dashboard: 22px;
  --tiltAngle_scaleMargin_Dashboard: 3px;
  --tiltAngle_tiltBargraphOpacity_Dashboard: 0.4;
  --tiltAngle_infoLineContainerWidth_Dashboard: 250px;
}

:global(#root) > .SE_0138_Tiltangle_DashboardTile {
  position: absolute;
  width: 590px;
  height: 697px;
  margin: 0;
  overflow: hidden;
  background: rgb(var(--bodyColorGrey));
}

:global(#root) > .SE_0138_Tiltangle_DashboardTile > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  /* background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot); */
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay {
  height: 100%;
  width: 100%;
  position: absolute;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltContent {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  overflow: hidden;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .horizonDecorator {
  position: absolute;
  bottom: 145px;
  height: 120px;
  width: 210%;
  left: -55%;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .horizonDecorator.base {
  background-image: linear-gradient(
    to bottom,
    rgba(var(--audiBlack), 0.7),
    transparent 100%
  );
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .carDecoratorWrapper {
  position: absolute;
  height: var(--carDecoratorHeight);
  width: 90%;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .carDecoratorWrapper
  .carDecorator {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  -webkit-mask-size: contain;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .carDecoratorWrapper
  .carDecorator.color {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
}

/*---Instrument---*/
.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument {
  position: absolute;
  width: var(--tiltAngle_instrumentDiameter_Dashboard);
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  bottom: var(--tiltAngle_groundHeight_Dashboard_Dashboard);
  height: calc(var(--tiltAngle_tiltInstrumentHeight_Dashboard));
  overflow: hidden;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument .background {
  background-color: rgb(var(--borderGrey));
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument .sideTilt {
  position: absolute;
  width: 100%;
  clip-path: polygon(0px 20%, 50% 50%, 100% 20%, 100% 80%, 50% 50%, 0px 80%);
  height: var(--tiltAngle_instrumentDiameter_Dashboard);
  top: calc(
    -1 * (
        var(--tiltAngle_instrumentDiameter_Dashboard) -
          var(--tiltAngle_tiltInstrumentHeight_Dashboard)
      ) / 2
  );
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTilt
  .scale {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-image: url(../img/ST_0004-InstrumentSideTilt_HIGH.png);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .rotatingElementsContainer {
  position: absolute;
  width: 100%;
  height: 100%;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine {
  position: absolute;
  width: 100%;
  height: 2px;
  top: calc(50% - 1px);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .valueBox {
  display: flex;
  align-items: center;
  justify-content: center;
  left: calc(
    50% + var(--tiltAngle_markerWidth_Dashboard) + 0.5 *
      var(--tiltAngle_frontTiltScaleWidth_Dashboard) +
      var(--tiltAngle_scaleMargin_Dashboard)
  );
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument .valueBox {
  position: absolute;
  width: var(--tiltAngle_valueBoxWidth_Dashboard);
  height: var(--tiltAngle_valueBoxHeight_Dashboard);
  transform: translateY(-50%);
  top: calc(2px - 50%);
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument .frontTilt {
  position: absolute;
  width: calc(
    var(--tiltAngle_frontTiltScaleWidth_Dashboard) + 2 *
      var(--tiltAngle_scaleMargin_Dashboard)
  );
  height: calc(var(--tiltAngle_frontTiltScaleHeight_Dashboard) + 6px);
  left: 50%;
  top: 50%;
  transform: translate(-50%, calc(-50%));
  /*background-color: rgba(var(--audiBlack), 0.5);*/
  background-color: rgba(var(--audiBlack), 0.7);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .frontTilt
  .scale {
  width: var(--tiltAngle_frontTiltScaleWidth_Dashboard);
  height: var(--tiltAngle_frontTiltScaleHeight_Dashboard);
  margin: 3px var(--tiltAngle_scaleMargin_Dashboard);
  background-image: url(../img/ST_0004-InstrumentLengthTilt_HIGH.png);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .frontTilt
  .filling {
  position: absolute;
  width: calc(100% - 2 * var(--tiltAngle_scaleMargin_Dashboard));
  margin: 3px var(--tiltAngle_scaleMargin_Dashboard);
  background-color: rgba(
    var(--audiWhite),
    var(--tiltAngle_tiltBargraphOpacity_Dashboard)
  );
  top: 118px;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTilt
  .filling {
  width: calc(100% - 2 * var(--tiltAngle_sideTiltScaleWidth_Dashboard));
  height: calc(100% - 2 * var(--tiltAngle_sideTiltScaleWidth_Dashboard));
  border-radius: 50%;
  border: solid var(--tiltAngle_sideTiltScaleWidth_Dashboard)
    rgba(var(--audiWhite), var(--tiltAngle_tiltBargraphOpacity_Dashboard));
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .carHorizonLine {
  position: absolute;
  width: calc(
    100% - 2 *
      (
        var(--tiltAngle_sideTiltScaleWidth_Dashboard) +
          var(--tiltAngle_scaleMargin_Dashboard)
      )
  );
  height: 2px;
  top: 50%;
  transform: translateY(-50%);
  margin: 0 17px;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .carHorizonLine
  .valueBox {
  display: flex;
  align-items: center;
  justify-content: center;
  left: var(--tiltAngle_markerWidth_Dashboard);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .landscapeHorizonLine {
  position: absolute;
  width: 254px;
  height: 2px;
  left: 50%;
  top: calc(50% - 1px);
  transform: translateX(-50%);
  background-color: rgb(var(--borderGrey));
}

/*---Infos---*/
.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoLine {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoLine.top {
  top: 46px;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .infoLine.top
  .textPrio1.value {
  margin-top: 0px;
  margin-left: 10px;
  text-align: right;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoLine.bottom {
  bottom: 28.5px;
  justify-content: space-evenly;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoContainer {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  position: relative;
  align-items: center;
  justify-content: center;
  width: fit-content;
  overflow: hidden;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoContainer.row {
  flex-direction: row;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoContainer .textPrio1 {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .infoLine .infoContainer {
  margin-left: 20px;
  max-width: 50%;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .infoLine
  .infoContainer:first-child {
  margin-left: 0;
  max-width: 50%;
}

/*-----markers----*/

.SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .tiltInstrument .marker {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  height: 3px;
  background-color: rgb(var(--audiWhite));
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .carHorizonLine
  .marker {
  background-color: rgb(var(--ambientcolor1));
  width: var(--tiltAngle_markerWidth_Dashboard);
  /*box-shadow: 0 0 2px;*/
  height: 3px;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .landscapeHorizonLine
  .marker {
  width: calc(var(--tiltAngle_markerWidth_Dashboard) * 2);
  left: 50%;
  transform: translate(-50%, -50%);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.left {
  left: 0;
  width: var(--tiltAngle_markerWidth_Dashboard);
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.center {
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(
    var(--tiltAngle_frontTiltScaleWidth_Dashboard) + 2 *
      (
        var(--tiltAngle_markerWidth_Dashboard) +
          var(--tiltAngle_scaleMargin_Dashboard)
      )
  );
  background-image: linear-gradient(
    to right,
    rgb(var(--ambientcolor1))
      calc(
        50% - var(--tiltAngle_frontTiltScaleWidth_Dashboard) / 2 -
          var(--tiltAngle_scaleMargin_Dashboard)
      ),
    transparent
      calc(
        50% - var(--tiltAngle_frontTiltScaleWidth_Dashboard) / 2 -
          var(--tiltAngle_scaleMargin_Dashboard)
      ),
    transparent
      calc(
        50% + var(--tiltAngle_frontTiltScaleWidth_Dashboard) / 2 +
          var(--tiltAngle_scaleMargin_Dashboard)
      ),
    rgb(var(--ambientcolor1))
      calc(
        50% + var(--tiltAngle_frontTiltScaleWidth_Dashboard) / 2 +
          var(--tiltAngle_scaleMargin_Dashboard)
      )
  );
  background-color: transparent;
  /*box-shadow: 0 0 2px;*/
  height: 3px;
}

.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltInstrument
  .sideTiltLine
  .marker.right {
  right: 0;
  width: var(--tiltAngle_markerWidth_Dashboard);
}

.SE_0138_Tiltangle_DashboardTile.hiddenStepperDecorator
  .tiltAngleDisplay
  .tiltContent
  .UE_0202_FloatingContainer.liftBargraphFloatingContainer
  .floatingContainer {
  left: 6px;
}
.SE_0138_Tiltangle_DashboardTile
  .tiltAngleDisplay
  .tiltContent
  .UE_0202_FloatingContainer.liftBargraphFloatingContainer
  .floatingContainer {
  top: calc(50% + 2px);
  transform: translateY(-50%);
  left: calc(var(--minTouchSize) + 6px);
  z-index: 1;
}
/*---Settings---*/
.SE_0138_Tiltangle_DashboardTile .displaySettingsConfig {
  position: absolute;
  top: 570px;
}

/* ------------------------- SMALL DISPLAY --------------------------*/
@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0138_Tiltangle_DashboardTile {
    height: 425px;
    width: 1526px;
  }
  :root {
    --tiltAngle_instrumentDiameter_Dashboard: 513px;
    --tiltAngle_frontTiltScaleHeight_Dashboard: 272px;
    --tiltAngle_tiltInstrumentHeight_Dashboard: 272px;
  }

  .SE_0138_Tiltangle_DashboardTile
    .tiltAngleDisplay
    .tiltInstrument
    .sideTilt
    .scale {
    background-image: url(../img/ST_0004-InstrumentSideTilt_LOW.png);
  }

  .SE_0138_Tiltangle_DashboardTile
    .tiltAngleDisplay
    .tiltInstrument
    .frontTilt
    .scale {
    background-image: url(../img/ST_0004-InstrumentLengthTilt_LOW.png);
  }

  .SE_0138_Tiltangle_DashboardTile .tiltAngleDisplay .horizonDecorator {
    top: calc(50% - 100vh / 2 - 35px);
  }
}

.settings {
  position: absolute;
  top: 820px;
  width: 50%;
  z-index: 100;
}
