/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --spriteSheetSize: 74px;
}

:global(#root) > .SpritesheetDigits {
  background-color: rgb(var(--bodyColorGrey));
  margin: 0;
  padding-top: 20px;
}

.digitalSpeedometer {
  position: relative;
  width: 192px;
  height: var(--spriteSheetSize);
}

.digit {
  position: absolute;
  width: var(--spriteSheetSize);
  height: var(--spriteSheetSize);
  background-image: url(../img/extended/Spritesheet_Extended_72px.png);
  background-size: cover;
}
.digit.normal {
  background-image: url(../img/Spritesheet_Normal_74px.png);
}
.digit.normal.size18 {
  background-image: url(../img/Spritesheet_Normal_18px.png) !important;
}
.digit.light.size32 {
  background-image: url(../img/Spritesheet_Light_32px.png) !important;
}

.digit.italic {
  width: 86px;
  height: 86px;
  background-image: url(../img/Spritesheet_Italic_86px.png);
}

.digit.light {
  background-image: url(../img/Spritesheet_Light_74px.png);
}

.digit.extended {
  background-image: url(../img/extended/Spritesheet_Extended_72px.png);
}
.digit.extended.size26 {
  background-image: url(../img/extended/Spritesheet_Extended_26px.png) !important;
}
.digit.extended.size34 {
  background-image: url(../img/extended/Spritesheet_Extended_34px.png) !important;
}
.digit.extended.size40 {
  background-image: url(../img/extended/Spritesheet_Extended_40px.png) !important;
}
.digit.extended.size16 {
  background-image: unset;
  -webkit-mask-image: url(../img/extended/Spritesheet_Extended_16px.png) !important;
  background-color: rgb(var(--audiWhite));
}

.digit.boundingBox {
  background-color: rgba(255, 255, 0, 0.5);
}

.center-line {
  position: absolute;
  width: 1px;
  height: var(--spriteSheetSize);
  background-color: rgba(255, 255, 255, 0.48);
  left: 50%;
}

.digit .first {
  background-color: green;
}

.digit .second {
  background-color: blue;
}

.digit .third {
  background-color: yellow;
}

.digit .fourth {
  background-color: pink;
}

/*------------------------ UNIT POSITIONING -------------------------------------*/
.unit {
  position: absolute;
  width: 77px;
  height: 15px;
  bottom: 0px;
  -webkit-mask-repeat: no-repeat;
  background-color: rgb(var(--fpkUnit));
}

.unit.italic:not(.custom) {
  bottom: 6px !important;
}
.unit.kmh-big {
  -webkit-mask-image: url(../img/Unit-kmh-24px.png);
  height: 24px;
  bottom: 1px;
}
.unit.kmh-big.size32 {
  -webkit-mask-image: url(../img/Unit-kmh-small-24px.png);
  height: 24px;
  bottom: 0px;
}
.unit.extended.kmh-big {
  -webkit-mask-image: url(../img/extended/Unit-kmh-extended-big.png);
  height: 16px;
  bottom: 1px;
}
.unit.mph-big {
