  height: 16px;
  bottom: 1px;
}
.unit.mph-big.size32 {
  -webkit-mask-image: url(../img/Unit-mph-small-24px.png);
  height: 24px;
  bottom: 1px;
}
.unit.kmh {
  -webkit-mask-image: url(../img/Unit-kmh-18px.png);
  height: 18px;
  bottom: -1px;
}
.unit.extended.kmh {
  -webkit-mask-image: url(../img/extended/Unit-kmh-extended.png);
  height: 16px;
  bottom: 1px;
}
.unit.mph {
  -webkit-mask-image: url(../img/Unit-mph-18px.png);
  height: 18px;
}
.unit.extended.mph {
  -webkit-mask-image: url(../img/extended/Unit-mph-extended.png);
  height: 16px;
  bottom: 1px;
}
.unit.km-big {
  -webkit-mask-image: url(../img/Unit-km-24px.png);
  height: 24px;
  bottom: 1px;
}
.unit.km {
  -webkit-mask-image: url(../img/Unit-km-15px.png);
}
.unit.extended.km {
  -webkit-mask-image: url(../img/extended/Unit-km-extended.png);
  height: 12px;
  bottom: 0px;
}
.unit.mi-big {
  -webkit-mask-image: url(../img/Unit-mi-24px.png);
  height: 24px;
  bottom: 1px;
}
.unit.mi {
  -webkit-mask-image: url(../img/Unit-mi-15px.png);
}
.unit.extended.mi {
  -webkit-mask-image: url(../img/extended/Unit-mi-extended.png);
  height: 12px;
