.unit.extended.percent {
  -webkit-mask-image: url(../img/extended/Unit-percentage-extended.png);
  height: 14px;
  bottom: 0px;
}
.unit.celsius {
  -webkit-mask-image: url(../img/Unit-C.png);
  height: 18px;
  bottom: -1px;
}
.unit.extended.celsius {
  -webkit-mask-image: url(../img/extended/Unit-C-extended.png);
  height: 14px;
  bottom: 0px;
}
.unit.fahrenheit {
  -webkit-mask-image: url(../img/Unit-F.png);
  height: 18px;
  bottom: -1px;
}
.unit.extended.fahrenheit {
  -webkit-mask-image: url(../img/extended/Unit-F-extended.png);
  height: 12px;
  bottom: 1px;
