.digitalSpeedometer.twoDigits.exceptionSecond .unit {
  left: 228px;
}

.digitalSpeedometer.twoDigits .unit {
  left: 249px;
}

.digitalSpeedometer.threeDigits.exceptionThird .unit {
  left: 269px;
}

.digitalSpeedometer.threeDigits .unit {
  left: 289px;
}

/*------------------------ SLIDERINPUT -------------------------------------*/
.settings {
  width: calc(100% - 50px);
  height: calc(100% - 200px);
  margin-top: 80px;
  margin-left: 50px;
  overflow: scroll;
  position: absolute;
}

.settings input[type='range'] {
  width: 600px;
  margin-bottom: 30px;
  margin-top: 5px;
}

.settings input[type='range'] {
  -webkit-appearance: none;
  height: 10px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}

.settings input[type='range']:hover {
  opacity: 1;
}

.settings input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #4caf50;
  cursor: pointer;
}

.settings input[type='range']::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #4caf50;
  cursor: pointer;
}
