:global(#root) > .SE_0099_CheckOrderScreen {
  width: 100%;
  height: 579px;
  margin: 0px;
  overflow: hidden;
}

.SE_0099_CheckOrderScreen {
}

.UE_0402_Tabbarmenu {
  height: var(--minTouchSize);
  position: relative;
  width: 100%;
  z-index: 1;
}

.checkOrderScreen {
  padding-left: var(--minTouchSize);
  padding-right: var(--minTouchSize);
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

.checkOrderScreen_top {
  margin-bottom: 40px;
}

.checkOrderScreen_centerArticle,
.checkOrderScreen_bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.checkOrderScreen_centerArticle:last-child {
  margin-top: var(--zA1);
}

.checkOrderScreen_centerArticle > p,
.checkOrderScreen_bottom > p {
  margin-right: 30px;
}

.checkOrderScreen_price {
  display: flex;
}

.checkOrderScreen_price .textPrio1,
.checkOrderScreen_price .textPrio2 {
  margin-top: 0;
}

.checkOrderScreen_centerArticle .checkOrderScreen_price p:last-child,
.checkOrderScreen_bottom .checkOrderScreen_price p:last-child {
  margin-left: 15px;
}

.checkOrderScreen_centerArticle .checkOrderScreen_price p:last-child {
  margin-right: 17px;
}

.checkOrderScreen_separatorLine {
  width: 100%;
  height: 2px;
  background-color: rgba(var(--audiWhite), 0.15);
  margin: 10px 0;
}

.checkOrderScreen_centerArticle .checkOrderScreen_price p:first-child,
.checkOrderScreen_bottom .checkOrderScreen_price p:first-child {
  max-width: 250px;
}
.checkOrderScreen_centerArticle .checkOrderScreen_price p:last-child,
.checkOrderScreen_bottom .checkOrderScreen_price p:last-child {
  max-width: 126px;
}

@media only screen and (min-width: 2219px) {
  .checkOrderScreen_top {
    margin-bottom: 80px;
  }

  .checkOrderScreen_top p:first-child,
  .checkOrderScreen_center .checkOrderScreen_centerArticle:first-child {
    margin-bottom: 10px;
  }

  .checkOrderScreen_separatorLine {
    margin: 20px 0;
  }
}
