/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --titleLineLogoWidth: 260px;
}

/* Standalone */

:global(#root) > .T_0303_FullScreenDialog .dialogFSPopup {
  width: 1788px;
  height: 804px;
  margin: 8px;
}

/* T_0303_FullScreenDialog */

.T_0303_FullScreenDialog {
}

.dialogFSPopup {
  width: 100%;
  height: 100%;
}

/*----------------POPUP FRAME DESIGN--------------*/
.dialogFSPopup .dialogFSPopupContainer {
  width: 100%;
  height: 100%;
}

/*----------------FSDialog Titleline--------------*/

.dialogFSTitleLine {
  position: relative;
  height: var(--minTouchSize);
  width: 100%;
  display: flex;
  flex-shrink: 0;
  flex-grow: 0;
}

.dialogFSTitleLine .button {
  position: relative;
  height: 100%;
  min-width: var(--minTouchSize);
  align-self: center;
}

.dialogFSTitleLine .button.close {
  margin-left: var(--popupInsetWidth);
}

.dialogFSTitleLine .button.right {
  margin-right: var(--popupInsetWidth);
}

.dialogFSTitleLine p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

/*----------------FSDialog CONTENT--------------*/

.dialogFSContent {
  flex: 1;
  display: flex;
  align-items: center;
  margin-left: var(--popupInsetWidth);
  width: calc(100% - var(--popupInsetWidth));
  overflow: hidden;
}

.withoutOperationPanel .dialogFSContent {
  margin-bottom: var(--popupInsetWidth);
}

.UE_0401_OperationPanel .interactionBackground {
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  border-radius: 0px;
}

.showCarToggleBar .dialogFSContent {
  margin-bottom: 0;
}

.dialogFSText {
  flex: 1;
  overflow: hidden;
  max-width: 100%;
  padding: var(--popupContentPadding);
}

.dialogFSContent > .T_0103_ListVertical {
  width: 100%;
  height: calc(100% - 5px);
}

.dialogFSContent > .T_0103_ListVertical .fadeOutTopBottom {
  -webkit-mask-image: none;
}

.dialogFSText p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: normal;
}

.splitContainer {
  display: flex;
  position: relative;
  width: 0px;
  flex-direction: column;
  flex-grow: 2;
}

.mapPlaceHolder {
  height: 100%;
  background-image: url(../img/nightMapAlternativeRoutes.webp);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  flex-grow: 1;
  box-shadow: inset calc(-1 * var(--popupInsetWidth)) 0 0 0
      rgba(var(--audiWhite), 0.07),
    inset 0 var(--popupInsetWidth) 0 0px rgba(var(--audiWhite), 0.07),
    inset 0 calc(-1 * var(--popupInsetWidth)) 0 0px rgba(var(--audiWhite), 0.07);
}

.SE_0052_CarToggleBar {
  width: 100%;
  height: var(--minTouchSize);
  position: relative;
}

/*-------------------- dialogFSTitleLine headline --------------------*/

.dialogFSTitleLine > .headline {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: var(--lA1);
  margin-right: var(--lA1);
}

.dialogFSTitleLine > .headline .firstLineWrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  margin: var(--zA1) 0;
}

.dialogFSTitleLine > .headline .firstLineWrapper p.textPrio1 {
  position: relative;
}

.dialogFSTitleLine > .headline p.textPrio2 {
  position: relative;
  flex-shrink: 0;
  padding-bottom: calc(var(--heightBleed) + 4px);
  opacity: var(--secondaryTextOpacity);
}

.dialogFSTitleLine > .headline .firstLineWrapper .touchIcon {
  position: relative;
  margin-right: var(--iA1);
  transform: none;
  left: 0;
  top: 0;
}

.dialogFSTitleLine .assistantLogo {
  position: absolute;
  width: var(--titleLineLogoWidth);
  height: calc(var(--minTouchSize) - var(--heightBleed) + 2px);
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(var(--audiGrey), 0.5);
  display: block;
}

/*--------------------- T_0303_FullScreenDialog variants ---------------------*/
/*---------- variantOne ----------*/
.FASEmergencyAssistPopup .dialogFSTitleLine > .headline,
.variantOne .dialogFSTitleLine > .headline {
  width: calc(
    100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2) -
      (var(--popupInsetWidth) * 2)
  );
}
.FASEmergencyAssistPopup .dialogFSTitleLine > .headline p,
.variantOne .dialogFSTitleLine > .headline p {
  text-align: center;
}

.FASEmergencyAssistPopup .dialogFSTitleLine > .headline .firstLineWrapper,
.variantOne .dialogFSTitleLine > .headline .firstLineWrapper,
.T_0303_01 .dialogFSTitleLine > .headline .firstLineWrapper,
.T_0303_02 .dialogFSTitleLine > .headline .firstLineWrapper {
  justify-content: center;
}

/*---------- variantTwo ----------*/
.variantTwo .dialogFSTitleLine > .headline {
  width: calc(
    50% - var(--titleLineLogoWidth) / 2 - var(--minTouchSize) * 2 - var(--lA1) *
      2 - var(--popupInsetWidth)
  );
}

.variantTwo .dialogFSTitleLine .button.next,
.variantTwo .dialogFSTitleLine .assistantLogo {
  display: block;
}

/*---------- variantThree ----------*/

.variantThree .dialogFSTitleLine > .headline {
  width: calc(
    100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2) -
      (var(--popupInsetWidth) * 2)
  );
}
.variantThree .dialogFSTitleLine > .headline p {
  text-align: center;
}

.variantThree .dialogFSTitleLine > .headline .firstLineWrapper {
  justify-content: center;
}

.variantThree .splitContainer {
  flex-grow: 1;
}

.variantThree .dialogFSContent .T_0103_ListVertical {
  display: none;
}

.variantThree .dialogFSContent {
  align-items: flex-start;
  padding: var(--iA3);
  box-sizing: border-box;
}

.XUrgentPopup {
  overflow: hidden;
  width: 100%;
}

.variantThree .XUrgentPopup_Title {
  display: flex;
  align-items: center;
  margin-bottom: var(--textPrio2LineHeight);
}

.variantThree .XUrgentPopup_Title p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: var(--iA1);
}

.variantThree .XUrgentPopup_Title .listIcon {
  margin-top: var(--zA1);
}

.variantThree .XUrgentPopup_Text {
  margin-left: calc(var(--listIconSize) + var(--iA1));
  margin-top: var(--iA4);
}

.variantThree .XUrgentPopup_Text p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/*---------- variantFour and variantBurgerMenuDetailScreen ----------*/
.variantFour .dialogFSTitleLine > .headline,
.burgerMenuDetailScreen .dialogFSTitleLine > .headline {
  width: calc(
    100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2) -
      (var(--popupInsetWidth) * 2)
  );
}
.variantFour .dialogFSContent .T_0103_ListVertical,
.burgerMenuDetailScreen .dialogFSContent .T_0203_BurgerMenu {
  height: calc(100% - (var(--popupInsetWidth)));
  align-self: flex-start;
}

.T_0303_FullScreenDialog
  .SE_0040_Native_User_Switch
  .nativeUserSwitch.userConsent
  .centerContent {
  height: 100%;
}

.burgerMenuDetailScreen .dialogFSContent .T_0203_BurgerMenu {
  width: calc(100% - (var(--popupInsetWidth)));
}
.burgerMenuDetailScreen
  .dialogFSContent
  .T_0203_BurgerMenu
  .speechDialogDetailScreen {
  background-color: unset;
  border: none;
  background-image: none;
  outline: none;
  box-shadow: none;
}

/*---------- T_0303_01 ----------*/
.T_0303_01 .dialogFSTitleLine > .headline,
.T_0303_02 .dialogFSTitleLine > .headline {
  width: calc(
    100% - (var(--minTouchSize) * 2) - (var(--lA1) * 2) -
      (var(--popupInsetWidth) * 2)
  );
}

.scrollableText p:first-child {
  margin-top: var(--zA4);
}
.scrollableText p:last-child {
  margin-bottom: var(--zA4);
}

.scrollableText .infoContainer p:first-child,
.scrollableText .infoContainer p:last-child {
  margin-top: unset;
  margin-bottom: unset;
  margin-left: var(--iA1);
}

/*---------- T_0303_02 ----------*/
.T_0303_02 .dialogFSContent .policycontainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 100%;
  max-width: 100%;
  width: 100%;
}

.T_0303_02 .dialogFSContent .dialogFSText {
  flex: 1;
  overflow: hidden;
  max-width: 100%;
  padding-left: var(--popupContentPadding);
  padding-right: var(--popupContentPadding);
  padding-top: var(--lA1);
  padding-bottom: 0px;
}

.T_0303_02 .policycontainer .Text.hyperlink {
  margin-left: var(--popupContentPadding);
  margin-top: 0px;
  height: 42px;
  flex: 1;
  text-align: right;
  color: white;
  text-underline-offset: 10px;
  opacity: var(--secondaryTextOpacity);
  text-decoration-thickness: 1px;
}

.T_0303_02 .dialogFSContent .checkContainer {
  padding-left: var(--popupContentPadding);
  padding-right: var(--popupContentPadding);
  margin-top: var(--iA1);
  margin-bottom: var(--iA1);
  display: flex;
  justify-content: flex-start;
  width: calc(100% - 2 * var(--popupContentPadding));
  height: var(--minTouchSize);
  align-items: center;
}

.T_0303_02 .dialogFSContent .checkContainer .checkBoxText {
  flex: 2;
  opacity: var(--secondaryTextOpacity);
}
.T_0303_02 .dialogFSContent .checkContainer .UE_1101_Checkbox {
  margin-left: 40px;
  margin-right: var(--iA1);
  height: 100%;
  width: 30px;
}

.T_0303_02 .dialogFSContent .checkContainer .UE_1103_Switch .switch {
  position: relative;
}

/*---------- variant ContentArea 1 ----------*/

.variantOne.contentArea .T_0102_DetailScreen,
.T_0304_GenericWizard,
.T_0104_TextAndButtons {
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
}

.SE_0040_Native_User_Switch .pinPadScreen {
  margin-top: var(--lA1) !important;
}

.SE_0040_Native_User_Switch .pinPad .buttonHandle {
  bottom: 20px;
}

.SE_0092_InteriorExperienceSettings {
  width: 100%;
}

/*---------- variantFive ----------*/

.childPresenceDetection {
  display: block;
  width: 100%;
  height: 100%;
}

.childPresenceDetection .buttonContainer {
  width: var(--minTouchSize);
}

.titleLine {
  width: calc(100% - var(--minTouchSize) * 2);
  height: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: center;
}

.childPresenceDetection .content {
  position: relative;
  height: calc(100% - (var(--minTouchSize) + 5px));
  display: flex;
  flex-direction: column;
}

.upper {
  display: flex;
  flex: 2;
  margin: var(--hA2) var(--hA2) 0 var(--hA2);
}

.upper > .left {
  width: 50%;
  height: 100%;
}

.SeatsBackrow {
  position: absolute;
  width: 50%;
  height: 72%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  top: 0;
  right: 0;
}

.left p.p2 {
  margin-top: var(--iA2);
}

.lower {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex: 1;
  margin-bottom: var(--iA2);
}

.childPresenceDetection .lower .button {
  position: absolute;
  height: var(--minTouchSize);
  width: 340px;
}

.T_0303_FullScreenDialog.variantFive .dialogFSPopupContainer {
  border-color: rgb(var(--fpkRed));
  box-shadow: inset 0 0 0 var(--popupInsetWidth) rgba(var(--fpkRed), 0.1);
  background-image: radial-gradient(
      120% 250% at center,
      rgba(var(--audiBlack), 0.6) 40%,
      rgba(var(--audiBlack), 0.3) 80%
    ),
    linear-gradient(
      -75deg,
      rgba(var(--fpkRed), 0.2) 0%,
      rgba(var(--fpkRed), 0) 100%
    ),
    linear-gradient(
      105deg,
      rgba(var(--popupBackground), 0.15) 0%,
      rgba(var(--popupBackground), 0) 100%
    );
}

.T_0303_FullScreenDialog.variantFive
  .dialogFSPopupContainer:not(.warning2)
  .dialogFSTitleLine::after {
  background-image: linear-gradient(
    to right,
    rgba(var(--audiWhite), 0.3) 0px,
    rgba(var(--audiWhite), 0.3) 20px,
    transparent 20px,
    transparent 25px,
    rgb(var(--fpkRed)) 25px,
    rgb(var(--fpkRed)) calc(100% - 25px),
    transparent calc(100% - 25px),
    transparent calc(100% - 20px),
    rgba(var(--audiWhite), 0.3) calc(100% - 20px),
    rgba(var(--audiWhite), 0.3) 100%
  );
}

.warning2 .dialogFSTitleLine {
  background-color: rgba(var(--fpkRed), 0.5);
}

/*---------------------------------------------------------------- Start of FASEmergencyAssistPopup logic------------------------------------------------------*/

.touchIconFAS {
  position: relative;
  top: unset;
  left: unset;
  transform: unset;
  margin-bottom: 35px;
}
.T_0303_FullScreenDialog.FASEmergencyAssistPopup .dialogFSPopupContainer {
  border-color: rgb(var(--fpkRed));
  box-shadow: inset 0 0 0 var(--popupInsetWidth) rgba(var(--fpkRed), 0.1);
  background-image: radial-gradient(
      120% 250% at center,
      rgba(var(--audiBlack), 0.6) 40%,
      rgba(var(--audiBlack), 0.3) 80%
    ),
    linear-gradient(
      -75deg,
      rgba(var(--fpkRed), 0.2) 0%,
      rgba(var(--fpkRed), 0) 100%
    ),
    linear-gradient(
      105deg,
      rgba(var(--popupBackground), 0.15) 0%,
      rgba(var(--popupBackground), 0) 100%
    );
}
.FASEmergencyAssistPopupContainer {
  display: flex;
  flex: 1;
  flex-direction: row-reverse;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: flex-end;
}

.FASEmergencyAssistPopupContainer .FASEmergencyAssistPopupIcon {
  flex: 0.2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.FASEmergencyAssistPopupContainer .FASEmergencyAssistPopupText {
  flex: 0.42 1 672px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(100% - var(--iA2) * 2);
  margin: 0 var(--iA2);
  height: 100%;
  margin-top: 160px;
  margin-left: -117px;
}
.FASEmergencyAssistPopupText p {
  text-align: center;
  max-width: 920px;
}

.FASEmergencyAssistPopupContainer .FASEmergencyAssistPopupImage {
  flex: 0.25;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.FASEmergencyAssistPopupContainer .FASEmergencyAssistPopupImage .decorator {
  position: relative;
  background-repeat: no-repeat;
}

.T_0303_FullScreenDialog
  .dialogFSPopup
  .FASEmergencyAssistPopupImage
  .decorator {
  width: 545px;
  height: 786px;
  margin-right: auto;
  margin-left: -27px;
}

@media only screen and (max-height: 720px) {
  .T_0303_FullScreenDialog .dialogFSPopup .decorator {
    width: 700px;
    height: 160px;
  }
}

/*------------------------------------------------------ End of FASEmergencyAssistPopup logic-----------------------------------------------------------*/

