/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --routeOverview_routeWidth: 14px;
  --routeOverview_tileHeight: 82px;
  --routeOverview_firstStackedTileHeight: 8px;
  --routeOverview_tileLineMargin: 5px;
  --routeOverview_secondStackedTileHeight: 7px;
  --routeOverview_openButtonSize: 100px;
  --routeOverview_directionArrowSize: 80px;
  --routeOverview_eoRRouteHeight: 80%;
  --routeOverview_flagWidth: 40px;
  --routeOverview_flagHeight: 25px;
  --routeOverview_routeMarkerHeight: 3px;
  --routeOverview_routeEventBorder: 1px solid rgba(var(--audiBlack), 0.9);
  --searchContainerWidth: 250px;
}

.left .legendRow:nth-of-type(1) .legendItem:first-child {
  --y: 60;
}

.right .legendRow:nth-of-type(2) .legendItem:first-child {
  --y: 12.5;
}

.left .legendRow:nth-of-type(2) .legendItem:first-child {
  --y: 40;
}

.left .legendRow:nth-of-type(3) .legendItem:first-child {
  --y: 70;
}

.left .legendRow:nth-of-type(4) .legendItem:first-child {
  --y: 35;
}

:global(#root) > .SE_0047_RouteOverview {
  position: absolute;
  display: flex;
  width: 705px;
  max-height: 534px;
  height: calc(100% - var(--minTouchSize) - (var(--zA8) * 3));
  margin: 0;
  justify-content: center;
}

.SE_0047_RouteOverview {
}

.routeOverview {
  position: relative;
  width: 100%;
  height: 100%;
  transition: var(--AG_0303_ScaleResize);
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--audiBlack), 0.85);
  top: 0;
  left: 0;
  border-radius: var(--standardBorderRadius);
}

.emptyRoad {
  position: absolute;
  height: calc(100% - 2px);
  margin: 1px auto;
  background-color: rgb(var(--audiGrey));
  width: var(--routeOverview_routeWidth);
  transform: translateX(-50%);
  left: 50%;
}

.routeOverviewContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  padding: 0 var(--iA1);
  box-sizing: border-box;
  opacity: 1;
  transition: var(--AG_0201_FadeIn);
  transition-delay: var(--animationFadeDuration);
  flex-direction: column;
  align-items: center;
}

.route {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
}

.SE_0047_RouteOverview:not(.off) .open .interactionBackground {
  background-image: linear-gradient(
    to bottom,
    var(--buttonBackgroundColor) 0%,
    var(--buttonBackgroundColor) 100%
  );
  background-color: rgba(var(--audiBlack), 0.98);
  opacity: 1;
  top: calc(var(--opticalTouchReduction) / 2);
  left: calc(var(--opticalTouchReduction) / 2);
  width: calc(100% - var(--opticalTouchReduction));
  height: calc(100% - var(--opticalTouchReduction));
}
.SE_0047_RouteOverview:not(.off) .open .uiState {
  left: var(--uiStateMargin);
  right: var(--uiStateMargin);
  top: var(--uiStateMargin);
  bottom: var(--uiStateMargin);
  height: calc(100% - 2 * var(--uiStateMargin));
  width: calc(100% - 2 * var(--uiStateMargin));
}

/* ------------ TRAFFIC EVENTS -------------- */
.bar {
  position: relative;
  display: flex;
  justify-content: center;
  width: calc(var(--routeOverview_routeWidth) + ((var(--iA2)) * 2));
  height: calc(100% - 2px);
  margin: 1px 0;
}
.activeRoute {
  position: absolute;
  top: 0;
  height: calc(100% - var(--routeOverview_directionArrowSize) / 2 - var(--zA8));
  background-color: rgb(var(--routeActiveColor));
  width: var(--routeOverview_routeWidth);
}
.traffic {
  position: absolute;
  display: flex;
  flex-direction: column-reverse;
  top: 0;
  height: calc(100% - var(--routeOverview_directionArrowSize) / 2 - var(--zA8));
  width: var(--routeOverview_routeWidth);
  overflow: hidden;
}
.trafficEvent {
  position: relative;
  width: 100%;
  border-top: var(--routeOverview_routeEventBorder);
  border-bottom: var(--routeOverview_routeEventBorder);
}
.trafficEvent + .trafficEvent {
  margin-top: -1px;
}
.routeActive {
  background: rgb(var(--routeActiveColor));
}
.trafficSome {
  background: rgb(var(--routeTrafficSome));
}
.trafficMore {
  background: rgb(var(--routeTrafficMore));
}
.trafficJam {
  background: rgb(var(--routeTrafficJam));
}

/* ------------ legend -------------- */
.legend {
  display: flex;
  width: calc(50% - var(--iA2) - var(--routeOverview_routeWidth) / 2);
  height: calc(100% - var(--routeOverview_openButtonSize));
  flex-direction: column-reverse;
}

.legendRow {
  position: relative;
  display: flex;
  flex: 1;
  margin-top: calc(var(--routeOverview_firstStackedTileHeight) / 2);
  padding-top: 3px;
  justify-content: flex-start;
  flex-direction: column-reverse;
  box-sizing: border-box;
}

.legendRow.hint {
  width: 299px;
}

.legendRow.hint .legendItem {
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.3) 0%,
    rgba(var(--interactionBackground), 0.3) 100%
  );
  background-color: rgba(var(--audiBlack), 0.5);
}

.legendItem {
  display: flex;
  width: 100%;
  box-sizing: border-box;
  flex-direction: column;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.15) 0%,
    rgba(var(--interactionBackground), 0.15) 100%
  );
  background-color: rgb(var(--audiBlack));
  border: 1px solid rgb(var(--audiGrey));
  box-shadow: 0 0 0 3px rgb(var(--audiBlack));
  position: initial;
  border-radius: var(--standardBorderRadius);
}

.legendItem .button .uiState.borderLeft {
  left: -14px;
}
.legendItem .button .uiState.borderRight {
  right: -14px;
}

.routeMarker {
  position: absolute;
  height: var(--routeOverview_routeMarkerHeight);
  width: var(--routeOverview_routeWidth);
  background-color: rgb(var(--audiWhite));
  border-top: 1px solid rgba(var(--audiBlack), 0.9);
  border-bottom: 1px solid rgba(var(--audiBlack), 0.9);
  bottom: calc(
    (var(--routeOverview_tileHeight)) / 2 -
      var(--routeOverview_routeMarkerHeight) + 1px
  );
}

.left .routeMarker {
  right: calc((var(--iA2) + var(--routeOverview_routeWidth)) * (-1));
}
.right .routeMarker {
  left: calc((var(--iA2) + var(--routeOverview_routeWidth)) * (-1));
}

.legendRow.hint .routeMarker {
  opacity: 0.6;
}

.legendItem .container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 calc(var(--iA1) + 2px);
  box-sizing: border-box;
}

.legendRow .textContainer {
  overflow: hidden;
  flex: 1;
}

.legendRow .textContainer > div {
  justify-content: flex-end;
}

.legendRow .textContainer .textRow {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.legendRow .textContainer .textRow .textPrio3 {
  margin-left: var(--zA8);
}

.legendRow .textContainer .textRow .inPrefix .textPrio3 {
  margin-left: 0;
  margin-right: var(--zA8);
}

.legendRow .textContainer .textRow .textPrio3 .inPrefix {
  margin-right: var(--zA8);
}

.legendItem:first-child {
  height: var(--routeOverview_tileHeight);
  margin-bottom: 0;
  margin-top: 0px;
  z-index: 2;
}

.legendItem:first-child.threeLines {
  height: 118px;
}

.legendItemStacked {
  position: relative;
}

.legendItemStacked:nth-child(1n) {
  transform: scaleX(0.9);
  height: var(--routeOverview_secondStackedTileHeight);
}

.legendItemStacked:nth-child(2n) {
  transform: scaleX(0.95);
  height: var(--routeOverview_firstStackedTileHeight);
  margin-bottom: 1px;
  z-index: 1;
}

.legendRow .legendItem .touchIcon {
  left: initial;
  transform: initial;
  top: initial;
  position: relative;
}

.textContainer .textContainer {
  margin-left: var(--zA8);
}

/* .legendItem .textPrio2 {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
} */

/* --------------------------------- loading -------------------------------- */
.legendLoading {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  align-items: center;
  width: inherit;
  display: flex;
}

.legendLoading .textContainer {
  overflow: hidden;
}

.legendLoading p {
  margin-left: var(--iA1);
  margin-right: var(--iA1);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* ------ connectors ------ */

.left .legendItem:first-child::before {
  content: '';
  position: absolute;
  right: calc(var(--iA2) * -1);
  width: calc(var(--iA2));
  height: 1px;
  top: calc(
    (
        100% - var(--routeOverview_tileHeight) +
          var(--routeOverview_tileLineMargin)
      ) +
      (
        var(--routeOverview_tileHeight) - 2 *
          var(--routeOverview_tileLineMargin)
      ) * (100 - var(--y)) / 100
  );
}

.right .legendItem:first-child::before {
  content: '';
  position: absolute;

  bottom: calc(var(--routeOverview_tileHeight) / 2);
  left: calc(var(--iA2) * -1);
  width: calc(var(--iA2));
  height: 1px;
}

.right .hint .legendItem:first-child::before {
  background-image: repeating-linear-gradient(
    to right,
    rgb(var(--audiWhite)) 0px,
    rgb(var(--audiWhite)) 6px,
    transparent 6px,
    transparent 12px
  );
  background-color: unset;
  opacity: 0.6;
}

/* ---- legend left/right colors ---- */
.left .legendItem.white {
  border-right: 3px solid rgb(var(--audiWhite));
}

.left .legendItem.trafficMore {
  border-right: 3px solid rgb(var(--routeTrafficMore));
}

.left .legendItem.trafficJam {
  border-right: 3px solid rgb(var(--routeTrafficJam));
}

.left .legendItem.trafficSome {
  border-right: 3px solid rgb(var(--routeTrafficSome));
}

.right .legendItem.white {
  border-left: 3px solid rgb(var(--audiWhite));
}

.right .legendRow.hint .legendItem.white {
  border-left: 3px dashed rgba(var(--audiWhite), 0.6);
  box-shadow: none;
}

.right .legendRow.hint .legendItem.white::after {
  content: '';
  width: 100%;
  height: 82px;
  position: absolute;
  left: 0;
  bottom: -3px;
  border: 3px rgba(var(--audiBlack), 0.9) solid;
  border-left: none;
  background-color: unset;
  pointer-events: none;
}

.right .legendRow.hint .legendItemStacked:nth-child(1n)::after {
  width: 299px;
  left: -3px;
  height: 4px;
  bottom: 2px;
  border-bottom: none;
}

.right .legendRow.hint .legendItemStacked:nth-child(2n)::after {
  width: 299px;
  left: -3px;
  height: 6px;
  bottom: 1px;
  border-bottom: none;
}

.right .legendItem.trafficSome {
  border-left: 3px solid rgb(var(--routeTrafficSome));
}

.right .legendItem.trafficMore {
  border-left: 3px solid rgb(var(--routeTrafficMore));
}

.right .legendItem.trafficJam {
  border-left: 3px solid rgb(var(--routeTrafficJam));
}

.white::after,
.white::before {
  background-color: rgb(var(--audiWhite));
}

.legendItem.trafficSome::after,
.trafficSome::before {
  background-color: rgb(var(--routeTrafficSome));
}
.trafficMore::after,
.trafficMore::before {
  background-color: rgb(var(--routeTrafficMore));
}
.trafficJam::after,
.trafficJam::before {
  background-color: rgb(var(--routeTrafficJam));
}

/* .legendItem.white {
  border-right: 3px solid rgb(var(--audiWhite));
}
 */
.legendItem.white::after,
.legendItem.white::before {
  background-color: rgb(var(--audiWhite));
}

/* ---- legend row poi ---- */

.poi .textContainer .iconContainer {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.poi .textContainer .iconContainer .listIcon {
  margin-left: var(--zA1);
  padding-left: 5px;
}

.poi .iconContainer .listIcon:first-child {
  margin-left: 0;
  padding-left: 0;
}

.poi .textContainer .textRow {
  justify-content: flex-end;
}

/* ---- legend row manual loading ---- */

.manualLoading .touchIcon + .touchIcon {
  margin-left: var(--iA1);
}

/* ---- legend row countryborder ---- */

.countryborder {
  align-items: center;
}

.countryborder .legendItem .routeMarker {
  bottom: calc(
    (var(--routeOverview_flagHeight) - 1px) / 2 -
      var(--routeOverview_routeMarkerHeight) + 2px
  );
}

.right .countryborder .legendItem:first-child::before {
  width: calc(var(--iA2) + 100%);
}

.countryborder .legendItem:first-child {
  width: fit-content;
  height: fit-content;
  background-color: transparent;
  border: 0px;
}

.countryborder .legendItem {
  padding-bottom: calc(
    (var(--routeOverview_tileHeight) - var(--routeOverview_tileLineMargin) * 2) *
      var(--y) / 100 - var(--routeOverview_flagHeight) / 2
  );
}

.countryborder .legendItem .flagIcon {
  width: var(--routeOverview_flagWidth);
  height: var(--routeOverview_flagHeight);
  background-image: linear-gradient(
    to right,
    green calc(100% / 3),
    white calc(100% / 3),
    white calc(2 * 100% / 3),
    red calc(2 * 100% / 3)
  );
  margin: 2px;
}

.countryborder .legendItem .infoBox {
  width: fit-content;
  height: fit-content;
  border: 1px solid rgba(var(--audiWhite), 0.9);
  background-color: rgb(var(--audiBlack));
  background-repeat: no-repeat;
  background-position: center;
  z-index: 2;
}

.countryborder .legendItem:before {
  display: none;
}

.countryborder .legendItem .infoBox:after,
.countryborder .legendItem .infoBox:before {
  content: '';
  position: absolute;
  top: calc(
    (
        100% - var(--routeOverview_tileHeight) +
          var(--routeOverview_tileLineMargin)
      ) +
      (
        var(--routeOverview_tileHeight) - 2 *
          var(--routeOverview_tileLineMargin)
      ) * (100 - var(--y)) / 100
  );
  height: 1px;
  background-image: repeating-linear-gradient(
    to right,
    rgb(var(--audiWhite)) 0px,
    rgb(var(--audiWhite)) 6px,
    transparent 6px,
    transparent 12px
  );
}

.countryborder .infoBox:before {
  left: calc(var(--iA2) * -1);
  width: calc(50% - (var(--routeOverview_flagWidth) + 10px) / 2 + var(--iA2));
}

.countryborder .infoBox:after {
  left: calc(50% + (var(--routeOverview_flagWidth) + 18px) / 2);
  width: calc(50% - (var(--routeOverview_flagWidth) + 10px) / 2);
}

/* ---- footer ---- */
.navigatorIcon {
  position: absolute;
  bottom: var(--zA8);
  left: 50%;
  transform: translateX(-50%);
  width: var(--routeOverview_directionArrowSize);
  height: var(--routeOverview_directionArrowSize);
  background-image: url(../img/SE_0047_RouteOverview_CCP.png);
}

.SE_0056_NextManeuverBox {
  width: 205px;
  height: 70px;
  position: absolute;
  bottom: var(--iA1);
  right: var(--iA1);
}

.SE_0056_NextManeuverBox .topBox {
  display: none;
}

.SE_0056_NextManeuverBox .bottomBox {
  padding: unset;
  margin-top: unset;
}

.UE_0101_ActionButton {
  position: absolute;
  top: calc(100% - var(--routeOverview_openButtonSize));
  height: var(--routeOverview_openButtonSize);
}

.UE_0101_ActionButton.open {
  left: 11px;
  width: var(--routeOverview_openButtonSize);
  min-width: 100px;
  transition: var(--AG_0303_ScaleResize), var(--AG_0101_Move);
}

.UE_0101_ActionButton:not(.buttonDecorator) .interactionBackground {
  background-color: rgba(var(--audiBlack), 0.85);
  background-image: unset;
  opacity: 0;
  transition: var(--AG_0201_FadeIn);
}

.searchContainer {
  position: relative;
  width: fit-content;
  max-width: 420px;
}

.searchContainer .legendRow {
  margin-top: 0;
  padding-top: 0;
}

.searchContainer .textContainer {
  flex-direction: column;
  width: 100%;
  display: flex;
  justify-content: center;
  height: 100%;
  align-items: center;
  padding: 0 var(--textButtonMargin);
  box-sizing: border-box;
}

.searchContainer .textContainer p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
}

/*-- end of Route Search--*/

.eoR_Search .UE_0202_FloatingContainer {
  display: initial;
}

.routeOverview .UE_0202_FloatingContainer .floatingContainer {
  min-height: 100px;
  max-height: 100px;
  background-image: linear-gradient(
    to bottom,
    rgba(var(--interactionBackground), 0.15) 0%,
    rgba(var(--interactionBackground), 0.15) 100%
  );
  background-color: rgb(var(--audiBlack));
}

.routeOverview
  .UE_0202_FloatingContainer
  .floatingContainer
  .backgroundOverlay {
  background-position: center
    calc(var(--minTouchSize) - var(--dashboardHeight) - var(--statusBarHeight));
}

.routeOverview
  .UE_0202_FloatingContainer
  .floatingContainer
  .textContainer
  .iconContainer
  .listIcon
  + .listIcon {
  margin-left: 4px;
  padding-left: 5px;
}

.searchContainer .legendRow,
.searchContainer .legendRow .legendItem {
  height: 100%;
  border: 0px;
}

.eoR_Search .routeOverviewContainer .route {
  height: var(--routeOverview_eoRRouteHeight);
}

/* ---- Settings ---- */
.settings {
  width: 50%;
  position: absolute;
  top: 550px;
  left: 0;
}

/* ---- ROUTE OVERVIEW OFF ---- */
.off .routeOverview .routeOverviewContainer {
  opacity: 0;
  transition-delay: 0s;
}

.off .routeOverview > .UE_0101_ActionButton {
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  left: 0;
  top: 0;
  background-color: rgba(var(--audiBlack), 0.85);
  border-radius: var(--standardBorderRadius);
}

/* ---- SMALL DISPLAY ---- */
@media only screen and (max-width: 1768px) {
  :root {
    --routeOverview_eoRRouteHeight: 75%;
  }

  .bar .barStack:nth-of-type(5),
  .left .legendRow:last-of-type,
  .eoR_Search .bar .barStack:nth-of-type(4),
  .eoR_Search .left .legendRow:nth-of-type(3),
  .eoR_Search .right .legendRow:nth-of-type(3) {
    display: none;
  }

  .right .legendRow:nth-of-type(1) .legendItem:first-child {
    --y: 45;
  }

  .right .legendRow:nth-of-type(2) .legendItem:first-child {
    --y: 30;
  }

  .right .legendRow:nth-of-type(3) .legendItem:first-child {
    --y: 30;
  }

  :global(#root) > .SE_0047_RouteOverview {
    max-height: 399px !important;
  }
}
