/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --popupVollHeight: 205px;
  --popupVollWidth: 872px;

  --popupVollMinWidth: 631px;
  --popupVollMaxWidth: 960px;
}

:global(#root) > .T_5201_PopUpVoll {
  position: relative;
  margin: 8px;
}

.dialogPopUpVoll {
  position: relative;
  height: var(--popupVollHeight);
  width: fit-content;
  min-width: var(--popupVollMinWidth);
  max-width: var(--popupVollMaxWidth);
  top: 0px;
  left: 0px;
}

/*----------------POPUP FRAME DESIGN--------------*/
.dialogPopUpVollContainer {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/*----------------POPUP CONTENT--------------*/
.dialogContent {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
}

.dialogContentEmergency {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: visible;
  display: flex;
}

.dialogIcon {
  width: 140px;
  height: 74px;
  margin: var(--iA2);
  background-color: rgb(var(--audiGrey));
  flex-shrink: 0;
}

.dialogText {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  flex: 1;
  margin-right: var(--zA6);
  margin-left: var(--zA6);
  max-width: calc(100% - var(--zA6) * 2);
  overflow: hidden;
}

.dialogText p {
  text-align: center;
}

.T_5201_02_Vollflaechig_mit_Icon_und_Text .dialogText p {
  text-align: left;
}

.T_5201_00_Vollflaechig_ohne_Ueberschrift_ohne_Icon,
.T_5201_01_Vollflaechig_ohne_Icon_mit_Bestaetigungstext,
.T_5201_02_Vollflaechig_mit_Icon_und_Text,
.T_5201_03_Vollflaechig_mit_Grafik_ohne_Text,
.T_5201_05_Vollflchig_ohne_Icon_mit_Fortschrittsbalken {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.emergencyBoundingBox {
  width: 106px;
  height: 72px;
  position: absolute;
  top: 210px;
  left: 280px;
  background-color: rgba(var(--boundingBoxGreen));
}

.T_5201_03_Vollflaechig_mit_Grafik_ohne_Text {
  flex-direction: row;
}

.T_5201_02_Vollflaechig_mit_Icon_und_Text {
  flex-direction: row;
  width: 100%;
}
.T_5201_02_Vollflaechig_mit_Icon_und_Text .dialogText {
  margin-left: 0px;
  max-width: calc(100% - var(--zA6));
}

.dialogText p.okText {
  margin-top: 14px;
  color: rgba(var(--audiWhite), 0.7);
}

.fsGrafik {
  position: relative;
  width: calc(var(--popupVollMinWidth) - var(--zA6) * 2);
  height: calc(100% - var(--zA6) * 2);
  background-color: rgb(var(--audiGrey));
}

.T_5201_05_Vollflchig_ohne_Icon_mit_Fortschrittsbalken > p {
  margin-right: var(--zA6);
  margin-left: var(--zA6);
  max-width: calc(100% - var(--zA6) * 2);
}

.UE_0702_ProgressBar {
  width: 100%;
}
.T_5201_05_Vollflchig_ohne_Icon_mit_Fortschrittsbalken
  .UE_0702_ProgressBar
  .progress {
  background-image: unset;
  background-color: rgb(var(--fpkOrange));
}

/*Settings */

.content_config {
  position: absolute;
  width: 100%;
  top: 400px;
}
