/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --lightMenuDecoratorHeight: 555px;
}

:global(#root) > .ST_0006_LightMenu {
  position: absolute;
  width: 1800px;
  height: 697px;
  margin: 0;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.ST_0006_LightMenu > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(
    to bottom,
    transparent 450px,
    rgba(var(--audiBlack), 0.6) 450px,
    rgba(var(--audiBlack), 0.4) 100%
  );
}

.lightMenu {
  height: 100%;
  width: 100%;
  position: absolute;
}

.carDecorator {
  position: absolute;
  height: var(--lightMenuDecoratorHeight);
  width: calc(100% - 134px);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  left: 50%;
  transform: translateX(-50%);
}

/***********  CONNECTORS  ************/

.lightMenu .leftConnector {
  position: absolute;
  top: 95px;
  left: 761px;
}

.lightMenu .rightConnector {
  position: absolute;
  top: 185px;
  right: 640px;
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0006_LightMenu {
    width: 1648px;
    height: 450px;
  }

  .lightMenu .leftConnector {
    top: 67px;
    left: 702px;
  }

  .lightMenu .rightConnector {
    top: 141px;
    right: 703px; /*original value: 666px needed to change to 703px to get it right on BL*/
  }

  .carDecorator {
    position: absolute;
    height: calc(100% - 23px);
    width: calc(100% - 134px);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
  }
}

@media only screen and (min-width: 2219px) {
  .carDecorator {
    top: calc(50% - (555px / 2));
  }

  .lightMenu .leftConnector {
    top: 170px;
  }

  .lightMenu .rightConnector {
    top: 260px;
    right: 640px;
  }
}
