/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .ST_0011_ChargingProfileDetail {
  height: 100vh;
  width: 100vw;
  margin: 0;
}

.ST_0011_ChargingProfileDetail {
}

.chargingProfileDetail {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.chargingContentContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}

.chargingContentContainer:first-child {
  margin-right: var(--textButtonMargin);
}

.chargingContentContainer:last-child {
  margin-left: var(--textButtonMargin);
  margin-right: var(--iA1);
}

.UE_0101_ActionButton {
  position: relative;
  display: flex;
  align-items: center;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}
/*------------------------------------------- LEFT CONTENT ---------------------------------------------*/

.chargingContentContainer.left .T_0103_ListVertical {
  height: 100%;
  width: 100%;
}

/*------------------------------------------- RIGHT CONTENT ---------------------------------------------*/

.chargingContentContainer.right .line {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--cA1) 0;
}

.chargingContentContainer.right .line:not(:last-child):after {
  content: '';
  position: absolute;
  bottom: 0;
  height: 1px;
  width: calc(100% - var(--minTouchSize) - var(--cA1));
  background-color: var(--listSeparatorLineColor);
}

.chargingContentContainer.right p {
  width: calc(100% - var(--iA1) * 2);
  margin-left: var(--iA1);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.chargingContentContainer.right .line .textContainer {
  width: calc(100% - var(--minTouchSize) - var(--cA1));
  overflow: hidden;
}

.chargingProfileDetail_textColor {
  opacity: 0.7;
}

/*------------------------------------------- SMALL DISPLAY ---------------------------------------------*/
@media only screen and (max-width: 1768px) {
  .ST_0011_ChargingProfileDetail
    .chargingProfileDetail
    .chargingContentContainer
    .line
    .textContainer
    .textPrio1 {
    display: none;
  }
}
