.interactiveLine {
  position: absolute;
  top: 252px;
  width: 750px;
  right: 150px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.interactiveLine--rhd {
  left: 150px;
  right: unset;
}

.interactiveLine.yellowBackground {
  background-color: rgba(255, 255, 0, 0.25);
}

.interactiveLine.low {
  top: 252px;
}

.interactiveLine.low--withPeaBoundingBoxIcon {
  background-color: transparent;
}

.interactiveLineIcon {
  width: 42px;
  min-width: 42px;
  height: 41px;
  background-image: url('../../../01_HTML/assets/CID-Icons_40x40 _ 34x34 (dummy).png');
}

.interactiveLineIcon + .interactiveLineIcon {
  margin-left: 9px;
}

.interactiveLineIcon + p {
  margin-left: 7px;
}

.interactiveLine > p {
  margin-bottom: 2px;
}

.pea__speedometer__img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
