/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --barHeight: 17px;
  --barBorderTopHeight: 4px;
  --barSegmentSpacing: 4px;
}

.Bargraph {
  width: 100%;
  height: var(--barHeight);
}

.bargraph {
  height: var(--barHeight);
  position: relative;
}

.masked {
  -webkit-mask-image: url(../../Globals/assets/bargraph/bargraph_left-clip-mask.png),
    linear-gradient(
      to right,
      transparent 15px,
      rgb(var(--audiWhite)) 15px,
      rgb(var(--audiWhite)) calc(100% - 15px),
      transparent calc(100% - 15px)
    ),
    url(../../Globals/assets/bargraph/bargraph_right-clip-mask.png);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: left, left, right;
}

.maskedLeft {
  -webkit-mask-image: url(../../Globals/assets/bargraph/bargraph_left-clip-mask.png),
    linear-gradient(
      to right,
      transparent 15px,
      rgb(var(--audiWhite)) 15px,
      rgb(var(--audiWhite)) calc(100% - 15px)
    );
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: left;
}

.maskedRight {
  -webkit-mask-image: linear-gradient(
      to right,
      rgb(var(--audiWhite)) 0px,
      rgb(var(--audiWhite)) calc(100% - 15px),
      transparent calc(100% - 15px)
    ),
    url(../../Globals/assets/bargraph/bargraph_right-clip-mask.png);
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: right;
}

.rightBorder {
  position: absolute;
  right: 1px;
  top: var(--barBorderTopHeight);
  height: calc(var(--barHeight) - 5px);
  width: 10px;
  background-repeat: no-repeat;
  background-position: right;
  background-image: url(../../Globals/assets/bargraph/UE_6000_Fuellstand_klein-BorderBottomRight.png);
  opacity: 0.6;
}
.leftBorder {
  position: absolute;
  left: 0px;
  top: var(--barBorderTopHeight);
  height: calc(var(--barHeight) - 5px);
  width: 10px;
  background-repeat: no-repeat;
  background-position: left;
  background-image: url(../../Globals/assets/bargraph/UE_6000_Fuellstand_klein-BorderBottomLeft.png);
  opacity: 0.6;
}

.background {
  height: var(--barHeight);
}

.markerGroup {
  position: absolute;
  top: 0;
}

.segmentNew {
  position: relative;
  height: 2px;
  background-color: rgba(var(--audiWhite), 0.3);
}
.segmentNew.green {
  position: relative;
  height: 2px;
  background-color: rgba(var(--fpkGreen), 0.3);
}

.segment {
  position: relative;
  height: calc(var(--barHeight));
  border-top: var(--barBorderTopHeight) solid transparent;
  border-bottom: 1px solid rgba(var(--audiWhite), 0.6);
  background-repeat: no-repeat;
  box-sizing: border-box;
  /* overflow: hidden; */
}

.segment:first-child,
.segment.borderLeft {
  border-left: 1px solid rgba(var(--audiWhite), 0.6);
}

.segment:last-of-type,
.segment.borderRight {
  border-right: 1px solid rgba(var(--audiWhite), 0.6);
}

.overlay {
  position: absolute;
  top: 0px;
  left: 0;
  width: 100%;
  height: 100%;
  /* border-top: var(--barBorderTopHeight) solid transparent; */
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--audiWhite), 0.1) 2px,
    rgba(var(--audiWhite), 0.1) 3px
  );
  box-sizing: border-box;
}

.overlay.red,
.overlay.disabledRed {
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--fpkRed), 0.1) 2px,
    rgba(var(--fpkRed), 0.1) 3px
  );
}

.segment:not(.disabled) > .overlay.green {
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--fpkGreen), 0.1) 2px,
    rgba(var(--fpkGreen), 0.1) 3px
  );
}

.bar:first-child .segment {
  border-left: 1px solid rgba(var(--audiWhite), 0.6);
}

.bar:last-child .segment {
  border-right: 1px solid rgba(var(--audiWhite), 0.6);
}

.inactive .bar:first-child .segment,
.inactive .bar:last-child .segment {
  border-color: rgba(var(--audiWhite), var(--disabledState));
}

.thumb {
  position: absolute;
  width: 2px;
  height: 16px;
  top: -14px;
  background-color: rgb(var(--audiWhite));
  border: 2px solid rgb(var(--audiBlack));
}

.fillingNew {
  position: absolute;
  top: -2px;
  height: 6px;
}

.filling {
  position: absolute;
  top: 1px;
  bottom: 0;
}
.fillingGlowWrapper {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.fillingGlowMask {
  position: relative;
  width: calc(100% + 25px);
  height: calc(100% + 30px);
  top: -15px;
  left: -10px;
  -webkit-mask-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.2) 0%,
    rgba(0, 0, 0, 1) 100%
  );
}

.fillingGlowWrapper.newStyle .fillingGlowMask {
  height: calc(100% + 15px);
  top: -15px;
}

.fillingGlow {
  position: relative;
  width: calc(100% - 19px);
  height: calc(var(--barHeight) + 7px);
  top: calc((100% - var(--barHeight)) / 2 - 3px);
  margin-left: 9px;
  opacity: 0;
  background-color: rgb(var(--fpkGreen));
  filter: blur(4.5px);
}

.fillingGlowWrapper.newStyle .fillingGlow {
  height: 7px;
  top: 13px;
  filter: blur(4.5px);
}

.inactive .bar .segment {
  border-top: var(--barBorderTopHeight) solid rgb(var(--FIDtelltaleGrey)) !important;
  border-bottom: 1px solid rgba(var(--audiWhite), var(--disabledState)) !important;
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--audiWhite), 0.1) 2px,
    rgba(var(--audiWhite), 0.1) 3px
  ) !important;
}

.inactive .leftBorder,
.inactive .rightBorder {
  opacity: var(--disabledState);
}

.leftLimited .leftBorder {
  opacity: var(--disabledState);
}

.rightLimited .rightBorder {
  opacity: var(--disabledState);
}

.inactive .fillingNew,
.inactive .filling {
  opacity: 0;
}

/* --- colors --- */
.white {
  --color: var(--audiWhite);
}

.fpkGreen {
  --color: var(--fpkGreen);
}

.red {
  --color: var(--fpkRed);
}

.segment {
  border-top-color: rgba(var(--audiWhite), var(--disabledState));
}

.segment.white {
  border-top-color: rgba(var(--audiWhite), 1);
}

.segment.red {
  border-top-color: rgba(var(--fpkRed), 1);
}

.segment.disabledRed {
  border-top-color: rgba(var(--fpkRed), var(--disabledState));
}

.segment.disabledRed .overlay {
  background-color: rgba(var(--fpkRed), 0.15);
}

.segment.green {
  border-top-color: rgba(var(--fpkGreen), 1);
  background-color: rgba(var(--fpkGreen), 0.15);
}

.segment.green .overlay {
  /* background-color: rgba(var(--fpkGreen), 0.15); */
}

.segment.yellow {
  border-top-color: rgba(var(--fpkYellow), 1);
}

.segment.yellow .overlay {
  background-color: rgba(var(--fpkYellow), 0.15);
}

.segment.orange {
  border-top-color: rgba(var(--fpkOrange), 1);
}

.segment.orange .overlay {
  background-color: rgba(var(--fpkOrange), 0.15);
}

.segment.gray {
  background-color: rgba(var(--audiWhite), 0.1);
}

.inactive .segment {
  border-color: rgba(var(--audiWhite), var(--disabledState));
  background-color: transparent;
  border-top: var(--barBorderTopHeight) solid rgb(var(--FIDtelltaleGrey)) !important;
  border-bottom: 1px solid rgba(var(--audiWhite), var(--disabledState)) !important;
}

.segment.disabled {
  /* background-color: rgba(var(--audiWhite), var(--disabledState)); */
  border-color: rgba(var(--audiWhite), var(--disabledState));
  background-color: rgba(var(--fpkWhite), 0.1);
}

.layer {
  position: absolute;
  top: 1px;
  bottom: 0;
  background-color: rgba(var(--audiWhite), 0.3);
  box-sizing: border-box;
}

.layer.transparent {
  background-color: transparent;
}
.overlay.redBackground {
  background-color: rgba(var(--fpkRed), 0.15);
  background-image: none;
}
.overlay.yellowBackground {
  background-color: rgba(var(--fpkYellow), 0.15);
  background-image: none;
}

.layer.markerEnd {
  border-right: 1px solid;
  border-right-color: rgb(var(--audiWhite));
}
.layer.marker {
  top: 0px;
  background-image: repeating-linear-gradient(
    to bottom,
    transparent,
    transparent 2px,
    rgba(var(--audiWhite), 0.1) 2px,
    rgba(var(--audiWhite), 0.1) 3px
  );
}

.fillingNew.white,
.filling.white {
  background-color: rgba(var(--audiWhite), 1);
}

.fillingNew.red,
.filling.red {
  background-color: rgba(var(--fpkRed), 1);
}

.fillingNew.green,
.filling.green {
  background-color: rgba(var(--fpkGreen), 1);
}

.fillingNew.yellow,
.filling.yellow {
  background-color: rgba(var(--fpkYellow), 1);
}

.fillingNew.orange,
.filling.orange {
  background-color: rgba(var(--fpkOrange), 1);
}

.fillingNew.disabledRed,
.filling.disabledRed {
  background-color: rgba(var(--fpkRed), 1);
}

.border {
  position: absolute;
  top: -4px;
  background-color: transparent;
  border-top: var(--barBorderTopHeight) solid transparent;
  /* border-bottom: 1px solid rgba(var(--audiWhite), 0.6); */
  border-top-color: rgba(var(--audiWhite), var(--disabledState));
  box-sizing: border-box;
}

.border.white {
  border-top-color: rgba(var(--audiWhite), 1);
}

.border.red {
  border-top-color: rgba(var(--fpkRed), 1);
}

.border.disabledRed {
  border-top-color: rgba(var(--fpkRed), var(--disabledState));
}

.border.green {
  border-top-color: rgba(var(--fpkGreen), 1);
}

.border.yellow {
  border-top-color: rgba(var(--fpkYellow), 1);
}

.border.orange {
  border-top-color: rgba(var(--fpkOrange), 1);
}

.marker {
  position: absolute;
  background-color: rgb(var(--audiGrey));
}
.marker.small {
  width: 2px;
  height: 10px;
}
.marker.large {
  width: 2px;
  height: 30px;
}
.marker.zero {
  width: 4px;
  height: 30px;
  background-color: rgb(var(--audiWhite));
}
.inactive .marker {
  opacity: var(--disabledState);
}

.transparent {
  background-image: none;
}
