/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0403_SourceSelection {
  position: absolute;
  height: 600px;
  width: 100%;
  margin: 30px;
  background-color: rgb(var(--bodyColorGrey));
}

.sourceSelection {
  position: absolute;
  width: fit-content;
  max-width: 63%;
}

.wrapper {
  width: fit-content;
  max-width: 100%;
}

/*---------------------- LIST ELEMENTS -------------------*/

.wrapper .separatorLine {
  height: calc(var(--zA1) - var(--zA2));
  margin: 0 var(--iA1);
  background-color: rgba(var(--audiGrey), 0.8);
}

.wrapper .phoneListElement {
  width: 100%;
  display: flex;
  position: relative;
  height: var(--minTouchSize);
  align-items: center;
}

.wrapper .phoneListElement:last-of-type .textContainer {
  width: 100%;
}

.leftContent {
  width: var(--minTouchSize);
}

.wrapper .leftContent,
.wrapper .rightContent {
  width: var(--minTouchSize);
}

.wrapper .rightContent {
  height: 100%;
}

.leftContent .listIcon {
  display: block;
  margin: 0 auto;
}

.centralContent {
  flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
}

.rightContent .button {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

/*---------------------- RADIO BUTTONS -------------------*/

.rightContent .UE_1102_RadioButton {
  height: 100%;
}

.radioButton .checkmark .point {
  content: '';
  position: absolute;
  display: none;
  top: 8px;
  left: 8px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgb(var(--audiWhite));
}
