/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --circleProgressContainerSize: 50px;
  --listHeaderHeight: calc(var(--circleProgressContainerSize) + 6px);
  --listHeaderBottom: 11px; /*-- old headerHeight 49px and top var(--hA3) --*/
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_1007_ListHeader {
  position: absolute;
  background-color: rgb(var(--bodyColorGrey));
  width: 100%;
  height: 100px;
}

/*-------------------- .UE_1007_ListHeader --------------------*/

.UE_1007_ListHeader {
}

.listHeader {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: flex-end;
}

.listHeaderContainer {
  position: relative;
  display: flex;
  width: 100%;
  height: var(--listHeaderHeight);
  bottom: var(--listHeaderBottom);
}

.background {
  position: absolute;
  width: 100%;
  height: 100%;
  border-bottom: 1px solid rgba(var(--audiWhite), 0.4);
  background-color: rgba(var(--interactionBackground), 0.15);
  border-radius: var(--standardBorderRadius);
  box-sizing: border-box;
}
.listHeaderText {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  flex: 1;
  overflow: hidden;
}

.headline {
  padding: 0 var(--lA1);
  text-transform: uppercase;
}

/*-------------- LOADER ---------------*/
.loaderContainer {
  width: var(--minTouchSize);
  height: var(--circleProgressContainerSize);
  display: flex;
  flex-shrink: 0;
}

.UE_0701_Loader {
  position: relative;
  width: auto;
  height: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

/*-------------- WITH BUTTON ---------------*/
.buttonContainer {
  position: relative;
  right: 0px;
  height: 100%;
  max-height: var(--minTouchSize);
  display: flex;
  align-items: center;
  overflow: hidden;
  max-width: 25%;
  min-width: var(--minTouchSize);
}

.listHeader .UE_0101_ActionButton {
  height: var(--listHeaderHeight);
}

.textTwoButtonsStepper {
  width: 60px;
  margin: 0 5px;
  text-align: center;
}

.toggleButtonContainer {
  position: relative;
  right: 0px;
  height: 100%;
  max-height: var(--minTouchSize);
  display: flex;
  overflow: hidden;
}

.UE_0201_ToggleButtonGroup .toggleButtonGroup {
  height: 100%;
}

.UE_0201_ToggleButtonGroup .toggleButtonGroup .UE_0101_ActionButton {
  flex: unset !important;
  height: 100%;
  width: unset;
  min-width: var(--minTouchSize);
  max-width: 400px;
}

.UE_0201_ToggleButtonGroup
  .toggleButtonGroup
  .UE_0101_ActionButton
  .btnContainerText {
  max-width: 100%;
}

.UE_0201_ToggleButtonGroup .toggleButtonGroup .UE_0101_ActionButton p {
  line-height: var(--listHeaderHeight);
  margin: 0 var(--textButtonMargin);
}

/*-------------- parkingSpecialPopup ---------------*/

.UE_1007_ListHeader.parkingSpecialPopup {
  height: 100%;
}

/*---------- Config --------*/

.settings {
  position: absolute;
  top: calc(var(--listHeaderHeight) + 100px);
  height: 400px;
  width: 100%;
  color: white;
  overflow-y: auto;
}
