:root {
  --chipTileWidth: 537px;
  --chipTileHeight: 50px;
}

:global(#root) > .SE_0094_Chip {
  position: absolute;
  margin: 10px;
  width: 100%;
  height: 500px;
  box-sizing: border-box;
}

.chip {
  width: fit-content;
  height: var(--chipTileHeight);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

/* UTILITY */
.wordProposal {
  box-sizing: border-box;
  height: var(--keyboardLineHeight);
  width: calc(100% - 16px);
  border-bottom: 1px solid rgb(var(--audiGrey));
  margin: 0 auto;
}

.maxWidth {
  max-width: 50%;
}

