.selected .text {
  background-color: rgba(var(--interactionBackground), 0.3);
  border: solid 1px rgba(var(--audiWhite), 0.8);
}

.chip .text {
  display: flex;
  gap: 4px;
  align-items: center;
  flex-direction: row;
  box-sizing: border-box;
  padding: 2px 10px;
  background-color: var(--buttonBackgroundColor);
  border-radius: var(--standardBorderRadius);
  margin: 0 var(--iA1);
  max-width: var(--chipTileWidth);
  z-index: 1;
}

.settings {
  position: absolute;
  top: 300px;
  width: 50%;
}
