:root {
}

/* ------------------------ Standalone ------------------------ */

:global(#root) > .SE_0036_EnlargedPictureView {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  width: 100%;
  height: 100%;
  position: absolute;
  margin: 0px;
}

/* ------------------------ .SE_0036_EnlargedPictureView ------------------------ */

.SE_0036_EnlargedPictureView {
}

.epv {
  height: 100%;
  width: 100%;
  position: relative;
  background-color: rgb(var(--audiBlack));
}

.epv .button {
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  position: absolute;
  z-index: 1;
}

.slideShowContainer {
  margin: 120px 150px;
  width: calc(100% - 300px);
  height: calc(100% - 240px);
  position: absolute;
}

.slideShowImage {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Cover/E98C_media_defaultcover.png);
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.cover {
  background-size: cover;
}

.epv.fullSize .slideShowContainer {
  margin: unset;
  width: 100%;
  height: 100%;
}

.epv.fullSize .slideShowImage {
  top: unset;
  left: unset;
  transform: unset;
}

.settings {
  position: absolute;
  top: 1000px;
}
