/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --darkOpacity: 0.1;
  --brightOpacity: 0.35;
  --liftGrey: var(--audiAluminiumSilver);
  --liftPosition: 0;
  --upperPosition: 25%;
  --zeroPosition: 50%;
  --lowerPosition: 75%;
  --bottomPosition: 100%;
  --markerHeight: 2px;
  --levelHeight: 4px;
  --b10BottomPosition: 100%;
}
:global(#root) > .SE_0039_Lift_Bargraph {
  background-color: rgb(var(--audiGrey));
}
.SE_0039_Lift_Bargraph .verticalBargraph {
  position: relative;
  display: flex;
  flex-direction: column;
  width: var(--minTouchSize);
  height: 356px;
}

.SE_0039_Lift_Bargraph.b10_variant .verticalBargraph {
  height: calc(356px + var(--minTouchSize) + 47px);
}

/*------------------- TOGGLEBUTTON -------------------*/
.SE_0039_Lift_Bargraph .verticalBargraph .button {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
}

/*------------------- SEGMENTCONTAINER -------------------*/
.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer {
  width: 65px;
  flex: 1;
  margin: 25px auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.SE_0039_Lift_Bargraph.b10_variant .verticalBargraph .segmentContainer {
  height: calc(100% - 50px - (2 * var(--minTouchSize)));
  flex: unset;
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .segment {
  flex: 1;
  width: 100%;
  background-color: rgb(var(--liftGrey), var(--darkOpacity));
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .segment:last-of-type {
  margin-bottom: 2px;
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .segment:last-of-type {
  margin-bottom: 0;
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .segment.center {
  background-color: rgb(var(--liftGrey), var(--brightOpacity));
}

.interactionBackground {
  position: absolute;
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0px,
    rgba(0, 0, 0, 1) 112px,
    rgba(0, 0, 0, 0) 113px,
    rgba(0, 0, 0, 0) 114px,
    rgba(0, 0, 0, 1) 115px,
    rgba(0, 0, 0, 1) 100%
  );
}

.SE_0039_Lift_Bargraph.b10_variant .interactionBackground {
  -webkit-mask-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 0px,
    rgba(0, 0, 0, 1) 112px,
    rgba(0, 0, 0, 0) 113px,
    rgba(0, 0, 0, 0) 114px,
    rgba(0, 0, 0, 1) 115px,
    rgba(0, 0, 0, 1) calc(100% - 114px),
    rgba(0, 0, 0, 0) calc(100% - 113px),
    rgba(0, 0, 0, 0) calc(100% - 112px),
    rgba(0, 0, 0, 1) calc(100% - 111px),
    rgba(0, 0, 0, 1) 100%
  );
}

/*------------------- MARKER -------------------*/
.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .markerContainer {
  position: absolute;
  height: 100%;
  width: 100%;
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker {
  background-color: rgb(var(--audiWhite));
  height: var(--markerHeight);
  width: 65px;
  position: absolute;
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.target {
  background-color: rgb(var(--ambientcolor1));
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .textLift.target
  p,
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .textZero.target
  p,
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .text_b10.target
  p {
  color: rgb(var(--ambientcolor1));
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .textLift
  p,
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .text_b10
  p {
  max-width: 49px;
  margin: 0 8px;
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.disabled {
  opacity: var(--touchDisabledState);
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .liftMarker {
  top: var(--liftPosition);
  clip-path: polygon(
    0% calc(0% - 2px),
    8px calc(0% - 2px),
    8px calc(0% + 2px),
    57px calc(0% + 2px),
    57px calc(0% - 2px),
    100% 0%,
    100% 100%,
    0% 100%
  );
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.first {
  top: calc(var(--upperPosition) - var(--markerHeight) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.first {
  top: calc(20% - var(--markerHeight) / 2);
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .zeroMarker {
  top: calc(var(--zeroPosition) - var(--markerHeight) / 2);
  clip-path: polygon(
    0% calc(0% - 2px),
    21px calc(0% - 2px),
    21px calc(0% + 2px),
    43px calc(0% + 2px),
    43px calc(0% - 2px),
    100% 0%,
    100% 100%,
    0% 100%
  );
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .zeroMarker {
  top: calc(40% - var(--markerHeight) / 2);
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.second {
  top: calc(var(--lowerPosition) - var(--markerHeight) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .marker.second {
  top: calc(60% - var(--markerHeight) / 2);
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .bottomMarker {
  top: calc(var(--bottomPosition) - var(--markerHeight));
  background-color: rgb(var(--liftGrey), 0.4);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .bottomMarker {
  top: calc(80% - var(--markerHeight));
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .markerContainer
  .bottomMarker_b10 {
  top: calc(var(--b10BottomPosition) - var(--markerHeight));
  clip-path: polygon(
    0% calc(0% - 2px),
    8px calc(0% - 2px),
    8px calc(0% + 2px),
    57px calc(0% + 2px),
    57px calc(0% - 2px),
    100% 0%,
    100% 100%,
    0% 100%
  );
}

/*------------------- LABELS -------------------*/
.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .labelContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  text-align: center;
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .label {
  position: absolute;
  width: 100%;
  text-align: center;
  color: rgb(var(--audiWhite));
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .textLift {
  top: calc(var(--lineHeightPrio4) / 2 - var(--lineHeightPrio4));
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .textLift.target
  .textPrio4,
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .text_b10.target
  .textPrio4 {
  color: rgb(var(--ambientcolor1));
}
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .textLift.disabled
  .textPrio4,
.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .text_b10.disabled
  .textPrio4 {
  opacity: var(--touchDisabledState);
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .labelContainer
  .text_b10 {
  bottom: calc(var(--lineHeightPrio4) / 2 - var(--lineHeightPrio4));
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .textZero {
  position: absolute;
  top: calc(var(--zeroPosition) - var(--lineHeightPrio4) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .textZero {
  top: calc(40% - var(--lineHeightPrio4) / 2);
}

.SE_0039_Lift_Bargraph
  .verticalBargraph
  .segmentContainer
  .textZero.target
  .textPrio4 {
  color: rgb(var(--ambientcolor1));
}

/*------------------- LEVEL POINTER -------------------*/
.SE_0039_Lift_Bargraph .verticalBargraph .level {
  position: absolute;
  width: calc(100% + 2 * 18px);
  height: var(--levelHeight);
  background-color: rgb(var(--ambientcolor1));
  clip-path: polygon(
    0 0,
    16px 0,
    16px 100%,
    85px 100%,
    85px 0,
    100% 0,
    100% 100%,
    0% 100%
  );
  transition: top 2s;
}
.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.lift {
  top: calc(var(--markerHeight) / 2 - var(--levelHeight) / 2);
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.firstup {
  top: calc(var(--upperPosition) - var(--levelHeight) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .level.firstup {
  top: calc(20% - var(--levelHeight) / 2);
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.zero {
  top: calc(var(--zeroPosition) - var(--levelHeight) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .level.zero {
  top: calc(40% - var(--levelHeight) / 2);
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.uplow {
  top: calc(var(--lowerPosition) - var(--levelHeight) / 2);
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .level.uplow {
  top: calc(60% - var(--levelHeight) / 2);
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.low {
  top: calc(
    var(--bottomPosition) - var(--levelHeight) / 2 - var(--markerHeight) / 2
  );
}
.SE_0039_Lift_Bargraph.b10_variant
  .verticalBargraph
  .segmentContainer
  .level.low {
  top: calc(80% - var(--levelHeight) / 2 - var(--markerHeight) / 2);
}

.SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer .level.b10_low {
  top: calc(
    var(--b10BottomPosition) - var(--levelHeight) / 2 - var(--markerHeight) / 2
  );
}

/*------------------ SMALL DISPLAY ------------------*/
@media only screen and (max-width: 1768px) {
  .SE_0039_Lift_Bargraph .verticalBargraph {
    height: 274px;
  }

  .SE_0039_Lift_Bargraph .verticalBargraph .segmentContainer {
    margin: 10px 27px;
  }
}
