.PSPDescription {
  height: 100%;
  display: flex;
  flex-direction: column;

  &__pageIndicatorContainer {
    visibility: hidden;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: var(--iA1);
    margin-bottom: var(--iA1);

    &--showPageIndicator {
      visibility: visible;
    }
  }

  &__ipaPanels {
    display: flex;
    overflow: hidden;
    flex: 1;

    &__leftPanel {
      flex: 2;
    }

    &__rightPanel {
      flex: 1;

      &__boundingBox {
        position: relative;
        width: 100%;
        height: 100%;
        background-color: rgb(var(--boundingBoxGreen));

        &--showRemoteExample {
          background-color: unset;
          background-image: url(../../img/RemoteParking.png);
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
        }
      }
    }
  }
}
