/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .SE_0104_Nav_More_Overlay {
  position: absolute;
  top: 50px;
  height: 600px;
  width: 850px;
  margin: 30px;
  background-color: rgb(var(--bodyColorGrey));
}

.navigationFoldingElement {
  position: absolute;
  width: 100%;
}

.arrowPopup {
  width: 100%;
}

.wrapper {
  width: fit-content;
  max-width: 100%;
}

.toggleBtnWrapper::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  left: 0px;
  background-color: rgba(var(--audiWhite), 0.3);
  top: calc(var(--minTouchSize) + var(--popupInsetWidth) * 2);
}

.placeHolderText {
  margin: 0 var(--lA1);
}

.toggleBtnWrapper {
  margin-top: var(--lA1);
  width: 100%;
}

.UE_0201_ToggleButtonGroup {
  width: 85%;
  margin: var(--cA1) auto;
  background-color: rgba(var(--audiBlack), 0.98);
}

/*----------------- settings -------------------*/

.settings {
  position: absolute;
  top: 500px;
  width: 700px;
}
