/* CLASSIFICATION: CONFIDENTIAL */
:root {
  --basicChargingBBWidth: 580px;
}

/* --------------- Standalone --------------- */

:global(#root) > .ST_0003_BasicChargingSettings {
  position: absolute;
  width: 1800px;
  height: 697px;
  margin: 0;
}

/* --------------- .ST_0003_BasicChargingSettings --------------- */

.ST_0003_BasicChargingSettings {
}

/* NOT PART OF THE DESIGN! */
:global(#root) > .ST_0003_BasicChargingSettings > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.chargingSettings {
  height: 100%;
  width: 100%;
  position: absolute;
}

.chargingSettings .carDecoratorWrapper {
  position: absolute;
  width: 100%;
  height: 350px;
  top: 55px;
  left: 0;
}

.chargingSettings .carDecoratorWrapper.phev {
  height: 550px;
  top: -50px;
}

.chargingSettings .carDecoratorWrapper .energyFlowPlaceholder,
.chargingSettings .carDecoratorWrapper .bodyLights,
.chargingSettings .carDecoratorWrapper .carDecorator,
.chargingSettings .carDecoratorWrapper .carDecoratorColor,
.chargingSettings .carDecoratorWrapper .carWheels {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.chargingSettings .carDecoratorWrapper .carDecoratorColor {
  background-color: rgb(var(--ambientcolor1));
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
}

.chargingFlapConnector {
  position: absolute;
  left: 1145px;
  top: 175px;
}
.ST_0003_BasicChargingSettings.phev .chargingFlapConnector {
  position: absolute;
  left: 1175px;
  top: 156px;
}
.antriebsModusConnector {
  position: absolute;
  left: 675px;
  top: 170px;
}

/*-------------------------- CHARGINGSTATION DECORATOR -------------------------*/
.chargingSettings .chargingStationWrapper {
  position: absolute;
  height: 282px;
  width: 221px;
  top: 92px;
  right: 295px;
}

.chargingSettings .chargingStationWrapper .decoratorChargingStation,
.chargingSettings .chargingStationWrapper .chargingAnimation {
  position: absolute;
  height: 100%;
  width: 100%;
}

.chargingSettings .chargingStationWrapper .decoratorChargingStation.baseLayer {
  background-image: url(../img/ST_0003_ChargingStation.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.chargingSettings .chargingStationWrapper .decoratorChargingStation.colorLayer {
  -webkit-mask-image: url(../img/ST_0003_ChargingStation_color.png);
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  background-color: rgb(var(--ambientcolor1));
}

.chargingSettings .chargingStationWrapper .chargingAnimation {
  position: absolute;
  top: 40px;
  width: 163px;
  height: 242px;
  background-image: url(../img/AS_4065_Charging_2D_placeholder.png);
  background-size: contain;
  background-repeat: no-repeat;
}

/*-------------------------- CHARGINGSTATION DECORATOR PHEV -------------------------*/
.chargingSettings .chargingStationWrapper.phev {
  position: absolute;
  height: 370px;
  width: 346px;
  top: 17%;
  right: 17%;
  z-index: 1;
}
.chargingSettings .chargingStationWrapper.phev .charger,
.chargingSettings .chargingStationWrapper.phev .chargerGreenHighlight,
.chargingSettings .chargingStationWrapper.phev .chargerHighlight,
.chargingSettings .chargingStationWrapper.phev .chargerAnimation {
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
}
.chargingSettings .chargingStationWrapper.phev .charger {
  background-image: url(../img/PHEV_Charger/Charger.png);
}
.chargingSettings .chargingStationWrapper.phev .chargerGreenHighlight {
  background-color: rgb(30, 255, 0);
  -webkit-mask-image: url(../img/PHEV_Charger/Charger_ON_BW.png);
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
}
.chargingSettings .chargingStationWrapper.phev .chargerHighlight {
  background-image: url(../img/PHEV_Charger/Charger_Highlights_GraphicColor.png);
}

.chargingSettings .chargingStationWrapper.phev .chargerAnimation {
  background-image: url(../img/PHEV_Charger/Charger_00108.png);
}

/*-------------------------- ENERGYFLOW EXAMPLE GRAPHICS -------------------------*/
.chargingSettings .energyFlow {
  background-image: url(../img/energyFlow.png);
  width: 404px;
  height: 180px;
  top: 227px;
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
}

.chargingSettings .energyFlow.left {
  left: 550px;
}

.chargingSettings .energyFlow.right {
  left: 1043px;
}

/* AU436_1 */
.ST_0003_BasicChargingSettings.AU436_1 .chargingSettings .energyFlow.right {
  left: 1089px;
}
/* AU401 / AU402 / AU401_LWB */
.ST_0003_BasicChargingSettings.AU401_0 .chargingSettings .energyFlow,
.ST_0003_BasicChargingSettings.AU402_0 .chargingSettings .energyFlow,
.ST_0003_BasicChargingSettings.AU401_0_LWB .chargingSettings .energyFlow {
  transform: scale(0.95);
}
.ST_0003_BasicChargingSettings.AU401_0 .chargingSettings .energyFlow.left,
.ST_0003_BasicChargingSettings.AU402_0 .chargingSettings .energyFlow.left {
  left: 555px;
}
.ST_0003_BasicChargingSettings.AU401_0 .chargingSettings .energyFlow.right,
.ST_0003_BasicChargingSettings.AU402_0 .chargingSettings .energyFlow.right {
  left: 1057px;
}
.ST_0003_BasicChargingSettings.AU401_0_LWB .chargingSettings .energyFlow.left {
  left: 547px;
}
.ST_0003_BasicChargingSettings.AU401_0_LWB .chargingSettings .energyFlow.right {
  left: 1063px;
}

/* AU512_4 / AU513_4 */
.ST_0003_BasicChargingSettings.AU512_4 .chargingSettings .energyFlow.left,
.ST_0003_BasicChargingSettings.AU513_4 .chargingSettings .energyFlow.left {
  left: 567px;
}
.ST_0003_BasicChargingSettings.AU512_4 .chargingSettings .energyFlow.right,
.ST_0003_BasicChargingSettings.AU513_4 .chargingSettings .energyFlow.right {
  left: 1065px;
}

/* AU591_0 / AU592_0 / AU592_0_Allroad */
.ST_0003_BasicChargingSettings.AU591_0 .chargingSettings .energyFlow.left,
.ST_0003_BasicChargingSettings.AU592_0 .chargingSettings .energyFlow.left,
.ST_0003_BasicChargingSettings.AU592_0_Allroad
  .chargingSettings
  .energyFlow.left {
  top: 234px;
  left: 550px;
}
.ST_0003_BasicChargingSettings.AU591_0 .chargingSettings .energyFlow.right,
.ST_0003_BasicChargingSettings.AU592_0 .chargingSettings .energyFlow.right,
.ST_0003_BasicChargingSettings.AU592_0_Allroad
  .chargingSettings
  .energyFlow.right {
  top: 234px;
  left: 1064px;
}

.SE_0002_SOCBargraph {
  position: absolute;
  width: 1000px;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.fastChargingWrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 310px;
  top: 0px;
}
.fastChargingWrapper .infoText {
  margin-top: 80px;
}

.fastChargingWrapper p {
  text-align: center;
  margin-left: var(--minTouchSize);
  margin-right: var(--minTouchSize);
}

.navigationButton {
  position: absolute;
  bottom: 0px;
  left: 50%;
  transform: translateX(-50%);
  max-width: 600px;
}

.newSOCPlaceholder {
  position: absolute;
  width: 995px;
  height: 167px;
  top: 382px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url(../../../SE_0002_SOCBargraph/01_HTML/img/New_SOC_placeholder.png);
}

.oneInfoContainer {
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: column;
  justify-content: space-between;
}

.firstChargeContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: calc(var(--lineHeightPrio3) + 4px);
}

.firstRow {
  display: flex;
  align-items: center;
  margin-top: 15px;
}
.oneInfoContainer.opti .firstRow {
  margin-top: 35px;
}

.oneInfoContainer.opti .firstRow .firstOptiElem {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  margin-left: calc(var(--minTouchSize));
  width: calc(100% - var(--cA1) - var(--minTouchSize) * 2);
}

.oneInfoContainer.opti .firstRow .firstOptiElem > p {
  margin-bottom: 45px;
  margin-top: calc(
    (var(--minTouchSize) - (2 * var(--lineHeightPrio2) + var(--zA2))) / 2
  );
  max-width: calc(100% - var(--minTouchSize));
}

.oneInfoContainer.opti .firstRow .firstOptiElem .firstOptiSubText {
  display: flex;
  flex-direction: column;
  margin-top: 45px;
}

.oneInfoContainer .SE_0002_SOCBargraph {
  top: 240px;
  left: calc(var(--minTouchSize) + var(--cA1));
  transform: none;
  width: calc(100% - var(--cA1) - var(--minTouchSize) * 2);
}

.secondRow {
  margin-left: calc(var(--minTouchSize) + var(--cA1));
  margin-right: var(--minTouchSize);
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.secondRowBoxContainer {
  display: flex;
  flex-direction: row;
}
.secondRowBox {
  display: flex;
  align-items: center;
  margin: 0 var(--iA1);
  margin-right: var(--iA1);
  margin-left: var(--iA2);
}

.secondRowBox .listIcon {
  margin-right: var(--iA1);
}

.secondRowBox p {
  min-width: 87px;
}

.thirdRow {
  margin-left: var(--zA4);
  z-index: 1;
}
.thirdRow.opti {
  margin-left: 0px;
  margin-bottom: calc(
    var(--minTouchSize) - (2 * var(--lineHeightPrio2) + var(--zA2))
  );
}

.fourthRow {
  width: calc(100% - var(--minTouchSize) + var(--cA1));
  height: var(--minTouchSize);
  margin-left: calc(var(--minTouchSize) + var(--cA1));
  display: flex;
  align-items: center;
}
.oneInfoContainer.laden .fourthRow > .textPrio2 {
  max-width: 985px;
}

.fourthRow > .textPrio2 {
  max-width: calc(100% - (310px + 20px));
  margin-left: 20px;
}

.fourthRow .btn,
.fourthRow .btnOpti {
  position: absolute;
  right: var(--minTouchSize);
  bottom: 0px;
  max-width: var(--basicChargingBBWidth);
}

.fourthRow > .twoLines {
  max-width: calc(
    100% - var(--basicChargingBBWidth) - var(--cA1) - var(--minTouchSize) * 3
  );
}

.btnOTopRight {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  position: absolute;
  top: 0px;
  right: 0px;
}

.oneInfoContainer .UE_0702_ProgressBar {
  width: calc(100% - var(--minTouchSize));
}

.ST_0003_BasicChargingSettings .UE_0702_ProgressBar .currentProgress {
  margin: 0;
}

.topTextContainer {
  display: flex;
  width: 100%;
  margin-bottom: 143px;
  justify-content: space-between;
}
.leftText {
  display: flex;
  align-items: center;
  justify-content: center;
}

.leftText .twoLines {
  max-width: 785px;
  margin: 0 var(--cA1);
}
.rightText {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: var(--minTouchSize);
}
.rightText .twoLines {
  margin-left: var(--iA1);
  max-width: 555px;
}

.settings {
  position: absolute;
  top: 830px;
  width: 800px;
}

/* ------------------------- OI DISPLAY --------------------------*/

@media only screen and (max-width: 1920px) {
  :global(#root) > .ST_0003_BasicChargingSettings {
    height: 578px;
    width: 1800px;
  }

  .chargingSettings .carDecoratorWrapper {
    width: 100%;
    height: 300px;
    top: 0px;
  }

  .chargingSettings .carDecoratorWrapper.phev {
    height: 460px;
    top: -60px;
  }

  /* AU336_0 / AU336_1 */
  .ST_0003_BasicChargingSettings.AU336_0 .chargingSettings .energyFlow,
  .ST_0003_BasicChargingSettings.AU336_1 .chargingSettings .energyFlow {
    transform: scale(0.85);
    top: 155px;
  }
  .ST_0003_BasicChargingSettings.AU336_0 .chargingSettings .energyFlow.left,
  .ST_0003_BasicChargingSettings.AU336_1 .chargingSettings .energyFlow.left {
    left: 606px;
  }
  .ST_0003_BasicChargingSettings.AU336_0 .chargingSettings .energyFlow.right,
  .ST_0003_BasicChargingSettings.AU336_1 .chargingSettings .energyFlow.right {
    left: 1020px;
  }

  .oneInfoContainer .SE_0002_SOCBargraph {
    top: 167px;
  }

  .topTextContainer {
    margin-bottom: 70px;
  }

  .oneInfoContainer.opti .firstRow .firstOptiElem {
    margin-left: calc(var(--minTouchSize) + var(--cA1));
    width: calc(100% - var(--cA1) * 2 - var(--minTouchSize) * 2);
  }

  .chargingFlapConnector {
    position: absolute;
    left: 1110px;
    top: 120px;
  }
  .antriebsModusConnector {
    position: absolute;
    left: 710px;
    top: 110px;
  }

  .chargingSettings .chargingStationWrapper {
    height: 210px;
    width: 231px;
    top: 14%;
    right: 17%;
  }

  .chargingSettings .chargingStationWrapper.phev {
    height: 300px;
    width: 346px;
    top: 16%;
    right: 19%;
  }
}

/* ------------------------- SMALL DISPLAY --------------------------*/

@media only screen and (max-width: 1768px) {
  :global(#root) > .ST_0003_BasicChargingSettings {
    height: 425px;
    width: 1526px;
  }

  .chargingSettings .carDecoratorWrapper {
    width: 986px;
    height: 427px;
    top: 10px;
  }

  .chargingSettings .carDecoratorWrapper .carDecorator,
  .chargingSettings .carDecoratorWrapper .carDecoratorColor,
  .chargingSettings .carDecoratorWrapper .carWheels {
    background-position: -225px 0px;
    /* background-position: 465px 0px; */
  }

  .chargingSettings .carDecoratorWrapper .carDecoratorColor {
    -webkit-mask-position: -225px 0px;
  }

  .topTextContainer {
    margin-bottom: var(--zA6);
  }
  .oneInfoContainer.opti .firstRow .firstOptiElem {
    height: calc(var(--minTouchSize) * 2 + var(--zA6) + var(--listIconSize));
  }

  .firstRow {
    margin-bottom: 0px;
  }
  .oneInfoContainer .SE_0002_SOCBargraph {
    transform: unset;
  }
}
