.tlcArrow {
  position: absolute;
  left: 380px;
  top: 116px;
  width: 345px;
  height: 202px;
  background-image: url('../../assets/TLC_arrow_left.png');
}
.tlcArrow--rhd {
  left: 234px;
}

.tlcVze {
  position: absolute;
  left: 853px;
  bottom: 4px;
  width: 56px;
  height: 68px;
  background-image: url('../../assets/VZE.png');
}
.tlcVze--rsView {
  left: 912px;
}
.tlcVze--rhd {
  left: unset;
  right: 853px;
}
.tlcVze--rhd--rsView {
  left: unset;
  right: 912px;
}

.folTlc {
  position: absolute;
  display: flex;
  left: 317px;
  align-items: end;
  bottom: 0;
}
.folTlc--rhd {
  left: unset;
  right: 317px;
}

.followIcon {
  width: 45px;
  height: 45px;
  margin-right: 4px;
  background: url('../../assets/follow_arrows\ \(dummy\).png') no-repeat;
  background-position: bottom;
  background-size: 45px 45px;
  align-self: flex-end;
}

.folTlc p {
  line-height: 0.75;
  margin-bottom: 5px;
  overflow: visible;
}
.folTlc p:nth-child(2) {
  margin-right: 2px;
  margin-bottom: 5px;
  overflow: inherit;
  line-height: 0.75;
}
