/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --keyCount: 11;
  --separatorCount: calc((var(--keyCount) - 1) * var(--keyboardSeparatorWidth));
  --keyWidth: calc(100% / var(--keyCount));
  --longPressExpensionALeft: 63px;
  --longPressExpensionOLeftOne: 65px;
  --longPressExpensionOLeftTwo: 237px;
  --longPressExpensionOLeftThree: 411px;
  --longPressExpensionOLeftFour: 584px;
  --longPressExpensionOLeftFive: 756px;
  --menuKeyWidth: 172px;
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_0507_EmojiKeyboard {
  margin: 0;
}

.UE_0507_EmojiKeyboard {
  width: 100%;
  height: 100%;
}

:global(#root) > .UE_0507_EmojiKeyboard .keyboard {
  top: calc(2 * var(--minTouchSize) + var(--iA3));
}

/*-------------------- .UE_0507_Keyboard --------------------*/

.mainKeyboardSection {
  display: flex;
  flex-direction: row;
  min-height: 376px;
}

.categoryWrapper {
  display: flex;
  flex-direction: row;
}

.firstEmojiContainer {
  padding-left: 4px;
}

.secondEmojiContainer {
  max-width: 79%;
  padding-left: 4px;
}

.emojiButton {
  margin-top: 14px;
  margin-left: -4px;
}

.keyboardLine {
  margin-top: -3.5px;
  height: calc(var(--keyboardLineHeight) - 1px);
  display: flex;
}

/* KEYBOARD BUTTON */

.keyboardLine .button {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.keyboard .keyboardLine .button p {
  text-align: center;
  align-self: center;
}

.firstSpecialButton {
  align-self: center;
}

.firstSpecialButton:after {
  content: ' ';
  position: absolute;
  border-left: 1px white solid;
  top: -106.5%;
  left: 7%;
  height: 317%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

.firstSpecialButton:before {
  content: ' ';
  position: absolute;
  border-left: 1px white solid;
  top: -106.5%;
  right: 2.8%;
  height: 317%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

.secondSpecialButton {
  align-self: center;
}

.secondSpecialButton:after {
  content: ' ';
  position: absolute;
  border-left: 1px white solid;
  top: -106.5%;
  left: 7%;
  height: 317%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

.secondSpecialButton:before {
  content: ' ';
  position: absolute;
  border-left: 1px white solid;
  top: -106.5%;
  right: 2.8%;
  height: 317%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

/* KEYBOARD FOOTER */

.footer {
  display: flex;
}

.footer .toggleGroupContainer {
  display: flex;
  flex-direction: row;
}

.toggleButton {
  height: calc(var(--minTouchSize) - 30px);
  min-width: var(--minTouchSize);
  justify-content: center;
  margin-left: -8px;
}

.toggleButton:after {
  content: ' ';
  position: absolute;
  border-left: 0.5px #6c757d solid;
  top: 35%;
  right: 2.8%;
  height: 30%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

.footer .wideButton {
  height: calc(var(--minTouchSize) - 30px);
  width: calc(var(--keyWidth) * 2);
}

.wideButton:after {
  content: ' ';
  position: absolute;
  border-left: 0.5px #6c757d solid;
  top: 35%;
  right: 0.7%;
  height: 30%;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: -3px;
}

.footer .secondWideButton {
  height: calc(var(--minTouchSize) - 30px);
  width: calc(var(--keyWidth) * 3);
  margin-left: -8px;
}

.SE_0081_LongPressExtension {
  position: absolute;
  z-index: 3;
}

.SE_0081_LongPressExtension.A_Extension {
  top: -68px;
}

.SE_0081_LongPressExtension.A_Extension.twoLines {
  top: -189px;
}

.SE_0081_LongPressExtension .button {
  width: var(--menuKeyWidth) !important;
}

.SE_0081_LongPressExtension.O_Extension {
  right: 350px;
  top: -157px;
}

.SE_0081_LongPressExtension.O_Extension.twoLines {
  top: -270px;
}

.SE_0081_LongPressExtension.A_Extension .button:hover .uiState,
.SE_0081_LongPressExtension.O_Extension .button:hover .uiState {
  width: 100%;
  opacity: 1;
}

@media only screen and (max-width: 1768px) {
  :root {
    --menuKeyWidth: 158.35px;
  }

  .SE_0081_LongPressExtension.O_Extension {
    right: 319px;
  }
}

.settings {
  position: relative;
  top: calc(2 * var(--minTouchSize) + var(--iA3));
}
