/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --numkeyCount: 3;
  --numSeparatorCount: calc(
    (var(--numkeyCount) - 1) * var(--keyboardSeparatorWidth)
  );
  --numkeyWidth: calc(100% / var(--numkeyCount));
}

:global(#root) > .UE_0503_NumericKeypad {
}
.UE_0503_NumericKeypad {
}

.UE_0503_NumericKeypad .keypad {
  position: relative;
  height: calc(4 * var(--keyboardLineHeight));
  display: flex;
  flex-direction: column;
}

.UE_0503_NumericKeypad .keypad.spinOi,
.UE_0503_NumericKeypad .keypad.spin {
  height: calc(5 * var(--keyboardLineHeight));
}

.UE_0503_NumericKeypad .keypad.pinPad {
  height: calc(4 * var(--keyboardLineHeight));
}

.UE_0503_NumericKeypad .keypad .inputLine {
  flex: 1;
  height: calc(var(--keyboardLineHeight) - 1px);
  display: flex;
  background-color: rgba(var(--audiBlack), 0.1);
  align-items: center;
  justify-content: space-evenly;
}

.UE_0503_NumericKeypad .keypad .inputLine .circle {
  background-color: none;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  border: 2px solid rgb(var(--audiWhite));
}

.UE_0503_NumericKeypad .keypad .inputLine .circle:nth-of-type(-n + 2) {
  background-color: rgb(var(--audiWhite));
}

.UE_0503_NumericKeypad .keypad .keyboardLine {
  height: calc(var(--keyboardLineHeight) - 1px);
  display: flex;
  border-bottom: 1px solid rgb(var(--audiGrey));
}

.UE_0503_NumericKeypad .keypad .keyboardLine:last-of-type {
  border-bottom: 1px solid rgba(var(--audiGrey), 0);
}

.UE_0503_NumericKeypad .keypad .keyboardLine .button {
  position: relative;
  width: var(--numkeyWidth);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  flex: 1;
}

.UE_0503_NumericKeypad .keypad.spin .keyboardLine .button.spin,
.UE_0503_NumericKeypad .keypad.pinPad .keyboardLine .button.spin {
  display: flex;
  justify-content: center;
  align-items: center;
}

.UE_0503_NumericKeypad .keypad .keyboardLine .button .keyContentWrapper {
  height: 75px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-self: center;
  width: fit-content;
}

.UE_0503_NumericKeypad .keypad.showLetters .keyboardLine .button .letters {
  display: block;
}

.UE_0503_NumericKeypad .keypad.sideButtons .keyboardLine .button.sideButton {
  display: flex;
  justify-content: center;
  align-items: center;
}

.emptyBtn:not(:first-of-type):before,
.UE_0503_NumericKeypad .keypad .keyboardLine .button:before {
  content: '';
  position: absolute;
  width: var(--keyboardSeparatorWidth);
  height: var(--keyboardSeparatorHeight);
  background: rgb(var(--audiGrey));
  left: 0;
}
.UE_0503_NumericKeypad .keypad .keyboardLine .button .Text {
  text-align: center;
  align-self: center;
}

.UE_0503_NumericKeypad .keypad .keyboardLine .noBorder:before,
.UE_0503_NumericKeypad .keypad .keyboardLine .button:first-child:before {
  background: none;
}

.inputLineContainer {
  width: 100%;
  display: flex;
  border-bottom: 1px solid rgb(var(--audiGrey));
}

.emptyBtn {
  flex: 1;
  display: flex;
  position: relative;
  align-items: center;
}

.spinOiBtn {
  height: 100%;
  /* width: var(--numkeyWidth); */
}
/*------------------ settings -----------------*/

.UE_0503_NumericKeypad .settings {
  position: absolute;
  top: calc(100% + 100px);
}

.UE_0503_NumericKeypad .settings p {
  color: white;
  width: auto;
}

.UE_0503_NumericKeypad .settings .textPrio2 {
  margin-bottom: 10px;
}

.UE_0503_NumericKeypad .settings .textSettingLabel {
  margin-bottom: 10px;
}
