/* CLASSIFICATION: CONFIDENTIAL */
:global(#root) > .SE_0090_Discharge_Screen {
  position: absolute;
  height: 554px;
  width: 1678px;
  margin: 0;
}

:global(#root) > .SE_0090_Discharge_Screen > .background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.SE_0090_Discharge_Screen .discharge {
  position: relative;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  margin-left: var(--iA2);
  margin-right: var(--iA2);
}

.SE_0090_Discharge_Screen .discharge .explainContainer,
.SE_0090_Discharge_Screen .discharge .powerContainer,
.SE_0090_Discharge_Screen .discharge .timeContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.SE_0090_Discharge_Screen .discharge p + .UE_0702_ProgressBar {
  margin-top: var(--zA1);
}

.SE_0090_Discharge_Screen .discharge p {
  width: calc(100% - 2 * 20px);
  margin: 0 20px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@media only screen and (max-width: 1768px) {
  :global(#root) > .SE_0090_Discharge_Screen {
    height: 425px;
    width: 1526px;
  }
}
