:global(#root) > .UE_0404_Titleline {
  height: var(--minTouchSize);
  background-color: rgb(var(--audiGrey));
}

.UE_0404_Titleline {
  position: relative;
  width: 100%;
  height: 100%;
  flex: 1;
  display: flex;
  justify-content: flex-start; /* align horizontal */
  align-items: center; /* align vertical */
  overflow: hidden;
  box-sizing: border-box;
}
.firstElement {
  margin-left: -5px;
  padding-left: 5px;
}
.lastElement {
  margin-right: -5px;
  padding-right: 5px;
}

.npsSourceIcon {
  position: relative;
  display: flex;
  height: calc(100% - 7px);
  width: var(--minTouchSize);
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
  flex-shrink: 0;
}

.npsSourceIcon .appIonBackground,
.npsSourceIcon .appListIcon {
  width: 60px;
  height: 60px;
}

.npsSourceIcon .appListIcon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.btnSourceIcon {
  height: 100%;
}

.appListIcon {
  position: relative;
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E724_main_menue_media_DA.svg);
}

.button {
  min-width: var(--minTouchSize);
  min-height: var(--minTouchSize);
}

.textContainer p {
  margin-left: var(--cA1);
  margin-right: var(--cA1);
  width: calc(100% - var(--cA1) * 2);
}

.headline p {
  text-align: center;
  z-index: 1;
}

.headline::before {
  position: absolute;
  left: calc(var(--opticalTouchReduction) / 2);
  bottom: calc(var(--opticalTouchReduction) / 2 + 1px);
  content: '';
  height: 2px;
  width: calc(100% - var(--opticalTouchReduction));
  background-color: var(--tabbarSeparatorColor);
}

.textContainer {
  width: 100%;
}

.npsSourceIcon:not(.hidden) ~ p {
  margin-left: unset;
}

.providerIconContainer {
  width: fit-content;
  margin-right: var(--lA1);
}

.providerIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  background-color: rgba(var(--boundingBoxGreenFull));
}

.providerIcon.large {
  width: calc(var(--listIconSize) * 2);
}
