/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --switchTrackWidth: 80px;
  --switchTrackBorderWidth: 2px;
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_1103_Switch {
  background-color: rgb(var(--bodyColorGrey));
  height: 150px;
  width: 400px;
}

/*-------------------- UE_1103_Switch --------------------*/

.UE_1103_Switch {
}

.switch {
  position: absolute;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  display: flex;
  justify-content: center;
  align-items: center;
}

.switchCheckbox {
  display: none;
}

.switchTrack {
  width: var(--switchTrackWidth);
  height: 36px;
  display: flex;
  align-items: center;
  overflow: hidden;
  cursor: pointer;
  border: var(--switchTrackBorderWidth) solid rgba(var(--audiWhite), 0.8);
  border-radius: calc(var(--minTouchSize) * 2);
  background-color: rgba(var(--interactionBackground), 0.07);
  transition: var(--AG_0303_ScaleResize),
    background-color var(--animationDuration) var(--animationCurve);
}

/* switch off */
.switchThumb {
  width: 26px;
  height: 26px;
  position: relative;
  left: 5px;
  border-radius: 50%;
  background-color: rgb(var(--audiWhite));
  transition: var(--AG_0303_ScaleResize),
    left var(--animationDuration) var(--animationCurve),
    opacity var(--animationDuration) var(--animationCurve);
}

/* switch on */
.switchCheckbox:checked + .switchTrack {
  background-color: rgba(var(--interactionBackground), 0.4);
  border: var(--switchTrackBorderWidth) solid rgba(var(--audiWhite), 1);
}
.switchCheckbox:checked + .switchTrack .switchThumb {
  left: 49px;
}

.settings {
  margin-top: 100px;
  position: absolute;
}
