.PSPTrainedParking {
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;

  &__upperPanel {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    flex: 1;
  
    &__tpHeadline {
      position: relative;
      height: fit-content;
      left: 24px;
      width: calc(100% - 43px);
      margin-top: var(--iA1);
      margin-bottom: var(--hA1);
      padding: 0 var(--lA1);
      box-sizing: border-box;
    }

    &__moreMenuDarkLayer {
      width: 100%;
      height: calc(100% - var(--minTouchSize));
      position: absolute;
      z-index: 2;
      background-color: rgba(var(--audiBlack), 0.7);
    }
    
    &__listItemContainer {
      width: calc(100% - 43px);
      left: 24px;
      position: relative;
    }

    &__listVerticalContainer {
      flex: 1;
      position: relative;
      overflow-y: scroll;
    }

    &__moreMenuContainer {
      position: absolute;
      top: 54px;
      right: 110px;
      left: unset;
      visibility: unset;
      z-index: 3;
    }
  }
}
