/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --FPKListItemHeight: 63px;
}

:global(#root) > .UE_1002_ListItemStandard {
  margin: 0px;
  border: 1px solid yellow;
  background-color: rgb(var(--bodyColorGrey));
  position: relative;
}

/*------------------------------- CURSOR ------------------------------*/
.UE_1002_ListItemStandard .cursorContainer,
.UE_1002_ListItemStandard .cursorContainer .UE_6100_C1Cursor,
.UE_1002_ListItemStandard .cursorContainer .UE_1009_C2Cursor {
  height: var(--minTouchSize);
  width: 100%;
  position: absolute;
}

.UE_1002_ListItemStandard.HDradioFID .cursorContainer .UE_6100_C1Cursor,
.UE_1002_ListItemStandard.HDradioFID .cursorContainer .UE_1009_C2Cursor {
  height: var(--FPKListItemHeight);
}

.UE_1002_ListItemStandard .cursorContainer div[class*='hidden'] {
  display: none !important;
}
/*------------------------------- LIST ELEMENTS --------------------------------*/
.UE_1002_ListItemStandard .listElement {
  width: calc(100% - var(--lA1));
  height: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  align-items: stretch;
  flex-wrap: nowrap;
  position: relative;
}

/* .UE_1002_ListItemStandard:not(.fpk) .listElement {
  padding-left: var(--lA1);
  width: calc(100% - var(--lA1));
} */

.leftAligned {
  padding-left: 0px !important;
  width: 100% !important;
}

.UE_1002_ListItemStandard .listElement.emailMarker::before {
  content: '';
  width: var(--zA1);
  height: calc(var(--minTouchSize) - 2px);
  position: absolute;
  background-color: rgb(var(--climateBlue));
  left: 6px;
  top: 1px;
}
.UE_1002_ListItemStandard .listElement .uiStateContainer.uiList {
  display: block;
}

.UE_1002_ListItemStandard .listElement .uiStateContainer {
  display: none;
  width: 100%;
  position: absolute;
  height: var(--minTouchSize);
  z-index: 1;
  left: -4px;
}

/* from .uiStates.css */
.uiList:active .uiState {
  height: 100%;
  margin: auto;
  opacity: 1;
}
.uiList:active .uiState.contextUIState {
  height: calc(100% - var(--uiStateMargin));
  bottom: calc(var(--uiStateMargin) / 2);
}
.uiList .uiState {
  height: 0;
}
.uiList:active .uiState {
  width: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}
.uiList .uiState {
  transition:
    opacity var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve);
  width: calc(100% - 2 * var(--uiStateMargin)) !important;
}
.uiList .uiState {
  position: absolute;
  width: 0%;
  height: 0%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  display: inline-flex;
  flex-direction: column;
}

.UE_1002_ListItemStandard .listElement .swipeToDelete {
  position: absolute;
  width: 100%;
  height: 100%;
  clip-path: inset(0px 0px 0px 100%);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.UE_1002_ListItemStandard .listElement .swipeToDelete .UE_0101_ActionButton {
  position: relative;
  width: var(--minTouchSize);
  height: var(--minTouchSize);
  right: 0px;
}

.UE_1002_ListItemStandard .listElement .swipeToDelete .background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--audiRed), 0.7);
}

/*------------------------------- DRAG STATE --------------------------------*/
.dragState {
  background-color: rgb(var(--interactionBackground), 0.2) !important;
  border-radius: var(--standardBorderRadius);
}

/*------------------------------- SWIPE TO DELETE --------------------------------*/
.UE_1002_ListItemStandard:not(.fpk):not(.HDradioFID)
  .listElement.showDeleteButton {
  left: calc(var(--minTouchSize) * -1);
}
.UE_1002_ListItemStandard:not(.fpk):not(.HDradioFID)
  .listElement.showDeleteButton
  .swipeToDelete {
  right: calc(var(--minTouchSize) * -1);
  clip-path: inset(0px 0px 0px calc(100% - var(--minTouchSize)));
}

.UE_1002_ListItemStandard:not(.fpk):not(.HDradioFID) .listElement.deleteRow {
  left: calc(100% * -1);
}
.UE_1002_ListItemStandard:not(.fpk):not(.HDradioFID)
  .listElement.deleteRow
  .swipeToDelete {
  right: calc(100% * -1);
  margin-left: 100%;
  clip-path: inset(0px 0px 0px calc(100% * -1));
}

