/* CLASSIFICATION: CONFIDENTIAL */

$climateSideMargin: 80px;

#root > .departureTimer {
  margin: 8px;
}

.departureTimer {
  /*DO NOT REMOVE THIS BLOCK*/

  &__textbox {
    position: relative;
    width: 100%;
    height: 130px;
    overflow: hidden;
    white-space: nowrap;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &__textPrio2 {
      margin-left: var(--iA2);
    }

    &__secondRow {
      display: flex;
      flex-direction: row;
      justify-content: center;
      margin-top: var(--zA6);

      &__textPrio2 {
        margin-left: var(--iA2);
      }
      &__am {
        margin-top: 3px;
      }

      &__pm {
        margin-top: 10px;
        margin-left: 1px;
      }
    }
  }
}
