.select_box {
  position: relative;
  display: flex;
  width: 250px;
  max-width: 550px;
  flex-direction: column;
  flex-shrink: 0;
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.select_box .options_container {
  background-color: rgb(var(--audiWhite));
  min-height: fit-content;
  max-height: 300px;
  pointer-events: none;
  max-width: 550px;
  min-width: 100%;
  width: max-content;
  right: 0;
  opacity: 0;
  overflow-y: scroll;
  overflow-x: hidden;
  order: 1;
  position: absolute;
  top: 91px;
  border-top: 1px solid rgba(var(--audiGrey), 0.9);
  box-sizing: border-box;
  border-radius: 0 0 5px 5px;
}

.selected {
  background-color: rgb(var(--audiWhite));
  position: relative;
  order: 0;
  height: 50px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  overflow: hidden;
  width: 100%;
}

.selected::after {
  content: '';
  position: absolute;
  right: 10px;
  border: solid rgb(var(--audiBlack));
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(45deg);
  top: 19px;
}

.selected .textPrio3 {
  font-size: 15px;
  color: rgb(var(--audiBlack));
  cursor: pointer;
}

.select_box .options_container.active {
  pointer-events: all;
  opacity: 1;
  z-index: 3;
}

.select_box .options_container.active + .selected::after {
  transform: rotate(225deg);
  top: 23px;
}

.select_box .options_container::-webkit-scrollbar {
  width: 4px;
  background: rgba(var(--audiGrey), 0.4);
  border-radius: 0 0 5px 0;
}

.select_box .options_container::-webkit-scrollbar-thumb {
  background: rgb(var(--audiWhite));
  border: 1px solid rgba(var(--audiGrey), 1);
}

.select_box .options_container.active .option label {
  height: unset;
}

.select_box .option,
.selected {
  padding: 5px 10px;
  cursor: pointer;
  background: rgb(var(--audiWhite));
}
.select_box .option {
  background: rgb(var(--audiWhite));
}

.selected {
  padding-right: 28px;
  border-radius: 5px;
}

.options_container.active ~ .selected {
  border-radius: 5px 5px 0 0;
}

.select_box .option:hover {
  background: rgba(var(--audiGrey), 0.7);
  color: rgb(var(--audiWhite));
}

.option.selectedOption {
  background: rgba(var(--audiGrey), 1);
  color: rgb(var(--audiWhite));
  scroll-margin-top: 40px;
  scroll-margin-bottom: 40px;
}

.select_box .options_container .option label {
  width: 100%;
  margin: 0;
  overflow: hidden;
  max-width: unset;
  font-size: 15px;
}

.select_box label {
  cursor: pointer;
  color: unset !important;
}

.select_box .option .radio {
  display: none;
}

.search_box {
  position: absolute;
  top: 50px;
  z-index: 1;
  width: 100%;
}

.search_box input {
  width: 100%;
  height: 41px;
  padding: 5px 10px;
  z-index: 100;
  border: 0;
  box-sizing: border-box;
  opacity: 0;
  pointer-events: none;
  font-size: 15px;
  border-top: 1px solid rgba(var(--audiGrey), 0.9);
  background-color: rgb(var(--audiWhite));
}

.search_box input:focus {
  outline: none;
}

.select_box .options_container.active ~ .search_box input {
  opacity: 1;
  pointer-events: auto;
}

@media only screen and (max-width: 1000px) {
  .select_box {
    max-width: 250px;
  }
  .select_box .options_container {
    max-width: unset;
    min-width: unset;
    width: 250px;
  }
}
