/* CLASSIFICATION: CONFIDENTIAL */
:global(#root) > .UE_1005_ListFolding {
  margin: 0;
  background-color: rgb(var(--bodyColorGrey));
}

.UE_1005_ListFolding {
}

.UE_1005_ListFolding .uiStateContainer.uiList {
  display: block;
}

.UE_1005_ListFolding .uiStateContainer {
  display: none;
  width: 100%;
  position: absolute;
  height: var(--minTouchSize);
  z-index: 1;
  left: -4px;
}

.foldingWidget {
  --listFoldingOpenHeight: 500px;
  position: relative;
  width: 100%;
  max-height: 119px;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.UE_1005_ListFolding .foldingWidget.activated {
  max-height: var(--listFoldingOpenHeight);
  transition: max-height 0.3s ease;
}

.foldingTrigger {
  position: relative;
  width: 100%;
}

.rotationContainer {
  position: absolute;
  height: auto;
  width: auto;
  pointer-events: none;
  margin-left: var(--lA1);
}

.foldingWidget .foldingTrigger .rotationContainer .touchIcon {
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Standard/E02E_listarrow_right.svg');
  transform-origin: top;
  transition: all 0.3s ease;
}

.foldingWidget.activated .foldingTrigger .rotationContainer .touchIcon {
  transform: rotate(90deg);
}

.UE_1002_ListItemStandard,
 li.UE_1002_ListItemStandard  /* overwrite ListVertical rule */ {
  flex: 1;
  overflow: hidden;
  border-bottom: 1px solid var(--listSeparatorLineColor);
}

.foldingTrigger.foldingSmallTouch > .UE_1002_ListItemStandard {
  margin-left: 0;
}

.foldingContent .UE_1002_ListItemStandard:last-child {
  border-bottom: 0;
}

.foldingWidget {
  position: relative;
  width: 100%;
  max-height: 119px;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.foldingTrigger {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.foldingArrow {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 10px 0 10px 15px;
  border-color: transparent transparent transparent rgb(var(--audiWhite));
  transition: all 0.3s ease;
}

.foldingTrigger.foldingSmallTouch .rotationContainer {
  position: relative;
  transform: unset;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  pointer-events: initial;
  margin-left: 10px;
}

.foldingWidget .foldingTrigger .rotationContainer .touchIcon {
  transition: all 0.3s ease;
}

.foldingWidget.activated .foldingTrigger .rotationContainer .touchIcon {
  transform: rotate(90deg);
}

.listIconContainer {
  height: var(--minTouchSize);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
  padding: 0 var(--iA1);
}
.listIcon + .listIcon {
  margin-left: 0;
  padding-left: 5px;
}

/* Seven_Days_List */
.foldingWidget.sevenDaysList .foldingContent {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
  margin-right: 50px;
  margin-bottom: 30px;
  min-height: 215px;
  height: fit-content;
}

.foldingWidget.maxCategories .foldingContent {
  height: 258px;
}

.foldingWidget .foldingContent .leftPart {
  flex: 1;
  flex-shrink: 0;
  margin-left: calc(var(--minTouchSize) + var(--lA1) + 10px);
  margin-top: 8px;
  margin-right: 30px;
}
.foldingWidget .foldingContent .leftPart .categorieLine {
  display: flex;
  position: relative;
}
.foldingWidget .foldingContent .leftPart .categorieLine:not(:first-child) {
  margin-top: 40px;
}
.foldingWidget .foldingContent .leftPart .categorieLine p {
  position: relative;
  margin-left: var(--iA2);
  width: 420px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.foldingWidget .foldingContent .leftPart .categorieLine::after {
  content: '';
  position: absolute;
  width: 450px;
  left: 55px;
  height: 1px;
  bottom: calc(50% - var(--lineHeightPrio2) / 2 - 5px);
  background-color: rgba(var(--audiWhite), 0.3);
}
.foldingWidget .foldingContent .leftPart .categorieLine.warning::after {
  background-color: rgba(var(--fpkRed), 1);
}

.foldingWidget .foldingContent .rightPart {
  flex: 1;
  flex-shrink: 0;
  margin-right: 20px;
  overflow: hidden;
}
.foldingWidget .foldingContent .rightPart .textWrapper {
  display: flex;
  margin-top: var(--zA1);
  overflow: hidden;
}
.foldingWidget .foldingContent .rightPart .leftText {
  display: flex;
  flex-direction: column;
  margin-right: var(--iA1);
  overflow: hidden;
}
.foldingWidget .foldingContent .rightPart .rightText {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  overflow: hidden;
}

.foldingWidget .foldingContent .rightPart p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 100%;
}

.foldingWidget .foldingContent .buttonPart {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  width: var(--minTouchSize);
  height: 165px;
}

.foldingWidget.maxCategories .foldingContent .buttonPart {
  height: calc(100% - 50px);
}

.foldingWidget .foldingContent .buttonPart .UE_0101_ActionButton {
  position: relative;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
}

.UE_1002_ListItemStandard .listElement .controlContainer {
  display: flex;
  z-index: 5;
}

/* from .uiStates.css */
.uiList:active .uiState {
  height: 100%;
  margin: auto;
  opacity: 1;
}
.uiList:active .uiState.contextUIState {
  height: calc(100% - var(--uiStateMargin));
  bottom: calc(var(--uiStateMargin) / 2);
}
.uiList .uiState {
  height: 0;
}
.uiList:active .uiState {
  width: calc(100% - 2 * var(--uiStateMargin));
  opacity: 1;
}
.uiList .uiState {
  transition: opacity var(--uiStateTransitionDuration) var(--animationCurve),
    height var(--uiStateTransitionDuration) var(--animationCurve);
  width: calc(100% - 2 * var(--uiStateMargin)) !important;
}
.uiList .uiState {
  position: absolute;
  width: 0%;
  height: 0%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  display: inline-flex;
  flex-direction: column;
}

.UE_1005_ListFolding .UE_0101_ActionButton {
  min-width: unset;
  width: var(--touchIconSize);
}

.UE_1005_ListFolding .UE_1002_ListItemStandard {
  margin-left: calc(var(--touchIconSize) + var(--lA1));
}

.UE_1005_ListFolding  .foldingTrigger.foldingSmallTouch .UE_0101_ActionButton {
  min-width: var(--minTouchSize);
  width: var(--minTouchSize);
}

.UE_1005_ListFolding .foldingContent.foldingSmallTouch .UE_1002_ListItemStandard {
  margin-left: calc(var(--minTouchSize) + 10px);
}

.uiList .settingsClass {
  left: -60px;
}

.settings {
  position: absolute;
  top: 400px;
  width: 60%;
}
