/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --stepperTopDistDashboard: 3px;
  --pageIndicatorWidth: 109px;
}

:global(#root) > .UE_1303_Stepper_Decorator {
  background: rgb(var(--bodyColorGrey));
  position: absolute;
  height: 554px;
  width: 1678px;
  margin: 0;
  overflow: hidden;
}

:global(#root) > .UE_1303_Stepper_Decorator .background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}

.stepperDecorator {
  position: absolute;
  height: 100%;
  width: 100%;
}

:global(#root) > .UE_1303_Stepper_Decorator .darkRectangle {
  background-color: rgba(var(--audiBlack), 0.5);
  position: absolute;
  height: 100%;
  width: 100%;
}

.UE_0101_ActionButton {
  position: absolute;
  width: var(--minTouchSize);
  height: 100%;
}

.button {
  width: var(--minTouchSize);
  height: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.imagesContainer {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  justify-content: center;
}

.imagesContainer > div {
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  margin-left: auto;
  margin-right: auto;
}

.basicImg {
  background-image: url('../img/Stepper-decorator-basic.png');
  opacity: 0.25;
}

.colorImg {
  -webkit-mask-image: url('../img/Stepper-decorator-color.png');
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-size: contain;
  -webkit-mask-position: center;
  background-color: rgb(var(--ambientcolor1));
}

.lightImg {
  background-image: url('../img/Stepper-decorator-light.png');
}

/*-------------------Page Indicators------------------*/
.UE_1303_Stepper_Decorator .stepperDecorator .UE_0801_PageIndicator {
  position: absolute;
  left: calc(50%);
  transform: translateX(-50%);
  top: 97%;
  display: block;
}
.UE_1303_Stepper_Decorator
  .stepperDecorator
  .UE_0801_PageIndicator
  .pageIndicator {
  padding-right: unset;
}

.UE_1303_Stepper_Decorator .stepperDecorator .numericPageIndicator {
  width: var(--pageIndicatorWidth);
  position: absolute;
  left: calc(50% - (var(--pageIndicatorWidth) / 2));
  bottom: var(--hA1);
  justify-content: center;
  align-items: center;
  overflow: hidden;
  display: flex;
}

.UE_1303_Stepper_Decorator .stepperDecorator .numericPageIndicator p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.settings {
  position: absolute;
  top: 600px;
}

