/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --timebarLeftGap: 40px;
  --textCoverGap: 80px;
  --firstColumnWidth: calc(var(--timebarLeftGap) + 40px);
  --threeLinesTitleLineHeight: calc(
    var(--lineHeightPrio1) + (var(--lineHeightPrio2) * 2)
  );
  --lineChannelTopMargin: 66px;
  --episodeNPSTopMargin: 59px;
  --lyricsDistance: 20px;
  --lyricsTop: 91px;
  --largeMargin: 95px;
  --coverColumnWidth: calc(var(--coverSize) / 2 + var(--tileWidth) / 2);
}

:global(#root) > .T_0102_DetailScreen {
  background-color: rgb(var(--bodyColorGrey));
  height: 526px;
  width: 100%;
  position: absolute;
  margin: 0;
}

.T_0102_DetailScreen .detailScreen {
  position: relative;
  display: flex;
  height: 100%;
  width: 100%;
}

.T_0102_DetailScreen .detailScreen .musicInfoContainer {
  width: calc(100% - var(--coverColumnWidth) - 75px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.T_0102_DetailScreen .detailScreen.immersiveMode .musicInfoContainer {
  justify-content: center;
}

.T_0102_DetailScreen .detailScreen .coverContainer {
  width: var(--coverColumnWidth);
  display: flex;
  justify-content: center;
}

.T_0102_DetailScreen .detailScreen .coverContainer.dashboardViewCover {
  width: 100%;
}

.T_0102_DetailScreen .detailScreen .cover {
  position: relative;
  height: var(--coverSize);
  width: var(--coverSize);
  justify-self: center;
  align-self: center;
  background-size: contain;
  background-image: url(../../../../../Globals/assets/defaultCover.png);
  transition: all var(--animationDuration) ease;
  margin-left: 0px;
}
.T_0102_DetailScreen.detailView .detailScreen .cover {
  margin-left: 65px;
}

.T_0102_DetailScreen .detailScreen .listIcon.hdVoice,
.T_0102_DetailScreen .detailScreen .musicInfoContainer.hide {
  display: none;
}

/*--------------------------------- TITLELINE ------------------------------------*/
.T_0102_DetailScreen .detailScreen .titleLine {
