  align-items: center;
  justify-self: stretch;
  position: relative;
  margin-left: var(--timebarLeftGap);
  opacity: 0;
  margin-top: 45px;
}

.T_0102_DetailScreen .detailScreen .titleLine p {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.T_0102_DetailScreen .detailScreen .titleLine .textWrapper {
