  position: relative;
  margin-left: var(--timebarLeftGap);
}

/*------------------------------------------- Extended Playback Controls ---------------------------------------------*/
.T_0102_DetailScreen .detailScreen .SE_0049_ExtendedPlaybackControls {
  position: absolute;
  height: 0px;
  bottom: 0px;
  width: calc(100% - var(--operationPanelReduction));
  left: var(--operationPanelShift);
  transition: var(--AG_0202_FadeOut), var(--AG_0303_ScaleResize);
  pointer-events: none;
}

.T_0102_DetailScreen
  .detailScreen
  .SE_0049_ExtendedPlaybackControls.showControls {
  height: calc(var(--minTouchSize) + (var(--opticalTouchReduction) / 2));
  transition: var(--AG_0201_FadeIn), var(--AG_0303_ScaleResize);
  pointer-events: all;
}

/* TODO: move to respective css files */
.T_0102_DetailScreen .detailScreen.epcOn .SE_0049_ExtendedPlaybackControls,
.T_0303_FullScreenDialog
