  transition: var(--AG_0201_FadeIn), var(--AG_0303_ScaleResize);
  pointer-events: all;
}

.T_0102_DetailScreen .detailScreen .epcLayer {
  position: absolute;
  top: -5px;
  height: calc(100% + 5px + var(--minTouchSize));
  width: 100%;
  pointer-events: none;
