/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --statusbarPartWidth: 684px;
  --defaultMultiFunctionWidth: 226px;
  --fullMultiFunctionWidth: 390px;
}

:global(#root) > .UE_6301_FIDUpperStatusBar {
  background-color: rgb(var(--audiBlack));
  width: var(--screenWidthFPK);
  height: var(--upperStatusBarHeightFPK);
}

.UE_6301_FIDUpperStatusBar p {
  margin: 0px !important;
}

.UE_6301_FIDUpperStatusBar .upperStatusbar {
  position: absolute;
  width: var(--screenWidthFPK);
  height: var(--upperStatusBarHeightFPK);
  background-image: url(../img/UE_6301_FIDUpperStatusBar_BlackOverlayGB.png);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar.kb,
.UE_6301_FIDUpperStatusBar .upperStatusbar.infoDimmerUpperStatusBar {
  background-image: url(../img/UE_6301_FIDUpperStatusBar_BlackOverlayKB.png);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .fpkStatusIcons {
  position: absolute;
  top: 0px;
  left: 0px;
  width: var(--statusbarPartWidth);
  height: var(--upperStatusBarHeightFPK);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status {
  position: absolute;
  top: 0px;
  left: 0px;
  width: var(--statusbarPartWidth);
  height: var(--upperStatusBarHeightFPK);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content {
  position: absolute;
  top: 0px;
  left: 0px;
  width: var(--statusbarPartWidth);
  height: var(--upperStatusBarHeightFPK);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content p {
  color: rgba(var(--audiWhite), 0.8) !important;
}

.UE_6301_FIDUpperStatusBar
  .upperStatusbar
  .status.right
  .content.fullMedia
  .multiFunction
  .textPrio2 {
  width: var(--fullMultiFunctionWidth);
  max-width: var(--fullMultiFunctionWidth);
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content .textPrio3.PM,
.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content .textPrio3.AM {
  position: relative;
  margin-left: 3px !important;
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content .textPrio3.PM {
  top: 5px;
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content .textPrio3.AM {
  top: 1px;
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status.right {
  left: 1236px;
}

.UE_6301_FIDUpperStatusBar .upperStatusbar .status.left .content .timestamp {
  position: absolute;
  left: 190px;
  top: 20px;
  width: 137px;
  display: flex;
}
.UE_6301_FIDUpperStatusBar .upperStatusbar .status .content .textPrio2.time {
  width: 83px;
  max-width: 83px;
  text-align: right;
}

.UE_6301_FIDUpperStatusBar
  .upperStatusbar
  .status.left
  .content
  .timestampBlackOverlay {
  position: absolute;
  left: 190px;
  top: 5px;
  width: 160px;
  height: 60px;
  background-image: url(../img/UE_6301_FIDUpperStatusBar_black_overlay_time.png);
  display: inline;
}

.UE_6301_FIDUpperStatusBar
  .upperStatusbar
  .status.left
  .content
