.ST_0135_DashcamList {
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;

  &__listItem {
    display: flex;
    height: 174px;
    width: 100%;
    border-bottom: 1px solid rgba(var(--audiGrey));
    align-items: center;
    justify-content: flex-start;

    &__image {
      width: 194px;
      height: 110px;
      background-color: rgba(var(--boundingBoxGreen));
      margin: 0 var(--cA1);
    }

    &__textBox {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-right: var(--cA1);
    }

    &__lastIconContainer {
      margin: 0 var(--cA1) 0 auto;
    }
  }
}
