$logoTextButtonScreenLogoHeight: 95px;
$logoTextButtonScreenLogoWidth: 524px;

#root > .SE_0002_LogoTextButtonScreen {
  width: 100%;
  height: 100%;
  margin: 0px;
  overflow: hidden;
}

.SE_0002_LogoTextButtonScreen {
  &__UE_0402_TabbarmenuContainer {
    height: var(--minTouchSize);
    position: relative;
    width: 100%;
    z-index: 1;
  }

  &__logoTextButtonScreen {
    height: calc(100% - 119px);
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    padding: 0 var(--iA3);
    box-sizing: border-box;

    &__textWrapper {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      width: 100%;
      text-align: center;
      max-width: 100%;
      white-space: break-spaces;
    }

    &__logoTextButtonScreenLogo {
      width: $logoTextButtonScreenLogoWidth;
      height: $logoTextButtonScreenLogoHeight;
      background-color: rgba(var(--boundingBoxGreenFull));
      margin-bottom: var(--zA4);
    }

    &__buttonsContainer {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 100%;
      margin: var(--zA4) 0;
    }
  }

  &__settings {
    bottom: 0px;
    position: absolute;
    width: 50%;
    height: calc(100% - 870px);
    overflow: auto;
    z-index: 999;
  }
}
