/* CLASSIFICATION: CONFIDENTIAL */

$etaBoxWidth: 464px;

#root > .SE_0006_MapBox {
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
  margin: 20px;
  width: calc($etaBoxWidth + var(--minTouchSize) * 2);
  height: var(--minTouchSize);
}

.SE_0006_MapBox {
  &--mapBoxReducedSize {
    & .mapButton {
      margin-left: 9px;
    }
  }
  .SE_0006_unit {
    color: rgba(var(--audiWhite), 0.7);
    margin-bottom: 2px;
    margin-left: var(--zA8);
  }
  .SE_0006_mapBox {
    position: relative;
    height: var(--minTouchSize);
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: rgba(var(--audiBlack), 0.85);
    border-radius: var(--standardBorderRadius);

    &__btnBg {
      width: 100%;
      height: 100%;
      position: absolute;
      background-color: rgba(var(--audiBlack), 0.85);
    }

    .interactionBackground {
      background-image: linear-gradient(
        to bottom,
        var(--buttonBackgroundColor) 0%,
        var(--buttonBackgroundColor) 100%
      );
      background-color: rgba(var(--audiBlack), 0.98);
    }
    .SE_0006_etaContent {
      position: relative;
      display: flex;
      flex-direction: row;
      width: calc(100% - 2 * var(--minTouchSize));
      justify-content: center;

      &--fitReducedSize {
        width: calc(100% - 1.07 * var(--minTouchSize));
      }

      .SE_0006_etaCol1 {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        margin-left: var(--iA2);
        margin-top: var(--iA1);
        margin-bottom: var(--iA1);
        width: 89px;
        .SE_0006_listIcon {
          width: 89px;
          height: 89px;
        }
      }

      .SE_0006_etaCol2 {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-end;
        margin-left: var(--iA1);
        margin-top: var(--iA1);
        margin-bottom: var(--iA1);
        width: 132px;
        .SE_0006_unit {
          width: 38px;
        }
        .SE_0006_etaCol2_1 {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          margin-bottom: 5px;
        }

        .SE_0006_etaCol2_2 {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          .SE_0006_unit {
            &--am {
              margin-bottom: 9px;
            }
            &--pm {
              margin-bottom: 2px;
            }
          }
        }
      }

      .SE_0006_etaCol3 {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: flex-end;
        margin-left: var(--iA1);
        margin-right: var(--iA2);
        margin-top: var(--iA1);
        margin-bottom: var(--iA1);
        width: 152px;
        .SE_0006_unit {
          width: 30px;
        }
        .SE_0006_etaCol3_1 {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          margin-bottom: 5px;
        }

        .SE_0006_etaCol3_2 {
          display: flex;
          flex-direction: row;
          align-items: flex-end;
          height: var(--lineHeightPrio2);
          .SE_0006_textAfterListIcon {
            margin-left: var(--zA8);
          }
          .SE_0006_listIcon {
            align-self: center;
          }
          .SE_0006_textPrio2 {
            display: flex;
            justify-content: flex-end;
          }
        }
      }
    }
  }
}
