/* CLASSIFICATION: CONFIDENTIAL */

:global(#root) > .UE_0002_StatusBar {
  position: relative;
  background-color: rgb(var(--audiBlack));
  margin: 0px;
  height: var(--minTouchSize);
  width: 100%;
}

:global(#root) > .UE_0002_StatusBar.b10Screen {
  height: calc(956px - var(--climateBarHeight));
  width: var(--directAccessWidth);
}

:root {
  --statusBar_buttonsWidth: 65px;
}

.UE_0002_StatusBar {
}

.statusBar {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  background-clip: content-box;
  background-color: rgb(var(--audiBlack));
}

.statusBarWarningIcon {
  position: absolute;
  width: 40px;
  height: 40px;
  left: 62px;
  top: 42px;
  background-color: rgba(var(--boundingBoxGreen));
}
.statusBarWarningIcon_warning {
  position: absolute;
  width: 40px;
  height: 40px;
  left: 72px;
  top: 32px;
  background-color: rgba(var(--boundingBoxGreen));
}

.statusBarWarningIcon_warning .listIcon,
.statusBarWarningIcon .listIcon {
  margin: 0;
}

.dashboardButton,
.statusButton {
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  top: 0;
  left: 0;
  position: relative;
  z-index: 2;
}

.button .touchIcon {
  width: var(--statusBar_buttonsWidth);
}

.dashboardIcon {
  background-image: url(../../../../../Globals/icons/IconMaster/CID-Icons/Detail/E775_main_menue_home_DA.svg);
  background-size: 100%;
}

.listIcon {
  margin-left: var(--iA1);
}
.listIcon:not(:first-child) {
  margin-left: calc(var(--iA1) + 5px);
}

.iconsContainer {
  position: relative;
  height: 100%;
  display: flex;
  flex: 1;
  align-items: center;
  max-width: calc(
    (
        100% -
          (
            var(--assistantWidthStatusbar) + (var(--minTouchSize) * 2) +
              (var(--iA1) * 2)
          )
      ) / 2
  );
}

/**************** iconsContainer Left *************************/

.iconsContainer.left {
  justify-content: flex-start;
  flex-direction: row;
  margin-left: calc(var(--iA1) + var(--minTouchSize));
}

.iconsContainer.left .leftTextContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.iconsContainer.left .leftTextContainer .listIcon {
  margin-left: var(--iA1);
}

.iconsContainer.left.shownText .text {
  margin-left: var(--cA1);
}

.iconsContainer.left.shownText .textIcon {
  display: flex !important;
}

/***************** centerContainer *************************/

.centerContainer {
  width: var(--assistantWidthStatusbar);
  display: flex;
  justify-content: center;
}

.centerContainer .assistantContent,
.statusBar.assistantVersion .centerContainer .assistantContent {
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../img/AI_logo.png);
}

.statusBar.callVersion .centerContainer .callContent {
  display: block;
  width: var(--minTouchSize);
  height: 100%;
}

/**************** iconsContainer Right *************************/

.iconsContainer.right {
  justify-content: flex-end;
  margin-right: var(--iA1);
}

/**************** chargingTime *******************/

.chargingTime {
  top: 21px;
  left: 789px;
  display: flex;
  justify-content: center;
  margin-left: var(--cA1);
}

.temperature,
.aqi {
  margin-left: var(--cA1);
}

.aqi {
  display: flex;
  width: 103px;
}

.aqi p {
  margin-top: 2px;
  max-width: 60px;
}

.aqi .listIcon {
  margin-left: unset;
  margin-right: 4px !important;
}

.chargingTime .text {
  margin-left: var(--zA1);
  margin-right: var(--zA1);
  margin-top: 2px;
}

.chargingTime .time_am {
  margin-top: 5px;
}

.chargingTime .time_pm {
  margin-top: 12px;
  margin-left: 1px;
}

.chargingTime .hourMins {
  margin-top: 2px;
  margin-left: 1px;
}

.chargingTime .hourMins {
  display: none;
  max-width: 100px;
}

.chargingTime.am_pm.am .time_am {
  display: inline-block;
}

.chargingTime.am_pm.pm .time_pm {
  display: inline-block;
}

.chargingTime.hourMins .hourMins {
  display: inline-block;
}

/*------------------------------------------- SETTINGS ---------------------------------------------*/

.settings {
  position: relative;
  top: 100px;
  margin: 20px;
}

.settings select {
  border: 0;
  background-color: white;
  height: 30px;
  width: 35px;
  border-radius: 4px;
  margin-right: 20px;
  font-size: 16px;
}

.settings .textPrio3 {
  margin-right: 20px;
}

.settings .showLeftTextLabel {
  display: block;
  margin-top: 15px;
}

.settings .textSettingLabel.cssDocu {
  margin: 0;
}

.settings .AMPM_config {
  margin-top: 10px;
}

/*------------------------------------------- B10 ---------------------------------------------*/

.b10Screen .statusBar {
  display: inline;
  position: absolute;
}

.b10Screen .centerContainer {
  position: absolute;
  height: var(--minTouchSize);
  align-items: center;
  justify-content: center;
  top: 0px;
  right: 25px;
}

.b10Screen .iconsContainer.bottom {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  bottom: 40px;
  right: 35px;
  height: calc(var(--listIconSize) * 2 + 20px);
  width: calc(var(--listIconSize) * 4 + (20px * 3));
  max-width: 220px;
}

.b10Screen .iconsContainer.bottom .iconsLine {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}
.b10Screen .iconsContainer.bottom .iconsLine:nth-child(2) {
  margin-top: 20px;
}

.b10Screen .iconsContainer.bottom .iconsLine .listIcon:first-child {
  margin-left: unset;
}

.b10Screen .iconsContainer.bottom .listIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  margin-left: 20px;
  padding: unset;
}

.b10Screen .iconsContainer.left {
  width: var(--listIconSize);
  height: fit-content;
  flex-direction: column;
  left: 60px;
  top: 160px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: none;
  max-width: unset;
  margin-left: unset;
}
.b10Screen .iconsContainer.left .listIcon {
  width: var(--listIconSize);
  height: var(--listIconSize);
  margin-top: 20px;
  margin-left: unset;
  padding: unset;
}
.b10Screen .iconsContainer.left .listIcon:first-child {
  margin-top: unset;
}

.b10Screen .containerAQI {
  display: flex;
  flex-direction: row;
  max-width: calc(var(--minTouchSize) * 2 + 8px);
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 140px;
  bottom: 200px;
  width: 246px;
}

.b10Screen .containerAQI .aqi {
  margin-left: unset;
}

.b10Screen .profileButton {
  position: absolute;
  bottom: 0px;
  left: 22px;
  height: var(--minTouchSize);
  width: var(--minTouchSize);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.b10Screen .profile {
  position: relative;
  background-image: url('../../../../../Globals/icons/IconMaster/CID-Icons/Special/E0AB_002_phone_generic_contactimage.svg');
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  height: 70px;
  width: 70px;
}

.directAccess {
  background-color: unset;
}

@media only screen and (min-width: 2219px) {
  .settings {
    position: relative;
    top: 1000px;
    margin: 20px;
  }
}
