/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --listHeaderExtendedButtons_height: 47px;
  --hudListItemHeight: 46px;
}

:global(#root) > .T_0103_ListVertical {
  height: 100%;
  background: rgb(var(--bodyColorGrey));
  height: 800px;
  width: 100%;
  margin: 0;
}

.T_0103_ListVertical.fullHeight {
  height: 100%;
}

.listContainer {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  overflow: hidden;
  flex: 1;
}

.fpk .listContainer {
  transition: var(--AG_0401_Scroll_List_Vertical);
  top: 0px;
}

.movingContainer {
  position: relative;
  transition:
    transform var(--animationScrollDuration) var(--animationCurve),
    top var(--animationScrollDuration) var(--animationCurve);
  transform: translateY(0px);

  top: 0px;
}

.listWrapper {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
}

.elementWrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.fadeOutTop {
  -webkit-mask-image: var(--listStartFadeout);
}

.fadeOutBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.fadeOutTopBottom {
  -webkit-mask-image: var(--listStartEndFadeout);
}

.T_0103_ListVertical.ListHeaderExtendedButtons .listContainer.fadeOutBottom,
.T_0103_ListVertical.ListHeaderExtendedButtons .listContainer.fadeOutTop,
.T_0103_ListVertical.ListHeaderExtendedButtons .listContainer.fadeOutTopBottom,
.T_0103_ListVertical.stickyElements .listContainer.fadeOutBottom,
.T_0103_ListVertical.stickyElements .listContainer.fadeOutTop,
.T_0103_ListVertical.stickyElements .listContainer.fadeOutTopBottom {
  -webkit-mask-image: var(--listEndFadeout);
}

.list {
  display: inline;
  width: calc(100% - 18px);
  height: calc(100% - 1px);
  list-style-type: none;
  padding: 0 var(--listVerticalPadding);
  margin: 0;
  overflow-y: auto;
  overflow-x: hidden;
}
.listContainer.hideScrollBar .list {
  width: calc(100% - 20px);
}

.listContainer.hideScrollBar .list::-webkit-scrollbar {
  display: none;
}

.list
  li:not(.sticky):not(.SE_0061_ListHeaderExtendedButtons):not(
    :global(.stickyHeaderStyling)
  ) {
  position: relative;
  width: calc(100% - 37px);
  margin-left: 22px;
  border-bottom: 1px solid var(--listSeparatorLineColor);
}

.listContainer .list.fpk li {
  position: relative;
  width: calc(100% - 24px) !important;
  margin-left: 15px !important;
  border-bottom: 1px solid var(--listSeparatorLineColor);
}

.listContainer .list.fpk li:first-child {
  margin-top: 10px;
}

.dark li {
  border-bottom: 1px solid rgba(0, 0, 0, 0.8);
}

.list li.wrapper {
  height: auto;
}

.list li .UE_1009_C2Cursor {
  position: absolute;
  height: 100%;
  width: 100%;
}

.UE_6100_C1Cursor {
  display: none;
}

.fpk .UE_6100_C1Cursor {
  display: block;
}

.UE_6100_C1Cursor,
.UE_1009_C2Cursor {
  position: absolute;
  width: 100%;
  height: var(--FPKListItemHeight);
}

.T_0103_ListVertical.fpkSecondLine .UE_6100_C1Cursor {
  height: var(--minTouchSize);
}

.T_0103_ListVertical.hudVariant
  .UE_1002_ListItemStandard.singleLine.fpk
  .listElement,
.T_0103_ListVertical.hudVariant .UE_6100_C1Cursor {
  height: var(--hudListItemHeight);
}

.T_0103_ListVertical .listContainer .list li:last-child {
  border-bottom: 0;
}

.T_0103_ListVertical .listContainer.arHUD .list li:last-child {
  border-bottom: 0;
  margin-bottom: 10px;
}

.T_0103_ListVertical .listContainer .list li.complex {
  padding: var(--lA1);
  width: calc(100% - 2 * var(--lA1) - 37px);
  min-height: 140px;
}

.T_0103_ListVertical .listContainer .list li.headline {
  min-height: auto;
  border-bottom: 0;
}

.list li.UE_1007_ListHeader {
  height: 100px !important;
}

.decorator {
  position: relative;
  height: var(--decoratorHeight);
  width: var(--decoratorWidth);
  align-self: center;
  margin: 0 auto;
  background-color: rgba(var(--boundingBoxGreen));
}
.decorator {
  display: none;
}

.fpk .listWrapper > .decorator {
  display: none;
}

/*----------------------------------------- SOCBAR LIST ITEM -----------------------------------------------*/
.SE_0002_SOCBargraph.complex {
  display: flex;
  align-items: center;
}

.SE_0002_SOCBargraph.complex.fullScreenDIalogOneInf {
  display: flex;
  align-items: unset;
  flex-direction: column;
  justify-content: flex-start;
}

.SE_0002_SOCBargraph.complex > .textPrio1 {
  flex: 1;
  margin-right: var(--cA1);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.SE_0002_SOCBargraph.complex.fullScreenDIalogOneInf > .textPrio1 {
  flex: unset;
  margin-right: unset;
}

.SE_0002_SOCBargraph.complex .SE_0002_SOCBargraph {
  position: absolute;
  width: calc(100% - 48px);
  left: 24px;
  top: -8px;
}

/*----------------------------------------- HALF LIST -----------------------------------------------*/
.halfList .decorator {
  display: block;
}

.halfList .list {
  width: calc(50% - 30px);
}

/*----------------------------------------- listheadline LIST -----------------------------------------------*/

.listheadline {
  overflow: hidden;
  width: calc(100% - 26px - var(--lA1) - 2 * var(--listVerticalPadding));
  margin-left: calc(var(--listVerticalPadding) + 15px + var(--lA1));
  margin-top: var(--lA1);
  margin-bottom: var(--lA1);
}

/*----------------------------------------- SMALL FPK LIST -----------------------------------------------*/
/* .fpk .list .UE_0601_Slider {
  display: none;
} */
.fpk .list .UE_1007_ListHeader {
  display: none;
}
.fpk .list .UE_1005_ListFolding {
  display: none;
}
.fpk .list .UE_0201_ToggleButtonGroup {
  display: none;
}
.fpk .list .UE_1003_ListItemButtons {
  display: none;
}

/*----------------------------------------- FPK Cursor Container ------------------------------------------*/
.fpk .cursorContainer {
  position: absolute;
  width: 100%;
  height: calc(100% - 1px);
  overflow-y: scroll;
}

.fpk .cursorContainer::-webkit-scrollbar {
  display: none;
}

.fpk .cursorContainer .cursorWrapper {
  position: absolute;
  height: calc(10px + 7 * (var(--FPKListItemHeight) + 1px) - 1px);
  width: calc(100% - 2 * 24px + 2px);
  left: 24px;
}

.fpk .cursorContainer .UE_6100_C1Cursor {
  width: calc(100% - 45px);
  left: 24px;
  transition: var(--AG_0401_Scroll_List_Vertical);
  top: 10px;
}

.fpk .wrapper .UE_6100_C1Cursor,
.fpk .wrapper .UE_1009_C2Cursor {
  height: var(--FPKListItemHeight);
}

/*---------- UE_0701_Loader ----------*/
.elementWrapper > .loaderContainer {
  position: relative;
  width: 100%;
  height: var(--minTouchSize);
}

.elementWrapper > .loaderContainer .UE_0701_Loader {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.T_0103_ListVertical.Loader .elementWrapper > .loaderContainer {
  display: block;
}

/*---------- SE_0061_ListHeaderExtendedButtons ----------*/
.list li.SE_0061_ListHeaderExtendedButtons {
  position: relative;
  width: calc(100% + var(--lA1));
  height: var(--listHeaderExtendedButtons_height);
  margin: 0;
  margin-left: calc(-1 * var(--listVerticalPadding));
  padding: 0 0 var(--zA5) 0;
  border: none;
  z-index: 10;
  transition: height 0.25s ease-in-out;
}

li.SE_0061_ListHeaderExtendedButtons.sticky {
  height: var(--minTouchSize);
}

/*---------- SE_0054_IntelligentCallStack ----------*/
.intelligentCallStackContainer {
}

li.intelligentCallStackContainer {
  display: grid;
  width: 100%;
  margin-bottom: 16px;
  grid-template-columns: repeat(3, 1fr) 9px;
  grid-gap: 15px;
  border-bottom: none;
  box-sizing: border-box;
}

.intelligentCallStackContainer.twoStacks {
  grid-template-columns: repeat(2, 1fr) 9px;
}

.intelligentCallStackContainer.oneStack {
  grid-template-columns: repeat(1, 1fr) 9px;
}
/* 
.intelligentCallStackContainer.twoStacks
  .SE_0054_IntelligentCallStack:last-of-type,
.intelligentCallStackContainer.oneStack
  .SE_0054_IntelligentCallStack:last-of-type,
.listContainer
  .intelligentCallStackContainer.oneStack
  .SE_0054_IntelligentCallStack:first-of-type {
  display: none;
} */

.SE_0054_IntelligentCallStack {
  min-width: 0;
}

/*UE_0203_ToggleTabGroup*/
.T_0103_ListVertical.toggleTabs
  .listContainer
  .list
  .UE_0201_ToggleButtonGroup.UE_0203_ToggleTabGroup {
  position: relative;
  width: calc(100% + var(--lA1));
  height: var(--minTouchSize);
  margin: 0;
  margin-left: calc(-1 * var(--listVerticalPadding));
  padding: 0 0 var(--zA5) 0;
  z-index: 10;
  border: none;
  display: block;
}

.T_0103_ListVertical.toggleTabs
  .listContainer
  .list
  .UE_0201_ToggleButtonGroup.UE_0203_ToggleTabGroup
  > :global(.stickyHeaderStyling) {
  display: list-item;
  position: relative;
  z-index: 10;
  width: 100%;
}

.T_0103_ListVertical.toggleTabs
  .listContainer
  .list
  .UE_0201_ToggleButtonGroup.UE_0203_ToggleTabGroup
  :global(.stickyHeaderStyling) {
  border-top: none;
}

.T_0103_ListVertical.toggleTabs
  .UE_0201_ToggleButtonGroup.UE_0203_ToggleTabGroup
  .uiState {
  height: 100%;
}

/* sticky elements */
.T_0103_ListVertical.stickyElements
  .listContainer
  .list
  .UE_1007_ListHeader
  .stickyEl {
  display: flex;
  position: relative;
  z-index: 10;
  margin: 0;
  margin-left: calc(
    -1 * (var(--listVerticalPadding) + var(--iA1)) + var(
        --opticalTouchReduction
      ) / 2
  );
  margin-right: var(--opticalTouchReduction);
  width: calc(100% + var(--listVerticalPadding));
  border-top: 1px solid rgba(var(--audiWhite), 0.3);
}

.T_0103_ListVertical.stickyElements :global(.stickyHeaderStyling.fixed),
.T_0103_ListVertical.stickyElements
  .listContainer
  .list
  .UE_1007_ListHeader:global(.fixed)
  .stickyEl,
.T_0103_ListVertical.toggleTabs
  .listContainer
  .list
  .UE_0201_ToggleButtonGroup.UE_0203_ToggleTabGroup
  > :global(.stickyHeaderStyling) {
  background-color: rgba(var(--audiBlack), 0.98);
}

/* sticky elements */
.T_0103_ListVertical.stickyElements .listContainer .list .UE_1007_ListHeader {
  position: relative;
  z-index: 10;
  margin-left: var(--iA1);
  width: 100%;
}
.T_0103_ListVertical.stickyElements .listContainer .list .UE_1007_ListHeader {
  height: auto;
}

.T_0103_ListVertical.stickyElements :global(.stickyHeaderStyling) {
  display: list-item;
  position: relative;
  z-index: 10;
  margin: 0;
  margin-left: calc(
    -1 * var(--listVerticalPadding) + var(--opticalTouchReduction) / 2
  );
  width: calc(100% + var(--lA1) - var(--opticalTouchReduction));
}

.T_0103_ListVertical
  .listContainer
  .list
  li.headline:not(.stickyHeaderStyling) {
  min-height: auto;
  border-bottom: 0;
}

.UE_1002_ListItemStandard.dragState {
  margin-left: calc(-1 * var(--listVerticalPadding)) !important;
  width: calc(100% + var(--lA1)) !important;
}

/*---------- settings ----------*/
.settings {
  position: relative;
  top: 40px;
}

.settings .textPrio2 {
  margin-bottom: 10px;
}

@media only screen and (min-width: 2219px) {
  li.intelligentCallStackContainer {
    grid-template-columns: auto auto auto;
    border-bottom: unset !important;
  }

  .intelligentCallStackContainer.twoStacks {
    grid-template-columns: auto auto;
  }

  .intelligentCallStackContainer.oneStack {
    grid-template-columns: auto;
  }
}
