/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --tachometerWidth: 403px;
}

:global(#root) > .UE_6050_Motoroeltemperatur {
  left: 10px;
}

.UE_6050_Motoroeltemperatur {
  position: absolute;
  width: var(--tachometerWidth);
  height: 70px;
}

.UE_6050_Motoroeltemperatur.UE_6050_01_Celsius {
}

.tachometerKlein {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}

.tachometerKlein .rpmContainer {
  position: absolute;
  width: var(--tachometerWidth);
  top: 34px;
  left: 0px;
}

/*     scale     */
.tachometerKlein .scale {
  position: absolute;
  top: 10px;
  left: 0px;
  width: 100%;
}

.UE_6050_Motoroeltemperatur .tachometerKlein .scale .digit1 {
  position: absolute;
  left: -2px;
}

.UE_6050_Motoroeltemperatur .tachometerKlein .scale .digit2 {
  position: absolute;
  left: 180px;
}

.UE_6050_Motoroeltemperatur .tachometerKlein .scale .digit3 {
  position: absolute;
  right: -1px;
}
.UE_6050_Motoroeltemperatur
  .tachometerKlein
  .scale
  .SpritesheetDigits.digit3
  .digit {
  background-color: rgb(var(--warningRed));
}

.tachometerKlein .legend {
  position: absolute;
  height: 38px;
  width: 50px;
  top: 47px;
  left: 182px;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-image: url('../../../../../Globals/icons/IconMaster/FID-Icons/fpkStatus/E13F_Oeltemperatur.png');
  background-color: rgb(var(--audiWhite));
}

/*--------------------------- Inactive State ----------------------------------*/
.UE_6050_Motoroeltemperatur.UE_6050_00_Off_Celsius
  .tachometerKlein
  .scale
  .SpritesheetDigits
  .digit,
.UE_6050_Motoroeltemperatur.UE_6050_03_Off_Fahrenheit
  .tachometerKlein
  .scale
  .SpritesheetDigits
  .digit,
.UE_6050_Motoroeltemperatur.UE_6050_00_Off_Celsius
  .tachometerKlein
  .scale
  .SpritesheetDigits
  .unit,
.UE_6050_Motoroeltemperatur.UE_6050_03_Off_Fahrenheit
  .tachometerKlein
  .scale
  .SpritesheetDigits
  .unit,
.UE_6050_Motoroeltemperatur.UE_6050_00_Off_Celsius .tachometerKlein .legend,
.UE_6050_Motoroeltemperatur.UE_6050_03_Off_Fahrenheit .tachometerKlein .legend {
  background-color: rgb(var(--bargraphInactive));
}

/*     bargraph     */
.UE_6050_Motoroeltemperatur .tachometerKlein .Bargraph {
  position: absolute;
  top: 26px;
  width: var(--tachometerWidth);
}

/*---------- UE_6050_02_Fahrenheit Variant ---------- */
.UE_6050_Motoroeltemperatur.UE_6050_03_Off_Fahrenheit
  .tachometerKlein
  .scale
  .digit2,
.UE_6050_Motoroeltemperatur.UE_6050_02_Fahrenheit
  .tachometerKlein
  .scale
  .digit2 {
  position: absolute;
  left: 178px;
}
.UE_6050_Motoroeltemperatur.UE_6050_03_Off_Fahrenheit
  .tachometerKlein
  .scale
  .digit1,
.UE_6050_Motoroeltemperatur.UE_6050_02_Fahrenheit
  .tachometerKlein
  .scale
  .digit1 {
  position: absolute;
  left: -4px;
}

/*---------- Bordcomputer Config ---------- */
.UE_6050_Motoroeltemperatur .tachometerKleinConfig {
  position: absolute;
  top: 200px;
  color: white;
}

.UE_6050_Motoroeltemperatur .tachometerKleinConfig .level {
  margin-top: 20px;
}

.settings {
  top: 30%;
  position: absolute;
}
