:global(#root) > .T_00010_BasicLayoutSettings {
  margin: 0;
  width: 100%;
  height: 100%;
}

.T_00010_BasicLayoutSettings {
}
.settings {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--ambientBackground1));
  background-image: var(--backgroundSpot);
}
.background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--audiBlack));
}

.btnReset {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  height: 33px;
  width: 136px;
}

.btnReset button {
  background-color: rgba(var(--ambientcolor1), 0.7);
  border: none;
  color: rgb(var(--audiWhite));
  letter-spacing: 0.5px;
  cursor: pointer;
  transition: 0.3s;
  border-radius: 5px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.btnReset button:hover {
  background-color: rgb(var(--audiBlack));
  border: 1px solid rgba(var(--ambientcolor1), 1);
  color: rgba(var(--ambientcolor1), 1);
}

.listContainer {
  position: relative;
  width: 100%;
  margin-top: 75px;
}
.header {
  position: fixed;
  width: 100%;
  height: 75px;
  z-index: 9999;
  top: 0;
  background-color: rgb(var(--ambientBackground1));
}
.header .background {
  position: absolute;
  height: 100%;
  width: calc(100% - 2px);
  left: 1px;
}

.separator {
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: rgba(var(--audiWhite), 0.7);
  box-shadow: 0 1px 4px 2px rgba(var(--audiWhite), 0.2);
  bottom: 0px;
  z-index: 1;
}

.sectionLining {
  width: 100%;
  height: calc(100%);
  position: absolute;
  opacity: var(--sectionLiningOpacity);
  top: 3px;
  display: block;
  background-image: var(--sectionLining);
  z-index: 0;
}
.list {
  position: relative;
  background-color: rgb(var(--audiBlack));
  display: flex;
  flex-direction: column;
}
.line {
  position: relative;
  height: fit-content;
  width: calc(100% - 40px);
  border-bottom: 1px solid rgba(var(--audiGrey), 0.8);
  display: flex;
  padding: 0 20px;
  min-height: 75px;
}

.containerRight {
  height: 100%;
  width: 60%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  flex-shrink: 0;
  min-height: 75px;
  justify-content: flex-start;
}

.containerRight > label,
.containerRight > .select_box,
.containerRight > .btn,
.containerRight > select {
  margin-left: 30px !important;
  margin-top: 10px;
  margin-bottom: 10px;
}

.T_00010_BasicLayoutSettings .containerLeft {
  width: 40%;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  padding-right: 20px;
  box-sizing: border-box;
}

.T_00010_BasicLayoutSettings .containerLeft::after {
  content: '';
  height: calc(100% - 20px);
  width: 1px;
  background-color: rgba(var(--audiGrey), 0.8);
  position: absolute;
  left: calc(40% + 5px);
}

.T_00010_BasicLayoutSettings .containerRight label input[type='checkbox'],
.T_00010_BasicLayoutSettings .containerRight label input[type='radio'],
.T_00010_BasicLayoutSettings .containerRight label input[type='number'] {
  margin: 0 15px 0 3px;
}

.T_00010_BasicLayoutSettings .settings .textPrio1 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: white;
  width: 100%;
  text-align: center;
}
.T_00010_BasicLayoutSettings .settings label {
  display: flex;
  align-items: center;
  color: white;
  margin-left: 30px;
  height: 50px;
  max-width: calc(100% - 30px);
  order: 1;
}

.T_00010_BasicLayoutSettings .settings label.labelInputMethods {
  white-space: nowrap;
}

.T_00010_BasicLayoutSettings .settings .list .line .textPrio2 {
  position: relative;
  font-size: 25px;
  font-weight: 600;
  letter-spacing: -0.7px;
}

.T_00010_BasicLayoutSettings .settings .dropDown.hidden,
.T_00010_BasicLayoutSettings .settings label.hidden,
.T_00010_BasicLayoutSettings .settings .routeOverviewCheckoboxes.hidden {
  display: none;
}
.T_00010_BasicLayoutSettings .settings .checkBox {
  position: relative;
  margin-right: 8px;
}

.T_00010_BasicLayoutSettings .settings .routeOverviewCheckoboxes {
  display: flex;
  flex-direction: row;
}

.T_00010_BasicLayoutSettings .settings .dropDown,
.T_00010_BasicLayoutSettings .settings .btn {
  width: 75px;
  height: 50px;
  background-color: rgb(var(--audiWhite));
  border-radius: 5px;
  flex-shrink: 0;
  cursor: pointer;
}

.containerRight > .btn {
  border: none;
  order: 0;
  transition: 0.3s;
}

.containerRight > .btn.btn_active {
  background-color: rgba(var(--ospGreen), 0.6);
  color: rgb(var(--audiWhite));
}

.T_00010_BasicLayoutSettings .settings .dropDown {
  width: fit-content;
  max-width: 550px;
  font-size: 15px;
  padding: 5px;
  cursor: pointer;
  background-color: rgb(var(--audiWhite));
  order: 1;
}

.T_00010_BasicLayoutSettings .settings .dropDown option {
  font-size: 15px;
  background-color: rgb(var(--audiWhite));
}

.T_00010_BasicLayoutSettings .settings .dropDown:disabled,
.T_00010_BasicLayoutSettings .settings .btn:disabled {
  background-color: rgba(var(--audiGrey), 0.9);
}
.T_00010_BasicLayoutSettings .settings .btn {
  margin-left: 10px;
}
.T_00010_BasicLayoutSettings .settings .btn.FullScreenDialog {
  margin-right: 10px;
}
.T_00010_BasicLayoutSettings .settings .btn.Dialog {
  margin-right: 10px;
}
.T_00010_BasicLayoutSettings .settings .btn.color1,
.T_00010_BasicLayoutSettings .settings .btn.color2 {
  margin-right: 10px;
}

.T_00010_BasicLayoutSettings .settings .btn.color3 {
  margin-right: 50px;
}

.T_00010_BasicLayoutSettings .settings .range {
  max-width: 650px;
  min-width: 288px;
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  order: 1;
}

.T_00010_BasicLayoutSettings .dropDown {
  margin-left: 10px;
}

.T_00010_BasicLayoutSettings .selectedAmbientColor {
  height: 50px;
  width: 50px;
  min-width: 50px;
  right: 20px;
  border-radius: 5px;
  background-color: rgb(var(--SE_5006_00_neutral));
  margin-left: 30px;
  margin-top: 10px;
  margin-bottom: 10px;
  order: 2;
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_00_neutral'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_00_neutral {
  background-color: rgb(var(--SE_5006_00_neutral));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_02_orange'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_02_orange {
  background-color: rgb(var(--SE_5006_02_orange));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_04_rot'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_04_rot {
  background-color: rgb(var(--SE_5006_04_rot));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_06_violett'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_06_violett {
  background-color: rgb(var(--SE_5006_06_violett));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_08_blau'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_08_blau {
  background-color: rgb(var(--SE_5006_08_blau));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_10_gruen'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_10_gruen {
  background-color: rgb(var(--SE_5006_10_gruen));
}
.T_00010_BasicLayoutSettings
  .dropDownAmbientColor
  option[value='SE_5006_12_rs'],
.T_00010_BasicLayoutSettings .selectedAmbientColor.SE_5006_12_rs {
  background-color: rgb(var(--SE_5006_12_rs));
}

.T_00010_BasicLayoutSettings .settings label + .dropDown,
.T_00010_BasicLayoutSettings .settings label + .btn {
  margin-left: 30px;
}

.containerRight > .select_box {
  order: 1;
}

@media only screen and (max-width: 1000px) {
  .T_00010_BasicLayoutSettings .settings .dropDown {
    max-width: 250px;
    width: 250px;
  }
}

@media only screen and (max-width: 789px) {
  .T_00010_BasicLayoutSettings .containerLeft {
    width: 50%;
  }

  .T_00010_BasicLayoutSettings .containerLeft::after {
    left: calc(50% - 1px);
  }

  .containerRight {
    width: 50%;
  }

  .T_00010_BasicLayoutSettings .settings .range {
    width: 100%;
    min-width: unset;
  }
}

@media only screen and (max-width: 575px) {
  .listContainer {
    margin-top: 115px;
  }

  .header {
    height: 115px;
  }

  .T_00010_BasicLayoutSettings .settings .header .textPrio1 {
    top: 10px;
    transform: unset;
  }

  .btnReset {
    right: 50%;
    transform: translateX(50%);
    top: unset;
    bottom: 15px;
  }

  .line {
    flex-direction: column;
  }

  .containerRight {
    width: 100%;
    flex-direction: column;
    min-height: unset;
  }

  .T_00010_BasicLayoutSettings .containerLeft {
    width: 100%;
    justify-content: center;
    margin-top: 10px;
    padding-right: 0;
    box-sizing: unset;
  }

  .T_00010_BasicLayoutSettings .containerLeft::after {
    content: unset;
  }

  .T_00010_BasicLayoutSettings .settings label {
    max-width: 100%;
  }

  .containerRight > label,
  .containerRight > .select_box,
  .containerRight > .btn,
  .containerRight > select {
    margin-left: 0px !important;
  }

  .T_00010_BasicLayoutSettings .selectedAmbientColor {
    margin-left: 0;
    margin-top: 10px;
    margin-bottom: 0;
  }

  .T_00010_BasicLayoutSettings .settings .range {
    width: 100%;
    min-width: unset;
    margin-left: 0;
  }
}

@media only screen and (min-width: 1255px) {
  .T_00010_BasicLayoutSettings .containerLeft {
    width: 30%;
  }

  .T_00010_BasicLayoutSettings .containerLeft::after {
    left: calc(30% + 7px);
  }

  .containerRight {
    width: 70%;
  }
}
