/* CLASSIFICATION: CONFIDENTIAL */

:root {
}

/*-------------------- standalone properties --------------------*/

:global(#root) > .UE_0505_Wordproposals {
  width: 100%;
  height: var(--keyboardLineHeight);
}

/*-------------------- .UE_0505_Wordproposals --------------------*/

.UE_0505_Wordproposals {
  width: 100%;
  height: var(--keyboardLineHeight);
}

.background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgb(var(--ambientBackground1));
}

.listContainer {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  list-style-type: none;
  border-bottom: 1px solid rgb(var(--audiGrey));
  border-left: 1px solid rgb(var(--audiGrey));
  border-right: 1px solid rgb(var(--audiGrey));
  display: flex;
}

.button {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex: 1;
}

.button:before {
  content: '';
  position: absolute;
  width: 1px;
  left: -1px;
  height: var(--keyboardSeparatorHeight);
  background: rgb(var(--audiGrey));
}

.button:not(:last-child) .uiState.fullSize:active {
  width: calc(100% + 10px);
}

.textContainer {
  position: relative;
  overflow-x: hidden;
  white-space: nowrap;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex: 1;
}

.textContainer p {
  text-align: center;
  align-self: center;
  overflow: hidden;
  width: 100%;
  margin: 0 var(--textButtonMargin);
}

.button:first-child:before {
  background: none;
}

.twoWordsProposal .button {
  max-width: calc(100% / 2);
}

.threeWordsProposal .button {
  max-width: calc(100% / 3);
}
