/* CLASSIFICATION: CONFIDENTIAL */
:root {
}

:global(#root) > .ST_0010_TimePickerOverlay {
  position: absolute;
  height: auto;
  width: auto;
  margin: 10px;
}

.ST_0010_TimePickerOverlay {
  height: 100%;
}

.timePicker {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.ST_0010_TimePickerOverlay .popupStyling {
  justify-content: unset;
}

.timePickerContent {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: var(--popupContentPadding);
}

.pickerWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: calc(50% - (var(--popupContentPadding) / 2));
  justify-content: center;
  flex: 1;
}

.pickerWrapper:first-child {
  margin-right: var(--popupContentPadding);
}

.timePickerHeadline {
  margin-bottom: var(--zA4);
}

.pickerContainer {
  position: relative;
  display: flex;
  height: auto;
  align-items: center;
  justify-content: space-between;
}

.settings {
  position: absolute;
  top: 790px;
  width: 50%;
}

/* ------------------------- UE_1301_DataPicker --------------------------*/

.UE_1301_DataPicker {
  width: var(--dataPickerWidthTime);
  height: var(--dataPickerHeight);
}

.UE_1301_DataPicker .dataPicker .list {
  z-index: 5;
}

/* ------------------------- UE_0401_OPERATIONPANEL --------------------------*/
.UE_0401_OperationPanel {
  width: 100%;
  height: var(--minTouchSize);
  position: relative;
}
