:global(#root) > .UE_60103_60104_Drehzahlmesser_DynamicPlus {
  position: absolute;
  background-color: black;
  margin: 0;
  width: var(--screenWidthFPK);
  height: var(--screenHeightFPK);
}

.UE_60103_60104_Drehzahlmesser_DynamicPlus .testTube {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
}
.UE_60103_60104_Drehzahlmesser_DynamicPlus.gb .testTube {
  background-image: url('../img/test_tube_GB.png');
}
.UE_60103_60104_Drehzahlmesser_DynamicPlus.kb .testTube {
  background-image: url('../img/test_tube_KB.png');
}

