/* CLASSIFICATION: CONFIDENTIAL */

:root {
  --stepSize: 30px;
  --skipSize: calc(var(--stepSize) / 3 * 2);
  --processingStepSize: 40px;
  --connectorStrength: 1px;
  --checkmarkHeight: calc(var(--stepSize) / 2);
  --checkmarkWidth: calc(var(--stepSize) / 4);
  --slashHeight: calc(var(--stepSize) / 8 * 5);
  --dotSize: 32px;
}

:global(#root) > .UE_0704_WizardBar {
  height: 100%;
  width: 100%;
  margin: 0;
  background-color: rgb(var(--lightGrey));
}

.UE_0704_WizardBar {
}

.wizardBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.headline {
  margin-bottom: var(--hA1);
}

/*-----------STEPS------------*/
.stepContainer {
  display: flex;
  height: calc(var(--processingStepSize) + 4px);
  width: 100%;
  align-items: center;
}

.step {
  display: flex;
  justify-content: center;
  align-items: center;
  height: var(--stepSize);
  width: var(--stepSize);
  border: 2px solid rgb(var(--audiWhite));
  border-radius: 50%;
}

.step.processing {
  height: var(--processingStepSize);
  width: var(--processingStepSize);
}

.step:nth-child(n + calc(2 * var(--stepNum))) {
  display: none;
}

.connector {
  height: var(--connectorStrength);
  flex: 1;
  max-width: calc(
    (
        100% - 4 * (var(--stepSize) + 4px) - (var(--processingStepSize) + 4px) -
          8px
      ) / 4
  );
  background-color: rgb(var(--audiWhite));
  margin: 0 1px 0 1px;
}

/*-----------MARKER------------*/
.step .checkmark {
  display: inline-block;
  margin-top: -5px;
}
.step .checkmark:after {
  content: '';
  display: block;
  width: var(--checkmarkWidth);
  height: var(--checkmarkHeight);
  border: solid rgba(var(--audiWhite), 1);
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.step .slash {
  display: inline-block;
  position: relative;
  height: var(--slashHeight);
  width: var(--slashHeight);
}
.step .slash:after {
  content: ' ';
  position: absolute;
  display: block;
  margin-left: -1px;
  left: calc(var(--slashHeight) / 2);
  height: var(--slashHeight);
  width: 2px;
  background-color: rgb(var(--audiWhite));
  transform: rotate(45deg);
}

.step .dot {
  display: inline-block;
  position: relative;
  height: var(--dotSize);
  width: var(--dotSize);
  border-radius: 50%;
  background-color: rgb(var(--audiWhite));
  left: calc(50% - 4px);
  top: calc(50% - 4px);
  transform: translate(-50%, -50%);
}

.step .skip {
  display: inline-block;
  position: relative;
  height: var(--skipSize);
  width: var(--skipSize);
  border-radius: 50%;
  border: solid 2px rgb(var(--audiWhite));
  left: calc(50% - 3px);
  top: calc(50% - 3px);
  transform: translate(-50%, -50%);
}

/*-----------STEP STATES------------*/

.step .checkmark,
.step .slash,
.step .skip,
.step .dot {
  display: none;
}

.step.processing.finished .checkmark,
.step.processing.impossible .slash,
.step.processing.skipped .skip {
  display: none;
}

.step.processing .dot {
  display: inline-block;
}

.step.finished .checkmark {
  display: inline-block;
}

.step.skipped .skip {
  display: inline-block;
}

.step.impossible .slash {
  display: inline-block;
}

.step.processing ~ .step,
.step.processing ~ .connector {
  opacity: 0.4;
}

.step.processing ~ .step .checkmark,
.step.processing ~ .step .skip,
.step.processing ~ .step .dot {
  display: none;
}

.step.hidden,
.connector.hidden {
  display: none;
}

/*-----------Explaination Wizard------------*/
.UE_0704_WizardBar.explain .step .checkmark,
.UE_0704_WizardBar.explain .step .skip,
.UE_0704_WizardBar.explain .step .slash {
  display: none;
}

.UE_0704_WizardBar.explain .connector {
  visibility: hidden;
}

/*-----------Settings------------*/

.settings {
  margin-top: 19px;
  background-color: black;
  height: 1200px;
  position: relative;
  top: 400px;
}
